import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const key = 'screeningMetrics';

const slice = createSlice({
  name: key,

  initialState: {
    data: undefined,
    loading: false,
    fetchError: null,
  },

  reducers: {
    fetchScreeningMetrics: (state) => {
      state.loading = true;
      state.fetchError = null;
    },

    fetchScreeningMetricsSuccess: (state, action) => {
      state.loading = false;
      state.fetchError = null;
      state.data = action.payload;
    },

    fetchScreeningMetricsError: (state, action) => {
      state.loading = false;
      state.fetchError = action.payload;
    },
  },
});

const {
  actions: {
    fetchScreeningMetrics,
    fetchScreeningMetricsSuccess,
    fetchScreeningMetricsError,
  },
  reducer,
} = slice;

reducer.key = key;

export default reducer;

export {
  fetchScreeningMetrics,
  fetchScreeningMetricsSuccess,
  fetchScreeningMetricsError,
};

export function selectScreeningSlice(state) {
  return state[reducer.key];
}

export function selectTargetScreening(state) {
  return get(selectScreeningSlice(state), 'data', undefined);
}

export function selectLoading(state) {
  return get(selectScreeningSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectScreeningSlice(state), 'fetchError', null);
}
