import React, { useMemo } from 'react';
import styledEmotion from 'styled-components';
import { styled } from '@connect-ui/styles';
import ExpandedCell from './ExpandedCell';
import { CellPropTypes } from './helpers';
import stripHTMLTags from '../../../helpers/strip-html-tags';
import legacyIntegration from '../../../helpers/legacy-integration';

const Container = styled('div', () => ({
  paddingRight: '44px',
}));

const ProjectTitle = styled('div', ({ $theme, $isExpanded }) => {
  return {
    color: $theme.colors.buttonMinimalText,
    fontWeight: 'bold',
    paddingBottom: $isExpanded ? '0px' : '4px',
    display: 'inline-block',
    ':hover': {
      textDecoration: 'underline',
      cursor: 'pointer',
    },
  };
});

const MoreLessButton = styled('span', ({ $theme }) => ({
  background: 'transparent',
  color: $theme.colors.buttonMinimalText,
}));

// This is usefull since we are using a CSSReset globally.
const RegularStyles = styledEmotion.div`
  html,
  body,
  div,
  span,
  object,
  iframe,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  blockquote,
  pre,
  abbr,
  code,
  em,
  img,
  small,
  strong,
  sub,
  sup,
  ol,
  ul,
  li,
  fieldset,
  form,
  label,
  legend,
  table,
  tbody,
  tfoot,
  thead,
  tr,
  th,
  td,
  article,
  aside,
  footer,
  header,
  nav,
  section,
  time,
  audio,
  video {
    margin-bottom: 1em;
  }

  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.6em;
  }

  h3 {
    font-size: 1.2em;
  }

  ul li {
    list-style: disc;
    position: relative;
    left: 18px;
  }

  ol li {
    list-style: decimal;
    position: relative;
    left: 18px;
  }
`;

export default function ProjectCell({ row }) {
  const {
    isExpanded,
    toggleRowExpanded,
    original: { name = '', description = '', id = '', disabled },
  } = row;

  const cleanDescription = useMemo(() => stripHTMLTags(description), [
    description,
  ]);

  const canDescriptionExpand = cleanDescription.length > 240;

  return (
    <Container>
      <ProjectTitle
        onClick={() =>
          legacyIntegration.informViewProject(parseInt(id, 10), disabled)
        }
        $isExpanded={isExpanded}
      >
        {name}
      </ProjectTitle>

      <br />

      <ExpandedCell isExpanded={isExpanded} label="Project description">
        {/* eslint-disable-next-line react/no-danger */}
        <RegularStyles dangerouslySetInnerHTML={{ __html: description }} />
        <br />
        {canDescriptionExpand && (
          <MoreLessButton type="button" onClick={() => toggleRowExpanded()}>
            Less
          </MoreLessButton>
        )}
      </ExpandedCell>

      {!isExpanded && (
        <>
          {cleanDescription.substr(0, 240)}
          {canDescriptionExpand && '... '}
          {canDescriptionExpand && (
            <MoreLessButton type="button" onClick={() => toggleRowExpanded()}>
              More
            </MoreLessButton>
          )}
        </>
      )}
    </Container>
  );
}

ProjectCell.propTypes = CellPropTypes;
