import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import {
  CardEditButton,
  CardHeader,
  CardTitle,
  ProjectViewCardContainer,
} from './styles';
import {
  selectMode,
  selectModeDisabled,
  selectProjectStatus,
  setMode as _setMode,
} from '../../../store/ducks/projectView';

export default function ProjectViewCard(props) {
  const { title, component: Component } = props;

  const dispatch = useDispatch();
  const mode = useSelector(selectMode);
  const disabled = useSelector(selectModeDisabled);

  const setMode = useCallback((nextMode) => dispatch(_setMode(nextMode)), [
    dispatch,
  ]);

  const status = useSelector(selectProjectStatus);

  return (
    <ProjectViewCardContainer>
      <CardHeader>
        <CardTitle>{title}</CardTitle>
        {mode === 'view' && status !== 'DRAFT' && (
          <CardEditButton disabled={disabled} onClick={() => setMode('edit')}>
            Edit
          </CardEditButton>
        )}
      </CardHeader>

      {Component && <Component mode={mode} setMode={setMode} />}
    </ProjectViewCardContainer>
  );
}

ProjectViewCard.propTypes = {
  title: PropTypes.string,
  component: PropTypes.elementType,
};

ProjectViewCard.defaultProps = {
  title: '',
  component: null,
};
