export default function findLocale(locales, predicate) {
  for (let i = 0; i < locales.length; i += 1) {
    const locale = locales[i];
    if (predicate(locale)) return locale;

    if (locale.languages) {
      const match = findLocale(locale.languages, predicate);
      if (match) return match;
    }
  }

  return null;
}
