import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import get from 'lodash/get';
import { Header, Spacer } from './styles';
import DragableItem from './DragableItem';
import Accordion from '../../Accordion';

export default function SideModules(props) {
  // Temporary hack to hide questions
  const questionsToExclude = [
    'Mobile device operating system',
    'Tablet device ownership',
  ];

  const {
    isLoading,
    modules,
    groups,
    categories,
    title,
    selectStepEditorDirty,
    selectProcessModules,
    renderAsAccordion,
  } = props;

  const hasModules = useMemo(() => Object.keys(groups).length > 0, [groups]);

  if (isLoading) {
    return (
      <>
        <Shimmer width={287} height={53} />
        <Spacer />
        <Shimmer width={287} height={53} />
        <Spacer />
        <Shimmer width={287} height={53} />
        <Spacer />
        <Shimmer width={287} height={53} />
        <Spacer />
        <Shimmer width={287} height={53} />
        <Spacer />
        <Shimmer width={287} height={53} />
      </>
    );
  }

  const renderDragableItem = (
    item,
    parentId = null,
    nested = false,
    canDrag = true,
  ) => {
    const subQuestions = modules.filter(
      (m) => get(m, 'parentQuestion.parentQuestionId', null) === item.id,
    );

    return (
      <DragableItem
        parentId={parentId}
        key={item.id}
        id={item.id}
        name={item.name}
        sequenceFlow={item.sequenceFlow}
        description={item.question}
        canBeDraggedMultipleTimes
        items={subQuestions || []}
        canDrag={canDrag}
        nested={nested}
        selectStepEditorDirty={selectStepEditorDirty}
        selectProcessModules={selectProcessModules}
      />
    );
  };

  return (
    <>
      <Header>{title}</Header>
      {hasModules && renderAsAccordion
        ? Object.keys(groups).map((module, index) => (
            <Accordion
              title={categories[module]}
              lastItem={groups.length === index + 1}
            >
              {groups[module].map((question) => {
                // Temporary hack to hide questions
                if (questionsToExclude.includes(question.name)) {
                  return null;
                }

                if (
                  get(question, 'parentQuestion.parentQuestionId', null) !==
                  null
                ) {
                  return null;
                }

                const subQuestions = modules.filter(
                  (m) =>
                    get(m, 'parentQuestion.parentQuestionId', null) ===
                    question.id,
                );

                const subQuestionsParents = modules.filter(
                  (m) =>
                    get(m, 'parentQuestion.parentQuestionId', null) ===
                    get(subQuestions, [0, 'id'], undefined),
                );

                const items = subQuestions.concat(subQuestionsParents);

                return (
                  <DragableItem
                    key={question.id}
                    id={question.id}
                    name={question.name}
                    sequenceFlow={question.sequenceFlow}
                    description={question.question}
                    items={items}
                    nested
                    selectStepEditorDirty={selectStepEditorDirty}
                    selectProcessModules={selectProcessModules}
                  >
                    {items.map((subQuestion) =>
                      renderDragableItem(subQuestion, question.id, true, false),
                    )}
                  </DragableItem>
                );
              })}
            </Accordion>
          ))
        : modules.map((item) => renderDragableItem(item, null, false))}
    </>
  );
}

SideModules.defaultProps = {
  modules: [],
  groups: [],
  categories: [],
  renderAsAccordion: false,
};

SideModules.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  modules: PropTypes.arrayOf(PropTypes.object),
  groups: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
  selectStepEditorDirty: PropTypes.func.isRequired,
  selectProcessModules: PropTypes.func.isRequired,
  categories: PropTypes.arrayOf(PropTypes.object),
  renderAsAccordion: PropTypes.bool,
};
