import { connect } from 'react-redux';
import ProjectOverview from './ProjectOverview';
import { projectTypes } from '../../../../helpers/mock-data';
import { findByValue } from '../../../../../../helpers/find';
import { ENTITIES, selectors } from '../../../../../../store/ducks/options';
import { selectProjectOverview } from '../../../../../../store/ducks/project';

function mapStateToProps(state) {
  const projectOverview = selectProjectOverview(state);

  const workTypesData = selectors.selectDataFor(
    ENTITIES.workTypes,
    null,
    state,
  );

  const taskVolumesData = selectors.selectDataFor(
    ENTITIES.taskVolumes,
    null,
    state,
  );

  const { taskType, taskVolume, type } = projectOverview;

  return {
    projectName: projectOverview.name,
    projectAlias: projectOverview.alias,
    projectDescription: projectOverview.description,
    projectType: (findByValue(projectTypes, type) || {}).label || '',
    taskType: (findByValue(workTypesData, taskType) || {}).label || '',
    taskVolume: (findByValue(taskVolumesData, taskVolume) || {}).label || '',
  };
}

export default connect(mapStateToProps)(ProjectOverview);
