import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const KEY = 'profileQuestions';
const UNDO_TYPE = `${KEY}/undo`;

const slice = createSlice({
  name: KEY,

  initialState: {
    data: [],
    loading: false,
    fetchError: null,
    saving: false,
    saveError: null,
  },

  reducers: {
    fetchProfileQuestions: (state) => {
      state.loading = true;
      state.fetchError = null;
    },

    fetchProfileQuestionsSuccess: (state, action) => {
      state.loading = false;
      state.fetchError = null;
      state.data = action.payload;
    },

    fetchProfileQuestionsError: (state, action) => {
      state.loading = false;
      state.fetchError = action.payload;
    },

    saveProfileQuestions: (state) => {
      state.saving = true;
      state.saveError = null;
    },

    saveProfileQuestionsSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.data = action.payload;
    },

    saveProfileQuestionsError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
    },

    saveCategory: (state) => {
      state.saving = true;
      state.saveError = null;
    },

    saveCategorySuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.data = action.payload;
    },

    saveCategoryError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
    },
  },
});

const {
  actions: {
    fetchProfileQuestions,
    fetchProfileQuestionsSuccess,
    fetchProfileQuestionsError,
    saveProfileQuestions,
    saveProfileQuestionsSuccess,
    saveProfileQuestionsError,
    saveCategory,
    saveCategorySuccess,
    saveCategoryError,
  },
  reducer,
} = slice;

reducer.key = KEY;

export default reducer;

export {
  fetchProfileQuestions,
  fetchProfileQuestionsSuccess,
  fetchProfileQuestionsError,
  saveProfileQuestions,
  saveProfileQuestionsSuccess,
  saveProfileQuestionsError,
  saveCategory,
  saveCategorySuccess,
  saveCategoryError,
};

export function selectProfileQuestionsSlice(state) {
  return state[reducer.key];
}

export function selectProfileQuestions(state) {
  return get(selectProfileQuestionsSlice(state), 'data', []);
}

export function selectLoading(state) {
  return get(selectProfileQuestionsSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectProfileQuestionsSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectProfileQuestionsSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectProfileQuestionsSlice(state), 'saveError', null);
}

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}
