import React from 'react';
import PropTypes from 'prop-types';
import { Divider, Container, NodesWrapper, Pusher } from './styles';

export default function HorizontalFlex(props) {
  const { hideDivider, dividerMargin, leftNodes, rightNodes } = props;

  return (
    <Container>
      {!hideDivider && (
        <Divider marginTop={dividerMargin} marginBottom={dividerMargin} />
      )}
      <NodesWrapper>
        {leftNodes}

        <Pusher />

        {rightNodes}
      </NodesWrapper>
    </Container>
  );
}

HorizontalFlex.propTypes = {
  dividerMargin: PropTypes.number,
  leftNodes: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  rightNodes: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  hideDivider: PropTypes.bool,
};

HorizontalFlex.defaultProps = {
  dividerMargin: 16,
  leftNodes: null,
  rightNodes: null,
  hideDivider: false,
};
