import React from 'react';
import PropTypes from 'prop-types';
import { Header, DemographicDistribution } from './styles';
import legacyIntegration from '../../../helpers/legacy-integration';
import DemographicsModal from '../../../components/ProjectForms/RecruitmentForm/Demographics/DemographicsModal';

export default function EmbeddedDemographics(props) {
  const { modalControls, project, parameters, loadingRequirements } = props;

  function closeModal() {
    legacyIntegration.informDemographicsClose(project);
    modalControls.closeModal();
  }

  return (
    <>
      <DemographicDistribution>
        <Header>
          <h2>Demographic Requirements</h2>
        </Header>
        {parameters.length && !loadingRequirements ? (
          <DemographicsModal
            closeModal={closeModal}
            openModal={modalControls.openModal}
            modalData={modalControls.modalData}
          />
        ) : (
          <div />
        )}
      </DemographicDistribution>
    </>
  );
}

EmbeddedDemographics.propTypes = {
  modalControls: PropTypes.objectOf(PropTypes.any).isRequired,
  parameters: PropTypes.arrayOf(PropTypes.object).isRequired,
  project: PropTypes.arrayOf(PropTypes.object).isRequired,
  loadingRequirements: PropTypes.bool.isRequired,
};
