import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { ReactComponent as RedIcon } from '../../../assets/icons/attention-red.svg';
import { ReactComponent as GreyIcon } from '../../../assets/icons/attention-grey.svg';
import { Icon } from './styles';

export default function WarningCell({ row }) {
  const {
    original: { warning, verified, userId, setCurrentUserId },
  } = row;

  useEffect(() => {
    setCurrentUserId(userId);
  }, [setCurrentUserId, userId]);

  const WarningIcon = <Icon>{verified ? <GreyIcon /> : <RedIcon />}</Icon>;

  return warning ? WarningIcon : null;
}

WarningCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({
      warning: PropTypes.bool,
      verified: PropTypes.bool,
      userId: PropTypes.number,
      setCurrentUserId: PropTypes.func,
    }),
  }).isRequired,
};
