import styled from 'styled-components';

export default styled.button`
  position: absolute;
  right: -20px;
  bottom: 0px;
  transform: ${(props) => `rotateX(${props.flip ? '-180deg' : '0 deg'})`};
  background: none;

  & > svg {
    position: relative;
    bottom: ${(props) => `${props.flip ? '5px' : '0px'}`};
  }
`;
