import { call, put, takeEvery } from 'redux-saga/effects';
import { setConfiguration, mutationFailed, mutationSucceeded } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { fromDTOToLocalState, fromLocalStateToDTO } from './converter';

function* createInvoiceConfigurationSaga(action) {
  const {
    payload: { projectId, configuration },
  } = action;

  try {
    const response = yield call(
      axios.put,
      ENDPOINTS.V2.INVOICE_CONFIGURATION({
        pathVariables: {
          id: projectId,
        },
      }),
      fromLocalStateToDTO(configuration),
    );

    yield put(
      mutationSucceeded({
        projectId,
        configuration: fromDTOToLocalState(response.data),
      }),
    );
  } catch (error) {
    yield put(mutationFailed(error));
  }
}

export default takeEvery(setConfiguration.type, createInvoiceConfigurationSaga);
