import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { InputBase } from '../../../../../../../Input/styles';

export default function InputCell(props) {
  const {
    column: { id: hiringTargetID },
    cell: { value },
    row: {
      original: { handleFieldChange },
    },
  } = props;

  const onChange = useCallback(
    (e) => {
      handleFieldChange(hiringTargetID, `${e.target.value}`);
    },
    [handleFieldChange, hiringTargetID],
  );

  return (
    <InputBase
      key={hiringTargetID}
      placeholder="0"
      onChange={onChange}
      value={value || ''}
    />
  );
}

InputCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  }).isRequired,
  row: PropTypes.shape({
    original: PropTypes.shape({
      handleFieldChange: PropTypes.func.isRequired,
    }).isRequired,
  }).isRequired,
  column: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
};
