import React, { useState, useEffect, useMemo } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';

import { noop } from 'lodash';

import Switch from '../../../../../components/Switch';

import Disabled from '../../../../../components/Disabled';
import { useModal } from '../../../../../components/Modal';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
  PopoverTrigger,
} from '../../../../../components/Popover';

import GoalsModal from '../GoalsModal';
import GoalsSettings from '../GoalsSettings';
import QualityMetricModal from '../QualityMetricModal';

import pencil from '../../../../../assets/icons/pencil.svg';
import star from '../../../../../assets/icons/star.svg';
import { ReactComponent as Elipsis } from '../../../../../assets/icons/elipsis.svg';
import { ReactComponent as AddNewIcon } from '../../../../../assets/icons/add-new-rounded.svg';

import {
  setQualityMainMetric,
  setQualityMetric,
  setProductivityMetric,
} from '../../../../../store/ducks/metricsAndGoals';
import { selectProject as currentProject } from '../../../../../store/ducks/project';

import {
  PRODUCTIVITY_METRICS_TYPE,
  QUALITY_METRICS_TYPE,
  METRIC_LIST,
  CALCULATION_LIST,
  generateEmptyQualityMetric,
} from '../constants';

import {
  Card,
  CardHeader,
  Title,
  CardBody,
  CardAppend,
  Tag,
  AlertMessage,
  AddNewButton,
} from './styles';
import { SectionDescription } from '../styles';

const renderDescription = (metric) => {
  const mName = METRIC_LIST.find((el) => el.value === metric.metric).label;
  const cName = CALCULATION_LIST.find((el) => el.value === metric.calculation)
    .label;

  return (
    <SectionDescription
      style={{ fontSize: 12, lineHeight: '16px', marginTop: 4 }}
    >
      {`${mName} | ${cName}`}
    </SectionDescription>
  );
};

export default function MetricsPanel({ mode, metric, panelType }) {
  const dispatch = useDispatch();

  const { rateType } = useSelector(currentProject);
  const [localMetric, setLocalMetric] = useState({});
  const [settingsModalProps, settingsModalControls] = useModal();
  const [goalsModalProps, goalsModalControls] = useModal();

  const {
    metricName,
    required,
    disabled,
    metricType,
    isCollapsed,
    primary,
    key,
    error,
  } = localMetric;

  const isProductivity = useMemo(
    () => panelType === PRODUCTIVITY_METRICS_TYPE,
    [panelType],
  );

  const isQuality = useMemo(() => panelType === QUALITY_METRICS_TYPE, [
    panelType,
  ]);

  const isGoalsConfigured = useMemo(() => {
    let flag = false;

    if (localMetric.qualityGoals && localMetric.qualityGoals.length) {
      flag = localMetric.qualityGoals[0].goalValueType === null;
    }

    return !flag;
  }, [localMetric]);

  const handleOpenModal = () => {
    settingsModalControls.openModal();
  };

  const handleMainMetricChange = () => {
    dispatch(setQualityMainMetric({ metric: localMetric }));
  };

  const handleQualityMetricsUpdate = (qualityMetric) => {
    dispatch(setQualityMetric({ qualityMetric }));
  };

  const handleAddNewQuizDate = () => {
    const newMetric = generateEmptyQualityMetric();
    const tMetric = { ...localMetric };
    tMetric.qualityGoals = localMetric.qualityGoals.concat(
      newMetric.qualityGoals,
    );

    setLocalMetric(tMetric);

    goalsModalControls.openModal();
  };

  const handleGoalsModalCancel = () => {
    goalsModalControls.closeModal();
    setLocalMetric({
      ...localMetric,
      qualityGoals: metric.qualityGoals,
      productivityGoals: metric.productivityGoals,
    });
  };

  const tooltipContent = (
    <>
      <PopoverItem
        onClick={handleOpenModal}
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={pencil} alt="Edit Metric Name icon" />
        <PopoverText>Edit Metric Name</PopoverText>
      </PopoverItem>

      <Disabled
        when={!!primary}
        message="You must have one main quality metric. Set another quality metric as the main one, to replace this one."
        focusOffset={1}
      >
        <PopoverItem
          onClick={() => handleMainMetricChange(metric.key)}
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={star} alt="Set as main quality metric icon" />
          <PopoverText>Set as main quality metric</PopoverText>
        </PopoverItem>
      </Disabled>
    </>
  );

  useEffect(() => {
    const metricValue = { ...metric };

    if (rateType === 'HOURLY') {
      metricValue.required = metric.metricType === 'HOURS_WEEK';
    }

    if (rateType === 'PIECERATE') {
      metricValue.required =
        metric.metricType === 'UNITS_WEEK' ||
        metric.metricType === 'UNITS_WORK_HOUR';
    }

    setLocalMetric(metricValue);
  }, [isProductivity, isQuality, metric, rateType]);

  const toggleActiveMetric = () => {
    const updatedMetric = { ...localMetric };
    updatedMetric.disabled = !localMetric.disabled;
    updatedMetric.isCollapsed = !localMetric.disabled;

    if (isProductivity) {
      dispatch(setProductivityMetric({ productivityMetric: updatedMetric }));
    }

    if (isQuality) {
      updatedMetric.primary = false;
      dispatch(setQualityMetric({ qualityMetric: updatedMetric }));
    }

    setLocalMetric(updatedMetric);
  };

  const handleCollapsePanel = () => {
    const updatedMetric = { ...localMetric };
    updatedMetric.isCollapsed = !localMetric.isCollapsed;
    setLocalMetric(updatedMetric);
  };

  return (
    <div key={key}>
      <Card className={error ? 'card required' : 'card'}>
        <CardHeader>
          <div
            style={{ flex: 1, cursor: 'pointer' }}
            onClick={() => handleCollapsePanel(key)}
            aria-hidden="true"
          >
            <Title>{metricName}</Title>
            {isQuality && renderDescription(metric)}
          </div>

          <span style={{ alignItems: 'center' }}>
            {required && <Tag>Required</Tag>}
            {primary && <Tag type="primary">Main Quality Metric</Tag>}
            <Switch
              name={`${metricType}-activateMetric`}
              onChange={() => toggleActiveMetric(key)}
              value={required || !disabled}
              disabled={mode === 'view' || required}
            />
            {isQuality && mode === 'edit' && (
              <Disabled when={disabled} message="" focusOffset={1}>
                <div style={{ cursor: 'pointer' }}>
                  <Popover content={tooltipContent} placement="bottom-end">
                    {(shown) => (
                      <PopoverTrigger
                        style={{
                          position: 'relative',
                          top: 2,
                          margin: '0 0 0 12px',
                        }}
                        isShown={shown}
                      >
                        <Elipsis />
                      </PopoverTrigger>
                    )}
                  </Popover>
                </div>
              </Disabled>
            )}
          </span>
        </CardHeader>
        <CardBody className={!isCollapsed ? 'active' : 'collapsed'}>
          <GoalsSettings
            mode={mode}
            metric={metric}
            showEdit={required || !disabled}
            handleEdit={goalsModalControls.openModal}
            panelType={panelType}
          />
        </CardBody>
        {mode === 'edit' && localMetric.quizBased && !isCollapsed && (
          <CardAppend>
            <Disabled
              when={!isGoalsConfigured}
              message="Required goals to be configured for current quiz date before creating a new quiz."
              focusOffset={1}
            >
              <AddNewButton onClick={() => handleAddNewQuizDate()}>
                <AddNewIcon />
                <span style={{ marginLeft: '7px' }}>Add Quiz Date</span>
              </AddNewButton>
            </Disabled>
          </CardAppend>
        )}
      </Card>
      <GoalsModal
        metric={localMetric}
        handleCancel={handleGoalsModalCancel}
        modalProps={goalsModalProps}
        modalControls={goalsModalControls}
        panelType={panelType}
      />

      <AlertMessage className={!error && 'hide'}>{error}</AlertMessage>
      <QualityMetricModal
        onChange={handleQualityMetricsUpdate}
        modalProps={settingsModalProps}
        modalControls={settingsModalControls}
        metric={localMetric}
      />
    </div>
  );
}

MetricsPanel.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']),
  style: PropTypes.shape({}),
  metric: PropTypes.shape({
    name: PropTypes.string,
    key: PropTypes.string,
    disabled: PropTypes.bool,
    metricType: PropTypes.string,
    isCollapsed: PropTypes.bool,
    productivityGoals: PropTypes.array,
    qualityGoals: PropTypes.array,
  }),
  panelType: PropTypes.oneOf([PRODUCTIVITY_METRICS_TYPE, QUALITY_METRICS_TYPE])
    .isRequired,
};

MetricsPanel.defaultProps = {
  mode: 'view',
  style: {},
  metric: {},
};
