import { useEffect } from 'react';
import { withFormik } from 'formik';
import * as yup from 'yup';
import { compose } from 'recompose';
import { batch, connect, useSelector, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import ProjectOverview from './ProjectOverview';
import { projectOverviewYupValidations } from '../../../../components/ProjectForms/ProjectOverviewForm';
import {
  cloneProject,
  createProject,
  patchProject,
  selectProject,
  selectProjectOverview,
  selectProjectTools,
} from '../../../../store/ducks/project';
import { selectAll as selectAllLocaleTenants } from '../../../../store/ducks/localeTenants';
import { selectAll as selectAllPayrates } from '../../../../store/ducks/payrates';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import {
  selectData as selectExperimentGroups,
  selectAccessHolder,
  fetchAccessHolder,
  fetchExperimentGroups,
} from '../../../../store/ducks/experimentGroups';
import STEP_NUMBER from '../../../../constants/stepNumber';
import { useClone } from '../../helpers/cloneContext';
import { useRawHiringTargets } from '../../../../components/ProjectForms/RecruitmentForm/HiringTargets/helpers';
import {
  fetchInvoiceConfiguration,
  selectInvoiceConfiguration,
} from '../../../../store/ducks/invoiceConfiguration';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
  selectors,
} from '../../../../store/ducks/options';
import {
  fetchAutoScreening,
  selectAutoScreenings,
} from '../../../../store/ducks/autoScreening';
import {
  fetchDemographics,
  selectDemographicType,
} from '../../../../store/ducks/projectDemographicRequirements';
import { fetchAll as fetchDemographicsParameters } from '../../../../store/ducks/demographicRequirements';
import {
  getOrInitializeRequirementsByGroup,
  getOrInitializeAggregatedRequirements,
} from '../../../../components/ProjectForms/RecruitmentForm/Demographics/DemographicsModal/DemographicData';
import {
  fetchAll as fetchProjectPages,
  selectAll as selectProjectPages,
} from '../../../../store/ducks/projectPages';
import useFetchUserGroups from '../../../../store/ducks/options/helpers/fetchUserGroups';

function useHooksAsProps() {
  const dispatch = useDispatch();

  const project = useSelector(selectProject) || {};
  const localeTenants = useSelector(selectAllLocaleTenants);
  const payrates = useSelector(selectAllPayrates);
  const experimentGroups = useSelector(selectExperimentGroups);
  const accessHolders = useSelector(selectAccessHolder);
  const autoScreenings = useSelector(selectAutoScreenings);
  const demographicType = useSelector(selectDemographicType);
  const { hasExternalSystem } = useSelector(selectProjectTools);
  const allProjectResources = useSelector(
    selectors.createSelectDataFor(ENTITIES.projectResources, project.id),
  );
  const enabledProjects = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.projects,
      ENTITY_METADATA.projects.nestedKeys.ENABLED,
    ),
  );

  const hiringTargets = useRawHiringTargets(project.id);
  const requirementsByGroup = getOrInitializeRequirementsByGroup();
  const aggregatedRequirements = getOrInitializeAggregatedRequirements();
  const userGroups = useFetchUserGroups();

  const {
    isCloning,
    setIsCloning,
    isCloneCreated,
    setIsCloneCreated,
    setBlockingRulesInconsistencies,
  } = useClone();

  useEffect(() => {
    batch(() => {
      if (isCloning) {
        dispatch(fetchProjectPages());
        dispatch(fetchDemographics());
        dispatch(fetchDemographicsParameters());
        dispatch(fetchAutoScreening({ projectId: project.id }));
        dispatch(fetchInvoiceConfiguration({ projectId: project.id }));
        dispatch(
          fetchAccessHolder({ projectId: project.id, hasExternalSystem }),
        );
        dispatch(
          fetch({
            optionsFor: ENTITIES.projectResources,
            nestedKey: project.id,
          }),
        );
        dispatch(
          fetch({
            optionsFor: ENTITIES.projects,
            nestedKey: ENTITY_METADATA.projects.nestedKeys.ENABLED,
          }),
        );
      }
    });
  }, [dispatch, hasExternalSystem, isCloning, project.id]);

  useEffect(() => {
    if (accessHolders !== undefined) {
      accessHolders.forEach((accessHolder) =>
        dispatch(fetchExperimentGroups({ experimentId: accessHolder.id })),
      );
    }
  }, [dispatch, accessHolders]);

  return {
    project,
    isCloning,
    setIsCloning,
    isCloneCreated,
    setIsCloneCreated,
    setBlockingRulesInconsistencies,
    localeTenants,
    payrates,
    hiringTargets,
    accessHolders,
    experimentGroups,
    hasExternalSystem,
    allProjectResources,
    autoScreenings,
    requirementsByGroup,
    aggregatedRequirements,
    demographicType,
    enabledProjects,
    userGroups,
  };
}

function mapStateToProps(state, outerProps) {
  const { isCloning, isCloneCreated, project } = outerProps;

  const projectOverview = selectProjectOverview(state);
  const invoiceConfig = get(
    selectInvoiceConfiguration(state, project.id),
    'configuration',
    null,
  );

  const projectPages = selectProjectPages(state);

  const initialValues = {};

  initialValues.projectName = projectOverview.name;
  initialValues.projectAlias = projectOverview.alias;
  initialValues.projectDescription = projectOverview.description;
  initialValues.projectType = projectOverview.type || undefined;
  initialValues.taskType = projectOverview.taskType || undefined;
  initialValues.taskVolume = projectOverview.taskVolume || 'AVERAGE';

  if (isCloning && !isCloneCreated) {
    delete initialValues.projectName;
    delete initialValues.projectAlias;
  }

  return {
    initialValues,
    hasProject: project.id && project.id > -1,
    invoiceConfig,
    projectPages,
  };
}

async function handleSubmit(values, { props }) {
  const { hasProject, dispatch, isCloning, isCloneCreated } = props;

  const payload = {};

  payload.name = values.projectName;
  payload.alias = values.projectAlias;
  payload.description = values.projectDescription;
  payload.projectType = values.projectType;
  payload.workType = values.taskType;
  payload.taskVolume = values.taskVolume;

  const nextStep = STEP_NUMBER.PROJECT_OVERVIEW + 1;

  dispatch(setDirty(false));

  if (isCloning && !isCloneCreated) {
    dispatch(
      cloneProject({
        project: payload,
        thenPut: values.navigate ? navigate(nextStep) : undefined,
        props,
      }),
    );
    return;
  }

  if (hasProject) {
    dispatch(
      patchProject({
        changes: payload,
        thenPut: values.navigate ? navigate(nextStep) : undefined,
      }),
    );
  } else {
    dispatch(
      createProject({
        project: payload,
        thenPut: values.navigate ? navigate(nextStep) : undefined,
      }),
    );
  }
}

export default compose(
  withHooks(useHooksAsProps),
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    enableReinitialize: true,
    validateOnMount: true,
    validationSchema: yup
      .object()
      .shape(projectOverviewYupValidations(selectProject)),
  }),
)(ProjectOverview);
