import React from 'react';
import PropTypes from 'prop-types';
import Checkbox from '../Checkbox';

export default function CheckboxRow({ row }) {
  const checkboxProps = row.getToggleRowSelectedProps();

  return (
    <div>
      <Checkbox
        onChange={checkboxProps.onChange}
        checked={checkboxProps.checked}
        title={checkboxProps.title}
        indeterminate={checkboxProps.indeterminate}
        style={checkboxProps.style}
        data-testid={`table--row-checkbox-${row.id}`}
      />
    </div>
  );
}

CheckboxRow.propTypes = {
  row: PropTypes.shape({
    getToggleRowSelectedProps: PropTypes.func.isRequired,
    id: PropTypes.string.isRequired,
  }).isRequired,
};
