import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { ReactComponent as Chevron } from '../../assets/icons/chevron-down.svg';
import {
  AccordionSection,
  AccordionHeader,
  AccordionTitle,
  AccordionContent,
  ChevronWrapper,
  Divisor,
} from './styles';

export default function Accordion({ title, children, lastItem }) {
  const [collapsed, setCollapsed] = useState('');

  const toggleAccordion = () => {
    setCollapsed(collapsed === '' ? 'active' : '');
  };

  return (
    <AccordionSection>
      <AccordionHeader onClick={toggleAccordion}>
        <AccordionTitle>{title}</AccordionTitle>
        <ChevronWrapper className={collapsed}>
          <Chevron />
        </ChevronWrapper>
      </AccordionHeader>
      <AccordionContent className={collapsed}>{children}</AccordionContent>
      {lastItem && <Divisor />}
    </AccordionSection>
  );
}

Accordion.defaultProps = {
  title: undefined,
  lastItem: false,
};

Accordion.propTypes = {
  title: PropTypes.string,
  children: PropTypes.oneOfType([PropTypes.node]).isRequired,
  lastItem: PropTypes.bool,
};
