import React from 'react';
import PropTypes from 'prop-types';
import noop from 'lodash/noop';
import AdvancedFilters from './AdvancedFilters';
import Select from '../../../../components/Select';
import Button from '../../../../components/Button';
import BulkModal from '../../../../components/BulkModal';
import ScreeningNumbersModal from '../../../../components/ScreeningNumbersModal';
import Input from '../../../../components/Input';
import Icon from '../../../../components/Icon';
import TitleTooltip from '../../../../components/Table/TitleTooltip';
import RichDescription from '../../../../components/RichDescription';
import { MENU_OPTIONS } from '../../../../constants/bulkUploadOptions';
import eye from '../../../../assets/icons/eye.svg';
import filter from '../../../../assets/icons/filter.svg';
import arrow from '../../../../assets/icons/chevron-down-blue.svg';
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg';
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close-blue.svg';
import {
  Container,
  EyeIcon,
  FilterButton,
  FilterIcon,
  ViewNumbersButton,
  Wrapper,
  DropdownItem,
  DropdownIcon,
  DropdownMenu,
  SelectedFilters,
} from './styles';

export default function Filters(props) {
  const {
    resetFilters,
    countSelectedFilters,
    statusOptions,
    showAdvancedFilters,
    filterSummary,
    handleNameChange,
    handleStatusChange,
    handleResetFilters,
    handleApplyFilters,
    handleFilterCountChange,
    handleShowAdvancedFilters,
    handleFilterSummary,
    handleBulkOption,
    handleAdvancedFiltersChange,
    bulkOption,
    openBulkModal,
    setOpenBulkModal,
    openNumbersModal,
    setOpenNumbersModal,
    values,
    submitCount,
    errors,
    selectedRows,
    bulkDisabled,
    metrics,
    showMenu,
    handleShowMenu,
  } = props;

  const menu = (isDisabled) => {
    if (!isDisabled) {
      return (
        <ul>
          {MENU_OPTIONS.map((key) => (
            <li key={key.id}>
              <DropdownItem
                type="tertiary"
                onClick={() =>
                  key.type === 'modal' ? handleBulkOption(key) : noop
                }
              >
                {key.label}
              </DropdownItem>
            </li>
          ))}
        </ul>
      );
    }
    return null;
  };

  return (
    <>
      <Container>
        <Wrapper>
          <div>
            <ViewNumbersButton onClick={() => setOpenNumbersModal(true)}>
              <EyeIcon src={eye} alt="View Numbers" />
              View Numbers
            </ViewNumbersButton>
          </div>
          <div style={{ width: 360 }}>
            <Input
              name="name"
              placeholder="Search Users"
              value={values.name}
              onChange={handleNameChange}
              error={submitCount > 0 && errors.name}
              iconBefore={
                <Icon
                  icon={<SearchIcon />}
                  inheritColor={false}
                  style={{ position: 'relative', top: 2 }}
                />
              }
            />
          </div>
          <div style={{ width: 194 }}>
            <Select
              id="status"
              name="status"
              placeholder="Select a status"
              options={statusOptions}
              value={values.status}
              onChange={handleStatusChange}
              error={submitCount > 0 && errors.status}
            />
          </div>
        </Wrapper>
        <Wrapper>
          <div>
            {countSelectedFilters > 0 && (
              <TitleTooltip
                title={<RichDescription html={filterSummary || '-'} sanitize />}
                placement="bottom"
                trigger="hover"
              >
                <SelectedFilters
                  type="secondary"
                  onClick={() => handleResetFilters(true)}
                >
                  {countSelectedFilters}{' '}
                  {countSelectedFilters === 1 ? 'Filter' : 'Filters'}
                  <CloseIcon style={{ marginLeft: 10 }} />
                </SelectedFilters>
              </TitleTooltip>
            )}
          </div>
          <div>
            <FilterButton type="secondary" onClick={handleShowAdvancedFilters}>
              <FilterIcon src={filter} alt="View Numbers" /> Filters
            </FilterButton>
          </div>
          <div>
            <Button
              type="secondary"
              disabled={bulkDisabled}
              onClick={handleShowMenu}
            >
              Bulk Upload
              <DropdownIcon src={arrow} alt="open options" />
            </Button>
            <DropdownMenu showMenu={showMenu}>
              {menu(bulkDisabled)}
            </DropdownMenu>
          </div>
        </Wrapper>
      </Container>
      <AdvancedFilters
        resetFilters={resetFilters}
        showAdvancedFilters={showAdvancedFilters}
        handleResetFilters={handleResetFilters}
        handleApplyFilters={handleApplyFilters}
        handleFilterCountChange={handleFilterCountChange}
        handleFilterSummary={handleFilterSummary}
        handleAdvancedFiltersChange={handleAdvancedFiltersChange}
      />
      <BulkModal
        open={openBulkModal}
        setOpen={setOpenBulkModal}
        bulkOption={bulkOption}
        selectedRows={selectedRows}
      />
      <ScreeningNumbersModal
        open={openNumbersModal}
        setOpen={setOpenNumbersModal}
        metrics={metrics}
        handleStatusChange={handleStatusChange}
      />
    </>
  );
}

Filters.propTypes = {
  resetFilters: PropTypes.bool.isRequired,
  countSelectedFilters: PropTypes.number.isRequired,
  statusOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ).isRequired,
  bulkOption: PropTypes.shape({
    label: PropTypes.string,
    id: PropTypes.string,
    type: PropTypes.string,
  }).isRequired,
  openBulkModal: PropTypes.bool.isRequired,
  setOpenBulkModal: PropTypes.func.isRequired,
  openNumbersModal: PropTypes.bool.isRequired,
  setOpenNumbersModal: PropTypes.func.isRequired,
  showAdvancedFilters: PropTypes.bool.isRequired,
  filterSummary: PropTypes.string,
  handleNameChange: PropTypes.func.isRequired,
  handleStatusChange: PropTypes.func.isRequired,
  handleApplyFilters: PropTypes.func.isRequired,
  handleFilterCountChange: PropTypes.func.isRequired,
  handleResetFilters: PropTypes.func.isRequired,
  handleBulkOption: PropTypes.func.isRequired,
  handleShowAdvancedFilters: PropTypes.func.isRequired,
  handleFilterSummary: PropTypes.func.isRequired,
  handleAdvancedFiltersChange: PropTypes.func.isRequired,
  values: PropTypes.shape({
    name: PropTypes.string,
    status: PropTypes.string,
  }).isRequired,
  submitCount: PropTypes.number.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  selectedRows: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
    }),
  ).isRequired,
  bulkDisabled: PropTypes.bool.isRequired,
  metrics: PropTypes.shape({
    stages: PropTypes.number,
  }),
  showMenu: PropTypes.bool.isRequired,
  handleShowMenu: PropTypes.func.isRequired,
};

Filters.defaultProps = {
  filterSummary: '',
  metrics: {},
};
