import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { Row, Col } from 'styled-bootstrap-grid';
import Button from '../../Button';
import CancelButton from './styles';
import Modal from '../../Modal';
import FloatContainer, { FloatRight } from '../../Float';
import { Section } from '../../../styles';
import ErrorPopover, { ErrorPopoverProps } from '../../ErrorPopover';

export default function DeleteModal(props) {
  const {
    data,
    modalProps,
    submitForm,
    errorPopoverProps,
    isSubmitting,
  } = props;

  const hasPayrate = get(get(data, 'userGroup', {}), 'hasPayrate', false);

  return (
    <Modal
      open={modalProps.open}
      onClose={modalProps.onClose}
      onExited={modalProps.onExited}
      title="Delete User Group Access"
      closeOnOverlayClick={!isSubmitting}
      closeOnEsc={!isSubmitting}
    >
      <Section marginBottom="56px">
        <Row>
          <Col col={12}>
            {hasPayrate && (
              <span>
                You already have a payrate configured for this user group. By
                deleting this user group access, users in this group wont be
                able to to view or work your project with the specified payrate.
                <br />
                <br />
                Are you sure you want to proceed?
              </span>
            )}
            {!hasPayrate && (
              <span>
                By deleting this user group access, users in this group wont be
                able to view or work on your project.
              </span>
            )}
          </Col>
        </Row>
      </Section>

      <ErrorPopover
        title="Failed to remove user group"
        {...errorPopoverProps}
      />

      <FloatContainer>
        <FloatRight>
          <CancelButton
            disabled={isSubmitting}
            type="secondary"
            onClick={modalProps.onClose}
            data-testid="project-setup--recruitment--tenant-modal-cancel"
          >
            Cancel
          </CancelButton>
          <Button
            loading={isSubmitting}
            onClick={submitForm}
            data-testid="project-setup--recruitment--tenant-modal-save"
          >
            Proceed
          </Button>
        </FloatRight>
      </FloatContainer>
    </Modal>
  );
}

DeleteModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  data: PropTypes.objectOf(PropTypes.any).isRequired,
  submitForm: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  hasPayrate: PropTypes.bool.isRequired,
};
