import { call, put, takeEvery, select } from 'redux-saga/effects';
import {
  putProjectStatus,
  selectProject,
  putProjectStatusError,
  putProjectStatusSuccess,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import { showInvoiceConfigError } from '../projectView';

function* putProjectStatusSaga(action) {
  const {
    payload: { status },
  } = action;

  const project = yield select(selectProject);

  if (!project) {
    yield put(
      putProjectStatusError(
        new Error("Can't update project status without a project id."),
      ),
    );
    return;
  }

  const { id } = project;

  try {
    if (status === 'ENABLED') {
      const { data: invoiceConfig } = yield call(
        axios.get,
        endpoints.V2.INVOICE_CONFIGURATION({
          pathVariables: {
            id,
          },
        }),
      );

      if (!invoiceConfig.configuredInvoice) {
        yield put(showInvoiceConfigError());
        yield put(putProjectStatusError());
        return;
      }
    }

    const { data: nextProject } = yield call(
      axios.put,
      endpoints.V2.PROJECT_STATUS({
        pathVariables: {
          id,
        },
      }),
      { status },
    );

    yield put(
      putProjectStatusSuccess({
        project: nextProject,
      }),
    );
  } catch (err) {
    yield put(putProjectStatusError({ error: err }));
  }
}

export default takeEvery(putProjectStatus.type, putProjectStatusSaga);
