import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import MessageError from '../MessageError';
import Tooltip from '../Tooltip';
import { Container, Item } from './styles';

export default function TabSwitcher(props) {
  const {
    tabs,
    value,
    onChange,
    disabled,
    style,
    width,
    error,
    errorIncreasesHeight,
  } = props;

  const handleChange = useCallback(
    (index) => (event) => {
      if (event.keyCode) {
        if (event.keyCode === 32) {
          onChange(index);
        }
        return;
      }

      onChange(index);
    },
    [onChange],
  );

  return (
    <>
      <Container role="radiogroup" width={width}>
        {tabs.map((tab, index) => {
          const { tooltip, label } = tab;
          return (
            <Tooltip
              key={label}
              placement={(tooltip && tooltip.placement) || 'bottom'}
              overlay={tooltip && tooltip.overlay}
              prefixCls={tooltip !== undefined && 'information_tooltip'}
            >
              <Item
                key={label}
                active={value === index}
                isFirst={index === 0}
                isLast={index === tabs.length - 1}
                disabled={disabled || tab.disabled}
                tabIndex={0}
                onClick={handleChange(index)}
                onKeyDown={handleChange(index)}
                role="radio"
                aria-checked={value === index}
                style={style}
                data-testid={tab['data-testid'] || undefined}
              >
                {label}
              </Item>
            </Tooltip>
          );
        })}
      </Container>
      <MessageError
        isVisible={!!error}
        message={error}
        increasesHeight={errorIncreasesHeight}
      />
    </>
  );
}

TabSwitcher.propTypes = {
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      disabled: PropTypes.bool,
      'data-testid': PropTypes.string,
    }).isRequired,
  ),
  value: PropTypes.number,
  onChange: PropTypes.func,
  disabled: PropTypes.bool,
  style: PropTypes.objectOf(PropTypes.any),
  error: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
    PropTypes.instanceOf(Error),
  ]),
  errorIncreasesHeight: PropTypes.bool,
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

TabSwitcher.defaultProps = {
  tabs: [],
  value: -1,
  onChange: () => {},
  disabled: false,
  style: {},
  error: '',
  errorIncreasesHeight: false,
  width: null,
};
