// Checkbox here is NOT a react component!
/* eslint-disable react/prop-types */
import React, { useEffect, useContext } from 'react';
import { ThemeContext } from 'styled-components';
import { Label } from './styles';

function Checkbox(props, ref) {
  const { indeterminate, checked, onChange, 'data-testid': dataTestId } = props;

  const defaultRef = React.useRef();
  const resolvedRef = ref || defaultRef;

  useEffect(() => {
    resolvedRef.current.indeterminate = indeterminate;
  }, [resolvedRef, indeterminate]);

  const theme = useContext(ThemeContext);

  return (
    <Label>
      <>
        <input
          type="checkbox"
          ref={resolvedRef}
          checked={checked}
          onChange={onChange}
          data-testid={dataTestId}
        />
        <span className="overlay">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="13"
            height="9"
            viewBox="0 0 13 9"
            className="icon"
          >
            <path
              fill={theme.colors.white}
              fillRule="nonzero"
              d="M11.89.61a1 1 0 0 1 0 1.415L5.524 8.39a1 1 0 0 1-1.414 0l-.03-.032a.996.996 0 0 1-.374-.236L.88 5.293a1 1 0 1 1 1.414-1.414L4.75 6.335 10.475.611a1 1 0 0 1 1.414 0z"
            />
          </svg>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 20 20"
            className="indeterminateIcon"
          >
            <g fill="none" fillRule="evenodd">
              <rect
                width="2"
                height="11"
                x="9"
                y="4.5"
                fill={theme.colors.white}
                fillRule="nonzero"
                rx="1"
              />
            </g>
          </svg>
        </span>
      </>
    </Label>
  );
}

export default React.forwardRef(Checkbox);
