import React from 'react';
import PropTypes from 'prop-types';
import capitalize from 'lodash/capitalize';
import Cell from './styles';

export function capitalizeWordsOrDot(value = '') {
  const isValid = !!value;
  if (!isValid) {
    return '.';
  }

  if (value.length < 3) {
    return value;
  }

  return capitalize(`${value || ''}`.toLowerCase());
}

export default function CellWithDefaultDot(props) {
  const {
    cell: { value },
  } = props;

  return <Cell>{capitalizeWordsOrDot(value)}</Cell>;
}

CellWithDefaultDot.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.string,
  }).isRequired,
};
