import React from 'react';
import PropTypes from 'prop-types';
import ExpandedCell from './ExpandedCell';
import CheckFrame from '../../../../components/CheckFrame';
import { IP_CHECK } from '../../../../components/CheckFrame/helpers/checkFrameTypes';
import { Container, Text } from './styles';

export default function StatusCell({ row }) {
  const {
    isExpanded,
    original: { status = '' },
  } = row;

  return (
    <Container>
      <Text>{status}</Text>
      <ExpandedCell isExpanded={isExpanded}>
        <CheckFrame type={IP_CHECK} user={row.original} />
      </ExpandedCell>
    </Container>
  );
}

StatusCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({ status: PropTypes.string }).isRequired,
  }).isRequired,
};
