import React from 'react';
import PropTypes from 'prop-types';
import { NoEntriesContainer, NoEntriesText, NoEntriesWrapper } from './styles';
import MessageError from '../MessageError/MessageError';

export default function NoEntries(props) {
  const { label, error, margin, width } = props;

  return (
    <NoEntriesContainer
      data-testid="no-entries-component"
      margin={margin}
      width={width}
    >
      <NoEntriesWrapper hasError={!!error}>
        <NoEntriesText>{label || 'No Entries Yet'}</NoEntriesText>
      </NoEntriesWrapper>
      <MessageError isVisible={!!error} message={error} />
    </NoEntriesContainer>
  );
}

NoEntries.propTypes = {
  error: PropTypes.oneOfType([PropTypes.instanceOf(Error), PropTypes.bool]),
  label: PropTypes.string,
  margin: PropTypes.string,
  width: PropTypes.string,
};

NoEntries.defaultProps = {
  error: null,
  label: '',
  margin: '32px 0',
  width: 'auto',
};
