import React from 'react';
import PropTypes from 'prop-types';
import { Label, Section } from '../../../../styles';
import ActionFooter from '../../../ActionFooter';
import Select from '../../../Select';
import handleKeyDown from '../../../../pages/ProjectSetup/helpers/handleKeyDown';
import close from '../../../../assets/icons/close.svg';
import { StepActionContainer } from '../styles';
import {
  SurveyContainer,
  SurveyContentColumn,
  SurveyRemoveColumn,
  RemoveSurveyButton,
} from './styles';

export default function SurveyQuestionsStep(props) {
  const {
    errors,
    submitCount,
    collapsed,
    submitForm,
    card: { attributes },
    values: { surveys },
    handleRemoveSurvey,
    handleSurveyChange,
    surveysOptions,
    selectedSurveys,
    cancelEditing,
  } = props;

  if (collapsed && attributes && attributes.length) {
    return (
      <SurveyContainer>
        {selectedSurveys.map(({ label, key }) => (
          <div key={key}>{label}</div>
        ))}
      </SurveyContainer>
    );
  }

  return (
    <>
      {surveys.map((survey, index) => (
        <SurveyContainer key={survey.key}>
          <SurveyContentColumn>
            <Section>
              <Label htmlFor={`survey`}>Survey</Label>
              <Select
                name={`survey${index}`}
                id={`survey${index}`}
                options={surveysOptions}
                onChange={handleSurveyChange(index)}
                value={survey.survey}
                placeholder="Select existing survey"
                error={submitCount >= 1 && errors[`survey${index}`]}
              />
            </Section>
          </SurveyContentColumn>
          <SurveyRemoveColumn>
            {surveys.length > 1 && index !== 0 && (
              <RemoveSurveyButton
                role="button"
                onClick={() => handleRemoveSurvey(index)}
                onKeyDown={handleKeyDown(() => handleRemoveSurvey(index))}
                tabIndex={0}
              >
                <img src={close} alt="close icon" />
              </RemoveSurveyButton>
            )}
          </SurveyRemoveColumn>
        </SurveyContainer>
      ))}
      <StepActionContainer>
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !(attributes && attributes.length)
                ? 'Add & Close'
                : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

SurveyQuestionsStep.propTypes = {
  values: PropTypes.shape({
    surveys: PropTypes.arrayOf(
      PropTypes.shape({
        survey: PropTypes.number,
        key: PropTypes.string.isRequired,
      }),
    ),
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        attributeOrder: PropTypes.number,
      }),
    ),
    subSteps: PropTypes.arrayOf(
      PropTypes.shape({
        attributes: PropTypes.arrayOf(
          PropTypes.shape({
            value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
            attributeOrder: PropTypes.number,
          }),
        ),
      }),
    ),
  }).isRequired,
  handleRemoveSurvey: PropTypes.func.isRequired,
  handleSurveyChange: PropTypes.func.isRequired,
  surveysOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  selectedSurveys: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
