import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { Pill, Indicator, Label } from './styles';

export default function Switch(props) {
  const { name, value, onChange, disabled, 'data-testid': dataTestid } = props;

  const handleChange = useCallback(() => {
    onChange({
      target: {
        name,
        value: !value,
      },
    });
  }, [name, onChange, value]);

  return (
    <Label disabled={disabled} data-testid={dataTestid}>
      <input
        name={name}
        type="checkbox"
        checked={!!value}
        onChange={handleChange}
        disabled={disabled}
      />

      <Pill disabled={disabled}>
        <Indicator disabled={disabled} />
      </Pill>
    </Label>
  );
}

Switch.propTypes = {
  value: PropTypes.bool,
  onChange: PropTypes.func,
  name: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  'data-testid': PropTypes.string,
};

Switch.defaultProps = {
  value: false,
  disabled: false,
  onChange: () => {},
  'data-testid': '',
};
