import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import ActionFooter from '../../../../../components/ActionFooter';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../../components/ErrorPopover';
import RecruitmentForm from '../../../../../components/ProjectForms/RecruitmentForm/RecruitmentForm';
import { selectSaving, selectError } from '../../../../../store/ducks/project';
import { setDirty, setMode } from '../../../../../store/ducks/projectView';
import { CardContentContainer, CardFooter } from '../../../styles';

export default function RecruitmentProjectViewForm(props) {
  const {
    dirty,
    submitForm,
    submitCount,
    showError,
    errorPopoverProps,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setDirty(dirty));
  }, [dirty, dispatch]);

  const cancelEditing = useCallback(() => dispatch(setMode('view')), [
    dispatch,
  ]);

  const saving = useSelector(selectSaving);
  const error = useSelector(selectError);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  return (
    <>
      <CardContentContainer>
        <RecruitmentForm renderedIn="project-view" />
      </CardContentContainer>
      <CardFooter>
        <ErrorPopover {...errorPopoverProps} />
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              text: 'Cancel',
              type: 'secondary',
              disabled: saving,
              onClick: cancelEditing,
            },
            {
              text: 'Save Changes',
              type: 'primary',
              loading: saving,
              onClick: submitForm,
            },
          ]}
        />
      </CardFooter>
    </>
  );
}

RecruitmentProjectViewForm.propTypes = {
  dirty: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  submitForm: PropTypes.func.isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  projectId: PropTypes.number.isRequired,
};
