import { call, put, takeEvery, select } from 'redux-saga/effects';
import {
  patchProject,
  selectProject,
  projectMutationError,
  projectMutationSuccess,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* patchProjectSaga(action) {
  const {
    payload: { changes, thenPut, catchPut },
  } = action;

  const project = yield select(selectProject);

  if (!project) {
    yield put(
      projectMutationError(
        new Error("Can't update project without a project id."),
      ),
    );
    return;
  }

  try {
    let { id, name, alias } = project;

    if (changes.id) {
      id = changes.id;
      name = changes.name;
      alias = changes.alias;
    }

    const { data: nextProject } = yield call(
      axios.patch,
      endpoints.V2.PROJECT({
        pathVariables: {
          id,
        },
      }),
      { ...project, ...(changes || {}), id, name, alias },
    );

    yield put(
      projectMutationSuccess({
        project: nextProject,
      }),
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }
  } catch (err) {
    yield put(projectMutationError({ error: err }));

    if (typeof catchPut === 'object') {
      yield put(catchPut);
    }
  }
}

export default takeEvery(patchProject.type, patchProjectSaga);
