import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import ProjectOverview from './Steps/ProjectOverview';
import RegistrationAndQualification from './Steps/RegistrationAndQualification';
import ProjectTools from './Steps/ProjectTools';
import Recruitment from './Steps/Recruitment';
import InvoicePayment from './Steps/InvoicePayment';
import UserProjectPage from './Steps/UserProjectPage';
import SupportTeam from './Steps/SupportTeam';
import Preview from './Steps/Preview';
import NavigationProvider from './helpers/navigationContext';
import { selectCurrentStep } from '../../store/ducks/projectSetup';
import ProjectAccess from './Steps/ProjectAccess';
import STEP_NUMBER from '../../constants/stepNumber';
import { useClone } from './helpers/cloneContext';

export default function ActiveComponent() {
  const currentStep = useSelector(selectCurrentStep);
  const { isCloning, isCloneCreated } = useClone();

  const component = useMemo(() => {
    if (isCloning && !isCloneCreated) {
      return <ProjectOverview />;
    }

    switch (currentStep) {
      case STEP_NUMBER.PROJECT_OVERVIEW:
        return <ProjectOverview />;

      case STEP_NUMBER.REGISTRATION_AND_QUALIFICATION:
        return <RegistrationAndQualification />;

      case STEP_NUMBER.RECRUITMENT:
        return <Recruitment />;

      case STEP_NUMBER.INVOICE_PAYMENT:
        return <InvoicePayment />;

      case STEP_NUMBER.PROJECT_TOOLS:
        return <ProjectTools />;

      case STEP_NUMBER.PROJECT_ACCESS:
        return <ProjectAccess />;

      case STEP_NUMBER.USER_PROJECT_PAGE:
        return <UserProjectPage />;

      case STEP_NUMBER.SUPPORT_TEAM:
        return <SupportTeam />;

      case STEP_NUMBER.PREVIEW:
        return <Preview />;

      default:
        return <div>Here be dragons</div>;
    }
  }, [currentStep, isCloneCreated, isCloning]);

  return <NavigationProvider>{component}</NavigationProvider>;
}
