import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveFalsePositiveSuccess,
  saveFalsePositiveError,
  saveFalsePositive,
} from '.';
import { updateCurrentUserState } from '../userScreeningDetails';
import ENDPOINTS from '../../../constants/endpoints';
import { userSvcAPI } from '../../../services/axios';

function* saveFalsePositiveSagaFn(action) {
  const {
    payload: { falsePositive, thenPut },
  } = action;

  try {
    const endpoint = ENDPOINTS.USER_SCREENING.FALSE_POSITIVES({
      query: falsePositive,
    });

    const response = yield call(userSvcAPI.post, endpoint);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    yield put(saveFalsePositiveSuccess(response.data));
    yield put(updateCurrentUserState(response.data));
  } catch (error) {
    yield put(saveFalsePositiveError(error));
  }
}

export default takeLatest(saveFalsePositive.type, saveFalsePositiveSagaFn);
