import { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import {
  selectAll as selectAllHiringTargets,
  // fetchAll as fetchAllHiringTargets,
} from '../../../../store/ducks/hiringTargets';
import {
  findById,
  findByValue,
  findByNestedValue,
} from '../../../../helpers/find';

// eslint-disable-next-line import/prefer-default-export
export function buildInternalUserName(internalUser, idLength) {
  if (!internalUser) {
    return '';
  }

  const { firstName: id, lastName: name } = internalUser;

  if (!idLength || idLength > id.length) {
    return `${name} (${id})`;
  }

  const length = Math.floor(idLength / 2);

  return `${name} (${id.substr(0, length)}...${id.substr(id.length - length)})`;
}

export function useHiringTargetDependencies() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.localeLanguages));
    dispatch(fetch(ENTITIES.internalUsers));
  }, [dispatch]);

  const countries = useSelector(
    selectors.createSelectDataFor(ENTITIES.countries),
  );

  const localeLanguages = useSelector(
    selectors.createSelectDataFor(ENTITIES.localeLanguages),
  );

  const internalUsers = useSelector(
    selectors.createSelectDataFor(ENTITIES.internalUsers),
  );

  return {
    countries,
    localeLanguages,
    internalUsers,
  };
}

export function useRawHiringTargets(projectId) {
  const dispatch = useDispatch();

  useEffect(() => {
    if (projectId) {
      // dispatch(fetchAllHiringTargets({ projectId }));
    }
  }, [dispatch, projectId]);

  const hiringTargets = useSelector(selectAllHiringTargets);

  return hiringTargets;
}

export function useHiringTargets(projectId) {
  // Get Hiring Target dependencies
  const {
    countries,
    internalUsers,
    localeLanguages,
  } = useHiringTargetDependencies();

  // Get Hiring Targets
  const rawHiringTargets = useRawHiringTargets(projectId);

  // Resolve ids for Hiring Targets properties
  const hiringTargets = useMemo(
    () =>
      (Array.isArray(rawHiringTargets) ? rawHiringTargets : []).map(
        (rawHiringTarget) => {
          const assignedOwner = findById(
            internalUsers,
            rawHiringTarget.assignedOwner,
          );

          const localeLanguage = findByNestedValue(
            localeLanguages,
            rawHiringTarget.localeLanguage,
            'options',
          );

          const localeDialect = findByValue(
            countries,
            rawHiringTarget.localeDialect,
          );

          const localeLanguageTo = findByNestedValue(
            localeLanguages,
            rawHiringTarget.localeLanguageTo,
            'options',
          );

          const localeDialectTo = findByValue(
            countries,
            rawHiringTarget.localeDialectTo,
          );

          const restrictToCountry = findByValue(
            countries,
            rawHiringTarget.restrictToCountry,
          );

          return {
            id: rawHiringTarget.id,
            hiringDeadline: rawHiringTarget.hiringDeadline,
            target: rawHiringTarget.target,

            localeLanguage: get(localeLanguage, 'label', '.'),
            localeDialect: get(localeDialect, 'label', '.'),
            localeLanguageTo: get(localeLanguageTo, 'label', '.'),
            localeDialectTo: get(localeDialectTo, 'label', '.'),
            restrictToCountry: get(restrictToCountry, 'label', '.'),
            assignedOwner: buildInternalUserName(assignedOwner || '', 8),
          };
        },
      ),
    [countries, internalUsers, localeLanguages, rawHiringTargets],
  );

  return hiringTargets;
}
