import { put, call, takeEvery } from 'redux-saga/effects';
import { setModulesError, loadQuestionById, setQuestions } from '.';
import { profileBuilderAPI } from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* fetchQuestionsSaga(action) {
  const { stepDefinitionId } = action.payload;

  const endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS({
    pathVariables: {
      id: stepDefinitionId,
    },
  });

  try {
    const response = yield call(profileBuilderAPI.get, endpoint);

    yield put(setQuestions(response.data));
  } catch (error) {
    yield put(setModulesError(error));
  }
}

export default takeEvery(loadQuestionById.type, fetchQuestionsSaga);
