import { useFormikContext } from 'formik';
import { useCallback } from 'react';
import shortid from 'shortid';

export default function useIPAddressMultilineInput() {
  const { values, setFieldValue } = useFormikContext();

  const { ipAddresses = [] } = values;

  const handleAddLine = useCallback(() => {
    setFieldValue('ipAddresses', [
      ...ipAddresses,
      { ipAddress: '', key: shortid.generate() },
    ]);

    setTimeout(() => {
      // We wait two frames to make sure react time to update the dom
      const inputs = Array.from(document.querySelectorAll('[name*=ipAddress]'));
      inputs[inputs.length - 1].focus();
    }, 32);
  }, [ipAddresses, setFieldValue]);

  const handleRemove = useCallback(
    (index) => {
      setFieldValue('ipAddresses', [
        ...ipAddresses.slice(0, index),
        ...ipAddresses.slice(index + 1, ipAddresses.length),
      ]);
    },
    [ipAddresses, setFieldValue],
  );

  const handleLineChange = useCallback(
    (index) => (event) => {
      const {
        target: { value },
      } = event;

      const item = ipAddresses[index];

      setFieldValue('ipAddresses', [
        ...ipAddresses.slice(0, index),
        { ...item, ipAddress: value },
        ...ipAddresses.slice(index + 1, ipAddresses.length),
      ]);
    },
    [ipAddresses, setFieldValue],
  );

  const handleToggleSecureIps = useCallback(
    (event) => {
      const {
        target: { value },
      } = event;

      if (!values.ipAddresses) {
        setFieldValue('ipAddresses', [
          { ipAddress: '', key: shortid.generate() },
        ]);
      }

      setFieldValue('hasIPRestrictions', value);
    },
    [setFieldValue, values.ipAddresses],
  );

  const handleKeyDown = useCallback(
    (event) => {
      const { keyCode, target } = event;
      const { value, name } = target;

      if (keyCode === 13) {
        event.preventDefault();

        if (value.length) {
          handleAddLine();

          const { form } = event.target;
          const index = Array.from(form).indexOf(event.target);

          setTimeout(() => {
            form.elements[index + 2].focus();
          });
        }
      }

      if (keyCode === 8 && !value.length) {
        const [, lineIndex] = name.match(/ipAddress(\d+)/);

        if (lineIndex > 0) {
          event.preventDefault();
          handleRemove(lineIndex);

          const { form } = event.target;
          const index = Array.from(form).indexOf(event.target);

          setTimeout(() => {
            form.elements[index - 2].focus();
          });
        }
      }
    },
    [handleAddLine, handleRemove],
  );

  return {
    handleAddLine,
    handleRemove,
    handleLineChange,
    handleToggleSecureIps,
    handleKeyDown,
  };
}
