import { useFormikContext } from 'formik';
import { useCallback } from 'react';
import { convertToRaw, ContentState, EditorState } from 'draft-js';
import htmlToDraft from 'html-to-draftjs';
import draftToHtml from 'draftjs-to-html';
import RichTextEditor from './RichTextEditor';

export default RichTextEditor;

export function useEditorChangeHandler(fieldName) {
  const { setFieldValue } = useFormikContext();

  const handleChange = useCallback(
    (rawDraftContentState) => {
      setFieldValue(fieldName, rawDraftContentState);
    },
    [setFieldValue, fieldName],
  );

  return handleChange;
}

export function draftToHTML(draftContentState) {
  if (!draftContentState || !draftContentState.getCurrentContent) {
    return '';
  }

  return draftToHtml(convertToRaw(draftContentState.getCurrentContent()));
}

export function HTMLToDraft(content) {
  if (!content || typeof content !== 'string') {
    return ContentState.createFromText('<p></p>');
  }

  const blocksFromHtml = htmlToDraft(content);

  const { contentBlocks, entityMap } = blocksFromHtml;

  return EditorState.createWithContent(
    ContentState.createFromBlockArray(contentBlocks, entityMap),
  );
}
