import React, { useCallback, useMemo, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import { useFormikContext } from 'formik';
import { useFeatures } from 'flagged';
import BlockingRules from './BlockingRules';
import LinkedProjects from './LinkedProjects';
import LocaleTenants from './LocaleTenants';
import HiringTargets from './HiringTargets';
import Demographics from './Demographics';
import AutoScreening from './AutoScreening';
import TargetScreening from './TargetScreening';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip';
import {
  selectProject,
  selectRegistrationAndQualification,
} from '../../../store/ducks/project';
import * as registrationProcesses from '../../../store/ducks/registrationProcesses';
import { Section, Label } from '../../../styles';
import {
  PopoverContainer,
  PopoverInfo,
  ErrorTitle,
  Wrapper,
} from './TargetScreening/styles';
import { CheckGroup, CheckAltMessage } from '../ProjectToolsForm/styles';
import Checkbox from '../../Checkbox';
import Textarea from '../../Textarea';
import {
  hasAnyDemographicData,
  removeRequirements,
} from '../../../store/ducks/projectDemographicRequirements';
import LegacyIntelligentAttributes from '../../LegacyIntelligentAttributes';
import { useClone } from '../../../pages/ProjectSetup/helpers/cloneContext';
import { CheckboxWrapper, PopoverWarning } from './styles';

export default function RecruitmentForm(props) {
  const { renderedIn } = props;

  const {
    values,
    handleChange,
    submitCount,
    errors,
    setFieldValue,
  } = useFormikContext();

  const { blockingRulesInconsistencies } = useClone();

  const dispatch = useDispatch();
  const { targetScreening, autoScreening } = useFeatures();

  const registrationAndQualification = useSelector(
    selectRegistrationAndQualification,
  );

  const project = useSelector(selectProject);
  const registrationSteps = useSelector(registrationProcesses.selectSteps);

  const { registrationProcessTemplate } = registrationAndQualification;

  useEffect(() => {
    dispatch(registrationProcesses.fetchOptions());
    if (registrationProcessTemplate) {
      dispatch(registrationProcesses.fetchSteps(registrationProcessTemplate));
    }
  }, [dispatch, registrationProcessTemplate]);

  let hasCompleteAdditionalAttributes = false;

  if (registrationSteps.steps.length) {
    const steps = registrationSteps.steps.map(
      (step) =>
        step.stepDefinition.sequenceFlow === 'completeProjectAttributesFlow',
    );
    hasCompleteAdditionalAttributes = steps.includes(true);
  }

  const isTranslationProject = useMemo(
    () => get(project, 'workType', '') === 'TRANSLATION',
    [project],
  );

  const projectId = useMemo(() => get(project, 'id', null), [project]);

  const hasDemographics = useSelector(hasAnyDemographicData);

  const handleDemographicsCheckbox = useCallback(
    (event) => {
      if (!event.target.value) {
        if (hasDemographics) {
          if (
            // FYI: This is a temporary solution agreed upon with the design team!
            // TODO: Replace this will a proper confirmation modal
            // eslint-disable-next-line no-restricted-globals, no-alert
            confirm(
              'This will delete all demographic requirement settings created so far. Do you want to proceed?',
            )
          ) {
            dispatch(removeRequirements());
            handleChange(event);
          }

          return;
        }
      }

      handleChange(event);
    },
    [dispatch, handleChange, hasDemographics],
  );

  return (
    <>
      <LocaleTenants />

      <HiringTargets
        projectId={projectId}
        isTranslationProject={isTranslationProject}
      />

      {autoScreening && (
        <>
          <Section marginBottom={30}>
            <CheckGroup>
              <Checkbox
                name="hasAutoScreening"
                onChange={handleChange}
                checked={values.hasAutoScreening}
                label="Auto-screening Configuration"
                data-testid="project-setup--recruitment--auto-screening-toggle"
              />
              <CheckAltMessage>
                Auto-screening configuration entries at the project level by
                locale.
                <br />
                Only required if the project has a registration/qualification
                process defined for it.
              </CheckAltMessage>
            </CheckGroup>
          </Section>
          {values.hasAutoScreening && (
            <Section marginBottom={48} marginLeft={34}>
              <AutoScreening mode="edit" />
            </Section>
          )}
        </>
      )}

      {targetScreening && (
        <>
          <Section marginBottom={30}>
            <CheckGroup>
              <Checkbox
                name="hasTargetScreening"
                onChange={handleChange}
                checked={values.hasTargetScreening}
                label="Target Screening and Data Requirements"
                data-testid="project-setup--recruitment--target-screening-toggle"
              />
              <CheckAltMessage>
                Configure the target audience required for the project using
                Global Profile data.
              </CheckAltMessage>
            </CheckGroup>
          </Section>
          {values.hasTargetScreening ? (
            <Section marginBottom={48} marginLeft={34} maxWidth={741}>
              <TargetScreening />
            </Section>
          ) : (
            hasCompleteAdditionalAttributes && (
              <PopoverContainer>
                <Wrapper maxWidth={640} padding={'0 0 30px 33px'}>
                  <PopoverInfo>
                    <ErrorTitle>Important</ErrorTitle>
                    There is a &quot;Complete Additional Attributes&quot; module
                    added to this project&apos;s registration process. If you
                    are collecting profile data from workers, target groups and
                    requirements will need to be configured.
                  </PopoverInfo>
                </Wrapper>
              </PopoverContainer>
            )
          )}
        </>
      )}

      <Section marginBottom="30px">
        <CheckGroup>
          <Checkbox
            name="showDemographics"
            onChange={handleDemographicsCheckbox}
            checked={values.showDemographics}
            label="Add Custom Demographic Requirements"
            data-testid="project-setup--recruitment--custom-demographics-toggle"
          />
          <CheckAltMessage>
            Recruiting team will use the parameters set as basis for
            recruitment.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      {values.showDemographics && (
        <Section marginBottom="48px" marginLeft="34px">
          <Demographics setFieldValue={setFieldValue} />
        </Section>
      )}
      <Section marginBottom="30px">
        <CheckGroup>
          <CheckboxWrapper>
            <Checkbox
              name="hasBlockingRules"
              onChange={handleChange}
              checked={values.hasBlockingRules}
              label="Blocking Rules"
              data-testid="project-setup--recruitment--blocking-rules--toggle"
            />
            <div style={{ marginLeft: -28, marginTop: 7 }}>
              <InformationTooltip
                title={
                  <>
                    If marked as permanent, any raters working on this project
                    will not be able to work on the blocked projects even if
                    they are revoked from this project.
                    <br />
                    <br />
                    You can only add blocking rules to enabled projects.
                  </>
                }
              />
            </div>
          </CheckboxWrapper>
          <CheckAltMessage>
            Blocking rules prevent raters from working on certain other projects
            <br />
            while they are working on this project.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      {values.hasBlockingRules && blockingRulesInconsistencies && (
        <PopoverContainer>
          <Wrapper maxWidth={640} padding={'0 0 33px 33px'}>
            <PopoverWarning>
              <ErrorTitle>Attention</ErrorTitle>
              Some of the projects listed on the blocking rules are currently
              disabled and could not be cloned.
            </PopoverWarning>
          </Wrapper>
        </PopoverContainer>
      )}

      {values.hasBlockingRules && (
        <Section marginBottom="48px" marginLeft="34px">
          <BlockingRules />
        </Section>
      )}

      {/* <Disabled
        when
        message="Currently adding Intelligent Attributes is not supported in the new Project Setup Experience"
      >
        <Section marginBottom="30px">
          <CheckGroup>
            <Checkbox
              name="hasIntelligentAttributes"
              onChange={handleChange}
              checked={values.hasIntelligentAttributes}
              label="Intelligent Attributes"
            />
            <CheckAltMessage>
              Configurable attributes used for profile data collection and auto-
              <br />
              screening at project qualification level.
            </CheckAltMessage>
          </CheckGroup>
        </Section>

        {values.hasIntelligentAttributes && (
          <Section marginBottom="48px" marginLeft="34px">
            <IntelligentAttributes />
            <Button disabled>Add Intelligent Attribute</Button>
          </Section>
        )}
      </Disabled> */}

      <Section marginBottom="30px">
        <CheckGroup>
          <Checkbox
            name="hasLinkedProjects"
            onChange={handleChange}
            checked={values.hasLinkedProjects}
            label="Linked Projects"
            data-testid="project-setup--recruitment--linked-projects--toggle"
          />
          <CheckAltMessage>
            {
              "Recruiting team will use user's qualifications on previous projects to"
            }
            <br />
            determine eligibility for new opportunities.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      {values.hasLinkedProjects && (
        <Section marginLeft="34px">
          <LinkedProjects />
        </Section>
      )}

      <Section marginBottom="12px">
        <CheckGroup>
          <Checkbox
            name="hasNotesToRecruiting"
            onChange={handleChange}
            checked={values.hasNotesToRecruiting}
            label="Add Notes to Recruiting Team"
            data-testid="project-setup--recruitment--notes-to-recruiting--toggle"
          />
        </CheckGroup>
      </Section>

      {values.hasNotesToRecruiting && (
        <Section marginBottom="12px" marginTop="30px" marginLeft="34px">
          <Label>Recruiting Notes</Label>
          <Textarea
            onChange={handleChange}
            name="notesToRecruiting"
            placeholder="Write notes to recruiting team"
            data-testid="project-setup--recruitment--notes-to-recruiting"
            rows={6}
            error={submitCount >= 1 && errors.notesToRecruiting}
            value={values.notesToRecruiting}
          />
        </Section>
      )}

      <Section
        marginBottom="12px"
        marginTop="30px"
        style={{ overflowX: renderedIn === 'project-view' ? 'none' : 'auto' }}
      >
        <LegacyIntelligentAttributes projectId={projectId} />
      </Section>
    </>
  );
}

RecruitmentForm.propTypes = {
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

RecruitmentForm.defaultProps = {
  renderedIn: 'project-setup',
};
