import * as yup from 'yup';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

export const WORKDAY_TASK_ID_RULE = /^(P|LR|CR)(\d{5})$/;

export default yup
  .object()
  .strict()
  .shape({
    notesToFinance: yup.string().when('hasNotesToFinance', {
      is: true,
      then: yup
        .string()
        .required(requiredMessage`When "Add Notes to Finance" is checked this`),
    }),

    workdayTaskId: yup
      .string()
      .required(requiredMessage`Workday Task Id`)
      .matches(WORKDAY_TASK_ID_RULE, 'Incorrect format type'),

    hasProductivityData: yup.boolean(),

    hasNotesToFinance: yup.boolean(),

    rateType: yup.string().required(requiredMessage`Rate Type`),
    businessUnit: yup
      .string()
      .required(requiredMessage`Project Business Unit`)
      .nullable(),
  });
