import { all, call, put, select, takeEvery } from 'redux-saga/effects';
import { cloneProject, projectMutationError, projectMutationSuccess } from '.';
import { selectAccessHolder } from '../experimentGroups';
import { fetchAccessHolderSagaFn } from '../experimentGroups/fetchAccessHolderSaga';
import cloneBasicProject, {
  storeTenants,
  storeHiringTargets,
  cloneProjectFeatures,
  cloneProjectDependencies,
} from '../../../pages/ProjectSetup/Steps/ProjectOverview/helpers/cloneProject';
import {
  getOrInitializeRequirementsByGroup,
  getOrInitializeAggregatedRequirements,
} from './helpers';
import { fetchHiringTargetsFn } from '../hiringTargets/fetchHiringTargetsSaga';
import {
  projectPagesSaga,
  selectAll as selectProjectPages,
} from '../projectPages/index';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* cloneProjectSaga(action) {
  const {
    payload: { project, thenPut, catchPut, props },
  } = action;

  if (!project) {
    yield put(
      projectMutationError(
        new Error(
          "Can't create project without at least project name and description.",
        ),
      ),
    );
    return;
  }

  try {
    const { data: nextProject } = yield call(
      axios.post,
      endpoints.V2.PROJECT(),
      project,
    );

    // Clones basic project structure
    yield call(cloneBasicProject, {
      ...props,
      id: nextProject.id,
      name: nextProject.name,
      alias: nextProject.alias,
    });

    // Clones tenants
    yield all(storeTenants({ ...props, projectId: nextProject.id }));

    // Clones hiring targets
    yield all(storeHiringTargets({ ...props, projectId: nextProject.id }));

    // Update hiring target state
    yield call(fetchHiringTargetsFn, {
      payload: { projectId: nextProject.id },
    });

    // Gets access holders from the source project
    const originalAccessHolders = yield select(selectAccessHolder);

    // Update access holders state
    yield call(fetchAccessHolderSagaFn, {
      payload: {
        projectId: nextProject.id,
        hasExternalSystem: project.hasExternalSystem,
      },
    });

    // Gets the access holders from the cloned project
    const newAccessHolders = yield select(selectAccessHolder);

    // Clones the remaining project features
    yield call(cloneProjectFeatures, {
      ...props,
      originalAccessHolders,
      newAccessHolders,
      projectId: nextProject.id,
    });

    // Get new requirements by group with new hiring targets
    const requirementsByGroup = yield select(
      getOrInitializeRequirementsByGroup,
      { ...props },
    );

    // Get new aggregated requirements with new hiring targets
    const aggregatedRequirements = yield select(
      getOrInitializeAggregatedRequirements,
      { ...props },
    );

    // Update default project page stage
    yield call(projectPagesSaga, {});
    const newProjectPages = yield select(selectProjectPages);

    // Clones the project dependencies
    yield call(cloneProjectDependencies, {
      ...props,
      requirementsByGroup,
      aggregatedRequirements,
      newProjectPages,
      projectId: nextProject.id,
    });

    yield put(
      projectMutationSuccess({
        project: nextProject,
      }),
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }
  } catch (err) {
    yield put(projectMutationError(err));

    if (typeof catchPut === 'object') {
      yield put(catchPut);
    }
  }
}

export default takeEvery(cloneProject.type, cloneProjectSaga);
