import React from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import Button from '../../../Button';
import NoEntries from '../../../NoEntries';
import Table from '../../../Table';
import ConfigurationModal from './ConfigurationModal';
import LocaleCell from './Cells/LocaleCell';
import SettingsCell from './Cells/SettingsCell';
import {
  ButtonContainer,
  ErrorTitle,
  PopoverContainer,
  PopoverWarning,
  TableContainer,
  Wrapper,
} from './styles';

export default function AutoScreening(props) {
  const {
    mode,
    header,
    autoScreenings,
    loadingConfigs,
    loadingLocales,
    modalProps,
    modalControls,
    hasCompleteAdditionalAttributes,
    handleAddConfiguration,
    handleEditConfiguration,
    handleDeleteConfiguration,
  } = props;

  const columns = [
    {
      Header: 'Locale',
      accessor: 'locale',
      key: 'locale',
      width: 60,
      Cell: LocaleCell,
    },
    {
      Header: 'Max users per day',
      accessor: 'maxUsersPerDay',
      key: 'maxUsersPerDay',
      width: 18,
    },
    {
      Header: 'Max active users',
      accessor: 'maxActiveUsers',
      key: 'maxActiveUsers',
      width: 18,
    },
  ];

  if (mode === 'edit') {
    columns.push({
      Header: '',
      accessor: 'id',
      key: 'settings',
      maxWidth: 4,
      disableSortBy: true,
      Cell: SettingsCell,
    });
  }

  if (loadingConfigs || loadingLocales) {
    return <Shimmer width={741} height={78} />;
  }

  return (
    <>
      {header || null}
      {autoScreenings.length ? (
        <TableContainer>
          <Table
            columns={columns}
            data={autoScreenings}
            initialState={{
              handleEditConfiguration,
              handleDeleteConfiguration,
            }}
          />
        </TableContainer>
      ) : (
        <NoEntries label="Not set" margin="32px 0 8px 0" width="741px" />
      )}
      {mode === 'edit' && (
        <>
          {!hasCompleteAdditionalAttributes && (
            <PopoverContainer>
              <Wrapper>
                <PopoverWarning>
                  <ErrorTitle>Attention</ErrorTitle>
                  You need to to add the Complete Additional Attributes module
                  into your registration process in order to collect profile
                  data from workers.
                </PopoverWarning>
              </Wrapper>
            </PopoverContainer>
          )}
          <ButtonContainer>
            <Button onClick={handleAddConfiguration}>Add Configuration</Button>
          </ButtonContainer>
          <ConfigurationModal
            modalProps={modalProps}
            modalControls={modalControls}
          />
        </>
      )}
    </>
  );
}

AutoScreening.propTypes = {
  mode: PropTypes.string,
  header: PropTypes.element,
  autoScreenings: PropTypes.arrayOf(
    PropTypes.shape({
      locale: PropTypes.string,
      maxUsersPerDay: PropTypes.number,
      maxActiveUsers: PropTypes.number,
    }),
  ).isRequired,
  loadingConfigs: PropTypes.bool.isRequired,
  loadingLocales: PropTypes.bool.isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  hasCompleteAdditionalAttributes: PropTypes.bool.isRequired,
  handleAddConfiguration: PropTypes.func.isRequired,
  handleEditConfiguration: PropTypes.func.isRequired,
  handleDeleteConfiguration: PropTypes.func.isRequired,
};

AutoScreening.defaultProps = {
  mode: 'view',
  header: null,
};
