import React from 'react';
import PropTypes from 'prop-types';
import capitalize from 'lodash/capitalize';
import { Tag } from '../../../Projects/Cells/StatusCell';

export default function StatusCell(props) {
  const {
    cell: { value },
  } = props;

  switch (value) {
    case 'PENDING':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    case 'ACCEPTED':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    case 'EXPIRED':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    case 'DENIED':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    case 'FAILED':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    case 'INVALID':
      return (
        <Tag currentStatus={`${value}`.toUpperCase()}>{capitalize(value)}</Tag>
      );

    default:
      return <div />;
  }
}

StatusCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.string.isRequired,
  }).isRequired,
};
