/* eslint-disable no-param-reassign */

import {
  call,
  put,
  select,
  takeLatest,
  all,
  debounce,
} from 'redux-saga/effects';
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import memoize from 'lodash/memoize';
import axios from '../../../services/axios';

function getCacheKey(stateKey) {
  return `${stateKey}`;
}

// type EntityConfig<T> = {
//   entityPK: (entity: T) => string | string;
//   buildURL: (state: any, action: any) => string;
//   transform?: (entity: T): any;
//   lazyFetch?: boolean // undefined and false means regular (fetch everytime its called)
// }

export default function createEntitiesSlice(
  keys /*: { [key: string]: EntityConfig } */,
) {
  const reducerKey = 'paginated';
  const initialState = {};

  Object.keys(keys).forEach((key) => {
    initialState[key] = {
      content: {},
      filters: keys[key].initialFilters || {},
      currentPage: 1,
      totalCount: 1,
      pageSize: 20,
      error: null,
      loading: false,
    };
  });

  // Selecting
  function selectSliceFor(stateKey, state) {
    const path = [reducerKey, stateKey];

    return get(state, path, {});
  }

  function selectDataFor(stateKey, state) {
    const slice = selectSliceFor(stateKey, state);
    return get(slice, `content[${slice.currentPage || 1}]`, []);
  }

  function selectLoadingFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'loading', false);
  }

  function selectErrorFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'error', null);
  }

  function selectCurrentPageFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'currentPage', null);
  }

  function selectTotalCountFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'totalCount', null);
  }

  function selectPageSizeFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'pageSize', null);
  }

  function selectPaginationInfoFor(stateKey, state) {
    return {
      currentPage: selectCurrentPageFor(stateKey, state),
      totalCount: selectTotalCountFor(stateKey, state),
      pageSize: selectPageSizeFor(stateKey, state),
    };
  }

  function selectFiltersFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'filters', null);
  }

  const createSelectSliceFor = memoize(
    (stateKey) => (state) => selectSliceFor(stateKey, state),
    getCacheKey,
  );

  const createSelectDataFor = memoize(
    (stateKey) => (state) => selectDataFor(stateKey, state),
    getCacheKey,
  );

  const createSelectLoadingFor = memoize((stateKey) => (state) =>
    selectLoadingFor(stateKey, state),
  );

  const createSelectErrorFor = memoize((stateKey) => (state) =>
    selectErrorFor(stateKey, state),
  );

  const createSelectCurrentPageFor = memoize((stateKey) => (state) =>
    selectCurrentPageFor(stateKey, state),
  );

  const createSelectTotalCountFor = memoize((stateKey) => (state) =>
    selectTotalCountFor(stateKey, state),
  );

  const createSelectPageSizeFor = memoize((stateKey) => (state) =>
    selectPageSizeFor(stateKey, state),
  );

  const createSelectPaginationInfoFor = memoize((stateKey) => (state) =>
    selectPaginationInfoFor(stateKey, state),
  );

  const createSelectFiltersFor = memoize((stateKey) => (state) =>
    selectFiltersFor(stateKey, state),
  );

  // Helpers
  function getEntityConfig(key) {
    return keys[key] || {};
  }

  function hasData(key, state) {
    const possibleData = selectDataFor(key, state);

    return !!possibleData.length;
  }

  // Managment
  const slice = createSlice({
    name: reducerKey,
    initialState,
    reducers: {
      /**
       * action.payload is a stateKey (string)
       * Usage: fetch('projects');
       */
      fetch(state, action) {
        const forKey = action.payload;
        const config = getEntityConfig(forKey);

        if (
          config.fetchMode !== 'lazy' ||
          (config.fetchMode === 'lazy' && !hasData(forKey, state))
        ) {
          state[forKey].loading = true;
        }
      },

      /**
       * // Action here is: {
       *  payload: {
       *    forKey: string;
       *    nextPage?: number
       *  }
       * }
       */
      nextPage(state, action) {
        const { forKey } = action.payload;

        state[forKey].currentPage =
          typeof action.payload.nextPage === 'number'
            ? action.payload.nextPage
            : state[forKey].currentPage + 1;

        state[forKey].loading = true;
      },

      /**
       * // Action here is: {
       *  payload: {
       *    forKey: string;
       *    prevPage?: number
       *  }
       * }
       */
      prevPage(state, action) {
        const { forKey } = action.payload;

        state[forKey].currentPage =
          typeof action.payload.prevPage === 'number'
            ? action.payload.prevPage
            : state[forKey].currentPage - 1;

        state[forKey].loading = true;
      },

      /**
       * // Action here is: {
       *  payload: {
       *    filters: { [key: string]: any };
       *    forKey: string;
       *  }
       * }
       */
      setFilters(state, action) {
        const { forKey, filters } = action.payload;
        const { loading = true } = action.payload;

        if (isEmpty(filters)) {
          state[forKey].filters = {};
        } else {
          state[forKey].filters = {
            ...state[forKey].filters,
            ...filters,
          };
        }

        state[forKey].loading = loading;
        state[forKey].currentPage = 1;
      },

      /**
       * // Action here is: {
       *  payload: {
       *    pageSize: number
       *    forKey: string;
       *  }
       * }
       */
      setPageSize(state, action) {
        const { forKey, pageSize } = action.payload;

        state[forKey].pageSize = pageSize;
        state[forKey].currentPage = 1;
      },

      /**
       * // Action here is: {
       *  payload: string;
       * }
       */
      resetFor(state, action) {
        const forKey = action.payload;

        state[forKey] = initialState[forKey];
      },

      /**
       * // Action here is: {
       *  payload: {
       *    data: any;
       *    forKey: string;
       *  }
       * }
       */
      fetchSucceded(state, action) {
        const key = action.payload.forKey;
        const config = getEntityConfig(key);

        const data =
          typeof config.transform === 'function'
            ? config.transform(action.payload.data)
            : action.payload.data;

        state[key].content[state[key].currentPage] = data || [];
        state[key].loading = false;
        state[key].error = null;
        state[key].totalCount = action.payload.totalCount;
      },

      /**
       * // Action here is: {
       *  payload: {
       *    error: Error;
       *    forKey: string;
       *  }
       * }
       */
      fetchFailed(state, action) {
        state[action.payload.forKey].loading = false;
        state[action.payload.forKey].error = action.payload.error;
      },
    },
  });

  slice.reducer.key = reducerKey;

  if (process.env.NODE_ENV === 'development' && window.Cypress) {
    window.paginatedActions = {
      ...slice.actions,
    };
  }

  // Fetching
  function* genericFetchSaga(action) {
    const state = yield select();

    const forKey =
      action.type === slice.actions.fetch.type
        ? action.payload
        : action.payload.forKey;

    const config = getEntityConfig(forKey);

    // prevent fetch if in lazy mode and already have data available
    if (
      action.type === slice.actions.fetch.type &&
      config.lazyFetch &&
      hasData(forKey, state)
    ) {
      return;
    }

    try {
      const url = config.buildURL(selectSliceFor(forKey, state), action, state);

      if (url) {
        const connection = config.connection ? config.connection : axios.get;
        const response = yield call(connection, url);
        const totalElements = get(response, ['data', 'totalElements'], null);
        const xTotalCount = get(response, ['headers', 'x-total-count'], null);

        const totalCount = parseInt(totalElements || xTotalCount || 1, 10);

        yield put(
          slice.actions.fetchSucceded({
            forKey,
            data: response.data,
            totalCount,
          }),
        );
      }
    } catch (err) {
      yield put(
        slice.actions.fetchFailed({
          forKey,
          error: err,
        }),
      );
    }
  }

  return {
    reducer: slice.reducer,
    fetchPaginated: slice.actions.fetch,
    nextPage: slice.actions.nextPage,
    prevPage: slice.actions.prevPage,
    setFilters: slice.actions.setFilters,
    setPageSize: slice.actions.setPageSize,
    resetFor: slice.actions.resetFor,
    saga: all([
      takeLatest(
        [
          slice.actions.fetch.type,
          slice.actions.prevPage.type,
          slice.actions.nextPage.type,
          slice.actions.setPageSize.type,
        ],
        genericFetchSaga,
      ),
      debounce(250, slice.actions.setFilters.type, genericFetchSaga),
    ]),
    selectors: {
      createSelectSliceFor,
      createSelectDataFor,
      createSelectLoadingFor,
      createSelectErrorFor,
      createSelectCurrentPageFor,
      createSelectTotalCountFor,
      createSelectPageSizeFor,
      createSelectPaginationInfoFor,
      createSelectFiltersFor,
      selectSliceFor,
      selectDataFor,
      selectLoadingFor,
      selectErrorFor,
      selectCurrentPageFor,
      selectTotalCountFor,
      selectFiltersFor,
      selectPageSizeFor,
      selectPaginationInfoFor,
    },
  };
}
