import createEntityStateProvider from '../../helpers/createEntityStateProvider';
import ENDPOINTS from '../../../constants/endpoints';

function getUrl() {
  return ENDPOINTS.V2.TEAM_TYPES();
}

const [
  {
    reducer,
    actions: {
      fetchAll,
      fetchFailed,
      setAll,
      addOne,
      addMany,
      removeOne,
      removeMany,
      removeAll,
      updateOne,
      updateMany,
      upsertOne,
      upsertMany,
    },
  },
  {
    selectIds,
    selectEntities,
    selectAll,
    selectTotal,
    selectById,
    selectLoading,
    selectError,
  },
  teamTypesSaga,
] = createEntityStateProvider('teamTypes', getUrl, null, null, true);

export default reducer;

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
  selectLoading,
  selectError,
  teamTypesSaga,
};

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.teamTypes = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
