/* eslint-disable no-param-reassign */
import { createSelector, createAction } from '@reduxjs/toolkit';
import { hasHiringTargets } from '../hiringTargets';
import { hasLocaleTenants } from '../localeTenants';
import { hasPayRates } from '../payrates';
import { projectMutationSuccess, selectProjectState } from '../project';
import {
  saveExperimentGroupsSuccess,
  saveAccessHolderError,
} from '../experimentGroups';
import {
  selectCompletedPercentageFromProject,
  selectRegistrationAndQualificationFromProject,
  selectRecruitmentFromProject,
  selectSupportTeamFromProject,
  selectUserProjectPageFromProject,
} from '../project/selectors';
import { hasAnyDemographicData } from '../projectDemographicRequirements';

const initialState = {
  // Navigation
  currentTab: 0,

  // Card
  mode: 'view',
  dirty: false,
  disabled: false,

  validateInvoiceConfig: false,
};

export const navigate = createAction('navigate');
export const setMode = createAction('setMode');
export const setModeDisabled = createAction('setModeDisabled');
export const setDirty = createAction('setDirty');
export const showInvoiceConfigError = createAction('showInvoiceConfigError');

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case navigate.type:
      return (() => {
        const { payload: tab = 0 } = action;

        if (
          state.dirty &&
          // eslint-disable-next-line no-alert
          !window.confirm('Changes you made may not be saved.')
        ) {
          return state;
        }

        if (typeof tab !== 'number') {
          return state;
        }

        return {
          dirty: false,
          currentTab: tab,
          mode: 'view',
        };
      })();

    case setMode.type:
      return (() => {
        const { payload: mode = 'view' } = action;

        if (
          state.dirty &&
          // eslint-disable-next-line no-alert
          !window.confirm('Changes you made may not be saved.')
        ) {
          return state;
        }

        return {
          ...state,
          dirty: false,
          mode,
        };
      })();

    case setModeDisabled.type:
      return (() => {
        const { payload: disabled = false } = action;

        return {
          ...state,
          dirty: false,
          disabled,
        };
      })();

    case setDirty.type:
      return (() => {
        const { payload: dirty = true } = action;

        return {
          ...state,
          dirty,
        };
      })();

    case projectMutationSuccess.type:
      return {
        ...state,
        dirty: false,
        mode: 'view',
      };

    case showInvoiceConfigError.type:
      return {
        ...state,
        currentTab: 3,
        validateInvoiceConfig: true,
      };

    case saveExperimentGroupsSuccess.type:
      return {
        ...state,
        dirty: false,
        mode: 'view',
      };

    case saveAccessHolderError.type:
      return {
        ...state,
        dirty: true,
        mode: 'edit',
      };

    default:
      return state;
  }
}

reducer.key = 'projectView';

function selectProjectViewFn(state) {
  return state[reducer.key];
}

export const selectCurrentTab = createSelector(
  selectProjectViewFn,
  (projectView) => projectView.currentTab,
);

export const selectMode = createSelector(
  selectProjectViewFn,
  (projectView) => projectView.mode,
);

export const selectModeDisabled = createSelector(
  selectProjectViewFn,
  (projectView) => projectView.disabled,
);

export const selectDirty = createSelector(
  selectProjectViewFn,
  (projectView) => projectView.dirty,
);

export const selectValidateInvoiceConfig = createSelector(
  selectProjectViewFn,
  (projectView) => projectView.validateInvoiceConfig,
);

export function selectOverviewAndTools(state) {
  const projectState = selectProjectState(state);

  if (projectState.loading || projectState.project === null) {
    return {
      createdBy: {},
      percentCompleted: {},
    };
  }

  const { project } = projectState;

  return {
    projectId: project.id,
    description: project.description,
    projectType: project.projectType,
    workType: project.workType,
    taskVolume: project.taskVolume,
    externalSystemId: project.externalSystemId,
    externalSystemUrl: project.externalSystemUrl,
    allowedIps: project.allowedIps,
    swfhProjectExternalUrl: project.swfhProjectExternalUrl,
    status: project.status,
    customerId: project.customerId,
    createdAt: project.createdDate,
    createdBy: project.createdBy,
    profileTargetId: project.profileTargetId,
    workdayTaskId: project.workdayTaskId,
    externalLinkEnabled: project.externalLinkEnabled,
    hasExternalProjectSettings: project.hasExternalProjectSettings,
    percentCompleted: selectCompletedPercentageFromProject(project, {
      hasLocaleTenants: hasLocaleTenants(state),
      hasHiringTargets: hasHiringTargets(state),
      hasPayRates: hasPayRates(state),
    }),
  };
}

export function selectRegistrationAndQualification(state) {
  const projectState = selectProjectState(state);

  if (projectState.loading || projectState.project === null) {
    return selectRegistrationAndQualificationFromProject({});
  }

  const { project } = projectState;

  return selectRegistrationAndQualificationFromProject(project);
}

export function selectSupportTeam(state) {
  const projectState = selectProjectState(state);

  if (projectState.loading || projectState.project === null) {
    return selectSupportTeamFromProject(null);
  }

  const { project } = projectState;

  return selectSupportTeamFromProject(project);
}

export function selectUserProjectPage(state) {
  const projectState = selectProjectState(state);

  if (projectState.loading || projectState.project === null) {
    return selectUserProjectPageFromProject(null);
  }

  const { project } = projectState;

  return selectUserProjectPageFromProject(project);
}

export function selectRecruitment(state) {
  const projectState = selectProjectState(state);

  const dependencyFlags = {
    hasDemographics: hasAnyDemographicData(state),
  };

  if (projectState.loading || projectState.project === null) {
    return selectRecruitmentFromProject({}, dependencyFlags);
  }

  const { project } = projectState;

  return selectRecruitmentFromProject(project, dependencyFlags);
}

export function selectProjectStatus(state) {
  const projectState = selectProjectState(state);

  if (projectState.loading || projectState.project === null) {
    return 'LOADING';
  }

  const { project } = projectState;

  return project.status;
}
