import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import * as yup from 'yup';
import get from 'lodash/get';
import { requiredMessage } from '../../../../../../pages/ProjectSetup/Steps/shared';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../../store/ducks/editableEntities';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
} from '../../../../../../store/ducks/options';
import DocumentResourceForm from './DocumentResourceForm';

function mapPropsToValues({ resource }) {
  const hasNotesToRaters = get(resource, 'noteToRater', '');

  return {
    ...(resource || { usageType: 'TRAINING' }),
    hasNotesToRaters: !!hasNotesToRaters,
  };
}

export default compose(
  connect((state) => ({
    resource: editor.selectors.selectEditorDataFor(
      EDITABLE_ENTITIES.PROJECT_RESOURCES,
      state,
    ),
  })), // injects dispatch
  withFormik({
    mapPropsToValues,
    handleSubmit: async (values, { props: { dispatch, projectId } }) => {
      const { hasNotesToRaters, noteToRater } = values;
      const newNoteToRater = hasNotesToRaters ? noteToRater : '';

      dispatch(
        editor.actions.save(
          EDITABLE_ENTITIES.PROJECT_RESOURCES,
          {
            type: ENTITY_METADATA.projectResources.types.document,
            active: true,
            ...values,
            noteToRater: newNoteToRater,
          },
          {
            projectId,
            thenPut: fetch({
              optionsFor: ENTITIES.projectResources,
              nestedKey: projectId,
            }),
          },
        ),
      );
    },
    validationSchema: yup.object().shape({
      resourceId: yup.number().required(requiredMessage`Document`),
      noteToRater: yup.string().when('hasNotesToRaters', {
        is: true,
        then: yup
          .string()
          .required(
            requiredMessage`When "Add Notes to Raters" is checked this`,
          ),
      }),
    }),
  }),
)(DocumentResourceForm);
