import get from 'lodash/get';
import { fetchProject, patchProject } from '../../../../../store/ducks/project';
import { createLocaleTenantSaga } from '../../../../../store/ducks/localeTenants/createLocaleTenantSaga';
import { createHiringTargetSaga } from '../../../../../store/ducks/hiringTargets/createHiringTargetSaga';
import { addOne as addOnePayrate } from '../../../../../store/ducks/payrates';
import { saveExperimentGroups as addOneExperimentGroup } from '../../../../../store/ducks/experimentGroups';
import { setConfiguration } from '../../../../../store/ducks/invoiceConfiguration';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../store/ducks/editableEntities';
import { saveAutoScreening } from '../../../../../store/ducks/autoScreening';
import {
  DEMOGRAPHIC_TYPE,
  setRequirementsByGroup,
  setAggregatedRequirements,
} from '../../../../../store/ducks/projectDemographicRequirements';

function onSuccess() {}
function onError() {}

export function storeTenants(props) {
  const { localeTenants, projectId } = props;

  return localeTenants.map((tenant) =>
    createLocaleTenantSaga({
      payload: {
        ...tenant,
        id: null,
        projectId,
      },
      meta: { onSuccess, onError },
    }),
  );
}

export function storeHiringTargets(props) {
  const { hiringTargets, projectId } = props;

  return hiringTargets.map((hiringTarget) =>
    createHiringTargetSaga({
      payload: {
        ...hiringTarget,
        hiringDeadline: hiringTarget.hiringDeadline
          ? hiringTarget.hiringDeadline
          : new Date(Date.now()),
        id: null,
        projectId,
      },
      meta: { onSuccess, onError },
    }),
  );
}

export function cloneProjectDependencies(props) {
  const {
    project,
    aggregatedRequirements,
    demographicType,
    dispatch,
    requirementsByGroup,
    projectPages,
    newProjectPages,
    projectId,
  } = props;

  if (demographicType === DEMOGRAPHIC_TYPE.BY_GROUP) {
    dispatch(
      setRequirementsByGroup(requirementsByGroup, {
        onSuccess,
        onError,
        projectId,
      }),
    );
  } else if (aggregatedRequirements) {
    dispatch(
      setAggregatedRequirements(aggregatedRequirements, {
        onSuccess,
        onError,
        projectId,
      }),
    );
  }

  // Updates default project page
  if (projectPages && newProjectPages) {
    const oldDefaultPage =
      projectPages.find((page) => page.id === project.defaultPageId) || {};
    if (oldDefaultPage) {
      const defaultPage = newProjectPages.find(
        (page) => page.tabName === oldDefaultPage.tabName,
      );
      dispatch(
        patchProject({
          changes: {
            defaultPageId: get(defaultPage, 'id', null),
          },
        }),
      );
    }
  }
}

export default async function cloneBasicProject(props) {
  const {
    dispatch,
    isCloning,
    isCloneCreated,
    project,
    enabledProjects,
    setBlockingRulesInconsistencies,
    id: projectId,
    name: projectName,
    alias: projectAlias,
  } = props;

  const enabledProjectsIds = enabledProjects.data.map((p) => p.id);

  // Clone only active projects
  const blockingRules = project.blockingRules
    ? project.blockingRules.filter((rule) =>
        enabledProjectsIds.includes(rule.blockedProjectId),
      )
    : [];

  // Updates all the project's basic data
  if (isCloning && !isCloneCreated) {
    dispatch(
      patchProject({
        changes: {
          ...project,
          blockingRules,
          id: projectId,
          alias: projectAlias,
          name: projectName,
          status: 'DRAFT',
        },
      }),
    );
  }

  if (
    blockingRules.length > 0 &&
    project.blockingRules.length > blockingRules.length
  ) {
    setBlockingRulesInconsistencies(true);
  }
}

export async function cloneProjectFeatures(props) {
  const {
    dispatch,
    originalAccessHolders,
    newAccessHolders,
    experimentGroups,
    payrates,
    setIsCloning,
    setIsCloneCreated,
    projectId,
    invoiceConfig,
    allProjectResources,
    autoScreenings,
    userGroups,
  } = props;

  const allUsersIds = userGroups.data
    .filter((userGroup) => userGroup.name.match(/all users/i) !== null)
    .map((userGroup) => userGroup.id);

  const groups = { allUsers: [], otherUsers: [] };

  payrates.forEach((payrate) => {
    if (allUsersIds.includes(payrate.userGroupId)) {
      groups.allUsers.push(payrate);
    } else {
      groups.otherUsers.push(payrate);
    }
  });

  if (groups.allUsers.length > 0 || groups.otherUsers.length > 0) {
    // Store all users payrates first
    await Promise.all(
      groups.allUsers.map(async (payrate) => {
        await dispatch(
          addOnePayrate({ ...payrate, projectId }, { onSuccess, onError }),
        );
      }),
    );

    await new Promise((resolve) => setTimeout(resolve, 1500));

    // Store payrates
    await Promise.all(
      groups.otherUsers.map(async (payrate) => {
        await dispatch(
          addOnePayrate({ ...payrate, projectId }, { onSuccess, onError }),
        );
      }),
    );
  }

  // Store invoice template setup
  if (invoiceConfig) {
    await dispatch(
      setConfiguration({
        projectId,
        configuration: { ...invoiceConfig },
      }),
    );
  }

  // Store project resources
  if (allProjectResources && allProjectResources.length > 0) {
    await Promise.all(
      allProjectResources.map(async (projectResource) =>
        dispatch(
          editor.actions.save(
            EDITABLE_ENTITIES.PROJECT_RESOURCES,
            {
              ...projectResource,
              id: null,
            },
            {
              projectId,
            },
          ),
        ),
      ),
    );
  }

  // Store auto-screenings
  if (autoScreenings && autoScreenings.length > 0) {
    await Promise.all(
      autoScreenings.map(async (autoScreening) =>
        dispatch(
          saveAutoScreening({
            changes: { ...autoScreening, id: null, projectId },
          }),
        ),
      ),
    );
  }

  // Store user groups
  if (newAccessHolders && newAccessHolders.length > 0) {
    await Promise.all(
      newAccessHolders.map(async (accessHolder, i) => {
        const experimentId = accessHolder.id;
        const originalExperimentId = get(
          originalAccessHolders,
          [i, 'id'],
          null,
        );

        const payloadExperimentGroups = {
          experimentId,
          experimentGroups: get(experimentGroups, originalExperimentId, []).map(
            (ug) => ({
              ...ug,
              id: null,
              experimentId,
            }),
          ),
        };

        await dispatch(
          addOneExperimentGroup({ changes: payloadExperimentGroups }),
        );
      }),
    );
  }

  // Reloads the application state
  dispatch(fetchProject({ projectId }));
  setIsCloning(false);
  setIsCloneCreated(true);
}
