import React, { useEffect } from 'react';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import { useFormikContext } from 'formik';
import { projectTypes } from '../../../pages/ProjectSetup/helpers/mock-data';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import { Label, Section } from '../../../styles';
import Input from '../../Input';
import Textarea from '../../Textarea';
import Select from '../../Select';

export default function ProjectOverviewForm() {
  const {
    handleChange,
    setFieldValue,
    errors,
    values,
    submitCount,
  } = useFormikContext();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.workTypes));
    dispatch(fetch(ENTITIES.taskVolumes));
  }, [dispatch]);

  const workTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.workTypes),
  );

  const taskVolumes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.taskVolumes),
  );

  return (
    <>
      <Section>
        <Row>
          <Col col={6}>
            <Label htmlFor="projectName">Project Name</Label>
            <Input
              name="projectName"
              onChange={handleChange}
              id="projectName"
              placeholder="Name of your project"
              value={values.projectName}
              showCheck={!errors.projectName}
              error={submitCount >= 1 && errors.projectName}
              data-testid="project-overview--project-name"
            />
          </Col>
          <Col col={6}>
            <Label htmlFor="projectAlias">Project Alias</Label>
            <Input
              onChange={handleChange}
              name="projectAlias"
              id="projectAlias"
              value={values.projectAlias}
              placeholder="Name shown to customer"
              showCheck={!errors.projectAlias}
              error={submitCount >= 1 && errors.projectAlias}
              data-testid="project-overview--project-alias"
            />
          </Col>
        </Row>
      </Section>
      <Section>
        <Row>
          <Col col={12}>
            <Label htmlFor="projectDescription">Project Description</Label>
            <Textarea
              onChange={handleChange}
              name="projectDescription"
              id="projectDescription"
              value={values.projectDescription}
              placeholder="Describe your project in a few words"
              rows={6}
              error={submitCount >= 1 && errors.projectDescription}
              data-testid="project-overview--project-description"
            />
          </Col>
        </Row>
      </Section>
      <Section>
        <Row>
          <Col col={4}>
            <Label htmlFor="projectType">Project Type</Label>
            <Select
              value={values.projectType}
              name="projectType"
              options={projectTypes}
              error={submitCount >= 1 && errors.projectType}
              onChange={({ value }) => setFieldValue('projectType', value)}
              data-testid="project-overview--project-type"
            />
          </Col>
          <Col col={4}>
            <Label htmlFor="taskType">Task Type</Label>
            <Select
              value={values.taskType}
              onChange={({ value }) => setFieldValue('taskType', value)}
              name="taskType"
              data-testid="project-overview--task-type"
              options={workTypes.data}
              loading={workTypes.loading}
              error={workTypes.error || (submitCount >= 1 && errors.taskType)}
            />
          </Col>
          <Col col={4}>
            <Label htmlFor="taskVolume">Task Volume</Label>
            <Select
              value={values.taskVolume}
              onChange={({ value }) => setFieldValue('taskVolume', value)}
              name="taskVolume"
              data-testid="project-overview--task-volume"
              options={taskVolumes.data}
              loading={taskVolumes.loading}
              error={
                taskVolumes.error || (submitCount >= 1 && errors.taskVolume)
              }
            />
          </Col>
        </Row>
      </Section>
    </>
  );
}
