/* eslint-disable no-plusplus */
import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import { ENTITIES, selectors } from '../../../../../../store/ducks/options';
import { mapToMacroLanguages } from '../../../../../ProcessBuilder/helpers/macroLanguages';
import LocaleCell from './LocaleCell';

function findLocale(locales, predicate) {
  for (let i = 0; i < locales.length; i++) {
    const locale = locales[i];
    if (predicate(locale)) return locale;

    if (locale.locales) {
      const match = findLocale(locale.locales, predicate);
      if (match) return match;
    }
  }

  return null;
}

function useHooksAsProps(props) {
  const {
    cell: {
      row: { original },
    },
  } = props;

  const { localeId } = original;

  const locales = useSelector(selectors.createSelectSliceFor(ENTITIES.locales));

  const macroLanguages = useMemo(() => {
    const locale = findLocale(locales.data, ({ id }) => id === localeId);
    return mapToMacroLanguages(locale ? [locale] : []);
  }, [locales.data, localeId]);

  return {
    locale: get(macroLanguages, [0, 'label'], 'Not defined'),
  };
}

export default withHooks(useHooksAsProps)(LocaleCell);
