import { withFormik } from 'formik';
import { compose } from 'recompose';
import { connect, useSelector } from 'react-redux';
import shortid from 'shortid';
import withHooks from 'hook-hoc';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { hasAnyDemographicData } from '../../../../../store/ducks/projectDemographicRequirements';
import RecruitmentProjectViewForm from './RecruitmentProjectViewForm';
import { selectRecruitmentFromProject } from '../../../../../store/ducks/project/selectors';
import {
  patchProject,
  selectProject,
} from '../../../../../store/ducks/project';
import { selectAutoScreenings } from '../../../../../store/ducks/autoScreening';
import { recruitmentYupValidations } from '../../../../../components/ProjectForms/RecruitmentForm';

function useHooksAsProps() {
  const autoScreenings = useSelector(selectAutoScreenings);

  return { autoScreenings };
}

function mapStateToProps(state, outerProps) {
  const { autoScreenings } = outerProps;

  const project = selectProject(state);
  const hasDemographics = hasAnyDemographicData(state);

  const initialValues = selectRecruitmentFromProject(project, {
    hasDemographics,
  });

  return {
    initialValues: { ...initialValues, autoScreenings },
    hasDemographics,
  };
}

function mapPropsToValues(props) {
  const { autoScreenings, initialValues, hasDemographics } = props;

  if (
    (Array.isArray(initialValues.blockingRules) &&
      initialValues.blockingRules.length) ||
    (Array.isArray(initialValues.linkedProjects) &&
      initialValues.linkedProjects.length) ||
    initialValues.notesToRecruiting
  ) {
    return { ...initialValues, showDemographics: hasDemographics };
  }

  return {
    blockingRules: [
      { key: shortid.generate(), projectId: null, permanent: false },
    ],
    linkedProjects: [
      { key: shortid.generate(), projectId: null, permanent: false },
    ],
    showDemographics: hasDemographics,
    hasAutoScreening: autoScreenings.length > 0,
    hasTargetScreening: initialValues.hasTargetScreening || false,
    profileTargetId: initialValues.profileTargetId || null,
  };
}

function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {};

  if (values.hasNotesToRecruiting) {
    patch.recruitingTeamNotes = values.notesToRecruiting;
  } else {
    patch.recruitingTeamNotes = null;
  }

  if (values.hasBlockingRules) {
    patch.blockingRules = (values.blockingRules || []).map((br) => ({
      blockedProjectId: br.projectId,
      permanent: br.permanent,
    }));
  } else {
    patch.blockingRules = null;
  }

  if (values.hasLinkedProjects) {
    patch.linkedProjectsIds = (values.linkedProjects || []).map(
      (lp) => lp.projectId,
    );
  } else {
    patch.linkedProjectsIds = null;
  }

  if (values.hasTargetScreening) {
    patch.profileTargetId = values.profileTargetId || null;
  } else {
    patch.profileTargetId = null;
  }

  dispatch(patchProject({ changes: patch }));
}

export default compose(
  withErrorPopover,
  withHooks(useHooksAsProps),
  connect(mapStateToProps),
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema: recruitmentYupValidations,
  }),
)(RecruitmentProjectViewForm);
