import React from 'react';
import PropTypes from 'prop-types';
import Table from '../../../../../../Table';
import { StyledTable, Flex, StandInTable } from './styles';
import { useDemographicsViewTypeContext } from '../../../../../../../helpers/demographics-view-type-context';

export default function AggregatedRequirements(props) {
  const { standInColumns, columns, tableData } = props;

  const { viewType } = useDemographicsViewTypeContext();
  const isInsideModal = viewType === 'modal';

  return (
    <StyledTable>
      {standInColumns.map(([standInCols, rows, , key]) => (
        <StandInTable
          key={key}
          multiplier={Math.floor(tableData.length / rows.length)}
        >
          <Table columns={standInCols} data={rows} />
        </StandInTable>
      ))}
      <Flex columns={isInsideModal ? standInColumns.length : -1}>
        <Table columns={columns} data={tableData} />
      </Flex>
    </StyledTable>
  );
}

AggregatedRequirements.propTypes = {
  standInColumns: PropTypes.arrayOf(
    PropTypes.arrayOf(
      PropTypes.oneOfType([
        PropTypes.arrayOf(
          PropTypes.shape({
            Header: PropTypes.string,
            accessor: PropTypes.string,
            disableSortBy: PropTypes.bool,
            key: PropTypes.string,
          }),
        ),
        PropTypes.arrayOf(
          PropTypes.shape({
            label: PropTypes.string,
            value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
          }),
        ),
        PropTypes.number,
        PropTypes.string,
      ]),
    ),
  ).isRequired,
  columns: PropTypes.arrayOf(PropTypes.object).isRequired,
  tableData: PropTypes.arrayOf(PropTypes.object).isRequired,
};
