import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';
import { v4 as uuid } from 'uuid';
import { isEmpty, sortBy, findLast, last } from 'lodash';

import { alertError } from '../../../../../components/Alert';
import Icon from '../../../../../components/Icon';
import Tooltip from '../../../../../components/Tooltip';

import { ReactComponent as Calendar } from '../../../../../assets/icons/calendar.svg';
import { ReactComponent as Pencil } from '../../../../../assets/icons/pencil.svg';
import { ReactComponent as Copy } from '../../../../../assets/icons/copy-goal.svg';
import { ReactComponent as HistoryClock } from '../../../../../assets/icons/history-clock.svg';

import {
  getMetricChangelogs,
  setMetricChangelogs,
} from '../../../../../store/ducks/metricsAndGoals';

import { Label, Text, SectionTitle } from '../../../../../styles';
import {
  Container,
  MatchDataLabel,
  EditButton,
  ChangelogButton,
  ChangelogDisclaimer,
  GoalDetail,
  TooltipContainer,
  TooltipDot,
  TooltipText,
} from './styles';

import {
  PRODUCTIVITY_METRICS_TYPE,
  QUALITY_METRICS_TYPE,
  CHANGE_REASON_DICT,
} from '../constants';

import { dateFormat } from '../helpers';

const renderChangelogs = (changelogs, panelType, market) => {
  const { original, latest } = changelogs;

  let oValue = original.goalValue;

  if (original.goalValueType === 'MATCH_DATA') {
    oValue = 'Match data';
  }

  if (original.goalValueType === 'PER_MARKET') {
    if (original.shouldManipulate) {
      oValue = 'Per Market Goals';
    } else {
      oValue = original.marketGoals.find((m) => m.market === market);
      oValue = oValue ? oValue.goalValue : 0;
    }
  }

  return (
    <TooltipContainer>
      <ul>
        {!isEmpty(latest) &&
          latest.map((changelog) => {
            const {
              shouldManipulate,
              goalValue,
              changeStartDate,
              changeEndDate,
              changeReason,
              currentGoal,
              marketGoals,
            } = changelog;

            let value = goalValue;

            if (changelog.goalValueType === 'MATCH_DATA') {
              value = 'Match data';
            }

            if (changelog.goalValueType === 'PER_MARKET') {
              if (shouldManipulate) {
                value = 'Per Market Goals';
              } else {
                value = marketGoals.find((m) => m.market === market);
                value = value ? value.goalValue : 0;
              }
            }

            const startDate = changeStartDate
              ? dateFormat(changeStartDate)
              : 'Start of project';

            const endDate = changeEndDate
              ? dateFormat(changeEndDate)
              : 'No end Date';

            const reason = CHANGE_REASON_DICT[changeReason];

            return (
              <li key={uuid()}>
                <TooltipDot current={currentGoal} />
                <TooltipText current={currentGoal}>
                  {value}
                  {panelType === QUALITY_METRICS_TYPE && '%'} - {startDate} to{' '}
                  {endDate} - due to {reason}{' '}
                  {currentGoal && <span>[current goal]</span>}
                </TooltipText>
              </li>
            );
          })}
        {!isEmpty(latest) && latest.length === 5 && (
          <li>
            <TooltipDot />
            <TooltipText>...</TooltipText>
          </li>
        )}
        <li>
          <TooltipDot current={original.currentGoal} />
          <TooltipText current={original.currentGoal}>
            {oValue}
            {panelType === QUALITY_METRICS_TYPE && '%'} - Original Goal
          </TooltipText>
        </li>
      </ul>
    </TooltipContainer>
  );
};

const renderViewType = (goal, panelType, changelogs) => {
  switch (goal.goalValueType) {
    case 'EQUAL': {
      const { goalValue } = goal;
      return (
        <li key={uuid()}>
          <Label className="label">All Market&apos;s goals</Label>
          {changelogs ? (
            <Tooltip
              overlay={renderChangelogs(changelogs, panelType)}
              placement="bottom"
              prefixCls="changelogs information_tooltip"
            >
              <Text>
                {goalValue}
                {panelType === QUALITY_METRICS_TYPE && '%'}
              </Text>
            </Tooltip>
          ) : (
            <Text>
              {goalValue}
              {panelType === QUALITY_METRICS_TYPE && '%'}
            </Text>
          )}
        </li>
      );
    }
    case 'PER_MARKET': {
      const marketGoals =
        panelType === PRODUCTIVITY_METRICS_TYPE
          ? goal.productivityMarketGoals
          : goal.qualityMarketGoals;

      return sortBy(marketGoals, ['market']).map((item) => {
        const { market, goalValue } = item;

        return (
          <li key={uuid()}>
            <Label className="label">{market}</Label>
            {changelogs ? (
              <Tooltip
                overlay={renderChangelogs(changelogs, panelType, market)}
                placement="bottom"
                prefixCls="changelogs information_tooltip"
              >
                <Text>
                  {goalValue}
                  {panelType === QUALITY_METRICS_TYPE && '%'}
                </Text>
              </Tooltip>
            ) : (
              <Text>
                {goalValue}
                {panelType === QUALITY_METRICS_TYPE && '%'}
              </Text>
            )}
          </li>
        );
      });
    }
    case 'MATCH_DATA':
      return (
        <MatchDataLabel key={uuid()}>
          <Icon icon={<Copy />} />
          <p>The goal value will always equal actual data values.</p>
        </MatchDataLabel>
      );
    default:
      return <p key={uuid()}>Goals not defined</p>;
  }
};

export default function GoalsSettings({
  mode,
  showEdit,
  metric,
  handleEdit,
  panelType,
}) {
  const dispatch = useDispatch();

  const { metricsChangelogs } = useSelector((state) => state.metricsAndGoals);

  const [projectGoal, setProjectGoal] = useState(null);
  const [workerGoal, setWorkerGoal] = useState(null);
  const [loadingChangelogs, setLoadingChangelogs] = useState(false);
  const [errorChangelogs, setErrorChangelogs] = useState(false);
  const [changelogsGoals, setChangelogsGoals] = useState(undefined);

  const quizBasedDate = () => {
    if (metric && !isEmpty(metric.qualityGoals)) {
      const goal = last(metric.qualityGoals);
      if (goal.quizDate && goal.quizDate !== '') {
        return dateFormat(goal.quizDate);
      }
    }

    return 'no quiz date yet';
  };

  const handleRequestChangelogs = () => {
    if (!loadingChangelogs) {
      setLoadingChangelogs(true);
      dispatch(getMetricChangelogs({ metricId: metric.id, type: panelType }));
    }
  };

  const hideChangelogs = () => {
    const obj = {};
    obj[`${metric.id}_${panelType}`] = undefined;
    dispatch(setMetricChangelogs(obj));
    setChangelogsGoals(undefined);
  };

  // TODO: define if all itens have same value

  useEffect(() => {
    if (metric) {
      let list = [];

      if (!isEmpty(metric.productivityGoals)) list = metric.productivityGoals;
      if (!isEmpty(metric.qualityGoals)) list = metric.qualityGoals;

      setProjectGoal(findLast(list, (el) => el.goalType === 'PROJECT'));
      setWorkerGoal(findLast(list, (el) => el.goalType === 'WORKER'));
    }
  }, [metric]);

  useEffect(() => {
    if (mode === 'edit') {
      setLoadingChangelogs(false);
      hideChangelogs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mode]);

  useEffect(() => {
    if (metricsChangelogs[`${metric.id}_${panelType}`]) {
      if (metricsChangelogs[`${metric.id}_${panelType}`].error) {
        setLoadingChangelogs(false);
        setErrorChangelogs(true);
        setChangelogsGoals(undefined);
      } else {
        setLoadingChangelogs(false);
        setErrorChangelogs(false);
        setChangelogsGoals(
          metricsChangelogs[`${metric.id}_${panelType}`].goals,
        );
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [metric.id, metricsChangelogs]);

  useEffect(() => {
    if (errorChangelogs) {
      alertError(
        'Some error happen when try to get changelogs for this goals.',
        {
          toastId: `metric-changelogs${metric.id}`,
        },
      );
    }
  }, [metric.id, errorChangelogs]);

  return (
    <Container>
      <Row className="metric-goals">
        <Col col={12}>
          <div>
            <div style={{ display: 'inline-block' }}>
              {metric.quizBased ? (
                <div style={{ marginBottom: '24px', display: 'inline-flex' }}>
                  <div style={{ display: 'flex', marginBottom: '18px' }}>
                    <Calendar />
                    <Label
                      style={{
                        display: 'inline-block',
                        margin: 0,
                        lineHeight: '20px',
                        marginLeft: '4px',
                      }}
                    >
                      {quizBasedDate()}
                    </Label>
                  </div>
                  {projectGoal && metric.id && (
                    <div>
                      <GoalDetail style={{ margin: '0 5px' }}>|</GoalDetail>
                      <GoalDetail>
                        Edited on:{' '}
                        {`${dateFormat(projectGoal.updatedAt)} - by ${
                          projectGoal.updatedByUserName
                        }`}
                      </GoalDetail>
                    </div>
                  )}
                </div>
              ) : (
                <>
                  {projectGoal && metric.id && (
                    <div style={{ marginBottom: '24px' }}>
                      <GoalDetail>
                        Start Date:{' '}
                        <span style={{ color: 'black' }}>
                          {projectGoal.changeStartDate
                            ? dateFormat(projectGoal.changeStartDate)
                            : 'Start of project'}
                        </span>
                      </GoalDetail>
                      <GoalDetail style={{ margin: '0 5px' }}>|</GoalDetail>
                      <GoalDetail>
                        End Date:{' '}
                        <span style={{ color: 'black' }}>
                          {projectGoal.changeEndDate
                            ? dateFormat(projectGoal.changeEndDate)
                            : 'No End Date'}
                        </span>
                      </GoalDetail>
                      <GoalDetail style={{ margin: '0 5px' }}>|</GoalDetail>
                      <GoalDetail>
                        Edited on:{' '}
                        {`${dateFormat(projectGoal.updatedAt)} - by ${
                          projectGoal.updatedByUserName
                        }`}
                      </GoalDetail>
                    </div>
                  )}
                </>
              )}
              <SectionTitle className="subtitle">
                Project-Market Goals
              </SectionTitle>
            </div>

            {/* show edit icon just when in edit mode and metric is enabled */}
            {mode === 'edit' && showEdit ? (
              <Tooltip
                placement="left"
                overlay="Edit Goals"
                prefixCls="information_tooltip"
                overlayClassName=""
              >
                <EditButton type="button" onClick={handleEdit}>
                  <Pencil />
                  <span>Edit</span>
                </EditButton>
              </Tooltip>
            ) : (
              metric.id && (
                <div style={{ display: 'flex', float: 'right' }}>
                  {!isEmpty(changelogsGoals) ? (
                    <>
                      <ChangelogDisclaimer>
                        Hover markets to see their changelog
                      </ChangelogDisclaimer>
                      <ChangelogButton
                        loading={loadingChangelogs}
                        loaderType="tertiary"
                        onClick={hideChangelogs}
                        active
                      >
                        <Tooltip
                          placement="left"
                          overlay="Hide Changelogs"
                          prefixCls="information_tooltip"
                          overlayClassName=""
                        >
                          <div style={{ display: 'flex' }}>
                            <HistoryClock />
                            <span>Changelog</span>
                          </div>
                        </Tooltip>
                      </ChangelogButton>
                    </>
                  ) : (
                    <ChangelogButton
                      loading={loadingChangelogs}
                      loaderType="tertiary"
                      onClick={handleRequestChangelogs}
                    >
                      <Tooltip
                        placement="left"
                        overlay="View Edits Changelog"
                        prefixCls="information_tooltip"
                        overlayClassName=""
                      >
                        <div style={{ display: 'flex' }}>
                          <HistoryClock />
                          <span>Changelog</span>
                        </div>
                      </Tooltip>
                    </ChangelogButton>
                  )}
                </div>
              )
            )}
          </div>

          {projectGoal ? (
            <ul className="goals-list">
              {renderViewType(
                projectGoal,
                panelType,
                changelogsGoals && changelogsGoals.project,
              )}
            </ul>
          ) : (
            <p className="message">No goal values yet.</p>
          )}
        </Col>
      </Row>
      <Row className="metric-goals">
        <Col col={12}>
          <SectionTitle className="subtitle">Worker Goals</SectionTitle>
          {workerGoal ? (
            <ul className="goals-list">
              {renderViewType(
                workerGoal,
                panelType,
                changelogsGoals && changelogsGoals.worker,
              )}
            </ul>
          ) : (
            <p className="message">No goal values yet.</p>
          )}
        </Col>
      </Row>
    </Container>
  );
}

GoalsSettings.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']),
  showEdit: PropTypes.bool,
  panelType: PropTypes.string.isRequired,
  metric: PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    productivityGoals: PropTypes.arrayOf(PropTypes.shape({})),
    qualityGoals: PropTypes.arrayOf(PropTypes.shape({})),
    quizBased: PropTypes.bool,
    updatedAt: PropTypes.string,
    updatedBy: PropTypes.string,
    changeStartDate: PropTypes.string,
    changeEndDate: PropTypes.string,
  }).isRequired,
  handleEdit: PropTypes.func.isRequired,
};

GoalsSettings.defaultProps = {
  mode: 'view',
  showEdit: false,
};
