import { withFormik } from 'formik';
import { withTheme } from 'styled-components';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import shortid from 'shortid';
import SupportTeamForm from './SupportTeamForm';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { selectSupportTeam } from '../../../../../store/ducks/projectView';
import { projectSupportTeamValidations } from '../../../../../components/ProjectForms/ProjectSupportTeamForm';
import { patchProject } from '../../../../../store/ducks/project';

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {
    supportTeam: ((values && values.supportTeam) || []).map(
      (supportMember) => ({
        userId: supportMember.member,
        role: supportMember.role,
      }),
    ),
  };

  dispatch(patchProject({ changes: patch }));
}

function mapStateToProps(state) {
  const supportTeam = selectSupportTeam(state);

  return {
    supportTeam,
  };
}

function mapPropsToValues(props) {
  const { supportTeam } = props;

  return supportTeam && supportTeam.supportTeam
    ? {
        supportTeam: (supportTeam.supportTeam || []).map((st) => ({
          role: st.role,
          member: st.member || st.userId,
        })),
      }
    : {
        supportTeam: [{ key: shortid.generate(), role: null, member: null }],
      };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: projectSupportTeamValidations,
  }),
)(withTheme(SupportTeamForm));
