import React, { useCallback, useEffect } from 'react';
import { Row, Col } from 'styled-bootstrap-grid';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { setDirty, setMode } from '../../../../../store/ducks/projectView';
import { selectSaving } from '../../../../../store/ducks/project';
import ActionFooter from '../../../../../components/ActionFooter';
import ProjectOverviewForm from '../../../../../components/ProjectForms/ProjectOverviewForm';
import ProjectToolsForm from '../../../../../components/ProjectForms/ProjectToolsForm';
import { Section } from '../../../../../styles';
import { CardContentContainer, CardFooter, StepName } from '../../../styles';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../../components/ErrorPopover';

export default function OverviewAndToolsForm(props) {
  const {
    dirty,
    submitForm,
    submitCount,
    showError,
    errors,
    errorPopoverProps,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setDirty(dirty));
  }, [dirty, dispatch]);

  const cancelEditing = useCallback(() => dispatch(setMode('view')), [
    dispatch,
  ]);

  const saving = useSelector(selectSaving);

  useEffect(() => {
    if (submitCount > 0) {
      Object.values(errors).forEach((error) => {
        showError(new Error(error));
      });
    }
  }, [submitCount, errors, showError]);

  return (
    <>
      <CardContentContainer>
        <Row>
          <Col col={8}>
            <Section>
              <StepName>Project Setup</StepName>
            </Section>
            <ProjectOverviewForm />

            <Section>
              <StepName>Project Tools</StepName>
            </Section>
            <ProjectToolsForm />
          </Col>
        </Row>
      </CardContentContainer>
      <CardFooter>
        <ErrorPopover {...errorPopoverProps} />
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              text: 'Cancel',
              type: 'secondary',
              disabled: saving,
              onClick: cancelEditing,
            },
            {
              text: 'Save Changes',
              type: 'primary',
              loading: saving,
              onClick: submitForm,
            },
          ]}
        />
      </CardFooter>
    </>
  );
}

OverviewAndToolsForm.propTypes = {
  dirty: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  submitForm: PropTypes.func.isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
};
