import get from 'lodash/get';
import store from '../../../store';
import { ENTITIES, selectors } from '../../../store/ducks/options';

export default function isSWFH(selectedId) {
  const externalSystem = selectors.selectById(
    ENTITIES.externalSystems,
    null,
    selectedId,
    store.getState(),
  );

  const externalSystemName = get(externalSystem, 'name', '');

  return !!`${externalSystemName}`.match(/^swfh$/i);
}
