import styled from 'styled-components';

export default styled.div`
  /* This is required to make the table full-width */
  display: block;
  max-width: 100%;

  /* This will make the table scrollable when it gets too small */
  .tableWrap {
    display: block;
    max-width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    padding-bottom: 4px;
    margin-bottom: 4px;

    table .sticky-column {
      background-color: ${(props) => props.theme.colors.white};

      &.last-sticky-column {
        box-shadow: 20px 0 10px -10px ${(props) => props.theme.colors.white};
      }
    }

    table .after-last-sticky-column {
      padding-left: 16px;
    }

    tbody tr:hover td.sticky-column {
      background-color: ${(props) => props.theme.colors.paleGrey};

      &.last-sticky-column {
        box-shadow: 20px 0 10px -10px ${(props) => props.theme.colors.paleGrey};
      }
    }

    ::-webkit-scrollbar {
      width: 8px; /* 1px wider than Lion. */
      height: 8px;
      /* This is more usable for users trying to click it. */
      background-color: rgba(0, 0, 0, 0);
      -webkit-border-radius: 100px;
      border-radius: 100px;
    }
    /* hover effect for both scrollbar area, and scrollbar 'thumb' */
    ::-webkit-scrollbar:hover {
      background-color: ${(props) => props.theme.colors.paleGrey};
    }
    /* hover effect for both scrollbar area, and scrollbar 'thumb' */
    ::-webkit-scrollbar:active {
      background-color: ${(props) => props.theme.colors.lightPeriwinkle};
    }

    /* The scrollbar 'thumb' ...that marque oval shape in a scrollbar */
    ::-webkit-scrollbar-thumb {
      background: ${(props) => props.theme.colors.lightPeriwinkle};
      -webkit-border-radius: 100px;
      border-radius: 100px;
    }
    ::-webkit-scrollbar-thumb:hover {
      background: ${(props) => props.theme.colors.lightBlueGrey};
      -webkit-border-radius: 100px;
      border-radius: 100px;
    }
    ::-webkit-scrollbar-thumb:active {
      background: ${(props) => props.theme.colors.blueGrey};
      -webkit-border-radius: 100px;
      border-radius: 100px;
    }
  }

  table {
    /* Make sure the inner table is always as wide as needed */
    width: 100%;
    border-spacing: 0;

    tbody {
      tr {
        :hover {
          background-color: ${(props) => props.theme.colors.paleGrey};
        }
      }
    }

    th,
    td {
      margin: 0;
      padding: 16px 0;
      border-bottom: 1px solid ${(props) => props.theme.colors.lightPeriwinkle};

      /* The secret sauce */
      /* Each cell should grow equally */
      width: 1%;
      /* But "collapsed" cells should be as small as possible */
      &.collapse {
        width: 0.0000000001%;
      }

      :last-child {
        border-right: 0;
      }
    }
  }
`;

export const Header = styled.div`
  position: relative;
  display: inline-block;
  font-size: 11px;
  font-family: 'Lato', sans-serif;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  line-height: 1.17;
  letter-spacing: 1.25px;
  color: ${(props) => props.theme.colors.blueGrey};
  user-select: none;
`;

export function withHiddenActionIcon() {
  return styled.div`
    margin: 32px 0;
    max-width: 100%;
    box-sizing: border-box;

    tr {
      & > td:last-child {
        svg {
          color: ${(props) => props.theme.colors.white};
        }
      }

      &:hover {
        & > td:last-child {
          svg {
            color: ${(props) => props.theme.colors.lightBlueGrey};

            &:hover {
              color: ${(props) => props.theme.colors.azure};
            }
          }
        }
      }
    }
  `;
}
