import { useCallback, useEffect, useMemo } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { withFormik } from 'formik';
import withHooks from 'hook-hoc';
import shortid from 'shortid';
import get from 'lodash/get';
import { compose, withProps } from 'recompose';
import { withErrorPopover } from '../../../../components/ErrorPopover';
import QuestionSetupModal from './QuestionSetupModal';
import { saveProfileQuestions } from '../../../../store/ducks/profileQuestions';
import {
  PAGINATED_ENTITIES,
  setFilters,
} from '../../../../store/ducks/paginated';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import validationSchema from './validations';
import {
  lockUpOptions,
  logicOptions,
  answerTypeOptions,
  answerTypeAcessoryOptions,
} from './constants';

function handleSubmit(values, { props }) {
  const {
    dispatch,
    modalControls: { closeModal },
  } = props;

  const {
    id,
    name,
    category,
    timer,
    question,
    questionType,
    answerList,
    systemFilled,
    parentQuestionId,
    notes,
    addQuestionNotes,
    answerStyle,
    enableExcludeCountries,
    excludeCountries,
    nestQuestion,
    logic,
    parentAnswerId,
    maxAnswerId,
    maxQuestionId,
    parentQuestion,
  } = values;

  let answerId = maxAnswerId;
  const operation = id ? 'update' : 'create';
  const newId = id || parseInt(maxQuestionId, 10) + 1;

  const metadataAnswered = systemFilled ? 'SYSTEM_ANSWERED' : 'VENDOR_ANSWERED';
  const questionLevel = parentQuestionId ? 1 : 0;
  const newLogic = get(logicOptions, [logic, 'value'], null);

  const newAnswerList = answerList.map((item, index) => {
    answerId = parseInt(answerId, 10) + 1;
    return {
      ...item,
      id: parseInt(answerId, 10),
      order: index,
    };
  });

  const payload = {
    id: newId,
    type: 'PROFILE',
    category,
    name,
    question,
    timer,
    questionType,
    answerList: newAnswerList,
    questionLevel,
    metadataAnswered,
    answerStyle,
    notes: null,
    excludeCountries: null,
    parentQuestion: null,
  };

  payload.notes = addQuestionNotes ? notes : '';

  if (nestQuestion) {
    payload.parentQuestion = {
      ...(parentQuestion && { id: parentQuestion.id }),
      subQuestionId: newId,
      parentQuestionId,
      parentAnswerId,
      subQuestionLogic: newLogic,
    };
  }

  if (enableExcludeCountries) {
    payload.excludeCountries = excludeCountries
      .map((country) => (typeof country === 'string' ? country : country.value))
      .join(',');
  }

  dispatch(
    saveProfileQuestions({
      changes: payload,
      thenPut: setFilters({
        forKey: PAGINATED_ENTITIES.profileQuestions,
        loading: false,
      }),
      closeModal,
      operation,
    }),
  );
}

function useHooksAsProps(outerProps) {
  const { values, setFieldValue } = outerProps;

  const { answerList, excludeCountries } = values;

  const dispatch = useDispatch();

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const countryOptions = useMemo(
    () => [
      { value: '*', label: 'All Countries' },
      ...countries.data.filter((candidate) => candidate.value !== '*'),
    ],
    [countries],
  );

  const countryTags = useMemo(() => {
    if (excludeCountries.find((candidate) => candidate.value === '*')) {
      return [
        {
          value: '*',
          label: 'All countries',
        },
      ];
    }

    return excludeCountries.map((country) => {
      if (typeof country === 'string') {
        return countryOptions.find((candidate) => candidate.value === country);
      }
      return countryOptions.find(
        (candidate) => candidate.value === country.value,
      );
    });
  }, [countryOptions, excludeCountries]);

  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
  }, [dispatch]);

  const createInputHandler = useCallback(
    (fieldName) => (event) => {
      const { value } = event;
      setFieldValue(fieldName, typeof event === 'number' ? event : value);
    },
    [setFieldValue],
  );

  const handleAddAnswerItem = useCallback(() => {
    setFieldValue('answerList', [...answerList, { key: shortid.generate() }]);
  }, [setFieldValue, answerList]);

  const handleRemoveAnswerItem = useCallback(
    (index) => {
      setFieldValue('answerList', [
        ...answerList.slice(0, index),
        ...answerList.slice(index + 1, answerList.length),
      ]);
    },
    [setFieldValue, answerList],
  );

  const handleAnswerListChange = useCallback(
    (index, { target: { value } }) => {
      const item = answerList[index];

      setFieldValue('answerList', [
        ...answerList.slice(0, index),
        { ...item, answer: value },
        ...answerList.slice(index + 1, answerList.length),
      ]);
    },
    [answerList, setFieldValue],
  );

  const handleCountryChange = useCallback(
    (nextValue) => {
      if (nextValue.some((country) => country.value === '*')) {
        setFieldValue('excludeCountries', [
          {
            value: '*',
            label: 'All countries',
          },
        ]);
      } else {
        setFieldValue('excludeCountries', nextValue);
      }
    },
    [setFieldValue],
  );

  const handleCountryRemoval = useCallback(
    (value) => {
      if (value === '*') {
        setFieldValue('excludeCountries', []);
      } else {
        setFieldValue(
          'excludeCountries',
          excludeCountries.filter(
            (country) => country.value !== value && country !== value,
          ),
        );
      }
    },
    [setFieldValue, excludeCountries],
  );

  const handleAnswerTypeChange = useCallback(
    ({ value }) => {
      if (value === 'YES_NO') {
        setFieldValue('answerList', [{ answer: 'Yes' }, { answer: 'No' }]);
      }
      setFieldValue('questionType', value);
    },
    [setFieldValue],
  );

  const handleLockUpPeriodChange = createInputHandler('timer');
  const handleAnswerStyleChange = createInputHandler('answerStyle');
  const handleParentQuestionIdChange = createInputHandler('parentQuestionId');
  const handleNestAnswerChange = createInputHandler('parentAnswerId');
  const handleLogicChange = createInputHandler('logic');

  return {
    countryOptions,
    countryTags,
    handleAddAnswerItem,
    handleRemoveAnswerItem,
    handleAnswerListChange,
    handleLockUpPeriodChange,
    handleAnswerTypeChange,
    handleAnswerStyleChange,
    handleParentQuestionIdChange,
    handleNestAnswerChange,
    handleLogicChange,
    handleCountryChange,
    handleCountryRemoval,
  };
}

function mapStateToProps() {
  const initialValues = {
    excludeCountries: [],
    answerList: [{ key: shortid.generate() }],
  };

  return { initialValues };
}

function mapPropsToValues(props) {
  const {
    initialValues,
    modalControls: { modalData },
  } = props;

  if (modalData) {
    const { masterProfileQuestionData } = modalData;
    const {
      addQuestionNotes,
      excludeCountries,
      metadataAnswered,
      notes,
      parentQuestion,
    } = masterProfileQuestionData;

    const logic = parentQuestion ? parentQuestion.subQuestionLogic : null;

    return {
      ...masterProfileQuestionData,
      ...(logic && { logic: logic === 'IF' ? 0 : 1 }),
      excludeCountries: excludeCountries ? excludeCountries.split(',') : [],
      addQuestionNotes: addQuestionNotes || (notes && notes.length > 0),
      enableExcludeCountries: excludeCountries && excludeCountries.length > 0,
      systemFilled: metadataAnswered === 'SYSTEM_ANSWERED' ? 1 : 0,
      parentId: parentQuestion ? parentQuestion.id : null,
      parentQuestionId: parentQuestion ? parentQuestion.parentQuestionId : null,
      parentAnswerId: parentQuestion ? parentQuestion.parentAnswerId : null,
      nestQuestion: parentQuestion ? !!parentQuestion.parentQuestionId : false,
    };
  }

  return initialValues;
}

function createProps(props) {
  const {
    modalControls: { modalData },
  } = props;

  let primaryQuestionOptions = [];
  let primaryAnswerOptions = {};

  if (modalData) {
    const { currentQuestionsList, masterProfileQuestionData } = modalData;

    const filteredList = currentQuestionsList.filter(
      ({ id }) => id !== masterProfileQuestionData.id,
    );

    primaryQuestionOptions = filteredList.map(({ id, name }) => ({
      value: id,
      label: name,
    }));

    primaryAnswerOptions = filteredList.reduce(
      (acc, question) => ({
        ...acc,
        [question.id]: question.answerList.map(({ id, answer }) => ({
          value: id,
          label: answer,
        })),
      }),
      {},
    );
  }

  return {
    lockUpOptions,
    logicOptions,
    answerTypeOptions,
    answerTypeAcessoryOptions,
    primaryQuestionOptions,
    primaryAnswerOptions,
  };
}

export default compose(
  withErrorPopover,
  withProps(createProps),
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema,
  }),
  withHooks(useHooksAsProps),
)(QuestionSetupModal);
