import React, { useEffect, useMemo, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useRouteMatch, useLocation } from 'react-router-dom';
import { useFeatures } from 'flagged';
import PropTypes from 'prop-types';
import { ProjectContainer, ProjectContent } from './styles';
import getActiveTab from './helpers/getActiveTab';
import ProjectViewCard from './ProjectViewCard';
import * as projectView from '../../store/ducks/projectView';
import {
  selectLoading as selectProjectLoading,
  selectProject,
  fetchProject,
  selectError as selectProjectError,
} from '../../store/ducks/project';
import Loading from './Loading';
import ProjectHeader from './ProjectHeader';
import Modal, { useModal } from '../../components/Modal';
import ProjectPreview from './ProjectPreview';
import ErrorPopover, { useErrorPopover } from '../../components/ErrorPopover';
import { buildTabs } from './helpers/projectViewTabs';

const useQuery = () => {
  return new URLSearchParams(useLocation().search);
};

export default function ProjectView(props) {
  const { navigate } = props;
  const query = useQuery();
  const features = useFeatures();

  const loading = useSelector(selectProjectLoading);
  const project = useSelector(selectProject);
  const currentTab = useSelector(projectView.selectCurrentTab);
  const projectError = useSelector(selectProjectError);
  const selectedTab = query.get('selectedTab');
  const tabs = buildTabs(features);
  const [prevTab, setPrevTab] = useState(undefined);

  const dispatch = useDispatch();
  const match = useRouteMatch();

  useEffect(() => {
    dispatch(fetchProject({ projectId: match.params.projectID }));
  }, [dispatch, match.params, match.params.projectID]);

  const { title, component } = useMemo(() => getActiveTab(currentTab), [
    currentTab,
  ]);

  const [modalProps, modalControls] = useModal();
  const [showError, hideError, errorPopoverProps] = useErrorPopover();

  useEffect(() => {
    if (projectError) {
      showError(new Error(projectError));
    }
  }, [projectError, showError]);

  useEffect(() => {
    hideError();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [hideError]);

  useEffect(() => {
    if (selectedTab && selectedTab !== prevTab) {
      const index = tabs.findIndex((el) => el.key === selectedTab);
      navigate(index >= 0 ? index : 0);
      setPrevTab(selectedTab);
    }
  }, [navigate, selectedTab, tabs, prevTab]);

  return (
    <ProjectContainer>
      {loading ? (
        <Loading />
      ) : (
        <>
          <ProjectHeader
            project={project || {}}
            currentTab={currentTab}
            navigateTo={navigate}
            openPreview={modalControls.openModal}
          />
          <ProjectContent>
            <ProjectViewCard title={title} component={component} />
            <ErrorPopover {...errorPopoverProps} />
          </ProjectContent>
        </>
      )}

      <Modal {...modalProps} title="Project Preview">
        <ProjectPreview closeModal={modalControls.closeModal} />
      </Modal>
    </ProjectContainer>
  );
}

ProjectView.propTypes = {
  navigate: PropTypes.func.isRequired,
};
