import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const key = 'targetScreening';

const slice = createSlice({
  name: key,

  initialState: {
    data: [],
    loading: false,
    fetchError: null,
    saving: 0,
    saveError: null,
  },

  reducers: {
    fetchTargetScreening: (state) => {
      state.loading = true;
      state.fetchError = null;
    },

    fetchTargetScreeningSuccess: (state, action) => {
      state.loading = false;
      state.fetchError = null;
      state.data = action.payload;
    },

    fetchTargetScreeningError: (state, action) => {
      state.loading = false;
      state.fetchError = action.payload;
    },

    saveTargetScreening: (state) => {
      state.saving = true;
      state.saveError = null;
    },

    saveTargetScreeningSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.data = action.payload;
    },

    saveTargetScreeningError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
    },
  },
});

const {
  actions: {
    fetchTargetScreening,
    fetchTargetScreeningSuccess,
    fetchTargetScreeningError,
    saveTargetScreening,
    saveTargetScreeningSuccess,
    saveTargetScreeningError,
  },
  reducer,
} = slice;

reducer.key = key;

export default reducer;

export {
  fetchTargetScreening,
  fetchTargetScreeningSuccess,
  fetchTargetScreeningError,
  saveTargetScreening,
  saveTargetScreeningSuccess,
  saveTargetScreeningError,
};

export function selectTargetScreeningSlice(state) {
  return state[reducer.key];
}

export function selectTargetScreening(state) {
  return get(selectTargetScreeningSlice(state), 'data', []);
}

export function selectLoading(state) {
  return get(selectTargetScreeningSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectTargetScreeningSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectTargetScreeningSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectTargetScreeningSlice(state), 'saveError', null);
}
