/* eslint-disable no-plusplus */
import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import noop from 'lodash/noop';
import Button from '../Button';
import Checkbox from '../Checkbox';
import Table from '../Table';
import AttributeCell from './Cells/AttributeCell';
import WarningCell from './Cells/WarningCell';
import { ENTITIES, selectors } from '../../store/ducks/options';
import { findByValue } from '../../helpers/find';
import { TableHeader, TableBody, VerifiedBy } from './styles';

export default function DuplicatesList(props) {
  const {
    values,
    data,
    handleChange,
    setCurrentUserId,
    submitForm,
    isSubmitting,
  } = props;

  const { verified } = data;

  const columns = [
    {
      Header: '',
      accessor: 'warning',
      key: 'warning',
      width: 20,
      disableSortBy: true,
      Cell: WarningCell,
      verified,
    },
    {
      Header: '',
      accessor: 'attribute',
      key: 'attribute',
      width: 240,
      disableSortBy: true,
      Cell: AttributeCell,
    },
    {
      id: 'expander',
      key: 'expander',
      accessor: 'expander',
      Header: () => null,
      disableSortBy: true,
    },
  ];

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const userId = get(data, 'userId', '-') || '-';
  const fullName = get(data, 'fullName', '-') || '-';
  const address = get(data, 'address', '-') || '-';
  const registrationIp = get(data, 'registrationIp', '-') || '-';
  const photoId = get(data, ['photo', 'id'], '-') || '-';
  const facebookAccount = get(data, 'facebookAccount', '#');
  const twitterAccount = get(data, 'twitterAccount', '#');
  const instagramAccount = get(data, 'instagramAccount', '#');
  const country = get(data, 'selfReportedCountry', '-') || '-';

  const emails = get(data, 'emails', [])
    .map((email) => `${email.full} (${email.stripped})`)
    .join(', ');
  const sessionIps = get(data, 'dynamicIps', [])
    .map((ip) => ip.key)
    .join(', ');
  const phones = get(data, 'phones', [])
    .map((phone) => phone.value)
    .join(', ');

  const selfReportedCountry =
    useMemo(() => get(findByValue(countries, country), 'label', ''), [
      countries,
      country,
    ]) || '-';

  const arrayMatch = (arr1, arr2) => {
    const arr = [];
    for (let i = 0; i < arr1.length; ++i) {
      for (let j = 0; j < arr2.length; ++j) {
        if (arr1[i] === arr2[j]) {
          arr.push(arr1[i]);
        }
      }
    }

    return arr;
  };

  const matchedAttribute = (attribute) => {
    const matchedAttributes = get(data, 'matchedAttributes', []);

    if (Array.isArray(attribute)) {
      const matched = arrayMatch(attribute, matchedAttributes);
      return matched > 0;
    }

    const matched = matchedAttributes.filter((attr) =>
      attr.attribute.includes(attribute),
    );

    return matched.length > 0;
  };

  const attributes = [
    {
      warning: matchedAttribute('full_name'),
      attribute: `Full name: ${fullName}`,
    },
    {
      warning: matchedAttribute('email'),
      attribute: `Email: ${emails || '-'}`,
    },
    {
      warning: matchedAttribute('phone'),
      attribute: `Mobile phone: ${phones || '-'}`,
    },
    {
      warning: matchedAttribute('address'),
      attribute: `Address: ${address}`,
    },
    {
      warning: matchedAttribute('country'),
      attribute: `Country: ${selfReportedCountry}`,
    },
    {
      warning: matchedAttribute('registration_ip'),
      attribute: `Registration IP: ${registrationIp}`,
    },
    {
      warning: matchedAttribute('dynamic_ips'),
      metadata: `User Session IPs: ${sessionIps || '-'}`,
    },
    {
      warning: matchedAttribute('photo'),
      attribute: `Photo ID: ${photoId}`,
    },
    {
      warning: matchedAttribute([
        'facebook_account',
        'twitter_account',
        'instagram_account',
      ]),
      attribute: 'Social media profile:',
      links: [
        { name: 'facebook', url: facebookAccount },
        { name: 'twitter', url: twitterAccount },
        { name: 'instagram', url: instagramAccount },
      ],
    },
    // TODO: PENDING FROM API
    // {
    //   warning: false,
    //   attribute: 'Documents:',
    //   links: [
    //     { name: 'Resume', url: 'https://appen.com' },
    //     { name: 'User ID', url: 'http://appen.com' },
    //   ],
    // },
  ];

  const userAttributes = attributes.map((attribute) => ({
    ...attribute,
    verified,
    userId,
    setCurrentUserId,
  }));

  return (
    <div>
      <TableHeader>Flagged Attributes</TableHeader>
      <TableBody>
        <Table
          columns={columns}
          data={userAttributes}
          renderExpandedComponent={noop}
        />
      </TableBody>
      {verified ? (
        <VerifiedBy>Verified by: Information not available</VerifiedBy>
      ) : (
        <>
          <div style={{ fontSize: 13 }}>
            <Checkbox
              name="isFalsePositive"
              onChange={handleChange}
              checked={values.isFalsePositive}
              bold={false}
              label={'This user is not a duplicate'}
            />
          </div>
          <Button
            onClick={submitForm}
            type="secondary"
            disabled={!values.isFalsePositive}
            loading={isSubmitting}
          >
            Save Changes
          </Button>
        </>
      )}
    </div>
  );
}

DuplicatesList.propTypes = {
  values: PropTypes.shape({
    isFalsePositive: PropTypes.bool,
  }).isRequired,
  data: PropTypes.shape({
    verified: PropTypes.bool.isRequired,
  }).isRequired,
  handleChange: PropTypes.func.isRequired,
  setCurrentUserId: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
};
