import { call, put, takeLatest } from 'redux-saga/effects';
import {
  deleteAutoScreening,
  deleteAutoScreeningSuccess,
  deleteAutoScreeningError,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* deleteAutoScreeningSagaFn(action) {
  const {
    payload: { changes: payload, thenPut },
  } = action;

  const { id: autoScreenId, projectId } = payload;

  try {
    const endpoint = ENDPOINTS.V2.AUTO_SCREENING({
      pathVariables: {
        autoScreenId,
        projectId,
      },
    });

    const response = yield call(axios.delete, endpoint, payload);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    yield put(deleteAutoScreeningSuccess({ ...response, ...payload }));
  } catch (error) {
    yield put(deleteAutoScreeningError(error));
  }
}

export default takeLatest(deleteAutoScreening.type, deleteAutoScreeningSagaFn);
