import { call, put, takeLatest } from 'redux-saga/effects';
import {
  deleteDataConsentForm,
  deleteDataConsentFormSuccess,
  deleteDataConsentFormError,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* deleteDataConsentFormSagaFn(action) {
  const {
    payload: {
      changes: { consentFormId: id },
      thenPut,
      closeModal,
    },
  } = action;

  try {
    const endpoint = ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM_DELETE({
      pathVariables: {
        id,
      },
    });
    const response = yield call(axios.delete, endpoint);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    if (typeof closeModal === 'function') {
      closeModal();
    }

    yield put(deleteDataConsentFormSuccess(response.data));
  } catch (error) {
    yield put(deleteDataConsentFormError(error));
  }
}

export default takeLatest(
  deleteDataConsentForm.type,
  deleteDataConsentFormSagaFn,
);
