import { useCallback, useEffect, useMemo, useState } from 'react';
import { useFormikContext } from 'formik';
import get from 'lodash/get';
import withHooks from 'hook-hoc';
import { useDispatch, useSelector } from 'react-redux';
import shortid from 'shortid';
import { compose } from 'recompose';

import mapToOptions from '../../pages/ProjectSetup/helpers/mapToOptions';
import { ENTITIES, fetch, selectors } from '../../store/ducks/options';
import { selectProject, selectProjectTools } from '../../store/ducks/project';
import {
  fetchExperimentGroups,
  selectAccessHolder,
} from '../../store/ducks/experimentGroups';

import UserGroupPicker from './UserGroupPicker';

function useHooksAsProps({ experimentId }) {
  const dispatch = useDispatch();

  const project = useSelector(selectProject);
  const projectId = project.id;
  const { customerId } = project;
  const { hasExternalSystem } = useSelector(selectProjectTools);
  const accessHolders = useSelector(selectAccessHolder);

  const accessHolder = (accessHolders || [{}])[0];

  const { values, setFieldValue } = useFormikContext();

  const allUserGroups = useSelector(
    selectors.createSelectSliceFor(ENTITIES.userGroups),
  );

  const { userGroups } = values;

  const [userGroupsByExperimentId, setUserGroupsByExperimentId] = useState([]);

  useEffect(() => {
    setUserGroupsByExperimentId(get(userGroups, experimentId, []));
  }, [experimentId, userGroups]);

  useEffect(() => {
    dispatch(fetch({ optionsFor: ENTITIES.userGroups, projectId }));
    dispatch(fetch(ENTITIES.accessHolderStatus));
    // dispatch(
    //   fetch({
    //     optionsFor: ENTITIES.experimentGroups,
    //     experimentId,
    //   }),
    // );
    dispatch(fetchExperimentGroups({ experimentId }));
  }, [dispatch, experimentId, projectId]);

  const handleChange = useCallback(
    (value) => {
      setFieldValue('userGroups', value);
    },
    [setFieldValue],
  );

  const handleAddLine = useCallback(() => {
    const updatedGroups = { ...userGroups };
    updatedGroups[experimentId].push({
      experimentId,
      groupId: null,
      disabled: false,
      key: shortid.generate(),
    });

    handleChange(updatedGroups);
  }, [userGroups, experimentId, handleChange]);

  const handleRemove = useCallback(
    (index) => {
      const groupUpdated = userGroups[experimentId]
        .map((group, key) => (key === index ? undefined : group))
        .filter((item) => item);

      const updatedGroups = { ...userGroups };
      updatedGroups[experimentId] = groupUpdated;

      handleChange(updatedGroups);
    },
    [experimentId, handleChange, userGroups],
  );

  const handleToggle = useCallback(
    (status, index) => {
      const { value } = status && status.target ? status.target : {};

      const groupUpdated = userGroups[experimentId].map((group, key) =>
        key === index
          ? {
              ...group,
              disabled: !!value,
            }
          : group,
      );

      const updatedGroups = { ...userGroups };
      updatedGroups[experimentId] = groupUpdated;

      handleChange(updatedGroups);
    },
    [experimentId, handleChange, userGroups],
  );

  const handleProjectChange = useCallback(
    ({ value: newValue }, index) => {
      const groupUpdated = userGroups[experimentId].map((group, key) =>
        key === index
          ? {
              ...group,
              groupId: newValue,
            }
          : group,
      );

      const updatedGroups = { ...userGroups };
      updatedGroups[experimentId] = groupUpdated;

      handleChange(updatedGroups);
    },
    [experimentId, handleChange, userGroups],
  );

  const selectedGroups = useMemo(
    () => userGroupsByExperimentId.map((group) => group.groupId),
    [userGroupsByExperimentId],
  );

  const projectOptions = useMemo(
    () => mapToOptions('id', 'name')(allUserGroups.data),
    [allUserGroups.data],
  );

  const notSelectedProjectOptions = projectOptions.filter(
    (p) => !selectedGroups.includes(p.value),
  );

  return {
    userGroups: userGroupsByExperimentId,
    accessHolder,
    handleProjectChange,
    handleToggle,
    handleRemove,
    handleAddLine,
    projectOptions,
    notSelectedProjectOptions,
    allUserGroups,
    hasExternalSystem,
    projectId,
    customerId,
  };
}

export default compose(withHooks(useHooksAsProps))(UserGroupPicker);
