import React from 'react';
import PropTypes from 'prop-types';
import { Text } from './styles';

export default function IdCell({ row }) {
  const {
    original: { id = '' },
  } = row;

  return <Text>{id}</Text>;
}

IdCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({ id: PropTypes.number }).isRequired,
  }).isRequired,
};
