import { call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction, addOne } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';
import normalizePayRatesParameters from './helpers/normalizePayRatesParameters';

function* createPayRateSagaFn(action) {
  const {
    payload,
    meta: { onSuccess, onError, createMany },
  } = action;

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can create a Pay Rate.');
    return;
  }

  try {
    const params = normalizePayRatesParameters(payload);

    const { data: payRate } = yield call(axios.post, ENDPOINTS.V2.PAY_RATES(), {
      ...params,
      projectId: payload.projectId || project.id,
    });

    yield put(
      updateOne(
        {
          id: payload.id,
          changes: {
            id: payRate.id,
            userGroup: payRate.groupId,
            userGroupId: payRate.groupId,
            taskType: payRate.taskType,
          },
        },
        { bypassUpdateSaga: true },
      ),
    );

    if (!createMany) {
      onSuccess();
    }
  } catch (error) {
    if (!createMany) {
      onError(
        error.message ||
          'There was an error creating this Pay Rate. Please try again.',
      );

      yield put(undoLastAction());
    }
  }
}

export default takeEvery(addOne.type, createPayRateSagaFn);
