import { call, put, takeEvery } from 'redux-saga/effects';
import { removeOne, undoLastAction } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* removePayRatesFn(action) {
  const {
    payload: id,
    meta: { onError },
  } = action;

  try {
    yield call(
      axios.delete,
      ENDPOINTS.V2.PAY_RATES({
        pathVariables: {
          id,
        },
      }),
    );
  } catch (error) {
    onError(
      error.message ||
        'There was an error deleting the Pay Rate. Please try again.',
    );

    yield put(undoLastAction());
  }
}

export default takeEvery(removeOne.type, removePayRatesFn);
