import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PayratesSetup from './PayratesSetup';
import * as payrates from '../../../../store/ducks/payrates';
import { selectAll as selectAllHiringTargets } from '../../../../store/ducks/hiringTargets';
import { findById, findByValue } from '../../../../helpers/find';
import { ENTITIES, selectors } from '../../../../store/ducks/options';
import { selectProject } from '../../../../store/ducks/project';

export { renderWordayTaskIdTooltip } from './PayratesSetup';

function mapStateToProps(state) {
  const project = selectProject(state);
  const projectPayrates = payrates.selectAllEager(state);
  const userGroups = selectors.selectDataFor(ENTITIES.userGroups, null, state);
  const taskTypes = selectors.selectDataFor(ENTITIES.taskTypes, null, state);

  return {
    payrates: projectPayrates.map((payrate) => {
      const userGroup = (
        findById(userGroups, payrate.userGroupId || payrate.userGroup) || {}
      ).name;

      const taskType = (findByValue(taskTypes, payrate.taskType) || {}).label;

      return {
        id: payrate.id,
        enabled: payrate.enabled,
        checked: payrate.checked,
        ...payrate,
        userGroup,
        taskType,
        userGroupId: payrate.userGroupId || payrate.userGroup,
        taskTypeValue: payrate.taskType,
      };
    }),
    hiringTargets: selectAllHiringTargets(state),
    customerId: project ? project.customerId : null,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    payratesActions: bindActionCreators(payrates, dispatch),
    dispatch,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(PayratesSetup);
