import React from 'react';
import PropTypes from 'prop-types';
import Button from '../../../../../Button';
import FloatContainer, { FloatRight, FloatLeft } from '../../../../../Float';
import { CurrentStepCount } from './styles';
import Divider from '../../../../../Divider/Divider';

export default function BottomControls(props) {
  const {
    currentStep,
    nextLabel,
    nextDisabled,
    nextLoading,
    onNext,
    backLabel,
    onBack,
  } = props;

  return (
    <>
      <Divider marginBottom={23} marginTop={currentStep === 2 ? 56 : 0} />
      <FloatContainer>
        <FloatLeft>
          <Button
            type="secondary"
            onClick={onBack}
            data-testid="demographics-modal--back-button"
          >
            {backLabel}
          </Button>
        </FloatLeft>
        <FloatRight>
          <CurrentStepCount>{currentStep} OF 2</CurrentStepCount>
          <Button
            onClick={onNext}
            disabled={nextDisabled}
            loading={nextLoading}
          >
            {nextLabel}
          </Button>
        </FloatRight>
      </FloatContainer>
    </>
  );
}

BottomControls.propTypes = {
  currentStep: PropTypes.number.isRequired,
  nextLabel: PropTypes.string.isRequired,
  onNext: PropTypes.func.isRequired,
  backLabel: PropTypes.string.isRequired,
  onBack: PropTypes.func.isRequired,
  nextDisabled: PropTypes.bool,
  nextLoading: PropTypes.bool,
};

BottomControls.defaultProps = {
  nextDisabled: false,
  nextLoading: false,
};
