import React from 'react';
import PropTypes from 'prop-types';
import { ReactComponent as Academy } from '../../../../../../assets/icons/mappings/academy.svg';
import { ReactComponent as Survey } from '../../../../../../assets/icons/mappings/survey.svg';
import { ReactComponent as Quiz } from '../../../../../../assets/icons/mappings/quiz.svg';
import { ReactComponent as Document } from '../../../../../../assets/icons/mappings/document.svg';
import { ReactComponent as Check } from '../../../../../../assets/icons/mappings/check.svg';
import {
  ResourceTypeCheck,
  ResourceTypeContainer,
  ResourceTypeItem,
  ResourceTypeName,
} from './styles';
import { ENTITY_METADATA } from '../../../../../../store/ducks/options';

export default function ResourceTypePicker(props) {
  const { setResourceType, resourceType, operation } = props;

  const handleClick = operation === 'create' ? setResourceType : () => {};

  const {
    quiz,
    survey,
    document,
    academy,
  } = ENTITY_METADATA.projectResources.types;

  return (
    <ResourceTypeContainer>
      <ResourceTypeItem
        isSelecter={resourceType === quiz}
        onClick={() => handleClick(quiz)}
      >
        {resourceType === quiz && (
          <ResourceTypeCheck>
            <Check />
          </ResourceTypeCheck>
        )}
        <Quiz />
        <ResourceTypeName>Quiz</ResourceTypeName>
      </ResourceTypeItem>

      <ResourceTypeItem
        isSelecter={resourceType === survey}
        onClick={() => handleClick(survey)}
      >
        {resourceType === survey && (
          <ResourceTypeCheck>
            <Check />
          </ResourceTypeCheck>
        )}
        <Survey />
        <ResourceTypeName>Survey</ResourceTypeName>
      </ResourceTypeItem>

      <ResourceTypeItem
        isSelecter={resourceType === academy}
        onClick={() => handleClick(academy)}
      >
        {resourceType === academy && (
          <ResourceTypeCheck>
            <Check />
          </ResourceTypeCheck>
        )}
        <Academy />
        <ResourceTypeName>Academy</ResourceTypeName>
      </ResourceTypeItem>

      <ResourceTypeItem
        isSelecter={resourceType === document}
        onClick={() => handleClick(document)}
      >
        {resourceType === document && (
          <ResourceTypeCheck>
            <Check />
          </ResourceTypeCheck>
        )}
        <Document />
        <ResourceTypeName>Document</ResourceTypeName>
      </ResourceTypeItem>
    </ResourceTypeContainer>
  );
}

ResourceTypePicker.propTypes = {
  setResourceType: PropTypes.func.isRequired,
  resourceType: PropTypes.oneOf(['quiz', 'survey', 'academy', 'document'])
    .isRequired,
  operation: PropTypes.string.isRequired,
};
