import React from 'react';
import PropTypes from 'prop-types';
import { RadialChart } from 'react-vis';
import { useTheme } from 'styled-components';
import { PROCESS_TYPE } from '../../../store/ducks/processBuilder';
import { Label } from '../../../styles';
import {
  FadedPercent,
  GraphContainer,
  GraphInfoContainer,
  GraphInnerCircle,
  GraphLabel,
  GraphLabels,
  PushCountRight,
  QualifiedNumber,
  QualifiedOf,
  StatusDistributionContainer,
  TotalLabel,
} from './styles';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip';
import Divider from '../../Divider';

function getPercent(count, total) {
  const percent = ((count || 0) / (total || 1)) * 100;

  return percent.toFixed(1);
}

export default function StatusDistribution(props) {
  const {
    processType = PROCESS_TYPE.REGISTRATION,
    total,
    registered,
    graphData,
    inProgress,
    rejected,
    abandoned,
  } = props;

  const theme = useTheme();

  const tooltipContent = (
    <GraphLabels inTooltip>
      <ul>
        <GraphLabel inTooltip dotColor={theme.colors.lightBlueGrey}>
          <PushCountRight>In Progress</PushCountRight>
          <strong>{inProgress}</strong>
          <FadedPercent>{`(${getPercent(inProgress, total)}%)`}</FadedPercent>
        </GraphLabel>

        <GraphLabel inTooltip dotColor={theme.colors.azure}>
          <PushCountRight>
            {processType === PROCESS_TYPE.REGISTRATION
              ? 'Registered'
              : 'Passed'}
          </PushCountRight>
          <strong>{registered}</strong>
          <FadedPercent>{`(${getPercent(registered, total)}%)`}</FadedPercent>
        </GraphLabel>

        <GraphLabel inTooltip dotColor={theme.colors.error}>
          <PushCountRight>Rejected</PushCountRight>
          <strong>{rejected}</strong>
          <FadedPercent>{`(${getPercent(rejected, total)}%)`}</FadedPercent>
        </GraphLabel>
        <GraphLabel inTooltip dotColor={theme.colors.warning}>
          <PushCountRight>Abandoned</PushCountRight>
          <strong>{abandoned}</strong>
          <FadedPercent>{`(${getPercent(abandoned, total)}%)`}</FadedPercent>
        </GraphLabel>
        <Divider />

        <TotalLabel inTooltip>
          <PushCountRight>Total</PushCountRight>
          <strong>{total}</strong>
          <FadedPercent>{`(100%)`}</FadedPercent>
        </TotalLabel>
      </ul>
    </GraphLabels>
  );

  return (
    <StatusDistributionContainer>
      <Label>
        {processType === PROCESS_TYPE.REGISTRATION ? 'Registered' : 'Qualified'}
      </Label>
      <QualifiedNumber>
        {total === 0 ? (
          '0'
        ) : (
          <>
            {registered} <QualifiedOf>{`(of ${total})`}</QualifiedOf>
          </>
        )}
      </QualifiedNumber>

      <GraphInfoContainer>
        <GraphLabels>
          <ul>
            <GraphLabel dotColor={theme.colors.lightBlueGrey}>
              In Progress
            </GraphLabel>

            <GraphLabel dotColor={theme.colors.azure}>
              {processType === PROCESS_TYPE.REGISTRATION
                ? 'Registered'
                : 'Passed'}
            </GraphLabel>

            <GraphLabel dotColor={theme.colors.error}>Rejected</GraphLabel>
            <GraphLabel dotColor={theme.colors.warning}>Abandoned</GraphLabel>
          </ul>
        </GraphLabels>

        <InformationTooltip title={tooltipContent}>
          <GraphContainer>
            <GraphInnerCircle />
            <RadialChart
              colorType="literal"
              width={140}
              height={140}
              data={graphData}
            />
          </GraphContainer>
        </InformationTooltip>
      </GraphInfoContainer>
    </StatusDistributionContainer>
  );
}

StatusDistribution.propTypes = {
  /* eslint-disable react/no-unused-prop-types */
  processType: PropTypes.oneOf(Object.values(PROCESS_TYPE)).isRequired,
  total: PropTypes.number.isRequired,
  inProgress: PropTypes.number.isRequired,
  registered: PropTypes.number.isRequired,
  rejected: PropTypes.number.isRequired,
  abandoned: PropTypes.number.isRequired,
  graphData: PropTypes.arrayOf(
    PropTypes.shape({ angle: PropTypes.number, color: PropTypes.string })
      .isRequired,
  ).isRequired,
};
