import { isEmpty } from 'lodash';
import { createSlice, createSelector } from '@reduxjs/toolkit';
import { v4 as uuidv4 } from 'uuid';

import { productiveMetricsMap } from '../../../pages/ProjectView/Tabs/MetricsAndGoals/constants';

const metricsAndGoalsSlice = createSlice({
  name: 'metricsAndGoals',
  initialState: {
    loading: false,
    saving: false,
    fatalError: false,
    metricsChangelogs: {},
  },
  reducers: {
    getMetricsAndGoals(state) {
      return { ...state, loading: true, saving: false, fatalError: false };
    },
    saveMetricsAndGoals(state) {
      return {
        ...state,
        success: false,
        error: false,
        saving: true,
      };
    },
    validateProductivity(state) {
      const { productivityMetrics } = state;
      let updatedMetricList = [];
      updatedMetricList = productivityMetrics.map((metric) => {
        if (metric.disabled) return null;
        // we have at least PROJECTs and WORKERs goals
        if (metric.productivityGoals.length < 2) {
          return { ...metric, hasError: true };
        }
        return { ...metric, hasError: false };
      });

      // remove null itens
      updatedMetricList = updatedMetricList.filter((i) => i !== null);

      return {
        ...state,
        productivityMetrics: updatedMetricList,
      };
    },
    setActiveMetric(state, action) {
      const { metric, disabled } = action.payload;
      const { productivityMetrics } = state;
      const { metricType } = metric;
      // force open panel in case of enabled
      let updatedMetricList = [];
      let isCollapsed = true;
      if (!disabled) {
        isCollapsed = false;
      }

      const existentMetric = productivityMetrics.find(
        (i) => i.metricType === metricType,
      );

      if (existentMetric) {
        updatedMetricList = productivityMetrics.map((i) => {
          if (i.metricType === existentMetric.metricType) {
            return { ...existentMetric, disabled, isCollapsed };
          }
          return i;
        });
      } else {
        updatedMetricList = productivityMetrics.map((i) => i);
        updatedMetricList.push({ ...metric, disabled, isCollapsed });
      }

      return {
        ...state,
        productivityMetrics: updatedMetricList,
      };
    },
    setWeekStartDate(state, action) {
      const { weekStartDate } = action.payload;
      return { ...state, weekStartDate };
    },
    setMarketAggregation(state, action) {
      const { marketAggregation } = action.payload;
      return { ...state, marketAggregation };
    },
    setMetricsAndGoalsFlat(state, action) {
      const data = action.payload;

      return { ...state, ...data };
    },
    setMetricsAndGoals(state, action) {
      const { data, rateType } = action.payload;

      if (data.qualityMetrics) {
        data.qualityMetrics = data.qualityMetrics.map((el) => {
          const key = el.key || uuidv4();

          return {
            ...el,
            key,
            isCollapsed: el.disabled,
            required: false,
          };
        });
      } else {
        data.qualityMetrics = [];
      }

      data.productivityMetrics = productiveMetricsMap(rateType).map(
        (metric) => {
          let metricValue = { ...metric };
          if (!isEmpty(data.productivityMetrics)) {
            const el = data.productivityMetrics.find(
              (i) => i.metricType === metric.metricType,
            );
            if (el) {
              metricValue = { ...el };
            }
          }

          const key = metricValue.key || uuidv4();

          return {
            ...metric,
            ...metricValue,
            key,
            isCollapsed: metricValue.disabled,
          };
        },
      );

      return {
        ...state,
        ...data,
        originalAggregation: data.marketAggregation,
        success: null,
        error: null,
        loading: false,
        rateType,
      };
    },
    setMetricsAndGoalsSucess(state, action) {
      const { success } = action.payload;
      return { ...state, success, error: false, saving: false };
    },
    setMetricsAndGoalsError(state, action) {
      const { error, fatalError } = action.payload;
      return { ...state, error, fatalError, success: false, saving: false };
    },
    setProductivityMetric(state, action) {
      const { productivityMetrics } = state;
      const { productivityMetric } = action.payload;
      let metrics = [];

      // Has configured metrics
      if (productivityMetrics && productivityMetrics.length > 0) {
        metrics = productivityMetrics.map((metric) => {
          if (metric.key === productivityMetric.metricKey) {
            return { ...metric, ...productivityMetric };
          }
          return metric;
        });
      } else {
        // Add first metric to productivityMetrics list
        metrics.push(productivityMetric);
      }

      return { ...state, productivityMetrics: metrics };
    },
    setQualityMainMetric(state, action) {
      const { qualityMetrics } = state;
      const { metric } = action.payload;
      let metrics = qualityMetrics;
      // Has configured metrics
      if (qualityMetrics && qualityMetrics.length > 0) {
        metrics = qualityMetrics.map((m) => {
          if (metric.key === m.key) {
            return { ...metric, primary: true };
          }

          return { ...m, primary: false };
        });
      }

      return { ...state, qualityMetrics: metrics };
    },
    setQualityMetric(state, action) {
      const { qualityMetrics } = state;
      const { qualityMetric } = action.payload;

      let metrics = [];

      if (qualityMetric.metric === 'QUIZ_BASED') {
        qualityMetric.quizBased = true;
      }

      // Has configured metrics
      if (isEmpty(qualityMetrics)) {
        qualityMetric.primary = true;
        metrics.push(qualityMetric);
      } else {
        metrics = qualityMetrics.map((metric) => {
          if (metric.key === qualityMetric.key) {
            return { ...metric, ...qualityMetric };
          }
          return metric;
        });

        if (metrics.find((m) => m.key === qualityMetric.key) === undefined) {
          metrics.push(qualityMetric);
        }
      }

      return { ...state, qualityMetrics: metrics };
    },
    getMetricChangelogs(state) {
      return {
        ...state,
        metricChangelogsError: false,
      };
    },
    setMetricChangelogs(state, action) {
      const data = action.payload;

      return {
        ...state,
        metricsChangelogs: {
          ...state.metricsChangelogs,
          ...data,
        },
      };
    },
  },
});

// Exporting actions
export const {
  setActiveMetric,
  saveMetricsAndGoals,
  getMetricsAndGoals,
  setWeekStartDate,
  setMetricsAndGoals,
  setMetricsAndGoalsSucess,
  setMetricsAndGoalsError,
  setMarketAggregation,
  setMetricsAndGoalsFlat,
  setProductivityMetric,
  validateProductivity,
  setQualityMainMetric,
  setQualityMetric,
  getMetricChangelogs,
  setMetricChangelogs,
} = metricsAndGoalsSlice.actions;

const { name, reducer } = metricsAndGoalsSlice;
reducer.key = name;
export default reducer;

export function selectMetricsAndGoalsState(state) {
  return state[reducer.key];
}

export const selectMetricsAndGoals = createSelector(
  selectMetricsAndGoalsState,
  (metricsState) => metricsState || null,
);

export const selectProductivityMetrics = createSelector(
  selectMetricsAndGoalsState,
  (metricsState) => metricsState.productivityMetrics || null,
);

export const selectMarketAggregation = createSelector(
  selectMetricsAndGoalsState,
  (metricsState) => metricsState.marketAggregation || null,
);

export const selectMetricByMetricType = (metricType) => {
  return createSelector(selectMetricsAndGoalsState, (metricsState) => {
    const { productivityMetrics } = metricsState;
    if (!productivityMetrics) return null;
    return productivityMetrics.find((i) => i.metricType === metricType);
  });
};
