import { select, put, call, takeEvery } from 'redux-saga/effects';
import {
  selectProcessType,
  PROCESS_TYPE,
  setStepDefinitionAttributes,
  setModulesError,
  loadStepById,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

export function* fetchStepDefinitionFn(action) {
  const { stepDefinitionID } = action.payload;
  const processType = yield select(selectProcessType);

  if (!processType) {
    if (process.env.NODE_ENV === 'development') {
      // eslint-disable-next-line no-console
      console.warn('No processType provided to fetchModulesSaga');
    }
    return;
  }

  const endpoint =
    processType === PROCESS_TYPE.REGISTRATION
      ? endpoints.V2.REGISTRATION_PROCESS_STEP_DEFINITION({
          pathVariables: {
            id: stepDefinitionID,
          },
        })
      : endpoints.V2.QUALIFICATION_PROCESS_STEP_DEFINITION({
          pathVariables: {
            id: stepDefinitionID,
          },
        });

  try {
    const response = yield call(axios.get, endpoint);

    yield put(setStepDefinitionAttributes(response.data));
  } catch (error) {
    yield put(setModulesError(error));
  }
}

export default takeEvery(loadStepById.type, fetchStepDefinitionFn);
