import React from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import classNames from './styles.module.css';

export default function TableShimmer(props) {
  const { cols } = props;

  return (
    <div className={classNames.table_shimmer_container}>
      {new Array(cols || 4).fill(0).map(() => (
        <Shimmer className={classNames.table_shimmer} />
      ))}
    </div>
  );
}

TableShimmer.propTypes = {
  cols: PropTypes.number,
};

TableShimmer.defaultProps = {
  cols: 4,
};
