import styled from 'styled-components';
import { enforcePx } from '../../styles';

function sumAccessoryHeights(props) {
  const { headerHeight, footerHeight } = props;

  if (typeof headerHeight !== 'number' || typeof footerHeight !== 'number') {
    return 0;
  }

  return headerHeight + footerHeight;
}

// eslint-disable-next-line import/prefer-default-export
export const EditorContainer = styled.div`
  position: relative;

  .components__RichTextEditor__Toolbar {
    border: solid 1px ${(props) => props.theme.colors.lightPeriwinkle};
    border-radius: 4px;
  }

  .rdw-option-wrapper:hover,
  .rdw-dropdown-wrapper:hover {
    border: 1px solid ${(props) => props.theme.colors.lightBlueGrey};
    box-shadow: none;
  }

  .rdw-option-wrapper:active {
    border: 1px solid ${(props) => props.theme.colors.azure};
    box-shadow: none;
  }

  .rdw-option-active {
    border: 1px solid ${(props) => props.theme.colors.azure}!important;
    box-shadow: none;
  }

  .components__RichTextEditor__Editor {
    border: solid 1px
      ${(props) =>
        props.hasError
          ? props.theme.colors.error
          : props.theme.colors.lightPeriwinkle};
    padding: 16px;
    border-radius: 4px;
    padding-top: ${(props) =>
      enforcePx(props.headerHeight ? props.headerHeight + 10 : null, '0px')};
    padding-bottom: ${(props) =>
      enforcePx(props.footerHeight ? props.footerHeight - 6 : null, '0px')};
    min-height: ${(props) => enforcePx(sumAccessoryHeights(props) + 108)};
    cursor: text;
  }
`;

export const AccessoryHeaderContainer = styled.div`
  position: absolute;
  top: 100px;
  left: 0px;
  width: 100%;
  padding-left: 16px;
`;

export const AccessoryFooterContainer = styled.div`
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  padding-left: 16px;
  padding-bottom: 16px;
`;
