import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import Img from '../PayratesSetup/HiringTargetCell/styles';
import ArrowRightIcon from '../../../../assets/icons/icon-right-arrow.svg';
import { ENTITIES, selectors } from '../../../../store/ducks/options';
import { findByNestedValue, findByValue } from '../../../../helpers/find';

const UNITED_STATES_LONG_NAME = 'United States of America';

export default function HiringTargetOption(props) {
  const {
    restrictToCountry: countryId,
    localeLanguage: localeLanguageId,
    localeDialect: localeDialectId,
    localeLanguageTo: localeLanguageToId,
    localeDialectTo: localeDialectToId,
  } = props;

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const localeLanguages = useSelector(
    selectors.createSelectSliceFor(ENTITIES.localeLanguages),
  );

  const localeDialectForLanguage = useMemo(
    () => findByValue(countries.data, localeDialectId),
    [countries, localeDialectId],
  );

  const localeDialectForLanguageTo = useSelector(
    () => findByValue(countries.data, localeDialectToId),
    [countries, localeDialectToId],
  );

  const localeLanguage = useMemo(
    () => findByNestedValue(localeLanguages.data, localeLanguageId, 'options'),
    [localeLanguages, localeLanguageId],
  );

  const localeLanguageTo = useMemo(
    () =>
      findByNestedValue(localeLanguages.data, localeLanguageToId, 'options'),
    [localeLanguages, localeLanguageToId],
  );

  const restrictToCountry = useMemo(
    () => findByValue(countries.data, countryId),
    [countries, countryId],
  );

  const localeLanguageLabel = (localeLanguage || {}).label || '.';
  const localeLanguageToLabel = (localeLanguageTo || {}).label || '.';
  const localeDialectToLabel = (localeDialectForLanguageTo || {}).label || '';

  let restrictToCountryLabel = (restrictToCountry || {}).label || '.';

  if (restrictToCountryLabel === UNITED_STATES_LONG_NAME) {
    restrictToCountryLabel = 'U.S.A.';
  }

  let localeDialectLabel = (localeDialectForLanguage || {}).label || '';

  if (localeDialectLabel === UNITED_STATES_LONG_NAME) {
    localeDialectLabel = 'U.S.A.';
  }

  const isTranslationHT = localeLanguageToId && localeDialectToId;

  return (
    <>
      {isTranslationHT ? 'From ' : ''}
      {`${localeLanguageLabel} (${localeDialectLabel})`}
      {isTranslationHT
        ? `  to ${localeLanguageToLabel} (${localeDialectToLabel})`
        : ''}
      <Img src={ArrowRightIcon} alt="arrow right icon" />
      {`${restrictToCountryLabel}`}
    </>
  );
}

HiringTargetOption.propTypes = {
  restrictToCountry: PropTypes.string.isRequired,
  localeLanguage: PropTypes.string.isRequired,
  localeDialect: PropTypes.string.isRequired,
  localeLanguageTo: PropTypes.string,
  localeDialectTo: PropTypes.string,
};

HiringTargetOption.defaultProps = {
  localeLanguageTo: '',
  localeDialectTo: '',
};
