import { call, put, select, all, takeLatest } from 'redux-saga/effects';
import uniq from 'lodash/uniq';
import { selectProject } from '../project';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import * as hiringTargetDuck from '../hiringTargets';
import * as demographicRequirements from '../demographicRequirements';
import * as aggreationConverter from '../projectDemographicRequirements/aggregatedRequirementsConverter';
import * as byGroupConverter from '../projectDemographicRequirements/requirementsByGroupConverter';
import * as projectDemographicMetrics from '.';
import fetchEntity from '../../helpers/fetchEntity';

function* fetchDemographicsSaga(action) {
  const { payload: actionPayload = {} } = action;

  const project = yield select(selectProject);

  if (!project || !project.id) {
    yield put(
      projectDemographicMetrics.fetchDemographicsFailed(
        new Error('Failed to fetch Pay Rates, missing project id.'),
      ),
    );
    return;
  }

  yield put(
    projectDemographicMetrics.updateViewAs(
      project.hiringTargetDemographicsType || project.demographicsTargetType,
    ),
  );

  const { id: projectID } = project;

  try {
    const { data } = yield call(
      axios,
      actionPayload.loadCustomMetrics
        ? endpoints.V2.DEMOGRAPHICS_REQUIREMENTS_METRICS({
            query: {
              projectId: projectID,
              ...(actionPayload.loadGeneralMetrics
                ? { generalMetrics: true }
                : {}),
            },
          })
        : endpoints.V2.DEMOGRAPHICS_REQUIREMENTS_METRICS({
            query: {
              projectId: projectID,
              generalMetrics: true,
            },
          }),
    );

    if (actionPayload.loadGeneralMetrics) {
      yield put(projectDemographicMetrics.updateViewGeneralMetrics(true));
    }

    const [requirements, hiringTargets] = yield all([
      call(
        fetchEntity,
        demographicRequirements.fetchAll,
        demographicRequirements.setAll,
        demographicRequirements.fetchFailed,
        demographicRequirements.selectAll,
      ),
      call(
        fetchEntity,
        () => hiringTargetDuck.fetchAll({ projectId: projectID }),
        hiringTargetDuck.setAll,
        hiringTargetDuck.fetchFailed,
        hiringTargetDuck.selectAll,
      ),
    ]);

    let projectRequirements = [];

    if (data.length) {
      if (data[0] && data[0].group) {
        const payload = byGroupConverter.convertFromDTO(
          data,
          requirements,
          !!actionPayload.loadCustomMetrics ||
            !!actionPayload.loadGeneralMetrics,
        );

        payload.map((p) => projectRequirements.push(p.requirementID));
        projectRequirements = uniq(projectRequirements);

        yield put(
          projectDemographicMetrics.setRequirementsByGroup(payload, {
            bypassUpdateSaga: true,
          }),
        );
      } else if (data[0] && data[0].aggregation) {
        const payload = aggreationConverter.convertFromDTO(
          data,
          requirements,
          hiringTargets,
          !!actionPayload.loadCustomMetrics,
        );

        projectRequirements = aggreationConverter.getRequirements(
          data[0].aggregation[0],
          requirements,
        );

        yield put(
          projectDemographicMetrics.setAggregatedRequirements(payload, {
            bypassUpdateSaga: true,
          }),
        );
      }
    }

    yield put(
      projectDemographicMetrics.setMetricsType(
        actionPayload.loadCustomMetrics
          ? projectDemographicMetrics.DEMOGRAPHIC_METRICS_TYPE.CUSTOM
          : projectDemographicMetrics.DEMOGRAPHIC_METRICS_TYPE.GENERAL,
      ),
    );

    if (actionPayload.loadCustomMetrics && data.length) {
      yield put(projectDemographicMetrics.setHasCustomMetrics(true));
    }

    // transform response into local state
    yield put(
      projectDemographicMetrics.fetchDemographicsSuccess(
        projectRequirements
          .map((pr) =>
            typeof pr.id !== 'undefined'
              ? pr
              : requirements.find((r) => r.id === pr),
          )
          .map((r) => ({
            value: r.id,
            label: r.name,
          })),
      ),
    );
  } catch (error) {
    yield put(projectDemographicMetrics.fetchDemographicsFailed(error));
  }
}

export default takeLatest(
  projectDemographicMetrics.fetchDemographics.type,
  fetchDemographicsSaga,
);
