import React from 'react';
import get from 'lodash/get';
import ExpandedCell from './ExpandedCell';
import { CellPropTypes } from './helpers';

export default function CustomerCell({ row }) {
  const {
    isExpanded,
    original: { projectType, customer },
  } = row;

  return (
    <>
      {get(customer, 'name', '')}
      <ExpandedCell isExpanded={isExpanded} label="Type of Project">
        {projectType}
      </ExpandedCell>
    </>
  );
}

CustomerCell.propTypes = CellPropTypes;
