import React, { useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import shortId from 'shortid';
import { useDispatch } from 'react-redux';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import { themePropTypes } from '../../../../styles/themes/default';
import * as teamTypes from '../../../../store/ducks/teamTypes';
import useFirstChange from '../../helpers/useFirstChange';
import { setDirty } from '../../../../store/ducks/projectSetup';
import StepFooter from '../../../../components/StepFooter';
import ProjectSupportTeamForm from '../../../../components/ProjectForms/ProjectSupportTeamForm';
import { ENTITIES, fetch } from '../../../../store/ducks/options';
import { useNavigationContext } from '../../helpers/navigationContext';

const defaultValue = [{ key: shortId.generate(), role: null, member: null }];

export default function SupportTeam(props) {
  const {
    setFieldValue,
    values: { supportTeam = defaultValue },
    submitForm,
    theme,
  } = props;

  function submitAndNavigate() {
    setFieldValue('navigate', true);
    submitForm();
  }

  const { goBack } = useNavigationContext();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(teamTypes.fetchAll());
    dispatch(fetch(ENTITIES.internalUsers));
  }, [dispatch]);

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    supportTeam,
  );

  return (
    <div>
      <WizardTitle>Support Team</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <ProjectSupportTeamForm theme={theme} />

      <WizardFooter>
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: Preview"
        />
      </WizardFooter>
    </div>
  );
}

SupportTeam.propTypes = {
  submitForm: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  theme: themePropTypes.isRequired,
  values: PropTypes.shape({
    supportTeam: PropTypes.arrayOf(
      PropTypes.shape({
        key: PropTypes.string.isRequired,
        role: PropTypes.string,
        member: PropTypes.number,
      }),
    ),
  }).isRequired,
};
