import styled, { keyframes } from 'styled-components';

const dotKeyframes = keyframes`
  0% {
    opacity: 0.4;
    transform: scale(1, 1);
  }

  50% {
    opacity: 1;
    transform: scale(1.2, 1.2);
  }

  100% {
    opacity: 0.4;
    transform: scale(1, 1);
  }
`;

export const LoadingText = styled.p`
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  color: ${(props) => props.theme.colors.lightGreyBlue};
  margin-bottom: 14px;
`;

export const Dots = styled.div`
  width: 100%;
`;

const backgroundColor = (props) => {
  const { dotType } = props;
  if (dotType === 'primary') {
    return props.theme.colors.white;
  }
  if (dotType === 'secondary') {
    return props.theme.colors.azure;
  }
  if (dotType === 'tertiary') {
    return props.theme.colors.blueGrey;
  }
  return props.theme.colors.blueGrey;
};

export const Dot = styled.div`
  animation: ${dotKeyframes} 1.5s infinite ease-in-out;
  background-color: ${backgroundColor};
  border-radius: 10px;
  display: inline-block;
  height: 8px;
  width: 8px;

  &:nth-child(2) {
    animation-delay: 0.5s;
    margin: 0 8px;
  }

  &:nth-child(3) {
    animation-delay: 1s;
  }
`;
