import React from 'react';
import PropTypes from 'prop-types';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../../Popover';
// import copy from '../../../../../assets/icons/copy.svg';
import pencil from '../../../../../assets/icons/pencil.svg';
import close from '../../../../../assets/icons/close.svg';
import { Container } from './styles';
import SettingsHoverable from '../../../../Icons/SettingsHoverable';

const actions = {
  // { src: copy, alt: 'copy locale tenant', text: 'Copy' },
  edit: { src: pencil, alt: 'edit locale tenant', text: 'Edit' },
  delete: { src: close, alt: 'delete locale tenant', text: 'Delete' },
};

export default function SettingsCell(props) {
  const {
    row: {
      values: { id },
    },
    initialState: { editLocaleTenant, deleteLocaleTenant, canDelete },
  } = props;

  const canBeDeleted = canDelete(id);

  const tooltipContent = (
    <>
      <PopoverItem
        onClick={() => editLocaleTenant(id)}
        onKeyDown={({ keyCode }) => keyCode === 13 && editLocaleTenant(id)}
        role="button"
        tabIndex={0}
        data-testid={`project-setup--recruitment--locale-tenants-edit-${id}`}
      >
        <PopoverIcon src={actions.edit.src} alt={actions.edit.alt} />
        <PopoverText>{actions.edit.text}</PopoverText>
      </PopoverItem>
      <PopoverItem
        onClick={() => canDelete(id) && deleteLocaleTenant(id)}
        onKeyDown={({ keyCode }) => keyCode === 13 && deleteLocaleTenant(id)}
        role="button"
        tabIndex={canBeDeleted ? 0 : -1}
        aria-disabled={!canBeDeleted}
        data-testid={`project-setup--recruitment--locale-tenants-delete-${id}`}
        isDisabled={!canBeDeleted}
        title={
          canBeDeleted
            ? ''
            : "You can't delete this Locale Tenant as a Hiring Target depends on it."
        }
      >
        <PopoverIcon src={actions.delete.src} alt={actions.delete.alt} />
        <PopoverText>{actions.delete.text}</PopoverText>
      </PopoverItem>
    </>
  );

  return (
    <Container
      className="settings-icon-container"
      data-testid={`project-setup--recruitment--locale-tenants-actions-${id}`}
    >
      <Popover content={tooltipContent} placement="bottom-end">
        <SettingsHoverable />
      </Popover>
    </Container>
  );
}

SettingsCell.propTypes = {
  row: PropTypes.shape({
    values: PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  }).isRequired,
  initialState: PropTypes.shape({
    // copyLocaleTenant: PropTypes.func.isRequired,
    editLocaleTenant: PropTypes.func.isRequired,
    deleteLocaleTenant: PropTypes.func.isRequired,
    canDelete: PropTypes.func.isRequired,
  }).isRequired,
};
