import { call, put, takeEvery } from 'redux-saga/effects';
import get from 'lodash/get';
import {
  fetchUserScreeningDetails,
  fetchUserScreeningDetailsSuccess,
  fetchUserScreeningDetailsError,
  setLoading,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import { userSvcAPI } from '../../../services/axios';

export function* fetchUserScreeningDetailsFn(action) {
  const { payload } = action;
  const userId = get(payload, 'userId', null);

  if (!userId) {
    return;
  }

  yield put(setLoading({ userId, status: true }));

  try {
    const { data: userScreeningDetails } = yield call(
      userSvcAPI.get,
      ENDPOINTS.USER_SCREENING.USERS({
        pathVariables: {
          id: userId,
        },
      }),
    );

    if (userScreeningDetails) {
      yield put(
        fetchUserScreeningDetailsSuccess({ ...userScreeningDetails, userId }),
      );
    }
  } catch (error) {
    yield put(fetchUserScreeningDetailsError({ ...error, userId }));
  } finally {
    yield put(setLoading({ userId, status: false }));
  }
}

export default takeEvery(
  fetchUserScreeningDetails.type,
  fetchUserScreeningDetailsFn,
);
