import React, { useCallback, useContext } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { navigate, selectCurrentStep } from '../../../store/ducks/projectSetup';
import { lockLeave } from '../../../store/ducks/pageLeave';

const NavigationContext = React.createContext({
  navigate: () => {},
  preventLeave: () => {},
  goBack: () => {},
  currentStep: 0,
});

const { Provider, Consumer } = NavigationContext;

export default function NavigationProvider({ children }) {
  const dispatch = useDispatch();

  const navigateFn = useCallback(
    (toStep = 0) => {
      dispatch(navigate(toStep));
    },
    [dispatch],
  );

  const preventLeaveFn = useCallback(
    (prevent = true) => {
      dispatch(lockLeave(prevent));
    },
    [dispatch],
  );

  const currentStep = useSelector(selectCurrentStep);

  const goBack = useCallback(() => {
    navigateFn(currentStep - 1);
  }, [currentStep, navigateFn]);

  return (
    <Provider
      value={{
        navigate: navigateFn,
        preventLeave: preventLeaveFn,
        goBack,
        currentStep,
      }}
    >
      {children}
    </Provider>
  );
}

export function useNavigationContext() {
  return useContext(NavigationContext);
}

export const NavigationConsumer = Consumer;

NavigationProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
