/* eslint-disable no-param-reassign */
import { createSlice, createSelector } from '@reduxjs/toolkit';
import { takeLeading, put, call, select } from 'redux-saga/effects';
import find from 'lodash/find';
import axios from '../../../services/axios';
import ENDPOINTS from '../../../constants/endpoints';
import * as hiringTargets from '../hiringTargets';
import * as localeTenants from '../localeTenants';

function getUrl(state, action) {
  const {
    payload: { hiringTarget, businessUnit, rateType },
  } = action;

  const { localeDialect } = hiringTargets.selectById(state, hiringTarget) || {};
  const localeTenant = localeTenants.selectByCountry(state)(localeDialect);

  return ENDPOINTS.V2.DEFAULT_PAYRATES({
    query: {
      country: localeDialect,
      businessUnit,
      rateType,
      ...(localeTenant ? { tenantId: localeTenant.tenant } : {}),
    },
  });
}

const slice = createSlice({
  name: 'defaultPayrates',

  initialState: {
    data: {},
    loading: false,
    error: null,
  },

  reducers: {
    fetchAll: (
      state /* , action = { payload: { hiringTarget, rateType, businessUnit } } */,
    ) => ({
      ...state,
      loading: true,
    }),

    setAll: (state, action) => {
      const {
        payload: { data, hiringTarget },
      } = action;

      state.loading = false;
      state.data[hiringTarget] = data.map((dpr) => ({
        ...dpr,
        hiringTarget,
        id: dpr.countryPayRateId,
        rate: dpr.rateValue,
        writtenFluency: 'NATIVE_OR_BILINGUAL',
        spokenFluency: 'NATIVE_OR_BILINGUAL',
      }));
    },

    fetchFailed: (state, action) => ({
      ...state,
      loading: false,
      error: action.payload,
    }),
  },
});

const {
  actions: { fetchAll, fetchFailed, setAll },
  reducer,
} = slice;

function* defaultPayratesSagaFn(action) {
  const {
    payload: { hiringTarget, rateType, businessUnit },
  } = action;

  const payrates = yield select(
    (state) => state.defaultPayrates.data[hiringTarget],
  );

  const paramsChanged = !find(payrates || [], { rateType, businessUnit });

  const state = yield select();

  if (!paramsChanged && payrates) {
    yield put(setAll({ hiringTarget, data: payrates }));

    return;
  }

  try {
    const response = yield call(axios, getUrl(state, action));

    yield put(setAll({ hiringTarget, data: response.data }));
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

reducer.key = 'defaultPayrates';

export default reducer;

export { fetchAll, fetchFailed, setAll };

export const defaultPayratesSaga = takeLeading(
  fetchAll.type,
  defaultPayratesSagaFn,
);

defaultPayratesSaga.fn = defaultPayratesSagaFn;

function selectDefaulyPayrates(state) {
  return state[reducer.key];
}

export function selectAll(state) {
  const defaultPayrates = selectDefaulyPayrates(state);

  return [].concat(
    ...Object.keys(defaultPayrates.data).map(
      (key) => defaultPayrates.data[key],
    ),
  );
}

export const selectLocaleDialects = createSelector(selectDefaulyPayrates);

export function createSelectByCountry(country) {
  return (state) => selectDefaulyPayrates(state).data[country] || [];
}

export function selectLoading(state) {
  return selectDefaulyPayrates(state).loading;
}

export function selectError(state) {
  return selectDefaulyPayrates(state).error;
}

export function selectByHiringTarget(hiringTarget) {
  return (state) => selectDefaulyPayrates(state).data[hiringTarget] || [];
}

export function selectById(state, id) {
  return selectAll(state).find((defaultPayrate) => defaultPayrate.id === id);
}

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.defaultPayrates = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
