import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';
import ProjectUserProjectPagePreview from '../../../../components/ProjectPreviews/ProjectUserProjectPagePreview';
import * as projectPages from '../../../../store/ducks/projectPages';
import { CardContentContainer } from '../../styles';
import UserProjectPageForm from './UserProjectPageForm';
import PostMessageLink from '../../../../components/PostMessageLink';
import { selectProject } from '../../../../store/ducks/project';
import Disabled from '../../../../components/Disabled/Disabled';

export default function UserProjectPage(props) {
  const { mode, defaultPageTask, hasSkillReview, isACANProject } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(projectPages.fetchAll());
  }, [dispatch]);

  const project = useSelector(selectProject);

  const userProjectPageUrl = useMemo(() => {
    if (project) {
      return `/qrp/core/vendors/project_site/${project.id}/${
        project.defaultPageId || ''
      }`;
    }

    return '';
  }, [project]);

  if (mode === 'edit') {
    return <UserProjectPageForm />;
  }

  return (
    <>
      <CardContentContainer>
        <Row>
          <Col col={8}>
            <ProjectUserProjectPagePreview
              defaultPageTask={defaultPageTask}
              hasSkillReview={hasSkillReview}
              isACANProject={isACANProject}
              renderedIn="project-view"
            />
          </Col>
        </Row>
        Go to{' '}
        <Disabled
          when={!project.defaultPageId}
          message="No default project page was set"
          focusOffset={1}
          inLine
        >
          <PostMessageLink
            to={userProjectPageUrl}
            disabled={!project.defaultPageId}
          >
            User Project Page
          </PostMessageLink>
        </Disabled>
      </CardContentContainer>
    </>
  );
}

UserProjectPage.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  defaultPageTask: PropTypes.string,
  hasSkillReview: PropTypes.bool,
  isACANProject: PropTypes.bool,
};

UserProjectPage.defaultProps = {
  defaultPageTask: '',
  hasSkillReview: false,
  isACANProject: false,
};
