import { call, put, select, takeEvery, all } from 'redux-saga/effects';
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import memoize from 'lodash/memoize';
import axios from '../../../services/axios';

export const MUTATION_METHODS = {
  DELETE: 'delete',
  PATCH: 'patch',
  POST: 'post',
  PUT: 'put',
};

function getCacheKey(stateKey, nestedKey) {
  if (nestedKey) {
    return `${stateKey}${nestedKey}`;
  }

  return `${stateKey}`;
}

export default function createEntitiesSlices(
  configs /*: { [key: string]: EntityConfig } */,
) {
  const reducerKey = 'editableEntities';
  const initialState = {};

  Object.entries(configs).forEach(([key, value]) => {
    if (process.env.NODE_ENV === 'development') {
      /* eslint-disable no-console */
      if (!value.url || typeof value.url !== 'function') {
        console.error(
          `Missing or wrong type for "url" for ${key} on editableEntities.
It should be a function (id, entity, reduxState) => string`,
        );
      }
    }

    initialState[key] = {
      open: false,
      operation: 'create',
      data: null,
      saving: false,
      saveError: null,
      deleting: false,
      deleteError: null,
    };
  });

  function getEntityConfig(key) {
    return configs[key] || {};
  }

  // Selecting
  function selectEditorSliceFor(stateKey, state) {
    const path = [reducerKey, stateKey];

    return get(state, path, {});
  }

  function selectEditorDataFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'data', {});
  }

  function selectEditorOpenFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'open', false);
  }

  function selectEditorOperationFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'operation', false);
  }

  function selectEditorSavingFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'saving', false);
  }

  function selectEditorSaveErrorFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'saveError', false);
  }

  function selectEditorDeletingFor(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'deleting', false);
  }

  function selectEditorDeleteError(stateKey, state) {
    return get(selectEditorSliceFor(stateKey, state), 'deleteError', null);
  }

  const createSelectEditorSliceFor = memoize(
    (stateKey) => (state) => selectEditorSliceFor(stateKey, state),
    getCacheKey,
  );

  const createSelectEditorDataFor = (stateKey) => (state) =>
    selectEditorDataFor(stateKey, state);

  const createSelectEditorOpenFor = (stateKey) => (state) =>
    selectEditorOpenFor(stateKey, state);

  const createSelectEditorOperationFor = (stateKey) => (state) =>
    selectEditorOperationFor(stateKey, state);

  const createSelectEditorSavingFor = (stateKey, nestedKey, id) => (state) =>
    selectEditorSavingFor(stateKey, nestedKey, id, state);

  const createSelectEditorSaveErrorFor = (stateKey, nestedKey, id) => (state) =>
    selectEditorSaveErrorFor(stateKey, nestedKey, id, state);

  const createSelectEditorDeletingFor = (stateKey, nestedKey, id) => (state) =>
    selectEditorDeletingFor(stateKey, nestedKey, id, state);

  const createSelectEditorDeleteError = (stateKey, nestedKey, id) => (state) =>
    selectEditorDeleteError(stateKey, nestedKey, id, state);

  // Managment
  const slice = createSlice({
    name: reducerKey,
    initialState,
    reducers: {
      create(state, action) {
        const { payload: entityKey } = action;

        if (state[entityKey]) {
          state[entityKey].open = true;
          state[entityKey].operation = 'create';
          state[entityKey].data = null;
        }
      },

      copy: {
        prepare(entityKey, entity) {
          return { payload: { entityKey, entity } };
        },
        reducer(state, action) {
          const {
            payload: { entityKey, entity },
          } = action;

          if (state[entityKey]) {
            state[entityKey].open = true;
            state[entityKey].operation = 'copy';
            state[entityKey].data = entity;
          }
        },
      },

      edit: {
        prepare(entityKey, entity) {
          return { payload: { entityKey, entity } };
        },
        reducer(state, action) {
          const {
            payload: { entityKey, entity },
          } = action;

          if (state[entityKey]) {
            state[entityKey].open = true;
            state[entityKey].operation = 'edit';
            state[entityKey].data = entity;
          }
        },
      },

      save: {
        prepare(entityKey, entityOrPatch, metadata = {}) {
          return { payload: { entityKey, entityOrPatch }, meta: metadata };
        },

        reducer(state, action) {
          const {
            payload: { entityKey, entityOrPatch },
          } = action;

          if (state[entityKey]) {
            state[entityKey].saving = true;
            state[entityKey].saveError = null;

            if (state[entityKey].operation === 'create') {
              state[entityKey].data = entityOrPatch || null;
            } else if (state[entityKey].data) {
              state[entityKey].data = {
                ...state[entityKey].data,
                ...(entityOrPatch || {}),
              };
            } else {
              state[entityKey].data = null;
            }
          }
        },
      },

      discart(state, action) {
        const { payload: entityKey } = action;

        state[entityKey].open = false;
        state[entityKey].data = null;
        state[entityKey].saving = false;
      },

      saveMutationSucceded(state, action) {
        const { payload: entityKey } = action;

        if (state[entityKey]) {
          state[entityKey].saving = false;
          state[entityKey].open = false;
          state[entityKey].data = null;
          state[entityKey].saveError = null;
        }
      },

      saveMutationFailed(state, action) {
        const {
          payload: { entityKey, errorMessage },
        } = action;

        if (state[entityKey]) {
          state[entityKey].saving = false;
          state[entityKey].saveError = errorMessage;
        }
      },

      delete: {
        prepare(entityKey, id, metadata = {}) {
          return { payload: { entityKey, id }, meta: metadata };
        },
        reducer(state, action) {
          const {
            payload: { entityKey },
          } = action;

          if (state[entityKey]) {
            state[entityKey].deleting = true;
            state[entityKey].deleteError = null;
          }
        },
      },

      deleleMutationSucceded(state, action) {
        const { payload: entityKey } = action;

        if (state[entityKey]) {
          state[entityKey].deleting = false;
          state[entityKey].deleteError = null;
        }
      },

      deleteMutationFailed(state, action) {
        const {
          payload: { entityKey, errorMessage },
        } = action;

        if (state[entityKey]) {
          state[entityKey].deleting = false;
          state[entityKey].deleteError = errorMessage;
        }
      },
    },
  });

  slice.reducer.key = reducerKey;

  if (process.env.NODE_ENV === 'development' && window.Cypress) {
    window.editableEntitiesActions = {
      ...slice.actions,
    };
  }

  // Fetching
  function* saveSaga(action) {
    const {
      payload: { entityKey },
    } = action;

    const config = getEntityConfig(entityKey);

    const state = yield select();

    // Transform payload if necessary

    let payload = yield selectEditorDataFor(entityKey, state);

    const primaryKey =
      typeof config.entityPK === 'function'
        ? config.entityPK(payload)
        : config.entityPK || 'id';

    const operation = yield selectEditorOperationFor(entityKey, state);

    const bag = {
      payload,
      primaryKey: payload[primaryKey],
      operation,
      state,
      action,
    };

    if (typeof config.transformBeforeSave === 'function') {
      payload = config.transformBeforeSave(bag);
    }

    const url = config.url(bag);

    const saveMethod =
      typeof config.saveMethod === 'function'
        ? config.saveMethod(bag)
        : config.saveMethod;

    try {
      yield call(axios[saveMethod || MUTATION_METHODS.POST], url, payload);

      yield put(slice.actions.saveMutationSucceded(entityKey));
      if (typeof action.meta.thenPut !== 'undefined') {
        yield put(action.meta.thenPut);
      }
    } catch (error) {
      yield put(
        slice.actions.saveMutationFailed({
          entityKey,
          errorMessage: error.message,
        }),
      );
    }
  }

  function* deleteSaga(action) {
    const {
      payload: { entityKey, id },
    } = action;

    const config = getEntityConfig(entityKey);

    const state = yield select();

    const bag = {
      payload: null,
      primaryKey: id,
      operation: 'delete',
      state,
      action,
    };

    const url = config.url(bag);

    try {
      yield call(axios[config.deleteMethod || MUTATION_METHODS.DELETE], url);

      yield put(slice.actions.deleleMutationSucceded(entityKey));

      if (typeof action.meta.thenPut !== 'undefined') {
        console.log(action);
        yield put(action.meta.thenPut);
      }
    } catch (error) {
      yield put(
        slice.actions.deleteMutationFailed({
          entityKey,
          errorMessage: error.message,
        }),
      );
    }
  }

  const saga = all([
    takeEvery(slice.actions.save.type, saveSaga),
    takeEvery(slice.actions.delete.type, deleteSaga),
  ]);

  return {
    reducer: slice.reducer,
    saga,
    editor: {
      actions: {
        create: slice.actions.create,
        copy: slice.actions.copy,
        edit: slice.actions.edit,
        save: slice.actions.save,
        discart: slice.actions.discart,
        saveMutationSucceded: slice.actions.saveMutationSucceded,
        saveMutationFailed: slice.actions.saveMutationFailed,
        delete: slice.actions.delete,
        deleleMutationSucceded: slice.actions.deleleMutationSucceded,
        deleteMutationFailed: slice.actions.deleteMutationFailed,
      },
      selectors: {
        createSelectEditorSliceFor,
        createSelectEditorDataFor,
        createSelectEditorOpenFor,
        createSelectEditorOperationFor,
        createSelectEditorSavingFor,
        createSelectEditorSaveErrorFor,
        createSelectEditorDeletingFor,
        createSelectEditorDeleteError,
        selectEditorSliceFor,
        selectEditorDataFor,
        selectEditorOpenFor,
        selectEditorOperationFor,
        selectEditorSavingFor,
        selectEditorSaveErrorFor,
        selectEditorDeletingFor,
        selectEditorDeleteError,
      },
    },
  };
}
