import React from 'react';
import PropTypes from 'prop-types';
import Count from './styles';

export default function HiringTargetHeader(props) {
  const {
    sum,
    target,
    percentChar,
    isRounded,
    restrictToCountry,
    localeDialect,
    localeLanguage,
  } = props;

  return (
    <>
      {`${restrictToCountry} > ${localeLanguage} (${localeDialect})`.toUpperCase()}
      <Count>
        {`(${
          isRounded ? sum : sum.toFixed(2)
        }${percentChar} of ${target}${percentChar})`}
      </Count>
    </>
  );
}

HiringTargetHeader.propTypes = {
  sum: PropTypes.number.isRequired,
  target: PropTypes.number.isRequired,
  percentChar: PropTypes.string.isRequired,
  isRounded: PropTypes.bool.isRequired,
  restrictToCountry: PropTypes.string.isRequired,
  localeDialect: PropTypes.string.isRequired,
  localeLanguage: PropTypes.string.isRequired,
};
