/* eslint-disable react/jsx-props-no-spreading */
import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { useTable, useFlexLayout, useSortBy } from 'react-table';
import cx from 'classnames';
import isFunction from 'lodash/isFunction';
import TitleTooltip from './TitleTooltip';
import SortArrow from './SortArrow';
import InformationTooltip from '../ProcessBuilder/InformationTooltip';
import Styles, { Header } from './styles';
import {
  useOnSelectTrigger,
  getConditionalEnhancers,
  useStickyColumns,
  getRowStyle,
  useOnSortTrigger,
  hasSortBy,
} from './helpers';

function renderHeaderGroup(column) {
  const headerProps = column.getHeaderProps(column.getSortByToggleProps());

  const { style, className } = column;

  delete headerProps.title;

  return (
    <th
      {...headerProps}
      style={{ ...headerProps.style, ...style }}
      className={cx(className, headerProps.className)}
    >
      <TitleTooltip
        title={!column.tooltipIcon ? column.tooltip : ''}
        placement="bottom"
        trigger="hover"
      >
        <Header>
          {column.render('Header')}
          <SortArrow column={column} />
          {column.tooltip && column.tooltipIcon && (
            <InformationTooltip
              tooltipIconStyle={{ bottom: 0 }}
              title={column.tooltip}
            />
          )}
        </Header>
      </TitleTooltip>
    </th>
  );
}

function renderRow(prepareRow, visibleColumns, renderExpandedComponent) {
  return (row) => {
    prepareRow(row);

    const expandedComponent = row.isExpanded
      ? renderExpandedComponent(row)
      : null;

    const { key, ...rowProps } = row.getRowProps();

    return (
      <React.Fragment key={key}>
        <tr {...rowProps}>
          {row.cells.map((cell) => {
            const {
              column: { style },
            } = cell;

            const cellProps = cell.getCellProps();

            return (
              <td
                {...cellProps}
                style={{
                  ...cellProps.style,
                  ...style,
                  ...getRowStyle(!!expandedComponent),
                }}
              >
                {cell.render('Cell')}
              </td>
            );
          })}
        </tr>
        {expandedComponent && expandedComponent}
      </React.Fragment>
    );
  };
}

export default function Table(props) {
  const {
    columns,
    data,
    style,
    onSelect,
    onSort,
    initialState,
    className,
    renderExpandedComponent,
  } = props;

  const hasSelection = isFunction(onSelect);
  const hasSort = useMemo(() => {
    return hasSortBy(columns || [], onSort);
  }, [columns, onSort]);

  const {
    getTableBodyProps,
    getTableProps,
    headerGroups,
    prepareRow,
    rows,
    visibleColumns,
    state,
  } = useTable(
    {
      columns,
      data,
      initialState,
      autoResetSortBy: !hasSort,
      manualSortBy: hasSort,
      autoResetSelectedRows: !hasSelection,
    },
    useSortBy,
    useFlexLayout,
    useStickyColumns,
    ...getConditionalEnhancers({
      onSelect,
      renderExpandedComponent,
    }),
  );

  useOnSelectTrigger(state.selectedRowIds, onSelect);
  useOnSortTrigger(state.sortBy, columns || [], onSort);

  return (
    <Styles style={style} className={className}>
      <div className="tableWrap">
        <table {...getTableProps()}>
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map(renderHeaderGroup)}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map(
              renderRow(prepareRow, visibleColumns, renderExpandedComponent),
            )}
          </tbody>
        </table>
      </div>
    </Styles>
  );
}

Table.propTypes = {
  /**
   * Columns follow the same signature of react-table columns
   * (https://github.com/tannerlinsley/react-table/blob/master/docs/api/README.md)
   * but have a few aditional functionalities.
   */
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      /**
       * Key passed down to React (Why? https://bit.ly/32VMuna (Medium))
       */
      key: PropTypes.string.isRequired,
      /**
       * Acessor is the name of the prop in the data[idx] object,
       * which is rendered for the column
       */
      accessor: PropTypes.string.isRequired,
      /**
       * Renders the column's Header. Can be a string or a function that returns JSX.
       */
      Header: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.node,
        PropTypes.func,
        PropTypes.elementType,
      ]).isRequired,
      /**
       * Column Cell. Renders the value for the column. You can pass any valid React Component.
       * You can access the original value inside props.cell.value
       */
      Cell: PropTypes.elementType,
      /**
       * Should this column remain in view when table overflows-x?
       */
      sticky: PropTypes.bool,
      /**
       * Disable column sorting.
       */
      disableSortBy: PropTypes.bool,
      /**
       * Show tooltip onto of the column's Header. It can be a string or a React element.
       */
      tooltip: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    }),
  ).isRequired,
  /**
   * Data rows.
   */
  data: PropTypes.arrayOf(PropTypes.object).isRequired,
  /**
   * JS styles object, the same you would pass to <div style={{ this }} />
   * This is applied to the table container.
   */
  style: PropTypes.objectOf(PropTypes.any),
  /**
   * ClassName passed to Table container.
   */
  className: PropTypes.string,
  /**
   * When you pass this function in, you are telling the Table component you are going
   * to use row selection. This function is called with an array of selected row indexes
   * whenever the user checks or unchecks any row.
   */
  onSelect: PropTypes.func,
  /**
   * This function is called with an array of objects with the id of the sorted
   * column and the direction of the sort.
   */
  onSort: PropTypes.func,
  initialState: PropTypes.objectOf(PropTypes.any),
  /**
   * When you pass this function in, you are telling the Table component all
   * your rows can be expanded. This function receives the record for the
   * current row and should return a ReactNode.
   */
  renderExpandedComponent: PropTypes.func,
};

Table.defaultProps = {
  initialState: {},
  style: {},
  onSelect: undefined,
  onSort: undefined,
  renderExpandedComponent: undefined,
  className: undefined,
};
