import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import NumberFormat from 'react-number-format';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import ActionFooter from '../../../../ActionFooter';
import ErrorPopover, { ErrorPopoverProps } from '../../../../ErrorPopover';
import Input from '../../../../Input';
import Modal from '../../../../Modal';
import Select from '../../../../Select';
import {
  selectSaveError,
  selectSaving,
} from '../../../../../store/ducks/autoScreening';
import { Section, Label } from '../../../../../styles';
import { Container, Footer } from './styles';

export default function ConfigurationModal(props) {
  const {
    localeOptions,
    handleLocaleChange,
    handleMaxActiveUsersChange,
    handleMaxUsersPerDayChange,

    // Modal
    modalProps: { open, onExited },
    modalControls: { closeModal },

    // Formik
    values: { id, localeId, maxUsersPerDay, maxActiveUsers },
    submitForm,
    submitCount,
    showError,
    errorPopoverProps,
    errors,
  } = props;

  const error = useSelector(selectSaveError);
  const saving = useSelector(selectSaving);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  const actionButtons = [
    {
      marginRight: '8px',
      type: 'secondary',
      text: 'Cancel',
      onClick: closeModal,
    },
    {
      text: 'Save',
      loading: saving,
      onClick: submitForm,
    },
  ];

  // Max signed int value
  const withValueLimit = ({ floatValue }) => floatValue <= 2 ** 31 - 1;

  return (
    <Modal
      open={open}
      onExited={onExited}
      onClose={closeModal}
      closeOnOverlayClick={!saving}
      closeOnEsc={!saving}
      title="Add Auto-screening Configuration"
    >
      <Container>
        <Section>
          <Label htmlFor="localeId">Locale</Label>
          <Select
            id="localeId"
            name="localeId"
            options={localeOptions}
            onChange={handleLocaleChange}
            value={localeId}
            error={submitCount >= 1 && errors.localeId}
            placeholder="Select locale"
            disabled={!!id}
          />
        </Section>
        <Section marginBottom={54}>
          <Row>
            <Col col={6}>
              <Label htmlFor="maxUsersPerDay">Max users per day</Label>
              <NumberFormat
                id="maxUsersPerDay"
                name="maxUsersPerDay"
                customInput={Input}
                allowNegative={false}
                onChange={handleMaxUsersPerDayChange}
                placeholder="Enter number"
                value={maxUsersPerDay}
                error={submitCount >= 1 && errors.maxUsersPerDay}
                isAllowed={withValueLimit}
                thousandSeparator="."
                decimalSeparator=","
                errorIncreasesHeight
              />
            </Col>
            <Col col={6}>
              <Label htmlFor="maxActiveUsers">Max active users</Label>
              <NumberFormat
                id="maxActiveUsers"
                name="maxActiveUsers"
                customInput={Input}
                allowNegative={false}
                onChange={handleMaxActiveUsersChange}
                placeholder="Enter number"
                value={maxActiveUsers}
                error={submitCount >= 1 && errors.maxActiveUsers}
                isAllowed={withValueLimit}
                thousandSeparator="."
                decimalSeparator=","
                errorIncreasesHeight
              />
            </Col>
          </Row>
        </Section>
      </Container>
      <ErrorPopover title="Problem while saving" {...errorPopoverProps} />
      <Footer>
        <ActionFooter hideDivider rightButtons={actionButtons} />
      </Footer>
    </Modal>
  );
}

ConfigurationModal.propTypes = {
  localeOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ).isRequired,
  handleLocaleChange: PropTypes.func.isRequired,
  handleMaxActiveUsersChange: PropTypes.func.isRequired,
  handleMaxUsersPerDayChange: PropTypes.func.isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  values: PropTypes.shape({
    id: PropTypes.number,
    localeId: PropTypes.number,
    maxUsersPerDay: PropTypes.number,
    maxActiveUsers: PropTypes.number,
  }).isRequired,
  submitForm: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
};

ConfigurationModal.defaultProps = {};
