import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import ProjectOverviewPreview from '../../../../../../components/ProjectPreviews/ProjectOverviewPreview';
import { ENTITIES, fetch } from '../../../../../../store/ducks/options';

export default function ProjectOverview(props) {
  const {
    projectName,
    projectAlias,
    projectDescription,
    projectType,
    taskType,
    taskVolume,
    navigateTo,
    renderedIn,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.workTypes));
    dispatch(fetch(ENTITIES.taskVolumes));
  }, [dispatch]);

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.PROJECT_OVERVIEW);
  }, [navigateTo]);

  return (
    <ProjectOverviewPreview
      projectName={projectName}
      projectAlias={projectAlias}
      projectDescription={projectDescription}
      projectType={projectType}
      taskType={taskType}
      taskVolume={taskVolume}
      editStep={editStep}
      renderedIn={renderedIn}
      stepNumber={STEP_NUMBER.PROJECT_OVERVIEW + 1}
    />
  );
}

ProjectOverview.propTypes = {
  projectName: PropTypes.string.isRequired,
  projectAlias: PropTypes.string.isRequired,
  projectDescription: PropTypes.string.isRequired,
  projectType: PropTypes.string.isRequired,
  taskType: PropTypes.string.isRequired,
  taskVolume: PropTypes.string.isRequired,
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectOverview.defaultProps = {
  renderedIn: 'project-setup',
};
