import React, { useMemo } from 'react';
import { Shimmer } from 'react-shimmer';
import PropTypes from 'prop-types';
import {
  Placeholder,
  PreviewButton,
  ProcessSelectedContainer,
  ProcessName,
  ProcessStep,
  ProcessCreatedBy,
  FlexShimmer,
  ShimmerSpacer,
} from './styles';
import Popover, { PopoverItem, PopoverIcon, PopoverText } from '../Popover';
import SettingsHoverable from '../Icons/SettingsHoverable';
import pencil from '../../assets/icons/pencil.svg';
import copy from '../../assets/icons/copy.svg';
import ordinalSuffix from '../../helpers/ordinal-suffix';
import { usePreviewModalControls } from '../Builder/helpers/preview-context';

export default function ProcessSteps(props) {
  const {
    processId,
    processType,
    processName,
    steps,
    creatorName,
    createdAt,
    loading,
    error,
    editExistingProcess,
    copyExistingProcess,
  } = props;

  const createdDate = useMemo(() => (createdAt ? new Date(createdAt) : null), [
    createdAt,
  ]);

  const createdBy = useMemo(
    () =>
      createdDate
        ? `Created by: ${creatorName || ''}, ${createdDate.toLocaleString(
            'dafault',
            {
              month: 'long',
            },
          )} ${ordinalSuffix(
            createdDate.getDate(),
          )} ${createdDate.getFullYear()}`
        : '',
    [createdDate, creatorName],
  );

  const previewModalControls = usePreviewModalControls();

  if (loading) {
    return (
      <ProcessSelectedContainer>
        <ProcessName>
          <Shimmer width={280} height={14} />
        </ProcessName>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={215} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={160} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={195} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessCreatedBy>
          <Shimmer width={260} height={14} />
        </ProcessCreatedBy>
      </ProcessSelectedContainer>
    );
  }

  if (error) {
    return (
      <ProcessSelectedContainer hasError>
        <ProcessStep isError>
          Error loading steps for selected process
        </ProcessStep>
      </ProcessSelectedContainer>
    );
  }

  if (Array.isArray(steps) && steps.length) {
    const tooltipContent = (
      <>
        {copyExistingProcess && (
          <PopoverItem
            onClick={() => copyExistingProcess(processId, processType)}
            onKeyDown={() => {}}
            role="button"
            tabIndex={0}
          >
            <PopoverIcon src={copy} alt={'copy icon'} />
            <PopoverText>Copy</PopoverText>
          </PopoverItem>
        )}
        {editExistingProcess && (
          <PopoverItem
            onClick={() => editExistingProcess(processId, processType)}
            onKeyDown={() => {}}
            role="button"
            tabIndex={0}
          >
            <PopoverIcon src={pencil} alt={'edit icon'} />
            <PopoverText>Edit</PopoverText>
          </PopoverItem>
        )}
      </>
    );
    return (
      <ProcessSelectedContainer>
        <ProcessName>{processName}</ProcessName>
        {steps.map((step) => (
          <ProcessStep
            key={step.order}
            data-testid="project-setup--reg-qua--process-steps-step"
          >
            {step.order} - {step.stepDefinition.name}
          </ProcessStep>
        ))}
        <ProcessCreatedBy>{createdBy}</ProcessCreatedBy>
        {(!!copyExistingProcess || !!editExistingProcess) && (
          <div className="registration_and_qualification_process_steps_actions">
            <Popover content={tooltipContent} placement="bottom-end">
              <SettingsHoverable />
            </Popover>
          </div>
        )}
        <PreviewButton
          onClick={() => previewModalControls.openModal({ steps, processType })}
          type="secondary"
        >
          Preview Process
        </PreviewButton>
      </ProcessSelectedContainer>
    );
  }

  if (processName) {
    return (
      <ProcessSelectedContainer hasError>
        <ProcessStep isError>Selected process has no steps.</ProcessStep>
      </ProcessSelectedContainer>
    );
  }

  return <Placeholder>No process selected</Placeholder>;
}

ProcessSteps.propTypes = {
  processName: PropTypes.string,
  steps: PropTypes.arrayOf(
    PropTypes.shape({
      order: PropTypes.number,
      stepDefinition: PropTypes.shape({
        name: PropTypes.string,
      }),
    }),
  ),
  creatorName: PropTypes.string,
  processId: PropTypes.number,
  processType: PropTypes.string.isRequired,
  createdAt: PropTypes.number,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.instanceOf(Error),
  editExistingProcess: PropTypes.func,
  copyExistingProcess: PropTypes.func,
};

ProcessSteps.defaultProps = {
  processName: undefined,
  steps: null,
  creatorName: undefined,
  createdAt: null,
  error: null,
  processId: undefined,
  editExistingProcess: undefined,
  copyExistingProcess: undefined,
};
