import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'styled-bootstrap-grid';
import Select from '../../../../../components/Select';
import Checkbox from '../../../../../components/Checkbox';
import Input from '../../../../../components/Input';
import ActionFooter from '../../../../../components/ActionFooter';
import CloseableTag from '../../../../../components/CloseableTag';
import {
  ActionContainer,
  Container,
  FilterContainer,
  InlineContainer,
  InputContainer,
  Spacer,
} from './styles';
import { Label } from '../../../../../styles';

export default function AdvancedFilters(props) {
  const {
    countryList,
    countriesOptions,
    localeDialects,
    localeDialectsOptions,
    localeLanguages,
    localeLanguagesOptions,
    countriesTags,
    localeLanguagesTags,
    localeDialectsTags,
    handleChange,
    handleApplyFilters,
    handleCountriesChange,
    handleCountryRemoval,
    handleLocaleLanguageChange,
    handleLocaleDialectChange,
    handleLocaleLanguageRemoval,
    handleLocaleDialectRemoval,
    calculateMultiValueCount,
    showAdvancedFilters,
    // Formik
    values,
    errors,
    submitCount,
  } = props;

  return (
    <Container showAdvancedFilters={showAdvancedFilters}>
      <FilterContainer>
        <Row>
          <Col col={2}>
            <Label htmlFor="languages">Locale Language</Label>
            <Select
              getMultiCount={calculateMultiValueCount}
              id="languages"
              name="languages"
              options={localeLanguagesOptions}
              value={values.languages}
              onChange={handleLocaleLanguageChange}
              loading={localeLanguages.loading}
              placeholder="Select"
              error={
                localeLanguages.error || (submitCount > 0 && errors.languages)
              }
              isMulti
            />
            {localeLanguagesTags.map((r) => (
              <CloseableTag
                key={r.label}
                label={r.label}
                onClose={() => handleLocaleLanguageRemoval(r.value)}
              />
            ))}
            <Spacer />
            <Label htmlFor="dialects">Locale Dialect</Label>
            <Select
              getMultiCount={calculateMultiValueCount}
              id="dialects"
              name="dialects"
              options={localeDialectsOptions}
              value={values.dialects}
              onChange={handleLocaleDialectChange}
              loading={localeDialects.loading}
              placeholder="Select"
              error={
                localeDialects.error || (submitCount > 0 && errors.dialects)
              }
              isMulti
            />
            {localeDialectsTags.map((r) => (
              <CloseableTag
                key={r.label}
                label={r.label}
                onClose={() => handleLocaleDialectRemoval(r.value)}
              />
            ))}
            <Spacer />
            <Label htmlFor="country">Country</Label>
            <Select
              getMultiCount={calculateMultiValueCount}
              id="country"
              name="country"
              options={countriesOptions}
              value={values.country}
              onChange={handleCountriesChange}
              loading={countryList.loading}
              placeholder="Select"
              error={countryList.error || (submitCount > 0 && errors.country)}
              isMulti
            />
            {countriesTags.map((r) => (
              <CloseableTag
                key={r.label}
                label={r.label}
                onClose={() => handleCountryRemoval(r.value)}
              />
            ))}
          </Col>
          <Col col={2} offset={1}>
            <Label htmlFor="ipCheckPassed">IP Check Result</Label>
            <>
              <Checkbox
                name="ipCheckPassed"
                onChange={handleChange}
                checked={values.ipCheckPassed}
                bold={false}
                label={'Passed'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="ipCheckFailed"
                onChange={handleChange}
                checked={values.ipCheckFailed}
                bold={false}
                label={'Failed'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </div>
            <Spacer />
            <Label htmlFor="ipFailureMismatch">IP Failure Reason</Label>
            <>
              <Checkbox
                name="ipFailureMismatch"
                onChange={handleChange}
                checked={values.ipFailureMismatch}
                bold={false}
                label={'IP Mismatch'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="ipFailureVPN"
                onChange={handleChange}
                checked={values.ipFailureVPN}
                bold={false}
                label={'VPN Provider'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </div>
          </Col>
          <Col col={2}>
            <Label htmlFor="similarityScoreMin">Similarity Score</Label>
            <InlineContainer>
              <div>From</div>
              <InputContainer>
                <Input
                  name="similarityScoreMin"
                  onChange={handleChange}
                  id="similarityScoreMin"
                  placeholder="0.0"
                  value={values.similarityScoreMin}
                  disabled // will be implemented in Phase 2: ACE-13356
                />
              </InputContainer>
              <div> to </div>
              <InputContainer>
                <Input
                  name="similarityScoreMax"
                  onChange={handleChange}
                  id="similarityScoreMax"
                  placeholder="1.0"
                  value={values.similarityScoreMax}
                  disabled // will be implemented in Phase 2: ACE-13356
                />
              </InputContainer>
            </InlineContainer>
            <Spacer height={70} />
            <Label htmlFor="maliciousnessLevelHigh">Maliciousness Level</Label>
            <>
              <Checkbox
                name="maliciousnessLevelHigh"
                onChange={handleChange}
                checked={values.maliciousnessLevelHigh}
                bold={false}
                label={'High'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="maliciousnessLevelMedium"
                onChange={handleChange}
                checked={values.maliciousnessLevelMedium}
                bold={false}
                label={'Medium'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </div>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="maliciousnessLevelLow"
                onChange={handleChange}
                checked={values.maliciousnessLevelLow}
                bold={false}
                label={'Low'}
                disabled // will be implemented in Phase 2: ACE-13356
              />
            </div>
          </Col>
          <Col col={2}>
            <Label htmlFor="ageGroupMin">Age Group</Label>
            <InlineContainer>
              <div>From</div>
              <InputContainer>
                <Input
                  name="ageGroupMin"
                  onChange={handleChange}
                  id="ageGroupMin"
                  placeholder="0"
                  value={values.ageGroupMin}
                />
              </InputContainer>
              <div> to </div>
              <InputContainer>
                <Input
                  name="ageGroupMax"
                  onChange={handleChange}
                  id="ageGroupMax"
                  placeholder="100"
                  value={values.ageGroupMax}
                />
              </InputContainer>
            </InlineContainer>
            <Spacer height={70} />
            <Label htmlFor="genderFemale">Gender</Label>
            <>
              <Checkbox
                name="genderFemale"
                onChange={handleChange}
                checked={values.genderFemale}
                bold={false}
                label={'Female'}
              />
            </>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="genderMale"
                onChange={handleChange}
                checked={values.genderMale}
                bold={false}
                label={'Male'}
              />
            </div>
          </Col>
          <Col col={2}>
            <Label htmlFor="directPay">Extra Filters</Label>
            <>
              <Checkbox
                name="directPay"
                onChange={handleChange}
                checked={values.directPay}
                bold={false}
                label={'Direct Pay'}
              />
            </>
            <div style={{ marginTop: -15 }}>
              <Checkbox
                name="optInA9"
                onChange={handleChange}
                checked={values.optInA9}
                bold={false}
                label={'A9 Opt In'}
              />
            </div>
            <Spacer />
            <Label htmlFor="">Mobile Device OS</Label>
            <Select
              // getMultiCount={calculateMultiValueCount}
              id="mobileDeviceOS"
              name="mobileDeviceOS"
              // options={countriesOptions}
              value={values.mobileDeviceOS}
              // onChange={handleCountriesChange}
              // loading={countries.loading}
              placeholder="Select"
              // error={
              //   countries.error ||
              //   (submitCount > 0 && errors.countriesCollectingData)
              // }
              isMulti
            />
          </Col>
        </Row>
      </FilterContainer>
      <ActionContainer>
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              text: 'Apply Filters',
              onClick: handleApplyFilters,
            },
          ]}
        />
      </ActionContainer>
    </Container>
  );
}

AdvancedFilters.propTypes = {
  countryList: PropTypes.arrayOf(PropTypes.object).isRequired,
  countriesOptions: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeDialects: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeDialectsOptions: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeLanguages: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeLanguagesOptions: PropTypes.arrayOf(PropTypes.object).isRequired,
  countriesTags: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeLanguagesTags: PropTypes.arrayOf(PropTypes.object).isRequired,
  localeDialectsTags: PropTypes.arrayOf(PropTypes.object).isRequired,
  values: PropTypes.shape({
    languages: PropTypes.arrayOf(PropTypes.object),
    dialects: PropTypes.arrayOf(PropTypes.object),
    country: PropTypes.arrayOf(PropTypes.object),
    ipCheckPassed: PropTypes.bool,
    ipCheckFailed: PropTypes.string,
    ipFailureMismatch: PropTypes.string,
    ipFailureVPN: PropTypes.string,
    similarityScoreMin: PropTypes.string,
    similarityScoreMax: PropTypes.string,
    maliciousnessLevelHigh: PropTypes.string,
    maliciousnessLevelMedium: PropTypes.string,
    maliciousnessLevelLow: PropTypes.string,
    ageGroupMin: PropTypes.string,
    ageGroupMax: PropTypes.string,
    genderFemale: PropTypes.string,
    genderMale: PropTypes.string,
    directPay: PropTypes.string,
    optInA9: PropTypes.string,
    mobileDeviceOS: PropTypes.string,
  }).isRequired,
  handleChange: PropTypes.func.isRequired,
  handleApplyFilters: PropTypes.func.isRequired,
  handleCountriesChange: PropTypes.func.isRequired,
  handleCountryRemoval: PropTypes.func.isRequired,
  handleLocaleLanguageChange: PropTypes.func.isRequired,
  handleLocaleDialectChange: PropTypes.func.isRequired,
  handleLocaleLanguageRemoval: PropTypes.func.isRequired,
  handleLocaleDialectRemoval: PropTypes.func.isRequired,
  calculateMultiValueCount: PropTypes.func.isRequired,
  showAdvancedFilters: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
};
AdvancedFilters.defaultProps = {};
