import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveDataConsentFormSuccess,
  saveDataConsentFormError,
  saveDataConsentForm,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* saveDataConsentFormSagaFn(action) {
  const {
    payload: {
      changes: { dataConsentForm, version },
      thenPut,
      closeModal,
    },
  } = action;

  try {
    let endpoint = ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM();

    if (version) {
      endpoint = ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM_VERSION({
        pathVariables: {
          id: version,
        },
      });
    }
    const response = yield call(axios.post, endpoint, dataConsentForm);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    if (typeof closeModal === 'function') {
      closeModal();
    }

    yield put(saveDataConsentFormSuccess(response.data));
  } catch (error) {
    yield put(saveDataConsentFormError(error));
  }
}

export default takeLatest(saveDataConsentForm.type, saveDataConsentFormSagaFn);
