import React from 'react';
import PropTypes from 'prop-types';
import Loader from '../../../../components/Loader';
import {
  CardContainer,
  CardHeader,
  CardTitle,
  CardShimmer,
  CardDescription,
  CardContent,
  Spacer,
  Shimmer,
} from './styles';
import { Label } from '../../../../styles';

export default function EmptyCard(props) {
  const {
    filters: {
      filters: { topic },
    },
    loading,
  } = props;

  if (loading) {
    return (
      <CardContainer alignCenter>
        <Spacer height={74} />
        <Loader dotType="tertiary" />
        <Spacer height={74} />
      </CardContainer>
    );
  }

  return (
    <CardContainer>
      <CardHeader>
        <CardShimmer>
          <Shimmer width={121} />
          <Spacer />
          <CardDescription>
            <Shimmer width={383} />
          </CardDescription>
        </CardShimmer>
        <Spacer />
        <CardTitle>
          {topic ? (
            <Label>No results for &apos;{topic}&apos;</Label>
          ) : (
            <Label>No results found</Label>
          )}
        </CardTitle>
      </CardHeader>
      <CardContent>
        <Shimmer width={517} />
      </CardContent>
    </CardContainer>
  );
}

EmptyCard.propTypes = {
  filters: PropTypes.shape({
    filters: PropTypes.shape({
      topic: PropTypes.string,
    }),
  }).isRequired,
  loading: PropTypes.bool,
};
EmptyCard.defaultProps = {
  loading: false,
};
