import { call, put, takeLatest } from 'redux-saga/effects';
import get from 'lodash/get';
import { fetchAll, fetchFailed, setAll } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchHiringTargetsFn(action) {
  const { payload } = action;
  const projectId = get(payload, 'projectId', null);

  if (!projectId) {
    return;
  }

  try {
    const { data: hiringTargets } = yield call(
      axios.get,
      ENDPOINTS.V2.HIRING_TARGETS({
        query: {
          projectId: payload.projectId || projectId,
          metrics: true,
        },
      }),
    );

    yield put(
      setAll(
        hiringTargets.map((hiringTarget) => ({
          id: hiringTarget.id,
          hiringDeadline: hiringTarget.deadline,
          target: hiringTarget.target,
          localeLanguage: hiringTarget.language,
          localeDialect: hiringTarget.languageCountry,
          localeLanguageTo: hiringTarget.languageTo,
          localeDialectTo: hiringTarget.languageCountryTo,
          restrictToCountry: hiringTarget.restrictToCountry,
          assignedOwner: hiringTarget.ownerId,
          metrics: hiringTarget.metrics,
        })),
      ),
    );
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

export default takeLatest(fetchAll.type, fetchHiringTargetsFn);
