import cx from 'classnames';
import memoize from 'lodash/memoize';
import noop from 'lodash/noop';
import isFunction from 'lodash/isFunction';
import { useRowSelect, useExpanded } from 'react-table';
import { useEffect, useRef } from 'react';
import CheckboxHeader from './CheckboxHeader';
import CheckboxRow from './CheckboxRow';
import ExpandButton from './ExpandButton';

export function doesTableHasExpandableRows(renderExpandedComponent) {
  return typeof renderExpandedComponent === 'function';
}

function renderCheckbox(columns) {
  const sticky = columns.some((c) => c.sticky);

  return [
    {
      id: 'selection',
      width: 48,
      Header: CheckboxHeader,
      Cell: CheckboxRow,
      sticky,
      style: {
        paddingLeft: 6,
      },
    },
    ...columns,
  ];
}

function renderExpander(columns) {
  const sticky = columns.some((c) => c.sticky);
  const overrideExpander = columns.some((c) => c.id === 'expander');

  if (overrideExpander) {
    return columns;
  }

  return [
    {
      id: 'expander',
      width: 32,
      Header: () => null,
      Cell: ExpandButton,
      sticky,
      style: {
        textAlign: 'center',
      },
    },
    ...columns,
  ];
}

function addCheckboxColumnHook(tableHooks) {
  tableHooks.visibleColumns.push(renderCheckbox);
}

function addExpandedColumnHook(tableHooks) {
  tableHooks.visibleColumns.push(renderExpander);
}

export function getConditionalEnhancers({ onSelect, renderExpandedComponent }) {
  // the length of this array must be fixed!
  const enhancers = [noop, noop, noop, noop];

  const hasSelection = isFunction(onSelect);
  const hasExpandableRow = doesTableHasExpandableRows(renderExpandedComponent);

  if (hasSelection) {
    enhancers[0] = useRowSelect;
    enhancers[1] = addCheckboxColumnHook;
  }

  if (hasExpandableRow) {
    enhancers[2] = useExpanded;
    enhancers[3] = addExpandedColumnHook;
  }

  return enhancers;
}

const getFirstStickyColumnIndex = memoize((columns) =>
  [...columns].findIndex((c) => c.sticky),
);

export function makeColumnsSticky(props, table) {
  const {
    cell,
    column,
    instance: { columns },
  } = table;

  const { sticky, totalLeft, id } = column || (cell && cell.column);
  const firstStikyColumnIndex = getFirstStickyColumnIndex(columns);

  if (firstStikyColumnIndex < 0) {
    return [props, {}];
  }

  const { id: lastStikyColumnID } = columns[firstStikyColumnIndex];
  const { id: afterLastStickyColumnId } =
    columns[firstStikyColumnIndex + 1] || {};

  if (sticky) {
    return [
      props,
      {
        style: {
          position: 'sticky',
          left: totalLeft,
          zIndex: 2,
        },
        className: cx('sticky-column', {
          'last-sticky-column': id === lastStikyColumnID,
        }),
      },
    ];
  }

  return [
    props,
    {
      className: cx({
        'after-last-sticky-column': id === afterLastStickyColumnId,
      }),
    },
  ];
}

export function getRowStyle(isExpanded) {
  if (isExpanded) {
    return {
      borderBottom: 'none',
    };
  }

  return {};
}

// Hooks

export function useStickyColumns(tableHooks) {
  tableHooks.getCellProps.push(makeColumnsSticky);
  tableHooks.getHeaderProps.push(makeColumnsSticky);
}

export function useOnSelectTrigger(selectedRowIds, handleSelect) {
  const ref = useRef();
  const hasSelection = isFunction(handleSelect);
  const prevSelectedRows = hasSelection ? ref.current : [];

  let currentSelectedRows = [];
  let areEqual = true;

  if (hasSelection) {
    currentSelectedRows = Object.keys(selectedRowIds).map((s) =>
      parseInt(s, 10),
    );

    areEqual =
      Array.isArray(prevSelectedRows) &&
      currentSelectedRows.length === prevSelectedRows.length &&
      currentSelectedRows.every((v, i) => prevSelectedRows[i] === v);
  }

  useEffect(() => {
    if (!areEqual) {
      ref.current = currentSelectedRows;
      handleSelect(currentSelectedRows);
    }
  });

  return areEqual ? prevSelectedRows : currentSelectedRows;
}

export function hasSortBy(columns, handleSort) {
  return isFunction(handleSort) && columns.some((col) => !col.disableSortBy);
}

export function useOnSortTrigger(sortByState, columns, handleSort) {
  const ref = useRef();
  const hasSort = hasSortBy(columns, handleSort);

  const prevSort = hasSort ? ref.current : [];

  let currentSort = [];
  let areEqual = true;

  if (hasSort) {
    currentSort = sortByState;

    areEqual =
      Array.isArray(prevSort) &&
      currentSort.length === prevSort.length &&
      currentSort.every(({ id }, i) => prevSort[i].id === id) &&
      currentSort.every(({ desc }, i) => prevSort[i].desc === desc);
  }

  useEffect(() => {
    if (!areEqual) {
      if (!ref.current && currentSort.length === 0) {
        ref.current = currentSort;
        return;
      }

      ref.current = currentSort;
      handleSort(currentSort);
    }
  });

  return areEqual ? prevSort : currentSort;
}
