import cleanDeep from 'clean-deep';
import { identity, memoize, pickBy } from 'lodash';
import shortid from 'shortid';
import { STEPS } from '../../../constants/projectSetup';
import features from '../../../constants/featureFlags';

// Makes keys consistent between selects
const getRandomKeyForValue = memoize(
  () => shortid.generate(),
  (value) => `${value}`,
);

function hasNotNullProperties(stepData) {
  const notNullProperties = Object.keys(
    // remove nill
    cleanDeep(
      // remove falsy
      pickBy(stepData, identity),
    ),
  );

  return notNullProperties.length > 0;
}

export function selectProjectOverviewFromProject(project = {}) {
  return {
    name: project.name,
    alias: project.alias,
    description: project.description,
    type: project.projectType || undefined,
    taskType: project.workType || undefined,
    taskVolume: project.taskVolume || undefined,
  };
}

export function selectRegistrationAndQualificationFromProject(project = {}) {
  return {
    registrationProcessTemplate:
      project.registrationProcessTemplateId || undefined,
    qualificationProcessTemplate:
      project.qualificationProcessTemplateId || undefined,
  };
}

export function selectProjectToolsFromProject(project = {}) {
  return {
    hasExternalSystem: !!project.externalSystemId,
    externalSystem: project.externalSystemId || undefined,
    externalSystemUrl: project.externalSystemUrl || undefined,
    swfhProjectExternalUrl: project.swfhProjectExternalUrl || undefined,
    hasIPRestrictions: !!project.allowedIps,
    ipAddresses: Array.isArray(project.allowedIps)
      ? project.allowedIps.map((ip) => ({
          key: getRandomKeyForValue(ip),
          ipAddress: ip,
        }))
      : undefined,
  };
}

export function selectRecruitmentFromProject(project = {}, dependencyFlags) {
  const { hasDemographics = false } = dependencyFlags;

  const hasNotesToRecruiting = !!project.recruitingTeamNotes;
  const hasTargetScreening = !!project.profileTargetId;
  const hasBlockingRules = Array.isArray(project.blockingRules);
  const hasLinkedProjects = Array.isArray(project.linkedProjectsIds);

  return {
    hasNotesToRecruiting,
    notesToRecruiting: hasNotesToRecruiting
      ? project.recruitingTeamNotes
      : undefined,

    hasBlockingRules,
    blockingRules: hasBlockingRules
      ? project.blockingRules.map((blockingRule) => ({
          key: getRandomKeyForValue(blockingRule.blockedProjectId),
          projectId: blockingRule.blockedProjectId,
          permanent: blockingRule.permanent,
        }))
      : undefined,

    hasLinkedProjects,
    linkedProjects: hasLinkedProjects
      ? project.linkedProjectsIds.map((linkedProject) => ({
          key: getRandomKeyForValue(linkedProject),
          projectId: linkedProject,
        }))
      : undefined,

    showDemographics: hasDemographics,

    hasTargetScreening,
    profileTargetId: hasTargetScreening ? project.profileTargetId : undefined,
  };
}

export function selectInvoiceAndPaymentFromProject(project = {}) {
  const hasWorkdayTaskId = !!project.workdayTaskId;
  const hasNotesToFinance = !!project.financeNotes;

  return {
    hasWorkdayTaskId,
    workdayTaskId: hasWorkdayTaskId ? project.workdayTaskId : undefined,
    rateType: project.rateType,
    businessUnit: project.businessUnit,
    hasProductivityData: !!project.productivityData,
    hasNotesToFinance: !!project.financeNotes,
    notesToFinance: hasNotesToFinance ? project.financeNotes : undefined,
    partnerData: project.partnerData || false,
  };
}

export function selectUserProjectPageFromProject(project = {}) {
  return {
    defaultPageTask: project.defaultPageId,
    hasSkillReview: !!project.skillsReview,
    isACANProject: !!project.acanProject,
  };
}

export function selectSupportTeamFromProject(project = {}) {
  return {
    supportTeam: Array.isArray(project.supportTeam)
      ? project.supportTeam.map((supportTeamMember) => ({
          key: getRandomKeyForValue(supportTeamMember.userId),
          member: supportTeamMember.userId,
          role: supportTeamMember.role,
        }))
      : undefined,
  };
}

function hasProject(project) {
  return !!project;
}

export function isProjectOverviewCompleteFromProject(project) {
  return hasProject(project)
    ? hasNotNullProperties(selectProjectOverviewFromProject(project))
    : false;
}

export function isRegistrationAndQualificationCompleteFromProject(project) {
  return hasProject(project)
    ? hasNotNullProperties(
        selectRegistrationAndQualificationFromProject(project),
      )
    : false;
}

export function isInvoiceAndPaymentCompleteFromProject(project) {
  if (!hasProject(project)) {
    return false;
  }

  const invoiceAndPayment = selectInvoiceAndPaymentFromProject(project);

  return (
    !!invoiceAndPayment.rateType &&
    !!invoiceAndPayment.businessUnit &&
    !!invoiceAndPayment.workdayTaskId
  );
}

export function isProjectToolsCompleteFromProject(project) {
  /**
   * Project Tools will be completed when businessUnit is selected because the
   * project setup reach at least the invoiceAndPayment step.
   */
  return hasProject(project) ? !!project.defaultPageId : false;
}

export function isSupportTeamCompleteFromProject(project) {
  return hasProject(project)
    ? hasNotNullProperties(selectSupportTeamFromProject(project))
    : false;
}

export function isUserAccessCompleteFromProject(project) {
  return hasProject(project);
}

export function isUserProjectPageCompleteFromProject(project) {
  if (!hasProject(project)) {
    return false;
  }

  const userProjectPage = selectUserProjectPageFromProject(project);

  return typeof userProjectPage.defaultPageTask === 'number';
}

export function selectCompletedPercentageFromProject(project, dependencyFlags) {
  if (!project) {
    return 0;
  }

  const {
    hasLocaleTenants = false,
    hasHiringTargets = false,
    hasPayRates = false,
    hasExperimentGroups = false,
  } = dependencyFlags;

  const steps = {
    projectOverview: isProjectOverviewCompleteFromProject(project),
    regAndQual: isRegistrationAndQualificationCompleteFromProject(project),
    recruitment: hasLocaleTenants && hasHiringTargets,
    invoiceAndPayment:
      isInvoiceAndPaymentCompleteFromProject(project) && hasPayRates,
    projectTools: isProjectToolsCompleteFromProject(project),
    userAccess: isUserAccessCompleteFromProject(project) && hasExperimentGroups,
    userProjectPage: isUserProjectPageCompleteFromProject(project),
    supportTeam: isSupportTeamCompleteFromProject(project),
  };

  if (!features.experimentGroups) {
    delete steps.userAccess;
  }

  const completed = Object.values(steps).filter(
    (isComplete) => isComplete === true,
  ).length;

  const availableSteps = STEPS.filter((step) => step.enabled);

  return Math.floor((100 * completed) / (availableSteps.length - 1));
}
