import OverviewAndTools from '../Tabs/OverviewAndTools';
import RegistrationAndQualification from '../Tabs/RegistrationAndQualification';
import Recruitment from '../Tabs/Recruitment';
import InvoicesAndPayment from '../Tabs/InvoicesAndPayment';
import UserProjectPage from '../Tabs/UserProjectPage';
import SupportTeam from '../Tabs/SupportTeam';
import MetricsAndGoals from '../Tabs/MetricsAndGoals';
import ProjectAccess from '../Tabs/ProjectAccess';

export default function getActiveTab(currentTab) {
  switch (currentTab) {
    case 0:
      return { title: 'Overview & Tools', component: OverviewAndTools };

    case 1:
      return {
        title: 'Registration & Qualification',
        component: RegistrationAndQualification,
      };

    case 2:
      return { title: 'Recruitment', component: Recruitment };

    case 3:
      return { title: 'Invoice & Payment', component: InvoicesAndPayment };

    case 4:
      return { title: 'Project Access', component: ProjectAccess };

    case 5:
      return { title: 'User Project Page', component: UserProjectPage };

    case 6:
      return { title: 'Support Team', component: SupportTeam };

    case 7:
      return { title: 'Metrics & Goals', component: MetricsAndGoals };

    default:
      return { title: '', component: null };
  }
}
