import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import sanitizeHtml from 'xss';
import CSSUnReset from './styles';

export default function RichDescription(props) {
  const { html, sanitize = true } = props;

  const cleanHtml = useMemo(() => (sanitize ? sanitizeHtml(html) : html), [
    html,
    sanitize,
  ]);

  return <CSSUnReset dangerouslySetInnerHTML={{ __html: cleanHtml }} />;
}

RichDescription.propTypes = {
  html: PropTypes.string.isRequired,
  sanitize: PropTypes.bool.isRequired,
};
