/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const initialState = {
  canLeave: true,
};

const slice = createSlice({
  name: 'pageLeave',

  initialState,

  reducers: {
    lockLeave(state) {
      state.canLeave = false;
    },

    allowLeave(state) {
      state.canLeave = true;
    },
  },
});

const {
  reducer,
  actions: { lockLeave, allowLeave },
} = slice;

reducer.key = 'pageLeave';

export default reducer;

export { lockLeave, allowLeave };

function selectPageLeaveSlice(state) {
  return state[reducer.key];
}

export function selectCanLeave(state) {
  return get(selectPageLeaveSlice(state), 'canLeave', true);
}
