import { connect } from 'react-redux';
import EmptyCard from './EmptyCard';
import {
  PAGINATED_ENTITIES,
  selectors,
} from '../../../../store/ducks/paginated';

function mapStateToProps(state) {
  const filters = selectors.selectSliceFor(
    PAGINATED_ENTITIES.profileQuestions,
    state,
  );

  return { filters };
}

export default connect(mapStateToProps)(EmptyCard);
