import { call, put, select, takeEvery } from 'redux-saga/effects';
import { undoLastAction, updateOne } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

function* savePayRatesFn(action) {
  const {
    payload: { changes, id },
    meta: { onSuccess, onError, bypassUpdateSaga },
  } = action;

  if (bypassUpdateSaga) {
    return;
  }

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can update a Pay Rate.');
    return;
  }

  if (
    !changes.rate &&
    typeof changes.enabled !== 'boolean' &&
    !changes.workdayTaskId
  ) {
    return;
  }

  // We only update these params
  const patchPayload = {
    ...(changes.rate ? { rateValue: changes.rate } : {}),
    ...(typeof changes.enabled === 'boolean'
      ? { disabled: !changes.enabled }
      : {}),
    ...(changes.workdayTaskId ? { workdayTaskId: changes.workdayTaskId } : {}),
    ...(changes.spokenFluency ? { spokenFluency: changes.spokenFluency } : {}),
    ...(changes.userGroup ? { groupId: changes.userGroup } : {}),
    ...(changes.writtenFluency
      ? { writtenFluency: changes.writtenFluency }
      : {}),
  };

  // We are not using PUT at the moment
  // const putPayload = {
  //   id: null,
  //   projectId: project.id,
  //   hiringTarget: changes.hiringTarget,
  //   writtenFluency: changes.writtenFluency,
  //   spokenFluency: changes.spokenFluency,
  //   certifiedFluency: changes.certifiedFluency,
  //   groupId: changes.userGroup,
  //   taskType: changes.taskType,
  //   workdayTaskId: changes.workdayTaskId,
  //   rateValue: changes.rate,
  //   defaultRate: false,
  //   disabled: typeof changes.enabled === 'boolean' ? !changes.enabled : false,
  //   language: changes.localeLanguage,
  //   languageCountry: changes.localeDialect,
  //   languageTo: changes.localeDialectTo,
  //   languageCountryTo: changes.localeDialectTo,
  //   restrictToCountry: changes.restrictToCountry,
  // };

  try {
    yield call(
      axios.patch,
      ENDPOINTS.V2.PAY_RATES({
        pathVariables: {
          id,
        },
      }),
      patchPayload,
    );

    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error updating a Pay Rate. Please try again.',
    );

    yield put(undoLastAction());
  }
}
export default takeEvery(updateOne.type, savePayRatesFn);
