import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { get, noop } from 'lodash';
import { Portal } from 'react-portal';
import moment from 'moment';
import { useWindowHeight } from '@react-hook/window-size';
import { useFormikContext } from 'formik';
import NoEntries from '../../../NoEntries';
import TabSwitcher from '../../../TabSwitcher';
import Button from '../../../Button';
import Select from '../../../Select';
import TargetBuilder from '../../../TargetBuilder';
import Disabled from '../../../Disabled';
import EllipsesHoverable from '../../../Icons/EllipsesHoverable';
import { ProcessBuilderWindow } from '../../RegistrationAndQualificationForm/styles';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../Popover';
import findLocale from '../../../../store/ducks/options/helpers/localeLanguages';
import {
  TabWrapper,
  TargetGroupContainer,
  TargetGroupLabel,
  InputWrapper,
  FlexItem,
  TopicContainer,
  TopicTitle,
  TopicDescription,
  TopicAnswers,
  CreatedBy,
  PopoverContainer,
  PopoverWarning,
  ErrorTitle,
  Wrapper,
} from './styles';
import pencil from '../../../../assets/icons/pencil.svg';
import close from '../../../../assets/icons/close.svg';
import { ReactComponent as Chevron } from '../../../../assets/icons/double-chevron-right.svg';

export default function TargetScreening(props) {
  const {
    currentUser,
    hasCompleteAdditionalAttributes,
    existingOrNewTabs,
    targetGroupOptions,
    targetBuilderOpen,
    targetGroups,
    modules,
    countries,
    languages,
    loading,
    handleEdit,
    handleDelete,
    handleTabChange,
    handleProfileTargetIdChange,
    handleCreateTargetBuilder,
    handleProcessChange,
    handleCancel,
  } = props;

  const {
    values: { currentTab = 0, profileTargetId, errors, submitCount },
  } = useFormikContext();

  const windowHeight = useWindowHeight();

  const questions = useMemo(() => {
    const filteredTargetGroups = targetGroups.filter(
      (target) => target.id === profileTargetId,
    );

    const targetGroup = get(filteredTargetGroups, [0, 'profileQuestions'], []);

    const parentModules = targetGroup.filter((m) => !m.parentQuestion);
    const childModules = targetGroup.filter((m) => m.parentQuestion);
    const sortedModules = [...parentModules, ...childModules];

    const finalQuestions = [];

    sortedModules.forEach((module) => {
      const filteredModule = modules.find(
        (m) => m.id === module.masterProfileQuestionId,
      );

      if (!filteredModule) {
        return;
      }

      const answerList = module.profileQuestionAnswerList
        .map((profileAnswer) =>
          filteredModule.answerList.find(
            (answer) =>
              answer.id === profileAnswer.masterProfileQuestionAnswerId &&
              profileAnswer.action === 'PASS_THROUGH',
          ),
        )
        .filter((answer) => answer);

      const question = {
        ...filteredModule,
        answerList,
        profileQuestionAnswerList: module.profileQuestionAnswerList,
        disabledSubQuestion: module.disabledSubQuestion,
      };

      if (!question.parentQuestion) {
        finalQuestions[question.id] = [question];
        return;
      }

      let parentQuestionId = get(
        question,
        'parentQuestion.parentQuestionId',
        null,
      );

      let parentQuestion = null;

      while (parentQuestionId && !parentQuestion) {
        // eslint-disable-next-line no-loop-func
        const parent = modules.find((m) => {
          return m.id === parentQuestionId;
        });

        if (parent.parentQuestion) {
          const id = get(parent, 'parentQuestion.parentQuestionId', null);
          parentQuestionId = id || parentQuestionId;
        } else {
          parentQuestion = parent;
        }
      }

      if (finalQuestions[parentQuestion.id]) {
        finalQuestions[parentQuestion.id].push(question);
      }
    });

    return finalQuestions.flat();
  }, [modules, profileTargetId, targetGroups]);

  const targetGroup = useMemo(() => {
    const filteredTargetGroups = targetGroups.filter(
      (target) => target.id === profileTargetId,
    );
    return get(filteredTargetGroups, 0, null);
  }, [profileTargetId, targetGroups]);

  const createdBy = useMemo(() => {
    if (targetGroup) {
      const autor = targetGroup.createdBy || '';
      return `Created by: ${autor}, ${moment(targetGroup.dateCreated).format(
        'MMMM Do YYYY',
      )}`;
    }
    return '';
  }, [targetGroup]);

  const userCanEdit = useMemo(() => {
    const fullName = get(currentUser, 'fullName', null);
    const author = get(targetGroup, 'createdBy', null);
    return (
      (fullName && fullName === author) ||
      process.env.NODE_ENV === 'development'
    );
  }, [currentUser, targetGroup]);

  /* eslint-disable no-plusplus */
  const renderQuestions = (items = []) => {
    let index = 0;
    return items.map((question) => {
      const disabledSubQuestion = get(question, 'disabledSubQuestion', false);
      const isParent = get(question, 'parentQuestion.parentQuestionId', null);
      if (disabledSubQuestion) return null;

      let action = 'Pass';
      let answerList = [];

      const dropDownSelection = [
        'COUNTRY_LIST',
        'LANGUAGE_LIST',
        'DROPDOWN_SELECTION',
      ];

      if (dropDownSelection.includes(question.questionType)) {
        const answer = get(question.profileQuestionAnswerList, 0, null);
        const options = {
          COUNTRY_LIST: get(countries, 'data', []),
          LANGUAGE_LIST: get(languages, 'data', []),
          DROPDOWN_SELECTION: question.answerList,
        };

        action = answer.action === 'PASS_THROUGH' ? 'Pass' : 'Reject';

        if (question.questionType === 'DROPDOWN_SELECTION') {
          answerList = options[question.questionType].map(
            (option) => option.answer,
          );
        } else if (answer) {
          answerList = answer.customAnswer.value.split(',').map((itemCode) => {
            const option = options[question.questionType] || null;

            const item = findLocale(option, ({ value }) => value === itemCode);
            const label = get(item, 'label', null);

            return label;
          });
        }
      } else if (question.questionType === 'DATE_PICKER') {
        answerList = question.profileQuestionAnswerList.map((answer) => {
          const { logic, lb, ub } = answer.customAnswer;
          action = answer.action === 'PASS_THROUGH' ? 'Pass' : 'Reject';

          let lbDate = moment(lb);
          let ubDate = moment(ub);

          lbDate = lbDate.isValid() ? lbDate.format('MMMM Do YYYY') : null;
          ubDate = ubDate.isValid() ? ubDate.format('MMMM Do YYYY') : null;

          return `${logic}: ${lbDate || ''}${ubDate ? ` AND ${ubDate}` : ''}`;
        });
      } else {
        answerList = question.answerList.map((answer) => answer.answer);
      }

      const answers = `${action}: ${answerList.join(', ')}`;

      return (
        <TopicContainer key={question.id}>
          <TopicTitle>
            {isParent ? (
              <Chevron
                style={{
                  verticalAlign: 'middle',
                  margin: '5px 7px 0 16px',
                }}
              />
            ) : (
              `${++index} - `
            )}
            {question.name}
          </TopicTitle>
          <TopicDescription>{question.question}</TopicDescription>
          {answerList.length > 0 && <TopicAnswers>{answers}</TopicAnswers>}
        </TopicContainer>
      );
    });
  };

  const tooltipContent = targetGroup ? (
    <>
      <Disabled
        when={!userCanEdit}
        message="Target audience groups can only be edited by their creators."
      >
        <PopoverItem
          onClick={() => handleEdit(targetGroup)}
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={pencil} alt={'edit icon'} />
          <PopoverText>Edit</PopoverText>
        </PopoverItem>
      </Disabled>
      <PopoverItem
        onClick={() => handleDelete(targetGroup)}
        onKeyDown={noop}
        role="button"
        tabIndex={-1}
      >
        <PopoverIcon src={close} alt={'clear icon'} />
        <PopoverText>Clear</PopoverText>
      </PopoverItem>
    </>
  ) : null;

  return (
    <>
      <TabWrapper>
        <TabSwitcher
          tabs={existingOrNewTabs}
          value={currentTab || 0}
          onChange={handleTabChange}
        />
      </TabWrapper>
      {currentTab === 0 && (
        <>
          <TargetGroupLabel paddingTop={32}>
            Select target audience group
          </TargetGroupLabel>
          <InputWrapper>
            <FlexItem>
              <Select
                name="profileTargetId"
                id="profileTargetId"
                data-testid="project-setup--target-screening--target-audience-group"
                options={targetGroupOptions}
                value={profileTargetId}
                error={submitCount > 0 && errors.profileTargetId}
                onChange={handleProfileTargetIdChange}
                loading={loading}
              />
            </FlexItem>
          </InputWrapper>
          {targetGroup ? (
            <TargetGroupContainer>
              <TargetGroupLabel>{targetGroup.name}</TargetGroupLabel>
              {renderQuestions(questions)}
              <CreatedBy>{createdBy}</CreatedBy>
              <div className="target_group_actions">
                <Popover content={tooltipContent} placement="bottom-end">
                  <EllipsesHoverable />
                </Popover>
              </div>
            </TargetGroupContainer>
          ) : (
            <NoEntries label="No group selected" margin={'16px 0'} />
          )}
          {!hasCompleteAdditionalAttributes && (
            <PopoverContainer>
              <Wrapper>
                <PopoverWarning>
                  <ErrorTitle>Attention</ErrorTitle>
                  You need to to add the Complete Additional Attributes module
                  into your registration process in order to collect profile
                  data from workers.
                </PopoverWarning>
              </Wrapper>
            </PopoverContainer>
          )}
        </>
      )}
      {currentTab === 1 && (
        <>
          <NoEntries label="Not set" />
          <Button onClick={handleCreateTargetBuilder}>
            Configure Target Audience
          </Button>
        </>
      )}
      {targetBuilderOpen && (
        <Portal>
          <ProcessBuilderWindow style={{ height: windowHeight }}>
            <TargetBuilder
              onProcessChange={handleProcessChange}
              onCancel={handleCancel}
            />
          </ProcessBuilderWindow>
        </Portal>
      )}
    </>
  );
}

TargetScreening.propTypes = {
  currentUser: PropTypes.objectOf(PropTypes.any).isRequired,
  hasCompleteAdditionalAttributes: PropTypes.bool.isRequired,
  existingOrNewTabs: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string }),
  ).isRequired,
  targetGroupOptions: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.any }),
  ).isRequired,
  targetBuilderOpen: PropTypes.bool.isRequired,
  targetGroups: PropTypes.arrayOf(PropTypes.object).isRequired,
  modules: PropTypes.arrayOf(PropTypes.object).isRequired,
  countries: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        label: PropTypes.string,
        value: PropTypes.string,
      }),
    ),
  }).isRequired,
  languages: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        label: PropTypes.string,
        value: PropTypes.string,
      }),
    ),
  }).isRequired,
  loading: PropTypes.bool.isRequired,
  handleEdit: PropTypes.func.isRequired,
  handleDelete: PropTypes.func.isRequired,
  handleProfileTargetIdChange: PropTypes.func.isRequired,
  handleTabChange: PropTypes.func.isRequired,
  handleCreateTargetBuilder: PropTypes.func.isRequired,
  handleProcessChange: PropTypes.func.isRequired,
  handleCancel: PropTypes.func.isRequired,
};

TargetScreening.defaultProps = {};
