import { useCallback } from 'react';
import withHooks from 'hook-hoc';
import ActionCell from './ActionCell';
import { useModal } from '../../../../components/Modal';

function useHooksAsProps() {
  const [modalProps, modalControls] = useModal();

  const handleOpenCloneModal = useCallback(
    (projectId) => {
      modalControls.openModal({ projectId });
    },
    [modalControls],
  );

  return { handleOpenCloneModal, modalProps, modalControls };
}

export default withHooks(useHooksAsProps)(ActionCell);
