import React, { useRef, useCallback } from 'react';
import PropTypes from 'prop-types';
import { IntelligentAttributesIframeWrapper } from './styles';
import PostMessageLink from '../PostMessageLink';

export default function LegacyIntelligentAttributes(props) {
  const baseUrl = process.env.REACT_APP_QRP_URL;
  const { projectId } = props;

  const url = `${baseUrl}/core/partners/project/view/${projectId}?showOnlyIntelligentAttributes=true`;
  const frameRef = useRef();

  const onFrameLoad = useCallback(() => {
    if (frameRef.current && frameRef.current.contentWindow) {
      let doc = null;
      try {
        doc = frameRef.current.contentWindow.document;
      } catch (e) {
        if (process.env.NODE_ENV === 'development') {
          doc = document;
        }
      }
      const bodyRef = doc.body;
      const htmlRef = doc.documentElement;

      const height = Math.max(
        bodyRef.scrollHeight,
        bodyRef.offsetHeight,
        htmlRef.clientHeight,
        htmlRef.scrollHeight,
        htmlRef.offsetHeight,
      );
      frameRef.current.style.height = `${height}px`;
    }
  }, []);

  return (
    <IntelligentAttributesIframeWrapper>
      <iframe
        ref={frameRef}
        title="Intelligent Attributes"
        name="intelligent-attr-iframe"
        src={url}
        onLoad={onFrameLoad}
      />
      <p>
        View{' '}
        <PostMessageLink to="/qrp/core/global_intelligentAttributes">
          Global Intelligent Attributes
        </PostMessageLink>
        .{' '}
      </p>
    </IntelligentAttributesIframeWrapper>
  );
}

LegacyIntelligentAttributes.propTypes = {
  projectId: PropTypes.number.isRequired,
};
