import { call, put, takeLeading } from 'redux-saga/effects';
import axios from '../../../services/axios';
import ENDPOINTS from '../../../constants/endpoints';
import { fetchOptionsSucceded, fetchOptionsFailed, fetchOptions } from '.';

function* fetchOptionsSaga() {
  try {
    const response = yield call(axios, ENDPOINTS.V2.REGISTRATION_PROCESSES());
    yield put(fetchOptionsSucceded(response.data));
  } catch (error) {
    yield put(fetchOptionsFailed(error));
  }
}

export default takeLeading(fetchOptions.type, fetchOptionsSaga);
