import React, { useCallback, useEffect, useMemo } from 'react';
import { useFormikContext } from 'formik';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';

import InformationTooltip from '../../ProcessBuilder/InformationTooltip';
import { CheckGroup, CheckAltMessage } from '../ProjectToolsForm/styles';
import {
  Li,
  Label,
  Section,
  SectionDescription,
  SectionTitle,
} from '../../../styles';
import { useModal } from '../../Modal';
import Checkbox from '../../Checkbox';
import Button from '../../Button';
import {
  FlexItem,
  ButtonWrapper,
  OrLabel,
} from '../RegistrationAndQualificationForm/styles';
import Input from '../../Input';
import Select from '../../Select';
import Textarea from '../../Textarea';
import PayratesSetup from './PayratesSetup';
import PayratesModal from './PayratesModal';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import { useErrorPopover } from '../../ErrorPopover';
import useFetchUserGroups from '../../../store/ducks/options/helpers/fetchUserGroups';
import InvoiceConfiguration from '../../InvoiceConfiguration';
import { selectProject } from '../../../store/ducks/project';
import { getCurrentUser } from '../../../store/ducks/security';
import { ROLES_CAN_EDIT_PAYRATE } from '../../../constants/rolesCanEditPayRate';

export default function InvoiceAndPaymentForm(props) {
  const { projectId } = props;

  const dispatch = useDispatch();

  const {
    handleChange,
    setFieldValue,
    handleBlur,
    values,
    touched,
    submitForm,
    errors,
    submitCount,
  } = useFormikContext();

  const project = useSelector(selectProject);

  const [modalProps, modalControls] = useModal();

  const currentUser = useSelector(getCurrentUser());

  const handlePayratesChange = useCallback(
    (value) => setFieldValue('payrates', value),
    [setFieldValue],
  );

  const handleRateTypeChange = useCallback(
    ({ value }) => {
      submitForm();
      return setFieldValue('rateType', value);
    },
    [setFieldValue, submitForm],
  );

  const handleBusinessUnitChange = useCallback(
    ({ value }) => setFieldValue('businessUnit', value),
    [setFieldValue],
  );

  const addPayrate = useCallback(
    (value) => {
      if (Array.isArray(value)) {
        setFieldValue('payrates', [...(values.payrates || []), ...value]);
      } else if ((values.payrates || []).map((p) => p.id).includes(value.id)) {
        // we are editing!
        const index = values.payrates.findIndex((p) => p.id === value.id);
        setFieldValue('payrates', [
          ...values.payrates.slice(0, index),
          value,
          ...values.payrates.slice(index + 1, values.payrates.length),
        ]);
      } else {
        setFieldValue('payrates', [...(values.payrates || []), value]);
      }
    },
    [setFieldValue, values.payrates],
  );

  useEffect(() => {
    dispatch(fetch(ENTITIES.businessUnits));
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.fluencies));
    dispatch(fetch(ENTITIES.localeLanguages));
    dispatch(fetch(ENTITIES.rateTypes));
    dispatch(fetch(ENTITIES.taskTypes));
  }, [dispatch]);

  useFetchUserGroups();

  const rateTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.rateTypes),
  );

  const businessUnits = useSelector(
    selectors.createSelectSliceFor(ENTITIES.businessUnits),
  );

  const [showModalError, hideModalError, errorPopoverProps] = useErrorPopover();

  const canEditRateType = useMemo(() => {
    if (project) {
      return project.status === 'DRAFT' || project.status === 'READY';
    }

    return false;
  }, [project]);

  const intersection =
    currentUser.roles.filter((element) =>
      ROLES_CAN_EDIT_PAYRATE.includes(element),
    ).length > 0;

  const handleWorkdayTaskIdChange = useCallback(
    ({ target: { value } }) => {
      const formattedValue = value ? value.toUpperCase() : '';

      if (formattedValue.startsWith('P') && formattedValue.length > 6) {
        return;
      }

      if (formattedValue.startsWith('CR') && formattedValue.length > 7) {
        return;
      }

      if (formattedValue.startsWith('LR') && formattedValue.length > 7) {
        return;
      }

      setFieldValue('workdayTaskId', formattedValue);
    },
    [setFieldValue],
  );

  return (
    <>
      <Section marginBottom={48}>
        <Row>
          <Col col={6}>
            <Label htmlFor="workdayTaskId">
              PROJECT WORKDAY TASK ID
              <InformationTooltip
                title={
                  <ul>
                    Format Options: <br />
                    <Li>P##### (5 digits)</Li>
                    <Li>CR##### (5 digits) </Li>
                    <Li>LR##### (5 digits) </Li>
                  </ul>
                }
              />
            </Label>
            <FlexItem>
              <Input
                name="workdayTaskId"
                onChange={handleWorkdayTaskIdChange}
                onBlur={handleBlur}
                id="workdayTaskId"
                placeholder="Workday task ID"
                value={values.workdayTaskId}
                error={touched.workdayTaskId && errors.workdayTaskId}
                data-testid="project-setup--invoice-n-payment--workday-task-id-input"
              />
            </FlexItem>
          </Col>
          <Col col={3}>
            <ButtonWrapper>
              <OrLabel>or</OrLabel>
              <Button disabled>Create New</Button>
            </ButtonWrapper>
          </Col>
        </Row>
      </Section>

      <Section marginBottom="32px">
        <Row>
          <Col col={6}>
            <Label htmlFor="rateType">Rate Type</Label>
            <Select
              name="rateType"
              id="rateType"
              options={rateTypes.data}
              loading={rateTypes.loading}
              error={rateTypes.error || (submitCount >= 1 && errors.rateType)}
              onChange={handleRateTypeChange}
              value={values.rateType}
              disabled={!canEditRateType}
              data-testid="project-setup--invoice-n-payment--select-rate-type"
            />
          </Col>
          <Col col={6}>
            <Label htmlFor="businessUnit">Project Business Unit</Label>
            <Select
              onChange={handleBusinessUnitChange}
              name="businessUnit"
              id="businessUnit"
              options={businessUnits.data}
              loading={businessUnits.loading}
              error={
                businessUnits.error || (submitCount >= 1 && errors.businessUnit)
              }
              value={values.businessUnit}
              data-testid="project-setup--invoice-n-payment--select-business-unit"
            />
          </Col>
        </Row>
      </Section>

      <Section marginBottom="46px">
        <SectionTitle>Pay Rates Setup</SectionTitle>
        <SectionDescription>
          Define the base rate per hour for work done in this project depending
          on Locale and Group.
        </SectionDescription>

        <PayratesSetup
          value={values.payrates}
          onChange={handlePayratesChange}
          modalControls={modalControls}
          enabled={!!(values.businessUnit && values.rateType)}
          submitCount={submitCount}
          validate={submitForm}
          canEditPayRate={intersection}
        />

        <PayratesModal
          modalProps={modalProps}
          modalControls={modalControls}
          addPayrate={addPayrate}
          businessUnit={values.businessUnit}
          rateType={values.rateType}
          showError={showModalError}
          hideError={hideModalError}
          errorPopoverProps={errorPopoverProps}
        />
      </Section>

      <Section marginBottom={16} maxWidth={512}>
        <CheckGroup>
          <Checkbox
            name="hasProductivityData"
            onChange={handleChange}
            checked={values.hasProductivityData}
            label="Available Productivity Data"
            data-testid="project-setup--invoice-n-payment--has-nproductivity-data-toggle"
          />
          <CheckAltMessage>
            If checked, it will be used for prompting the user to enter task
            timings for the task types in the data file.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      <Section marginBottom="46px" marginTop="48px">
        <SectionTitle>Invoice Configuration</SectionTitle>
        <SectionDescription>
          Configure invoice type, approval type and payment.
        </SectionDescription>

        <InvoiceConfiguration
          projectId={projectId}
          hasProductivityData={
            !!values.hasProductivityData && !!project.productivityData
          }
        />
      </Section>

      <Section marginBottom={16}>
        <CheckGroup>
          <Checkbox
            name="hasNotesToFinance"
            onChange={handleChange}
            checked={values.hasNotesToFinance}
            label="Add Notes to Finance"
            data-testid="project-setup--invoice-n-payment--has-notes-to-finance-toggle"
          />
        </CheckGroup>
      </Section>

      {values.hasNotesToFinance && (
        <Textarea
          onChange={handleChange}
          value={values.notesToFinance}
          name="notesToFinance"
          placeholder="Write notes to finance department"
          data-testid="project-setup--invoice-n-payment--notes-to-finance-input"
          rows={6}
          error={submitCount >= 1 && errors.notesToFinance}
        />
      )}
    </>
  );
}

InvoiceAndPaymentForm.propTypes = {
  projectId: PropTypes.number,
};

InvoiceAndPaymentForm.defaultProps = {
  projectId: undefined,
};
