import React, { useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import { Feature } from 'flagged';
import Divider from '../../../components/Divider/Divider';
import OverviewPreview from '../../ProjectSetup/Steps/Preview/Sections/ProjectOverview';
import RegistrationAndQualificationPreview from '../../ProjectSetup/Steps/Preview/Sections/RegistrationAndQualification';
import ProjectToolsPreview from '../../ProjectSetup/Steps/Preview/Sections/ProjectTools';
import RecruitmentPreview from '../../ProjectSetup/Steps/Preview/Sections/Recruitment';
import InvoicePaymentPreview from '../../ProjectSetup/Steps/Preview/Sections/InvoicePayment';
import UserProjectPagePreview from '../../ProjectSetup/Steps/Preview/Sections/UserProjectPage';
import SupportTeamPreview from '../../ProjectSetup/Steps/Preview/Sections/SupportTeam';
import ProjectAccessPreview from '../../ProjectSetup/Steps/Preview/Sections/ProjectAccess';
import { Section } from '../../../styles';
import ActionFooter from '../../../components/ActionFooter';
import { FloatingFooter, ProjectPreviewContainer } from './styles';
import {
  selectProject,
  selectProjectStatus,
  selectLoading,
} from '../../../store/ducks/project';
import Loading from '../../ProjectSetup/Loading';
import legacyIntegration from '../../../helpers/legacy-integration';
import { PreviewContextProvider } from '../../../components/ProjectPreviews/components/PreviewSectionText';

export default function ProjectPreview(props) {
  const { closeModal } = props;

  const project = useSelector(selectProject);
  const projectLoading = useSelector(selectLoading);

  const resumeSetup = useCallback(() => {
    if (project) {
      legacyIntegration.informLoadProjectSetup(parseInt(project.id, 10));
    }
  }, [project]);

  const status = useSelector(selectProjectStatus);

  const buttons = useMemo(() => {
    const closeButton = {
      text: 'Close',
      type: 'secondary',
      onClick: closeModal,
    };

    const resumeSetupButton = {
      text: 'Resume Setup',
      type: 'primary',
      onClick: resumeSetup,
    };

    if (status === 'DRAFT') {
      return [closeButton, resumeSetupButton];
    }

    return [closeButton];
  }, [closeModal, resumeSetup, status]);

  if (projectLoading) {
    return <Loading />;
  }

  return (
    <PreviewContextProvider value={{ validateEmpty: true }}>
      <ProjectPreviewContainer>
        <Section marginBottom="40px" marginTop="41px">
          <OverviewPreview navigateTo={resumeSetup} renderedIn="project-view" />
        </Section>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <RegistrationAndQualificationPreview
            navigateTo={resumeSetup}
            renderedIn="project-view"
          />
        </Section>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <RecruitmentPreview
            navigateTo={resumeSetup}
            renderedIn="project-preview-modal"
          />
        </Section>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <InvoicePaymentPreview
            navigateTo={resumeSetup}
            renderedIn="project-preview-modal"
          />
        </Section>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <ProjectToolsPreview
            navigateTo={resumeSetup}
            renderedIn="project-view"
          />
        </Section>

        <Divider />

        <Feature name="experimentGroups">
          <Section marginBottom="40px" marginTop="41px">
            <ProjectAccessPreview
              navigateTo={resumeSetup}
              renderedIn="project-view"
            />
          </Section>
        </Feature>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <UserProjectPagePreview
            navigateTo={resumeSetup}
            renderedIn="project-preview-modal"
          />
        </Section>

        <Divider />

        <Section marginBottom="40px" marginTop="41px">
          <SupportTeamPreview
            navigateTo={resumeSetup}
            renderedIn="project-view"
          />
        </Section>
      </ProjectPreviewContainer>
      <FloatingFooter>
        <ActionFooter rightButtons={buttons} />
      </FloatingFooter>
    </PreviewContextProvider>
  );
}

ProjectPreview.propTypes = {
  closeModal: PropTypes.func.isRequired,
};
