import React, { useCallback, useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import {
  ResourceBlockActions,
  ResourceBlockContainer,
  ResourceBlockSecondaryInfo,
  ResourceBlockType,
} from './styles';
import { Label, Section } from '../../../../../styles';
import { ReactComponent as Check } from '../../../../../assets/icons/mappings/small-check.svg';
import close from '../../../../../assets/icons/close.svg';
import eye from '../../../../../assets/icons/eye.svg';
import pencil from '../../../../../assets/icons/pencil.svg';
import copy from '../../../../../assets/icons/copy.svg';
import Popover, {
  PopoverIcon,
  PopoverItem,
  PopoverText,
} from '../../../../Popover';
import SettingsHoverable, {
  SettingsStyled,
} from '../../../../Icons/SettingsHoverable';
import { createKeydownHandler } from '../../../../../helpers/a11y';
import Disabled from '../../../../Disabled';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../store/ducks/editableEntities';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
  selectors,
} from '../../../../../store/ducks/options';
import { findById } from '../../../../../helpers/find';

export default function ResourceBlock(props) {
  const { resource, mode, projectId } = props;

  const {
    id,
    type,
    resourceId,
    maxQuizAttempts,
    subsetSize,
    passingScore,
    allowActiveUserAccess,
    active,
    allowQualifyingApplicantAccess,
    usageType,
    questionsShuffled,
    showFeedback,
    requireAcknowledgement,
  } = resource;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.academies));
    dispatch(fetch(ENTITIES.documents));
    dispatch(fetch(ENTITIES.quizzes));
    dispatch(fetch(ENTITIES.surveys));
  }, [dispatch]);

  const academies = useSelector(
    selectors.createSelectDataFor(ENTITIES.academies),
  );

  const documents = useSelector(
    selectors.createSelectDataFor(ENTITIES.documents),
  );

  const quizzes = useSelector(selectors.createSelectDataFor(ENTITIES.quizzes));
  const surveys = useSelector(selectors.createSelectDataFor(ENTITIES.surveys));

  const typeName = useMemo(() => {
    switch (type) {
      case ENTITY_METADATA.projectResources.types.quiz:
        return 'Quiz';

      case ENTITY_METADATA.projectResources.types.survey:
        return 'Survey';

      case ENTITY_METADATA.projectResources.types.academy:
        return 'Academy';

      case ENTITY_METADATA.projectResources.types.document:
      default:
        return 'Document';
    }
  }, [type]);

  const name = useMemo(() => {
    switch (type) {
      case ENTITY_METADATA.projectResources.types.quiz:
        return (findById(quizzes, resourceId) || {}).name || '-';

      case ENTITY_METADATA.projectResources.types.survey:
        return (findById(surveys, resourceId) || {}).name || '-';

      case ENTITY_METADATA.projectResources.types.academy:
        return (findById(academies, resourceId) || {}).name || '-';

      case ENTITY_METADATA.projectResources.types.document:
      default:
        return (findById(documents, resourceId) || {}).name || '-';
    }
  }, [academies, documents, quizzes, resourceId, surveys, type]);

  const access = useMemo(() => {
    if (type === ENTITY_METADATA.projectResources.types.academy) {
      return [
        allowActiveUserAccess && 'Active users',
        allowQualifyingApplicantAccess && 'Qualifying applicants',
      ]
        .filter(Boolean)
        .join(' and ');
    }

    return '';
  }, [allowActiveUserAccess, allowQualifyingApplicantAccess, type]);

  const blockInformation = useMemo(() => {
    switch (type) {
      case ENTITY_METADATA.projectResources.types.quiz:
        return `Max attempts: ${
          maxQuizAttempts === -1 ? 'Unlimited' : maxQuizAttempts
        }, Subset size: ${
          subsetSize === -1 ? 'All questions' : subsetSize
        }, Passing score: ${passingScore}%`;

      case ENTITY_METADATA.projectResources.types.survey:
        return `Max attempts: ${
          maxQuizAttempts === -1 ? 'Unlimited' : maxQuizAttempts
        }, Subset size: ${subsetSize === -1 ? 'All questions' : subsetSize}`;

      case ENTITY_METADATA.projectResources.types.academy:
        return `Access: ${access}`;

      case ENTITY_METADATA.projectResources.types.document:
      default:
        return (
          <>
            Document type:{' '}
            {usageType === 'GUIDELINE' ? 'Guideline' : 'Upskilling'}
            <br />
            {allowQualifyingApplicantAccess &&
              active &&
              'Access: Active users and Qualifying applicants'}
          </>
        );
    }
  }, [
    access,
    active,
    allowQualifyingApplicantAccess,
    maxQuizAttempts,
    passingScore,
    subsetSize,
    type,
    usageType,
  ]);

  const copyResource = useCallback(
    () =>
      dispatch(
        editor.actions.copy(EDITABLE_ENTITIES.PROJECT_RESOURCES, resource),
      ),
    [dispatch, resource],
  );

  const editResource = useCallback(
    () =>
      dispatch(
        editor.actions.edit(EDITABLE_ENTITIES.PROJECT_RESOURCES, resource),
      ),
    [dispatch, resource],
  );

  const deleteResource = useCallback(
    () =>
      dispatch(
        editor.actions.delete(EDITABLE_ENTITIES.PROJECT_RESOURCES, id, {
          projectId,
          resourceType: type,
          thenPut: fetch({
            optionsFor: ENTITIES.projectResources,
            nestedKey: projectId,
          }),
        }),
      ),
    [dispatch, id, projectId, type],
  );

  const poppoverContent = (
    <>
      <Disabled when message="Will be available soon">
        <PopoverItem
          onClick={() => {}}
          onKeyDown={createKeydownHandler(() => {})}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={eye} alt={'preview icon'} />
          <PopoverText>Preview</PopoverText>
        </PopoverItem>
      </Disabled>

      <PopoverItem
        onClick={copyResource}
        onKeyDown={createKeydownHandler(copyResource)}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={copy} alt={'copy icon'} />
        <PopoverText>Copy</PopoverText>
      </PopoverItem>

      <PopoverItem
        onClick={editResource}
        onKeyDown={createKeydownHandler(editResource)}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={pencil} alt={'edit icon'} />
        <PopoverText>Edit</PopoverText>
      </PopoverItem>

      <PopoverItem
        onClick={deleteResource}
        onKeyDown={createKeydownHandler(deleteResource)}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={close} alt={'delete icon'} />
        <PopoverText>Delete</PopoverText>
      </PopoverItem>
    </>
  );

  return (
    <ResourceBlockContainer>
      <Section marginBottom={12} marginTop={0}>
        <Label>
          <ResourceBlockType>{`${typeName}`.toUpperCase()}</ResourceBlockType>
          {` | `}
          {name}
        </Label>
      </Section>

      <Section marginBottom={8} marginTop={0}>
        {blockInformation}
      </Section>

      {questionsShuffled && (
        <ResourceBlockSecondaryInfo>
          <Check /> Shuffle questions
        </ResourceBlockSecondaryInfo>
      )}

      {showFeedback && (
        <ResourceBlockSecondaryInfo>
          <Check /> Show question feedback
        </ResourceBlockSecondaryInfo>
      )}

      {requireAcknowledgement && (
        <ResourceBlockSecondaryInfo>
          <Check /> Require acknowledgement
        </ResourceBlockSecondaryInfo>
      )}

      <ResourceBlockActions>
        {mode === 'edit' && (
          <Popover content={poppoverContent} placement="bottom-end">
            <SettingsStyled>
              <SettingsHoverable />
            </SettingsStyled>
          </Popover>
        )}
      </ResourceBlockActions>
    </ResourceBlockContainer>
  );
}

ResourceBlock.propTypes = {
  resource: PropTypes.shape({
    id: PropTypes.number.isRequired,
    type: PropTypes.oneOf(['quiz', 'survey', 'document', 'academy']).isRequired,
    resourceId: PropTypes.number.isRequired,
    maxQuizAttempts: PropTypes.number,
    subsetSize: PropTypes.number,
    passingScore: PropTypes.number,
    allowActiveUserAccess: PropTypes.bool,
    active: PropTypes.bool,
    allowQualifyingApplicantAccess: PropTypes.bool,
    usageType: PropTypes.oneOf(['TRAINING', 'GUIDELINE']).isRequired,
    questionsShuffled: PropTypes.bool,
    showFeedback: PropTypes.bool,
    requireAcknowledgement: PropTypes.bool,
  }).isRequired,
  mode: PropTypes.oneOf(['view', 'edit']).isRequired,
  projectId: PropTypes.number.isRequired,
};
