import React from 'react';
import PropTypes from 'prop-types';
import { Label } from '../../../../styles';
import { ExpandedWrapper } from './styles';

export default function ExpandedCell(props) {
  const { isExpanded, label, children } = props;

  if (!isExpanded) {
    return null;
  }

  return (
    <ExpandedWrapper>
      <Label>{label}</Label>
      {children}
    </ExpandedWrapper>
  );
}

ExpandedCell.propTypes = {
  isExpanded: PropTypes.bool,
  label: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

ExpandedCell.defaultProps = {
  label: null,
  isExpanded: false,
};
