import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import { Target as OriginalTarget } from '../../../../../../components/ProjectForms/RecruitmentForm/LocaleTenants/styles';
import RecruitmentPreview from '../../../../../../components/ProjectPreviews/RecruitmentPreview';
import AutoScreenHeader from '../../../../../../components/ProjectForms/RecruitmentForm/AutoScreening/Header';

const Target = styled(OriginalTarget)`
  color: ${(props) => props.theme.colors.charcoal};
`;

// Not a Functional Component!
// eslint-disable-next-line react/prop-types
export function renderTarget({ cell: { value } }) {
  return <Target>{value}</Target>;
}

export default function Recruitment(props) {
  const { navigateTo, renderedIn } = props;

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.RECRUITMENT);
  }, [navigateTo]);

  return (
    <RecruitmentPreview
      editStep={editStep}
      stepNumber={STEP_NUMBER.RECRUITMENT + 1}
      renderedIn={renderedIn}
      autoScreenHeader={<AutoScreenHeader />}
    />
  );
}

Recruitment.propTypes = {
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf([
    'project-view',
    'project-setup',
    'project-preview-modal',
  ]),
};

Recruitment.defaultProps = {
  renderedIn: 'project-setup',
};
