import React from 'react';
import PropTypes from 'prop-types';
import ExpandedCell from './ExpandedCell';
import DuplicatesList from '../../../../DuplicatesList';
import { Text } from './styles';

export default function AttributeCell({ row }) {
  const {
    isExpanded,
    original: { userId },
  } = row;

  return (
    <div style={{ width: '600%' }}>
      <Text>{userId}</Text>
      <ExpandedCell isExpanded={isExpanded}>
        <DuplicatesList data={row.original} />
      </ExpandedCell>
    </div>
  );
}

AttributeCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({
      userId: PropTypes.string,
    }),
  }).isRequired,
};
