import styled from 'styled-components';
import { Label } from '../../styles';

export const Container = styled.div`
  width: 100%;
  height: ${(props) => (props.keepInDocumentFlow ? 'unset' : '1px')};
  position: ${(props) => (props.fixedToFooter ? 'fixed' : 'relative')};
  z-index: ${(props) => (props.keepInDocumentFlow ? '1' : '1001')};
  bottom: ${(props) =>
    props.keepInDocumentFlow || props.fixedToFooter ? '0px' : '-40px'};
  ${(props) => props.fixedToFooter && { right: '0', left: '0' }}
`;

export const Wrapper = styled.div`
  min-height: 76px;
  width: 100%;
  position: ${(props) => (props.keepInDocumentFlow ? 'relative' : 'absolute')};
  bottom: 0px;
  left: 0px;
  animation: fadeInDown 225ms ease;

  @keyframes fadeInDown {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
`;

export const Popover = styled.div`
  border-radius: 4px;
  box-shadow: 1px 1px 5px 0 ${(props) => props.theme.colors.lightPeriwinkle};
  border: solid 1px ${(props) => props.theme.colors.error};
  padding: 16px;
  background-color: ${(props) => props.theme.colors.errorLight};
  color: ${(props) => props.theme.colors.error};
  position: relative;
  line-height: 1.5;
`;

export const ErrorTitle = styled(Label)`
  color: ${(props) => props.theme.colors.error};
  cursor: default;
`;

export const Img = styled.img`
  position: absolute;
  right: 16px;
  top: 16px;
  cursor: pointer;
`;
