import React from 'react';
import PropTypes from 'prop-types';

// styles
import { LoadingText, Dots, Dot } from './styles';

const LOADER_DOTS_COUNT = 3;

const loaderArr = Array(LOADER_DOTS_COUNT)
  .fill(0)
  .map((x, index) => index);

export default function Loader({ text, dotType }) {
  return (
    <div aria-busy="true" aria-label="Loading">
      {text && <LoadingText>{text}</LoadingText>}
      <Dots>
        {loaderArr.map((value) => (
          <Dot key={value} dotType={dotType} />
        ))}
      </Dots>
    </div>
  );
}

Loader.propTypes = {
  text: PropTypes.string,
  dotType: PropTypes.string,
};

Loader.defaultProps = {
  text: undefined,
  dotType: 'primary',
};
