import * as Yup from 'yup';
import createArrayValidation from '../../../helpers/array-validation';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

function transformError(error, value, index) {
  if (error.path === 'member') {
    return new Yup.ValidationError(
      requiredMessage`Member name`,
      value,
      `memberInput${index}`,
    );
  }

  return new Yup.ValidationError(
    requiredMessage`User Role`,
    value,
    `roleInput${index}`,
  );
}

const validateArray = createArrayValidation(
  {
    role: Yup.string().required(),
    member: Yup.number().required(),
  },
  transformError,
);

export default Yup.object()
  .strict()
  .shape({
    supportTeam: Yup.array().test('memberAndRole', '', validateArray),
  });
