import styled from 'styled-components';

function getBorderColor(props) {
  const {
    theme: { colors },
    error,
    focused,
  } = props;

  if (focused) {
    return colors.azure;
  }

  if (error) {
    return colors.error;
  }

  return colors.lightBlueGrey;
}

function getIconColor(props) {
  const {
    theme: { colors },
    error,
    focused,
  } = props;

  if (focused) {
    return colors.azure;
  }

  if (error) {
    return colors.error;
  }

  return colors.blueGrey;
}

function getDisabled(props) {
  const { disabled } = props;
  if (disabled) {
    return `default`;
  }
  return `pointer`;
}

export default styled.div`
  height: 36px;
  border-radius: 4px;
  width: 100%;
  border: solid 1px ${getBorderColor};
  font-size: ${(props) => props.theme.sizes.p};
  line-height: 1.33;
  color: ${(props) => props.theme.colors.charcoal};
  position: relative;
  cursor: ${getDisabled};

  &::placeholder {
    color: ${(props) => props.theme.colors.lightGreyBlue};
    font-family: Lato;
  }
`;

export const Icon = styled.div`
  position: absolute;
  top: 0;
  right: 0px;
  padding: 7px 8px;
  border-left: solid 1px ${getBorderColor};
  fill: currentColor;
  color: ${getIconColor};
  cursor: ${getDisabled};
`;
