import { call, put, select, takeLatest } from 'redux-saga/effects';
import { selectProject } from '../project';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import * as hiringTargetDuck from '../hiringTargets';
import * as demographicRequirements from '../demographicRequirements';
import * as aggreationConverter from './aggregatedRequirementsConverter';
import * as byGroupConverter from './requirementsByGroupConverter';
import * as projectDemographicRequirements from '.';
import { findById } from '../../../helpers/find';

function* saveDemographicsSaga(action) {
  const project = yield select(selectProject);

  const {
    payload,
    meta: { onSuccess, onError, bypassUpdateSaga, projectId },
  } = action;

  if (bypassUpdateSaga) {
    return;
  }

  if (!project || !project.id) {
    yield put(
      projectDemographicRequirements.fetchDemographicsFailed(
        new Error('Failed to fetch Pay Rates, missing project id.'),
      ),
    );
    return;
  }

  const demographicType = yield select(
    projectDemographicRequirements.selectDemographicType,
  );

  const hiringTargets = yield select(hiringTargetDuck.selectAll);
  const viewAs = yield select(projectDemographicRequirements.selectViewAs);

  const allRequirements = yield select(demographicRequirements.selectAll);
  const projectRequirements = yield select(
    projectDemographicRequirements.selectRequirements,
  );

  const requirements = projectRequirements.map(({ value }) =>
    findById(allRequirements, value),
  );

  try {
    let demographicRequirementsDTO;

    if (
      demographicType ===
      projectDemographicRequirements.DEMOGRAPHIC_TYPE.AGGREGATED
    ) {
      demographicRequirementsDTO = aggreationConverter.convertToDTO(
        requirements,
        payload,
        hiringTargets,
      );
    } else {
      demographicRequirementsDTO = byGroupConverter.convertToDTO(
        payload,
        hiringTargets,
      );
    }

    yield call(axios.post, endpoints.V2.DEMOGRAPHICS_REQUIREMENTS(), {
      projectId: projectId || project.id,
      demographicTargetType: viewAs,
      demographicRequirements: demographicRequirementsDTO,
    });

    onSuccess();
  } catch (error) {
    onError(error.message);
    yield put(projectDemographicRequirements.undoSetRequirements());
  }
}

export default takeLatest(
  [
    projectDemographicRequirements.setAggregatedRequirements.type,
    projectDemographicRequirements.setRequirementsByGroup.type,
  ],
  saveDemographicsSaga,
);
