import React, { useCallback, useEffect, useRef } from 'react';
import { Shimmer } from 'react-shimmer';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import scrollIntoView from 'scroll-into-view-if-needed';
import {
  ProcessSelectedContainer,
  ProcessName,
  ProcessStep,
  ProcessCreatedBy,
  FlexShimmer,
  ShimmerSpacer,
  ArrowContainer,
} from './styles';
import Popover, { PopoverItem, PopoverIcon, PopoverText } from '../Popover';
import SettingsHoverable from '../Icons/SettingsHoverable';
import pencil from '../../assets/icons/pencil.svg';
import Modal from '../Modal';
import arrowRightIcon from '../../assets/icons/icon-right-arrow.svg';
import {
  createSelectInvoiceConfiguration,
  fetchInvoiceConfiguration,
  openModal,
  closeModal as closeInvoiceConfigModal,
  selectModalState,
  INVOICE_TYPES_NAMES,
} from '../../store/ducks/invoiceConfiguration';
import { useAccessibleCallback } from '../../helpers/a11y';
import Button from '../Button/Button';
import InvoiceTemplateModal from './InvoiceTemplateModal';
import { ENTITIES, fetch, selectors } from '../../store/ducks/options';
import { findByValue } from '../../helpers/find';
import NoEntries from '../NoEntries';
import Disabled from '../Disabled/Disabled';
import { userHasAnyRole } from '../../store/ducks/security';
import ROLES from '../../constants/roles';
import { PreviewSectionText } from '../ProjectPreviews/styles';
import { Text } from '../../styles';

export default function InvoiceConfiguration(props) {
  const {
    projectId,
    hasProductivityData,
    showErrorWhenEmpty = false,
    previewMode = false,
  } = props;

  const isFromFinanceTeam = useSelector(
    userHasAnyRole(ROLES.ADMIN, ROLES.FINANCE, ROLES.FINANCE_SUPER),
  );

  const invoiceConfig = useSelector(
    createSelectInvoiceConfiguration(projectId),
  );

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.invoiceGenerationTypes));
  }, [dispatch]);

  const invoiceGenerationTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.invoiceGenerationTypes),
  );

  useEffect(() => {
    if (projectId) {
      dispatch(fetchInvoiceConfiguration({ projectId }));
    }
  }, [dispatch, projectId]);

  const setInvoiceConfig = useCallback(() => {
    dispatch(openModal({ projectId, hasProductivityData, editing: false }));
  }, [dispatch, hasProductivityData, projectId]);

  const [
    editInvoiceConfig,
    editInvoiceConfigAccessible,
  ] = useAccessibleCallback(() => {
    dispatch(openModal({ projectId, hasProductivityData, editing: true }));
  }, [dispatch, hasProductivityData, projectId]);

  const closeModal = useCallback(() => {
    dispatch(closeInvoiceConfigModal());
  }, [dispatch]);

  const modalState = useSelector(selectModalState);

  const positionRef = useRef();

  useEffect(() => {
    if (
      showErrorWhenEmpty &&
      !invoiceConfig.loading &&
      !invoiceConfig.error &&
      !invoiceConfig.configuration &&
      positionRef.current
    ) {
      scrollIntoView(positionRef.current);
    }
  }, [
    invoiceConfig.configuration,
    invoiceConfig.error,
    invoiceConfig.loading,
    showErrorWhenEmpty,
  ]);

  if (invoiceConfig.loading) {
    return (
      <ProcessSelectedContainer>
        <ProcessName>
          <Shimmer width={280} height={14} />
        </ProcessName>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={215} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={160} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessStep>
          <FlexShimmer>
            <Shimmer width={14} height={14} />
            <ShimmerSpacer />
            <Shimmer width={195} height={14} />
          </FlexShimmer>
        </ProcessStep>
        <ProcessCreatedBy>
          <Shimmer width={260} height={14} />
        </ProcessCreatedBy>
      </ProcessSelectedContainer>
    );
  }

  if (invoiceConfig.error && !modalState.modalOpen) {
    return (
      <ProcessSelectedContainer hasError>
        <ProcessStep isError>Error loading invoice configuration.</ProcessStep>
      </ProcessSelectedContainer>
    );
  }

  const modalElement = (
    <Modal
      open={modalState.modalOpen}
      onClose={closeModal}
      title="Invoice Template Setup"
    >
      {modalState.modalData && (
        <InvoiceTemplateModal
          projectId={projectId}
          hasProductivityData={hasProductivityData}
          closeModal={closeModal}
        />
      )}
    </Modal>
  );

  if (invoiceConfig.configuration) {
    const {
      invoiceType,
      baseAutoGenerationOn,
      allowSelfReporting,
      autoApproveInvoicesBy,
      varianceThreshold,
      pmApprovalAtLineLevel,
    } = invoiceConfig.configuration;

    const tooltipContent = (
      <>
        <PopoverItem
          onClick={editInvoiceConfig}
          onKeyDown={editInvoiceConfigAccessible}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={pencil} alt={'edit icon'} />
          <PopoverText>Edit</PopoverText>
        </PopoverItem>
      </>
    );

    return (
      <>
        <ProcessSelectedContainer previewMode={previewMode}>
          {previewMode ? (
            <Text style={{ marginBottom: '8px' }}>
              {INVOICE_TYPES_NAMES[invoiceType]}
            </Text>
          ) : (
            <ProcessName>{INVOICE_TYPES_NAMES[invoiceType]}</ProcessName>
          )}

          <ProcessStep>
            Auto-generation <ArrowContainer src={arrowRightIcon} />{' '}
            {(
              findByValue(invoiceGenerationTypes.data, baseAutoGenerationOn) ||
              {}
            ).label || 'Not set'}
            {allowSelfReporting ? ', Self reporting allowed' : ''}
          </ProcessStep>

          {autoApproveInvoicesBy && (
            <ProcessStep>
              Auto-approval <ArrowContainer src={arrowRightIcon} />{' '}
              {(
                findByValue(
                  invoiceGenerationTypes.data,
                  autoApproveInvoicesBy,
                ) || {}
              ).label || 'Not set'}
              {typeof varianceThreshold === 'number' &&
                ` (${varianceThreshold * 100}%)`}
            </ProcessStep>
          )}

          {pmApprovalAtLineLevel && (
            <ProcessStep>PM approval at line item level required</ProcessStep>
          )}

          {isFromFinanceTeam && !previewMode && (
            <div className="invoice_configuration_actions">
              <Popover content={tooltipContent} placement="bottom-end">
                <SettingsHoverable />
              </Popover>
            </div>
          )}
        </ProcessSelectedContainer>
        {modalElement}
      </>
    );
  }

  return (
    <>
      {previewMode ? (
        <PreviewSectionText isEmpty />
      ) : (
        <NoEntries
          label="Not set"
          error={
            showErrorWhenEmpty
              ? 'This is a required field. Please ask the finance team to fill this before enabling this project.'
              : undefined
          }
        />
      )}
      <div ref={positionRef} />
      {!previewMode && (
        <Disabled
          when={!isFromFinanceTeam}
          message="For now, request configuration of invoicing settings to the finance team using the e-mail template available."
        >
          <Button onClick={setInvoiceConfig} type="primary">
            Set Template
          </Button>
        </Disabled>
      )}

      {modalElement}
    </>
  );
}

InvoiceConfiguration.propTypes = {
  projectId: PropTypes.number,
  hasProductivityData: PropTypes.bool.isRequired,
  showErrorWhenEmpty: PropTypes.bool,
  previewMode: PropTypes.bool,
};

InvoiceConfiguration.defaultProps = {
  projectId: undefined,
  showErrorWhenEmpty: false,
  previewMode: false,
};
