/* eslint-disable react/jsx-props-no-spreading, import/no-cycle */
import React, { useMemo } from 'react';
import { Field } from 'formik';
import { Row, Col } from 'styled-bootstrap-grid';
import get from 'lodash/get';
import PropTypes from 'prop-types';
import ActionBuilder from '../../ActionBuilder';
import getNormalizedLabel from '../../helpers/getNormalizedLabel';
import Select from '../../../../Select';
import DateInput from '../../../../DateInput';
import SecondaryQuestions from '../../SecondaryQuestions';
import { Label } from '../../../../../styles';
import {
  ButtonLink,
  Container,
  DateContainer,
  RemoveIcon,
  AddIcon,
  Acessory,
  Separator,
} from './styles';
import close from '../../../../../assets/icons/close.svg';
import add from '../../../../../assets/icons/add.svg';
import { ReactComponent as Icon } from '../../../../../assets/icons/datepicker.svg';

export default function DatePicker(props) {
  const {
    data: { id, questionType },
    values: { answers },
    setFieldValue,
    errors,
    submitCount,
    handleAddLine,
    handleRemove,
    handleLogic,
    logicOptions,
    items,
  } = props;

  const datePickerAnswer = answers[id] || [];

  const label = useMemo(() => getNormalizedLabel(questionType), [questionType]);

  function filterDate(date) {
    return new Date(date).valueOf() > new Date(1900, 1, 1).setHours(0, 0, 0, 0);
  }

  return (
    <>
      <Label>
        <Icon style={{ verticalAlign: 'middle', margin: '-5px 7px 0 0' }} />{' '}
        {label}
      </Label>
      <Field
        {...props}
        component={ActionBuilder}
        id={id}
        firstOption={
          <Container>
            <Row>
              <DateContainer noMargin>
                <Label>Select Logic</Label>
              </DateContainer>
              <DateContainer noMargin>
                <Label>Date</Label>
              </DateContainer>
            </Row>
            {datePickerAnswer.map((answer, index) => {
              const { logic, lb, ub, isBetween } = answer;

              const fieldBaseName = `answers[${id}][${index}]`;
              const logicBaseName = `${fieldBaseName}.logic`;
              const firstDateBaseName = `${fieldBaseName}.lb`;
              const secondDateBaseName = `${fieldBaseName}.ub`;
              const betweenBaseName = `${fieldBaseName}.isBetween`;
              const error = get(errors, 'answers.id.index', null);

              return (
                <Row>
                  <DateContainer>
                    <Select
                      id={logicBaseName}
                      name={logicBaseName}
                      placeholder="Select logic"
                      options={logicOptions}
                      value={logic}
                      onChange={({ value }) =>
                        handleLogic(logicBaseName, value, betweenBaseName)
                      }
                      error={submitCount >= 1 && get(error, 'logic', null)}
                      increasesHeight
                    />
                  </DateContainer>
                  <DateContainer>
                    <DateInput
                      value={lb ? new Date(lb) : undefined}
                      id={firstDateBaseName}
                      name={firstDateBaseName}
                      onChange={(value) =>
                        setFieldValue(firstDateBaseName, value)
                      }
                      filterDate={filterDate}
                      error={submitCount >= 1 && get(error, 'lb', null)}
                      placeholder="Select date"
                    />
                  </DateContainer>
                  {isBetween && (
                    <>
                      <Separator>and</Separator>
                      <DateContainer>
                        <DateInput
                          value={ub ? new Date(ub) : undefined}
                          minDate={lb}
                          id={secondDateBaseName}
                          name={secondDateBaseName}
                          onChange={(value) =>
                            setFieldValue(secondDateBaseName, value)
                          }
                          filterDate={filterDate}
                          error={submitCount >= 1 && get(error, 'ub', null)}
                          placeholder="Select date"
                        />
                      </DateContainer>
                    </>
                  )}
                  <>
                    {index > 0 && (
                      <RemoveIcon
                        src={close}
                        alt="Remove current date group"
                        onClick={() => handleRemove(index)}
                      />
                    )}
                  </>
                </Row>
              );
            })}
          </Container>
        }
        secondOption={'All others.'}
        firstOptionAccessory={
          <Acessory>
            <Row>
              <Col col={3} />
              <Col col={9}>
                <ButtonLink type="button" onClick={handleAddLine}>
                  <AddIcon src={add} alt="Add new date group" />
                  Add Date Group
                </ButtonLink>
              </Col>
            </Row>
          </Acessory>
        }
      />
      {items.map((item) => (
        <Field
          {...props}
          data={item}
          component={SecondaryQuestions}
          id={id}
          item={item}
          key={item.id}
        />
      ))}
    </>
  );
}

DatePicker.propTypes = {
  data: PropTypes.shape({
    id: PropTypes.number,
    questionType: PropTypes.string,
  }).isRequired,
  values: PropTypes.shape({
    answers: PropTypes.object,
  }).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  errors: PropTypes.PropTypes.shape({
    answers: PropTypes.array,
  }).isRequired,
  submitCount: PropTypes.number.isRequired,
  handleAddLine: PropTypes.func.isRequired,
  handleRemove: PropTypes.func.isRequired,
  handleLogic: PropTypes.func.isRequired,
  logicOptions: PropTypes.arrayOf(PropTypes.object).isRequired,
  items: PropTypes.arrayOf(PropTypes.object),
};

DatePicker.defaultProps = {
  items: [],
};
