import styled from 'styled-components';
import { transparentize, darken } from 'polished';
import { enforcePx } from '../../styles';

const getIpTagColor = (status, theme) =>
  ({
    true: theme.colors.error,
    false: theme.colors.green,
  }[status || 'true']);

const getIpTagBackground = (status, theme) =>
  ({
    true: transparentize(0.9, theme.colors.error),
    false: transparentize(0.9, theme.colors.green),
  }[status || 'true']);

const getMaliciousTagColor = (level, theme) => {
  switch (true) {
    case level.toLowerCase() === 'low':
      return theme.colors.blueGrey;
    case level.toLowerCase() === 'medium':
      return darken(0.35, theme.colors.warning);
    case level.toLowerCase() === 'high':
      return theme.colors.error;
    default:
      return theme.colors.blueGrey;
  }
};

const getMaliciousTagBackground = (level, theme) => {
  switch (true) {
    case level.toLowerCase() === 'low':
      return transparentize(0.9, theme.colors.blueGrey);
    case level.toLowerCase() === 'medium':
      return transparentize(0.8, theme.colors.warning);
    case level.toLowerCase() === 'high':
      return transparentize(0.9, theme.colors.error);
    default:
      return transparentize(0.9, theme.colors.blueGrey);
  }
};

export const Frame = styled.div`
  background: #ffffff;
  border: 1px solid #dfe0e6;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 16px;
  height: 155px;
  width: ${(props) => enforcePx(props.width, 154)};
`;

export const Text = styled.span`
  display: block;
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #2f3838;
`;

export const IpTag = styled.div`
  display: inline-block;
  border-radius: 4px;
  padding: 4px 8px;
  font-weight: bold;
  font-size: 12px;
  margin: ${(props) => (props.margin ? props.margin : '20px 0')};
  color: ${(props) => getIpTagColor(props.failed, props.theme)};
  background-color: ${(props) => getIpTagBackground(props.failed, props.theme)};
`;

export const MaliciousTag = styled.div`
  display: inline-block;
  border-radius: 4px;
  padding: 4px 8px;
  font-weight: bold;
  font-size: 12px;
  margin: ${(props) => (props.margin ? props.margin : '20px 0')};
  color: ${(props) => getMaliciousTagColor(props.level, props.theme)};
  background-color: ${(props) =>
    getMaliciousTagBackground(props.level, props.theme)};
`;

export const Review = styled.div`
  display: block;
  color: ${(props) => props.theme.colors.blueGrey};
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  display: flex;
  align-items: center;
  &:hover {
    text-decoration: underline;
    cursor: pointer;
  }
`;

export const ProgressBarWrapper = styled.div`
  margin: 27px 0;
`;

export const ShimmerSpacer = styled.div`
  height: 20px;
`;
