import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import NumberFormat from 'react-number-format';
import { Label, Section } from '../../../../styles';
import Select from '../../../Select';
import Input from '../../../Input';
import ActionFooter from '../../../ActionFooter';
import close from '../../../../assets/icons/close.svg';
import {
  QuizContainer,
  QuizTitle,
  QuizContentColumn,
  QuizRemoveColumn,
  RemoveQuizButton,
} from './styles';
import LabeledSwitch from '../../../LabeledSwitch';
import handleKeyDown from '../../../../pages/ProjectSetup/helpers/handleKeyDown';
import { StepActionContainer } from '../styles';
import InformationTooltip, { LabelWrapper } from '../../InformationTooltip';

function isPercentageAllowed(formatedPayload) {
  if (typeof formatedPayload.floatValue !== 'number') {
    return true;
  }

  return formatedPayload.floatValue <= 100;
}

export default function InternalQuizStep(props) {
  const {
    errors,
    submitCount,
    collapsed,
    submitForm,
    card: { attributes },
    // card: { subSteps },
    values: { quizes },
    // handleAddInternalQuiz,
    handleRemoveInternalQuiz,
    handleQuizChange,
    handleMaximumAttempsChange,
    handleUnlimitedChange,
    handleSubsetSizeChange,
    handleAllQuestionsChange,
    handlePassingScoreChange,
    quizesOptions,
    selectedQuizes,
    cancelEditing,
  } = props;

  // if (collapsed && subSteps && subSteps.length) {
  if (collapsed && attributes && attributes.length) {
    return (
      <QuizContainer>
        {selectedQuizes.map(({ label, key }) => (
          <div key={key}>{label}</div>
        ))}
      </QuizContainer>
    );
  }

  return (
    <>
      {quizes.map((internalQuiz, index) => (
        <QuizContainer key={internalQuiz.key}>
          <QuizContentColumn>
            {quizes.length > 1 && (
              <QuizTitle>Internal Quiz {index + 1}</QuizTitle>
            )}
            <Section>
              <Label htmlFor={`quiz${index}`}>Quiz</Label>
              <Select
                name={`quiz${index}`}
                id={`quiz${index}`}
                options={quizesOptions}
                onChange={handleQuizChange(index)}
                value={internalQuiz.quiz}
                placeholder="Select existing quiz"
                error={submitCount >= 1 && errors[`quiz${index}`]}
              />
            </Section>
            <Section>
              <Row>
                <Col col={4}>
                  <LabelWrapper>
                    <Label htmlFor="maximumAttemps">Maximum attempts</Label>
                    <InformationTooltip
                      lineLength={42}
                      title="The max number of times an applicant may attempt to take the specified internal screening quiz."
                    />
                  </LabelWrapper>
                  <NumberFormat
                    customInput={Input}
                    allowNegative={false}
                    name={`maximumAttemps${index}`}
                    onChange={handleMaximumAttempsChange(index)}
                    id="maximumAttemps"
                    placeholder={internalQuiz.unlimited ? '' : 'Enter number'}
                    value={
                      internalQuiz.unlimited ? '' : internalQuiz.maximumAttemps
                    }
                    disabled={internalQuiz.unlimited}
                    error={submitCount >= 1 && errors[`maximumAttemps${index}`]}
                    errorIncreasesHeight
                  />
                  <Section marginTop={12} marginBottom={0}>
                    <LabeledSwitch
                      name={`unlimited${index}`}
                      onChange={handleUnlimitedChange(index)}
                      value={internalQuiz.unlimited}
                      label="UNLIMITED"
                    />
                  </Section>
                </Col>
                <Col col={4}>
                  <LabelWrapper>
                    <Label htmlFor="subsetSize">Subset size</Label>
                    <InformationTooltip
                      lineLength={42}
                      title="The number of questions in the quiz to be displayed when an applicant takes the specified screening quiz."
                    />
                  </LabelWrapper>
                  <NumberFormat
                    customInput={Input}
                    allowNegative={false}
                    name={`subsetSize${index}`}
                    onChange={handleSubsetSizeChange(index)}
                    id="subsetSize"
                    placeholder={
                      internalQuiz.allQuestions
                        ? 'All questions'
                        : 'Enter number'
                    }
                    disabled={internalQuiz.allQuestions}
                    value={
                      internalQuiz.allQuestions ? '' : internalQuiz.subsetSize
                    }
                    error={submitCount >= 1 && errors[`subsetSize${index}`]}
                    errorIncreasesHeight
                  />
                  <Section marginTop={12} marginBottom={0}>
                    <LabeledSwitch
                      name={`allQuestions${index}`}
                      onChange={handleAllQuestionsChange(index)}
                      value={internalQuiz.allQuestions}
                      label="ALL QUESTIONS"
                    />
                  </Section>
                </Col>
                <Col col={4}>
                  <Label htmlFor="passingScore">Passing score</Label>
                  <NumberFormat
                    suffix="%"
                    isAllowed={isPercentageAllowed}
                    allowNegative={false}
                    customInput={Input}
                    name={`passingScore${index}`}
                    onChange={handlePassingScoreChange(index)}
                    id="passingScore"
                    placeholder="Enter percentage"
                    value={internalQuiz.passingScore}
                    error={submitCount >= 1 && errors[`passingScore${index}`]}
                  />
                </Col>
              </Row>
            </Section>
          </QuizContentColumn>
          <QuizRemoveColumn>
            {quizes.length > 1 && index !== 0 && (
              <RemoveQuizButton
                role="button"
                onClick={() => handleRemoveInternalQuiz(index)}
                onKeyDown={handleKeyDown(() => handleRemoveInternalQuiz(index))}
                tabIndex={0}
              >
                <img src={close} alt="close icon" />
              </RemoveQuizButton>
            )}
          </QuizRemoveColumn>
        </QuizContainer>
      ))}
      <StepActionContainer>
        <ActionFooter
          hideDivider
          leftButtons={
            [
              // TODO: Implement ability to add multiple quizes on the backend
              // {
              //   type: 'secondary',
              //   text: 'Add Other Quiz',
              //   onClick: handleAddInternalQuiz,
              // },
            ]
          }
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !(attributes && attributes.length)
                ? 'Add & Close'
                : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

InternalQuizStep.propTypes = {
  // handleChange: PropTypes.func.isRequired,
  values: PropTypes.shape({
    quizes: PropTypes.arrayOf(
      PropTypes.shape({
        quiz: PropTypes.number,
        maximumAttemps: PropTypes.string,
        unlimited: PropTypes.bool,
        subsetSize: PropTypes.string,
        allQuestions: PropTypes.bool,
        passingScore: PropTypes.string,
        key: PropTypes.string.isRequired,
      }),
    ),
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        attributeOrder: PropTypes.number,
      }),
    ),
    subSteps: PropTypes.arrayOf(
      PropTypes.shape({
        attributes: PropTypes.arrayOf(
          PropTypes.shape({
            value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
            attributeOrder: PropTypes.number,
          }),
        ),
      }),
    ),
  }).isRequired,
  // handleAddInternalQuiz: PropTypes.func.isRequired,
  handleRemoveInternalQuiz: PropTypes.func.isRequired,
  handleQuizChange: PropTypes.func.isRequired,
  handleMaximumAttempsChange: PropTypes.func.isRequired,
  handleUnlimitedChange: PropTypes.func.isRequired,
  handleSubsetSizeChange: PropTypes.func.isRequired,
  handleAllQuestionsChange: PropTypes.func.isRequired,
  handlePassingScoreChange: PropTypes.func.isRequired,
  quizesOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  selectedQuizes: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
