import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'styled-bootstrap-grid';
import ProjectOverviewPreview from '../../../../components/ProjectPreviews/ProjectOverviewPreview';
import ProjectToolsPreview from '../../../../components/ProjectPreviews/ProjectToolsPreview';
import { CardContentContainer, InformationCard, StepName } from '../../styles';
import { Label, Section } from '../../../../styles';
import { ReactComponent as Check } from '../../../../assets/icons/circle-check.svg';
import {
  DraftCardTitle,
  ProgressBarContainer,
  ProgressBar,
  ProgressIndicator,
  ProgressBarPercentageNumber,
  CheckIconContainer,
} from './styles';
import OverviewAndToolsForm from './OverviewAndToolsForm';
import PostMessageLink from '../../../../components/PostMessageLink';

export default function OverviewAndTools(props) {
  const {
    mode,
    description,
    projectType,
    taskType,
    taskVolume,
    externalSystem,
    externalSystemUrl,
    ipAddresses,
    swfhProjectExternalUrl,
    workdayTaskId,
    externalLinkEnabled,
    hasExternalProjectSettings,
    status,
    statusDescription,
    HighlightIcon,
    customer,
    createdAt,
    createdBy,
    percentCompleted,
  } = props;

  if (mode === 'edit') {
    return <OverviewAndToolsForm />;
  }

  const statusValue = `${status}`.toUpperCase();

  return (
    <>
      <CardContentContainer>
        <Row>
          <Col col={8}>
            <Section>
              <StepName>Project Setup</StepName>
            </Section>
            <ProjectOverviewPreview
              projectDescription={description}
              projectType={projectType}
              taskType={taskType}
              taskVolume={taskVolume}
            />

            <Section>
              <StepName>Project Tools</StepName>
            </Section>
            <ProjectToolsPreview
              hasExternalSystem={!!externalSystem}
              externalSystem={externalSystem}
              externalSystemUrl={externalSystemUrl}
              hasIPRestrictions={!!ipAddresses}
              ipAddresses={ipAddresses}
              swfhProjectExternalUrl={swfhProjectExternalUrl}
              isSWFHProject={!!swfhProjectExternalUrl}
              workdayTaskId={workdayTaskId}
              externalLinkEnabled={externalLinkEnabled}
              hasExternalProjectSettings={hasExternalProjectSettings}
            />
          </Col>
          <Col col={4}>
            <InformationCard>
              <Label>Status</Label>
              <DraftCardTitle>{status}</DraftCardTitle>
              {statusDescription}
              {(statusValue === 'DRAFT' || statusValue === 'ENABLED') && (
                <ProgressBarContainer>
                  <ProgressBar>
                    <ProgressIndicator
                      style={{ width: `${Math.min(percentCompleted, 100)}%` }}
                    />
                  </ProgressBar>
                  <ProgressBarPercentageNumber>{`${percentCompleted}%`}</ProgressBarPercentageNumber>
                </ProgressBarContainer>
              )}
              <CheckIconContainer>
                {statusValue === 'READY' && <Check />}
              </CheckIconContainer>
            </InformationCard>

            <InformationCard>
              <HighlightIcon />
              <Section>
                <Label>{taskType}</Label>
                {`${projectType} project`}
                <br />
                {`${taskVolume} task volume`}
              </Section>

              <Section>
                <Label>Customer</Label>
                {customer}
              </Section>

              <Label>Creation</Label>
              {createdAt}
              <br />
              <PostMessageLink
                disabled={!createdBy}
                to={`/qrp/core/vendor/view/${(createdBy && createdBy.id) || 0}`}
              >
                {(createdBy && createdBy.fullName) || 'System'}
              </PostMessageLink>
            </InformationCard>
          </Col>
        </Row>
      </CardContentContainer>
    </>
  );
}

OverviewAndTools.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  description: PropTypes.string.isRequired,
  projectType: PropTypes.oneOf(['Regular', 'Express']).isRequired,
  taskType: PropTypes.string,
  taskVolume: PropTypes.string,
  externalSystem: PropTypes.string,
  externalSystemUrl: PropTypes.string,
  ipAddresses: PropTypes.string.isRequired,
  swfhProjectExternalUrl: PropTypes.string,
  status: PropTypes.string.isRequired,
  statusDescription: PropTypes.string.isRequired,
  HighlightIcon: PropTypes.elementType.isRequired,
  customer: PropTypes.string,
  createdAt: PropTypes.string.isRequired,
  percentCompleted: PropTypes.number.isRequired,
  workdayTaskId: PropTypes.string,
  externalLinkEnabled: PropTypes.bool,
  hasExternalProjectSettings: PropTypes.bool,
  createdBy: PropTypes.shape({
    id: PropTypes.number.isRequired,
    fullName: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
  }).isRequired,
};

OverviewAndTools.defaultProps = {
  taskType: '-',
  taskVolume: '-',
  externalSystem: '-',
  externalSystemUrl: '',
  customer: '-',
  swfhProjectExternalUrl: undefined,
  workdayTaskId: '',
  externalLinkEnabled: false,
  hasExternalProjectSettings: false,
};
