import { call, takeLeading } from 'redux-saga/effects';
import { lockLeave, allowLeave } from '.';
import {
  preventLeave as preventLeavePM,
  allowLeave as allowLeavePM,
} from '../../../services/postMessage';

function* preventLeaveSagaFn(action) {
  if (action.type === lockLeave.type) {
    yield call(preventLeavePM);
  } else {
    yield call(allowLeavePM);
  }
}

export default takeLeading(
  [lockLeave.type, allowLeave.type],
  preventLeaveSagaFn,
);
