import React, { useContext, useRef, useEffect, useMemo } from 'react';
import scrollIntoView from 'scroll-into-view-if-needed';
import { ThemeContext } from 'styled-components';
import PropTypes from 'prop-types';
import cx from 'classnames';
import noop from 'lodash/noop';
import {
  Tick,
  OptionBase,
  ChildrenContainer,
  CheckboxContainer,
  ChildrenWrapper,
  OptionDescription,
} from './styles';
import Checkbox from '../../Checkbox';

function ignoreEvent(event) {
  event.preventDefault();
}

export default function Option(props) {
  const {
    data,
    className,
    getStyles,
    isSelected,
    isDisabled,
    isFocused,
    children,
    innerRef,
    innerProps,
    selectProps,
  } = props;

  const {
    isMulti,
    multiMaxSelectedOptions,
    multiMaxSelectedMessage,
    value,
  } = selectProps;

  const styledTheme = useContext(ThemeContext);

  const ref = useRef();

  useEffect(() => {
    if (typeof innerRef === 'function') {
      innerRef(ref.current);
    }
  }, [innerRef]);

  useEffect(() => {
    if (!isMulti && isSelected && ref.current && ref.current.parentElement) {
      const parent =
        Array.from(ref.current.parentElement.classList).length === 0
          ? ref.current.parentElement.parentElement.parentElement
          : ref.current.parentElement;

      scrollIntoView(ref.current, {
        behavior: 'instant',
        scrollMode: 'if-needed',
        block: 'start',
        boundary: parent,
      });
    }
  }, [isMulti, isSelected]);

  const canSelect = useMemo(() => {
    if (
      isMulti &&
      Array.isArray(value) &&
      multiMaxSelectedOptions > 0 &&
      value.length >= multiMaxSelectedOptions &&
      !isSelected
    ) {
      return false;
    }

    return true;
  }, [isMulti, isSelected, multiMaxSelectedOptions, value]);

  const { id, onClick, onMouseOver, tabIndex } = innerProps;

  const inputDisabled = useMemo(() => {
    if (!canSelect) {
      return true;
    }

    if (data && typeof data.disabled === 'boolean') {
      return data.disabled;
    }

    return isDisabled;
  }, [canSelect, data, isDisabled]);

  const disabledMessage = useMemo(() => {
    if (inputDisabled) {
      if (!canSelect) {
        return multiMaxSelectedMessage;
      }

      if (data && typeof data.disabledMessage === 'string') {
        return data.disabledMessage;
      }

      return 'Disabled';
    }

    return undefined;
  }, [canSelect, data, inputDisabled, multiMaxSelectedMessage]);

  return (
    <OptionBase
      id={id}
      onClick={inputDisabled ? noop : onClick}
      onFocus={inputDisabled ? noop : onMouseOver}
      tabIndex={tabIndex}
      ref={ref}
      css={getStyles('option', props)}
      className={cx('select-menu select-option', className)}
      focus={ref.current && isFocused}
      isDisabled={inputDisabled}
      title={disabledMessage}
      blocking={inputDisabled}
    >
      <ChildrenContainer>
        {isMulti && (
          <CheckboxContainer>
            <Checkbox
              name={`${id}_checkbox`}
              onChange={ignoreEvent}
              checked={isSelected}
            />
          </CheckboxContainer>
        )}
        <ChildrenWrapper>
          <div>{children}</div>
          {data && data.description && (
            <OptionDescription>{data.description}</OptionDescription>
          )}
        </ChildrenWrapper>
        {!isMulti && isSelected && (
          <Tick>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="13"
              height="10"
              viewBox="0 0 13 10"
              data-testid="tick"
            >
              <path
                fill={styledTheme.colors.azure}
                fillRule="nonzero"
                d="M11.581 1.24l.178.179a1 1 0 0 1 0 1.414L5.211 9.381l-.052.05a1.126 1.126 0 0 1-1.644.055L.33 6.3a1.126 1.126 0 0 1 1.592-1.592l2.389 2.388 5.856-5.855a1 1 0 0 1 1.414 0z"
              />
            </svg>
          </Tick>
        )}
      </ChildrenContainer>
    </OptionBase>
  );
}

Option.propTypes = {
  data: PropTypes.shape({
    value: PropTypes.any.isRequired,
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.element])
      .isRequired,
    disabled: PropTypes.bool,
    disabledMessage: PropTypes.string,
    description: PropTypes.string,
  }).isRequired,
  className: PropTypes.string,
  getStyles: PropTypes.func.isRequired,
  isSelected: PropTypes.bool.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  isFocused: PropTypes.bool.isRequired,
  children: PropTypes.node.isRequired,
  innerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]),
  innerProps: PropTypes.shape({
    id: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    onMouseMove: PropTypes.func.isRequired,
    onMouseOver: PropTypes.func.isRequired,
    tabIndex: PropTypes.number.isRequired,
  }).isRequired,
  selectProps: PropTypes.shape({
    isMulti: PropTypes.bool,
    multiMaxSelectedOptions: PropTypes.number,
    multiMaxSelectedMessage: PropTypes.string,
    value: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
      PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        label: PropTypes.any,
      }),
      PropTypes.arrayOf(
        PropTypes.oneOfType([
          PropTypes.string,
          PropTypes.number,
          PropTypes.shape({
            value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
            label: PropTypes.any,
          }),
        ]),
      ),
    ]),
  }).isRequired,
};

Option.defaultProps = {
  className: '',
  innerRef: undefined,
};
