import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import {
  ProgressIndicatorContainer,
  ProgressIndicatorBar,
  ProgressIndicatorInnerCircle,
  ProgressIndicatorOuterCircle,
  ProgressIndicatorText,
  ProgressIndicatorWrapper,
} from './styles';

export default function ProgressIndicator(props) {
  const { percentCompleted } = props;

  const floatPercentCompleted = useMemo(() => {
    if (percentCompleted < 0) {
      return 0;
    }

    if (percentCompleted > 1) {
      return 1;
    }

    if (percentCompleted > Math.floor(percentCompleted)) {
      return parseFloat(percentCompleted.toFixed(1));
    }

    return percentCompleted;
  }, [percentCompleted]);

  const absolutePercentCompleted = useMemo(() => {
    if (percentCompleted > 1) {
      return parseInt(percentCompleted * 100, 10);
    }

    return parseFloat(Number(percentCompleted * 100).toFixed(1));
  }, [percentCompleted]);

  return (
    <ProgressIndicatorWrapper>
      <ProgressIndicatorContainer
        width="44px"
        height="44px"
        viewBox="0 0 66 66"
        xmlns="http://www.w3.org/2000/svg"
      >
        <ProgressIndicatorOuterCircle cx="33" cy="33" r="32" />
        <ProgressIndicatorInnerCircle cx="33" cy="33" r="28" />
        <ProgressIndicatorBar
          strokeDashoffset={(1 - floatPercentCompleted) * 187}
          cx="33"
          cy="33"
          r="30"
        />
      </ProgressIndicatorContainer>
      <ProgressIndicatorText>{absolutePercentCompleted}%</ProgressIndicatorText>
    </ProgressIndicatorWrapper>
  );
}

ProgressIndicator.propTypes = {
  percentCompleted: PropTypes.number.isRequired,
};
