/* eslint-disable react/forbid-prop-types */
import React, { useMemo } from 'react';
import get from 'lodash/get';
import PropTypes from 'prop-types';
import { v4 as uuid } from 'uuid';
import Filters from './Filters';
import Card from './Card';
import EmptyCard from './Card/EmptyCard';
import { Container, Spacer } from './styles';

export default function ProfileQuestions(props) {
  const {
    categories,
    maxAnswerId,
    maxQuestionId,
    profileQuestions,
    loading,
  } = props;

  const hasQuestions = useMemo(() => Object.keys(profileQuestions).length > 0, [
    profileQuestions,
  ]);

  if (loading) {
    return (
      <Container>
        <Filters />
        <Spacer />
        <EmptyCard loading={loading} />
      </Container>
    );
  }

  return (
    <Container>
      <Filters />
      <Spacer />
      {hasQuestions ? (
        Object.keys(profileQuestions).map((category) => {
          const topics = profileQuestions[category];
          const title = get(
            categories.filter((c) => c.value === category),
            [0, 'label'],
            null,
          );
          return (
            <Card
              title={title}
              description={'description'}
              maxAnswerId={maxAnswerId}
              maxQuestionId={maxQuestionId}
              topics={topics}
              category={category}
              key={uuid()}
            />
          );
        })
      ) : (
        <EmptyCard />
      )}
    </Container>
  );
}

ProfileQuestions.propTypes = {
  categories: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      label: PropTypes.string,
    }),
  ).isRequired,
  maxAnswerId: PropTypes.number.isRequired,
  maxQuestionId: PropTypes.number.isRequired,
  profileQuestions: PropTypes.object,
  loading: PropTypes.bool.isRequired,
};

ProfileQuestions.defaultProps = {
  profileQuestions: {},
};
