import { connect } from 'react-redux';
import UserProjectPage from './UserProjectPage';
import * as projectPages from '../../../../../../store/ducks/projectPages';
import { findById } from '../../../../../../helpers/find';
import { selectUserProjectPage } from '../../../../../../store/ducks/project';

function mapStateToProps(state) {
  const userProjectPage = selectUserProjectPage(state);

  const projectPagesData = projectPages.selectAll(state);

  const { defaultPageTask, hasSkillReview, isACANProject } = userProjectPage;

  return {
    defaultPageTask: (findById(projectPagesData, defaultPageTask) || {})
      .tabName,
    hasSkillReview,
    isACANProject,
  };
}

export default connect(mapStateToProps)(UserProjectPage);
