import { all, call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction, addMany } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';
import normalizePayRatesParameters from './helpers/normalizePayRatesParameters';

function* createManyPayRateSagaFn(action) {
  const {
    payload,
    meta: { onSuccess, onError, createMany },
  } = action;

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can create a Pay Rate.');
    return;
  }

  try {
    const payRates = payload.map((payRate) => {
      const params = normalizePayRatesParameters(payRate);
      return {
        ...params,
        projectId: payRate.projectId || project.id,
      };
    });

    const { data: payRatesResponse } = yield call(
      axios.post,
      ENDPOINTS.V2.BULK_PAY_RATES(),
      payRates,
    );

    yield all(
      payload.map((payRate, i) => {
        const currentPayRate = payRatesResponse[i];
        if (currentPayRate) {
          return put(
            updateOne(
              {
                id: payRate.id,
                changes: {
                  id: currentPayRate.id,
                  userGroup: currentPayRate.groupId,
                  userGroupId: currentPayRate.groupId,
                  taskType: currentPayRate.taskType,
                },
              },
              { bypassUpdateSaga: true },
            ),
          );
        }
        return null;
      }),
    );

    if (!createMany) {
      onSuccess();
    }
  } catch (error) {
    if (!createMany) {
      onError(
        error.message ||
          'There was an error creating this Pay Rate. Please try again.',
      );

      yield put(undoLastAction());
    }
  }
}

export default takeEvery(addMany.type, createManyPayRateSagaFn);
