import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import ProjectToolsPreview from '../../../../../../components/ProjectPreviews/ProjectToolsPreview';
import { ENTITIES, fetch } from '../../../../../../store/ducks/options';

export default function ProjectTools(props) {
  const {
    hasExternalSystem,
    externalSystem,
    externalSystemUrl,
    hasIPRestrictions,
    ipAddresses,
    swfhProjectExternalUrl,
    navigateTo,
    isSWFHProject,
    renderedIn,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.externalSystems));
  }, [dispatch]);

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.PROJECT_TOOLS);
  }, [navigateTo]);

  return (
    <ProjectToolsPreview
      hasExternalSystem={hasExternalSystem}
      externalSystem={externalSystem}
      externalSystemUrl={externalSystemUrl}
      hasIPRestrictions={hasIPRestrictions}
      ipAddresses={ipAddresses}
      swfhProjectExternalUrl={swfhProjectExternalUrl}
      isSWFHProject={isSWFHProject}
      editStep={editStep}
      stepNumber={STEP_NUMBER.PROJECT_TOOLS + 1}
      renderedIn={renderedIn}
    />
  );
}

ProjectTools.propTypes = {
  hasExternalSystem: PropTypes.bool,
  externalSystem: PropTypes.string,
  externalSystemUrl: PropTypes.string,
  hasIPRestrictions: PropTypes.bool,
  ipAddresses: PropTypes.string,
  swfhProjectExternalUrl: PropTypes.string,
  navigateTo: PropTypes.func.isRequired,
  isSWFHProject: PropTypes.bool.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectTools.defaultProps = {
  hasExternalSystem: false,
  externalSystem: '',
  externalSystemUrl: '',
  hasIPRestrictions: false,
  ipAddresses: '',
  swfhProjectExternalUrl: '',
  renderedIn: 'project-setup',
};
