import { useCallback } from 'react';
import withHooks from 'hook-hoc';
import TopicsCell from './TopicsCell';
import { useModal } from '../../../../../../components/Modal';

function useHooksAsProps(props) {
  const [modalProps, modalControls] = useModal();
  const {
    data: currentQuestionsList,
    row: { original: masterProfileQuestionData },
  } = props;

  const openModal = useCallback(() => {
    modalControls.openModal({
      masterProfileQuestionData,
      currentQuestionsList,
    });
  }, [modalControls, currentQuestionsList, masterProfileQuestionData]);

  return {
    modalProps,
    modalControls,
    openModal,
    masterProfileQuestionData,
  };
}

export default withHooks(useHooksAsProps)(TopicsCell);
