/* eslint-disable react/jsx-props-no-spreading, import/no-cycle */
import React, { useMemo } from 'react';
import { Field } from 'formik';
import get from 'lodash/get';
import PropTypes from 'prop-types';
import ActionBuilder from '../../ActionBuilder';
import Component from '../../../../Checkbox';
import SecondaryQuestions from '../../SecondaryQuestions';
import MessageError from '../../../../MessageError';
import { CheckGroup } from './styles';
import { Label } from '../../../../../styles';
import getNormalizedLabel from '../../helpers/getNormalizedLabel';
import { ReactComponent as Icon } from '../../../../../assets/icons/checkbox-group.svg';

export default function Checkbox(props) {
  const {
    data: { id, questionType },
    values: { answers },
    setFieldValue,
    errors,
    items,
  } = props;

  const checkboxAnswer = answers[id] || [];
  const type = useMemo(() => getNormalizedLabel(questionType), [questionType]);

  return (
    <>
      <Label>
        <Icon style={{ verticalAlign: 'middle', margin: '-5px 7px 0 0' }} />{' '}
        {type}
      </Label>
      <CheckGroup>
        {checkboxAnswer.map((currentAnswer, index) => {
          const { answer, value } = currentAnswer;
          const fieldBaseName = `answers[${id}][${index}].value`;

          return (
            <Component
              name={fieldBaseName}
              onChange={(event) =>
                setFieldValue(fieldBaseName, event.target.value)
              }
              checked={value}
              label={answer}
              bold={false}
            />
          );
        })}
        <MessageError
          isVisible={!!errors.answers}
          message={get(errors.answers, id, null)}
        />
        <Field {...props} component={ActionBuilder} id={id} />
      </CheckGroup>
      {items.map((item) => (
        <Field
          {...props}
          data={item}
          component={SecondaryQuestions}
          id={id}
          item={item}
          key={item.id}
        />
      ))}
    </>
  );
}

Checkbox.propTypes = {
  data: PropTypes.shape({
    id: PropTypes.number,
    questionType: PropTypes.string,
  }).isRequired,
  values: PropTypes.shape({
    answers: PropTypes.object,
  }).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  errors: PropTypes.PropTypes.shape({
    answers: PropTypes.array,
  }).isRequired,
  items: PropTypes.arrayOf(PropTypes.object),
};

Checkbox.defaultProps = {
  items: [],
};
