import * as yup from 'yup';
import createArrayValidation from '../../../helpers/array-validation';
import ipValidation from '../../../helpers/ip-validation';
import isUrl from '../../../helpers/url-validation';
import isSWFH from '../../../pages/ProjectSetup/helpers/isSWFH';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

function transformError(error, value, index) {
  if (error.type === 'required') {
    return new yup.ValidationError(
      requiredMessage`If "Secure IP Access" is checked this`,
      value,
      `ipAddress${index}`,
    );
  }

  return new yup.ValidationError(error.message, value, `ipAddress${index}`);
}

const validateArray = createArrayValidation(
  {
    ipAddress: yup
      .string()
      .required()
      .test('valid-ip', 'IP or CIDR is not valid', ipValidation),
  },
  transformError,
);

export default {
  hasExternalSystem: yup.boolean(),
  externalSystem: yup.string().when('hasExternalSystem', {
    is: true,
    then: yup
      .string()
      .required('External system is required for a External Project'),
  }),

  swfhProjectExternalUrl: yup.string().when('externalSystem', {
    is: isSWFH,
    then: yup
      .string()
      .test('swfhUrl', 'SWFH Url must be a valid URL', (value) =>
        value ? isUrl(value) : true,
      )
      .required(requiredMessage`SWFH Url`),
  }),

  hasIPRestrictions: yup.boolean(),
  ipAddresses: yup.array().when('hasIPRestrictions', {
    is: true,
    then: yup.array().test('ipAddressExists', '', validateArray),
  }),
};
