import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import ProjectHeader from './ProjectHeader';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import { selectProjectStatus } from '../../../store/ducks/projectView';
import { useModal } from '../../../components/Modal';

function useHooksAsProps() {
  const dispatch = useDispatch();
  const [modalProps, modalControls] = useModal();

  useEffect(() => {
    dispatch(fetch(ENTITIES.projectStatus));
  }, [dispatch]);

  const projectStatus = useSelector(selectProjectStatus);

  const status = useSelector(
    selectors.createSelectById(ENTITIES.projectStatus, null, projectStatus),
  );

  const handleOpenCloneModal = useCallback(
    (projectId) => {
      modalControls.openModal({ projectId });
    },
    [modalControls],
  );

  return {
    projectStatus: status || { value: 'LOADING', label: '-' },
    handleOpenCloneModal,
    modalProps,
    modalControls,
  };
}

export default withHooks(useHooksAsProps)(ProjectHeader);
