import React, { useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { noop } from 'lodash';
import PropTypes from 'prop-types';
import Tooltip from 'rc-tooltip';
import { useFeatures } from 'flagged';
import { Row, Col } from 'styled-bootstrap-grid';
import { navigateTo as navigateToPostMessage } from '../../../services/postMessage';
import Modal from '../../../components/Modal';
import Button from '../../../components/Button';
import FloatContainer, { FloatRight } from '../../../components/Float';
import userProjectPage from '../../../assets/icons/user-project-page.svg';
import chart from '../../../assets/icons/chart.svg';
import copy from '../../../assets/icons/copy.svg';
import close from '../../../assets/icons/close.svg';
import eye from '../../../assets/icons/eye.svg';
import pause from '../../../assets/icons/pause.svg';
import { ReactComponent as Elipsis } from '../../../assets/icons/elipsis.svg';
import { Tag } from '../../Projects/Cells/StatusCell';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
  PopoverTrigger,
} from '../../../components/Popover';
import Disabled from '../../../components/Disabled';
import MainActionButton from './MainActionButton';
import legacyIntegration from '../../../helpers/legacy-integration';
import { putProjectStatus } from '../../../store/ducks/project';
import { userHasAnyRole } from '../../../store/ducks/security';
import ROLES from '../../../constants/roles';
import { buildTabs } from '../helpers/projectViewTabs';
import { Section } from '../../../styles';
import StylesInformationTooltip from '../../../components/ProcessBuilder/InformationTooltip/styles';
import {
  ProjectHeaderContainer,
  ProjectHeaderColumn,
  ProjectHeaderRow,
  ProjectTitle,
  ProjectId,
  HeaderActionContainer,
  TabsContainer,
  Tabs,
  ProjectPreviewButton,
  Tab,
  CancelButton,
} from './styles';

const projectPropsShape = {
  id: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  customerId: PropTypes.number.isRequired,
};

export default function ProjectHeader(props) {
  const {
    project,
    currentTab,
    navigateTo,
    projectStatus,
    openPreview,
    modalProps,
    handleOpenCloneModal,
  } = props;

  const dispatch = useDispatch();

  const features = useFeatures();

  const tabs = buildTabs(features);

  const tooltipStatusText = (status) => {
    switch (status) {
      case 'DRAFT':
        return 'Finish project setup to start recruiting workers for this project.';

      case 'READY':
        return 'Your project is ready to be enabled. Click on Enable Project to start recruitment.';

      case 'ENABLED':
        return 'Your project is now running and recruitment is in progress.';

      case 'PAUSED':
        return 'Your project is currently paused. Click on Enable Project to resume recruitment.';

      case 'DISABLED':
      default:
        return 'Your project is currently disabled. Click on Enable Project to resume recruitment.';
    }
  };

  const userProjectPageUrl = useMemo(() => {
    if (project) {
      return `/qrp/core/vendors/project_site/${project.id}/${project.defaultPageId}`;
    }
    return '';
  }, [project]);

  const isAdminOrDirector = useSelector(
    userHasAnyRole(ROLES.ADMIN, ROLES.DIRECTOR),
  );

  const isExpressProject = useMemo(
    () =>
      project.projectType &&
      project.projectType.length &&
      project.projectType.toUpperCase() === 'EXPRESS',
    [project],
  );

  const handleUserProjectPageClick = (event) => {
    event.preventDefault();
    navigateToPostMessage(userProjectPageUrl);
  };

  const handleProjectStatusUpdate = useCallback(
    (newStatus) => {
      const patch = { status: newStatus };
      dispatch(putProjectStatus(patch));
    },
    [dispatch],
  );

  const showPausedStatus = useMemo(
    () => ({ ENABLED: true, DISABLED: true }[project.status] || false),
    [project.status],
  );

  const tooltipContent = (
    <>
      <Disabled
        when={!project.defaultPageId}
        message="No default project page was set"
        focusOffset={1}
      >
        <PopoverItem
          onClick={handleUserProjectPageClick}
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={userProjectPage} alt="User project page icon" />
          <PopoverText>User Project Page</PopoverText>
        </PopoverItem>
      </Disabled>

      <PopoverItem
        onClick={() =>
          legacyIntegration.informOpenTracker(parseInt(project.id, 10))
        }
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={chart} alt="chart icon" />
        <PopoverText>Open Tracker</PopoverText>
      </PopoverItem>

      {showPausedStatus && (
        <PopoverItem
          onClick={() => handleProjectStatusUpdate('PAUSED')}
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={pause} alt="Pause icon" />
          <PopoverText>Pause Project</PopoverText>
        </PopoverItem>
      )}

      <PopoverItem
        onClick={handleOpenCloneModal}
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={copy} alt="Copy icon" />
        <PopoverText>Clone Project</PopoverText>
      </PopoverItem>

      <Disabled when message="This option is not available." focusOffset={1}>
        <PopoverItem
          onClick={() =>
            legacyIntegration.informProjectDeletion(parseInt(project.id, 10))
          }
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={close} alt="Delete icon" />
          <PopoverText>Delete</PopoverText>
        </PopoverItem>
      </Disabled>
    </>
  );

  return (
    <>
      <StylesInformationTooltip />
      <ProjectHeaderContainer>
        <ProjectHeaderColumn>
          <ProjectHeaderRow>
            <Tooltip
              placement="bottomLeft"
              trigger={['hover', 'focus', 'click']}
              overlay={tooltipStatusText(project.status)}
              prefixCls="information_tooltip"
              overlayClassName=""
            >
              <Tag currentStatus={projectStatus.value}>
                {projectStatus.label}
              </Tag>
            </Tooltip>
            <ProjectId>
              ID {project.id} | {project.alias}
            </ProjectId>
          </ProjectHeaderRow>
          <ProjectTitle>{project.name}</ProjectTitle>
        </ProjectHeaderColumn>
        <HeaderActionContainer>
          <Disabled
            when={
              (projectStatus.value === 'READY' ||
                projectStatus.value === 'DISABLED') &&
              isExpressProject &&
              !isAdminOrDirector
            }
            message="Only directors can launch Express Projects"
            focusOffset={1}
          >
            <MainActionButton status={projectStatus.value} project={project} />
          </Disabled>
          <Popover content={tooltipContent} placement="bottom-end">
            {(shown) => (
              <PopoverTrigger
                style={{
                  position: 'relative',
                  top: 2,
                  margin: '0 10px 0 8px',
                }}
                isShown={shown}
              >
                <Elipsis />
              </PopoverTrigger>
            )}
          </Popover>
        </HeaderActionContainer>
      </ProjectHeaderContainer>
      <TabsContainer>
        <Tabs>
          {tabs.map(
            (tab, index) =>
              tab.enabled && (
                <Tab
                  data-testid={`project-view-tab-${index}`}
                  key={tab.title}
                  active={index === currentTab}
                  onClick={() => navigateTo(index)}
                >
                  {tab.title}
                </Tab>
              ),
          )}
        </Tabs>
        <ProjectPreviewButton
          src={eye}
          onClick={() => openPreview()}
          alt="Preview project icon"
        />
      </TabsContainer>
      <Modal
        open={modalProps.open}
        onClose={modalProps.onClose}
        onExited={modalProps.onExited}
        center={false}
        title="Clone Project"
      >
        <Section marginBottom="56px">
          <Row>
            <Col col={10}>
              <span>
                A new pre-filled project setup experience will be created for
                you based on this project configuration. <br />
                <br /> Please make sure to review all steps before creating the
                new project.
              </span>
            </Col>
          </Row>
        </Section>

        <FloatContainer>
          <FloatRight>
            <CancelButton type="secondary" onClick={modalProps.onClose}>
              Cancel
            </CancelButton>
            <Button
              onClick={() => {
                legacyIntegration.informCloneProject(
                  parseInt(project.id, 10),
                  parseInt(project.customerId, 10) || null,
                );
              }}
            >
              Proceed
            </Button>
          </FloatRight>
        </FloatContainer>
      </Modal>
    </>
  );
}

ProjectHeader.propTypes = {
  project: PropTypes.shape(projectPropsShape).isRequired,
  currentTab: PropTypes.number.isRequired,
  navigateTo: PropTypes.func.isRequired,
  projectStatus: PropTypes.shape({
    value: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
  }).isRequired,
  openPreview: PropTypes.func.isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  handleOpenCloneModal: PropTypes.func.isRequired,
};
