import React from 'react';
import PropTypes from 'prop-types';
import {
  Divider,
  Container,
  ButtonsWrapper,
  ButtonContainer,
  AccessoryContainer,
  Pusher,
  RightButtonWrapper,
} from './styles';
import Button from '../Button';

const buttonPropsShape = {
  type: PropTypes.oneOf(['primary', 'secondary']),
  onClick: PropTypes.func.isRequired,
  text: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  disabledTitle: PropTypes.string,
  testId: PropTypes.string,
  loading: PropTypes.bool,
  marginRight: PropTypes.string,
};

function renderButtonForSide(side, isBeforeAccessory) {
  function render(props, index, buttons) {
    const {
      type,
      onClick,
      text,
      disabled,
      disabledTitle,
      testId,
      loading,
      marginRight,
    } = props;

    return (
      <ButtonContainer
        key={text}
        alignTo={side}
        isFirst={isBeforeAccessory && index === 0}
        isLast={index + 1 === buttons.length}
        marginRight={marginRight}
      >
        <Button
          type={type}
          onClick={onClick}
          disabled={disabled}
          disabledTitle={disabledTitle}
          data-testid={testId}
          loading={loading}
          responsive
        >
          {text}
        </Button>
      </ButtonContainer>
    );
  }

  render.propTypes = buttonPropsShape;

  render.defaultProps = {
    type: 'primary',
    disabled: false,
    disabledTitle: '',
    testId: undefined,
    loading: false,
    marginRight: null,
  };

  return render;
}

export default function ActionFooter(props) {
  const {
    hideDivider,
    dividerMargin,
    leftButtons,
    accessoryLeft,
    renderAccessoryLeftBeforeButtons,
    rightButtons,
    accessoryRight,
    renderAccessoryRightBeforeButtons,
  } = props;

  return (
    <Container>
      {!hideDivider && (
        <Divider marginTop={dividerMargin} marginBottom={dividerMargin} />
      )}
      <ButtonsWrapper>
        {renderAccessoryLeftBeforeButtons && (
          <AccessoryContainer isFirst alignTo="left">
            {accessoryLeft}
          </AccessoryContainer>
        )}
        {(leftButtons || []).map(
          renderButtonForSide('left', !renderAccessoryLeftBeforeButtons),
        )}
        {!renderAccessoryLeftBeforeButtons && (
          <AccessoryContainer alignTo="left">
            {accessoryLeft}
          </AccessoryContainer>
        )}

        <Pusher />

        {renderAccessoryRightBeforeButtons && (
          <AccessoryContainer alignTo="right">
            {accessoryRight}
          </AccessoryContainer>
        )}
        <RightButtonWrapper>
          {(rightButtons || []).map(renderButtonForSide('right', false))}
        </RightButtonWrapper>
        {!renderAccessoryRightBeforeButtons && (
          <AccessoryContainer alignTo="right">
            {accessoryRight}
          </AccessoryContainer>
        )}
      </ButtonsWrapper>
    </Container>
  );
}

ActionFooter.propTypes = {
  dividerMargin: PropTypes.number,
  leftButtons: PropTypes.arrayOf(PropTypes.shape(buttonPropsShape)),
  accessoryLeft: PropTypes.oneOfType([PropTypes.element, PropTypes.bool]),
  renderAccessoryLeftBeforeButtons: PropTypes.bool,
  rightButtons: PropTypes.arrayOf(PropTypes.shape(buttonPropsShape)).isRequired,
  accessoryRight: PropTypes.oneOfType([PropTypes.element, PropTypes.bool]),
  renderAccessoryRightBeforeButtons: PropTypes.bool,
  hideDivider: PropTypes.bool,
};

ActionFooter.defaultProps = {
  dividerMargin: 16,
  leftButtons: [],
  accessoryLeft: null,
  renderAccessoryLeftBeforeButtons: false,
  accessoryRight: null,
  renderAccessoryRightBeforeButtons: true,
  hideDivider: false,
};
