import { useCallback } from 'react';
import { connect } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import get from 'lodash/get';
import { saveFalsePositive } from '../../store/ducks/userScreeningFalsePositives';
import DuplicatesList from './DuplicatesList';

function useHooksAsProps(props) {
  const { setFieldValue } = props;

  const setCurrentUserId = useCallback(
    (userId) => {
      setFieldValue('duplicateUserId', userId);
    },
    [setFieldValue],
  );

  return { setCurrentUserId };
}

function handleSubmit(values, { props }) {
  const { dispatch } = props;
  const { referenceWorkerId, duplicateUserId } = values;

  dispatch(
    saveFalsePositive({
      falsePositive: {
        duplicateUserId,
        userId: referenceWorkerId,
      },
    }),
  );
}

function mapPropsToValues(props) {
  const referenceWorkerId = get(props, ['data', 'referenceWorkerId'], null);
  return { referenceWorkerId };
}

export default compose(
  connect(),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(DuplicatesList);
