import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import {
  DEMOGRAPHIC_METRICS_TYPE,
  selectMetricsType,
} from '../../../../../../store/ducks/projectDemographicMetrics';
import { MetricCell, Count, Target } from './styles';

export default function InputCell(data) {
  const {
    cell: {
      value: { value, target },
    },
    row: {
      original: { viewAsPercent },
    },
  } = data;
  const metricsType = useSelector(selectMetricsType);

  if (value) {
    const count = viewAsPercent
      ? Math.round((value.count / (target || 1)) * 100)
      : value.count || 0;
    const ratio =
      !value.target || value.target === 0
        ? 0
        : Math.min((count / value.target) * 100, 100);
    const suffix = viewAsPercent ? '%' : '';

    return (
      <MetricCell>
        <Count ratio={ratio}>{`${count}${suffix}`}</Count>
        {metricsType === DEMOGRAPHIC_METRICS_TYPE.CUSTOM && value.target && (
          <Target>{` of ${value.target}${suffix}`}</Target>
        )}
      </MetricCell>
    );
  }

  return <MetricCell>0</MetricCell>;
}

InputCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.shape({
      hiringTargetID: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      value: PropTypes.string,
    }).isRequired,
  }).isRequired,
  row: PropTypes.shape({
    original: PropTypes.shape({
      groupID: PropTypes.string.isRequired,
      handleFieldChange: PropTypes.func.isRequired,
      viewAs: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
};
