import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import UserScreening from './UserScreening';
import {
  fetchScreeningMetrics,
  selectTargetScreening,
} from '../../../store/ducks/screeningMetrics';
import {
  fetchPaginated,
  nextPage,
  PAGINATED_ENTITIES,
  resetFor,
  selectors as paginatedSelectors,
  setPageSize,
  setFilters,
  SORT_DIRECTIONS,
} from '../../../store/ducks/paginated';

function useHooksAsProps() {
  const dispatch = useDispatch();

  const users = useSelector(
    paginatedSelectors.createSelectDataFor(PAGINATED_ENTITIES.userScreening),
  );
  const usersLoading = useSelector(
    paginatedSelectors.createSelectLoadingFor(PAGINATED_ENTITIES.userScreening),
  );

  const pagination = useSelector(
    paginatedSelectors.createSelectPaginationInfoFor(
      PAGINATED_ENTITIES.userScreening,
    ),
  );

  const metrics = useSelector(selectTargetScreening);

  useEffect(() => {
    dispatch(fetchPaginated(PAGINATED_ENTITIES.userScreening));
    dispatch(fetchScreeningMetrics());

    return () => {
      dispatch(resetFor(PAGINATED_ENTITIES.userScreening));
    };
  }, [dispatch]);

  const handlePageChange = useCallback(
    (page) => {
      window.scrollTo(0, 0);
      dispatch(
        nextPage({
          forKey: PAGINATED_ENTITIES.userScreening,
          nextPage: page,
        }),
      );
    },
    [dispatch],
  );

  const handlePageSizeChange = useCallback(
    (size) => {
      window.scrollTo(0, 0);
      dispatch(
        setPageSize({
          forKey: PAGINATED_ENTITIES.userScreening,
          pageSize: size,
        }),
      );
    },
    [dispatch],
  );

  const handleSortChange = useCallback(
    ([sortedCol]) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.userScreening,
          filters: sortedCol
            ? {
                sort: sortedCol.id,
                direction: sortedCol.desc
                  ? SORT_DIRECTIONS.DESC
                  : SORT_DIRECTIONS.ASC,
              }
            : {
                sort: undefined,
                direction: undefined,
              },
        }),
      );
    },
    [dispatch],
  );

  return {
    users: get(users, 'content', []),
    metrics: metrics || [],
    usersLoading,
    pagination,
    handlePageChange,
    handlePageSizeChange,
    handleSortChange,
  };
}

export default withHooks(useHooksAsProps)(UserScreening);
