import styled from 'styled-components';
import { transparentize } from 'polished';

const getTagColor = (status, theme) =>
  ({
    true: theme.colors.blueGrey,
    false: theme.colors.error,
  }[status || false]);

const getTagBackground = (status, theme) =>
  ({
    true: transparentize(0.9, theme.colors.blueGrey),
    false: transparentize(0.9, theme.colors.error),
  }[status || false]);

export const Tag = styled.div`
  display: inline-block;
  height: 24px;
  width: 23px;
  border-radius: 4px;
  padding: 4px 8px;
  font-weight: bold;
  font-size: 12px;
  color: ${(props) => getTagColor(props.verified, props.theme)};
  background-color: ${(props) => getTagBackground(props.verified, props.theme)};
  margin: 0 0 0 15px;
`;

export const Profile = styled.div`
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  color: #0091ff;
`;

export const Text = styled.div`
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  color: #2f3838;
`;

export const Verified = styled.div`
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #848899;
  padding-top: 3px;
`;
