import _ from 'lodash';

export const dateFormat = (date) => {
  if (date === null || date === undefined) return '';

  const newDate = new Date(date);
  const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(newDate);
  const mo = new Intl.DateTimeFormat('en', { month: 'short' }).format(newDate);
  const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(newDate);
  return `${mo} ${da}, ${ye}`;
};

export const aggregatedList = (hiringTargets, agregationType) => {
  let keys = ['localeLanguage', 'localeDialect', 'restrictToCountry'];

  switch (agregationType) {
    case 'DIAL_LG_COUNTRY':
      keys = ['localeLanguage', 'localeDialect', 'restrictToCountry'];

      break;
    case 'LANGUAGE_COUNTRY':
      keys = ['localeLanguage', 'restrictToCountry'];

      break;
    case 'LANGUAGE':
      keys = ['localeLanguage'];

      break;
    case 'COUNTRY':
      keys = ['restrictToCountry'];

      break;
    default:
      break;
  }

  const marketList = hiringTargets.map((hiringTarget) => {
    return keys
      .map((k) => hiringTarget[k])
      .join('-')
      .toUpperCase();
  });

  return _.uniq(marketList).sort();
};
