import React, { useCallback, useEffect, useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import {
  EDITABLE_ENTITIES,
  editor,
  useCloseEditor,
} from '../../store/ducks/editableEntities';
import {
  selectData as selectDataConsentForm,
  fetchDataConsentForm,
  deleteDataConsentForm,
  selectSaveError,
} from '../../store/ducks/dataConsentForm';
import { ENTITIES, fetch, selectors } from '../../store/ducks/options';
import { Section } from '../../styles';
import Button from '../Button';
import Modal from '../Modal';
import NoEntries from '../NoEntries';
import DataConsentModal from './DataConsentModal';
import ErrorPopover from '../ErrorPopover';
import Popover, { PopoverItem, PopoverIcon, PopoverText } from '../Popover';
import pencil from '../../assets/icons/pencil.svg';
import close from '../../assets/icons/close.svg';
import SettingsHoverable from '../Icons/SettingsHoverable';
import { useAccessibleCallback } from '../../helpers/a11y';
import { EEA_COUNTRY_CODES } from './constants';

import {
  ProcessSelectedContainer,
  ProcessName,
  ProcessStep,
  PopoverContainer,
  PopoverWarning,
  PopoverInfo,
  ErrorTitle,
  Wrapper,
} from './styles';

export default function DataConsentForm(props) {
  const {
    projectId,
    hasConsentForm,
    hasDataCollectionPiiConsentForm,
    mode,
    showErrorWhenEmpty = false,
  } = props;

  const dispatch = useDispatch();

  const [error, setError] = useState(null);

  const saveError = useSelector(selectSaveError);
  const consentForm = useSelector(selectDataConsentForm);
  const allCountries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );
  const consentFormModalOpen = useSelector(
    editor.selectors.createSelectEditorOpenFor(
      EDITABLE_ENTITIES.PROJECT_LEVEL_DATA_CONSENT_FORM,
    ),
  );
  const electronicDocuments = useSelector(
    selectors.createSelectDataFor(ENTITIES.electronicDocuments),
  );

  const { countries } = consentForm;
  let countryNames = [];

  if (Object.keys(consentForm).length > 0) {
    countryNames = allCountries.data
      .filter((country) => countries.includes(country.value))
      .map((country) => country.label);

    const isEEACountry =
      countries.length === EEA_COUNTRY_CODES.length - 1
        ? countries.every((candidate) => EEA_COUNTRY_CODES.includes(candidate))
        : false;

    if (isEEACountry) {
      countryNames = ['European Economic Area (EEA)'];
    }
  }

  const openModal = useCallback(
    () =>
      consentForm
        ? dispatch(
            editor.actions.edit(
              EDITABLE_ENTITIES.PROJECT_LEVEL_DATA_CONSENT_FORM,
              consentForm.data,
            ),
          )
        : dispatch(
            editor.actions.create(
              EDITABLE_ENTITIES.PROJECT_LEVEL_DATA_CONSENT_FORM,
            ),
          ),
    [consentForm, dispatch],
  );

  const closeModal = useCloseEditor(
    EDITABLE_ENTITIES.PROJECT_LEVEL_DATA_CONSENT_FORM,
  );

  const fetchConsentForm = useCallback(
    () => dispatch(fetchDataConsentForm(projectId)),
    [dispatch, projectId],
  );

  const removeConsentForm = useCallback(() => {
    dispatch(
      deleteDataConsentForm({
        changes: { consentFormId: consentForm.id || null },
        thenPut: fetchDataConsentForm({ projectId }),
      }),
    );
  }, [consentForm.id, dispatch, projectId]);

  const [
    editConsentForm,
    editConsentFormAccessible,
  ] = useAccessibleCallback(() => {
    dispatch(openModal({ projectId, editing: true }));
  }, [dispatch, projectId]);

  useEffect(() => {
    dispatch(fetchDataConsentForm({ projectId }));
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.electronicDocuments));
  }, [dispatch, projectId]);

  useEffect(() => {
    if (saveError) {
      setError(saveError);
    }
  }, [saveError]);

  const hasConsentData = Object.keys(consentForm).length > 0;

  const thirdPartyAgeType = hasConsentData
    ? {
        ADULT: 'Adults only',
        MINOR: 'Minors only',
        ADULT_AND_MINOR: 'Adults and minors',
      }[consentForm.thirdPartyAgeType]
    : null;

  const thirdParty = thirdPartyAgeType ? 'Third party' : null;

  const sensitive = hasConsentData
    ? consentForm.piiData
        .map((pii) => {
          return pii.sensitive ? pii.name : null;
        })
        .filter((pii) => pii)
        .join(', ')
    : null;

  const nonSensitive = hasConsentData
    ? consentForm.piiData
        .map((pii) => {
          return !pii.sensitive ? pii.name : null;
        })
        .filter((pii) => pii)
        .join(', ')
    : null;

  const processName = useMemo(
    () =>
      ({
        CUSTOMER_OWNS_ALL: 'CUSTOMER OWNS ALL DATASETS',
        APPEN_OWNS_SOME_OR_ALL: 'APPEN OWNS SOME/ALL DATASETS',
        CUSTOMER_TEMPLATE: 'CUSTOMER TEMPLATE',
      }[consentForm.datasetOwnerType]),
    [consentForm.datasetOwnerType],
  );

  const selectedDocument = useMemo(
    () =>
      electronicDocuments.filter(
        (document) => document.id === consentForm.documentId,
      ),
    [consentForm.documentId, electronicDocuments],
  );

  const isCustomerTemplate = useMemo(
    () => consentForm.datasetOwnerType === 'CUSTOMER_TEMPLATE',
    [consentForm.datasetOwnerType],
  );

  if (consentForm.loading) {
    return (
      <Section marginLeft="34px">
        <NoEntries label="Loading..." />;
      </Section>
    );
  }

  const tooltipContent = (
    <>
      <PopoverItem
        onClick={editConsentForm}
        onKeyDown={editConsentFormAccessible}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={pencil} alt={'edit icon'} />
        <PopoverText>Edit</PopoverText>
      </PopoverItem>
      <PopoverItem onClick={removeConsentForm} role="button" tabIndex={0}>
        <PopoverIcon src={close} alt={'delete icon'} />
        <PopoverText>Delete</PopoverText>
      </PopoverItem>
    </>
  );

  return (
    <>
      <Section marginLeft={mode !== 'view' && 34}>
        {!hasConsentData && (
          <>
            <NoEntries
              label="Not set"
              error={
                showErrorWhenEmpty
                  ? 'This is a required field. Please configure a template.'
                  : undefined
              }
            />
            {mode !== 'view' && (
              <Button onClick={openModal}>Set Template</Button>
            )}
          </>
        )}
        {hasConsentData && (
          <ProcessSelectedContainer>
            <ProcessName>{processName}</ProcessName>
            {!isCustomerTemplate && (
              <>
                {sensitive && <ProcessStep>Sensitive: {sensitive}</ProcessStep>}
                {nonSensitive && (
                  <ProcessStep>Non-sensitive: {nonSensitive}</ProcessStep>
                )}
                {countryNames.length > 0 && (
                  <ProcessStep>
                    Countries: {countryNames.join(', ')}
                  </ProcessStep>
                )}
                {thirdParty && thirdParty.length && (
                  <ProcessStep>
                    {thirdParty}, {thirdPartyAgeType}
                  </ProcessStep>
                )}
              </>
            )}
            {isCustomerTemplate &&
              selectedDocument &&
              selectedDocument.length > 0 && (
                <ProcessStep>
                  Sign Document: {get(selectedDocument, [0, 'title'], null)}
                </ProcessStep>
              )}
            {mode !== 'view' && (
              <div className="invoice_configuration_actions">
                <Popover content={tooltipContent} placement="bottom-end">
                  <SettingsHoverable />
                </Popover>
              </div>
            )}
          </ProcessSelectedContainer>
        )}
        <Modal
          open={consentFormModalOpen}
          onClose={closeModal}
          hideError={() => fetchConsentForm}
          title="Data Collection Consent Form Template"
        >
          <DataConsentModal
            closeModal={closeModal}
            projectId={projectId}
            hasDataCollectionPiiConsentForm={hasDataCollectionPiiConsentForm}
            key={`${consentFormModalOpen}`}
          />
        </Modal>
        <ErrorPopover
          title="Failed to save Data Consent Form"
          hideError={() => setError(null)}
          errorShown={!!error}
          error={error}
        />
        {!hasConsentForm && !thirdPartyAgeType && mode !== 'view' && (
          <PopoverContainer>
            <Wrapper>
              <PopoverWarning>
                <ErrorTitle>Attention</ErrorTitle>
                You need to add the Data Collection PII Consent Form module
                into your registration process in order to collect consent from
                primary workers.
              </PopoverWarning>
            </Wrapper>
          </PopoverContainer>
        )}
        {!hasConsentForm && thirdPartyAgeType && mode !== 'view' && (
          <PopoverContainer>
            <Wrapper>
              <PopoverInfo>
                <ErrorTitle>Important</ErrorTitle>
                This project will only collect consent forms from third parties.
                If you also want consent from the primary worker, make sure to
                add the Data Collection PII Consent Form module into your
                registration process
              </PopoverInfo>
            </Wrapper>
          </PopoverContainer>
        )}
      </Section>
    </>
  );
}

DataConsentForm.propTypes = {
  projectId: PropTypes.number.isRequired,
  mode: PropTypes.string.isRequired,
  hasConsentForm: PropTypes.bool.isRequired,
  hasDataCollectionPiiConsentForm: PropTypes.bool.isRequired,
  showErrorWhenEmpty: PropTypes.bool,
};

DataConsentForm.defaultProps = {
  showErrorWhenEmpty: false,
};
