import { takeLeading, call, put, all } from 'redux-saga/effects';
import { fetchProject, fetchProjectSuccess, fetchProjectError } from '.';
import { fetchAll as fetchDemographicsParameters } from '../demographicRequirements';
import { fetchDemographics } from '../projectDemographicRequirements';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import { fetchPayRatesFn } from '../payrates/fetchPayRatesSaga';
// import { fetchHiringTargetsFn } from '../hiringTargets/fetchHiringTargetsSaga';
import { fetchLocaleTenantsFn } from '../localeTenants/fetchLocaleTenantsSaga';

function* fetchProjectSaga(action) {
  const {
    payload: { projectId, thenPut, catchPut },
  } = action;

  try {
    yield all([
      call(fetchPayRatesFn, action),
      // call(fetchHiringTargetsFn, action),
      call(fetchLocaleTenantsFn, action),
      put(fetchDemographics({ projectId })),
      put(fetchDemographicsParameters()),
    ]);

    const { data: project } = yield call(
      axios.get,
      endpoints.V2.PROJECT({
        pathVariables: {
          id: projectId,
        },
      }),
    );

    yield put(
      fetchProjectSuccess({
        project,
      }),
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }
  } catch (err) {
    yield put(fetchProjectError(err));

    if (typeof catchPut === 'object') {
      yield put(catchPut);
    }
  }
}

export default takeLeading(fetchProject.type, fetchProjectSaga);
