import { select, put, takeLatest, call } from 'redux-saga/effects';
import {
  selectProcessType,
  PROCESS_TYPE,
  setModules,
  setModulesError,
  fetchModules,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* fetchModulesSaga(action) {
  let processType;

  if (action && action.payload) {
    processType = action.payload.processType;
  } else {
    processType = yield select(selectProcessType);
  }

  if (!processType) {
    if (process.env.NODE_ENV === 'development') {
      // eslint-disable-next-line no-console
      console.warn('No processType provided to fetchModulesSaga');
    }
    return;
  }

  const endpoint =
    processType === PROCESS_TYPE.REGISTRATION
      ? endpoints.V2.REGISTRATION_PROCESS_STEPS()
      : endpoints.V2.QUALIFICATION_PROCESS_STEPS();

  try {
    const response = yield call(axios.get, endpoint);

    yield put(setModules(response.data));
  } catch (error) {
    yield put(setModulesError(error));
  }
}

export default takeLatest(fetchModules.type, fetchModulesSaga);
