import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { PreviewSectionText } from '../../styles';
import {
  ResendPartnerDataButtonIcon,
  ResendPartnerDataButtonMessage,
  ResendPartnerDataButtonRow,
  ResendPartnerDataContainer,
} from './styles';
import Button from '../../../Button';
import { ReactComponent as Check } from '../../../../assets/icons/circle-check.svg';
import {
  resend,
  selectReseting,
  selectError,
} from '../../../../store/ducks/resendPartnerData';
import { Section } from '../../../../styles';
import MessageError from '../../../MessageError';

export default function ResendPartnerData(props) {
  const { projectId, externalSystemName } = props;

  const dispatch = useDispatch();

  const reseting = useSelector(selectReseting);
  const error = useSelector(selectError);

  const handleResend = useCallback(() => {
    dispatch(resend(projectId));
  }, [dispatch, projectId]);

  if (!externalSystemName) {
    return null;
  }

  return (
    <>
      <ResendPartnerDataContainer hasError={error}>
        <Section marginBottom="16px">
          <PreviewSectionText>
            Manually reload partner data from ADAP.
          </PreviewSectionText>
        </Section>
        <ResendPartnerDataButtonRow>
          <Button
            type="secondary"
            onClick={handleResend}
            disabled={reseting > 0}
          >
            Resend Partner Data
          </Button>
          {reseting === 1 && (
            <ResendPartnerDataButtonMessage>
              Resending...
            </ResendPartnerDataButtonMessage>
          )}
          {reseting === 2 && (
            <ResendPartnerDataButtonIcon>
              <Check />
            </ResendPartnerDataButtonIcon>
          )}
        </ResendPartnerDataButtonRow>
      </ResendPartnerDataContainer>
      <MessageError isVisible={!!error} increasesHeight message={error} />
    </>
  );
}

ResendPartnerData.propTypes = {
  projectId: PropTypes.number.isRequired,
  externalSystemName: PropTypes.string,
};

ResendPartnerData.defaultProps = {
  externalSystemName: null,
};
