import React from 'react';
import PropTypes from 'prop-types';
import { Container, SecondaryInfo } from './styles';
import { Label } from '../../../../../styles';
import {
  CardTitle,
  CardDescription,
  CardInfo,
} from '../../../../Builder/ProcessesColumn/Card/styles';
import { ReactComponent as Chevron } from '../../../../../assets/icons/double-chevron-right.svg';

export default function Separator(props) {
  const { label, name, description } = props;

  return (
    <Container>
      <SecondaryInfo>
        <Label>
          <Chevron
            style={{ verticalAlign: 'middle', margin: '-4px 7px 0 0' }}
          />{' '}
          {label}
        </Label>
      </SecondaryInfo>
      <CardInfo>
        <CardTitle inlineText>{name}</CardTitle>
        <CardDescription inlineText>{description}</CardDescription>
      </CardInfo>
    </Container>
  );
}

Separator.propTypes = {
  label: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
};

Separator.defaultProps = {};
