import withHooks from 'hook-hoc';
import { useFormikContext } from 'formik';
import { useEffect, useCallback, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import DocumentEditor from './DocumentEditor';
import axios from '../../../../../services/axios';
import ENDPOINTS from '../../../../../constants/endpoints';
import { HTMLToDraft, draftToHTML } from '../../../../RichTextEditor';
import { fetch, ENTITIES, selectors } from '../../../../../store/ducks/options';
import { useErrorPopover } from '../../../../ErrorPopover';

function useHooksAsProps(outerProps) {
  const { setFieldValue, submitForm, resetForm } = useFormikContext();
  const {
    documentId,
    values,
    closeModal,
    modalOpen,
    refetchStepDefinition,
  } = outerProps;

  const [showError, hideError, errorProps] = useErrorPopover();

  const currentDocument = useSelector(
    selectors.createSelectDataFor(ENTITIES.documents, documentId),
  );

  const resetModalFormState = useCallback(
    (newDocumentId) => {
      // Reset form state
      setFieldValue('documentTitle', '');
      setFieldValue('documentBody', '');
      setFieldValue('required', '');
      setFieldValue('hasInternalNotes', '');
      setFieldValue('internalNotes', '');
      setFieldValue('changeLog', '');
      resetForm();

      // Hide error popover
      hideError();

      // Recover outer form state
      setFieldValue('documentToSign', newDocumentId || values.documentToSign);
      setFieldValue('isNewDocument', values.isNewDocument);
      setFieldValue('editingExistingDocument', false);
    },
    [
      hideError,
      resetForm,
      setFieldValue,
      values.documentToSign,
      values.isNewDocument,
    ],
  );

  const initializeModalFormState = useCallback(() => {
    setFieldValue('documentTitle', currentDocument.title);
    setFieldValue('documentBody', HTMLToDraft(currentDocument.content));
    setFieldValue('required', !currentDocument.optional);
    setFieldValue('hasInternalNotes', !!currentDocument.internalDescription);
    setFieldValue('internalNotes', currentDocument.internalDescription);
    setFieldValue('editingExistingDocument', true);
  }, [
    currentDocument.content,
    currentDocument.internalDescription,
    currentDocument.optional,
    currentDocument.title,
    setFieldValue,
  ]);

  useEffect(() => {
    if (!documentId || documentId === -1) {
      resetModalFormState();
      return;
    }

    if (modalOpen) {
      initializeModalFormState();
    }
  }, [documentId, initializeModalFormState, modalOpen, resetModalFormState]);

  const dispatch = useDispatch();

  const [isSubmitting, setIsSubmitting] = useState(false);

  const updateDocument = useCallback(async () => {
    setIsSubmitting(true);
    submitForm();

    try {
      const {
        data: { id: newDocumentId },
      } = await axios.post(
        ENDPOINTS.V2.ELECTRONIC_DOCUMENT_VERSION({
          pathVariables: {
            id: documentId,
          },
        }),
        {
          changeLog: values.changeLog,
          content: draftToHTML(values.documentBody),
        },
      );

      dispatch(
        fetch({ optionsFor: ENTITIES.documents, nestedKey: newDocumentId }),
      );

      refetchStepDefinition();

      closeModal();
      resetModalFormState(newDocumentId);
    } catch (error) {
      showError(error);
    }

    setIsSubmitting(false);
  }, [
    closeModal,
    dispatch,
    documentId,
    refetchStepDefinition,
    resetModalFormState,
    showError,
    submitForm,
    values.changeLog,
    values.documentBody,
  ]);

  const cancelEditing = useCallback(() => {
    resetModalFormState();
    closeModal();
  }, [closeModal, resetModalFormState]);

  return {
    updateDocument,
    cancelEditing,
    isSubmitting,
    errorProps,
  };
}

export default withHooks(useHooksAsProps)(DocumentEditor);
