import { call, put, takeLeading } from 'redux-saga/effects';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import {
  submitUserResponse,
  submitUserResponseSuccess,
  submitUserResponseError,
} from '.';

function* submitUserResponseSaga(action) {
  const {
    payload: { status, consentFormToken },
  } = action;

  try {
    const { data } = yield call(axios.put, endpoints.V2.THIRD_PARTY_ANSWER(), {
      status,
      consentFormToken,
    });

    yield put(submitUserResponseSuccess(data));
  } catch (err) {
    yield put(submitUserResponseError(err));
  }
}

export default takeLeading(submitUserResponse.type, submitUserResponseSaga);
