import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import sortBy from 'lodash/sortBy';

export const PROCESS_TYPE = {
  REGISTRATION: 'REGISTRATION_PROJECT',
  QUALIFICATION: 'QUALIFICATION_PROJECT',
};

const initialState = {
  processBuilderOpen: false,
  detailsModalOpen: false,
  affectedProjectsModalOpen: false,

  processType: null,
  processID: null,
  copyFromProcessID: null,

  processName: '',
  processDescription: '',
  processModules: [],
  processModulesPristine: true,

  processLoading: false,
  processSaving: false,
  processError: null,

  modules: [],
  modulesLoading: true,
  modulesError: null,

  stepEditorDirty: false,

  affectedProjects: [],
  affectedProjectsLoading: false,
  affectedProjectsError: null,
};

const processBuilderSlice = createSlice({
  name: 'processBuilder',
  initialState: {
    ...initialState,
  },

  reducers: {
    /**
     * type RequestOpenProcessBuilderAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *     processID?: number,
     *     copyFromProcessID?: number,
     *   }
     * }
     */
    requestOpenProcessBuilder(state, action) {
      state.processLoading = true;
      state.processType = action.payload.processType;
      state.processBuilderOpen = true;

      if (action.payload.processID) {
        state.detailsModalOpen = false;
        state.affectedProjectsModalOpen = state.affectedProjects.length > 0;
        state.affectedProjectsLoading = true;
      } else {
        state.detailsModalOpen = true;
        state.affectedProjectsModalOpen = false;
      }
    },

    /**
     * type OpenProcessBuilderAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *     processID?: number
     *     processName?: string,
     *     processDescription?: string,
     *     copyFromProcessID?: number
     *   }
     * }
     */
    openProcessBuilder(state, action) {
      state.processLoading = false;
      state.processType = action.payload.processType;

      if (action.payload.processID) {
        // We are editing
        state.processID = action.payload.processID;
        state.processName = action.payload.processName;
        state.processDescription = action.payload.processDescription;
        state.instantiated = action.payload.instantiated;

        // Hide the information modal
        state.detailsModalOpen = false;

        // Show affected projects information
        state.affectedProjectsModalOpen = state.affectedProjects.length > 0;
        state.affectedProjectsLoading = false;
      } else if (action.payload.copyFromProcessID) {
        // We are copying from another project
        state.processName = action.payload.processName;
        state.processDescription = action.payload.processDescription;
        state.copyFromProcessID = action.payload.copyFromProcessID;
      }
    },

    closeProcessBuilder() {
      return initialState;
    },

    toggleDetailsModal(state, action) {
      state.detailsModalOpen = action.payload;
    },

    setAffectedProjects(state, action) {
      state.affectedProjects = action.payload;
      state.affectedProjectsLoading = false;
      state.affectedProjectsError = null;
    },

    setAffectedProjectsError(state, action) {
      state.affectedProjectsError = action.payload;
    },

    closeAffectedProjectsModal(state) {
      state.affectedProjectsModalOpen = false;
    },
    /**
     * type StartEmptyProcessAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *     processName: string,
     *     processDescription: string,
     *     copyFromProcessID?: number
     *   }
     * }
     */
    startEmptyProcess: {
      reducer: (state, action /*: StartEmptyProcessAction */) => {
        if (action.copyFromProcessID) {
          state.processLoading = true;
        }

        state.processType = action.payload.processType;
        state.processName = action.payload.processName;
        state.processDescription = action.payload.processDescription;
        state.copyFromProcessID = action.payload.copyFromProcessID;
      },
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },

    /**
     * type LoadProcessByIdAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *     id: number
     *   }
     * }
     */
    loadProcessById: {
      reducer(state /* action: StartEmptyProcessAction */) {
        state.processLoading = true;
      },

      prepare(payload, meta) {
        return {
          payload,
          meta,
        };
      },
    },

    /**
     * type SetProcessDetailsAction = {
     *   payload: {
     *     processID?: number
     *     processName: string
     *     processDescription: string
     *     copyFromProcessID?: number
     *   }
     * }
     */
    setProcessDetails: {
      reducer(state, action /* : SetProcessDetailsAction */) {
        state.processID = action.payload.processID || state.processID || null;
        state.processName = action.payload.processName || state.processName;
        state.processDescription =
          action.payload.processDescription || state.processDescription;
      },

      prepare(payload, meta) {
        return {
          payload,
          meta,
        };
      },
    },

    /**
     * type SetProcessModulesAction = {
     *   payload: Array<ProcessModule>
     * }
     */
    setProcessModules: {
      reducer: (state, action /* : SetProcessModulesAction */) => {
        state.processModules = action.payload;
        state.processModulesPristine = action.meta.isPristine;
      },
      prepare: (payload, meta) => ({
        payload,
        meta: { isPristine: !!(meta && meta.isPristine) },
      }),
    },

    /**
     * type SetProcessAttributesAction = {
     *   payload: {
     *     id?: number,
     *     key: string,
     *     attributes: any[]
     *   }
     *   payload: Array<ProcessModule>
     *   payload: Array<ProcessModule>
     * }
     */
    setProcessAttributes(state, action /* : SetProcessAttributesAction */) {
      const { id, key, attributes, subSteps, dirty } = action.payload;

      const processModuleIndex = state.processModules.findIndex(
        (process) => (process.id && process.id === id) || process.key === key,
      );

      if (processModuleIndex === -1) {
        return;
      }

      state.processModules[processModuleIndex] = {
        ...state.processModules[processModuleIndex],
        ...(attributes ? { attributes } : {}),
        ...(subSteps ? { subSteps } : {}),
        dirty,
      };
    },

    /**
     * type FetchModulesAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *   }
     * }
     */
    fetchModules(state /* action: FetchModulesAction */) {
      state.modulesLoading = true;
    },

    /**
     * type SaveOrCreateProcessAction = {
     *   payload: {
     *     processType: PROCESS_TYPE,
     *   }
     * }
     */
    saveOrCreateProcess: {
      reducer: (state /* action: SaveOrCreateProcessAction */) => {
        state.processSaving = true;
      },
      prepare: (payload, meta) => ({ payload, meta }),
    },

    setModules(state, action) {
      state.modules = action.payload;
      state.modulesLoading = false;
    },

    loadStepById(state) {
      state.modulesLoading = true;
    },

    setStepDefinitionAttributes(state, action) {
      const { id, attributes } = action.payload;

      const moduleIndex = state.modules.findIndex((mod) => mod.id === id);

      if (moduleIndex === -1) {
        return;
      }

      state.modules[moduleIndex] = {
        ...state.modules[moduleIndex],
        attributes: sortBy(attributes, 'attributeOrder'),
      };
    },

    setModulesError(state, action) {
      state.modulesError = action.payload;
    },

    setProcessLoading(state, action) {
      state.processLoading = action.payload;
    },

    setProcessSaving(state, action) {
      state.processSaving = action.payload;
    },

    setProcessError(state, action) {
      state.processError = action.payload;
    },

    setStepEditorDirty(state, action) {
      state.stepEditorDirty = action.payload;
    },

    resetState() {
      return { ...initialState };
    },
  },
});

const {
  reducer,
  actions: {
    // sidebar
    fetchModules,
    setModules,
    setModulesError,
    loadStepById,
    setStepDefinitionAttributes,
    closeProcessBuilder,

    // Triggers
    requestOpenProcessBuilder,
    loadProcessById,
    saveOrCreateProcess,

    // Step state managment
    openProcessBuilder,
    setProcessDetails,
    setProcessError,
    setProcessLoading,
    setProcessModules,
    setProcessSaving,
    setStepEditorDirty,
    setProcessAttributes,
    resetState,
    toggleDetailsModal,
    setAffectedProjects,
    setAffectedProjectsError,
    closeAffectedProjectsModal,
  },
} = processBuilderSlice;

reducer.key = 'processBuilder';

export default reducer;

export {
  // sidebar
  fetchModules,
  setModules,
  setModulesError,
  loadStepById,
  setStepDefinitionAttributes,
  // Triggers
  requestOpenProcessBuilder,
  loadProcessById,
  saveOrCreateProcess,
  closeProcessBuilder,
  // Step state managment
  openProcessBuilder,
  setProcessDetails,
  setProcessError,
  setProcessLoading,
  setProcessModules,
  setProcessSaving,
  setStepEditorDirty,
  setProcessAttributes,
  resetState,
  toggleDetailsModal,
  setAffectedProjects,
  setAffectedProjectsError,
  closeAffectedProjectsModal,
};

// SELECTORS

export function selectProcessBuilder(state) {
  return state[reducer.key];
}

export function selectProcessType(state) {
  return get(selectProcessBuilder(state), 'processType', null);
}

export function selectProcessModules(state) {
  return get(selectProcessBuilder(state), 'processModules', []);
}

export function isProcessModulePristine(state) {
  return get(selectProcessBuilder(state), 'processModulesPristine', []);
}

export function selectTemplateProcessModules(state) {
  return get(selectProcessBuilder(state), 'modules', []);
}

export function createSelectStepDefinition(id) {
  return (state) => {
    const steps = get(selectProcessBuilder(state), 'modules', []);
    return steps.find((step) => step.id === id);
  };
}

export function selectProcessDetails(state) {
  const processBuilderState = selectProcessBuilder(state);

  return {
    processName: processBuilderState.processName,
    processDescription: processBuilderState.processDescription,
    processType: processBuilderState.processType,
    processID: processBuilderState.processID,
    copyFromProcessID: processBuilderState.copyFromProcessID,
    instantiated: processBuilderState.instantiated,
  };
}

export function selectProjectSaving(state) {
  return get(selectProcessBuilder(state), 'processSaving', null);
}

export function selectProjectLoading(state) {
  return get(selectProcessBuilder(state), 'processLoading', null);
}

export function selectStepEditorDirty(state) {
  return get(selectProcessBuilder(state), 'stepEditorDirty', false);
}

export function createSelectModuleAttributes(id, key) {
  return (state) => {
    const processBuilderState = selectProcessBuilder(state);

    return get(
      processBuilderState.processModules.find(
        (process) => process.id === id || process.key === key,
      ),
      'attributes',
      [],
    );
  };
}

export function createSelectModuleSubSteps(id, key) {
  return (state) => {
    const processBuilderState = selectProcessBuilder(state);

    return get(
      processBuilderState.processModules.find(
        (process) => process.id === id || process.key === key,
      ),
      'subSteps',
      [],
    );
  };
}

export function selectProcessBuilderOpen(state) {
  return get(selectProcessBuilder(state), 'processBuilderOpen', null);
}

export function selectDetailsModalOpen(state) {
  return get(selectProcessBuilder(state), 'detailsModalOpen', null);
}

export function selectAffectedProjectsModalOpen(state) {
  return get(selectProcessBuilder(state), 'affectedProjectsModalOpen', null);
}

export function selectAffectedProjects(state) {
  const processBuilderState = selectProcessBuilder(state);

  return {
    projects: get(processBuilderState, 'affectedProjects', []),
    loading: get(processBuilderState, 'affectedProjectsLoading', false),
    error: get(processBuilderState, 'affectedProjectsError', null),
  };
}

export function hasAnyDirtyProcessModule(state) {
  const modules = selectProcessModules(state);

  return modules.some((processModule) => processModule.dirty);
}
