import React, { useState, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { v4 as uuid } from 'uuid';
import { isEmpty, isNaN } from 'lodash';

import TabSwitcher from '../../../../../../components/TabSwitcher';
import Icon from '../../../../../../components/Icon';
import InformationTooltip from '../../../../../../components/ProcessBuilder/InformationTooltip';
import Input from '../../../../../../components/Input';
import Button from '../../../../../../components/Button';
import Select from '../../../../../../components/Select';
import DateInput from '../../../../../../components/DateInput';
import Checkbox from '../../../../../../components/Checkbox';
import Switch from '../../../../../../components/Switch';

import FloatContainer, { FloatRight } from '../../../../../../components/Float';
import { setProductivityMetric } from '../../../../../../store/ducks/metricsAndGoals';
import { ReactComponent as Copy } from '../../../../../../assets/icons/copy-goal.svg';
import {
  SectionTitle,
  Label,
  SectionDescription,
} from '../../../../../../styles';

import {
  Settings,
  Text,
  Card,
  CardHeader,
  Title,
  CardBody,
  CardAppend,
} from '../styles';
import {
  GOAL_TYPE_VALUE_MAP,
  GOAL_VALUE_TYPE_MAP,
  CHANGE_REASON_OPTIONS,
} from '../../constants';

const viewAsTabs = [
  {
    key: 0,
    label: 'Equal Goals',
    value: 'EQUALS',
    tooltip: {
      overlay: 'All markets under this metric will have the same goal value',
    },
  },
  {
    key: 1,
    label: 'Per market goals',
    value: 'PER_MARKET',
    tooltip: {
      overlay: 'Each market under this metric can have a unique goal value',
    },
  },
  {
    key: 2,
    label: 'Match Data',
    value: 'MATCH_DATA',
    tooltip: {
      overlay:
        "This metric's goal value will equal the actual client data values.",
    },
  },
];

const EQUALS_GOALS = 0;
const PER_MARKET_GOALS = 1;
const MATCH_DATA = 2;

export default function ProductivityGoalsForm({ closeModal, values }) {
  const metricsAndGoals = useSelector((state) => state.metricsAndGoals);
  const { weekStartDate } = metricsAndGoals;

  const dispatch = useDispatch();
  const { metricId, metricTitle, projectGoals, workerGoals } = values;
  const [projectGoalState, setProjectGoalState] = useState(projectGoals);
  const [workerGoalState, setWorkerGoalState] = useState(workerGoals);
  const [changeReason, setChangeReason] = useState(null);
  const [changeReasonError, setChangeReasonError] = useState(null);
  const [changeReasonDetail, setChangeReasonDetail] = useState(null);
  const [changeReasonDetailError, setChangeReasonDetailError] = useState(null);
  const [isStartProject, setIsStartProject] = useState(false);
  const [isEndProject, setIsEndProject] = useState(false);
  const [changeStartDate, setChangeStartDate] = useState(null);
  const [changeStartDateError, setChangeStartDateError] = useState(null);
  const [changeEndDate, setChangeEndDate] = useState(null);
  const [changeEndDateError, setChangeEndDateError] = useState(null);
  const [projectGoalsTab, setProjectGoalsTab] = useState(
    GOAL_TYPE_VALUE_MAP[projectGoals.goalValueType] || 0,
  );
  const [workerGoalsTab, setWorkerGoalsTab] = useState(
    GOAL_TYPE_VALUE_MAP[workerGoals.goalValueType] || 0,
  );

  const isWeekdate = (date) => {
    const day = date.getDay();

    switch (weekStartDate) {
      case 'SUNDAY':
        return day === 0;
      case 'MONDAY':
        return day === 1;
      case 'TUESDAY':
        return day === 2;
      case 'WEDNESDAY':
        return day === 3;
      case 'THURSDAY':
        return day === 4;
      case 'FRIDAY':
        return day === 5;
      case 'SATURDAY':
        return day === 6;

      default:
        return day === 0;
    }
  };

  const handleProjectGoalsTabChange = useCallback(
    (nextTab) => {
      setProjectGoalState({
        ...projectGoalState,
        goalValueType: GOAL_VALUE_TYPE_MAP[nextTab],
      });

      setProjectGoalsTab(nextTab);
    },
    [projectGoalState],
  );

  const handleWorkerGoalsTabChange = useCallback(
    (nextTab) => {
      setWorkerGoalState({
        ...workerGoalState,
        goalValueType: GOAL_VALUE_TYPE_MAP[nextTab],
      });

      setWorkerGoalsTab(nextTab);
    },
    [workerGoalState],
  );

  const updateMarketValues = useCallback((marketValues, market, newValue) => {
    let updatedMarketGoals = [];

    if (!marketValues) {
      updatedMarketGoals.push({ market, goalValue: newValue });
    } else {
      updatedMarketGoals = marketValues.map((i) => {
        if (i.market === market) {
          i.goalValue = newValue;
          i.error = null;
        }

        return i;
      });
    }
    return updatedMarketGoals;
  }, []);

  const handlePerMarketChange = useCallback(
    (goalType, market, newValue) => {
      if (goalType === 'PROJECT') {
        const marketValues = projectGoalState.productivityMarketGoals;
        const prjValues = updateMarketValues(marketValues, market, newValue);
        setProjectGoalState({
          ...projectGoalState,
          goalValueType: 'PER_MARKET',
          productivityMarketGoals: prjValues,
        });
      }
      if (goalType === 'WORKER') {
        const marketValues = workerGoalState.productivityMarketGoals;
        const workerValues = updateMarketValues(marketValues, market, newValue);
        setWorkerGoalState({
          ...workerGoalState,
          goalValueType: 'PER_MARKET',
          productivityMarketGoals: workerValues,
        });
      }
    },
    [projectGoalState, updateMarketValues, workerGoalState],
  );

  const handleEqualGoalChange = useCallback(
    (goalType, newValue) => {
      if (goalType === 'PROJECT') {
        setProjectGoalState({
          ...projectGoalState,
          goalValueType: 'EQUAL',
          goalValue: newValue,
          equalsError: null,
        });
      }
      if (goalType === 'WORKER') {
        setWorkerGoalState({
          ...workerGoalState,
          goalValueType: 'EQUAL',
          goalValue: newValue,
          equalsError: null,
        });
      }
    },
    [projectGoalState, workerGoalState],
  );

  const renderGoal = useCallback(
    (goal, goalValueType) => {
      switch (goalValueType) {
        case 'EQUAL': {
          const { goalType, goalValue, equalsError } = goal;
          return (
            <li key={goalType} className="equal-goals">
              <Label className="label">all Markets goals</Label>
              <Input
                id={`equal-goal-value-${goalType}`}
                name="goalValue"
                type="number"
                value={goalValue}
                error={equalsError}
                onChange={({ target: { value } }) => {
                  const isPositive =
                    !isNaN(Number(value)) && Number(value) >= 0;

                  handleEqualGoalChange(goalType, isPositive ? value : '');
                }}
              />
            </li>
          );
        }
        case 'PER_MARKET': {
          // Load configured values
          const { productivityMarketGoals, goalType } = goal;
          if (productivityMarketGoals) {
            return productivityMarketGoals.map((item) => {
              const { market, goalValue, error } = item;
              return (
                <li key={`${goalType}-${market}`}>
                  <Label className="label">{market}</Label>
                  <Input
                    id={`mkt-goal-value-${uuid()}`}
                    name={market}
                    type="number"
                    value={String(goalValue)}
                    error={error}
                    onChange={({ target: { value, name } }) => {
                      const isPositive =
                        !isNaN(Number(value)) && Number(value) >= 0;

                      handlePerMarketChange(
                        goalType,
                        name,
                        isPositive ? value : '',
                      );
                    }}
                  />
                </li>
              );
            });
          }
          break;
        }
        case 'MATCH_DATA': {
          return (
            <div style={{ backgroundColor: 'rgba(223, 224, 230, 0.3)' }}>
              <Text>
                <Icon icon={<Copy />} />
                <p>The goal value will always equal actual data values.</p>
              </Text>
            </div>
          );
        }

        default:
          return <p>Goals not defined</p>;
      }
      return null;
    },
    [handleEqualGoalChange, handlePerMarketChange],
  );

  const validate = (metric) => {
    let valid = true;
    const transformedProjectGoal = { ...projectGoalState };
    const transformedWorkerGoal = { ...workerGoalState };

    if (projectGoalsTab === EQUALS_GOALS) {
      if (
        metric.productivityGoals[0].goalValue === null ||
        metric.productivityGoals[0].goalValue === ''
      ) {
        valid = false;
        setProjectGoalState({
          ...transformedProjectGoal,
          equalsError: 'is required',
        });
      }
    } else if (projectGoalsTab === PER_MARKET_GOALS) {
      transformedProjectGoal.productivityMarketGoals = transformedProjectGoal.productivityMarketGoals.map(
        (g) => {
          const newGoal = { ...g };

          if (g.goalValue === '' || g.goalValue === null) {
            valid = false;
            newGoal.error = 'is required';
          }

          return newGoal;
        },
      );

      setProjectGoalState(transformedProjectGoal);
    }

    if (workerGoalsTab === EQUALS_GOALS) {
      if (
        metric.productivityGoals[1].goalValue === null ||
        metric.productivityGoals[1].goalValue === ''
      ) {
        valid = false;
        setWorkerGoalState({
          ...transformedWorkerGoal,
          equalsError: 'is required',
        });
      }
    } else if (workerGoalsTab === PER_MARKET_GOALS) {
      transformedWorkerGoal.productivityMarketGoals = transformedWorkerGoal.productivityMarketGoals.map(
        (g) => {
          const newGoal = { ...g };

          if (g.goalValue === '' || g.goalValue === null) {
            valid = false;
            newGoal.error = 'is required';
          }

          return newGoal;
        },
      );

      setWorkerGoalState(transformedWorkerGoal);
    }

    if (metricId) {
      if (isEmpty(changeReason)) {
        valid = false;
        setChangeReasonError('is required');
      }
      if (!isStartProject && isEmpty(changeStartDate)) {
        valid = false;
        setChangeStartDateError('is required');
      }
      if (!isEndProject && isEmpty(changeEndDate)) {
        valid = false;
        setChangeEndDateError('is required');
      }
      if (changeReason === 'OTHER' && isEmpty(changeReasonDetail)) {
        valid = false;
        setChangeReasonDetailError('is required');
      }
    }

    return valid;
  };

  const renderFormSettings = (title, goalsTabs, handleTabChange, goalState) => {
    return (
      <Settings>
        <Row>
          <Col col={7} className="header">
            <SectionTitle className="title">{title}</SectionTitle>
            <TabSwitcher
              tabs={viewAsTabs}
              value={goalsTabs}
              onChange={handleTabChange}
            />
          </Col>
        </Row>
        {goalsTabs === EQUALS_GOALS && (
          <ul className="goals-list">{renderGoal(goalState, 'EQUAL')}</ul>
        )}
        {goalsTabs === PER_MARKET_GOALS && (
          <ul className="goals-list">{renderGoal(goalState, 'PER_MARKET')}</ul>
        )}
        {goalsTabs === MATCH_DATA && <>{renderGoal(goalState, 'MATCH_DATA')}</>}
        {title === 'Worker Goals' && (
          <div style={{ marginTop: '24px' }}>
            <Checkbox
              name="override-worker-goal"
              onChange={() => {
                setWorkerGoalState({
                  ...workerGoalState,
                  overrideIndividualGoal: !workerGoalState.overrideIndividualGoal,
                });
              }}
              checked={workerGoalState.overrideIndividualGoal}
              bold={false}
            >
              Overwrite individual worker goals
              <InformationTooltip
                placement="right"
                title={
                  <>
                    If checked, the new worker goal values will be set for all
                    workers, including the ones that had been uniquely edited.
                  </>
                }
              />
            </Checkbox>
          </div>
        )}
      </Settings>
    );
  };

  const cleanUpGoal = (goal) => {
    const transformed = {
      ...goal,
      changeReason,
      changeReasonDetail,
      changeStartDate,
      changeEndDate,
    };

    const { goalValueType } = goal;
    if (goalValueType === 'PER_MARKET') {
      // Remove empty / non-configured itens
      const { productivityMarketGoals } = goal;
      const marketValues = productivityMarketGoals.filter(
        (item) => item.goalValue !== null,
      );

      transformed.goalValue = null;
      transformed.productivityMarketGoals = marketValues;
    }

    if (goalValueType === 'EQUAL') {
      transformed.productivityMarketGoals = null;
    }

    if (goalValueType === 'MATCH_DATA') {
      transformed.productivityMarketGoals = null;
      transformed.goalValue = null;
    }

    return transformed;
  };

  const handleSubmit = () => {
    const projectGoal = cleanUpGoal(projectGoalState);
    const workerGoal = cleanUpGoal(workerGoalState);
    const productivityMetric = {
      ...values,
      disabled: false,
      isCollapsed: false,
      productivityGoals: [projectGoal, workerGoal],

      error: false,
    };

    if (validate(productivityMetric)) {
      dispatch(setProductivityMetric({ productivityMetric }));
      closeModal();
    }
  };

  return (
    <>
      <Card className="card">
        <CardHeader>
          <Title>{metricTitle}</Title>
        </CardHeader>
        <CardBody className="active">
          {renderFormSettings(
            'Project-Market Goals',
            projectGoalsTab,
            handleProjectGoalsTabChange,
            projectGoalState,
          )}

          {renderFormSettings(
            'Worker Goals',
            workerGoalsTab,
            handleWorkerGoalsTabChange,
            workerGoalState,
          )}
        </CardBody>
        {metricId && (
          <CardAppend>
            <Settings>
              <Row>
                <Col col={12} className="header">
                  <SectionTitle className="title">Change Log</SectionTitle>
                  <SectionDescription
                    className="description"
                    style={{ margin: '0 0 24px 0' }}
                  >
                    Select the time frame of this goal change and specify the
                    change reason.
                  </SectionDescription>
                </Col>
              </Row>
              <Row>
                <Col col={5} className="header">
                  <Label htmlFor="change-reason">reason of change</Label>
                  <Select
                    value={changeReason}
                    name="change-reason"
                    error={changeReasonError}
                    onChange={({ value }) => {
                      setChangeReason(value);
                      setChangeReasonError(null);
                    }}
                    options={CHANGE_REASON_OPTIONS}
                    placeholder="Select"
                    errorIncreasesHeight
                  />
                </Col>
                {changeReason === 'OTHER' && (
                  <Col col={5} className="header">
                    <div style={{ paddingTop: '21px' }}>
                      <Input
                        name="change-reason-detail"
                        value={changeReasonDetail}
                        placeholder="Some reason here"
                        error={changeReasonDetailError}
                        errorIncreasesHeight
                        onChange={({ target: { value } }) => {
                          setChangeReasonDetail(value);
                          setChangeReasonDetailError(null);
                        }}
                      />
                    </div>
                  </Col>
                )}
              </Row>
              <Row>
                <Col col={5} className="header">
                  <Label htmlFor="change-start-date">Start date</Label>
                  <DateInput
                    value={
                      changeStartDate ? new Date(changeStartDate) : undefined
                    }
                    id="change-start-date"
                    name="change-start-datee"
                    disabled={isStartProject}
                    error={changeStartDateError}
                    filterDate={(e) => isWeekdate(e)}
                    maxDate={
                      changeEndDate ? new Date(changeEndDate) : undefined
                    }
                    onChange={(value) => {
                      const date = value ? value.toISOString() : undefined;

                      setChangeStartDate(date);
                      setChangeStartDateError(null);
                    }}
                    placeholder="Select Start Date"
                  />
                  <div style={{ display: 'flex', marginTop: '12px' }}>
                    <Switch
                      name="is-start-of-project"
                      onChange={() => {
                        setIsStartProject(!isStartProject);
                        setChangeStartDateError(null);
                      }}
                      value={isStartProject}
                    />
                    <span style={{ marginLeft: '8px' }}> Start of project</span>
                  </div>
                </Col>
                <Col col={5} className="header">
                  <Label htmlFor="change-end-date">end date</Label>
                  <DateInput
                    value={changeEndDate ? new Date(changeEndDate) : undefined}
                    id="change-end-date"
                    name="change-end-date"
                    disabled={isEndProject}
                    error={changeEndDateError}
                    filterDate={(e) => isWeekdate(e)}
                    minDate={
                      changeStartDate ? new Date(changeStartDate) : undefined
                    }
                    onChange={(value) => {
                      const date = value ? value.toISOString() : undefined;

                      setChangeEndDate(date);
                      setChangeEndDateError(null);
                    }}
                    placeholder="Select End Date"
                  />
                  <div style={{ display: 'flex', marginTop: '12px' }}>
                    <Switch
                      name="is-end-of-project"
                      onChange={() => {
                        setIsEndProject(!isEndProject);
                        setChangeEndDateError(null);
                      }}
                      value={isEndProject}
                    />
                    <span style={{ marginLeft: '8px' }}>No end date</span>
                  </div>
                </Col>
              </Row>
            </Settings>
          </CardAppend>
        )}
      </Card>
      <FloatContainer className="actions">
        <FloatRight>
          <Button type="secondary" onClick={closeModal}>
            Cancel
          </Button>
          <Button onClick={handleSubmit}>Save</Button>
        </FloatRight>
      </FloatContainer>
    </>
  );
}

ProductivityGoalsForm.propTypes = {
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  closeModal: PropTypes.func.isRequired,
};
