import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { PreviewSectionTextBase, RequiredFieldEmpty } from './styles';

export const PreviewContext = React.createContext({ validateEmpty: false });

export const PreviewContextProvider = PreviewContext.Provider;

export default function PreviewSectionText(props) {
  const { children, isEmpty } = props;

  const { validateEmpty } = useContext(PreviewContext);

  if ((validateEmpty && !children) || isEmpty) {
    return (
      // eslint-disable-next-line react/jsx-filename-extension
      <PreviewSectionTextBase>
        <RequiredFieldEmpty>Required field is empty</RequiredFieldEmpty>
      </PreviewSectionTextBase>
    );
  }

  return <PreviewSectionTextBase>{children || '-'}</PreviewSectionTextBase>;
}

PreviewSectionText.propTypes = {
  children: PropTypes.node,
  isEmpty: PropTypes.bool,
};

PreviewSectionText.defaultProps = {
  children: null,
  isEmpty: false,
};
