import React, { useMemo, useCallback } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import {
  PROCESS_TYPE,
  selectAffectedProjects,
  closeAffectedProjectsModal,
} from '../../../store/ducks/processBuilder';
import Table from '../../Table';
import { Paragraph, StyledTable, ShimmerContainer } from './styles';
import ActionFooter from '../../ActionFooter';
import { Spacer } from '../../Builder/ProcessesColumn/styles';
import TableShimmer from '../../Shimmers/TableShimmer';
import ParagraphShimmer from '../../Shimmers/ParagraphShimmer';

const columns = [
  {
    Header: 'PROJECT NAME',
    accessor: 'name',
    key: 'name',
  },
  {
    Header: 'PROJECT ALIAS',
    accessor: 'alias',
    key: 'alias',
  },
];

export default function AffectedProjectsModal(props) {
  const { processType, onCancel } = props;

  const processTypeLabel = useMemo(() => {
    return processType === PROCESS_TYPE.REGISTRATION
      ? 'registration'
      : 'qualification';
  }, [processType]);

  const affectedProjects = useSelector(selectAffectedProjects);

  const dispatch = useDispatch();

  const proceedAction = useCallback(() => {
    dispatch(closeAffectedProjectsModal());
  }, [dispatch]);

  if (affectedProjects.loading) {
    return (
      <ShimmerContainer>
        <ParagraphShimmer lines={2} />
        <Spacer />
        <TableShimmer cols={2} />
        <Spacer />
        <ParagraphShimmer lines={1} />
      </ShimmerContainer>
    );
  }

  return (
    <div>
      <Paragraph>
        {`This ${processTypeLabel} process is being used by the following projects and all updates will also affect their ${processTypeLabel}.`}
      </Paragraph>
      <StyledTable>
        <Table columns={columns} data={affectedProjects.projects} />
      </StyledTable>
      <Paragraph>Are you sure you want to proceed? </Paragraph>
      <ActionFooter
        rightButtons={[
          {
            text: 'Cancel',
            type: 'secondary',
            onClick: onCancel,
          },
          {
            text: 'Proceed',
            onClick: proceedAction,
          },
        ]}
      />
    </div>
  );
}

AffectedProjectsModal.propTypes = {
  processType: PropTypes.string.isRequired,
  onCancel: PropTypes.func.isRequired,
};
