import styled from 'styled-components';

export const ProgressBar = styled.div`
  width: ${(props) => props.width || '208px'};
  height: 6px;
  margin-top: 30px;
  background-color: ${(props) => props.theme.colors.lightPeriwinkle};
  border-radius: 3px;
`;

export const ProgressFiller = styled.div`
  background-color: ${(props) =>
    props.progressColor || props.theme.colors.azure};
  border-radius: 3px;
  width: ${(props) => `${props.progress || '0'}%`};
  height: 100%;
  transition: width 225ms ease;
`;

export const ProgressLabel = styled.div`
  float: right;
  height: 100%;
  margin-top: -12px;
  color: ${(props) =>
    props.color ? props.color : props.theme.colors.lightGreyBlue};
  font-size: 12px;
  font-weight: 900;
  line-height: 1.33;
  margin-right: ${(props) => (props.emptyState ? '-28px' : '-35px')};
`;
