import {
  handleProjectCreated,
  closeDemographics,
  deleteProject,
  cloneProject,
  viewProject,
  createProject,
  viewProjectExperiments,
  switchCustomer,
  loadProjectSetup,
  viewProjectList,
  openTracker,
} from '../services/postMessage/index';
import features from '../constants/featureFlags';

const PROJECT_VIEW_PATH = 'core/partners/project_setup/view';

function removeTrailingSlashes(url) {
  return url && url.length ? url.replace(/\/+$/, '').replace(/^\/+/g, '') : '';
}

function getBaseUrl() {
  return removeTrailingSlashes(process.env.REACT_APP_QRP_URL);
}

function redirectToPath(path) {
  try {
    const url = `${getBaseUrl()}/${path}`;
    window.top.location.href = url;
    return true;
  } catch (error) {
    return false;
  }
}

function informProjectCreation(project) {
  try {
    const { id } = project || {};
    handleProjectCreated(id);
    return true;
  } catch (error) {
    return false;
  }
}

function informDemographicsClose(project) {
  try {
    const { id } = project || {};
    closeDemographics(id);
    return true;
  } catch (error) {
    return false;
  }
}

function informProjectDeletion(projectId) {
  try {
    deleteProject(projectId);
    return true;
  } catch (error) {
    return false;
  }
}

function informCloneProject(projectId, customerId) {
  try {
    cloneProject(projectId, customerId);
    return true;
  } catch (error) {
    return false;
  }
}

function informViewProject(projectId, disabled) {
  try {
    if (features.oldProjectSetup) {
      viewProject(projectId, disabled);
    } else {
      redirectToPath(`${PROJECT_VIEW_PATH}/${projectId}`);
    }
    return true;
  } catch (error) {
    return false;
  }
}

function informViewProjectExperiments(projectId) {
  viewProjectExperiments(projectId);
}

function informCreateProject() {
  try {
    createProject();
    return true;
  } catch (error) {
    return false;
  }
}

function informSwitchCustomer(customerId) {
  try {
    switchCustomer(customerId);
    return true;
  } catch (error) {
    return false;
  }
}

function informLoadProjectSetup(projectId) {
  try {
    loadProjectSetup(projectId);
    return true;
  } catch (error) {
    return false;
  }
}

function informOpenTracker(trackerId) {
  try {
    openTracker(trackerId);
    return true;
  } catch (error) {
    return false;
  }
}

function informViewProjectList() {
  try {
    viewProjectList();
    return true;
  } catch (error) {
    return false;
  }
}

export default {
  informProjectCreation,
  informDemographicsClose,
  informProjectDeletion,
  informCloneProject,
  informViewProject,
  informCreateProject,
  informViewProjectExperiments,
  informSwitchCustomer,
  informLoadProjectSetup,
  informViewProjectList,
  informOpenTracker,
  redirectToPath,
};
