import React, { useMemo, useState, useEffect } from 'react';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Carousel } from 'react-responsive-carousel';
import { useSelector } from 'react-redux';
import shortid from 'shortid';
import flatten from 'lodash/flatten';
import get from 'lodash/get';
import { usePreviewContext } from '../../Builder/helpers/preview-context';
import { Modal } from '../../Modal/Modal';
import {
  PROCESS_TYPE,
  selectProcessDetails,
  selectProcessModules,
} from '../../../store/ducks/processBuilder';
import ActionFooter from '../../ActionFooter';
import IFrame from './IFrame';
import {
  CarouselContainer,
  Indicators,
  Indicator,
  IFrameLoadingContainer,
  StepTitle,
} from './styles';
import Arrow from './Arrow';
import { createKeydownHandler } from '../../../helpers/a11y';
import { createDTOToProcessModuleMapper } from '../../../store/ducks/processBuilder/helpers';

function renderArrowPrev(handleClick, hasPrev) {
  return <Arrow onClick={handleClick} direction="backward" hasPrev={hasPrev} />;
}

function renderArrowNext(handleClick, hasNext) {
  return <Arrow onClick={handleClick} direction="forward" hasNext={hasNext} />;
}

function flattenQuizes(processModules) {
  return flatten(
    processModules.map((processModule) => {
      if (
        Array.isArray(processModule.subSteps) &&
        processModule.subSteps.length
      ) {
        return processModule.subSteps.map((subStep) => ({
          name: processModule.name,
          sequenceFlow: processModule.sequenceFlow,
          index: processModule.index || -1,
          ...subStep,
        }));
      }

      return [processModule];
    }),
  );
}

function addIDsToProcessModules(processModules) {
  return processModules.map((p) => ({
    processModule: p,
    id: shortid.generate(),
  }));
}

function getStepName(processModulesWithIdentifiers, currentStep) {
  if (processModulesWithIdentifiers.length === 1) {
    const { index, name } = get(
      processModulesWithIdentifiers,
      '[0].processModule',
      { index: -1, name: '' },
    );

    if (!index || index === -1) {
      return name;
    }

    return `STEP ${index + 1}: ${name}`;
  }

  const name = get(
    processModulesWithIdentifiers,
    `[${currentStep}].processModule.name`,
    '',
  );

  return `STEP ${currentStep + 1}: ${name}`;
}

export default function PreviewModal() {
  const { modalProps, modalControls } = usePreviewContext();
  const [currentStep, setCurrentStep] = useState(0);

  const processDetails = useSelector(selectProcessDetails);
  const processModules = useSelector(selectProcessModules);

  const processType = useMemo(() => {
    if (processDetails && processDetails.processType) {
      return processDetails.processType;
    }

    if (modalControls.modalData && modalControls.modalData.processType) {
      return modalControls.modalData.processType;
    }

    return null;
  }, [modalControls.modalData, processDetails]);

  useEffect(() => {
    setCurrentStep(0);
  }, [processModules, modalProps.open]);

  const processModulesWithIds = useMemo(() => {
    if (!modalProps.open) {
      return [];
    }

    let modules = [];

    if (modalControls.modalData) {
      if (modalControls.modalData.only) {
        modules = flattenQuizes([modalControls.modalData.only]);
      }

      if (modalControls.modalData.steps) {
        modules = flattenQuizes(
          (modalControls.modalData.steps || []).map(
            createDTOToProcessModuleMapper(),
          ),
        );
      }
    } else {
      modules = flattenQuizes(processModules);
    }

    return addIDsToProcessModules(modules);
  }, [modalControls.modalData, modalProps.open, processModules]);

  const stepName = useMemo(() => {
    return getStepName(processModulesWithIds, currentStep);
  }, [currentStep, processModulesWithIds]);

  return (
    <Modal
      {...modalProps}
      title={`Preview ${
        processType === PROCESS_TYPE.REGISTRATION
          ? 'Registration'
          : 'Qualification'
      } Process`}
    >
      {modalProps.open ? (
        <>
          <StepTitle>{stepName}</StepTitle>

          <CarouselContainer>
            <Carousel
              renderArrowPrev={renderArrowPrev}
              renderArrowNext={renderArrowNext}
              showIndicators={false}
              showStatus={false}
              showThumbs={false}
              onChange={setCurrentStep}
              selectedItem={currentStep}
            >
              {processModulesWithIds.map(({ id, processModule }) => (
                <IFrame key={id} id={id} processModule={processModule} />
              ))}
            </Carousel>
          </CarouselContainer>
        </>
      ) : (
        <IFrameLoadingContainer />
      )}

      <Indicators>
        {processModulesWithIds.length !== 1 &&
          processModulesWithIds.map((x, index) => (
            <Indicator
              isActive={index === currentStep}
              onClick={() => setCurrentStep(index)}
              onKeyDown={createKeydownHandler(() => setCurrentStep(index), 32)}
              role="button"
              tabIndex={0}
            />
          ))}
      </Indicators>

      <ActionFooter
        rightButtons={[
          { text: 'Exit Preview Mode', onClick: modalControls.closeModal },
        ]}
      />
    </Modal>
  );
}
