import { useMemo } from 'react';
import withHooks from 'hook-hoc';
import HiringTargetHeader from './HiringTargetHeader';
import { fromPercent } from '../../../helpers';

function renderCountryAbbre(abbrev) {
  if (abbrev === '*') {
    return 'All';
  }

  return abbrev;
}

function useHooksAsProps(outerProps) {
  const {
    column: { hiringTarget, viewAsPercent },
    data,
  } = outerProps;

  const sum = (data || [])
    .map((item) => {
      return item[hiringTarget.id] && item[hiringTarget.id].count
        ? parseInt(item[hiringTarget.id].count, 10)
        : 0;
    })
    .filter((item) => !Number.isNaN(item))
    .reduce((current, accumulated) => accumulated + current, 0);

  const target = useMemo(
    () => (viewAsPercent ? 100 : parseInt(hiringTarget.target || 0, 10)),
    [hiringTarget.target, viewAsPercent],
  );

  const hasTooMany = useMemo(
    () => (viewAsPercent ? fromPercent(sum, target) > target : sum > target),
    [sum, target, viewAsPercent],
  );

  const percentChar = useMemo(() => (viewAsPercent ? '%' : ''), [
    viewAsPercent,
  ]);

  return {
    sum,
    target,
    percentChar,
    hasTooMany,
    isRounded: sum === Math.round(sum),
    restrictToCountry: renderCountryAbbre(hiringTarget.restrictToCountry),
    localeDialect: renderCountryAbbre(hiringTarget.localeDialect),
    localeLanguage: hiringTarget.localeLanguage,
  };
}

export default withHooks(useHooksAsProps)(HiringTargetHeader);
