import React, { useEffect, useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useSelector, useDispatch } from 'react-redux';
import get from 'lodash/get';
import Button from '../../../../Button';
import CancelButton from './styles';
import Modal from '../../../../Modal';
import FloatContainer, { FloatRight } from '../../../../Float';
import Select from '../../../../Select';
import { Label, Section } from '../../../../../styles';
import * as tenantsByCountry from '../../../../../store/ducks/tenantsByCountry';
import { findById } from '../../../../../helpers/find';
import ErrorPopover, { ErrorPopoverProps } from '../../../../ErrorPopover';
import mapToOptions from '../../../../../pages/ProjectSetup/helpers/mapToOptions';
import { ENTITIES, fetch, selectors } from '../../../../../store/ducks/options';

function mapNameToLabel(tenant) {
  const name = get(tenant, 'name', '');
  const employmentType = get(tenant, ['employmentType', 'label'], false);

  return employmentType ? `${name} (${employmentType})` : name;
}

export default function AddTenantModal(props) {
  const {
    modalProps,
    values,
    submitCount,
    errors,
    setFieldValue,
    submitForm,
    initialValues,
    canDelete,
    errorPopoverProps,
    isSubmitting,
  } = props;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
  }, [dispatch]);

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const tenantsLoading = useSelector(tenantsByCountry.selectLoading);
  const tenantsError = useSelector(tenantsByCountry.selectError);

  const handleCountryChange = useCallback(
    ({ value }) => {
      setFieldValue('country', value);

      dispatch(tenantsByCountry.fetchAll(value));
    },
    [dispatch, setFieldValue],
  );

  const selectByCountry = useMemo(
    () => tenantsByCountry.createSelectByCountry(values.country),
    [values.country],
  );

  const tenantsDataByCountry = useSelector(selectByCountry);

  const tenantOptions = mapToOptions(
    'id',
    mapNameToLabel,
  )(tenantsDataByCountry);

  const handleTenantChange = useCallback(
    ({ value }) => {
      const tenant = findById(tenantsDataByCountry, value);

      setFieldValue('tenant', value);
      setFieldValue('tenantType', get(tenant, ['employmentType', 'value'], ''));
    },
    [setFieldValue, tenantsDataByCountry],
  );

  const canSave = useMemo(() => {
    if (values.id) {
      if (!canDelete(values.id)) {
        // if the country is the same, is ok to edit
        return initialValues.country === values.country;
      }
    }

    return true;
  }, [canDelete, initialValues.country, values.country, values.id]);

  return (
    <Modal
      open={modalProps.open}
      onClose={modalProps.onClose}
      onExited={modalProps.onExited}
      title="Add Tenant"
      closeOnOverlayClick={!isSubmitting}
      closeOnEsc={!isSubmitting}
    >
      <Section marginBottom="56px">
        <Row>
          <Col col={6}>
            <Label htmlFor="country">Country</Label>
            <Select
              value={values.country}
              onChange={handleCountryChange}
              placeholder="Select Country"
              name="country"
              id="country"
              options={countries.data}
              loading={countries.loading}
              error={countries.error || (submitCount >= 1 && errors.country)}
              data-testid="project-setup--recruitment--tenant-modal-country-select"
            />
          </Col>
          <Col col={6}>
            <Label htmlFor="tenant">Tenant</Label>
            <Select
              value={values.tenant}
              onChange={handleTenantChange}
              placeholder="Select Tenant"
              name="tenant"
              id="tenant"
              options={tenantOptions}
              loading={tenantsLoading}
              error={tenantsError || (submitCount >= 1 && errors.tenant)}
              data-testid="project-setup--recruitment--tenant-modal-tenant-select"
            />
          </Col>
        </Row>
      </Section>

      <ErrorPopover
        title="Failed to save Locale Tenant"
        {...errorPopoverProps}
      />

      <FloatContainer>
        <FloatRight>
          <CancelButton
            disabled={isSubmitting}
            type="secondary"
            onClick={modalProps.onClose}
            data-testid="project-setup--recruitment--tenant-modal-cancel"
          >
            Cancel
          </CancelButton>
          <span
            title={
              canSave
                ? ''
                : "You can't change this Locale Tenant's country as a Hiring Target depends on it."
            }
          >
            <Button
              loading={isSubmitting}
              onClick={submitForm}
              disabled={!canSave}
              canSave={canSave}
              data-testid="project-setup--recruitment--tenant-modal-save"
            >
              Save
            </Button>
          </span>
        </FloatRight>
      </FloatContainer>
    </Modal>
  );
}

AddTenantModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  values: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ).isRequired,
  initialValues: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  canDelete: PropTypes.func.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
};
