import React, { useState, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { findIndex, isEmpty, isNaN } from 'lodash';

import { Row, Col } from 'styled-bootstrap-grid';
import { v4 as uuid } from 'uuid';
import TabSwitcher from '../../../../../../components/TabSwitcher';
import Icon from '../../../../../../components/Icon';
import InformationTooltip from '../../../../../../components/ProcessBuilder/InformationTooltip';
import Input from '../../../../../../components/Input';
import Button from '../../../../../../components/Button';
import Select from '../../../../../../components/Select';
import Disabled from '../../../../../../components/Disabled';
import DateInput from '../../../../../../components/DateInput';
import Checkbox from '../../../../../../components/Checkbox';
import Switch from '../../../../../../components/Switch';
import FloatContainer, { FloatRight } from '../../../../../../components/Float';
import { setQualityMetric } from '../../../../../../store/ducks/metricsAndGoals';
import { ReactComponent as Copy } from '../../../../../../assets/icons/copy-goal.svg';
import { ReactComponent as Calendar } from '../../../../../../assets/icons/calendar.svg';

import {
  SectionTitle,
  Label,
  SectionDescription,
} from '../../../../../../styles';

import {
  Settings,
  Text,
  Card,
  CardHeader,
  Title,
  CardBody,
  CardAppend,
  PercentageBox,
} from '../styles';

import {
  GOAL_TYPE_VALUE_MAP,
  GOAL_VALUE_TYPE_MAP,
  CHANGE_REASON_OPTIONS,
} from '../../constants';

const tabs = [
  {
    key: 0,
    label: 'Equal Goals',
    value: 'EQUALS',
    tooltip: {
      overlay: 'All markets under this metric will have the same goal value',
    },
  },
  {
    key: 1,
    label: 'Per market goals',
    value: 'PER_MARKET',
    tooltip: {
      overlay: 'Each market under this metric can have a unique goal value',
    },
  },
  {
    key: 2,
    label: 'Match Data',
    value: 'MATCH_DATA',
    tooltip: {
      overlay:
        "This metric's goal value will equal the actual client data values.",
    },
  },
];
const EQUAL = 0;
const PER_MARKET = 1;
const MATCH_DATA = 2;

const updateMarketValues = (marketValues, market, newValue) => {
  let updatedMarketGoals = [];

  if (!marketValues) {
    updatedMarketGoals.push({ market, goalValue: newValue });
  } else {
    updatedMarketGoals = marketValues.map((i) => {
      if (i.market === market) i.goalValue = newValue;
      return i;
    });
  }

  return updatedMarketGoals;
};

export default function QualityGoalsForm({ closeModal, values }) {
  const metricsAndGoals = useSelector((state) => state.metricsAndGoals);
  const { weekStartDate } = metricsAndGoals;
  const dispatch = useDispatch();
  const {
    metricTitle,
    projectGoals,
    workerGoals,
    versions,
    sVersion,
    sDate,
    isQuizBased,
    isEdit,
  } = values;

  const [selectedVersion, setSelectedVersion] = useState(sVersion);
  const [copyVersion, setCopyVersion] = useState(null);
  const [selectedDate, setSelectedDate] = useState(sDate);
  const [selectedDateError, setSelectedDateError] = useState('');
  const [changeReason, setChangeReason] = useState(null);
  const [changeReasonError, setChangeReasonError] = useState(null);
  const [changeReasonDetail, setChangeReasonDetail] = useState(null);
  const [changeReasonDetailError, setChangeReasonDetailError] = useState(null);
  const [isStartProject, setIsStartProject] = useState(false);
  const [isEndProject, setIsEndProject] = useState(false);
  const [changeStartDate, setChangeStartDate] = useState(null);
  const [changeStartDateError, setChangeStartDateError] = useState(null);
  const [changeEndDate, setChangeEndDate] = useState(null);
  const [changeEndDateError, setChangeEndDateError] = useState(null);

  const [projectGoalsTab, setProjectGoalsTab] = useState(
    GOAL_TYPE_VALUE_MAP[projectGoals.goalValueType] || 0,
  );
  const [workerGoalsTab, setWorkerGoalsTab] = useState(
    GOAL_TYPE_VALUE_MAP[workerGoals.goalValueType] || 0,
  );

  const [localProjectGoal, setLocalProjectGoal] = useState(projectGoals);
  const [localWorkerGoal, setLocalWorkerGoal] = useState(workerGoals);

  const isWeekdate = (date) => {
    const day = date.getDay();

    switch (weekStartDate) {
      case 'SUNDAY':
        return day === 0;
      case 'MONDAY':
        return day === 1;
      case 'TUESDAY':
        return day === 2;
      case 'WEDNESDAY':
        return day === 3;
      case 'THURSDAY':
        return day === 4;
      case 'FRIDAY':
        return day === 5;
      case 'SATURDAY':
        return day === 6;

      default:
        return day === 0;
    }
  };

  const handleProjectGoalsTabChange = useCallback(
    (nextTab) => {
      setLocalProjectGoal({
        ...localProjectGoal,
        goalValueType: GOAL_VALUE_TYPE_MAP[nextTab],
      });
      setProjectGoalsTab(nextTab);
    },
    [localProjectGoal],
  );

  const handleWorkerGoalsTabChange = useCallback(
    (nextTab) => {
      setLocalWorkerGoal({
        ...localWorkerGoal,
        goalValueType: GOAL_VALUE_TYPE_MAP[nextTab],
      });
      setWorkerGoalsTab(nextTab);
    },
    [localWorkerGoal],
  );

  const handlePerMarketChange = useCallback(
    (goalType, market, newValue) => {
      if (goalType === 'PROJECT') {
        const marketValues = localProjectGoal.qualityMarketGoals;
        const prjValues = updateMarketValues(marketValues, market, newValue);
        setLocalProjectGoal({
          ...localProjectGoal,
          goalValueType: 'PER_MARKET',
          qualityMarketGoals: prjValues,
        });
      }
      if (goalType === 'WORKER') {
        const marketValues = localWorkerGoal.qualityMarketGoals;
        const workerValues = updateMarketValues(marketValues, market, newValue);
        setLocalWorkerGoal({
          ...localWorkerGoal,
          goalValueType: 'PER_MARKET',
          qualityMarketGoals: workerValues,
        });
      }
    },
    [localProjectGoal, localWorkerGoal],
  );

  const handleEqualGoalChange = useCallback(
    (goalType, newValue) => {
      if (goalType === 'PROJECT') {
        setLocalProjectGoal({
          ...localProjectGoal,
          goalValueType: 'EQUAL',
          goalValue: newValue,
        });
      }
      if (goalType === 'WORKER') {
        setLocalWorkerGoal({
          ...localWorkerGoal,
          goalValueType: 'EQUAL',
          goalValue: newValue,
        });
      }
    },
    [localProjectGoal, localWorkerGoal],
  );

  const handleChangeVersion = useCallback(
    (value) => {
      const version = versions.find((el) => el.value === value);
      setLocalProjectGoal(version.projectGoals);
      setLocalWorkerGoal(version.workerGoals);
      setSelectedVersion(version.value);
    },
    [versions],
  );

  const renderGoal = useCallback(
    (goal, goalValueType) => {
      switch (goalValueType) {
        case EQUAL: {
          const { goalType, goalValue, equalsError } = goal;

          return (
            <ul className="goals-list">
              <li key={goalType} className="equal-goals">
                <Label className="label">all Markets goals</Label>
                <Input
                  id={`equal-goal-value-${goalType}`}
                  name="goalValue"
                  type="number"
                  value={goalValue}
                  error={equalsError}
                  onChange={({ target: { value } }) => {
                    const isPositive =
                      !isNaN(Number(value)) && Number(value) >= 0;

                    handleEqualGoalChange(goalType, isPositive ? value : '');
                  }}
                  iconAfter={<PercentageBox>%</PercentageBox>}
                />
              </li>
            </ul>
          );
        }
        case PER_MARKET: {
          // Load configured values
          const { qualityMarketGoals, goalType } = goal;

          return (
            <ul className="goals-list">
              {qualityMarketGoals.map((item) => {
                const { market, goalValue, error } = item;

                return (
                  <li key={`${goalType}-${market}`}>
                    <Label className="label">{market}</Label>
                    <Input
                      id={`mkt-goal-value-${uuid()}`}
                      name={market}
                      type="number"
                      value={String(goalValue)}
                      error={error}
                      iconAfter={<PercentageBox>%</PercentageBox>}
                      onChange={({ target: { value, name } }) => {
                        const isPositive =
                          !isNaN(Number(value)) && Number(value) >= 0;

                        handlePerMarketChange(
                          goalType,
                          name,
                          isPositive ? value : '',
                        );
                      }}
                    />
                  </li>
                );
              })}
            </ul>
          );
        }
        case MATCH_DATA: {
          return (
            <div style={{ backgroundColor: 'rgba(223, 224, 230, 0.3)' }}>
              <Text>
                <Icon icon={<Copy />} />
                <p>The goal value will always equal actual data values.</p>
              </Text>
            </div>
          );
        }

        default:
          return <p>Goals not defined</p>;
      }
    },
    [handleEqualGoalChange, handlePerMarketChange],
  );

  const cleanUpGoal = (goal) => {
    const { goalValueType } = goal;
    const transformed = {
      ...goal,
      changeReason,
      changeReasonDetail,
      changeStartDate,
      changeEndDate,
    };

    if (goalValueType === 'PER_MARKET') {
      transformed.goalValue = null;
    }
    if (goalValueType === 'EQUAL') {
      transformed.qualityMarketGoals = null;
    }

    if (goalValueType === 'MATCH_DATA') {
      transformed.qualityMarketGoals = null;
      transformed.goalValue = null;
    }

    return transformed;
  };

  const handleCopy = (value) => {
    const version = versions.find((el) => el.value === value);

    setLocalProjectGoal({
      ...localProjectGoal,
      ...version.projectGoals,
      key: localProjectGoal.key,
      id: localProjectGoal.id,
    });

    setLocalWorkerGoal({
      ...localWorkerGoal,
      ...version.workerGoals,
      key: localWorkerGoal.key,
      id: localWorkerGoal.id,
    });

    setProjectGoalsTab(GOAL_TYPE_VALUE_MAP[localProjectGoal.goalValueType]);
    setWorkerGoalsTab(GOAL_TYPE_VALUE_MAP[localWorkerGoal.goalValueType]);
    setCopyVersion(version.value);
  };

  const handleSelectDate = (value) => {
    const quizDate = value ? value.toISOString() : undefined;

    setLocalProjectGoal({ ...localProjectGoal, quizDate });
    setLocalWorkerGoal({ ...localWorkerGoal, quizDate });
    setSelectedDate(quizDate);
    setSelectedDateError('');
  };

  const validate = () => {
    let valid = true;
    const transformedProjectGoal = { ...localProjectGoal };
    const transformedWorkerGoal = { ...localWorkerGoal };

    if (isQuizBased) {
      if (
        isEmpty(transformedProjectGoal.quizDate) ||
        isEmpty(transformedWorkerGoal.quizDate)
      ) {
        valid = false;
        setSelectedDateError('is required');
      }
    }

    if (projectGoalsTab === EQUAL) {
      if (
        transformedProjectGoal.goalValue === null ||
        transformedProjectGoal.goalValue === ''
      ) {
        valid = false;
        setLocalProjectGoal({
          ...transformedProjectGoal,
          equalsError: 'is required',
        });
      }
    } else if (projectGoalsTab === PER_MARKET) {
      transformedProjectGoal.qualityMarketGoals = transformedProjectGoal.qualityMarketGoals.map(
        (g) => {
          const newGoal = { ...g };

          if (g.goalValue === '' || g.goalValue === null) {
            valid = false;
            newGoal.error = 'is required';
          }

          return newGoal;
        },
      );

      setLocalProjectGoal(transformedProjectGoal);
    }

    if (workerGoalsTab === EQUAL) {
      if (
        transformedWorkerGoal.goalValue === null ||
        transformedWorkerGoal.goalValue === ''
      ) {
        valid = false;
        setLocalWorkerGoal({
          ...transformedWorkerGoal,
          equalsError: 'is required',
        });
      }
    } else if (workerGoalsTab === PER_MARKET) {
      transformedWorkerGoal.qualityMarketGoals = transformedWorkerGoal.qualityMarketGoals.map(
        (g) => {
          const newGoal = { ...g };

          if (g.goalValue === '' || g.goalValue === null) {
            valid = false;
            newGoal.error = 'is required';
          }

          return newGoal;
        },
      );

      setLocalWorkerGoal(transformedWorkerGoal);
    }

    if (localProjectGoal.id) {
      if (isEmpty(changeReason)) {
        valid = false;
        setChangeReasonError('is required');
      }
      if (changeReason === 'OTHER' && isEmpty(changeReasonDetail)) {
        valid = false;
        setChangeReasonDetailError('is required');
      }
      if (!isQuizBased) {
        if (!isStartProject && isEmpty(changeStartDate)) {
          valid = false;
          setChangeStartDateError('is required');
        }
        if (!isEndProject && isEmpty(changeEndDate)) {
          valid = false;
          setChangeEndDateError('is required');
        }
      }
    }

    return valid;
  };

  const handleSubmit = () => {
    const projectGoal = cleanUpGoal(localProjectGoal);
    const workerGoal = cleanUpGoal(localWorkerGoal);

    let qualityGoals = [];
    if (values.goals.length > 0) {
      qualityGoals = [...values.goals];
      const pIndex = findIndex(
        qualityGoals,
        (el) => el.key === projectGoal.key,
      );
      const wIndex = findIndex(qualityGoals, (el) => el.key === workerGoal.key);

      if (pIndex !== undefined) {
        qualityGoals[pIndex] = projectGoal;
      } else {
        qualityGoals.push(projectGoal);
      }

      if (wIndex !== undefined) {
        qualityGoals[wIndex] = workerGoal;
      } else {
        qualityGoals.push(workerGoal);
      }
    }

    const qualityMetric = {
      ...values.metric,
      disabled: false,
      qualityGoals,
      error: false,
    };

    if (validate(qualityMetric)) {
      dispatch(setQualityMetric({ qualityMetric }));
      closeModal();
    }
  };

  return (
    <>
      {isQuizBased && isEdit && (
        <div style={{ width: '300px', marginBottom: '24px' }}>
          <div
            style={{
              display: 'flex',
              alignItems: 'flex-end',
              marginBottom: '8px',
            }}
          >
            <div style={{ display: 'inline-flex', marginRight: '6px' }}>
              <Calendar />
            </div>
            <Label
              style={{ display: 'inline-flex', margin: 0 }}
              htmlFor="quiz-date-select"
            >
              select a quiz
            </Label>
          </div>
          <Disabled
            focusOffset={1}
            when={versions.length === 1}
            message="This is your first quiz. This will be enabled when you add more quizes."
          >
            <Select
              value={selectedVersion}
              name="quiz-date-select"
              onChange={({ value }) => {
                handleChangeVersion(value);
              }}
              options={versions}
              placeholder="Select Week Start Date"
              disabled={versions.length === 1}
            />
          </Disabled>
        </div>
      )}

      <Card className="card">
        <CardHeader>
          <Title>{metricTitle}</Title>
        </CardHeader>
        <CardBody className="active">
          {isQuizBased && (
            <div style={{ display: 'flex', marginBottom: '24px' }}>
              <div style={{ width: '300px', marginRight: '24px' }}>
                <Label htmlFor="quiz-date">Quiz date</Label>
                <DateInput
                  value={selectedDate ? new Date(selectedDate) : undefined}
                  id="quiz-date"
                  name="quiz-date"
                  onChange={(value) => handleSelectDate(value)}
                  placeholder="Select Date"
                  error={selectedDateError}
                />
              </div>
              <div style={{ width: '300px' }}>
                <Label htmlFor="copy-quiz-date-select">
                  copy goal value from previous date
                </Label>
                <Select
                  value={copyVersion}
                  name="copy-quiz-date-select"
                  onChange={({ value }) => {
                    handleCopy(value);
                  }}
                  options={versions}
                  placeholder="Do not copy"
                  disabled={versions.length === 1}
                />
              </div>
            </div>
          )}
          <Settings>
            <Row>
              <Col col={7} className="header">
                <SectionTitle className="title">
                  Project-Market Goals
                </SectionTitle>
                <TabSwitcher
                  tabs={tabs}
                  value={projectGoalsTab}
                  onChange={handleProjectGoalsTabChange}
                />
              </Col>
            </Row>
            {renderGoal(localProjectGoal, projectGoalsTab)}
          </Settings>
          <Settings>
            <Row>
              <Col col={7} className="header">
                <SectionTitle className="title">Worker Goals</SectionTitle>
                <TabSwitcher
                  tabs={tabs}
                  value={workerGoalsTab}
                  onChange={handleWorkerGoalsTabChange}
                />
              </Col>
            </Row>
            {renderGoal(localWorkerGoal, workerGoalsTab)}
            <div style={{ marginTop: '24px' }}>
              <Checkbox
                name="override-worker-goal"
                onChange={() => {
                  setLocalWorkerGoal({
                    ...localWorkerGoal,
                    overrideIndividualGoal: !localWorkerGoal.overrideIndividualGoal,
                  });
                }}
                checked={localWorkerGoal.overrideIndividualGoal}
                bold={false}
              >
                Overwrite individual worker goals
                <InformationTooltip
                  placement="right"
                  title={
                    <>
                      If checked, the new worker goal values will be set for all
                      workers, including the ones that had been uniquely edited.
                    </>
                  }
                />
              </Checkbox>
            </div>
          </Settings>
        </CardBody>
        {localProjectGoal.id && (
          <CardAppend>
            <Settings>
              <Row>
                <Col col={12} className="header">
                  <SectionTitle className="title">Change Log</SectionTitle>
                  <SectionDescription
                    className="description"
                    style={{ margin: '0 0 24px 0' }}
                  >
                    Select the time frame of this goal change and specify the
                    change reason.
                  </SectionDescription>
                </Col>
              </Row>
              <Row>
                <Col col={5} className="header">
                  <Label htmlFor="change-reason">reason of change</Label>
                  <Select
                    value={changeReason}
                    name="change-reason"
                    error={changeReasonError}
                    onChange={({ value }) => {
                      setChangeReason(value);
                      setChangeReasonError(null);
                    }}
                    options={CHANGE_REASON_OPTIONS}
                    placeholder="Select"
                    errorIncreasesHeight
                  />
                </Col>
                {changeReason === 'OTHER' && (
                  <Col col={5} className="header">
                    <div style={{ paddingTop: '21px' }}>
                      <Input
                        name="change-reason-detail"
                        value={changeReasonDetail}
                        placeholder="Some reason here"
                        error={changeReasonDetailError}
                        errorIncreasesHeight
                        onChange={({ target: { value } }) => {
                          setChangeReasonDetail(value);
                          setChangeReasonDetailError(null);
                        }}
                      />
                    </div>
                  </Col>
                )}
              </Row>
              {!isQuizBased && (
                <Row>
                  <Col col={5} className="header">
                    <Label htmlFor="change-start-date">Start date</Label>
                    <DateInput
                      value={
                        changeStartDate ? new Date(changeStartDate) : undefined
                      }
                      id="change-start-date"
                      name="change-start-datee"
                      disabled={isStartProject}
                      error={changeStartDateError}
                      filterDate={(e) => isWeekdate(e)}
                      maxDate={
                        changeEndDate ? new Date(changeEndDate) : undefined
                      }
                      onChange={(value) => {
                        const date = value ? value.toISOString() : undefined;

                        setChangeStartDate(date);
                        setChangeStartDateError(null);
                      }}
                      placeholder="Select Start Date"
                    />
                    <div style={{ display: 'flex', marginTop: '12px' }}>
                      <Switch
                        name="is-start-of-project"
                        onChange={() => {
                          setIsStartProject(!isStartProject);
                          setChangeStartDateError(null);
                        }}
                        value={isStartProject}
                      />
                      <span style={{ marginLeft: '8px' }}>
                        {' '}
                        Start of project
                      </span>
                    </div>
                  </Col>
                  <Col col={5} className="header">
                    <Label htmlFor="change-end-date">end date</Label>
                    <DateInput
                      value={
                        changeEndDate ? new Date(changeEndDate) : undefined
                      }
                      id="change-end-date"
                      name="change-end-date"
                      disabled={isEndProject}
                      error={changeEndDateError}
                      filterDate={(e) => isWeekdate(e)}
                      minDate={
                        changeStartDate ? new Date(changeStartDate) : undefined
                      }
                      onChange={(value) => {
                        const date = value ? value.toISOString() : undefined;

                        setChangeEndDate(date);
                        setChangeEndDateError(null);
                      }}
                      placeholder="Select End Date"
                    />
                    <div style={{ display: 'flex', marginTop: '12px' }}>
                      <Switch
                        name="is-end-of-project"
                        onChange={() => {
                          setIsEndProject(!isEndProject);
                          setChangeEndDateError(null);
                        }}
                        value={isEndProject}
                      />
                      <span style={{ marginLeft: '8px' }}>No end date</span>
                    </div>
                  </Col>
                </Row>
              )}
            </Settings>
          </CardAppend>
        )}
      </Card>
      <FloatContainer className="actions">
        <FloatRight>
          <Button type="secondary" onClick={closeModal}>
            Cancel
          </Button>
          <Button onClick={handleSubmit}>Save</Button>
        </FloatRight>
      </FloatContainer>
    </>
  );
}

QualityGoalsForm.propTypes = {
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  closeModal: PropTypes.func.isRequired,
};
