import PropTypes from 'prop-types';
import get from 'lodash/get';
import {
  TableContainer,
  MarginHeader,
  HideDisabled,
  HideDisabledContinaer,
} from './styles';
import { Li } from '../../../../styles';
import Table from '../../../Table';
import EnabledCell from './EnabledCell';
import RateCell from './RateCell';
import SpokenWrittenFluencyCell from './SpokenWrittenFluencyCell';
import HiringTargetCell from './HiringTargetCell';
import SettingsCell from './SettingsCell';
import Button from '../../../Button';
import FloatContainer, { FloatRight, FloatLeft } from '../../../Float';
import NoEntries from '../../../NoEntries';
import Switch from '../../../Switch';
import { findById } from '../../../../helpers/find';
import CellWithDefaultDot from './CellWithDefaultDot';
import WordayTaskIdCell from './WordayTaskIdCell';
import { selectById as selectHiringTargetById } from '../../../../store/ducks/hiringTargets';
import store from '../../../../store';
import ErrorPopover from '../../../ErrorPopover';
import UserGroupCell from './UserGroupCell';
import { ENTITIES, fetch } from '../../../../store/ducks/options';
import sortByEnabled from '../../../../helpers/sortByEnabled';
import { alertError } from '../../../Alert';

// eslint-disable-next-line react/prop-types
export const renderHeaderWithMargin = (Header) => (
  <MarginHeader>{Header}</MarginHeader>
);

function sortByRate(rowA, rowB) {
  return rowA.values.rate - rowB.values.rate;
}

function sortByHiringTarget(rowA, rowB) {
  const htA = selectHiringTargetById(
    store.getState(),
    rowA.values.hiringTarget,
  );

  const htB = selectHiringTargetById(
    store.getState(),
    rowB.values.hiringTarget,
  );
  return htA.restrictToCountry.localeCompare(htB.restrictToCountry);
}

export function renderWordayTaskIdTooltip() {
  return (
    <ul>
      Format Options: <br />
      <Li>P##### (5 digits)</Li>
      <Li>CR##### (5 digits)</Li>
      <Li>LR##### (5 digits)</Li>
      <Li>PROJECT_PLAN_TASK-#-### (last digits variable length)</Li>
    </ul>
  );
}

const baseColumns = [
  {
    Header: 'HIRING TARGET',
    accessor: 'hiringTarget',
    key: 'hiringTarget',
    sticky: true,
    width: 250,
    Cell: HiringTargetCell,
    sortType: sortByHiringTarget,
  },
  {
    Header: 'SPOKEN AND WRITTEN FLUENCY',
    accessor: 'spokenFluency',
    key: 'spokenFluency',
    disableSortBy: true,
    width: 250,
    Cell: SpokenWrittenFluencyCell,
  },
  {
    Header: renderHeaderWithMargin('GROUP'),
    accessor: 'userGroup',
    key: 'userGroup',
    disableSortBy: true,
    width: 140,
    Cell: UserGroupCell,
  },
  {
    Header: renderHeaderWithMargin('TASK TYPE'),
    accessor: 'taskType',
    key: 'taskType',
    disableSortBy: true,
    width: 140,
    Cell: CellWithDefaultDot,
  },
  {
    Header: renderHeaderWithMargin('WORKDAY TASK ID'),
    accessor: 'workdayTaskId',
    key: 'workdayTaskId',
    disableSortBy: true,
    width: 210,
    Cell: WordayTaskIdCell,
    tooltip: renderWordayTaskIdTooltip(),
  },
  {
    Header: 'RATE',
    accessor: 'rate',
    key: 'rate',
    maxWidth: 156,
    sortType: sortByRate,
    Cell: RateCell,
  },
  {
    Header: 'ENABLED',
    accessor: 'enabled',
    key: 'enabled',
    maxWidth: 72,
    Cell: EnabledCell,
    sortType: sortByEnabled,
  },
  {
    Header: '',
    accessor: 'id',
    key: 'settings',
    maxWidth: 44,
    disableSortBy: true,
    Cell: SettingsCell,
  },
];

const dataHasTenantWithState = memoize((dataSource) => {
  return !!(dataSource || []).find((item) => !!item.state);
});

const getColumns = memoize((hasTenantWithState) => {
  if (hasTenantWithState) {
    return [
      ...baseColumns.slice(0, 1),
      {
        Header: renderHeaderWithMargin('STATE'),
        accessor: 'state',
        key: 'state',
        disableSortBy: true,
        width: 120,
        Cell: CellWithDefaultDot,
      },
      ...baseColumns.slice(1),
    ];
  }

  return baseColumns;
});

const anyDisabled = memoize(
  (payrates) => !!(payrates || []).filter((payrate) => !payrate.enabled).length,
);

function filterDisabled(value, shouldFilter, canEditPayRate) {
  let filtersDisabled = value;

  if (shouldFilter) {
    filtersDisabled = (value || []).filter((payrate) => payrate.enabled);
  }

  return filtersDisabled.map((payrate) => ({
    ...payrate,
    canEditPayRate,
  }));
}

const payrateErrors = {
  businessUnitRateTypeRequired: new Error(
    'Please select a Rate Type and a Business unit before adding a Pay Rate.',
  ),
  hiringTargetsRequired: new Error(
    'Please create at least one Hiring Target before adding a Pay Rate',
  ),
  payrateRequired: new Error('You must add at least one Pay Rate.'),
};

export default class PayratesSetup extends Component {
  constructor(props) {
    super(props);

    this.toggleChecked = this.toggleChecked.bind(this);
    this.toggleHideDisabled = this.toggleHideDisabled.bind(this);
    this.handleAdd = this.handleAdd.bind(this);
    this.hideError = this.hideError.bind(this);

    this.actionHandlers = {
      toggleEnabled: this.toggleEnabled.bind(this),
      copyPayrate: this.copyPayrate.bind(this),
      editPayrate: this.editPayrate.bind(this),
    };

    this.state = {
      hideDisabled: props.payrates.length > 10,
      showError: null,
      actionError: null,
    };
  }

  componentDidMount() {
    const { dispatch, customerId } = this.props;

    dispatch(fetch({ optionsFor: ENTITIES.userGroups, customerId }));
  }

  getPayrate(id) {
    const { payrates } = this.props;

    return { ...findById(payrates, id) };
  }

  getTableError() {
    const { /* submitCount, payrates, */ enabled, hiringTargets } = this.props;
    const { actionError } = this.state;

    if (!enabled) {
      return payrateErrors.businessUnitRateTypeRequired;
    }

    if (hiringTargets.length === 0) {
      return payrateErrors.hiringTargetsRequired;
    }

    if (actionError) {
      return actionError;
    }

    // Pay Rates required error
    // if (submitCount > 0 && !(payrates || []).length) {
    //   return payrateErrors.payrateRequired;
    // }

    return null;
  }

  toggleEnabled(id, status, toggleAllRowsSelected) {
    const { payrates, payratesActions } = this.props;

    const enabled = get(status, ['target', 'value'], false);

    const selectedPayrates = payrates.filter((payrate) => payrate.checked);

    if (selectedPayrates.length > 0) {
      const changedPayrates = selectedPayrates.map((currentPayrate) => ({
        ...currentPayrate,
        changes: { enabled, checked: false },
      }));

      payratesActions.updateMany(changedPayrates, {
        onSuccess: () => {
          this.setState({
            showError: false,
            actionError: null,
          });
          toggleAllRowsSelected(false);
        },
        onError: (message) => {
          this.setState({
            showError: true,
            actionError: {
              message,
            },
          });
        },
      });
    } else {
      payratesActions.updateOne(
        {
          id,
          changes: {
            enabled,
          },
        },
        {
          onSuccess: () => {
            this.setState({
              showError: false,
              actionError: null,
            });
          },
          onError: (message) => {
            // show only alert in this case.
            alertError(message);
          },
        },
      );
    }
  }

  toggleChecked(checked) {
    if (checked.length) {
      const { payrates, payratesActions } = this.props;

      const nextValue = payrates.map((item, index) => ({
        ...item,
        checked: checked.includes(index),
      }));

      payratesActions.setAll(nextValue);
    }
  }

  copyPayrate(id) {
    const {
      modalControls: { openModal },
    } = this.props;

    const payrate = this.getPayrate(id);

    delete payrate.id;

    openModal(payrate);
  }

  editPayrate(id) {
    const {
      modalControls: { openModal },
    } = this.props;

    const payrate = this.getPayrate(id);

    openModal(payrate);
  }

  deletePayrate(id) {
    const { payratesActions } = this.props;

    payratesActions.removeOne(id, {
      onError: (message) => {
        this.setState({
          showError: true,
          actionError: {
            title: 'Failed to delete Pay Rate',
            message,
          },
        });
      },
    });
  }

  toggleHideDisabled() {
    this.setState((state) => ({ hideDisabled: !state.hideDisabled }));
  }

  handleAdd() {
    const { modalControls, validate, enabled } = this.props;

    const error = this.getTableError();

    if (error) {
      this.setState({
        showError: true,
      });

      if (!enabled) {
        // calls submitForm to increase submitCount and show required erros
        validate();
      }
      return;
    }

    this.setState({
      showError: false,
    });

    modalControls.openModal();
  }

  hideError() {
    this.setState({ actionError: null });
    this.setState({ showError: false });
  }

  render() {
    const { payrates, hiringTargets, canEditPayRate } = this.props;
    const { hideDisabled, showError, actionError } = this.state;
    const showHideDisabled = anyDisabled(payrates);
    const filteredValue = filterDisabled(
      payrates,
      showHideDisabled && hideDisabled,
      canEditPayRate,
    );

    const error = this.getTableError();
    const hasTenantWithState = dataHasTenantWithState(filteredValue);
    const columns = getColumns(hasTenantWithState);

    return (
      <>
        {filteredValue.length ? (
          <TableContainer data-testid="payrates--table-container">
            <Table
              columns={columns}
              data={filteredValue}
              initialState={{ ...this.actionHandlers, hiringTargets }}
              onSelect={this.toggleChecked}
            />
          </TableContainer>
        ) : (
          <NoEntries error={showError && error} />
        )}

        <FloatContainer>
          {showError && error && (
            <ErrorPopover
              title={error.message}
              errorShown={showError}
              hideError={this.hideError}
              error={error}
            />
          )}
          {actionError && (
            <ErrorPopover
              title={actionError.title || 'Failed to update Pay Rate'}
              errorShown={!!actionError}
              hideError={this.hideError}
              error={actionError}
            />
          )}

          <FloatLeft>
            <Button
              data-testid="project-setup--invoice-n-payment--add-payrate"
              onClick={this.handleAdd}
            >
              Add Pay Rate
            </Button>
          </FloatLeft>
          <FloatRight>
            {showHideDisabled && (
              <HideDisabledContinaer>
                <FloatContainer>
                  <FloatLeft>
                    <HideDisabled>HIDE DISABLED</HideDisabled>
                  </FloatLeft>
                  <FloatRight>
                    <Switch
                      name="hide-disabled"
                      value={hideDisabled}
                      onChange={this.toggleHideDisabled}
                      data-testid="project-setup--invoice-n-payment--hide-disabled-payrates-toggle"
                    />
                  </FloatRight>
                </FloatContainer>
              </HideDisabledContinaer>
            )}
          </FloatRight>
        </FloatContainer>
      </>
    );
  }
}

PayratesSetup.propTypes = {
  payrates: PropTypes.arrayOf(PropTypes.object),
  hiringTargets: PropTypes.arrayOf(PropTypes.object).isRequired,
  payratesActions: PropTypes.objectOf(PropTypes.func).isRequired,
  modalControls: PropTypes.shape({
    openModal: PropTypes.func.isRequired,
  }).isRequired,
  enabled: PropTypes.bool,
  // submitCount: PropTypes.number.isRequired,
  validate: PropTypes.func.isRequired,
  dispatch: PropTypes.func.isRequired,
  customerId: PropTypes.number,
  canEditPayRate: PropTypes.bool,
};

PayratesSetup.defaultProps = {
  payrates: [],
  enabled: false,
  customerId: null,
  canEditPayRate: false,
};
