import React, { useCallback } from 'react';
import { Portal } from 'react-portal';
import { useDispatch, useSelector } from 'react-redux';
import { useWindowHeight } from '@react-hook/window-size';
import { ProcessBuilderWindow } from '../ProjectForms/RegistrationAndQualificationForm/styles';
import {
  closeProcessBuilder,
  selectProcessModules,
} from '../../store/ducks/processBuilder';
import Button from '../Button';
import { PreviewProvider } from '../Builder/helpers/preview-context';
import PreviewModal from '../ProcessBuilder/PreviewModal';
import TargetBuilder from './index';
import {
  requestOpenTargetBuilder,
  selectTargetBuilderOpen,
} from '../../store/ducks/targetBuilder';

export default function Page() {
  const dispatch = useDispatch();

  const windowHeight = useWindowHeight();
  const targetBuilderOpen = useSelector(selectTargetBuilderOpen);

  const handleProcessChange = () => {};

  const handleCancel = useCallback(() => {
    dispatch(closeProcessBuilder());
  }, [dispatch]);

  const processModules = useSelector(selectProcessModules);

  const createTargetBuilder = useCallback(() => {
    dispatch(
      requestOpenTargetBuilder({
        processID: 1,
      }),
    );
  }, [dispatch]);

  return (
    <PreviewProvider processModules={processModules}>
      <Button onClick={createTargetBuilder}>Target Builder</Button>
      {targetBuilderOpen && (
        <Portal>
          <ProcessBuilderWindow style={{ height: windowHeight }}>
            <TargetBuilder
              onProcessChange={handleProcessChange}
              onCancel={handleCancel}
            />
          </ProcessBuilderWindow>
        </Portal>
      )}
      <PreviewModal />
    </PreviewProvider>
  );
}
