import { useEffect } from 'react';
import { withFormik } from 'formik';
import { compose, withProps } from 'recompose';
import * as yup from 'yup';
import { connect, useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import { get, first } from 'lodash';

import ProjectAccess from './ProjectAccess';
import {
  fetchAccessHolder,
  selectData as selectExperimentGroups,
  saveExperimentGroups,
  selectAccessHolder,
} from '../../../../store/ducks/experimentGroups';
import {
  selectProjectId,
  selectProjectTools,
  selectProjectStatus,
} from '../../../../store/ducks/project';
import { navigate } from '../../../../store/ducks/projectSetup';
import { requiredMessage } from '../shared';
import store from '../../../../store';
import { withErrorPopover } from '../../../../components/ErrorPopover';
import { ENTITIES, selectors } from '../../../../store/ducks/options';
import { selectAllEager as selectPayrates } from '../../../../store/ducks/payrates';
import createArrayValidation from '../../../../helpers/array-validation';
import { findByUserGroupId } from '../../../../helpers/find';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  const projectPayrates = selectPayrates(state);
  const userGroups = selectExperimentGroups(state);

  const newUserGroups = {};

  Object.keys(userGroups).forEach((key) => {
    newUserGroups[key] = userGroups[key].map((group) => {
      return {
        ...group,
        hasPayrate: !!findByUserGroupId(projectPayrates, group.groupId),
      };
    });
  });

  return {
    initialValues: {
      userGroups: newUserGroups,
    },
    projectPayrates,
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  if (!values.hasExternalSystem) {
    dispatch(navigate(6));
    return;
  }

  const payload = {};

  const experimentId = get(first(values.accessHolder), 'id', null);

  const experimentGroups = get(values.userGroups, experimentId, []).map(
    (ug) => ({
      ...ug,
    }),
  );

  payload.experimentGroups = experimentGroups;

  payload.experimentId = experimentId;

  const nextStep = STEP_NUMBER.PROJECT_ACCESS + 1;

  dispatch(
    saveExperimentGroups({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

function transformUserGroupError(error, value, index) {
  return new yup.ValidationError(
    requiredMessage`User Group`,
    value,
    `userGroup${index}group`,
  );
}

function useHooksAsProps() {
  const dispatch = useDispatch();

  const projectId = useSelector(selectProjectId);
  const { hasExternalSystem } = useSelector(selectProjectTools);
  const accessHolder = useSelector(selectAccessHolder);
  const projectStatus = useSelector(selectProjectStatus);
  const allUserGroups = useSelector(
    selectors.createSelectSliceFor(ENTITIES.userGroups),
  );

  useEffect(() => {
    dispatch(fetchAccessHolder({ projectId, hasExternalSystem }));
  }, [dispatch, projectId, hasExternalSystem]);

  return {
    hasExternalSystem,
    accessHolder,
    allUserGroups: get(allUserGroups, 'data', []),
    projectStatus,
  };
}

function mapPropsToValues(props) {
  const {
    initialValues,
    hasExternalSystem,
    accessHolder,
    allUserGroups,
    projectPayrates,
    projectStatus,
  } = props;

  let { userGroups } = initialValues;

  const experimentId = get(first(accessHolder), 'id', null);

  const defaultUserGroups = {};
  defaultUserGroups[experimentId] = [];

  (allUserGroups || []).forEach((userGroup) => {
    const currentUserGroup = findByUserGroupId(projectPayrates, userGroup.id)
      ? userGroup
      : null;

    if (currentUserGroup) {
      defaultUserGroups[experimentId].push({
        experimentId,
        groupId: currentUserGroup && currentUserGroup.id,
        disabled: false,
        hasPayrate: true,
      });
    }
  });

  if (projectStatus === 'DRAFT') {
    const userGroupsLength =
      experimentId && userGroups[experimentId]
        ? userGroups[experimentId].length
        : 0;
    userGroups = userGroupsLength > 0 ? userGroups : defaultUserGroups;
  }

  return {
    ...initialValues,
    userGroups,
    accessHolder,
    hasExternalSystem,
  };
}

function createProps(props) {
  const { accessHolder } = props;
  const experimentId = get(first(accessHolder), 'id', null);

  return {
    experimentId,
  };
}

const validateUserGroupsArray = createArrayValidation(
  { groupId: yup.number().required(), disabled: yup.bool() },
  transformUserGroupError,
);

const validateUserGroupsObject = (value) => {
  // eslint-disable-next-line no-restricted-globals
  const isObjectKeysValid = !Object.keys(value || {}).some((el) => isNaN(el));

  if (!isObjectKeysValid) {
    return false;
  }

  return validateUserGroupsArray;
};

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withHooks(useHooksAsProps),
  withProps(createProps),
  withFormik({
    handleSubmit,
    enableReinitialize: true,
    mapPropsToValues,
    validationSchema: yup.object().shape({
      userGroups: yup.mixed().when('theFollowingValidation', {
        is: () => selectProjectTools(store.getState()).hasExternalSystem,
        then: yup
          .mixed()
          .test('userGroups', '', validateUserGroupsObject)
          .required(requiredMessage`Project`),
      }),
      iUndestand: yup.bool().oneOf([true], 'Field must be checked'),
    }),
  }),
)(ProjectAccess);
