import { ReactComponent as Checkbox } from '../../../../../../assets/icons/checkbox-group.svg';
import { ReactComponent as DatePicker } from '../../../../../../assets/icons/datepicker.svg';
import { ReactComponent as Dropdown } from '../../../../../../assets/icons/dropdown.svg';
import { ReactComponent as Text } from '../../../../../../assets/icons/text.svg';
import { ReactComponent as Radio } from '../../../../../../assets/icons/radio.svg';

// eslint-disable-next-line import/prefer-default-export
export const getLabel = (questionType) => {
  switch (questionType) {
    case 'CHECKBOX_GROUP':
      return ['Checkbox Group', Checkbox];
    case 'MULTIPLE_CHOICE':
      return ['Multiple Choice', Radio];
    case 'OPEN_TEXT':
      return ['Open Text', Text, '-3px 7px 0 0'];
    case 'OPEN_TEXT_PARAGRAPH':
      return ['Open Text Paragraph', Text, '-3px 7px 0 0'];
    case 'YES_NO':
      return ['Yes/No', Radio];
    case 'COUNTRY_LIST':
    case 'LANGUAGE_LIST':
    case 'DROPDOWN_SELECTION':
      return ['Dropdown Selection', Dropdown];
    case 'DATE_PICKER':
      return ['Date Picker', DatePicker];
    default:
      return ['Not Defined', Text];
  }
};
