import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import { ENTITIES, fetch } from '../../../../store/ducks/options';

export default function ConsentFormStep(props) {
  const {
    card: { attributes }, // Attributes[0] is ConsentFormData
    collapsed,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.appenEntities));
    dispatch(fetch(ENTITIES.piiDataTypes));
    dispatch(fetch(ENTITIES.countries));
  }, [dispatch]);

  if (collapsed && attributes.length) {
    // collapsed view
    return <></>;
  }

  return <></>;
}

ConsentFormStep.propTypes = {
  collapsed: PropTypes.bool.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        attributeOrder: PropTypes.number,
      }),
    ),
  }).isRequired,
};
