import { withFormik } from 'formik';
import cloneDeep from 'lodash/cloneDeep';
import { selectAll as selectAllHiringTargets } from '../../../../../../store/ducks/hiringTargets';
import { selectMarketAggregation } from '../../../../../../store/ducks/metricsAndGoals';

import store from '../../../../../../store';
import { createAgregationList } from '../../constants';
import ProductivityGoalsForm from './ProductivityGoalsForm';

const matchMarketValues = (projectMarkets, previousMetricConf) => {
  return projectMarkets.map((prjMk) => {
    const configuredMarket = previousMetricConf.find(
      (i) => i.market === prjMk.market,
    );
    // set previous configured value to your respective market in market list
    if (configuredMarket && configuredMarket.goalValue) {
      prjMk.goalValue = configuredMarket.goalValue;
    }
    return { ...prjMk, ...configuredMarket };
  });
};

const formatDefaultValues = (goal, marketList) => {
  // Project with brand new metrics and goals configuration
  if (goal.goalValueType === null) {
    return {
      ...goal,
      goalValueType: 'EQUAL',
      productivityMarketGoals: marketList,
    };
  }

  // Formating object for EQUAL_VALUE
  if (goal.goalValueType === 'EQUAL') {
    return {
      ...goal,
      productivityMarketGoals: marketList,
    };
  }

  // Formating object for PER_MARKET
  if (goal.goalValueType === 'PER_MARKET') {
    const { productivityMarketGoals } = goal;
    const marketValues = matchMarketValues(marketList, productivityMarketGoals);
    return {
      ...goal,
      productivityMarketGoals: marketValues,
    };
  }

  // Formating object for MATCH_DATA
  return {
    ...goal,
    goalValueType: 'MATCH_DATA',
    goalValue: null,
    productivityMarketGoals: null,
  };
};

const mapPropsToValues = ({
  goals,
  metricTitle,
  metricKey,
  metricId,
  metricRequired,
}) => {
  const hiringTargets = selectAllHiringTargets(store.getState());
  const marketAggregation = selectMarketAggregation(store.getState());
  const previousProjectGoals = goals.find((el) => el.goalType === 'PROJECT');
  const previousWorkerGoals = goals.find((el) => el.goalType === 'WORKER');
  let marketList = {};

  if (hiringTargets && hiringTargets.length > 0) {
    marketList = createAgregationList(hiringTargets, marketAggregation);
  }

  const projectGoals = formatDefaultValues(
    { ...previousProjectGoals },
    cloneDeep(marketList),
  );
  const workerGoals = formatDefaultValues(
    { ...previousWorkerGoals },
    cloneDeep(marketList),
  );

  return {
    metricTitle,
    projectGoals,
    workerGoals,
    metricKey,
    metricId,
    required: metricRequired,
  };
};

export default withFormik({
  mapPropsToValues,
})(ProductivityGoalsForm);
