import { connect } from 'react-redux';
import get from 'lodash/get';
import InvoicePaymentPreview from './InvoicePaymentPreview';
import { selectProject } from '../../../store/ducks/project';
import * as payrates from '../../../store/ducks/payrates';
import {
  findById,
  findByValueIgnoringCase as findByValue,
} from '../../../helpers/find';
import { ENTITIES, selectors } from '../../../store/ducks/options';
import { selectInvoiceAndPaymentFromProject } from '../../../store/ducks/project/selectors';

function mapPayRate(state) {
  const allPayrates = payrates.selectAllEager(state);

  const userGroupsData = selectors.selectDataFor(
    ENTITIES.userGroups,
    null,
    state,
  );

  const taskTypesData = selectors.selectDataFor(
    ENTITIES.taskTypes,
    null,
    state,
  );

  return ({ id, enabled, checked }) => {
    const payrate = findById(allPayrates, id);
    const userGroup = get(
      findById(userGroupsData, payrate.userGroup),
      'name',
      '',
    );
    const taskType = get(
      findByValue(taskTypesData, payrate.taskType),
      'label',
      '',
    );
    return {
      ...payrate,
      id,
      enabled,
      checked,
      userGroup,
      taskType,
      userGroupId: payrate.userGroupId || payrate.userGroup,
      taskTypeValue: payrate.taskType,
    };
  };
}

function mapStateToProps(state) {
  const project = selectProject(state);
  const invoicePayment = selectInvoiceAndPaymentFromProject(project);

  const businessUnitsData = selectors.selectDataFor(
    ENTITIES.businessUnits,
    null,
    state,
  );

  const rateTypesData = selectors.selectDataFor(
    ENTITIES.rateTypes,
    null,
    state,
  );

  const projectPayrates = payrates.selectAll(state);

  const externalSystem = selectors.selectById(
    ENTITIES.externalSystems,
    null,
    project.externalSystemId,
    state,
  );

  const {
    hasWorkdayTaskId,
    workdayTaskId,
    rateType,
    businessUnit,
    hasNotesToFinance,
    notesToFinance,
    hasProductivityData,
    partnerData,
  } = invoicePayment;

  return {
    projectId: project ? project.id : undefined,
    rateType: (findByValue(rateTypesData, rateType) || {}).label,
    businessUnit: (findByValue(businessUnitsData, businessUnit) || {}).label,
    allPayrates: projectPayrates.map(mapPayRate(state)),
    hasWorkdayTaskId,
    workdayTaskId,
    hasNotesToFinance,
    notesToFinance,
    hasProductivityData,
    externalSystemName: externalSystem ? externalSystem.name : null,
    hasPartnerData: partnerData,
  };
}

export default connect(mapStateToProps)(InvoicePaymentPreview);
