import styled from 'styled-components';
import { lighten } from 'polished';

function shouldMove(props) {
  const { isHovered, ignoreOffset, isDragging } = props;

  return !ignoreOffset && isHovered && !isDragging;
}

export const CardWrapper = styled.div`
  padding-top: ${(props) => (props.isFirst ? '24px' : '0px')};
  transition: padding 125ms ease;
  padding-bottom: 8px;
  opacity: ${(props) => (props.isDragging ? 0.4 : 1)};
  ${(props) => (props.isSecondary ? 'padding: 8px 46px 0 30px;' : '')}
`;

export const DropBoxContainer = styled.div`
  transition: height 175ms ease;
  height: ${(props) => (shouldMove(props) ? '102px' : '0px')};
  overflow: hidden;
  margin-bottom: ${(props) => (shouldMove(props) ? '8px' : '0px')};
`;

export const CardContainer = styled.div`
  background-color: ${(props) => props.theme.colors.white};
  border-radius: 4px;
  border: solid 1px ${(props) => props.theme.colors.lightPeriwinkle};
  box-shadow: 0px 0px 4px 0 ${(props) => props.theme.colors.lightPeriwinkle};
  animation: dropped 500ms ease;
  max-width: 768px;
  min-width: 738px;

  @keyframes dropped {
    0% {
      background-color: ${(props) => lighten(0.45, props.theme.colors.azure)};
    }

    100% {
      background-color: ${(props) => props.theme.colors.white};
    }
  }
`;

export const CardIndex = styled.div`
  font-family: 'Lato', sans-serif;
  font-size: 15px;
  font-weight: bold;
  margin-right: 16px;
  width: 16px;
  opacity: ${(props) => (props.opacity ? 0.5 : 1)};
`;

export const CardHeader = styled.div`
  display: flex;
  flex-direction: row;
  padding: 20px;
  border-bottom: 1px solid ${(props) => props.theme.colors.lightPeriwinkle};

  .process_builder__card__index {
    & > div:first-child {
      display: block;
    }

    & div:last-child {
      display: none;

      & img {
        width: 18px;
      }
    }
  }

  &:hover {
    .process_builder__card__index {
      & > div:first-child {
        display: none;
      }

      & div:last-child {
        display: block;
        cursor: ${(props) => (props.isDirty ? 'no-drop' : 'grab')};
        color: ${(props) => props.theme.colors.blueGrey};

        & > svg {
          max-width: 16px;
        }
      }
    }
  }
`;

export const CardInfo = styled.div`
  flex: 1;
  padding-right: 16px;
  opacity: ${(props) => (props.opacity ? 0.5 : 1)};
`;

export const CardActions = styled.div`
  &:hover {
    path.settings-icon {
      fill: ${(props) => props.theme.colors.azure};
    }
  }
`;

export const CardTitle = styled.div`
  font-family: 'Lato', sans-serif;
  font-size: 15px;
  font-weight: bold;
  flex: ${(props) => (props.inlineText ? '0' : '1')};
  ${(props) => props.inlineText && 'float: left;'}
`;

export const CardDescription = styled.div`
  font-size: 15px;
  margin-top: ${(props) => (props.inlineText ? '0px' : '4px')};
  ${(props) =>
    props.inlineText
      ? `
      &::before {
        content: '|';
        margin: 8px 8px;
      }
    `
      : `color: ${props.theme.colors.blueGrey}`};
`;

export const CadDisabled = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4px 8px;
  float: right;
  position: relative;
  width: 64px;
  height: 24px;
  background: rgba(132, 136, 153, 0.2);
  border-radius: 4px;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  color: #848899;
`;

export const CardContent = styled.div`
  cursor: ${(props) => (props.isExpanded ? 'initial' : 'pointer')};
`;

export const SettingsButtonContainer = styled.div`
  position: relative;
  left: 10px;
  bottom: 6px;
  height: 20px;
`;

export const MenuContainer = styled.div`
  height: 20px;
`;
