import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import * as registrationProcesses from '../../../../../../store/ducks/registrationProcesses';
import * as qualificationProcesses from '../../../../../../store/ducks/qualificationProcesses';
import {
  PreviewSectionTitle,
  PreviewSection,
  PreviewSectionItem,
  PreviewSectionText,
  EditButtonContainer,
  EditStepButton,
} from '../../styles';

export default function RegistrationAndQualification(props) {
  const {
    registrationProcessTemplate,
    qualificationProcessTemplate,
    navigateTo,
    renderedIn,
  } = props;

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.REGISTRATION_AND_QUALIFICATION);
  }, [navigateTo]);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(registrationProcesses.fetchOptions());
    dispatch(qualificationProcesses.fetchOptions());
  }, [dispatch]);

  return (
    <>
      <PreviewSectionTitle>
        02: Registration and Qualification
        <EditButtonContainer>
          {renderedIn === 'project-setup' && (
            <EditStepButton onClick={editStep}>Edit step</EditStepButton>
          )}
        </EditButtonContainer>
      </PreviewSectionTitle>
      <PreviewSection>
        <PreviewSectionItem>Registration Process</PreviewSectionItem>
        <PreviewSectionText>{registrationProcessTemplate}</PreviewSectionText>
      </PreviewSection>
      <PreviewSection>
        <PreviewSectionItem>Qualification Process</PreviewSectionItem>
        <PreviewSectionText>{qualificationProcessTemplate}</PreviewSectionText>
      </PreviewSection>
    </>
  );
}

RegistrationAndQualification.propTypes = {
  registrationProcessTemplate: PropTypes.string,
  qualificationProcessTemplate: PropTypes.string,
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

RegistrationAndQualification.defaultProps = {
  registrationProcessTemplate: '',
  qualificationProcessTemplate: '',
  renderedIn: 'project-setup',
};
