import { call, put, takeLatest } from 'redux-saga/effects';
import { fetchAll, fetchFailed, setAll } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchPayRatesFn(action) {
  const {
    payload: { projectId },
  } = action;

  try {
    const { data: payRates } = yield call(
      axios.get,
      ENDPOINTS.V2.PAY_RATES({
        query: {
          projectId,
        },
      }),
    );

    yield put(
      setAll(
        payRates.map((payRate) => ({
          id: payRate.id,
          rate: payRate.rateValue,
          writtenFluency: payRate.writtenFluency,
          spokenFluency: payRate.spokenFluency,
          userGroup: payRate.groupId,
          userGroupId: payRate.groupId,
          certifiedFluency: payRate.certifiedFluency || false,
          taskType: payRate.taskType,
          workdayTaskId: payRate.workdayTaskId,
          enabled: !payRate.disabled,
          localeLanguage: payRate.language,
          localeDialect: payRate.languageCountry,
          localeLanguageTo: payRate.languageTo,
          localeDialectTo: payRate.languageCountryTo,
          restrictToCountry: payRate.restrictToCountry,
          defaultRate: payRate.defaultRate,
          state: payRate.state,
          countryPayRateId: payRate.countryPayRateId || null,
        })),
      ),
    );
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

export default takeLatest(fetchAll.type, fetchPayRatesFn);
