import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveAutoScreeningSuccess,
  saveAutoScreeningError,
  saveAutoScreening,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* saveAutoScreeningSagaFn(action) {
  const {
    payload: { changes: payload, thenPut },
  } = action;

  const { id: autoScreenId, projectId } = payload;

  try {
    const endpoint = ENDPOINTS.V2.AUTO_SCREENING({
      pathVariables: {
        ...(autoScreenId && { autoScreenId }),
        projectId,
      },
    });

    const method = autoScreenId ? axios.patch : axios.post;
    const response = yield call(method, endpoint, payload);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    yield put(saveAutoScreeningSuccess(response.data));
  } catch (error) {
    yield put(saveAutoScreeningError(error));
  }
}

export default takeLatest(saveAutoScreening.type, saveAutoScreeningSagaFn);
