import withHooks from 'hook-hoc';
import { useSelector } from 'react-redux';
import { findById } from '../../../../helpers/find';
import * as projectPages from '../../../../store/ducks/projectPages';
import { selectUserProjectPage } from '../../../../store/ducks/projectView';

import UserProjectPage from './UserProjectPage';

function useHooksAsProps() {
  const userProjectPageData = useSelector(selectUserProjectPage);

  const projectPagesData = useSelector(projectPages.selectAll);

  const {
    defaultPageTask,
    hasSkillReview,
    isACANProject,
  } = userProjectPageData;

  return {
    defaultPageTask: (findById(projectPagesData, defaultPageTask) || {})
      .tabName,
    hasSkillReview,
    isACANProject,
  };
}

export default withHooks(useHooksAsProps)(UserProjectPage);
