import styled from 'styled-components';
import { transparentize } from 'polished';

// eslint-disable-next-line import/prefer-default-export
export const AccessHolderViewModeContainer = styled.div`
  padding: 21px 24px 19px;
  border: 1px solid ${(props) => props.theme.colors.lightPeriwinkle};
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
`;

export const AccessHolderName = styled.div`
  flex: 1;
`;

function getColorForStatus(target, status, theme) {
  if (target === 'bg') {
    switch (status) {
      case 'OPENED':
        return transparentize(0.9, theme.colors.azure);

      case 'DRAFT':
      default:
        return transparentize(0.8, theme.colors.blueGrey);
    }
  }

  switch (status) {
    case 'OPENED':
      return theme.colors.azure;

    case 'DRAFT':
    default:
      return theme.colors.blueGrey;
  }
}

export const AccessHolderStatus = styled.div`
  border-radius: 4px;
  display: inline-block;
  padding: 6px 12px;
  font-weight: bold;
  font-size: 12px;
  color: ${(props) =>
    getColorForStatus('text', props.currentStatus, props.theme)};
  background-color: ${(props) =>
    getColorForStatus('bg', props.currentStatus, props.theme)};
`;
