import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import get from 'lodash/get';

import NoEntries from '../../NoEntries';
import Table from '../../Table';
import { LabelWrapper } from '../../ProcessBuilder/InformationTooltip';
import { Label } from '../../../styles';
import {
  PreviewSectionTitle,
  PreviewSectionItem,
  PreviewSectionText,
  PreviewSection,
  EditButtonContainer,
  EditStepButton,
} from '../styles';

export default function ProjectAccessPreview(props) {
  const {
    userGroups,
    accessHolder,
    allUserGroups,
    editStep,
    stepNumber,
    renderedIn,
  } = props;

  const groupList = [];

  if (allUserGroups.data && userGroups) {
    userGroups.forEach((group) => {
      allUserGroups.data.forEach((el) => {
        if (el.id === group.groupId) {
          groupList.push({
            ...el,
            experimentId: group.experimentId,
          });
        }
      });
    });
  }

  const COLUMNS = [
    {
      Header: (
        <LabelWrapper>
          <Label>User Group</Label>
        </LabelWrapper>
      ),
      accessor: 'name',
      key: 'name',
      disableSortBy: true,
    },
  ];

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: Project Access
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}
      <PreviewSection>
        {accessHolder && (
          <Row>
            <Col col={4}>
              <PreviewSectionItem>Access Holder</PreviewSectionItem>
              <PreviewSectionText>
                {get(accessHolder, 'name', 'NOT FOUND')}
              </PreviewSectionText>
            </Col>
            <Col col={4}>
              <PreviewSectionItem>Access Holder Status</PreviewSectionItem>
              <PreviewSectionText>
                {get(accessHolder, 'status', 'NOT FOUND')}
              </PreviewSectionText>
            </Col>
          </Row>
        )}
      </PreviewSection>
      <PreviewSection>
        {groupList && groupList.length ? (
          <Table columns={COLUMNS} data={groupList} />
        ) : (
          <NoEntries error={null} label="Not Set" />
        )}
      </PreviewSection>
    </>
  );
}

ProjectAccessPreview.propTypes = {
  editStep: PropTypes.func,
  stepNumber: PropTypes.number,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
  userGroups: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      group: PropTypes.number,
      enabled: PropTypes.bool,
    }),
  ),
  allUserGroups: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
      }).isRequired,
    ).isRequired,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.instanceOf(Error),
  }).isRequired,
  accessHolder: PropTypes.shape({
    name: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired,
  }).isRequired,
};

ProjectAccessPreview.defaultProps = {
  renderedIn: 'project-setup',
  stepNumber: 6,
  editStep: undefined,
  userGroups: [],
};
