import { call, put, takeLatest } from 'redux-saga/effects';
import { SVC_ENDPOINTS } from '../../../constants/endpoints';
import { svcAPI } from '../../../services/axios';
import { getMetricChangelogs, setMetricChangelogs } from '.';
import { changelogsA } from './mocked';

function* fetchMetricChangelogsFn(action) {
  const {
    payload: { metricId, type },
  } = action;
  const obj = {};

  try {
    const { data } = yield call(
      svcAPI.get,
      SVC_ENDPOINTS.V1.METRIC_CHANGELOGS({
        pathVariables: { id: metricId, type },
      }),
    );
    obj[`${metricId}_${type}`] = { ...data, error: false };

    yield put(setMetricChangelogs(obj));
  } catch (error) {
    const { response } = error;
    // eslint-disable-next-line no-console
    console.error(response);
    obj[`${metricId}_${type}`] = { ...changelogsA, error: false };

    yield put(setMetricChangelogs(obj));
  }
}

export default takeLatest(getMetricChangelogs.type, fetchMetricChangelogsFn);
