import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import { setDirty } from '../../../../store/ducks/projectSetup';
import useFirstChange from '../../helpers/useFirstChange';
import StepFooter from '../../../../components/StepFooter';
import ProjectOverviewFrom from '../../../../components/ProjectForms/ProjectOverviewForm';

export default function ProjectOverview(props) {
  const { setFieldValue, submitForm, values } = props;

  function submitAndNavigate() {
    setFieldValue('navigate', true);
    submitForm();
  }

  const dispatch = useDispatch();

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    values,
  );

  return (
    <div>
      <WizardTitle>Project Overview</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <ProjectOverviewFrom />

      <WizardFooter>
        <StepFooter
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: Registration and Qualification"
        />
      </WizardFooter>
    </div>
  );
}

ProjectOverview.propTypes = {
  submitForm: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
};
