import styled from 'styled-components';

export const Container = styled.div``;

export const Option = styled.span`
  cursor: pointer;
  display: ${(props) => (props.isVertical ? 'block' : 'inline')};
  margin-bottom: ${(props) => (props.isVertical ? '8px' : '0px')};
  margin-left: ${(props) =>
    !props.isVertical && !props.isFirst ? '32px' : '0px'};
`;

export const Circle = styled.div`
  display: inline-block;
  margin-right: 8px;
`;

function getContourBackgroundColor(props) {
  if (props.hasError) {
    return props.theme.colors.error;
  }

  return props.isActive
    ? props.theme.colors.azure
    : props.theme.colors.lightBlueGrey;
}

export const CircleContour = styled.div`
  display: inline-block;
  height: 20px;
  width: 20px;
  background-color: ${getContourBackgroundColor};
  border-radius: 50%;
  transition: all ease 170ms;
  position: relative;
  bottom: 2px;
`;

export const CircleCenter = styled.div`
  display: inline-block;
  height: 12px;
  width: 12px;
  border: 3px solid white;
  border-radius: 50%;
  transition: background-color ease 170ms;
  background-color: ${(props) =>
    props.isActive ? props.theme.colors.azure : props.theme.colors.white};

  position: relative;
  left: 4px;
  top: 1px;
`;

export const Label = styled.label`
  position: relative;
  bottom: 2px;
`;
