import { useCallback } from 'react';
import withHooks from 'hook-hoc';
import shortid from 'shortid';
import Card from './Card';
import { useModal } from '../../../components/Modal';

function useHooksAsProps(props) {
  const {
    title,
    description,
    category,
    maxAnswerId,
    maxQuestionId,
    topics,
  } = props;
  const [modalProps, modalControls] = useModal();
  const [modalPropsCategory, modalControlsCategory] = useModal();

  const handleNewTopic = useCallback(() => {
    modalControls.openModal({
      currentQuestionsList: topics,
      masterProfileQuestionData: {
        answerList: [{ key: shortid.generate() }],
        category,
        maxAnswerId,
        maxQuestionId,
      },
    });
  }, [modalControls, topics, category, maxAnswerId, maxQuestionId]);

  const handleEditCategory = useCallback(() => {
    modalControlsCategory.openModal({
      title,
      description,
    });
  }, [modalControlsCategory, title, description]);

  return {
    modalProps,
    modalControls,
    modalPropsCategory,
    modalControlsCategory,
    handleNewTopic,
    handleEditCategory,
  };
}

export default withHooks(useHooksAsProps)(Card);
