import * as Yup from 'yup';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

export const checkboxValidator = Yup.array()
  .of(
    Yup.object().shape({
      value: Yup.boolean(),
    }),
  )
  .compact((v) => !v.value)
  .required(requiredMessage`Checkbox Group`);

export const radioValidator = Yup.array()
  .of(
    Yup.object().shape({
      value: Yup.boolean(),
    }),
  )
  .compact((v) => !v.value)
  .required(requiredMessage`Checkbox Group`);

export const inputValidator = Yup.string().required(requiredMessage`Input`);

export const selectValidator = Yup.string().required(requiredMessage`Action`);

export const selectGroupValidator = Yup.array().of(
  Yup.object().shape({
    action: Yup.string().required(requiredMessage`Action`),
  }),
);

export const selectDatePickerValidator = Yup.array().of(
  Yup.object().shape({
    logic: Yup.string()
      .required(requiredMessage`Logic`)
      .nullable(),
    lb: Yup.string()
      .required(requiredMessage`Date`)
      .nullable(),
    ub: Yup.mixed()
      .when('logic', {
        is: (value) => value === 'between',
        then: Yup.mixed().required(
          requiredMessage`When "between" is selected this`,
        ),
        otherwise: Yup.string().notRequired(),
      })
      .nullable(),
  }),
);
