import styled from 'styled-components';
import { transparentize } from 'polished';

export const OptionBase = styled.div`
  position: relative;
  min-height: 36px;
  line-height: 36px;
  padding-left: 16px;
  background-color: ${(props) =>
    props.focus
      ? transparentize(0.9, props.theme.colors.charcoal)
      : props.theme.colors.white};
  opacity: ${(props) => (props.isDisabled ? 0.3 : 1)};
  overflow: hidden;

  &:hover {
    background-color: ${(props) =>
      transparentize(0.9, props.theme.colors.charcoal)};
  }

  cursor: ${(props) => (props.blocking ? 'not-allowed' : 'initial')};
  opacity: ${(props) => (props.blocking ? 0.4 : 1)};

  & > div {
    pointer-events: ${(props) => (props.blocking ? 'none' : 'all')};
  }
`;

export const ChildrenContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

export const CheckboxContainer = styled.div`
  margin-right: 12px;
  width: 20px;
  position: relative;
  top: 3px;
  pointer-events: none;
`;

export const ChildrenWrapper = styled.div`
  flex: 1;
`;

export const Tick = styled.div`
  position: absolute;
  right: 16px;
  top: 0;
  bottom: 0;
`;

export const OptionDescription = styled.div`
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 20px;

  color: ${(props) => props.theme.colors.blueGrey};
  padding-bottom: 16px;
`;
