import React from 'react';
import PropTypes from 'prop-types';
import { Tag } from './styles';

export default function SignalCell({ row }) {
  const {
    original: { numSignals, verified },
  } = row;

  return <Tag verified={verified}>{numSignals}</Tag>;
}

SignalCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      numSignals: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      verified: PropTypes.bool,
    }),
  }).isRequired,
};
