import { withFormik } from 'formik';
import * as Yup from 'yup';
import { compose } from 'recompose';
import shortid from 'shortid';
import AddTenantModal from './AddTenantModal';
import {
  selectAllEager as selectAllLocaleTenants,
  updateOne as updateLocaleTenant,
  addOne as addLocaleTenant,
} from '../../../../../store/ducks/localeTenants';
import store from '../../../../../store';
import { requiredMessage } from '../../../../../pages/ProjectSetup/Steps/shared';
import resetForm from '../../../../../pages/ProjectSetup/helpers/resetForm';
import { withErrorPopover } from '../../../../ErrorPopover';

async function handleSubmit(values, formikBag) {
  const {
    props: {
      modalControls: { closeModal },
      showError,
      hideError,
    },
  } = formikBag;

  const { country, tenant, tenantType, id } = values;

  const data = {
    id: id || shortid.generate(),
    country,
    tenant,
    tenantType,
  };

  const localeTenants = selectAllLocaleTenants(store.getState());

  const isDuplicate = !!(localeTenants || []).find(
    (lt) => lt.country === data.country && lt.tenant === data.tenant,
  );

  if (isDuplicate) {
    showError(
      new Error(
        'There is a Locale Tenant with the same options set for this project.',
      ),
    );

    return Promise.resolve();
  }

  return new Promise((resolve) => {
    function onSuccess() {
      hideError();
      closeModal();

      // The timeout makes sure modal close animation is finished
      // before cleaning up modal state
      setTimeout(() => {
        resetForm(
          formikBag,
          {
            country: '',
            tenant: '',
          },
          resolve,
        );
      }, 225);
    }

    function onError(message) {
      showError(new Error(message));

      // Error is shown one tick before button is clickable
      setImmediate(resolve());
    }

    if (id) {
      store.dispatch(
        updateLocaleTenant({ id, changes: data }, { onSuccess, onError }),
      );
    } else {
      store.dispatch(addLocaleTenant(data, { onSuccess, onError }));
    }
  });
}

function mapPropsToValues(props) {
  const { data } = props;

  if (data) {
    return data;
  }

  return {};
}

export default compose(
  withErrorPopover,
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validateOnMount: true,
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
      country: Yup.string().required(requiredMessage`Country`),
      tenant: Yup.number().required(requiredMessage`Tenant`),
    }),
  }),
)(AddTenantModal);
