import { createSlice, createSelector } from '@reduxjs/toolkit';
import { memoize } from 'lodash';
import { hasHiringTargets } from '../hiringTargets';
import { hasLocaleTenants } from '../localeTenants';
import { hasPayRates } from '../payrates';
import { hasAnyDemographicData } from '../projectDemographicRequirements';
import { hasExperimentGroups } from '../experimentGroups';
import {
  selectCompletedPercentageFromProject,
  selectInvoiceAndPaymentFromProject,
  selectProjectOverviewFromProject,
  selectProjectToolsFromProject,
  selectRecruitmentFromProject,
  selectRegistrationAndQualificationFromProject,
  selectSupportTeamFromProject,
  selectUserProjectPageFromProject,
  isProjectOverviewCompleteFromProject,
  isRegistrationAndQualificationCompleteFromProject,
  isInvoiceAndPaymentCompleteFromProject,
  isProjectToolsCompleteFromProject,
  isUserProjectPageCompleteFromProject,
  isSupportTeamCompleteFromProject,
  isUserAccessCompleteFromProject,
} from './selectors';

const initialState = {
  project: null,
  loading: true,
  saving: false,
  error: null,
};

const slice = createSlice({
  name: 'project',
  initialState,
  reducers: {
    // Saga Trigger
    createProject(state) {
      state.saving = true;
    },

    cloneProject(state) {
      state.saving = true;
    },

    // Saga Trigger
    patchProject(state) {
      state.saving = true;
      state.error = null;
    },

    projectMutationSuccess(state, action) {
      const { payload: { project = null } = {} } = action || {
        payload: { project: null },
      };

      state.saving = false;
      state.project = project;
    },

    projectMutationError(state, action) {
      const { payload } = action;

      state.saving = false;
      state.error = payload.error;
    },

    // Saga Trigger
    fetchProject(state) {
      state.loading = true;
    },

    fetchProjectSuccess(state, action) {
      const { payload: { project = null } = {} } = action || {
        payload: { project: null },
      };

      state.loading = false;
      state.project = project;
    },

    fetchProjectError(state, action) {
      const { payload: { error = null } = {} } = action;

      state.loading = false;
      state.error = error;
    },

    // Saga Trigger
    putProjectStatus(state) {
      state.saving = true;
    },

    putProjectStatusSuccess(state, action) {
      const { payload: { project = null } = {} } = action || {
        payload: { project: null },
      };

      state.saving = false;
      state.project = project;
    },

    putProjectStatusError(state, action) {
      const { payload: { error = null } = {} } = action;

      state.saving = false;
      state.error = error;
    },

    setProject(state, action) {
      const { payload: { project = null } = {} } = action || {
        payload: { project: null },
      };

      state.project = project;
    },

    setLoading(state, action) {
      const { payload: { loading = true } = {} } = action || {
        payload: { loading: true },
      };

      state.loading = loading;
    },

    setSaving(state, action) {
      const { payload: { saving = true } = {} } = action || {
        payload: { saving: true },
      };

      state.saving = saving;
    },

    setError(state, action) {
      state.error = (action.payload && action.payload.error) || null;
    },
  },
});

const {
  name,
  reducer,
  actions: {
    createProject,
    cloneProject,
    patchProject,
    projectMutationError,
    projectMutationSuccess,
    fetchProject,
    fetchProjectError,
    fetchProjectSuccess,
    putProjectStatus,
    putProjectStatusError,
    putProjectStatusSuccess,
    setProject,
    setLoading,
    setSaving,
    setError,
  },
} = slice;

reducer.key = name;

export default reducer;

export {
  createProject,
  cloneProject,
  patchProject,
  projectMutationError,
  projectMutationSuccess,
  fetchProject,
  fetchProjectError,
  fetchProjectSuccess,
  putProjectStatus,
  putProjectStatusError,
  putProjectStatusSuccess,
  setProject,
  setLoading,
  setSaving,
  setError,
};

export function selectProjectState(state) {
  return state[reducer.key];
}

export const selectProject = createSelector(
  selectProjectState,
  (projectState) => projectState.project || null,
);

export const selectProjectId = createSelector(
  selectProjectState,
  (projectState) => (projectState.project ? projectState.project.id : null),
);

export const selectLoading = createSelector(
  selectProjectState,
  (projectState) => projectState.loading,
);

export const selectSaving = createSelector(
  selectProjectState,
  (projectState) => projectState.saving,
);

export const selectError = createSelector(
  selectProjectState,
  (projectState) => projectState.error,
);

export const selectProjectOverview = createSelector(
  selectProjectState,
  (projectState) =>
    selectProjectOverviewFromProject(projectState.project || undefined),
);

export const selectRegistrationAndQualification = createSelector(
  selectProjectState,
  (projectState) =>
    selectRegistrationAndQualificationFromProject(
      projectState.project || undefined,
    ),
);

export const selectProjectTools = createSelector(
  selectProjectState,
  (projectState) =>
    selectProjectToolsFromProject(projectState.project || undefined),
);

export function selectRecruitment(state) {
  const project = selectProject(state);
  const hasDemographics = hasAnyDemographicData(state);

  return selectRecruitmentFromProject(project || undefined, {
    hasDemographics,
  });
}

export const selectInvoiceAndPayment = createSelector(
  selectProjectState,
  (projectState) =>
    selectInvoiceAndPaymentFromProject(projectState.project || undefined),
);

export const selectUserProjectPage = createSelector(
  selectProjectState,
  (projectState) =>
    selectUserProjectPageFromProject(projectState.project || undefined),
);

export const selectSupportTeam = createSelector(
  selectProjectState,
  (projectState) =>
    selectSupportTeamFromProject(projectState.project || undefined),
);

export function selectCompletedPercentage(state) {
  const dependencies = {
    hasLocaleTenants: hasLocaleTenants(state),
    hasHiringTargets: hasHiringTargets(state),
    hasPayRates: hasPayRates(state),
    hasExperimentGroups: hasExperimentGroups(state),
  };

  return selectCompletedPercentageFromProject(
    selectProject(state),
    dependencies,
  );
}

export const selectProjectStatus = createSelector(
  selectProjectState,
  (projectState) =>
    (projectState.project && projectState.project.status) || null,
);

export function isProjectOverviewComplete(state) {
  return isProjectOverviewCompleteFromProject(selectProject(state));
}

export function isRegistrationAndQualificationComplete(state) {
  return isRegistrationAndQualificationCompleteFromProject(
    selectProject(state),
  );
}

export function isRecruitmentComplete(state) {
  return hasLocaleTenants(state) && hasHiringTargets(state);
}

export function isInvoiceAndPaymentComplete(state) {
  return (
    isInvoiceAndPaymentCompleteFromProject(selectProject(state)) &&
    hasPayRates(state)
  );
}

export function isProjectToolsComplete(state) {
  return isProjectToolsCompleteFromProject(selectProject(state));
}

export function isUserProjectPageComplete(state) {
  return isUserProjectPageCompleteFromProject(selectProject(state));
}

export function isSupportTeamComplete(state) {
  return isSupportTeamCompleteFromProject(selectProject(state));
}

export function isUserAccessComplete(state) {
  return isUserAccessCompleteFromProject(state) && hasExperimentGroups(state);
}

export const STEPS = {
  PROJECT_OVERVIEW: 0,
  REGISTRATION_AND_QUALIFICATION: 1,
  RECRUITMENT: 2,
  INVOICE_PAYMENT: 3,
  PROJECT_TOOLS: 4,
  USER_ACCESS: 5,
  USER_PROJECT_PAGE: 6,
  SUPPORT_TEAM: 7,
  PREVIEW: 8,
};

export const selectStepsCompletedStatus = memoize(
  (state) => {
    return {
      [STEPS.PROJECT_OVERVIEW]: isProjectOverviewComplete(state),
      [STEPS.REGISTRATION_AND_QUALIFICATION]: isRegistrationAndQualificationComplete(
        state,
      ),
      [STEPS.RECRUITMENT]: isRecruitmentComplete(state),
      [STEPS.INVOICE_PAYMENT]: isInvoiceAndPaymentComplete(state),
      [STEPS.PROJECT_TOOLS]: isProjectToolsComplete(state),
      [STEPS.USER_ACCESS]: isUserAccessComplete(state),
      [STEPS.USER_PROJECT_PAGE]: isUserProjectPageComplete(state),
      [STEPS.SUPPORT_TEAM]: isSupportTeamComplete(state),
      [STEPS.PREVIEW]: isProjectOverviewComplete(state),
    };
  },
  (state) => {
    return (
      `${isProjectOverviewComplete(state)}` +
      `${isRegistrationAndQualificationComplete(state)}` +
      `${isRecruitmentComplete(state)}` +
      `${isInvoiceAndPaymentComplete(state)}` +
      `${isProjectToolsComplete(state)}` +
      `${isUserAccessComplete(state)}` +
      `${isUserProjectPageComplete(state)}` +
      `${isSupportTeamComplete(state)}` +
      `${isProjectOverviewComplete(state)}`
    );
  },
);
