import { call, put, takeLatest } from 'redux-saga/effects';
import { SVC_ENDPOINTS } from '../../../constants/endpoints';
import { svcAPI } from '../../../services/axios';
import {
  saveMetricsAndGoals,
  setMetricsAndGoalsSucess,
  setMetricsAndGoalsError,
  getMetricsAndGoals,
} from '.';

function* saveMetricsAndGoalsSagaFn(action) {
  const { payload } = action;

  const cleaned = { ...payload };

  if (cleaned.productivityMetrics) {
    cleaned.productivityMetrics = cleaned.productivityMetrics.filter(
      (m) => m.id || !m.disabled,
    );
  }

  if (cleaned.qualityMetrics) {
    cleaned.qualityMetrics = cleaned.qualityMetrics.filter(
      (m) => m.id || !m.disabled,
    );
  }

  try {
    const response = yield call(
      svcAPI.post,
      SVC_ENDPOINTS.V1.SAVE_METRICS(),
      cleaned,
    );

    yield put(
      setMetricsAndGoalsSucess({
        data: response.data,
        success: true,
        saving: false,
      }),
    );

    yield put(
      getMetricsAndGoals({
        projectId: payload.projectId,
        rateType: payload.rateType,
      }),
    );
  } catch (error) {
    yield put(
      setMetricsAndGoalsError({ error: error.message, success: false }),
    );
  }
}

export default takeLatest(saveMetricsAndGoals.type, saveMetricsAndGoalsSagaFn);
