import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import {
  PreviewSectionTitle,
  PreviewSectionItem,
  PreviewSectionText,
  PreviewSection,
  EditButtonContainer,
  EditStepButton,
} from '../styles';

export default function ProjectSupportTeam(props) {
  const { supportTeam, editStep, stepNumber, renderedIn } = props;

  const supportTeamWithKeys = useMemo(
    () =>
      supportTeam.map((member) => ({
        label: member,
        key: shortid.generate(),
      })),
    [supportTeam],
  );

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: Support Team
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}

      <PreviewSection>
        <PreviewSectionItem>Support User Role</PreviewSectionItem>
        <PreviewSectionText>
          {supportTeamWithKeys.map(({ label, key }) => (
            <div key={key}>{label}</div>
          ))}
          {(!supportTeam || !supportTeam.length) && <PreviewSectionText />}
        </PreviewSectionText>
      </PreviewSection>
    </>
  );
}

ProjectSupportTeam.propTypes = {
  supportTeam: PropTypes.arrayOf(PropTypes.string).isRequired,
  editStep: PropTypes.func,
  stepNumber: PropTypes.number,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectSupportTeam.defaultProps = {
  editStep: undefined,
  stepNumber: 8,
  renderedIn: 'project-setup',
};
