import React from 'react';
import PropTypes from 'prop-types';
import Tooltip from 'rc-tooltip';
import Styles from './styles';

export default function TitleTooltip(props) {
  const { trigger, placement, children, title } = props;

  if (!title) {
    return children;
  }

  return (
    <>
      <Styles />
      <Tooltip
        placement={placement}
        trigger={Array.isArray(trigger) ? trigger : [trigger]}
        overlay={title}
        prefixCls="table_header_tooltip"
      >
        <span>{children}</span>
      </Tooltip>
    </>
  );
}

const triggers = ['hover', 'focus', 'click'];

TitleTooltip.propTypes = {
  trigger: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.oneOf(triggers)),
    PropTypes.oneOf(triggers),
  ]).isRequired,
  placement: PropTypes.oneOf([
    'top',
    'left',
    'right',
    'bottom',
    'topLeft',
    'topRight',
    'bottomLeft',
    'bottomRight',
    'leftTop',
    'leftBottom',
    'rightTop',
    'rightBottom',
  ]).isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
  title: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]),
};

TitleTooltip.defaultProps = {
  title: '',
};
