import get from 'lodash/get';
import set from 'lodash/set';
import clone from 'lodash/cloneDeep';
import flattendepth from 'lodash/flattenDepth';
import { findById } from '../../../helpers/find';

// ===== Local Tree Operators =====

function createLocalTree(requirements, levels, hiringTargets) {
  // Get requirement for current recursion level.
  const requirement = requirements[requirements.length - levels];

  // Build tree recursivly
  return {
    id: requirement.id,
    label: requirement.name,
    levels,
    groups: requirement.groups.map((g) => {
      const group = {
        id: g.id,
        label: g.label,
        leaf: levels === 1,
      };

      if (group.leaf) {
        group.hiringTargets = {};

        hiringTargets.forEach((ht) => {
          group.hiringTargets[ht.id] = 0;
        });
      } else {
        group.value = createLocalTree(requirements, levels - 1, hiringTargets);
      }

      return group;
    }),
  };
}

function getAccessorStringForLocalTreeOn(path, tree) {
  // Clone objects so they have safe references
  const pathCopy = [...path];
  const treeCopy = clone(tree);

  // Get first path
  const firstPath = pathCopy.shift();

  // Find group for this path
  let pointerIndex = treeCopy.groups.findIndex(
    (group) => group.id === firstPath,
  );

  // Accessor is used to save the path inside the tree for a given value
  let accessor = `groups[${pointerIndex}].value`;

  // Pointer is the current node in the tree
  let pointer = treeCopy.groups[pointerIndex].value;

  // Keeps track of how deep we went into the tree
  let levels = 0;

  // We use 3 iterations tops here because the tree is atmost 3 leves deep
  for (levels = 0; levels < 3; levels += 1) {
    const currentPath = pathCopy.shift();

    if (!pointer) {
      break;
    }

    pointerIndex = pointer.groups.findIndex(
      (group) => group.id === currentPath,
    );

    // If we dont find a node, break out
    if (pointerIndex === -1) {
      break;
    }

    pointer = pointer.groups[pointerIndex];

    // Check if we are on a leaf node
    if (!pointer.value || !Array.isArray(pointer.value.groups)) {
      // If we are, set the accessor appropriately
      accessor = `${accessor}.groups[${pointerIndex}].hiringTargets`;
      levels += 1;
      break;
    }

    pointer = pointer.value;

    accessor = `${accessor}.groups[${pointerIndex}].value`;
  }

  if (levels === 0) {
    // if we broke out of the first iteration, the first level
    // for the object is the only one, ajust accessor accordingly
    accessor = `groups[${pointerIndex}].hiringTargets`;
  }

  return accessor;
}

function getLeafValueForLocalTree(path, tree) {
  return [path, get(tree, getAccessorStringForLocalTreeOn(path, tree))];
}

function setLeafValueOnLocalTreeFor(path, value, tree) {
  // Clone tree for safe references.
  const treeCopy = clone(tree);

  return set(treeCopy, getAccessorStringForLocalTreeOn(path, tree), value);
}

function getPossiblesPathsForLocalTree(requirements) {
  const groups = requirements.map((r) => r.groups);

  return flattendepth(
    groups[0].map((g) => {
      if (groups[1]) {
        return groups[1].map((xg) => {
          if (groups[2]) {
            return groups[2].map((yg) => [g, xg, yg]);
          }

          return [g, xg];
        });
      }
      return [g];
    }),
    groups.length - 1,
  ).map((path) => path.map((portion) => portion.id));
}

// ===== DTO Tree Operators =====

function createAggregationTreeForDTO(requirements, levels) {
  // Get requirement for current recursion level
  const requirement = requirements[requirements.length - levels];

  // Creates tree recursivly
  return requirement.groups.map((group) => {
    const param = {
      parameter: requirement.id,
      value: group.id,
    };

    if (levels === 1) {
      param.target = 0;
    } else {
      param.parameters = createAggregationTreeForDTO(requirements, levels - 1);
    }

    return param;
  });
}

function setValueOnDTOTreeFor(tree, path, value) {
  // Clones object for safe reference
  const nextTree = clone(tree);

  // Bootstrap the accessor
  let accessor = 'aggregation';

  // Navigate the tree saving the acessor
  path.forEach((groupId) => {
    // Get the current pointer
    const target = get(tree, accessor, []);

    // Find group indes
    const groupIndex = target.findIndex((group) => group.value === groupId);

    // Get group
    const group = target[groupIndex];

    // If we are on a leaf, task will not be undefined
    if (typeof group.target !== 'undefined') {
      accessor = `${accessor}[${groupIndex}].target`;
    } else {
      accessor = `${accessor}[${groupIndex}].parameters`;
    }
  });

  return set(nextTree, accessor, value);
}

function getValueOnDTOTreeFor(path, dataTransferObject, includeMetrics) {
  // breaks pointer references so we can work with object safely.

  // Object which will contain values for all hiring targets on the given path
  const values = {};

  // For each tree (hiring target) on the Demographics DTO, we traverse
  // it and get the value
  dataTransferObject.forEach((demographics) => {
    const { hiringTargetId, aggregation } = demographics;

    const pathCopy = [...path];

    // Save idea, get first path portion
    const firstPath = pathCopy.shift();

    // Find the first pointer
    let pointer = clone(aggregation).find(
      (parameter) => parameter.value === firstPath,
    );

    // We use 3 iterations tops here because the tree is atmost 3 leves deep
    for (let index = 0; index < 3; index += 1) {
      // If parameters is not an array, we are on a leaf node
      if (!Array.isArray(pointer.parameters)) {
        break;
      }

      // Get next path
      const currentPath = pathCopy.shift();

      // Get next pointer
      pointer = pointer.parameters.find((p) => p.value === currentPath);
    }

    // Sets this hiring target value on value map
    if (includeMetrics) {
      values[hiringTargetId] = {
        target: pointer.target,
        count: pointer.count,
      };
    } else {
      values[hiringTargetId] = pointer.target;
    }
  });

  // Returns [string[], { [key: number]: number }]
  return [path, values];
}

// ===== PUBLIC FUNCTIONS =====

export function getRequirements(item, allRequirements) {
  // Clones item to break pointer references
  let pointer = clone(item);

  // Declare object to hold requirements present on item
  const requirementIds = [];

  // Dive in a single tree branch. We don't need to visit every node, since there
  // is only one parameter (aka requirement) per level
  while (typeof pointer.target === 'undefined') {
    // push current level to the list
    requirementIds.push(pointer.parameter);

    // Disable eslint here so it is clearer what we are doing
    // eslint-disable-next-line prefer-destructuring
    pointer = pointer.parameters[0];
  }

  // We are on a leaf node, push the parameter o requirementIds
  requirementIds.push(pointer.parameter);

  // Exchange requirementIds for requirement objects
  return requirementIds.map((requirementId) =>
    findById(allRequirements, requirementId),
  );
}

export function convertFromDTO(
  aggregatedRequirementsDTO,
  allRequirements,
  hiringTargets,
  includeMetrics,
) {
  if (
    !Array.isArray(aggregatedRequirementsDTO) ||
    !aggregatedRequirementsDTO.length
  ) {
    return {};
  }

  // DTO has one tree per HiringTarget, localy we have one tree with all hiring targets.
  // All hiring targets trees are the same, only values differ.

  const [
    {
      aggregation: [firstItem],
    },
  ] = aggregatedRequirementsDTO;

  if (!firstItem) {
    return {};
  }

  // We get requirements in order they were nested
  const requirements = getRequirements(
    aggregatedRequirementsDTO[0].aggregation[0],
    allRequirements,
  );

  // Get all possible paths
  const possiblePaths = getPossiblesPathsForLocalTree(requirements);

  // forEach path, get all hiring target values
  const pathsWithValues = possiblePaths.map((path) =>
    getValueOnDTOTreeFor(path, aggregatedRequirementsDTO, includeMetrics),
  );

  // We create the single local tree
  let localTree = createLocalTree(
    requirements,
    requirements.length,
    hiringTargets,
  );

  // Apply all values to local tree
  pathsWithValues.forEach(([path, value]) => {
    localTree = setLeafValueOnLocalTreeFor(path, value, localTree);
  });

  return localTree;
}

export function convertToDTO(
  requirements,
  aggregatedRequirements,
  hiringTargets,
) {
  // Create a base tree the DTO expects
  const baseTree = createAggregationTreeForDTO(
    requirements,
    requirements.length,
  );

  // Replicate the base tree to each hiring target
  const forest = hiringTargets.map((hiringTarget) => ({
    hiringTargetId: hiringTarget.id,
    aggregation: [...baseTree].map((t) => clone(t)),
  }));

  // Calculate all possible paths for tree
  const possiblePaths = getPossiblesPathsForLocalTree(requirements);

  // Load all values for each path
  const pathsWithValues = possiblePaths.map((path) =>
    getLeafValueForLocalTree(path, aggregatedRequirements),
  );

  // For each tree
  return forest.map((hiringTargetTree) => {
    const { aggregation, hiringTargetId } = hiringTargetTree;

    // Copy the tree object, so its references are safe to work with.
    let treeCopy = { aggregation: [...aggregation].map((t) => clone(t)) };

    // For each possible path, get the leaf value for a given hiring target
    // and apply it to treeCopy
    possiblePaths.forEach((path) => {
      const pathKey = path.join();

      const [, values] = pathsWithValues.find((pathWithValue) => {
        const pathWithValueKey = pathWithValue[0].join();

        return pathWithValueKey === pathKey;
      });

      treeCopy = setValueOnDTOTreeFor(treeCopy, path, values[hiringTargetId]);
    });

    return {
      hiringTargetId,
      aggregation: treeCopy.aggregation,
    };
  });
}
