import React, { useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { getParentOrigin, navigateTo } from '../../services/postMessage';
import { LinkBase } from './styles';
import { safeUrl } from '../../constants/endpoints';

export default function PostMessageLink(props) {
  const { children, disabled, to } = props;

  const handleClick = useCallback(
    (event) => {
      event.preventDefault();
      navigateTo(to);
    },
    [to],
  );

  const toHref = useMemo(() => {
    if (`${to}`.includes('http')) {
      return to;
    }

    const parentOrigin = getParentOrigin();

    return safeUrl(`${parentOrigin}/${to}`);
  }, [to]);

  return (
    <LinkBase href={toHref} isDisabled={disabled} onClick={handleClick}>
      {children}
    </LinkBase>
  );
}

PostMessageLink.propTypes = {
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
};

PostMessageLink.defaultProps = {
  disabled: false,
};
