import React, { useCallback, useState, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useDrop, useDrag } from 'react-dnd';
import shortid from 'shortid';
import noop from 'lodash/noop';
import get from 'lodash/get';
import { useSelector } from 'react-redux';
import { CARD_FROM_SIDEBAR, CARD_FROM_BUILDER } from '../../constants';
import Menu from '../../../Icons/Menu';
import SettingsHoverable from '../../../Icons/SettingsHoverable';
import { SettingsStyled } from '../../../ProjectForms/RecruitmentForm/Demographics/styles';
import close from '../../../../assets/icons/close.svg';
import eye from '../../../../assets/icons/eye.svg';
import pencil from '../../../../assets/icons/pencil.svg';
import { AddStepBox } from '../styles';
import { createKeydownHandler } from '../../../../helpers/a11y';
import { selectStepEditorDirty } from '../../../../store/ducks/processBuilder';
import StepSwitcher from '../StepSwitcher';
import QuestionLoader from '../../../TargetBuilder/QuestionLoader';
import { hasStepComponent } from '../../helpers/getStepComponent';
import { usePreviewModalControls } from '../../helpers/preview-context';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../Popover';
import {
  CardWrapper,
  CardContainer,
  CardIndex,
  CardHeader,
  CardTitle,
  CardDescription,
  CardInfo,
  CardActions,
  CadDisabled,
  DropBoxContainer,
  CardContent,
  SettingsButtonContainer,
  MenuContainer,
} from './styles';
import { ReactComponent as Chevron } from '../../../../assets/icons/double-chevron-right.svg';

function collect(monitor) {
  return {
    hovered: monitor.isOver(),
  };
}

const popoverModifiers = {
  offset: {
    offset: '0, -2',
  },
};

export default function Card(props) {
  const {
    id,
    stepDefinitionId,
    sequenceFlow,
    name,
    description,
    attributes,
    subSteps,
    removable,
    cardIndex,
    cardKey,
    ignoreOffset,
    setProcessModules,
    removeProcessModule,
    inlineText,
    showPreview,
    items,
    isSecondary,
    // processModules,
    // previousModules,
    // openPreview,
  } = props;

  const dirty = useSelector(selectStepEditorDirty);

  const [{ hovered }, dropRef] = useDrop({
    accept: [CARD_FROM_SIDEBAR, CARD_FROM_BUILDER],
    collect,
    // hover: console.log,
    drop: (item) => {
      if (item.type === CARD_FROM_BUILDER) {
        setProcessModules((currentCards) => {
          return [
            ...currentCards.slice(0, cardIndex),
            { ...item, key: shortid.generate() },
            ...currentCards.slice(cardIndex),
          ].filter((card) => card.key !== item.cardKey);
        });
      } else {
        setProcessModules((currentCards) => [
          ...currentCards.slice(0, cardIndex),
          {
            ...item,
            key: shortid.generate(),
            dirty: hasStepComponent(item.sequenceFlow),
          },
          ...currentCards.slice(cardIndex),
        ]);
      }

      return { handled: true };
    },
  });

  const [{ isDragging }, dragRef, previewRef] = useDrag({
    item: {
      id,
      stepDefinitionId,
      name,
      description,
      sequenceFlow,
      attributes,
      subSteps,
      type: CARD_FROM_BUILDER,
      cardIndex,
      cardKey,
      items,
      isSecondary,
    },
    canDrag: !dirty,
    collect: (monitor) => ({ isDragging: monitor.isDragging() }),
  });

  const [collapsed, setCollapsed] = useState(true);

  const editCard = useCallback(() => {
    if (collapsed) {
      setCollapsed(false);
    }
  }, [collapsed]);

  const hasData = useMemo(
    () => (attributes && attributes.length) || (subSteps && subSteps.length),
    [attributes, subSteps],
  );

  const removeModule = useCallback(() => {
    if (hasData) {
      if (
        // eslint-disable-next-line no-restricted-globals,no-alert
        confirm(
          'Are you sure you want to delete this step? This is not reversible.',
        )
      ) {
        removeProcessModule(cardKey);
      }
      return;
    }

    removeProcessModule(cardKey);
  }, [cardKey, hasData, removeProcessModule]);

  const cancelEditing = useCallback(() => {
    if (hasData) {
      setCollapsed(true);
      return;
    }

    removeModule();
  }, [hasData, removeModule]);

  const modalControls = usePreviewModalControls();

  const previewStep = () => {
    modalControls.openModal({
      only: {
        id,
        stepDefinitionId,
        name,
        description,
        sequenceFlow,
        attributes,
        subSteps,
        index: cardIndex,
        items,
        isSecondary,
      },
    });
  };

  const renderSubQuestions = (subquestions = []) => {
    return subquestions.map((question) => {
      const enabled = get(
        question,
        ['attributes', 0, 'enabled', question.id],
        true,
      );

      if (!collapsed || !hasData) return null;

      return (
        <CardWrapper isSecondary>
          <CardContainer>
            <CardHeader isDirty={dirty}>
              <CardIndex opacity={!enabled}>
                <Chevron
                  style={{ verticalAlign: 'middle', margin: '-4px 0 0 0' }}
                />
              </CardIndex>
              <CardInfo opacity={!enabled}>
                <CardTitle inlineText={inlineText}>{question.name}</CardTitle>
                <CardDescription inlineText={inlineText}>
                  {question.question}
                </CardDescription>
              </CardInfo>
              {!enabled && <CadDisabled>Disabled</CadDisabled>}
            </CardHeader>
          </CardContainer>
        </CardWrapper>
      );
    });
  };

  const poppoverContent = (
    <>
      {hasData ? (
        <PopoverItem
          onClick={editCard}
          onKeyDown={createKeydownHandler(editCard)}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={pencil} alt="Edit current step" />
          <PopoverText>Edit</PopoverText>
        </PopoverItem>
      ) : null}
      {(hasStepComponent(sequenceFlow) ? !!hasData : showPreview) && (
        <PopoverItem
          onClick={previewStep}
          onKeyDown={createKeydownHandler(previewStep)}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={eye} alt="Preview current step" />
          <PopoverText>Preview</PopoverText>
        </PopoverItem>
      )}
      <PopoverItem
        onClick={removable ? removeModule : undefined}
        onKeyDown={() => removable && createKeydownHandler(removeModule)}
        role="button"
        tabIndex={removable ? 0 : -1}
        aria-disabled={!removable}
        isDisabled={!removable}
        title={
          removable
            ? ''
            : "You can't remove a step from a process that has been already started."
        }
      >
        <PopoverIcon src={close} alt="Remove current step from process" />
        <PopoverText>Remove</PopoverText>
      </PopoverItem>
    </>
  );

  // TODO: Find the best way to handle it
  const Component = useMemo(() => {
    return sequenceFlow ? StepSwitcher : QuestionLoader;
  }, [sequenceFlow]);

  return (
    <CardWrapper
      isDragging={isDragging}
      ref={dropRef}
      isFirst={cardIndex === 0}
    >
      <DropBoxContainer
        isHovered={hovered}
        ignoreOffset={ignoreOffset}
        isDragging={isDragging}
      >
        <AddStepBox isHovered={false} />
      </DropBoxContainer>

      <CardContainer>
        <CardHeader ref={previewRef} isDirty={dirty}>
          <CardIndex
            ref={dragRef}
            title={
              dirty
                ? "You can't reorder steps while there are unsaved changes."
                : ''
            }
            className="process_builder__card__index"
          >
            <div>{cardIndex + 1}</div>
            <MenuContainer>
              <Menu aria-hidden />
            </MenuContainer>
          </CardIndex>
          <CardInfo>
            <CardTitle inlineText={inlineText}>{name}</CardTitle>
            <CardDescription inlineText={inlineText}>
              {description}
            </CardDescription>
          </CardInfo>
          <CardActions className="process_builder__card__actions">
            {(collapsed || !hasData) && (
              <SettingsButtonContainer>
                <Popover
                  content={poppoverContent}
                  placement="bottom-end"
                  modifiers={popoverModifiers}
                >
                  <SettingsStyled>
                    <SettingsHoverable />
                  </SettingsStyled>
                </Popover>
              </SettingsButtonContainer>
            )}
          </CardActions>
        </CardHeader>
        <CardContent
          isExpanded={!collapsed}
          onClick={collapsed ? () => setCollapsed(false) : noop}
        >
          <Component
            id={id}
            stepDefinitionId={stepDefinitionId}
            sequenceFlow={sequenceFlow}
            cardKey={cardKey}
            cardIndex={cardIndex}
            mode={typeof id === 'number' ? 'edit' : 'create'}
            removeModule={removeModule}
            collapsed={collapsed}
            setCollapsed={setCollapsed}
            cancelEditing={cancelEditing}
            attributes={attributes || []}
            subSteps={subSteps || []}
            items={items}
          />
        </CardContent>
      </CardContainer>
      {renderSubQuestions(items)}
    </CardWrapper>
  );
}

Card.propTypes = {
  id: PropTypes.number,
  sequenceFlow: PropTypes.string.isRequired,
  stepDefinitionId: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  attributes: PropTypes.arrayOf(
    PropTypes.shape({
      attributeOrder: PropTypes.number,
      value: PropTypes.any,
    }),
  ),
  subSteps: PropTypes.arrayOf(
    PropTypes.shape({
      attributes: PropTypes.arrayOf(
        PropTypes.shape({
          attributeOrder: PropTypes.number,
          value: PropTypes.any,
        }),
      ).isRequired,
    }),
  ),
  description: PropTypes.string.isRequired,
  cardIndex: PropTypes.number.isRequired,
  ignoreOffset: PropTypes.bool.isRequired,
  cardKey: PropTypes.string.isRequired,
  setProcessModules: PropTypes.func.isRequired,
  removeProcessModule: PropTypes.func.isRequired,
  removable: PropTypes.bool,
  inlineText: PropTypes.bool,
  showPreview: PropTypes.bool,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  isSecondary: PropTypes.bool,
  // openPreview: PropTypes.func.isRequired,
  // processModules: PropTypes.arrayOf(PropTypes.object).isRequired,
  // previousModules: PropTypes.arrayOf(PropTypes.object).isRequired,
};

Card.defaultProps = {
  id: null,
  attributes: undefined,
  subSteps: undefined,
  removable: true,
  inlineText: false,
  showPreview: true,
  isSecondary: false,
};
