import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'styled-bootstrap-grid';
import RecruitmentPreview from '../../../../components/ProjectPreviews/RecruitmentPreview';
import { Section } from '../../../../styles';
import { CardContentContainer, StepName, Subtitle } from '../../styles';
import { HiringTargetLink } from '../../../../components/ProjectForms/RecruitmentForm/HiringTargets/styles';
import RecruitmentProjectViewForm from './RecruitmentProjectViewForm';
import { HiringTargetProgressContainer } from './styles';
import ProgressIndicator from './ProgressIndicator';
import { hiringTargetsGuidelineLink } from '../../../../constants/externalLinks';
import AutoScreenHeader from '../../../../components/ProjectForms/RecruitmentForm/AutoScreening/Header';

export default function Recruitment(props) {
  const { mode, averageProgress, hasStatistics } = props;

  if (mode === 'edit') {
    return <RecruitmentProjectViewForm />;
  }
  // Edit mode
  return (
    <CardContentContainer>
      {/* Pass in aditional columns and stats for them */}
      <RecruitmentPreview
        localeTenantsHeader={
          <Section marginBottom={14}>
            <StepName>Locale Tenants</StepName>
            <Subtitle>The project locale tenants for each locale.</Subtitle>
          </Section>
        }
        hiringTargetsHeader={
          <Section marginBottom={14}>
            <Row>
              <Col col={8}>
                <StepName>Hiring Targets</StepName>
                <Subtitle>
                  The project hiring targets for each Locale. Please, check{' '}
                  <HiringTargetLink
                    target="blank"
                    href={hiringTargetsGuidelineLink}
                  >
                    hiring target guidelines
                  </HiringTargetLink>
                  .
                </Subtitle>
              </Col>
              <Col col={4}>
                {hasStatistics && (
                  <HiringTargetProgressContainer>
                    <ProgressIndicator percentCompleted={averageProgress} />
                  </HiringTargetProgressContainer>
                )}
              </Col>
            </Row>
          </Section>
        }
        demographicsHeader={
          <Section marginBottom={30}>
            <StepName>Custom Demographic Requirements</StepName>
            <Subtitle>
              Recruiting team will use the parameters set as basis for
              recruitment.
            </Subtitle>
          </Section>
        }
        autoScreenHeader={<AutoScreenHeader />}
        renderedIn="project-view"
      />
    </CardContentContainer>
  );
}

Recruitment.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  averageProgress: PropTypes.number.isRequired,
  hasStatistics: PropTypes.bool.isRequired,
};
