import { connect } from 'react-redux';
import { compose } from 'recompose';
import TargetBuilder from './TargetBuilder';
import { selectProcessModules } from '../../store/ducks/targetBuilder';

function mapStateToProps(state) {
  const processModules = selectProcessModules(state);

  return {
    processModules,
  };
}

export default compose(connect(mapStateToProps))(TargetBuilder);
