import { useCallback, useState } from 'react';
import withHooks from 'hook-hoc';
import RecruitingAutomation from './RecruitingAutomation';

const tabsOptions = [
  { label: 'User screening' },
  { label: 'Project qualification' },
];

function useHooksAsProps() {
  const [currentTab, setCurrentTab] = useState(0);

  const handleTabChange = useCallback(
    (nextTab) => {
      setCurrentTab(nextTab);
    },
    [setCurrentTab],
  );

  return { currentTab, tabsOptions, handleTabChange };
}

export default withHooks(useHooksAsProps)(RecruitingAutomation);
