/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import undoable from 'redux-undo';
import get from 'lodash/get';
import { selectAll as selectAllRequirements } from '../demographicRequirements';

export const DEMOGRAPHIC_TYPE = {
  BY_GROUP: 'by-group',
  AGGREGATED: 'aggregated',
};

export const DEMOGRAPHIC_VIEW_AS = {
  NUMBER: 'NUMBER',
  PERCENTAGE: 'PERCENT',
};

export const DEMOGRAPHIC_METRICS_TYPE = {
  CUSTOM: 'custom',
  GENERAL: 'general',
};

const initialState = {
  demographicType: DEMOGRAPHIC_TYPE.BY_GROUP,
  viewAs: DEMOGRAPHIC_VIEW_AS.PERCENTAGE,
  viewGeneralMetrics: false,
  requirements: [],
  loading: true,
  error: null,
  requirementsByGroup: [],
  aggregatedRequirements: {},
};

const slice = createSlice({
  name: 'projectDemographicMetrics',
  initialState,
  reducers: {
    fetchDemographics(state) {
      state.loading = true;
    },

    fetchDemographicsFailed(state, action) {
      state.error = action.payload;
      state.loading = false;
    },

    fetchDemographicsSuccess(state, action) {
      state.requirements = action.payload;
      state.loading = false;
    },

    updateDemographicType(state, action) {
      state.requirementsByGroup = [];
      state.aggregatedRequirements = {};
      state.demographicType = action.payload;
    },

    setHasCustomMetrics(state, action) {
      state.hasCustomMetrics = action.payload;
    },

    setMetricsType(state, action) {
      state.metricsType = action.payload;
    },

    updateViewAs(state, action) {
      state.viewAs = action.payload;

      state.requirementsByGroup = [];
      state.aggregatedRequirements = {};
    },

    updateViewGeneralMetrics(state, action) {
      state.viewGeneralMetrics = action.payload;
    },

    updateRequirements(state, action) {
      state.requirements = action.payload;

      state.requirementsByGroup = [];
      state.aggregatedRequirements = {};
    },

    setRequirementsByGroup: {
      reducer(state, action) {
        state.requirementsByGroup = action.payload;
        state.demographicType = DEMOGRAPHIC_TYPE.BY_GROUP;
      },

      prepare(payload, meta) {
        return {
          payload,
          meta,
        };
      },
    },

    setAggregatedRequirements: {
      reducer(state, action) {
        state.aggregatedRequirements = action.payload;
        state.demographicType = DEMOGRAPHIC_TYPE.AGGREGATED;
      },

      prepare(payload, meta) {
        return {
          payload,
          meta,
        };
      },
    },

    removeRequirements() {
      return {
        ...initialState,
        loading: false,
      };
    },

    undoSetRequirements() {},
  },
});

const {
  reducer,
  actions: {
    fetchDemographics,
    fetchDemographicsFailed,
    fetchDemographicsSuccess,
    updateRequirements,
    updateDemographicType,
    setMetricsType,
    updateViewAs,
    updateViewGeneralMetrics,
    setRequirementsByGroup,
    setAggregatedRequirements,
    setHasCustomMetrics,
    removeRequirements,
    undoSetRequirements,
  },
} = slice;

const projectDemographicMetricsReducer = undoable(reducer, {
  limit: 1,
  undoType: undoSetRequirements.type,
});

projectDemographicMetricsReducer.key = 'projectDemographicMetrics';

export default projectDemographicMetricsReducer;

export {
  fetchDemographics,
  fetchDemographicsFailed,
  fetchDemographicsSuccess,
  updateRequirements,
  updateDemographicType,
  setMetricsType,
  updateViewAs,
  updateViewGeneralMetrics,
  setRequirementsByGroup,
  setAggregatedRequirements,
  setHasCustomMetrics,
  removeRequirements,
  undoSetRequirements,
};

export function selectProjectDemographicRequirements(state) {
  return state[projectDemographicMetricsReducer.key].present || {};
}

export function selectDemographicType(state) {
  return get(
    selectProjectDemographicRequirements(state),
    ['demographicType'],
    DEMOGRAPHIC_TYPE.BY_GROUP,
  );
}

export function selectViewAs(state) {
  return (
    get(selectProjectDemographicRequirements(state), 'viewAs') ||
    DEMOGRAPHIC_VIEW_AS.NUMBER
  );
}

export function selectViewGeneralMetrics(state) {
  return (
    get(selectProjectDemographicRequirements(state), 'viewGeneralMetrics') ||
    false
  );
}

export function selectMetricsType(state) {
  return (
    get(selectProjectDemographicRequirements(state), 'metricsType') ||
    DEMOGRAPHIC_METRICS_TYPE.GENERAL
  );
}

export function selectHasCustomMetrics(state) {
  return (
    get(selectProjectDemographicRequirements(state), 'hasCustomMetrics') ||
    false
  );
}

export function selectRequirements(state) {
  return get(selectProjectDemographicRequirements(state), ['requirements'], []);
}

export function selectError(state) {
  return get(selectProjectDemographicRequirements(state), ['error'], null);
}

export function selectLoading(state) {
  return get(selectProjectDemographicRequirements(state), ['loading'], false);
}

export function selectRequirementsByGrup(state) {
  return get(
    selectProjectDemographicRequirements(state),
    ['requirementsByGroup'],
    [],
  );
}

export function hasRequirementsByGroup(state) {
  return selectRequirementsByGrup(state).length > 0;
}

export function selectAggregatedRequirements(state) {
  return get(
    selectProjectDemographicRequirements(state),
    ['aggregatedRequirements'],
    [],
  );
}

export function hasAggregatedRequirements(state) {
  return Object.keys(selectAggregatedRequirements(state)).length > 0;
}

export function hasAnyDemographicData(state) {
  return hasAggregatedRequirements(state) || hasRequirementsByGroup(state);
}

export function selectRequirementsEager(state) {
  const allRequirements = selectAllRequirements(state);
  const { requirements } = selectProjectDemographicRequirements(state);

  return requirements.map((r) =>
    allRequirements.find((ar) => ar.id === r.value),
  );
}
