import { call, put, takeEvery } from 'redux-saga/effects';
import { createProject, projectMutationError, projectMutationSuccess } from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* patchProjectSaga(action) {
  const {
    payload: { project, thenPut, catchPut },
  } = action;

  if (!project) {
    yield put(
      projectMutationError(
        new Error(
          "Can't create project without at least project name and description.",
        ),
      ),
    );
    return;
  }

  try {
    const { data: nextProject } = yield call(
      axios.post,
      endpoints.V2.PROJECT(),
      project,
    );

    yield put(
      projectMutationSuccess({
        project: nextProject,
      }),
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }
  } catch (err) {
    yield put(projectMutationError(err));

    if (typeof catchPut === 'object') {
      yield put(catchPut);
    }
  }
}

export default takeEvery(createProject.type, patchProjectSaga);
