import { put, takeEvery, call, select, all } from 'redux-saga/effects';
import get from 'lodash/get';
import find from 'lodash/find';
import {
  setProcessModules,
  setProcessLoading,
  setProcessError,
  openTargetBuilder,
  requestOpenTargetBuilder,
  setProcessDetails,
  isProcessModulePristine,
} from '.';
import { profileBuilderAPI } from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import { ENTITIES, selectors } from '../options';
import findLocale from '../options/helpers/localeLanguages';
import { findByIgnoringCase } from '../../../helpers/find';

/**
 * type OpenProcessBuilderAction = {
 *   payload: {
 *     processType: PROCESS_TYPE,
 *     processID?: number,
 *     copyFromProcessID?: number,
 *   }
 * }
 */
function* fetchTargetBuilderSaga(action) {
  const {
    payload: {
      processType,
      processID,
      copyFromProcessID,
      processName,
      processDescription,
    },
    meta,
  } = action;

  const isCopying = !processID && !!copyFromProcessID;

  if (meta && meta.preventFetch) {
    return;
  }

  if (!processID && !copyFromProcessID) {
    // Starting an empty project
    yield put(
      openTargetBuilder({
        processType,
      }),
    );

    return;
  }

  const isPristine = yield select(isProcessModulePristine);

  const state = yield select();
  const countries = selectors.selectSliceFor(ENTITIES.countries, state);
  const languages = selectors.selectSliceFor(ENTITIES.localeLanguages, state);

  if (!isPristine) {
    return;
  }

  const endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER({
    pathVariables: {
      id: processID || copyFromProcessID,
    },
  });

  try {
    yield put(setProcessLoading(true));
    const response = yield call(profileBuilderAPI.get, endpoint);

    yield put(
      openTargetBuilder({
        processType,
        processName: processName || response.data.name,
        processDescription: processDescription || response.data.description,
        processID: isCopying ? null : processID,
        copyFromProcessID: isCopying ? copyFromProcessID : null,
        instantiated: response.data.instantiated,
      }),
    );

    const questionsResponse = yield all(
      response.data.profileQuestions.map((question) =>
        call(
          profileBuilderAPI.get,
          endpoints.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS({
            pathVariables: {
              id: question.masterProfileQuestionId,
            },
          }),
        ),
      ),
    );

    const modules = response.data.profileQuestions.map((profileQuestion) => {
      const questionResponse = questionsResponse.filter(
        (resp) => resp.data.id === profileQuestion.masterProfileQuestionId,
      );
      const question = get(questionResponse, [0, 'data'], {});

      const firstAction = profileQuestion.allChecked
        ? 'PASS_THROUGH'
        : 'REJECT';

      const firstOption = {
        [question.id]: {
          value: profileQuestion.allChecked ? 'passThrough' : 'reject',
        },
      };

      const secondOption = {
        [question.id]: {
          value: profileQuestion.allUnchecked ? 'passThrough' : 'reject',
        },
      };

      const answerList = profileQuestion.profileQuestionAnswerList
        .map((answer) => {
          const questionAnswer = find(question.answerList, {
            id: answer.masterProfileQuestionAnswerId,
          });

          if (
            findByIgnoringCase(
              ['COUNTRY LIST', 'LANGUAGE LIST'],
              questionAnswer.answer,
            )
          ) {
            if (answer.customAnswer) {
              const selectedItems = answer.customAnswer.value
                .split(',')
                .map((itemCode) => {
                  const options =
                    {
                      'COUNTRY LIST': countries.data,
                      'LANGUAGE LIST': languages.data,
                    }[questionAnswer.answer.toUpperCase()] || null;

                  const option = findLocale(
                    options,
                    ({ value }) => value === itemCode,
                  );
                  const label = get(option, 'label', null);

                  return {
                    id: questionAnswer.id,
                    label,
                    value: itemCode,
                    questionType: answer.customAnswer.type,
                    profileAnswerId: answer.id,
                  };
                });

              return selectedItems.flat();
            }

            return [
              {
                id: questionAnswer.id,
                label: null,
                value: null,
                profileAnswerId: answer.id,
                questionType: {
                  'COUNTRY LIST': 'COUNTRY_LIST',
                  'LANGUAGE LIST': 'LANGUAGE_LIST',
                }[questionAnswer.answer.toUpperCase()],
              },
            ];
          }

          if (questionAnswer.answer === 'Custom answer') {
            return {
              ...questionAnswer,
              ...answer.customAnswer,
              id: questionAnswer.id,
              profileAnswerId: answer.id,
              isBetween: get(answer.customAnswer, 'logic', null) === 'BETWEEN',
            };
          }

          if (question.questionType === 'DROPDOWN_SELECTION') {
            return {
              ...questionAnswer,
              id: questionAnswer.id,
              value: questionAnswer.id,
              label: questionAnswer.answer,
              action: answer.action,
              profileAnswerId: answer.id,
              questionType: 'DROPDOWN_SELECTION',
            };
          }

          return {
            ...questionAnswer,
            value: answer.action === firstAction,
            action: answer.action,
            profileAnswerId: answer.id,
          };
        })
        .flat();

      const profileQuestionId = {
        [question.id]: get(profileQuestion, 'id', null),
      };

      const attributes = {
        answers: { [question.id]: answerList },
        enabled: { [question.id]: !profileQuestion.disabledSubQuestion },
        firstOption,
        secondOption,
        questionType: question.questionType,
        logic: '',
        profileQuestionId,
      };

      attributes[`firstOption${question.id}`] = true;
      attributes[`secondOption${question.id}`] = true;

      return {
        ...question,
        attributes: [attributes],
        key: question.id,
        stepDefinitionId: question.id,
        description: question.question,
        dirty: false,
        name: question.name,
      };
    });

    const parentModules = modules.filter((m) => m.parentQuestion === null);
    const childModules = modules.filter((m) => m.parentQuestion !== null);
    const sortedModules = [...parentModules, ...childModules];

    const groupedModules = sortedModules.reduce((acc, module) => {
      let parentQuestionId = get(
        module,
        'parentQuestion.parentQuestionId',
        null,
      );

      let parentModule = null;

      while (parentQuestionId && !parentModule) {
        // eslint-disable-next-line no-loop-func
        const parent = sortedModules.find((m) => {
          return m.id === parentQuestionId;
        });

        if (parent.parentQuestion) {
          const id = get(parent, 'parentQuestion.parentQuestionId', null);
          parentQuestionId = id || parentQuestionId;
        } else {
          parentModule = parent;
        }
      }

      if (parentModule) {
        const parentAttr = get(parentModule, ['attributes', 0], {});
        const attributes = get(module, ['attributes', 0], {});
        parentAttr.answers[module.id] = attributes.answers[module.id];
        parentAttr.enabled[module.id] = attributes.enabled[module.id];
        parentAttr.firstOption[module.id] = attributes.firstOption[module.id];
        parentAttr.secondOption[module.id] = attributes.secondOption[module.id];
        parentAttr.profileQuestionId[module.id] =
          attributes.profileQuestionId[module.id];

        parentAttr[`firstOption${module.id}`] = true;
        parentAttr[`secondOption${module.id}`] = true;

        let { items } = acc[parentQuestionId];

        if (!items) {
          items = [module];
        } else {
          items.push(module);
        }

        acc[parentQuestionId] = {
          ...parentModule,
          attributes: [parentAttr],
          items,
        };

        return {
          ...acc,
        };
      }

      return {
        ...acc,
        [module.id]: module,
      };
    }, {});

    yield put(
      setProcessModules(
        modules.map((module) => groupedModules[module.id]).filter((m) => m),
        { isPristine: true },
      ),
    );
    yield put(setProcessLoading(false));
  } catch (error) {
    yield put(setProcessError(error));
  }
}

export default takeEvery(
  [requestOpenTargetBuilder.type, setProcessDetails.type],
  fetchTargetBuilderSaga,
);
