/* eslint-disable react/forbid-prop-types */
import React, { useMemo } from 'react';
import { Row, Col } from 'styled-bootstrap-grid';
import get from 'lodash/get';
import PropTypes from 'prop-types';
import Input from '../../../../components/Input';
import Select from '../../../../components/Select';
import Button from '../../../../components/Button';
import Checkbox from '../../../../components/Checkbox';
import Modal from '../../../../components/Modal';
import ActionFooter from '../../../../components/ActionFooter';
import LabeledSwitch from '../../../../components/LabeledSwitch';
import TabSwitcher from '../../../../components/TabSwitcher';
import Textarea from '../../../../components/Textarea';
import InformationTooltip from '../../../../components/ProcessBuilder/InformationTooltip';
import Disabled from '../../../../components/Disabled';
import CloseableTag from '../../../../components/CloseableTag';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../components/ErrorPopover';
import { Section, Label } from '../../../../styles';
import {
  Container,
  Footer,
  SubTitle,
  LabelWrapper,
  TooltipContent,
  AccessoryLeft,
  AnswersContainer,
  AnswerWrapper,
  RemoveButton,
} from './styles';
import close from '../../../../assets/icons/close.svg';

export default function QuestionSetupModal(props) {
  const {
    modalProps,
    modalControls: { closeModal },
    lockUpOptions,
    logicOptions,
    answerTypeOptions,
    answerTypeAcessoryOptions,
    primaryQuestionOptions,
    primaryAnswerOptions,
    countryOptions,
    countryTags,

    // Handlers
    handleAddAnswerItem,
    handleRemoveAnswerItem,
    handleAnswerListChange,
    handleLockUpPeriodChange,
    handleAnswerTypeChange,
    handleAnswerStyleChange,
    handleParentQuestionIdChange,
    handleNestAnswerChange,
    handleLogicChange,
    handleCountryChange,
    handleCountryRemoval,

    // Formik
    values: {
      id,
      name,
      timer,
      question,
      notes,
      addQuestionNotes,
      questionType,
      answerStyle,
      answerList,
      enableExcludeCountries,
      excludeCountries,
      nestQuestion,
      systemFilled,
      parentQuestionId,
      logic,
      parentAnswerId,
    },
    submitForm,
    submitCount,
    errors,
    errorPopoverProps,
    handleChange,
  } = props;

  const title = useMemo(() => (id ? 'Edit Topic' : 'Add New Topic'), [id]);

  const acessoryOptions = useMemo(
    () => answerTypeAcessoryOptions[questionType] || [],
    [answerTypeAcessoryOptions, questionType],
  );

  const acessoryTitle = useMemo(
    () =>
      questionType === 'DROPDOWN_SELECTION'
        ? 'Answer Parameter'
        : 'Answer style',
    [questionType],
  );

  const showAnswerItems = useMemo(
    () =>
      ['MULTIPLE_CHOICE', 'CHECKBOX_GROUP', 'YES_NO'].includes(questionType),
    [questionType],
  );

  const showYesOrNoItems = useMemo(() => questionType === 'YES_NO', [
    questionType,
  ]);

  const renderAnswerItems = useMemo(
    () =>
      answerList.map((item, index) => {
        const fieldBaseName = `answerList${index}`;
        const { answer } = item;
        const disabled = !answerList[index].key;
        return (
          <AnswerWrapper key={item.id}>
            <Disabled
              when={disabled}
              message="You can't edit items from enabled questions"
              opacity={1}
            >
              <div>
                <Input
                  name={fieldBaseName}
                  id={fieldBaseName}
                  placeholder="Enter answer item"
                  value={answer}
                  onChange={(value) => handleAnswerListChange(index, value)}
                  error={submitCount >= 1 && errors[fieldBaseName]}
                  errorIncreasesHeight
                  disabled={disabled}
                />
              </div>
            </Disabled>
            <Disabled
              when={disabled || index === 0}
              message="List item can't be deleted."
              padding={'8px 0'}
            >
              <RemoveButton
                onClick={() => handleRemoveAnswerItem(index)}
                disabled={disabled}
                error={submitCount >= 1 && errors[fieldBaseName]}
              >
                <img src={close} alt="remove answer" />
              </RemoveButton>
            </Disabled>
          </AnswerWrapper>
        );
      }),
    [
      answerList,
      errors,
      handleAnswerListChange,
      handleRemoveAnswerItem,
      submitCount,
    ],
  );

  const renderYesOrNoItems = useMemo(
    () => (
      <>
        <AnswerWrapper>
          <Input name="yes" value="Yes" disabled />
        </AnswerWrapper>
        <AnswerWrapper>
          <Input name="no" value="No" disabled />
        </AnswerWrapper>
      </>
    ),
    [],
  );

  return (
    <Modal
      open={modalProps.open}
      onExited={modalProps.onExited}
      onClose={closeModal}
      title={title}
    >
      <Container>
        <SubTitle>Question Setup</SubTitle>
        <Section>
          <Row>
            <Col col="7">
              <Label htmlFor="name">Topic name</Label>
              <Input
                name="name"
                id="name"
                placeholder="Enter name"
                value={name}
                onChange={handleChange}
                error={submitCount >= 1 && errors.name}
              />
            </Col>
            <Col col="4">
              <LabelWrapper>
                <Label htmlFor="timer">Lock-up period</Label>
                <InformationTooltip
                  title={
                    <TooltipContent>
                      Lock-up period is a window of time when workers will not
                      be able to edit the answer to this question. After lock-up
                      period ends, workers will be able to review and save this
                      information again. All edits will trigger a new lock-up
                      period, to avoid fraud.
                    </TooltipContent>
                  }
                />
              </LabelWrapper>
              <Select
                value={timer}
                name="timer"
                id="timer"
                onChange={handleLockUpPeriodChange}
                options={lockUpOptions}
                error={submitCount >= 1 && errors.timer}
                placeholder="Select period"
              />
            </Col>
          </Row>
        </Section>
        <Section>
          <Row>
            <Col col="11">
              <Label htmlFor="question">Question text</Label>
              <Input
                name="question"
                id="question"
                placeholder="Enter question"
                value={question}
                onChange={handleChange}
                error={submitCount >= 1 && errors.question}
              />
            </Col>
          </Row>
        </Section>
        <Section>
          <Row>
            <Col col="11">
              <Checkbox
                name="addQuestionNotes"
                checked={addQuestionNotes}
                bold={false}
                onChange={handleChange}
                label="Add question notes"
              />
            </Col>
          </Row>
          {addQuestionNotes && (
            <Section marginLeft={34}>
              <Row>
                <Col col="11">
                  <Label htmlFor="notes">Notes</Label>
                  <Textarea
                    onChange={handleChange}
                    name="notes"
                    id="notes"
                    value={notes}
                    placeholder="Enter notes"
                    rows={3}
                    error={submitCount >= 1 && errors.notes}
                  />
                </Col>
              </Row>
            </Section>
          )}
        </Section>
        <SubTitle>Answer Setup</SubTitle>
        <Section>
          <Row>
            <Col col="4">
              <Label htmlFor="questionType">Answer type</Label>
              <Select
                value={questionType}
                name="questionType"
                id="questionType"
                onChange={handleAnswerTypeChange}
                options={answerTypeOptions}
                error={submitCount >= 1 && errors.questionType}
                errorIncreasesHeight
                placeholder="Select type"
              />
            </Col>
            {acessoryOptions.length > 0 && (
              <Col col="4">
                <Label htmlFor="answerStyle">{acessoryTitle}</Label>
                <Select
                  value={answerStyle}
                  name="answerStyle"
                  id="answerStyle"
                  onChange={handleAnswerStyleChange}
                  options={get(answerTypeAcessoryOptions, questionType, [])}
                  error={submitCount >= 1 && errors.answerStyle}
                  placeholder="Select style"
                />
              </Col>
            )}
          </Row>
        </Section>
        {showAnswerItems && (
          <Section>
            <Row>
              <Col col="11">
                <Label htmlFor="answerStyle">Answer items</Label>
                <AnswersContainer>
                  {!showYesOrNoItems && renderAnswerItems}
                  {showYesOrNoItems && renderYesOrNoItems}
                </AnswersContainer>
                {!showYesOrNoItems && (
                  <Button type="secondary" onClick={handleAddAnswerItem}>
                    Add Other Item
                  </Button>
                )}
              </Col>
            </Row>
          </Section>
        )}
        <SubTitle>Extra Configuration</SubTitle>
        <Section>
          <Row>
            <Col col="12">
              <Checkbox
                name="enableExcludeCountries"
                checked={enableExcludeCountries}
                bold={false}
                onChange={handleChange}
                label="Exclude countries"
              />
            </Col>
          </Row>
          {enableExcludeCountries && (
            <Section marginLeft={34}>
              <Row>
                <Col col="11">
                  <Label htmlFor="excludeCountries">
                    Select excluded countries
                  </Label>
                  <Select
                    id="excludeCountries"
                    name="excludeCountries"
                    options={countryOptions}
                    value={excludeCountries}
                    onChange={handleCountryChange}
                    placeholder="Select Countries"
                    error={submitCount > 0 && errors.excludeCountries}
                    isMulti
                  />
                  {countryTags.map((r) => (
                    <CloseableTag
                      key={r.label}
                      label={r.label}
                      onClose={() => handleCountryRemoval(r.value)}
                    />
                  ))}
                </Col>
              </Row>
            </Section>
          )}
          <Row>
            <Col col="12">
              <Checkbox
                name="nestQuestion"
                checked={nestQuestion}
                bold={false}
                onChange={handleChange}
                label="Nest question"
              />
            </Col>
          </Row>
          {nestQuestion && (
            <>
              <Section marginLeft={34}>
                <Row>
                  <Col col="11">
                    <Label htmlFor="parentQuestionId">
                      Select primary question
                    </Label>
                    <Select
                      value={parentQuestionId}
                      name="parentQuestionId"
                      id="parentQuestionId"
                      onChange={handleParentQuestionIdChange}
                      options={primaryQuestionOptions}
                      error={submitCount >= 1 && errors.parentQuestionId}
                      placeholder="Select question"
                    />
                  </Col>
                </Row>
              </Section>
              <Section marginLeft={34}>
                <Row>
                  <Col col="3">
                    <Label htmlFor="logic">Select Logic</Label>
                    <TabSwitcher
                      tabs={logicOptions}
                      value={logic}
                      onChange={handleLogicChange}
                      error={submitCount >= 1 && errors.logic}
                      errorIncreasesHeight
                    />
                  </Col>
                  <Col col="8">
                    <Label htmlFor="parentAnswerId">Select answer</Label>
                    <Select
                      value={parentAnswerId}
                      name="parentAnswerId"
                      id="parentAnswerId"
                      onChange={handleNestAnswerChange}
                      options={get(primaryAnswerOptions, parentQuestionId, [])}
                      error={submitCount >= 1 && errors.parentAnswerId}
                      placeholder="Select answer"
                    />
                  </Col>
                </Row>
              </Section>
            </>
          )}
        </Section>
      </Container>
      <ErrorPopover title="Problem while saving" {...errorPopoverProps} />
      <Footer>
        <ActionFooter
          hideDivider
          accessoryLeft={
            <Disabled when message="Option unavailable." opacity={1}>
              <AccessoryLeft>
                <LabeledSwitch
                  name="systemFilled"
                  label="SYSTEM FILLED"
                  value={!!systemFilled}
                  onChange={handleChange}
                />
              </AccessoryLeft>
            </Disabled>
          }
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: closeModal,
            },
            {
              text: 'Save',
              onClick: submitForm,
            },
          ]}
        />
      </Footer>
    </Modal>
  );
}

QuestionSetupModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  lockUpOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  logicOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ).isRequired,
  answerTypeOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ).isRequired,
  answerTypeAcessoryOptions: PropTypes.object,
  primaryQuestionOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  primaryAnswerOptions: PropTypes.object.isRequired,
  countryTags: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ),
  countryOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    }),
  ).isRequired,

  // Handlers
  handleAddAnswerItem: PropTypes.func.isRequired,
  handleRemoveAnswerItem: PropTypes.func.isRequired,
  handleAnswerListChange: PropTypes.func.isRequired,
  handleLockUpPeriodChange: PropTypes.func.isRequired,
  handleAnswerTypeChange: PropTypes.func.isRequired,
  handleAnswerStyleChange: PropTypes.func.isRequired,
  handleParentQuestionIdChange: PropTypes.func.isRequired,
  handleNestAnswerChange: PropTypes.func.isRequired,
  handleLogicChange: PropTypes.func.isRequired,
  handleCountryChange: PropTypes.func.isRequired,
  handleCountryRemoval: PropTypes.func.isRequired,

  // Formik
  values: PropTypes.shape({
    id: PropTypes.number,
    name: PropTypes.string,
    timer: PropTypes.number,
    question: PropTypes.string,
    notes: PropTypes.string,
    addQuestionNotes: PropTypes.bool,
    questionType: PropTypes.string,
    answerStyle: PropTypes.string,
    answerList: PropTypes.arrayOf(PropTypes.any),
    enableExcludeCountries: PropTypes.bool,
    excludeCountries: PropTypes.arrayOf(PropTypes.any),
    nestQuestion: PropTypes.bool,
    systemFilled: PropTypes.number,
    parentQuestionId: PropTypes.number,
    logic: PropTypes.string,
    parentAnswerId: PropTypes.string,
  }).isRequired,
  submitForm: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  handleChange: PropTypes.func.isRequired,
};

QuestionSetupModal.defaultProps = {
  answerTypeAcessoryOptions: {},
  countryTags: [],
};
