import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import { putProjectStatus } from '../../../../store/ducks/project';
import legacyIntegration from '../../../../helpers/legacy-integration';
import Button from '../../../../components/Button';

export default function MainActionButton(props) {
  const { status, project } = props;

  const dispatch = useDispatch();

  const handleProjectStatusUpdate = useCallback(
    (newStatus) => {
      const patch = { status: newStatus };
      dispatch(putProjectStatus(patch));
    },
    [dispatch],
  );

  switch (status) {
    case 'DRAFT':
      return (
        <Button
          onClick={() =>
            legacyIntegration.informLoadProjectSetup(parseInt(project.id, 10))
          }
        >
          Resume Setup
        </Button>
      );

    case 'ENABLED':
      return (
        <Button
          onClick={() => handleProjectStatusUpdate('DISABLED')}
          type="secondary"
        >
          Disable Project
        </Button>
      );

    case 'PAUSED':
      return (
        <>
          <Button
            onClick={() => handleProjectStatusUpdate('ENABLED')}
            marginRight={10}
          >
            Enable Project
          </Button>
          <Button onClick={() => handleProjectStatusUpdate('DISABLED')}>
            Disable Project
          </Button>
        </>
      );

    case 'READY':
    case 'DISABLED':
    default:
      return (
        <Button onClick={() => handleProjectStatusUpdate('ENABLED')}>
          Enable Project
        </Button>
      );
  }
}

MainActionButton.propTypes = {
  project: PropTypes.shape({
    id: PropTypes.number.isRequired,
  }).isRequired,
  status: PropTypes.string.isRequired,
};
