import { INVOICE_TYPES } from '.';

export function fromDTOToLocalState(responseData) {
  const {
    // autoApproveInvoice,
    autoApproveInvoiceBasedon,
    autoGenerateInvoice,
    autoGenerateInvoiceBasedon,
    // autoRejectInvoice,
    disableUserReportTime,
    invoiceLineItemApproval,
    projectInvoiceVarianceThreshold,
    requiresPMApproval,
    configuredInvoice,
  } = responseData;

  if (configuredInvoice) {
    const allowSelfReporting = !disableUserReportTime;

    let invoiceType = INVOICE_TYPES.USER_SELF_REPORTED;

    if (autoGenerateInvoice) {
      if (disableUserReportTime) {
        invoiceType = INVOICE_TYPES.SYSTEM_GENERATED;
      } else {
        invoiceType = INVOICE_TYPES.USER_SELF_REPORTED_AND_SYSTEM_APPROVED;
      }
    }

    return {
      invoiceType,
      baseAutoGenerationOn: autoGenerateInvoiceBasedon,
      allowSelfReporting,
      autoApproveInvoicesBy: autoApproveInvoiceBasedon,
      varianceThreshold: projectInvoiceVarianceThreshold,
      pmApprovalAtLineLevel: invoiceLineItemApproval && requiresPMApproval,
    };
  }

  return null;
}

export function fromLocalStateToDTO(localState = {}) {
  return {
    disableUserReportTime:
      localState.invoiceType === INVOICE_TYPES.SYSTEM_GENERATED,

    autoApproveInvoice: !!localState.autoApproveInvoicesBy,
    autoApproveInvoiceBasedon: localState.autoApproveInvoicesBy || null,

    autoGenerateInvoice: !!localState.baseAutoGenerationOn,
    autoGenerateInvoiceBasedon: localState.baseAutoGenerationOn || null,

    invoiceLineItemApproval: !!localState.pmApprovalAtLineLevel,
    requiresPMApproval: !!localState.pmApprovalAtLineLevel,

    projectInvoiceVarianceThreshold:
      typeof localState.varianceThreshold === 'number'
        ? localState.varianceThreshold / 100
        : null,

    autoRejectInvoice: false,
    configuredInvoice: true,
  };
}
