import { call, put, select, takeEvery } from 'redux-saga/effects';
import { undoLastAction, updateMany } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

function* updateManyPayRatesFn(action) {
  const {
    payload,
    meta: { onSuccess, onError, bypassUpdateSaga },
  } = action;

  if (bypassUpdateSaga) {
    return;
  }

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can update a Pay Rate.');
    return;
  }

  const patchPayload = payload.map((payRate) => {
    const { changes } = payRate;
    // We only update these params
    return {
      id: payRate.id,
      ...(changes.rate ? { rateValue: changes.rate } : {}),
      ...(typeof changes.enabled === 'boolean'
        ? { disabled: !changes.enabled }
        : {}),
      ...(changes.workdayTaskId
        ? { workdayTaskId: changes.workdayTaskId }
        : {}),
    };
  }, {});

  try {
    yield call(axios.patch, ENDPOINTS.V2.BULK_PAY_RATES(), patchPayload);

    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error when updating the selected payment rates. Please try again.',
    );

    yield put(undoLastAction());
  }
}

export default takeEvery(updateMany.type, updateManyPayRatesFn);
