import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { selectRegistrationAndQualification } from '../../../../../store/ducks/projectView';
import { patchProject } from '../../../../../store/ducks/project';
import RegQuaProjectViewForm from './RegQuaProjectViewForm';
import { registrationAndQualificationYupValidations } from '../../../../../components/ProjectForms/RegistrationAndQualificationForm';

function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {
    registrationProcessTemplateId: values.registrationProcessTemplate,
    qualificationProcessTemplateId: values.qualificationProcessTemplate,
  };

  dispatch(patchProject({ changes: patch }));
}

function mapStateToProps(state) {
  const registrationAndQualification = selectRegistrationAndQualification(
    state,
  );

  return {
    registrationProcessTemplate:
      registrationAndQualification.registrationProcessTemplate,
    qualificationProcessTemplate:
      registrationAndQualification.qualificationProcessTemplate,
  };
}

function mapPropsToValues(props) {
  const { registrationProcessTemplate, qualificationProcessTemplate } = props;

  return {
    registrationProcessTemplate,
    qualificationProcessTemplate,
  };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: registrationAndQualificationYupValidations,
  }),
)(RegQuaProjectViewForm);
