import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import shortid from 'shortid';
import * as yup from 'yup';
import {
  fetchAll as fetchUserThirdPartyList,
  saveUserThirdPartyList,
} from '../../../store/ducks/userThirdPartyList';
import { requiredMessage } from '../../ProjectSetup/Steps/shared';
import ContentFormGeneratorModal from './ContentFormGeneratorModal';
import { withErrorPopover } from '../../../components/ErrorPopover';

function handleSubmit(values, { props }) {
  const { projectId, userId, dispatch, closeModal, showError } = props;
  const { thirdParties } = values;

  const newThirdParties = thirdParties.map((thirdParty) => {
    return {
      ...thirdParty,
      projectId,
      userId,
    };
  });

  function onError(message) {
    showError(new Error(message));
  }

  dispatch(
    saveUserThirdPartyList({
      changes: { thirdParties: newThirdParties },
      thenPut: fetchUserThirdPartyList({ userId, projectId }),
      closeModal,
      onError,
    }),
  );
}

const mapPropsToValues = () => ({
  thirdParties: [{ key: shortid.generate() }],
});

const validationSchema = yup.object().shape({
  thirdParties: yup.array().of(
    yup.object().shape({
      firstName: yup.string().required(requiredMessage`First name`),
      lastName: yup.string().required(requiredMessage`Last name`),
      age: yup.number().required(requiredMessage`Age`),
      email: yup
        .string()
        .email('E-mail must be a valid e-mail address')
        .required(requiredMessage`Email`),
      gender: yup.string().required(requiredMessage`Gender`),
    }),
  ),
});

export default compose(
  withErrorPopover,
  connect(),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema,
  }),
)(ContentFormGeneratorModal);
