import { withFormik } from 'formik';
import ConsentFormStep from './ConsentFormStep';
import { setProcessAttributes } from '../../../../store/ducks/processBuilder';
import store from '../../../../store';

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  const { editingExistingDocument } = values;

  if (editingExistingDocument) {
    return;
  }

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      attributes: [],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

export default withFormik({
  handleSubmit,
})(ConsentFormStep);
