import React from 'react';
import PropTypes from 'prop-types';
import { Container, Popover, SuccessTitle, Img, Wrapper } from './styles';
import closeGreen from '../../assets/icons/close-green.svg';

export default function SuccessPopover(props) {
  const {
    title,
    successShown,
    hideSuccess,
    success,
    keepInDocumentFlow,
    fixedToFooter,
  } = props;

  if (!success || !successShown) {
    return null;
  }

  return (
    <Container
      fixedToFooter={fixedToFooter}
      keepInDocumentFlow={keepInDocumentFlow}
    >
      <Wrapper keepInDocumentFlow={keepInDocumentFlow}>
        <Popover>
          <SuccessTitle>{title || 'Success'}</SuccessTitle>
          {!keepInDocumentFlow && (
            <Img
              role="button"
              onClick={() => hideSuccess()}
              onKeyDown={({ keyCode }) => keyCode === 13 && hideSuccess()}
              tabIndex={0}
              src={closeGreen}
              alt="hide success message"
            />
          )}
          {success.message}
        </Popover>
      </Wrapper>
    </Container>
  );
}

SuccessPopover.propTypes = {
  title: PropTypes.string,
  successShown: PropTypes.bool.isRequired,
  hideSuccess: PropTypes.func.isRequired,
  success: PropTypes.oneOfType([PropTypes.instanceOf(Error), PropTypes.bool]),
  keepInDocumentFlow: PropTypes.bool,
  fixedToFooter: PropTypes.bool,
};

SuccessPopover.defaultProps = {
  title: '',
  success: null,
  keepInDocumentFlow: false,
  fixedToFooter: false,
};
