import PropTypes from 'prop-types';
import moment from 'moment';
import { useMemo } from 'react';

export default function SignedAtCell(props) {
  const {
    cell: { value },
  } = props;

  const formatted = useMemo(() => {
    if (!value) {
      return '';
    }

    const signedAt = moment(value);

    if (!signedAt.isValid()) {
      return 'Invalid date';
    }

    return signedAt.format('MMM Do[,] YYYY');
  }, [value]);

  return formatted;
}

SignedAtCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.string,
  }).isRequired,
};
