import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import ENDPOINTS from '../../../constants/endpoints';
import { ENTITY_METADATA } from '../options';
import createEntitiesSlices, { MUTATION_METHODS } from './engine';

export const EDITABLE_ENTITIES = {
  // HIRING_TARGETS: 'hiringTargets',
  // LOCALE_TENANTS: 'localeTenants',
  // PAY_RATES: 'payRates',
  PROJECT_RESOURCES: 'projectResources',
  THIRD_PARTIES: 'thirdParties',
  PROJECT_LEVEL_DATA_CONSENT_FORM: 'projectLevelDataConsentForm',
  ACCESS_HOLDER: 'accessHolder',
  MASTER_PROFILE_QUESTION: 'masterProfileQuestions',
};

const ENTITIES_CONFIG = {
  [EDITABLE_ENTITIES.PROJECT_RESOURCES]: {
    entityPK: 'id',
    url: ({ operation, primaryKey, action }) => {
      if (operation === 'edit' || operation === 'delete') {
        return ENDPOINTS.V2.PROJECT_RESOURCES({
          pathVariables: { id: action.meta.projectId, resourceId: primaryKey },
          query: {
            resourceType:
              action.meta.resourceType ===
              ENTITY_METADATA.projectResources.types.survey
                ? ENTITY_METADATA.projectResources.types.quiz
                : action.meta.resourceType,
          },
        });
      }

      return ENDPOINTS.V2.PROJECT_RESOURCES({
        pathVariables: { id: action.meta.projectId },
      });
    },
    saveMethod: ({ operation }) => {
      return operation === 'create' || operation === 'copy'
        ? MUTATION_METHODS.POST
        : MUTATION_METHODS.PUT;
    },
    transformBeforeSave: ({ payload, action }) => ({
      ...payload,
      type:
        payload.type === ENTITY_METADATA.projectResources.types.survey
          ? ENTITY_METADATA.projectResources.types.quiz
          : payload.type,
      projectId: action.meta.projectId,
      usageType: payload.usageType || 'TRAINING',
    }),
  },

  [EDITABLE_ENTITIES.THIRD_PARTIES]: {
    entityPK: 'id',
    saveMethod: MUTATION_METHODS.POST,
    url: () => ENDPOINTS.V2.THIRD_PARTIES(),
  },

  [EDITABLE_ENTITIES.PROJECT_LEVEL_DATA_CONSENT_FORM]: {
    url: ({ action }) => {
      if (action.meta.id) {
        return ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM_VERSION({
          pathVariables: { id: action.meta.id },
        });
      }

      return ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM();
    },
    saveMethod: MUTATION_METHODS.POST,
  },

  [EDITABLE_ENTITIES.ACCESS_HOLDER]: {
    url: (id) => ENDPOINTS.V2.ACCESS_HOLDER({ pathVariable: { id } }),
    saveMethod: MUTATION_METHODS.PUT,
  },

  [EDITABLE_ENTITIES.MASTER_PROFILE_QUESTION]: {
    url: (id) =>
      ENDPOINTS.V2.MASTER_PROFILE_QUESTIONS({ pathVariable: { id } }),
    saveMethod: MUTATION_METHODS.PUT,
  },
};

const { editor, saga, reducer } = createEntitiesSlices(ENTITIES_CONFIG);

export default reducer;
export { saga, editor };

export function useCloseEditor(entityPK) {
  const dispatch = useDispatch();

  return useCallback(() => {
    dispatch(editor.actions.discart(entityPK));
  }, [dispatch, entityPK]);
}
