import React, { useCallback, useMemo, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useSelector, useDispatch } from 'react-redux';
import Select from '../../../Select';
import { CancelButton } from './styles';
import { Section, Label } from '../../../../styles';
import FloatContainer, { FloatRight } from '../../../Float';
import Button from '../../../Button';
import Modal from '../../../Modal';
import CustomPayrateForm from './CustomPayrateForm';
import RadioButton from '../../../RadioButton';
import DefaultPayrateForm from './DefaultPayrateForm';
import { selectAll as selectAllHiringTargets } from '../../../../store/ducks/hiringTargets';
import * as defaultPayrates from '../../../../store/ducks/defaultPayrates';
import HiringTargetOption from '../HiringTargetOption';
import ErrorPopover, { ErrorPopoverProps } from '../../../ErrorPopover';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';

const payrateOptions = [
  {
    key: 'defaultPayrate',
    label: 'Default Pay Rates',
    'data-testid': 'payrates-modal--default-payrate-toggle',
  },
  {
    key: 'customPayrate',
    label: 'Custom Pay Rates',
    'data-testid': 'payrates-modal--custom-payrate-toggle',
  },
];

export default function PayratesModal(props) {
  const {
    modalProps,
    modalControls,
    values,
    handleChange,
    handleBlur,
    setFieldValue,
    submitForm,
    resetForm,
    errors,
    touched,
    submitCount,
    businessUnit,
    rateType,
    errorPopoverProps,
    isSubmitting,
  } = props;

  const { mode = 'add' } = values;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.fluencies));
    dispatch(fetch(ENTITIES.localeLanguages));
    dispatch(fetch(ENTITIES.countries));
  }, [dispatch]);

  const allHiringTargets = useSelector(selectAllHiringTargets);

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const localeLanguages = useSelector(
    selectors.createSelectSliceFor(ENTITIES.localeLanguages),
  );

  const dialectsLoading = useSelector(
    selectors.createSelectLoadingFor(ENTITIES.localeDialects),
  );

  const dialectsError = useSelector(
    selectors.createSelectErrorFor(ENTITIES.localeDialects),
  );

  const error = countries.error || localeLanguages.error || dialectsError;

  const hiringTargetsOptions = useMemo(
    () =>
      allHiringTargets.map((hiringTarget) => ({
        value: hiringTarget.id,
        label: (
          <HiringTargetOption
            restrictToCountry={hiringTarget.restrictToCountry}
            localeLanguage={hiringTarget.localeLanguage}
            localeDialect={hiringTarget.localeDialect}
            localeLanguageTo={hiringTarget.localeLanguageTo}
            localeDialectTo={hiringTarget.localeDialectTo}
          />
        ),
      })),
    [allHiringTargets],
  );

  const saveDisabled = useMemo(() => {
    const { customPayrate, selectedPayrates } = values || {};
    return (
      mode === 'add' &&
      !customPayrate &&
      (!selectedPayrates || !selectedPayrates.length)
    );
  }, [mode, values]);

  const title = useMemo(() => {
    if (mode === 'add') {
      return 'Add';
    }

    return mode === 'edit' ? 'Edit' : 'Copy';
  }, [mode]);

  const hiringTargetNotFoundError = useMemo(() => {
    return mode === 'edit' && !values.hiringTarget
      ? `Hiring target not found. Include target on "Recruitment" step.`
      : null;
  }, [mode, values]);

  const handleInputChange = useCallback(
    (index) => {
      setFieldValue('customPayrate', index);
      setFieldValue('payrates', null);
    },
    [setFieldValue],
  );

  const handleHiringTargetChange = useCallback(
    ({ value }) => {
      dispatch(
        defaultPayrates.fetchAll({
          hiringTarget: value,
          businessUnit,
          rateType,
        }),
      );
      setFieldValue('hiringTarget', value);
    },
    [businessUnit, dispatch, rateType, setFieldValue],
  );

  const onExited = () => {
    resetForm();
    return modalProps.onExited;
  };

  return (
    <Modal
      open={modalProps.open}
      onClose={modalProps.onClose}
      onExited={onExited}
      title={`${title} Pay Rate`}
      closeOnOverlayClick={!isSubmitting}
      closeOnEsc={!isSubmitting}
    >
      <Section>
        <Row>
          <RadioButton
            checkedIndex={values.customPayrate}
            handleChange={handleInputChange}
            options={payrateOptions}
            isDisabled={values.id || mode === 'copy'}
            tooltips={[
              <>
                Default payrates are rates previously set for each market/locale
                on our platform, including individual US states.
                <br />
                You can use default payrates to ensure your project rates are
                compliant to locale requirements and laws (e.g. minimum wage).
                Projects using default payrates will be automatically refreshed
                if the original rate is updated.
                <br />
                You can also create a custom payrate from a default payrate
                using the copy function on the payrates table.
              </>,
            ]}
          />
        </Row>
      </Section>

      <Section>
        <Row>
          <Col>
            <Label htmlFor="hiringTarget">Hiring Target</Label>
            <Select
              value={values.hiringTarget}
              name="hiringTarget"
              onChange={handleHiringTargetChange}
              options={hiringTargetsOptions}
              loading={
                countries.loading || localeLanguages.loading || dialectsLoading
              }
              error={
                error ||
                (submitCount >= 1 && errors.hiringTarget) ||
                hiringTargetNotFoundError
              }
              placeholder="Select hiring target"
              disabled={!!values.id}
              data-testid="payrates-modal--select-hiring-target"
            />
          </Col>
        </Row>
      </Section>

      {values.customPayrate || mode === 'edit' ? (
        <CustomPayrateForm
          values={values}
          handleChange={handleChange}
          touched={touched}
          setFieldValue={setFieldValue}
          handleBlur={handleBlur}
          errors={errors}
          hiringTarget={values.hiringTarget}
          rateType={rateType}
          submitCount={submitCount}
        />
      ) : (
        values.hiringTarget && (
          <DefaultPayrateForm
            setFieldValue={setFieldValue}
            hiringTarget={values.hiringTarget}
          />
        )
      )}

      <ErrorPopover
        title="Failed to save Hiring Target"
        {...errorPopoverProps}
      />

      <FloatContainer>
        <FloatRight>
          <CancelButton
            disabled={isSubmitting}
            type="secondary"
            onClick={modalControls.closeModal}
          >
            Cancel
          </CancelButton>

          <Button
            loading={isSubmitting}
            type="primary"
            marginLeft={16}
            onClick={submitForm}
            disabled={saveDisabled}
            data-testid="payrates-modal--save-button"
          >
            Save
          </Button>
        </FloatRight>
      </FloatContainer>
    </Modal>
  );
}

PayratesModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  resetForm: PropTypes.func.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  touched: PropTypes.objectOf(PropTypes.boolean).isRequired,
  submitCount: PropTypes.number.isRequired,
  businessUnit: PropTypes.string,
  rateType: PropTypes.string,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
};

PayratesModal.defaultProps = {
  businessUnit: '',
  rateType: '',
};
