import { createSlice } from '@reduxjs/toolkit';
import { get, first } from 'lodash';

const key = 'experimentGroups';

const slice = createSlice({
  name: key,

  initialState: {
    data: {},
    loading: false,
    fetchError: null,
    saving: 0,
    accessHolderSaveError: null,
    experimentGroupsSaveError: null,
  },

  reducers: {
    fetchExperimentGroups: (state) => {
      state.loading = true;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;
    },

    fetchExperimentGroupsSuccess: (state, { payload }) => {
      state.loading = false;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;
      state.data[payload.experimentId] = payload.data;
    },

    fetchExperimentGroupsError: (state, action) => {
      state.loading = false;
      state.experimentGroupsError = action.payload;
      state.experimentGroupsSaveError = null;
    },

    fetchAccessHolder: (state) => {
      state.loading = true;
      state.accessHolderError = null;
      state.accessHolderSaveError = null;
    },

    fetchAccessHolderSuccess: (state, action) => {
      state.loading = false;
      state.accessHolderError = null;
      state.accessHolderSaveError = null;
      state.accessHolder = action.payload;
    },

    fetchAccessHolderError: (state, action) => {
      state.loading = false;
      state.accessHolderError = action.payload;
      state.accessHolderSaveError = null;
    },

    saveExperimentGroups: (state) => {
      state.saving = true;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;
    },

    saveExperimentGroupsSuccess: (state, action) => {
      state.saving = false;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;
      state.data = action.payload;
    },

    saveExperimentGroupsError: (state, action) => {
      state.saving = false;
      state.experimentGroupsError = action.payload;
      state.experimentGroupsSaveError = action.payload;
    },

    saveAccessHolder: (state) => {
      state.saving = true;
      state.accessHolderSaveError = null;
      state.accessHolderError = null;
    },

    saveAccessHolderSuccess: (state) => {
      state.saving = false;
      state.accessHolderSaveError = null;
      state.accessHolderError = null;
    },

    saveAccessHolderError: (state, action) => {
      state.saving = false;
      state.accessHolderSaveError = action.payload;
      state.accessHolderError = action.payload;
    },

    toggleExperimentGroup: (state) => {
      state.saving = true;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;
    },

    toggleExperimentGroupSuccess: (state, action) => {
      state.saving = false;
      state.experimentGroupsError = null;
      state.experimentGroupsSaveError = null;

      const updatedIndex = state.data.findIndex(
        (experiment) => experiment.id === action.payload.id,
      );

      if (updatedIndex !== -1) {
        state.data = [
          ...state.data.slice(0, updatedIndex),
          action.payload,
          state.data.slice(updatedIndex + 1),
        ];
      }
    },

    toggleExperimentGroupError: (state, action) => {
      state.saving = false;
      state.experimentGroupsError = action.payload;
      state.experimentGroupsSaveError = action.payload;
    },
  },
});

const {
  actions: {
    fetchExperimentGroups,
    fetchExperimentGroupsSuccess,
    fetchExperimentGroupsError,
    fetchAccessHolder,
    fetchAccessHolderSuccess,
    fetchAccessHolderError,
    saveExperimentGroups,
    saveExperimentGroupsSuccess,
    saveExperimentGroupsError,
    saveAccessHolder,
    saveAccessHolderSuccess,
    saveAccessHolderError,
    toggleExperimentGroup,
    toggleExperimentGroupSuccess,
    toggleExperimentGroupError,
  },
  reducer,
} = slice;

reducer.key = key;

export default reducer;

export {
  fetchExperimentGroups,
  fetchExperimentGroupsSuccess,
  fetchExperimentGroupsError,
  fetchAccessHolder,
  fetchAccessHolderSuccess,
  fetchAccessHolderError,
  saveExperimentGroups,
  saveExperimentGroupsSuccess,
  saveExperimentGroupsError,
  saveAccessHolder,
  saveAccessHolderSuccess,
  saveAccessHolderError,
  toggleExperimentGroup,
  toggleExperimentGroupSuccess,
  toggleExperimentGroupError,
};

export function selectExperimentGroupsSlice(state) {
  return state[reducer.key];
}

export function selectData(state) {
  return get(selectExperimentGroupsSlice(state), 'data', []);
}

export function selectAccessHolder(state) {
  return get(selectExperimentGroupsSlice(state), 'accessHolder', []);
}

export function selectLoading(state) {
  return get(selectExperimentGroupsSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectExperimentGroupsSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectExperimentGroupsSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  const experimentGroupsSaveError = get(
    selectExperimentGroupsSlice(state),
    'experimentGroupsSaveError',
    null,
  );

  const accessHolderSaveError = get(
    selectExperimentGroupsSlice(state),
    'accessHolderSaveError',
    null,
  );

  return accessHolderSaveError || experimentGroupsSaveError || null;
}

export function hasExperimentGroups(state) {
  const experimentGroups = selectData(state);
  const accessHolder = first(selectAccessHolder(state));

  const groupByExperimentId = accessHolder
    ? experimentGroups[accessHolder.id]
    : [];

  const experimentGroupsLength = groupByExperimentId
    ? groupByExperimentId.length
    : 0;

  return !!experimentGroupsLength;
}
