import React from 'react';
import {
  Radio,
  Label,
  Text,
  Overlay,
  InsideCircle,
  TooltipWrapper,
} from './styles';
import InformationTooltip from '../ProcessBuilder/InformationTooltip';

export default function RadioButton(props) {
  const { options, handleChange, checkedIndex, isDisabled, tooltips } = props;

  return options.map((option, index) => (
    <Label
      isDisabled={isDisabled}
      key={option.key}
      hasTooltip={!!tooltips[index]}
    >
      <Radio
        checked={checkedIndex === index}
        onChange={() => !isDisabled && handleChange(index)}
        data-testid={option['data-testid']}
      />
      <Overlay checked={checkedIndex === index} className="overlay">
        <InsideCircle checked={checkedIndex === index} className="icon" />
      </Overlay>
      <Text isDisabled={isDisabled} bold>
        {option.label}
      </Text>
      {tooltips[index] && (
        <TooltipWrapper>
          <InformationTooltip title={tooltips[index]} />
        </TooltipWrapper>
      )}
    </Label>
  ));
}
