import { select, put, call, takeEvery } from 'redux-saga/effects';
import get from 'lodash/get';
import {
  setProcessError,
  setProcessDetails,
  selectProcessDetails,
  selectProcessModules,
  setProcessSaving,
  saveOrCreateProcess,
  setProcessModules,
} from '.';
import { profileBuilderAPI } from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function getPayload(
  { processID, processName, processDescription },
  processModules,
) {
  const profileQuestions = processModules
    .map((module) => {
      const attributes = get(module, ['attributes', 0], {});
      const answers = get(attributes, 'answers', {});
      const enabled = get(attributes, 'enabled', {});
      const firstOption = get(attributes, 'firstOption', {});
      const secondOption = get(attributes, 'secondOption', {});
      const profileQuestionIds = get(attributes, 'profileQuestionId', {});

      return Object.keys(answers).map((masterProfileQuestionId) => {
        const profileQuestionId = profileQuestionIds[masterProfileQuestionId];
        const selectedFirstOption = get(
          firstOption[masterProfileQuestionId],
          'value',
          null,
        );
        const selectedSecondOption = get(
          secondOption[masterProfileQuestionId],
          'value',
          null,
        );

        const firstAction =
          selectedFirstOption === 'passThrough' ? 'PASS_THROUGH' : 'REJECT';
        const secondAction =
          selectedSecondOption === 'passThrough' ? 'PASS_THROUGH' : 'REJECT';

        const currentAnswer = answers[masterProfileQuestionId]
          ? answers[masterProfileQuestionId]
          : [];

        const countryOrLanguage = currentAnswer.filter((answer) =>
          ['COUNTRY_LIST', 'LANGUAGE_LIST'].includes(answer.questionType),
        );

        const datePicker = currentAnswer.filter((answer) => answer.lb);

        let profileQuestionAnswerList = [];
        const customAnswer = null;

        if (datePicker.length) {
          let masterProfileQuestionAnswerId = null;
          profileQuestionAnswerList = currentAnswer.map((answer) => {
            masterProfileQuestionAnswerId =
              answer.id || masterProfileQuestionAnswerId;

            return {
              id: answer.profileAnswerId,
              masterProfileQuestionAnswerId,
              action: firstAction,
              customAnswer: {
                type: 'DATE_PICKER',
                lb: answer.lb || null,
                ub: answer.ub || null,
                logic: answer.logic,
              },
            };
          });
        } else if (countryOrLanguage.length) {
          const singleAnswer = get(currentAnswer, 0, null);

          if (singleAnswer) {
            const action = singleAnswer ? firstAction : secondAction;
            const value = currentAnswer.map((answer) => answer.value).join(',');

            profileQuestionAnswerList = [
              {
                id: singleAnswer.profileAnswerId,
                masterProfileQuestionAnswerId: singleAnswer.id,
                action,
                ...(value && {
                  customAnswer: {
                    type: singleAnswer.questionType,
                    value,
                  },
                }),
              },
            ];
          }
        } else {
          profileQuestionAnswerList = currentAnswer.map((answer) => {
            let action = answer.value ? firstAction : secondAction;
            const yesOrNoType =
              currentAnswer.length === 2 &&
              ['Yes', 'No'].includes(answer.answer);

            if (yesOrNoType) {
              action = answer.answer === 'Yes' ? firstAction : secondAction;
            }

            const openTexts = ['OPEN_TEXT', 'OPEN_TEXT_PARAGRAPH'];

            if (openTexts.includes(answer.questionType)) {
              action = 'PASS_THROUGH';
            }

            return {
              id: answer.profileAnswerId,
              masterProfileQuestionAnswerId: answer.id,
              action,
              customAnswer,
            };
          });
        }

        return {
          id: profileQuestionId,
          masterProfileQuestionId,
          disabledSubQuestion: !enabled[masterProfileQuestionId],
          allChecked: firstAction === 'PASS_THROUGH',
          allUnchecked: secondAction === 'PASS_THROUGH',
          profileQuestionAnswerList,
        };
      });
    })
    .flat();

  return {
    id: processID,
    name: processName,
    description: processDescription,
    profileQuestions,
  };
}

function* saveOrCreateTargetBuilderSaga(action) {
  const processDetails = yield select(selectProcessDetails);
  const processModules = yield select(selectProcessModules);

  const payload = getPayload(processDetails, processModules);

  try {
    let endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER();

    if (processDetails.processID) {
      endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER({
        pathVariables: {
          id: processDetails.processID,
        },
      });
    }

    const response = yield call(
      processDetails.processID ? profileBuilderAPI.put : profileBuilderAPI.post,
      endpoint,
      payload,
    );

    yield put(
      setProcessModules(processModules, {
        isPristine: true,
      }),
    );

    if (!processDetails.processID) {
      yield put(
        setProcessDetails(
          {
            processID: response.data.id,
          },
          { preventFetch: true },
        ),
      );
    }

    if (
      action &&
      action.meta &&
      typeof action.meta.onProcessChange === 'function'
    ) {
      action.meta.onProcessChange(
        processDetails.processType,
        response.data.id,
        !processDetails.processID,
      );
    }
  } catch (error) {
    yield put(setProcessError(error));

    if (action && action.meta) {
      if (typeof action.meta.onError === 'function') {
        action.meta.onError(error);
      }
    }
  } finally {
    yield put(setProcessSaving(false));
  }
}

export default takeEvery(
  saveOrCreateProcess.type,
  saveOrCreateTargetBuilderSaga,
);
