import React from 'react';
import { useHistory } from 'react-router-dom';
import { noop } from 'lodash';
import { Row, Col } from 'styled-bootstrap-grid';
import { ReactComponent as Elipsis } from '../../../../assets/icons/elipsis.svg';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
  PopoverTrigger,
} from '../../../../components/Popover';
import close from '../../../../assets/icons/close.svg';
import chart from '../../../../assets/icons/chart.svg';
import copy from '../../../../assets/icons/copy.svg';
import eye from '../../../../assets/icons/eye.svg';
import settingsGrey from '../../../../assets/icons/settings-grey.svg';
import userProjectPage from '../../../../assets/icons/user-project-page.svg';
import Disabled from '../../../../components/Disabled';
import legacyIntegration from '../../../../helpers/legacy-integration';
import Modal from '../../../../components/Modal';
import FloatContainer, { FloatRight } from '../../../../components/Float';
import Button from '../../../../components/Button';
import CancelButton from '../styles';
import { Section } from '../../../../styles';
import { CellPropTypes } from '../helpers';

export default function ActionCell(props) {
  const history = useHistory();

  const { modalProps, handleOpenCloneModal, row } = props;

  const {
    original: { id = '', customer = {}, disabled, status = {}, trackerId },
  } = row;

  const tooltipContent = (
    <>
      <Disabled when message="This will be available soon." focusOffset={1}>
        <PopoverItem onClick={noop} onKeyDown={noop} role="button" tabIndex={0}>
          <PopoverIcon src={userProjectPage} alt="User project icon" />
          <PopoverText>User Project Page</PopoverText>
        </PopoverItem>
      </Disabled>

      {disabled && status.value === 'DRAFT' && (
        <PopoverItem
          onClick={() =>
            legacyIntegration.informLoadProjectSetup(parseInt(id, 10))
          }
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={settingsGrey} alt="Project icon" />
          <PopoverText>Resume Project Setup</PopoverText>
        </PopoverItem>
      )}

      <PopoverItem
        onClick={() =>
          legacyIntegration.informViewProjectExperiments(parseInt(id, 10))
        }
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={eye} alt="Eye icon" />
        <PopoverText>View Experiments</PopoverText>
      </PopoverItem>

      <PopoverItem
        onClick={() =>
          legacyIntegration.informOpenTracker(parseInt(trackerId, 10))
        }
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={chart} alt="chart icon" />
        <PopoverText>Open Tracker</PopoverText>
      </PopoverItem>

      <PopoverItem
        onClick={() => history.push(`/clone/${parseInt(trackerId, 10)}`)}
        onKeyDown={noop}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={copy} alt="Copy icon" />
        <PopoverText>Clone Project</PopoverText>
      </PopoverItem>

      <Disabled when message="This option is not available." focusOffset={1}>
        <PopoverItem
          onClick={() =>
            legacyIntegration.informProjectDeletion(parseInt(id, 10))
          }
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={close} alt="Delete icon" />
          <PopoverText>Delete</PopoverText>
        </PopoverItem>
      </Disabled>
    </>
  );

  return (
    <>
      <Popover content={tooltipContent} placement="bottom-end">
        {(shown) => (
          <PopoverTrigger
            style={{ position: 'relative', top: 2 }}
            isShown={shown}
          >
            <Elipsis />
          </PopoverTrigger>
        )}
      </Popover>
      <Modal
        open={modalProps.open}
        onClose={modalProps.onClose}
        onExited={modalProps.onExited}
        title="Clone Project"
        styles={{
          modal: {
            marginTop: '10vh',
          },
        }}
      >
        <Section marginBottom="56px">
          <Row>
            <Col col={10}>
              <span>
                A new pre-filled project setup experience will be created for
                you based on this project configuration. <br />
                <br /> Please make sure to review all steps before creating the
                new project.
              </span>
            </Col>
          </Row>
        </Section>

        <FloatContainer>
          <FloatRight>
            <CancelButton type="secondary" onClick={modalProps.onClose}>
              Cancel
            </CancelButton>
            <Button
              onClick={() => {
                legacyIntegration.informCloneProject(
                  parseInt(id, 10),
                  parseInt(customer.id, 10) || null,
                );
              }}
            >
              Proceed
            </Button>
          </FloatRight>
        </FloatContainer>
      </Modal>
    </>
  );
}

ActionCell.propTypes = CellPropTypes;
