import { call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

function* updateLocaleTenantFn(action) {
  const {
    payload: { changes },
    meta: { onSuccess, onError, bypassUpdateSaga },
  } = action;

  if (bypassUpdateSaga) {
    return;
  }

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can update a Locale Tenant.');

    return;
  }

  try {
    yield call(
      axios.put,
      ENDPOINTS.V2.LOCALE_TENANTS({
        pathVariables: {
          id: changes.id,
        },
      }),
      {
        country: changes.country,
        tenantId: changes.tenant,
        projectId: project.id,
      },
    );

    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error updating a Locale Tenant. Please try again.',
    );

    yield put(undoLastAction());
  }
}

export default takeEvery(updateOne.type, updateLocaleTenantFn);
