import { connect } from 'react-redux';
import RegistrationAndQualification from './RegistrationAndQualification';
import * as registrationProcesses from '../../../../../../store/ducks/registrationProcesses';
import * as qualificationProcesses from '../../../../../../store/ducks/qualificationProcesses';
import { findById } from '../../../../../../helpers/find';
import { selectRegistrationAndQualification } from '../../../../../../store/ducks/project';

function mapStateToProps(state) {
  const registrationAndQualification = selectRegistrationAndQualification(
    state,
  );

  const registrationData = registrationProcesses.selectOptions(state);
  const qualificationData = qualificationProcesses.selectOptions(state);

  const {
    registrationProcessTemplate,
    qualificationProcessTemplate,
  } = registrationAndQualification;

  return {
    registrationProcessTemplate: (
      findById(registrationData, registrationProcessTemplate) || {}
    ).name,

    qualificationProcessTemplate: (
      findById(qualificationData, qualificationProcessTemplate) || {}
    ).name,
  };
}

export default connect(mapStateToProps)(RegistrationAndQualification);
