import React from 'react';
import PropTypes from 'prop-types';
import BottomControls from '../BottomControls';
import { DEMOGRAPHIC_TYPE } from '../../../../../../store/ducks/projectDemographicRequirements';
import DemographicsGroupTable from './DemographicsGroupTable';
import LabeledSwitch from '../../../../../LabeledSwitch';
import AggregatedRequirements from './AggregatedDemographicsTable';
import ErrorPopover, { ErrorPopoverProps } from '../../../../../ErrorPopover';

export default function DemographicData(props) {
  const {
    submitForm,
    goBack,
    requirementHeaders,
    demographicType,
    values,
    setRequirementsByGroupValue,
    setAggregatedRequirementsValue,
    distributeEvenly,
    isSubmitting,
    errorPopoverProps,
  } = props;

  return (
    <div>
      {demographicType === DEMOGRAPHIC_TYPE.BY_GROUP ? (
        requirementHeaders.map((rh) => (
          <DemographicsGroupTable
            key={rh.id}
            tableName={rh.name}
            requirementID={rh.id}
            groups={rh.groups}
            value={values.requirementsByGroup}
            setRequirementsByGroupValue={setRequirementsByGroupValue}
          />
        ))
      ) : (
        <AggregatedRequirements
          value={values.aggregatedRequirements}
          setAggregatedRequirementsValue={setAggregatedRequirementsValue}
        />
      )}
      <LabeledSwitch
        name="distributedEvenly"
        onChange={distributeEvenly}
        value={values.distributedEvenly}
        label="DISTRIBUTE EVENLY"
        data-testid="demographics-modal--distribute-evenly"
      />

      <ErrorPopover
        title="Failed to save Demographic Requirements"
        {...errorPopoverProps}
      />

      <BottomControls
        currentStep={2}
        nextLabel="Save Requirements"
        nextLoading={isSubmitting}
        onNext={submitForm}
        backLabel="Back"
        onBack={goBack}
      />
    </div>
  );
}

DemographicData.propTypes = {
  submitForm: PropTypes.func.isRequired,
  goBack: PropTypes.func.isRequired,
  requirementHeaders: PropTypes.arrayOf(PropTypes.object).isRequired,
  demographicType: PropTypes.oneOf(Object.values(DEMOGRAPHIC_TYPE)).isRequired,
  values: PropTypes.shape({
    requirementsByGroup: PropTypes.array,
    aggregatedRequirements: PropTypes.object,
    distributedEvenly: PropTypes.bool,
  }).isRequired,
  setRequirementsByGroupValue: PropTypes.func.isRequired,
  setAggregatedRequirementsValue: PropTypes.func.isRequired,
  distributeEvenly: PropTypes.func.isRequired,
  submittionError: PropTypes.instanceOf(Error),
  isSubmitting: PropTypes.bool.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
};

DemographicData.defaultProps = {
  submittionError: null,
};
