import { useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { compose } from 'recompose';
import { withFormik, useFormikContext } from 'formik';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import * as yup from 'yup';
import AdvancedFilters from './AdvancedFilters';
import { ENTITIES, selectors, fetch } from '../../../../../store/ducks/options';

const fromToTitle = {
  languages: 'Language',
  dialects: 'Dialect',
  countries: 'Countries',
  ipCheckPassed: 'IP check',
  ipCheckFailed: 'IP check',
  ipFailureMismatch: 'IP failure reason',
  ipFailureVPN: 'IP failure reason',
  similarityScoreMin: 'Similarity score',
  similarityScoreMax: 'Similarity score',
  maliciousnessLevelHigh: 'Maliciousness level',
  maliciousnessLevelMedium: 'Maliciousness level',
  maliciousnessLevelLow: 'Maliciousness level',
  ageGroupMin: 'Age group',
  ageGroupMax: 'Age group',
  genderFemale: 'Gender',
  genderMale: 'Gender',
  directPay: 'Extra filters',
  optInA9: 'Extra filters',
  mobileDeviceOS: 'Mobile device OS',
};

const fromToDescription = {
  ipCheckPassed: 'Passed',
  ipCheckFailed: 'Failed',
  ipFailureMismatch: 'IP Mismatch',
  ipFailureVPN: 'VPN Provider',
  maliciousnessLevelHigh: 'High',
  maliciousnessLevelMedium: 'Medium',
  maliciousnessLevelLow: 'Low',
  genderFemale: 'Female',
  genderMale: 'Male',
  directPay: 'Direct Pay',
  optInA9: 'A9 Opt In',
};

function updateFilterSummary(values, props) {
  const { handleFilterSummary } = props;

  const filterKeys = Object.keys(values);

  const summary = {};

  filterKeys.forEach((key) => {
    if (values[key]) {
      const title = fromToTitle[key];
      let description;

      if (typeof values[key] === 'object') {
        description = values[key].map((value) => value.label).join(', ');
      } else {
        description = fromToDescription[key]
          ? fromToDescription[key]
          : values[key];
      }

      if (summary[title]) {
        description = `${summary[title]}, ${description}`;
      }

      summary[title] = description;
    }
  });

  const titles = Object.keys(summary);
  const descriptions = Object.values(summary);

  const finalSummary = titles.map((title, i) => `${title}: ${descriptions[i]}`);

  handleFilterSummary(finalSummary.join('<br/>'));
}

function updateSelectedFiltersCount(values, props) {
  const { handleFilterCountChange } = props;
  const filterValues = Object.values(values);
  handleFilterCountChange(filterValues.filter((value) => !!value).length);

  updateFilterSummary(values, props);
}

function mapPropsToValues() {
  return {};
}

function useHooksAsProps(outerProps) {
  const dispatch = useDispatch();
  const { setFieldValue, resetForm, values } = useFormikContext();

  const {
    resetFilters,
    showAdvancedFilters,
    handleFilterCountChange,
    handleResetFilters,
    handleAdvancedFiltersChange,
  } = outerProps;

  // Fetching data
  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.localeLanguages));
  }, [dispatch]);

  useEffect(() => {
    if (values.languages) {
      dispatch(
        fetch({
          optionsFor: ENTITIES.localeDialects,
          nestedKey: get(values.languages, [0, 'value'], null),
        }),
      );
    }
  }, [dispatch, values.languages]);

  useEffect(() => {
    if (resetFilters) {
      resetForm();
      handleResetFilters(false);
      handleFilterCountChange(0);
    }
  }, [handleFilterCountChange, handleResetFilters, resetFilters, resetForm]);

  useEffect(() => {
    if (!showAdvancedFilters) {
      updateSelectedFiltersCount(values, outerProps);
    }
  });

  useEffect(() => {
    handleAdvancedFiltersChange(values);
  }, [handleAdvancedFiltersChange, values]);

  const countryList = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const localeLanguages = useSelector(
    selectors.createSelectSliceFor(ENTITIES.localeLanguages),
  );

  const localeDialects = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.localeDialects,
      get(values.languages, [0, 'value'], null) || 'no-language',
    ),
  );

  const countriesOptions = useMemo(
    () => [
      { value: '*', label: 'All Countries' },
      ...countryList.data.filter((candidate) => candidate.value !== '*'),
    ],
    [countryList],
  );

  const localeLanguagesOptions = useMemo(
    () => [
      ...localeLanguages.data.filter((candidate) => candidate.value !== '*'),
    ],
    [localeLanguages],
  );

  const localeDialectsOptions = useMemo(() => {
    return localeDialects.data.filter((locale) => locale.value !== '*');
  }, [localeDialects]);

  const countriesTags = useMemo(() => {
    const { countries = [] } = values;

    if (countries.find((candidate) => candidate.value === '*')) {
      return [
        {
          value: '*',
          label: 'All countries',
        },
      ];
    }

    return countries;
  }, [values]);

  const localeLanguagesTags = useMemo(() => {
    const { languages = [] } = values;
    return languages;
  }, [values]);

  const localeDialectsTags = useMemo(() => {
    const { dialects = [] } = values;
    return dialects;
  }, [values]);

  // Functions
  const calculateMultiValueCount = useCallback((value) => value.length, []);

  const handleCountriesChange = useCallback(
    (nextValue) => {
      if (nextValue.some((country) => country.value === '*')) {
        setFieldValue('countries', [
          {
            value: '*',
            label: 'All countries',
          },
        ]);
      } else {
        setFieldValue('countries', nextValue);
      }
    },
    [setFieldValue],
  );

  const handleLocaleLanguageChange = useCallback(
    (nextValue) => setFieldValue('languages', nextValue),
    [setFieldValue],
  );

  const handleLocaleDialectChange = useCallback(
    (nextValue) => setFieldValue('dialects', nextValue),
    [setFieldValue],
  );

  const handleCountryRemoval = useCallback(
    (value) => {
      const { countries = [] } = values;

      if (value === '*') {
        setFieldValue('countries', []);
      } else {
        setFieldValue(
          'countries',
          countries.filter((item) => item.value !== value),
        );
      }
    },
    [setFieldValue, values],
  );

  const handleLocaleLanguageRemoval = useCallback(
    (value) => {
      const { languages = [] } = values;

      setFieldValue(
        'languages',
        languages.filter((item) => item.value !== value),
      );
    },
    [setFieldValue, values],
  );

  const handleLocaleDialectRemoval = useCallback(
    (value) => {
      const { dialects = [] } = values;

      setFieldValue(
        'dialects',
        dialects.filter((item) => item.value !== value),
      );
    },
    [setFieldValue, values],
  );

  return {
    countryList,
    countriesOptions,
    localeDialects,
    localeDialectsOptions,
    localeLanguages,
    localeLanguagesOptions,
    countriesTags,
    localeLanguagesTags,
    localeDialectsTags,
    handleCountriesChange,
    handleCountryRemoval,
    handleLocaleLanguageChange,
    handleLocaleDialectChange,
    handleLocaleLanguageRemoval,
    handleLocaleDialectRemoval,
    calculateMultiValueCount,
  };
}

const validationSchema = yup.object().shape({});

export default compose(
  withFormik({
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(AdvancedFilters);
