import React, { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import noop from 'lodash/noop';
import {
  fetchPaginated,
  PAGINATED_ENTITIES,
  selectors as paginatedSelectors,
  nextPage,
  resetFor,
  setPageSize,
  setFilters,
  SORT_DIRECTIONS,
} from '../../store/ducks/paginated';
import Table from '../../components/Table';
import ProjectCell from './Cells/ProjectCell';
import AliasCell from './Cells/AliasCell';
import CustomerCell from './Cells/CustomerCell';
import StatusCell from './Cells/StatusCell';
import ActionsCell from './Cells/ActionCell';
import Pagination from '../../components/Pagination';
import { TableContainer, ProjectsContainer } from './styles';
import Filters from './Filters';
import { updateParentHeight } from '../../services/postMessage';
import NoEntries from '../../components/NoEntries';

const columns = [
  {
    Header: 'ID',
    accessor: 'id',
    key: 'id',
    width: 45,
  },
  {
    Header: 'PROJECT',
    accessor: 'name',
    key: 'name',
    Cell: ProjectCell,
    width: 560,
  },
  {
    Header: 'TYPE OF TASK',
    accessor: 'workType',
    key: 'workType',
    Cell: AliasCell,
  },
  {
    Header: 'CUSTOMER',
    accessor: 'customer',
    key: 'customer',
    Cell: CustomerCell,
  },
  {
    Header: 'STATUS',
    accessor: 'status',
    key: 'status',
    Cell: StatusCell,
    style: {
      maxWidth: '81px',
    },
  },
  {
    Header: '',
    accessor: 'action',
    key: 'action',
    Cell: ActionsCell,
    disableSortBy: true,
    style: {
      textAlign: 'right',
      paddingRight: '16px',
      maxWidth: '83px',
    },
  },
];

export default function Projects() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchPaginated(PAGINATED_ENTITIES.projects));

    return () => {
      dispatch(resetFor(PAGINATED_ENTITIES.projects));
    };
  }, [dispatch]);

  const projects = useSelector(
    paginatedSelectors.createSelectDataFor(PAGINATED_ENTITIES.projects),
  );

  const projectsLoading = useSelector(
    paginatedSelectors.createSelectLoadingFor(PAGINATED_ENTITIES.projects),
  );

  const pagination = useSelector(
    paginatedSelectors.createSelectPaginationInfoFor(
      PAGINATED_ENTITIES.projects,
    ),
  );

  const handlePageChange = useCallback(
    (page) => {
      window.scrollTo(0, 0);
      dispatch(
        nextPage({
          forKey: PAGINATED_ENTITIES.projects,
          nextPage: page,
        }),
      );
    },
    [dispatch],
  );

  const handlePageSizeChange = useCallback(
    (size) => {
      window.scrollTo(0, 0);
      dispatch(
        setPageSize({
          forKey: PAGINATED_ENTITIES.projects,
          pageSize: size,
        }),
      );
    },
    [dispatch],
  );

  const handleSortChange = useCallback(
    ([sortedCol]) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.projects,
          filters: sortedCol
            ? {
                sort: sortedCol.id,
                direction: sortedCol.desc
                  ? SORT_DIRECTIONS.DESC
                  : SORT_DIRECTIONS.ASC,
              }
            : {
                sort: undefined,
                direction: undefined,
              },
        }),
      );
    },
    [dispatch],
  );

  updateParentHeight();

  return (
    <ProjectsContainer>
      <Filters />

      <TableContainer>
        {projects.length > 0 ? (
          <Table
            columns={columns}
            data={projects}
            renderExpandedComponent={noop}
            onSort={handleSortChange}
          />
        ) : (
          <NoEntries
            label={projectsLoading ? 'Loading...' : 'No Results Found'}
          />
        )}
      </TableContainer>

      <Pagination
        current={pagination.currentPage}
        pageSize={pagination.pageSize}
        onChange={handlePageChange}
        onPageSizeChange={handlePageSizeChange}
        total={pagination.totalCount}
      />
    </ProjectsContainer>
  );
}
