export default function normalizePayRatesParameters(payload) {
  return {
    writtenFluency: payload.writtenFluency,
    spokenFluency: payload.spokenFluency,
    certifiedFluency: payload.certifiedFluency,
    workdayTaskId: payload.workdayTaskId,
    rateValue: payload.rate,
    disabled: false,

    // Information from hiring target
    language: payload.localeLanguage,
    languageCountry: payload.localeDialect,
    languageTo: payload.localeLanguageTo,
    languageCountryTo: payload.localeDialectTo,
    restrictToCountry: payload.restrictToCountry,

    // shared for both default and custom PayRate
    groupId: payload.defaultRate ? 1 : payload.userGroup,
    taskType: payload.defaultRate ? 'DEFAULT' : payload.taskType,
    defaultRate: payload.defaultRate || false,

    // Only on default payrate
    countryPayRateId: payload.countryPayRateId,
    state: payload.state || null,
  };
}
