export const getFraudByFlag = (flag) => {
  const ipSignals = [
    'high_risk_timezone_country_mismatch',
    'high_risk_ip_country_mismatch',
    'half_ips_match_malicious_raters',
    'ip_country_mismatch',
    'timezone_country_mismatch',
    'half_ips_match_same_project_raters',
    'half_ips_match_active_raters',
    'half_ips_match_suspicious_raters',
    'registration_ip_vpn',
    'half_toolbar_ips_match_same_project_raters',
    'too_many_ips',
    'high_pct_close_time_ip_match_malicious',
    'high_pct_close_time_ip_match_any_users',
    'high_pct_close_time_ip_rep_country_mismatch',
  ];

  const profileSignals = [
    'num_locales',
    'num_self_reported_languages',
    'sim_address_same_project',
    'same_photo_id_and_hash',
    'same_soundex_and_location',
    'same_phone_number',
  ];

  const behaviorSignals = [
    'excessive_invoice',
    'sim_tic_bodies',
    'unusual_quiz_performance',
    'similar_quiz_history',
  ];

  switch (flag) {
    case ipSignals.includes(flag):
      return 'IP';
    case profileSignals.includes(flag):
      return 'Profile';
    case behaviorSignals.includes(flag):
      return 'Behavior';
    default:
      return 'Default';
  }
};

const getFraudByType = (type) => {
  switch (type) {
    case 'ip_signals':
      return 'IP';
    case 'profile_signals':
      return 'Profile';
    case 'behavior_signals':
      return 'Behavior';
    default:
      return 'Default';
  }
};

export default getFraudByType;
