import { useEffect } from 'react';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as yup from 'yup';
import { connect, useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import withHooks from 'hook-hoc';
import { withFeature } from 'flagged';

import ProjectAccess from './ProjectAccess';
import {
  selectProjectTools,
  selectProjectId,
} from '../../../../store/ducks/project';
import {
  selectData as selectExperimentGroups,
  selectAccessHolder,
  saveExperimentGroups,
  saveAccessHolder,
  fetchAccessHolder,
} from '../../../../store/ducks/experimentGroups';
import { selectAllEager as selectPayrates } from '../../../../store/ducks/payrates';
import { requiredMessage } from '../../../ProjectSetup/Steps/shared';
import createArrayValidation from '../../../../helpers/array-validation';
import store from '../../../../store';
import { withErrorPopover } from '../../../../components/ErrorPopover';
import { findByUserGroupId } from '../../../../helpers/find';

function mapStateToProps(state) {
  const projectPayrates = selectPayrates(state);
  const userGroups = selectExperimentGroups(state);
  const accessHolder = selectAccessHolder(state);

  const newUserGroups = {};

  Object.keys(userGroups).forEach((key) => {
    newUserGroups[key] = userGroups[key].map((group) => {
      return {
        ...group,
        hasPayrate: !!findByUserGroupId(projectPayrates, group.groupId),
      };
    });
  });

  return {
    userGroups: newUserGroups,
    accessHolder,
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const { userGroups, accessHolder: accessHolders } = values;

  if (accessHolders.length <= 0) {
    return;
  }

  accessHolders.forEach((accessHolder) => {
    const experimentId = accessHolder.id;

    const payloadExperimentGroups = {
      experimentId,
      experimentGroups: get(userGroups, experimentId, []).map((ug) => ({
        ...ug,
      })),
    };

    dispatch(saveExperimentGroups({ changes: payloadExperimentGroups }));
  });

  dispatch(saveAccessHolder({ changes: accessHolders }));
}

function transformUserGroupError(error, value, index) {
  return new yup.ValidationError(
    requiredMessage`User Group`,
    value,
    `userGroup${index}group`,
  );
}

const validateUserGroupsArray = createArrayValidation(
  { groupId: yup.number().required(), disabled: yup.bool() },
  transformUserGroupError,
);

const validateUserGroupsObject = (value) => {
  // eslint-disable-next-line no-restricted-globals
  const isObjectKeysValid = !Object.keys(value).some((el) => isNaN(el));

  if (!isObjectKeysValid) {
    return false;
  }

  return validateUserGroupsArray;
};

function mapPropsToValues(props) {
  const { accessHolder, userGroups } = props;

  const accessHolderName = get(
    accessHolder ? accessHolder[0] : {},
    'name',
    null,
  );
  const accessHolderStatus = get(
    accessHolder ? accessHolder[0] : {},
    'status',
    null,
  );

  return {
    accessHolder,
    userGroups,
    accessHolderName,
    accessHolderStatus,
  };
}

function useHooksAsProps() {
  const dispatch = useDispatch();

  const projectId = useSelector(selectProjectId);
  const { hasExternalSystem } = useSelector(selectProjectTools);

  useEffect(() => {
    dispatch(fetchAccessHolder({ projectId, hasExternalSystem }));
  }, [dispatch, projectId, hasExternalSystem]);
}

export default compose(
  withFeature('experimentGroups'),
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    enableReinitialize: true,
    mapPropsToValues,
    validationSchema: yup.object().shape({
      userGroups: yup.mixed().when('theFollowingValidation', {
        is: () => selectProjectTools(store.getState()).hasExternalSystem,
        then: yup
          .mixed()
          .test('userGroups', '', validateUserGroupsObject)
          .required(requiredMessage`Project`),
      }),
    }),
  }),
  withHooks(useHooksAsProps),
)(ProjectAccess);
