import React from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';

export default function InputShimmer(props) {
  const { inputWidth } = props;

  return (
    <div>
      <div style={{ marginBottom: 8 }}>
        <Shimmer width={108} height={14} />
      </div>
      <Shimmer width={inputWidth || 360} height={36} />
    </div>
  );
}

InputShimmer.propTypes = {
  inputWidth: PropTypes.number,
};

InputShimmer.defaultProps = {
  inputWidth: 0,
};
