import takeLatest from 'promise-latest';
import memoize from 'lodash/memoize';
import axios from '../../../services/axios';

export default function createRemoteFieldValidation(getUrl, validateData) {
  return takeLatest(
    memoize(async (value) => {
      if (value) {
        try {
          const { data } = await axios.get(getUrl(value));

          return validateData(data, value);
        } catch (error) {
          return false;
        }
      }

      return true;
    }),
  );
}
