export function convertFromDTO(DTO, requirements, includeMetrics) {
  const requirementsByGroup = [].concat(
    ...requirements.map((requirement) =>
      requirement.groups.map((g) => ({
        groupID: g.id,
        requirementID: requirement.id,
        hiringTargets: {},
      })),
    ),
  );

  DTO.forEach((requirement) => {
    const { hiringTargetId, group: groups } = requirement;

    groups.forEach((group) => {
      const groupID = group.value;

      const targetIndex = requirementsByGroup.findIndex(
        (rg) => rg.groupID === groupID,
      );

      if (includeMetrics) {
        requirementsByGroup[targetIndex].hiringTargets[hiringTargetId] = {
          target: group.target,
          count: group.count,
        };
      } else {
        requirementsByGroup[targetIndex].hiringTargets[hiringTargetId] =
          group.target;
      }
    });
  });

  return requirementsByGroup.filter(
    (item) => !!Object.keys(item.hiringTargets).length,
  );
}

export function convertToDTO(requirementsByGroup, hiringTargets) {
  return hiringTargets.map((hiringTarget) => ({
    hiringTargetId: hiringTarget.id,
    group: requirementsByGroup.map((requirement) => ({
      paramenter: requirement.requirementID,
      value: requirement.groupID,
      target: requirement.hiringTargets[hiringTarget.id],
    })),
  }));
}
