import styled from 'styled-components';
import { transparentize } from 'polished';

export const Container = styled.div`
  height: 35px;
  width: ${(props) => props.width || 'unset'};
  display: flex;
  flex-direction: row;
`;

export const Item = styled.div`
  flex: 1;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-family: Lato;
  font-size: 10px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 1.25px;
  color: ${(props) =>
    props.active ? props.theme.colors.azure : props.theme.colors.blueGrey};
  border: ${(props) =>
    props.active
      ? `1px solid ${props.theme.colors.azure}`
      : `1px solid ${props.theme.colors.lightBlueGrey}`};
  border-top-left-radius: ${(props) => (props.isFirst ? '4px' : '0px')};
  border-top-right-radius: ${(props) => (props.isLast ? '4px' : '0px')};
  border-bottom-left-radius: ${(props) => (props.isFirst ? '4px' : '0px')};
  border-bottom-right-radius: ${(props) => (props.isLast ? '4px' : '0px')};
  opacity: ${(props) => (props.disabled ? 0.4 : 1)};
  pointer-events: ${(props) => (props.disabled ? 'none' : 'all')};
  background-color: ${(props) =>
    props.active
      ? transparentize(0.9, props.theme.colors.azure)
      : 'transparent'};
  cursor: ${(props) => (props.disabled ? 'default' : 'pointer')};
`;
