import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { ThemeContext } from 'styled-components';
import shortId from 'shortid';
import get from 'lodash/get';
import { useFormikContext } from 'formik';
import {
  FlexContainer,
  InputContainer,
  RestContainer,
  DeleteButtonContainer,
  DeleteButtom,
} from '../BlockingRules/styles';
import Select from '../../../Select';
import Button from '../../../Button';
import { FloatLeft } from '../../../Float';
import { Label, Section } from '../../../../styles';

export default function LinkedProjects(props) {
  const {
    linkedProjects,
    handleProjectChange,
    handleRemove,
    handleAddLine,
    projectOptions,
    filteredProjectOptions,
    projects,
  } = props;

  const { submitCount, errors } = useFormikContext();
  const theme = useContext(ThemeContext);

  return (
    <>
      <Label>LINKED PROJECT</Label>
      <FlexContainer>
        <InputContainer>
          <Select
            name="linkedProject0projectId"
            onChange={(value) => handleProjectChange(value, 0)}
            placeholder="Type a project name"
            options={projectOptions}
            loading={projects.loading}
            data-testid="project-setup--recruitment--linked-projects--select-0"
            error={
              projects.error ||
              (submitCount >= 1 &&
                get(
                  errors,
                  [`linkedProject${0}projectId`],
                  errors.linkedProjects,
                ))
            }
            value={linkedProjects[0].projectId}
            errorIncreasesHeight
          />
        </InputContainer>
        <RestContainer />
      </FlexContainer>

      {linkedProjects.map((linkedProject, index) =>
        index === 0 ? null : (
          <FlexContainer key={linkedProject.key}>
            <InputContainer>
              <Select
                name={`linkedProject${index}projectId`}
                onChange={(value) => handleProjectChange(value, index)}
                id="projectName"
                placeholder="Name of your project"
                data-testid={`project-setup--recruitment--linked-projects--select-${index}`}
                options={
                  linkedProject.projectId
                    ? projectOptions
                    : filteredProjectOptions
                }
                loading={projects.loading}
                value={linkedProject.projectId}
                error={
                  projects.error ||
                  (submitCount >= 1 &&
                    get(errors, [`linkedProject${index}projectId`], false))
                }
                errorIncreasesHeight
              />
            </InputContainer>
            <RestContainer>
              <FloatLeft>
                <DeleteButtonContainer>
                  <DeleteButtom
                    data-testid={`project-setup--recruitment--linked-projects--delete-${index}`}
                    onClick={() => handleRemove(index)}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      xmlnsXlink="http://www.w3.org/1999/xlink"
                      width="14"
                      height="14"
                      viewBox="0 0 14 14"
                    >
                      <defs>
                        <path
                          id="a"
                          d="M1.742.312L7 5.569 12.258.312a1 1 0 0 1 1.414 0l.016.016a1 1 0 0 1 0 1.414L8.431 7l5.257 5.258a1 1 0 0 1 0 1.414l-.016.016a1 1 0 0 1-1.414 0L7 8.431l-5.258 5.257a1 1 0 0 1-1.414 0l-.016-.016a1 1 0 0 1 0-1.414L5.569 7 .312 1.742a1 1 0 0 1 0-1.414L.328.312a1 1 0 0 1 1.414 0z"
                        />
                      </defs>
                      <use
                        fill={theme.colors.lightGreyBlue}
                        fillRule="nonzero"
                        xlinkHref="#a"
                      />
                    </svg>
                  </DeleteButtom>
                </DeleteButtonContainer>
              </FloatLeft>
            </RestContainer>
          </FlexContainer>
        ),
      )}

      <Section marginTop={24} marginBotton={24}>
        <Button
          data-testid="project-setup--recruitment--linked-projects--add"
          onClick={handleAddLine}
        >
          Add Other Project
        </Button>
      </Section>
    </>
  );
}

LinkedProjects.propTypes = {
  linkedProjects: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      projectId: PropTypes.number,
      permanent: PropTypes.bool,
    }),
  ),
  handleProjectChange: PropTypes.func.isRequired,
  handleRemove: PropTypes.func.isRequired,
  handleAddLine: PropTypes.func.isRequired,
  projectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  filteredProjectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  projects: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
      }).isRequired,
    ).isRequired,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.instanceOf(Error),
  }).isRequired,
};

LinkedProjects.defaultProps = {
  linkedProjects: [
    { key: shortId.generate(), projectId: null, permanent: false },
  ],
};
