import createEntitiesSlice, {
  OPTIONS_TYPE,
  OPTIONS_FETCH_MODES,
} from './engine';
import ENDPOINTS from '../../../constants/endpoints';
import { profileBuilderAPI } from '../../../services/axios';

export const ENTITIES = {
  // TODO: Migrate all commented out entities to this state manager
  // projectPages: 'projectPages',
  // teams: 'teams',
  // tenantsByCountry: 'tenantsByCountry',

  businessUnits: 'businessUnits',
  countries: 'countries',
  customers: 'customers',
  documents: 'documents',
  electronicDocuments: 'electronicDocuments',
  experimentGroups: 'experimentGroups',
  externalSystems: 'externalSystems',
  fluencies: 'fluencies',
  internalUsers: 'internalUsers',
  localeDialects: 'localeDialects',
  localeLanguages: 'localeLanguages',
  locales: 'locales',
  projects: 'projects',
  projectStatus: 'projectStatus',
  rateTypes: 'rateTypes',
  taskTypes: 'taskTypes',
  taskVolumes: 'taskVolumes',
  userGroups: 'userGroups',
  userGroupsByProject: 'userGroupsByProject',
  workTypes: 'workTypes',

  accessHolder: 'accessHolder',
  accessHolderStatus: 'accessHolderStatus',

  registrationMetrics: 'registrationMetrics',
  qualificationMetrics: 'qualificationMetrics',
  invoiceGenerationTypes: 'invoiceGenerationTypes',

  projectResources: 'projectResources',
  quizzes: 'quizzes',
  surveys: 'surveys',
  screening_survey: 'screening_survey',
  academies: 'academies',

  thirdParties: 'thirdParties',
  appenEntities: 'appenEntities',
  piiDataTypes: 'piiDataTypes',
  projectLevelDataConsentForm: 'projectLevelDataConsentForm',

  masterProfileCategories: 'masterProfileCategories',
  masterProfileQuestions: 'masterProfileQuestions',
};

export const ENTITY_METADATA = {
  projects: {
    nestedKeys: {
      ALL: 'all',
      ENABLED: 'enabled',
      DISABLED: 'disabled',
    },
  },
  projectResources: {
    types: {
      document: 'PROJECT_DOCUMENT',
      academy: 'PROJECT_ACADEMY',
      quiz: 'PROJECT_QUIZ',
      survey: 'PROJECT_SURVEY',
    },
  },
};

const ENTITIES_CONFIG = {
  [ENTITIES.accessHolder]: {
    entityPK: 'id',
    type: OPTIONS_TYPE.NESTED_OBJECT,
    url: (state, action) =>
      ENDPOINTS.V2.ACCESS_HOLDER({
        query: {
          projectId: action.payload.nestedKey,
          accessHolder: true,
        },
      }),
  },
  [ENTITIES.accessHolderStatus]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.ACCESS_HOLDER_STATUS(),
  },
  // All customers signed up in the platform
  [ENTITIES.customers]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.CUSTOMERS(),
    transform: (customers) => [{ id: -1, name: 'All customers' }, ...customers],
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },

  [ENTITIES.documents]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.DOCUMENTS(),
  },

  [ENTITIES.electronicDocuments]: {
    entityPK: 'id',
    url: (state, action) =>
      ENDPOINTS.V2.ELECTRONIC_DOCUMENTS({
        query: {
          activitiName: action.payload.activitiName,
        },
      }),
  },

  [ENTITIES.businessUnits]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.BUSINESS_UNITS(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.countries]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.COUNTRIES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.externalSystems]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.EXTERNAL_SYSTEMS(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.fluencies]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.FLUENCIES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.internalUsers]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.USERS({ query: { type: 'internal' } }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.localeDialects]: {
    type: OPTIONS_TYPE.NESTED_ARRAY,
    entityPK: 'value',
    url: (state, action) =>
      ENDPOINTS.V2.COUNTRIES({
        query: {
          language: action.payload.nestedKey,
        },
      }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.localeLanguages]: {
    entityPK: 'value',
    transform: (localeLanguages = []) => {
      const transformedLanguages = [];
      localeLanguages.forEach((lang) => {
        if (lang.group && lang.group === lang.value && lang.languages) {
          transformedLanguages.push({
            ...lang,
            value: `macro-${lang.value}`,
            macroLanguage: true,
            options: [
              { ...lang, label: `All ${lang.label} languages` },
              ...lang.languages,
            ],
          });
        } else {
          transformedLanguages.push(lang);
        }
      });
      return transformedLanguages;
    },
    url: ENDPOINTS.V2.LOCALE_LANGUAGES({
      query: { groupByMacroLanguage: true },
    }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.locales]: {
    entityPK: 'value',
    url: (state, action) =>
      ENDPOINTS.V2.LOCALES({
        query: {
          groupByMacroLanguage: true,
          includeWildcard: action.payload.includeWildcard || false,
        },
      }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  // [ENTITIES.projectPages]: {
  //   entityPK: 'id',
  //   url: ENDPOINTS.V2.PROJECT_PAGES(),
  //   transform: (projectPages) =>
  //     [...projectPages].map((projectPage) => ({
  //       ...projectPage,
  //       id: projectPage.id || 'DEFAULT',
  //     })),
  // },
  [ENTITIES.projects]: {
    type: OPTIONS_TYPE.NESTED_ARRAY,
    entityPK: 'id',
    url: (state, action) => {
      let disabledFlag;

      if (
        action.payload.nestedKey !== ENTITY_METADATA.projects.nestedKeys.ALL
      ) {
        if (
          action.payload.nestedKey ===
          ENTITY_METADATA.projects.nestedKeys.ENABLED
        ) {
          disabledFlag = false;
        } else {
          disabledFlag = true;
        }
      }
      return ENDPOINTS.V2.PROJECTS({
        query: {
          // should the backend return disabled projects?
          disabled: disabledFlag,
          sort: 'name',
          direction: 'ASC',
        },
      });
    },
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.projectStatus]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.PROJECT_STATUSES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.rateTypes]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.RATE_TYPES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.taskTypes]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.TASK_TYPES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },

  [ENTITIES.taskVolumes]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.TASK_VOLUMES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  // [ENTITIES.teams]: {
  //   entityPK: 'value',
  //   url: ENDPOINTS.V2.TEAM_TYPES(),
  // },
  // [ENTITIES.tenantsByCountry]: {
  //   type: OPTIONS_TYPE.NESTED_ARRAY,
  //   entityPK: 'value',
  //   url: (state, action /* fetchAction */) =>
  //     ENDPOINTS.V2.TENANTS_FOR_COUNTRY({
  //       query: {
  //         country: action.payload.nestedKey,
  //       },
  //     }),
  // },
  [ENTITIES.userGroups]: {
    entityPK: 'id',
    url: (state, action) =>
      ENDPOINTS.V2.USER_GROUPS({
        query: {
          customerId: action.payload.customerId,
          projectId: action.payload.projectId,
        },
      }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.userGroupsByProject]: {
    entityPK: 'id',
    url: (state, action) =>
      ENDPOINTS.V2.USER_GROUPS({
        query: {
          projectId: action.payload.projectId,
        },
      }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.experimentGroups]: {
    entityPK: 'id',
    url: (state, action) =>
      ENDPOINTS.V2.EXPERIMENT_GROUPS({
        pathVariables: {
          id: action.payload.experimentId,
        },
      }),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.workTypes]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.WORK_TYPES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },

  [ENTITIES.registrationMetrics]: {
    entityPK: 'id',
    type: OPTIONS_TYPE.NESTED_OBJECT,
    url: (state, action) =>
      ENDPOINTS.V2.REGISTRATION_PROCESSES({
        query: {
          projectId: action.payload.projectId,
          metrics: true,
        },
        pathVariables: {
          id: action.payload.nestedKey,
        },
      }),
  },

  [ENTITIES.qualificationMetrics]: {
    entityPK: 'id',
    type: OPTIONS_TYPE.NESTED_OBJECT,
    url: (state, action) =>
      ENDPOINTS.V2.QUALIFICATION_PROCESSES({
        query: {
          projectId: action.payload.projectId,
          metrics: true,
        },
        pathVariables: {
          id: action.payload.nestedKey,
        },
      }),
  },

  [ENTITIES.invoiceGenerationTypes]: {
    entityPK: 'value',
    url: ENDPOINTS.V2.INVOICE_GENERATION_TYPES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
    transform: (generationTypes = []) => {
      return generationTypes.map((item) => ({
        ...item,
        label:
          item.value === 'BOTH' ? 'Time Spent & Allotted Time' : item.label,
      }));
    },
  },

  [ENTITIES.projectResources]: {
    entityPK: 'id',
    url: (state, action) =>
      ENDPOINTS.V2.PROJECT_RESOURCES({
        pathVariables: { id: action.payload.nestedKey },
      }),
    transform: (resources = {}) => {
      const resourceList = [];

      Object.entries(resources).forEach(([, value]) => {
        if (Array.isArray(value)) {
          value.forEach((resource) => {
            if (resource.type === ENTITY_METADATA.projectResources.types.quiz) {
              resourceList.push({
                ...resource,
                type:
                  parseInt(resource.passingScore, 10) > 0
                    ? ENTITY_METADATA.projectResources.types.quiz
                    : ENTITY_METADATA.projectResources.types.survey,
              });
            } else {
              resourceList.push(resource);
            }
          });
        }
      });

      return resourceList;
    },
    type: OPTIONS_TYPE.NESTED_ARRAY,
  },
  [ENTITIES.quizzes]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.QUIZZES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.surveys]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.SURVEYS(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.screening_survey]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.SCREENING_SURVEY(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.academies]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.ACADEMIES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
  [ENTITIES.thirdParties]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.THIRD_PARTIES(),
  },

  [ENTITIES.appenEntities]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.APPEN_ENTITIES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },

  [ENTITIES.piiDataTypes]: {
    entityPk: 'value',
    url: ENDPOINTS.V2.PII_DATA_TYPES(),
    transform: (dataTypes = []) => {
      const optionsMap = {};

      dataTypes.forEach((dataType) => {
        const type = dataType.sensitive ? 'sensitive' : 'non-sensitive';
        if (optionsMap[type]) {
          optionsMap[type].push({
            label: dataType.name,
            ...dataType,
          });
        } else {
          optionsMap[type] = [
            {
              label: dataType.name,
              ...dataType,
            },
          ];
        }
      });

      return Object.entries(optionsMap).map(([key, group]) => ({
        label: key,
        options: group,
      }));
    },
  },

  [ENTITIES.projectLevelDataConsentForm]: {
    entityPK: 'id',
    type: OPTIONS_TYPE.NESTED_OBJECT,
    url: (state, action) =>
      ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM({
        query: {
          projectId: action.payload.projectId || action.payload.nestedKey,
        },
      }),
  },

  [ENTITIES.masterProfileCategories]: {
    entityPK: 'id',
    url: ENDPOINTS.PROFILE_BUILDER.MASTER_PROFILE_CATEGORIES(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
    connection: profileBuilderAPI,
  },

  [ENTITIES.masterProfileQuestions]: {
    entityPK: 'id',
    url: ENDPOINTS.V2.MASTER_PROFILE_QUESTIONS(),
    fetchMode: OPTIONS_FETCH_MODES.LAZY,
  },
};

const options = createEntitiesSlice(ENTITIES_CONFIG);

export default options.reducer;

const { fetch, saga, selectors } = options;
export { fetch, saga, selectors };
