import React from 'react';
import PropTypes from 'prop-types';
import { Button, Separator, Metadata, Container, BUTTON_TYPES } from './styles';
import ActionFooter, { AccessoryText } from '../ActionFooter';
import Select from '../Select';
import { ReactComponent as ChevronRight } from '../../assets/icons/chevron-right.svg';
import { ReactComponent as ChevronLeft } from '../../assets/icons/chevron-left.svg';
import Icon from '../Icon';

function getLabel({ type, label }) {
  switch (type) {
    case BUTTON_TYPES.prevPage:
      return <Icon icon={<ChevronLeft />} />;

    case BUTTON_TYPES.nextPage:
      return <Icon icon={<ChevronRight />} />;

    default:
      return label;
  }
}

const pageSizeOptions = [
  { value: 10, label: 'Show 10 items' },
  { value: 15, label: 'Show 15 items' },
  { value: 20, label: 'Show 20 items' },
  { value: 30, label: 'Show 30 items' },
  { value: 50, label: 'Show 50 items' },
];

export default function Pagination(props) {
  const {
    pages,
    pageSize,
    handlePageSizeChange,
    total,
    firstResult,
    lastResult,
    hideAccessoryRight,
  } = props;

  return (
    <Container>
      <ActionFooter
        hideDivider
        rightButtons={[]}
        accessoryLeft={
          <Metadata>
            <Select
              value={pageSize}
              name="pagesOptions"
              id="pagesOptions"
              options={pageSizeOptions}
              onChange={handlePageSizeChange}
            />
            <AccessoryText textTransform="initial">
              Showing {total === 0 ? 0 : firstResult + 1} -{' '}
              {total === 0 ? 0 : lastResult + 1} of {total}
            </AccessoryText>
          </Metadata>
        }
        accessoryRight={
          !hideAccessoryRight && (
            <>
              {pages.map((page) =>
                page.type !== 'separator' ? (
                  <Button
                    type="button"
                    key={page.key}
                    onClick={page.onChange}
                    disabled={page.disabled}
                    isActive={page.active}
                    buttonType={page.type}
                  >
                    {getLabel(page)}
                  </Button>
                ) : (
                  <Separator key={page.key} onClick={page.onClick}>
                    ...
                  </Separator>
                ),
              )}
            </>
          )
        }
      />
    </Container>
  );
}

Pagination.propTypes = {
  pages: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.shape({
        type: PropTypes.oneOf(Object.values(BUTTON_TYPES)).isRequired,
        label: PropTypes.string,
        onChange: PropTypes.func.isRequired,
        disabled: PropTypes.bool.isRequired,
      }).isRequired,
      PropTypes.shape({
        type: PropTypes.oneOf(Object.values(BUTTON_TYPES)).isRequired,
        onClick: PropTypes.func.isRequired,
      }).isRequired,
    ]),
  ).isRequired,
  handlePageSizeChange: PropTypes.func.isRequired,
  pageSize: PropTypes.number.isRequired,
  total: PropTypes.number.isRequired,
  firstResult: PropTypes.number.isRequired,
  lastResult: PropTypes.number.isRequired,
  hideAccessoryRight: PropTypes.bool,
};

Pagination.defaultProps = {
  hideAccessoryRight: false,
};
