import React, { useState, useEffect } from 'react';
import noop from 'lodash/noop';
import PropTypes from 'prop-types';
import Table from '../../../components/Table';
import Pagination from '../../../components/Pagination';
import NoEntries from '../../../components/NoEntries';
import IdCell from './Cells/IdCell';
import NameCell from './Cells/NameCell';
import IpCheckCell from './Cells/IpCheckCell';
import DuplicatesCell from './Cells/DuplicatesCell';
import StatusCell from './Cells/StatusCell';
import LastUpdatedCell from './Cells/LastUpdatedCell';
import Filters from './Filters';
import { PaginationContainer, TableContainer } from './styles';

const columns = [
  {
    Header: 'ID',
    accessor: 'id',
    key: 'id',
    Cell: IdCell,
    width: 45,
  },
  {
    Header: 'USER',
    accessor: 'name',
    key: 'name',
    Cell: NameCell,
    width: 560,
  },
  {
    Header: 'STATUS',
    accessor: 'status',
    key: 'status',
    Cell: StatusCell,
    style: {
      maxWidth: '178px',
    },
  },
  {
    Header: 'LAST UPDATE',
    accessor: 'dateUpdated',
    key: 'dateUpdated',
    Cell: LastUpdatedCell,
    style: {
      maxWidth: '178px',
    },
  },
  {
    Header: 'IP CHECK',
    accessor: 'ipCheck',
    key: 'ipCheck',
    Cell: IpCheckCell,
    style: {
      maxWidth: '178px',
    },
  },
  {
    Header: 'DUPLICATES',
    accessor: 'duplicates',
    key: 'duplicates',
    Cell: DuplicatesCell,
    style: {
      maxWidth: '178px',
    },
  },
];

export default function UserScreening(props) {
  const {
    users,
    metrics,
    usersLoading,
    pagination,
    handlePageChange,
    handlePageSizeChange,
    handleSortChange,
  } = props;

  const [selectedRowsIds, setSelectedRowsIds] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [bulkDisabled, setBulkDisabled] = useState(true);

  const handleSelectRows = (rows) => {
    setSelectedRowsIds(rows);
  };

  useEffect(() => {
    if (users.length > 0) {
      const filteredData = selectedRowsIds.map((item) => users[item]);
      setSelectedRows(filteredData);
      // check status for each selected row
      const statusArray = [...new Set(filteredData.map((item) => item.status))];
      if (statusArray.length !== 1) {
        setBulkDisabled(true);
      } else {
        setBulkDisabled(false);
      }
    }
  }, [selectedRowsIds, users]);

  return (
    <>
      <Filters
        selectedRows={selectedRows}
        bulkDisabled={bulkDisabled}
        metrics={metrics}
      />
      <TableContainer>
        {users.length > 0 ? (
          <Table
            columns={columns}
            data={users}
            renderExpandedComponent={noop}
            onSort={handleSortChange}
            onSelect={handleSelectRows}
          />
        ) : (
          <NoEntries label={usersLoading ? 'Loading...' : 'No Results Found'} />
        )}
        <PaginationContainer>
          <Pagination
            current={pagination.currentPage}
            pageSize={pagination.pageSize}
            onChange={handlePageChange}
            onPageSizeChange={handlePageSizeChange}
            total={pagination.totalCount}
          />
        </PaginationContainer>
      </TableContainer>
    </>
  );
}

UserScreening.propTypes = {
  users: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      status: PropTypes.string,
      dateUpdated: PropTypes.string,
    }),
  ).isRequired,
  usersLoading: PropTypes.bool.isRequired,
  pagination: PropTypes.shape({
    currentPage: PropTypes.number.isRequired,
    pageSize: PropTypes.number.isRequired,
    totalCount: PropTypes.number.isRequired,
  }).isRequired,
  handlePageChange: PropTypes.func.isRequired,
  handlePageSizeChange: PropTypes.func.isRequired,
  handleSortChange: PropTypes.func.isRequired,
  metrics: PropTypes.shape({
    staged: PropTypes.any,
  }).isRequired,
};
