import { call, put, takeEvery } from 'redux-saga/effects';
import {
  fetchExperimentGroupsSuccess,
  fetchExperimentGroupsError,
  fetchExperimentGroups,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchExperimentGroupsSagaFn(action) {
  const {
    payload: { experimentId },
  } = action;

  try {
    const response = yield call(
      axios,
      ENDPOINTS.V2.EXPERIMENT_GROUPS({
        pathVariables: {
          id: experimentId,
        },
      }),
    );

    const experimentGroups = {
      experimentId,
      data: response.data,
    };

    yield put(fetchExperimentGroupsSuccess(experimentGroups));
  } catch (error) {
    yield put(fetchExperimentGroupsError(error));
  }
}

export default takeEvery(
  fetchExperimentGroups.type,
  fetchExperimentGroupsSagaFn,
);
