import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';
import { useFormikContext } from 'formik';

import UserGroupPicker from '../../../../../components/UserGroupPicker';

import { ENTITIES, fetch, selectors } from '../../../../../store/ducks/options';
import {
  selectProjectId,
  selectProjectTools,
} from '../../../../../store/ducks/project';
import { setDirty } from '../../../../../store/ducks/projectView';
import {
  AccessHolderName,
  AccessHolderStatus,
  AccessHolderViewModeContainer,
} from './styles';
import {
  Label,
  Section,
  SectionDescription,
  SectionTitle,
} from '../../../../../styles';
import Input from '../../../../../components/Input';
import Select from '../../../../../components/Select';
import NoEntries from '../../../../../components/NoEntries';

export default function AccessHolderInput(props) {
  const dispatch = useDispatch();

  const { dirty, mode, handleChange } = props;

  const { values } = useFormikContext();

  const projectId = useSelector(selectProjectId);
  const { hasExternalSystem } = useSelector(selectProjectTools);

  useEffect(() => {
    if (mode === 'edit') {
      dispatch(setDirty(dirty));
    }
  }, [dirty, dispatch, mode]);

  useEffect(() => {
    dispatch(fetch(ENTITIES.accessHolderStatus));
  }, [dispatch, projectId]);

  const statuses = useSelector(
    selectors.createSelectSliceFor(ENTITIES.accessHolderStatus),
  );

  const accessHolderDescription = useMemo(
    () =>
      hasExternalSystem
        ? 'Add groups that will be able to view and work on this project.'
        : 'Create an experiment group, configure internal access parameters and add groups that will be able to view and work on this project.',
    [hasExternalSystem],
  );

  if (!values.accessHolderStatus) {
    const errorMessage = hasExternalSystem
      ? "The system should've created an access holder automatically. Please open a support ticket."
      : 'Required field is empty';
    return (
      <Section maxWidth="680px">
        <NoEntries label="Not found" error={errorMessage} />
      </Section>
    );
  }

  if (mode === 'edit') {
    return (
      <Section maxWidth="680px">
        {values.accessHolder.map((item) => (
          <>
            <Row key={item.id}>
              <Col col={8}>
                <Label htmlFor="accessHolderName">Project Name</Label>
                <Input
                  name="accessHolderName"
                  id="accessHolderName"
                  placeholder="Access holder Name"
                  value={item.name}
                  data-testid="access-holder-name"
                  disabled
                />
              </Col>
              <Col col={4}>
                <Label htmlFor="accessHolderStatus">Status</Label>
                <Select
                  value={item.status}
                  name="accessHolderStatus"
                  id="accessHolderStatus"
                  options={statuses.data}
                  loading={statuses.loading}
                  disabled={!item.name}
                  error={statuses.error}
                  onChange={(newValue) => handleChange(item.id, newValue)}
                  data-testid="invoice-template-setup-modal-invoice-type-select"
                />
                <br />
              </Col>
            </Row>
            <Section>
              <Section marginBottom="48px">
                <UserGroupPicker experimentId={item.id} mode={mode} />
              </Section>
            </Section>
          </>
        ))}
      </Section>
    );
  }

  return (
    <Section maxWidth="680px">
      {values.accessHolder.map((item, index) => (
        <>
          <Section maxWidth="680px">
            <SectionTitle>Access Configuration {index + 1}</SectionTitle>
            <SectionDescription>{accessHolderDescription}</SectionDescription>
          </Section>
          <AccessHolderViewModeContainer key={item.id}>
            <AccessHolderName>{item.name || 'Not set'}</AccessHolderName>
            <AccessHolderStatus currentStatus={item.status || 'DRAFT'}>
              {item.status || 'No status'}
            </AccessHolderStatus>
          </AccessHolderViewModeContainer>

          <Section>
            <Section marginBottom="48px">
              <UserGroupPicker experimentId={item.id} mode={mode} />
            </Section>
          </Section>
        </>
      ))}
      {values.accessHolder.length === 0 && (
        <AccessHolderViewModeContainer>
          <AccessHolderName>
            {values.accessHolderName || 'Not set'}
          </AccessHolderName>
          <AccessHolderStatus
            currentStatus={values.accessHolderStatus || 'DRAFT'}
          >
            {values.accessHolderStatus || 'No status'}
          </AccessHolderStatus>
        </AccessHolderViewModeContainer>
      )}
    </Section>
  );
}

AccessHolderInput.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  handleChange: PropTypes.func.isRequired,
  dirty: PropTypes.bool.isRequired,
};
