import React from 'react';
import PropTypes from 'prop-types';
import { Label, Section } from '../../../../styles';
// import eye from '../../../../assets/icons/eye.svg';
import pencil from '../../../../assets/icons/pencil.svg';
import close from '../../../../assets/icons/close.svg';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../Popover';
import Modal from '../../../Modal';
import {
  Container,
  TextContainer,
  ActionContainer,
  SettingsStyled,
} from './styles';
import SettingsHoverable from '../../../Icons/SettingsHoverable';
import Button from '../../../Button';
import DemographicsModal from './DemographicsModal';
import handleKeyDown from '../../../../pages/ProjectSetup/helpers/handleKeyDown';

export default function Demographics(props) {
  const {
    demographicType,
    hasDemographics,
    loading, // Requirements request in-flight
    requirements, // Requirements the user selected
    modalControls,
    modalProps,
    removeDemographicRequirements,
  } = props;

  const poppoverContent = (
    <>
      <PopoverItem
        onClick={modalControls.openModal}
        onKeyDown={handleKeyDown(modalControls.openModal)}
        role="button"
        tabIndex={0}
        data-testid="demographics-modal--actions--edit"
      >
        <PopoverIcon src={pencil} alt="Edit demographic requirements" />
        <PopoverText>Edit</PopoverText>
      </PopoverItem>
      <PopoverItem
        onClick={removeDemographicRequirements}
        onKeyDown={handleKeyDown(removeDemographicRequirements)}
        role="button"
        tabIndex={0}
        data-testid="demographics-modal--actions--clear"
      >
        <PopoverIcon src={close} alt="Clear demographic requirements" />
        <PopoverText>Clear</PopoverText>
      </PopoverItem>
    </>
  );

  return (
    <>
      <Section marginTop={24} marginBotton={24}>
        {hasDemographics ? (
          <Container
            isLoading={loading}
            data-testid="demographics-modal--action-hoverable"
          >
            <TextContainer>
              <Label>{demographicType}</Label>
              <p>{requirements}</p>
            </TextContainer>
            <ActionContainer>
              <Popover content={poppoverContent} placement="bottom-end">
                <SettingsStyled data-testid="demographics-modal--action-trigger">
                  <SettingsHoverable />
                </SettingsStyled>
              </Popover>
            </ActionContainer>
          </Container>
        ) : (
          <Button onClick={modalControls.openModal}>Set Demographics</Button>
        )}
      </Section>

      <Modal
        title="Demographic Requirements Setup"
        // eslint-disable-next-line react/jsx-props-no-spreading
        {...modalProps}
      >
        <DemographicsModal
          closeModal={modalControls.closeModal}
          openModal={modalControls.openModal}
          modalData={modalControls.modalData}
        />
      </Modal>
    </>
  );
}

Demographics.propTypes = {
  hasDemographics: PropTypes.bool.isRequired,
  loading: PropTypes.bool.isRequired, // Requirements request in-flight
  demographicType: PropTypes.string.isRequired,
  requirements: PropTypes.string.isRequired, // Requirements the user selected
  modalControls: PropTypes.objectOf(PropTypes.any).isRequired,
  modalProps: PropTypes.objectOf(PropTypes.any).isRequired,
  removeDemographicRequirements: PropTypes.func.isRequired,
};
