import { select, takeEvery } from 'redux-saga/effects';
import { navigate, selectDirty } from '.';

function* navigateSagaFn() {
  const dirty = yield select(selectDirty);

  if (dirty) {
    return;
  }

  document.body.scrollTop = 0; // For Safari
  document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
}

export default takeEvery(navigate.type, navigateSagaFn);
