import React, {
  useState,
  useRef,
  useCallback,
  useEffect,
  useMemo,
} from 'react';
import PropTypes from 'prop-types';
import { Manager, Reference, Popper } from 'react-popper';
import defaultsDeep from 'lodash/defaultsDeep';
import { Container, Content, ReferenceStyle } from './styles';

const popperPositionModifiers = {
  flip: {
    enabled: false,
  },
  preventOverflow: {
    escapeWithReference: true,
  },

  offset: {
    enabled: true,
    offset: '0, 8',
  },
};

function useTooltipHover() {
  const [showTooltip, setTriggerHovered] = useState(false);
  const tooltipHovered = useRef(false);
  const redraw = useRef(() => {});

  const triggerHandlers = {
    onMouseEnter: useCallback(() => {
      setTriggerHovered(true);
      redraw.current();
    }, []),
    onMouseLeave: useCallback(() => {
      setTimeout(() => {
        if (tooltipHovered.current) {
          return;
        }

        setTriggerHovered(false);
        tooltipHovered.current = false;
      }, 175);
    }, []),
  };

  const tooltipHandlers = {
    onMouseEnter: useCallback(() => {
      tooltipHovered.current = true;
      redraw.current();
    }, []),
    onMouseLeave: useCallback(() => {
      setTriggerHovered(false);
      tooltipHovered.current = false;
    }, []),
  };

  return [showTooltip, triggerHandlers, tooltipHandlers, redraw];
}

export default function Popover(props) {
  const { content, placement, modifiers, children, styles } = props;

  const [
    showTooltip,
    triggerHandlers,
    tooltipHandlers,
    redraw,
  ] = useTooltipHover();

  useEffect(() => {
    redraw.current();
  }, [redraw]);

  const { tooltip: tooltipStyle, content: contentStyle } = styles;

  const finalModifiers = useMemo(() => {
    return defaultsDeep(modifiers, popperPositionModifiers);
  }, [modifiers]);

  return (
    <Manager>
      <Reference>
        {({ ref }) => (
          <ReferenceStyle
            ref={ref}
            onMouseEnter={triggerHandlers.onMouseEnter}
            onMouseLeave={triggerHandlers.onMouseLeave}
            tabIndex={0}
            onFocus={triggerHandlers.onMouseEnter}
            onBlur={triggerHandlers.onMouseLeave}
          >
            {typeof children === 'function' ? children(showTooltip) : children}
          </ReferenceStyle>
        )}
      </Reference>
      <Popper placement={placement} modifiers={finalModifiers} positionFixed>
        {(renderProps) => {
          const {
            ref,
            style,
            scheduleUpdate,
            placement: innerPlacement,
          } = renderProps;

          redraw.current = scheduleUpdate;

          return (
            <Container
              ref={ref}
              data-placement={innerPlacement}
              hidden={!showTooltip}
              style={{ ...style, ...tooltipStyle }}
              onMouseEnter={tooltipHandlers.onMouseEnter}
              onMouseLeave={tooltipHandlers.onMouseLeave}
              onFocus={tooltipHandlers.onMouseEnter}
              onBlur={tooltipHandlers.onMouseLeave}
            >
              <Content style={contentStyle}>{content}</Content>
            </Container>
          );
        }}
      </Popper>
    </Manager>
  );
}

Popover.propTypes = {
  content: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    ),
  ]).isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    ),
    PropTypes.func,
  ]).isRequired,
  placement: PropTypes.oneOf([
    'top',
    'top-start',
    'top-end',
    'left',
    'right',
    'bottom',
    'bottom-start',
    'bottom-end',
  ]).isRequired,
  styles: PropTypes.shape({
    tooltip: PropTypes.objectOf(PropTypes.any),
    content: PropTypes.objectOf(PropTypes.any),
    arrow: PropTypes.objectOf(PropTypes.any),
  }),
  modifiers: PropTypes.shape({
    offset: PropTypes.shape({
      offset: PropTypes.string,
    }),
  }),
};

Popover.defaultProps = {
  styles: {},
  modifiers: {},
};
