import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import shortId from 'shortid';
import { ThemeContext } from 'styled-components';
import { useFormikContext } from 'formik';
import {
  FlexContainer,
  InputContainer,
  RestContainer,
  DeleteButtonContainer,
  PermanentText,
  DeleteButtom,
} from './styles';
import Select from '../../../Select';
import Button from '../../../Button';
import Switch from '../../../Switch';
import FloatContainer, { FloatLeft, FloatRight } from '../../../Float';
import { Label, Section } from '../../../../styles';

export default function BlockingRules(props) {
  const {
    blockingRules,
    handleProjectChange,
    handleToggle,
    handleRemove,
    handleAddLine,
    projectOptions,
    notSelectedProjectOptions,
    enabledProjects,
  } = props;

  const { submitCount, errors } = useFormikContext();
  const theme = useContext(ThemeContext);

  return (
    <>
      <Label>BLOCK PROJECT</Label>
      <FlexContainer>
        <InputContainer>
          <Select
            name="blockingRule0projectId"
            onChange={(value) => handleProjectChange(value, 0)}
            placeholder="Type a project name"
            value={blockingRules[0].projectId}
            options={projectOptions}
            loading={enabledProjects.loading}
            error={
              enabledProjects.error ||
              (submitCount >= 1 &&
                get(
                  errors,
                  [`blockingRule${0}projectId`],
                  errors.blockingRules,
                ))
            }
            errorIncreasesHeight
            data-testid="project-setup--recruitment--blocking-rules--select-0"
          />
        </InputContainer>
        <RestContainer>
          <DeleteButtonContainer />
          <FloatRight>
            <FloatContainer>
              <FloatLeft>
                <Switch
                  name="blockingRule[0].permanent"
                  onChange={(value) => handleToggle(value, 0)}
                  value={blockingRules[0].permanent}
                  data-testid="project-setup--recruitment--blocking-rules--switch-0"
                />
              </FloatLeft>
              <FloatRight>
                <PermanentText>PERMANENT</PermanentText>
              </FloatRight>
            </FloatContainer>
          </FloatRight>
        </RestContainer>
      </FlexContainer>

      {blockingRules.map((blockingRule, index) =>
        index === 0 ? null : (
          <FlexContainer key={blockingRule.key}>
            <InputContainer>
              <Select
                name={`blockingRule${index}projectId`}
                onChange={(value) => handleProjectChange(value, index)}
                placeholder="Type a project name"
                value={blockingRule.projectId}
                options={
                  blockingRule.projectId
                    ? projectOptions
                    : notSelectedProjectOptions
                }
                loading={enabledProjects.loading}
                error={
                  enabledProjects.error ||
                  (submitCount >= 1 &&
                    get(errors, [`blockingRule${index}projectId`], false))
                }
                data-testid={`project-setup--recruitment--blocking-rules--select-${index}`}
                errorIncreasesHeight
              />
            </InputContainer>
            <RestContainer>
              <FloatLeft>
                <DeleteButtonContainer>
                  <DeleteButtom
                    onClick={() => handleRemove(index)}
                    data-testid={`project-setup--recruitment--blocking-rules--delete-${index}`}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      xmlnsXlink="http://www.w3.org/1999/xlink"
                      width="14"
                      height="14"
                      viewBox="0 0 14 14"
                    >
                      <defs>
                        <path
                          id="a"
                          d="M1.742.312L7 5.569 12.258.312a1 1 0 0 1 1.414 0l.016.016a1 1 0 0 1 0 1.414L8.431 7l5.257 5.258a1 1 0 0 1 0 1.414l-.016.016a1 1 0 0 1-1.414 0L7 8.431l-5.258 5.257a1 1 0 0 1-1.414 0l-.016-.016a1 1 0 0 1 0-1.414L5.569 7 .312 1.742a1 1 0 0 1 0-1.414L.328.312a1 1 0 0 1 1.414 0z"
                        />
                      </defs>
                      <use
                        fill={theme.colors.lightGreyBlue}
                        fillRule="nonzero"
                        xlinkHref="#a"
                      />
                    </svg>
                  </DeleteButtom>
                </DeleteButtonContainer>
              </FloatLeft>
              <FloatRight>
                <FloatContainer>
                  <FloatLeft>
                    <Switch
                      name="blockingRule[0].permanent"
                      onChange={(value) => handleToggle(value, index)}
                      value={blockingRule.permanent}
                      data-testid={`project-setup--recruitment--blocking-rules--switch-${index}`}
                    />
                  </FloatLeft>
                  <FloatRight>
                    <PermanentText>PERMANENT</PermanentText>
                  </FloatRight>
                </FloatContainer>
              </FloatRight>
            </RestContainer>
          </FlexContainer>
        ),
      )}

      <Section marginTop={24} marginBotton={24}>
        <Button
          data-testid="project-setup--recruitment--blocking-rules--add"
          onClick={handleAddLine}
        >
          Add Other Project
        </Button>
      </Section>
    </>
  );
}

BlockingRules.propTypes = {
  blockingRules: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      projectId: PropTypes.number,
      permanent: PropTypes.bool,
    }),
  ),
  handleProjectChange: PropTypes.func.isRequired,
  handleToggle: PropTypes.func.isRequired,
  handleRemove: PropTypes.func.isRequired,
  handleAddLine: PropTypes.func.isRequired,
  projectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  notSelectedProjectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  enabledProjects: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
      }).isRequired,
    ).isRequired,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.instanceOf(Error),
  }).isRequired,
};

BlockingRules.defaultProps = {
  blockingRules: [
    { key: shortId.generate(), projectId: null, permanent: false },
  ],
};
