export default function resetForm(formikBag, initialValues, callback) {
  const { resetForm: resetFormState, validateForm, setErrors } = formikBag;

  // Resets form state.
  resetFormState(initialValues);

  // Resets validation state.
  validateForm();
  setErrors({});

  // Schedule callback to next tick so we can be sure form state is updated.
  setImmediate(() => callback());
}
