import React, { useCallback } from 'react';
import { useDrop } from 'react-dnd';
import { Shimmer } from 'react-shimmer';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import { useDispatch } from 'react-redux';
import get from 'lodash/get';
import {
  Container,
  AddStepBox,
  Spacer,
  PopoverContainer,
  Popover,
  ErrorTitle,
  Wrapper,
} from './styles';
import { CARD_FROM_BUILDER, CARD_FROM_SIDEBAR } from '../constants';
import Card from './Card';
import usePrevious from '../helpers/usePrevious';
import { hasStepComponent } from '../helpers/getStepComponent';

function collect(monitor) {
  const item = monitor.getItem();
  return {
    hovered: monitor.isOver(),
    hoveredType: item && item.type,
    hoveredIndex: (!!item && item.cardIndex) || -1,
  };
}

function renderLabel(label, isBlank, isReorder) {
  if (isBlank) {
    return label;
  }

  if (isReorder) {
    return '';
  }

  return 'Add step';
}

export default function ProcessesColumn(props) {
  const {
    openPreview,
    instantiated,
    isLoading,
    processModules,
    label,
    setProcessModules,
    inlineText,
    showPreview,
  } = props;

  const dispatch = useDispatch();

  const addCardToEnd = useCallback(
    (item, monitor) => {
      const result = monitor.getDropResult();

      if (result && result.handled) {
        return { handled: result.handled };
      }

      if (item.type === CARD_FROM_BUILDER) {
        dispatch(
          setProcessModules([
            ...processModules.filter((card) => card.key !== item.cardKey),
            { ...item, key: shortid.generate() },
          ]),
        );
      } else {
        // Target Builder
        dispatch(
          setProcessModules([
            ...processModules,
            {
              ...item,
              key: shortid.generate(),
              dirty: hasStepComponent(item.sequenceFlow), // blocks the save action until the card is added
            },
          ]),
        );
      }

      return { handled: true };
    },
    [dispatch, processModules, setProcessModules],
  );

  const boundSetProcessModules = useCallback(
    (setter) => dispatch(setProcessModules(setter(processModules))),
    [dispatch, processModules, setProcessModules],
  );

  const removeProcessModule = useCallback(
    (key) =>
      dispatch(
        setProcessModules(
          processModules.filter((process) => process.key !== key),
        ),
      ),
    [dispatch, processModules, setProcessModules],
  );

  const [{ hovered: dropBoxHovered }, addBoxDropRef] = useDrop({
    accept: [CARD_FROM_BUILDER, CARD_FROM_SIDEBAR],
    drop: addCardToEnd,
    collect,
  });

  const [
    { hovered: containerHovered, hoveredIndex, hoveredType },
    containerDropRef,
  ] = useDrop({
    accept: [CARD_FROM_BUILDER, CARD_FROM_SIDEBAR],
    collect,
    drop: addCardToEnd,
  });

  const previousModules = usePrevious(processModules, []);

  const hasDataCollectionConsentForm = processModules
    .map((step) => step.sequenceFlow === 'dataCollectionPiiConsentFormFlow')
    .filter((step) => step)
    .includes(true);

  const hasCompleteAdditionalAttributes = processModules
    .map((step) => step.sequenceFlow === 'completeProjectAttributesFlow')
    .filter((step) => step)
    .includes(true);

  return (
    <Container ref={containerDropRef} isHovered={containerHovered}>
      {processModules.map((card, index) => (
        <Card
          // Card Props
          id={card.id}
          stepDefinitionId={card.stepDefinitionId}
          name={card.name}
          description={card.description}
          sequenceFlow={card.sequenceFlow}
          attributes={card.attributes}
          subSteps={card.subSteps}
          removable={!instantiated || !card.id}
          items={card.items}
          isSecondary={get(card, 'parentQuestion.parentQuestionId', null)}
          // Control Props
          key={card.key}
          cardKey={card.key}
          cardIndex={index}
          ignoreOffset={
            hoveredType === CARD_FROM_BUILDER && hoveredIndex + 1 === index
          }
          // Outer actions
          setProcessModules={boundSetProcessModules}
          removeProcessModule={removeProcessModule}
          openPreview={openPreview}
          processModules={processModules}
          previousModules={previousModules}
          inlineText={inlineText}
          showPreview={showPreview}
        />
      ))}
      {!processModules.length && <Spacer />}
      {isLoading ? (
        <Shimmer width={634} height={141} />
      ) : (
        <AddStepBox
          isHidden={
            (!!processModules.length && !containerHovered) ||
            (!dropBoxHovered && hoveredType === CARD_FROM_BUILDER)
          }
          isHovered={dropBoxHovered && hoveredType !== CARD_FROM_BUILDER}
          ref={addBoxDropRef}
        >
          {renderLabel(
            label,
            processModules.length === 0 && !containerHovered,
            hoveredType === CARD_FROM_BUILDER,
          )}
        </AddStepBox>
      )}
      {hasDataCollectionConsentForm && (
        <PopoverContainer>
          <Wrapper>
            <Popover>
              <ErrorTitle>Attention</ErrorTitle>
              Make sure to configure consent form in step 7 of project setup in
              order to get consent from workers.
            </Popover>
          </Wrapper>
        </PopoverContainer>
      )}
      {hasCompleteAdditionalAttributes && (
        <PopoverContainer>
          <Wrapper>
            <Popover>
              <ErrorTitle>Attention</ErrorTitle>
              Make sure to configure Target Groups and/or Intelligent Attributes
              in step 3 in order to define the correct screening rules.
            </Popover>
          </Wrapper>
        </PopoverContainer>
      )}
    </Container>
  );
}

ProcessesColumn.propTypes = {
  openPreview: PropTypes.func.isRequired,
  instantiated: PropTypes.bool,
  isLoading: PropTypes.bool.isRequired,
  processModules: PropTypes.arrayOf(PropTypes.object).isRequired,
  label: PropTypes.string.isRequired,
  setProcessModules: PropTypes.func.isRequired,
  inlineText: PropTypes.bool,
  showPreview: PropTypes.bool,
};

ProcessesColumn.defaultProps = {
  instantiated: false,
  inlineText: false,
  showPreview: true,
};
