import React from 'react';
import ReactModal from 'react-responsive-modal';
import PropTypes from 'prop-types';
import { H2 } from '../Layout/styles';
import {
  MainContent,
  ModalHeader,
  ModalBody,
  ModalFooter,
  SideBar,
} from './styles';
import closeIcon from '../../assets/icons/close.svg';
import classNames from './styles.module.css';

const modalClassNames = {
  modal: classNames.modal,
  overlay: classNames.overlay,
};

export function LargeModal(props, ref) {
  const {
    open,
    onClose,
    onExited,
    title,
    sidebar,
    footer,
    children,
    hideCloseButton,
    closeOnOverlayClick,
    closeOnEsc,
    styles,
  } = props;

  return (
    <ReactModal
      open={open}
      onClose={onClose}
      onExited={onExited}
      classNames={modalClassNames}
      showCloseIcon={false}
      center
      closeOnOverlayClick={closeOnOverlayClick}
      closeOnEsc={closeOnEsc}
      styles={styles}
    >
      <ModalHeader>
        <H2>{title}</H2>
        {!hideCloseButton && (
          <button onClick={onClose} type="button">
            <img src={closeIcon} alt="Close modal" />
          </button>
        )}
      </ModalHeader>
      <ModalBody>
        <SideBar>{sidebar}</SideBar>
        <MainContent ref={ref && ref.current ? ref : undefined}>
          {children}
        </MainContent>
      </ModalBody>
      <ModalFooter>{footer}</ModalFooter>
    </ReactModal>
  );
}

export default React.forwardRef(LargeModal);

LargeModal.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onExited: PropTypes.func,
  title: PropTypes.string,
  sidebar: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
  footer: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
  closeOnOverlayClick: PropTypes.bool,
  closeOnEsc: PropTypes.bool,
  hideCloseButton: PropTypes.bool,
  styles: PropTypes.oneOfType([PropTypes.object]),
};

LargeModal.defaultProps = {
  title: '',
  closeOnOverlayClick: true,
  closeOnEsc: true,
  hideCloseButton: false,
  onExited: undefined,
  styles: {},
};
