import './services/postMessage';

(async () => {
  // const validEmbedding = isInsideIframe() && isPermittedParentHost();
  const validSession = true; // await validateUserSession();

  if (process.env.NODE_ENV === 'development' || validSession) {
    ReactDOM.render(React.createElement(App), document.getElementById('root'));
  } else {
    const forbiddenEl = document.createElement('h1');
    forbiddenEl.innerText = 'Forbidden';
    document.getElementById('root').appendChild(forbiddenEl);
  }
})();

