import { withFormik } from 'formik';
import shortid from 'shortid';
import { useCallback, useEffect, useMemo } from 'react';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import * as Yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import InternalLocaleBasedQuizStep from './InternalLocaleBasedQuizStep';
import {
  setProcessAttributes,
  loadStepById,
  createSelectStepDefinition,
} from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import createArrayValidation from '../../../../helpers/array-validation';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import { findByValue, findByNestedValue } from '../../../../helpers/find';
import {
  formatMacroLanguageLabel,
  mapToMacroLanguages,
} from '../../helpers/macroLanguages';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';

const initialFormValues = {
  quizes: [
    {
      quiz: null,
      locale: null,
      maximumAttemps: '',
      unlimited: false,
      subsetSize: '',
      allQuestions: false,
      passingScore: '',
      key: shortid.generate(),
    },
  ],
};

function transformError(error, value, index) {
  switch (error.path) {
    case 'quiz':
      return new Yup.ValidationError(
        requiredMessage`Quiz`,
        value,
        `quiz${index}`,
      );

    case 'locale':
      return new Yup.ValidationError(
        requiredMessage`Locale`,
        value,
        `locale${index}`,
      );

    case 'maximumAttemps':
      return new Yup.ValidationError(
        error.value === 0
          ? "Maximum attempts can't be zero"
          : requiredMessage`Maximum attempts`,
        value,
        `maximumAttemps${index}`,
      );

    case 'subsetSize':
      return new Yup.ValidationError(
        error.value === 0
          ? "Subset size can't be zero"
          : requiredMessage`Subset size`,
        value,
        `subsetSize${index}`,
      );

    case 'passingScore':
    default:
      return new Yup.ValidationError(
        requiredMessage`Passing score`,
        value,
        `passingScore${index}`,
      );
  }
}

const validateArray = createArrayValidation(
  {
    quiz: Yup.number().required(),
    locale: Yup.string().required(),
    maximumAttemps: Yup.string().when('unlimited', {
      is: (value) => !value,
      then: Yup.number().min(1).required(),
      otherwise: Yup.string().notRequired(),
    }),
    subsetSize: Yup.string().when('allQuestions', {
      is: (value) => !value,
      then: Yup.number().min(1).required(),
      otherwise: Yup.string().notRequired(),
    }),
    passingScore: Yup.string()
      .test('passingScore', 'error', (value) =>
        value ? !Number.isNaN(parseInt(value.replace('%', ''), 10)) : true,
      )
      .required(),
  },
  transformError,
);

function useHooksAsProps(outerProps) {
  const {
    setFieldValue,
    values: { quizes = [] },
    card: { stepDefinitionId },
  } = outerProps;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
    dispatch(
      fetch({
        optionsFor: ENTITIES.locales,
        includeWildcard: true,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  const stepDefinition = useSelector(
    createSelectStepDefinition(stepDefinitionId),
  );

  const locales = useSelector(selectors.createSelectSliceFor(ENTITIES.locales));
  const localeOptions = mapToMacroLanguages(locales.data);

  const quizesOptions = useMemo(() => {
    return mapToOptions(
      'id',
      'name',
    )(get(stepDefinition, 'attributes[1].options', []));
  }, [stepDefinition]);

  const selectedQuizes = useMemo(() => {
    return quizes
      .filter((internalQuiz) => internalQuiz.quiz && internalQuiz.locale)
      .map((internalQuiz) => ({
        key: shortid.generate(),
        quiz: get(findByValue(quizesOptions, internalQuiz.quiz), 'label', ''),
        locale: formatMacroLanguageLabel(
          findByNestedValue(localeOptions, internalQuiz.locale),
        ),
      }));
  }, [localeOptions, quizes, quizesOptions]);

  const handleAddInternalQuiz = useCallback(() => {
    setFieldValue('quizes', [
      ...quizes,
      { ...initialFormValues.quizes[0], key: shortid.generate() },
    ]);
  }, [quizes, setFieldValue]);

  const handleRemoveInternalQuiz = useCallback(
    (index) => {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        ...quizes.slice(index + 1, quizes.length),
      ]);
    },
    [quizes, setFieldValue],
  );

  const createInputChangeHandler = (fieldName) => (index) => (changeEvent) => {
    const item = quizes[index];

    let value;

    if (changeEvent.target) {
      value = changeEvent.target.value;
    } else {
      value = changeEvent.value;
    }

    if (
      (fieldName === 'maximumAttemps' || fieldName === 'subsetSize') &&
      value &&
      Number.isNaN(parseInt(value, 10))
    ) {
      return;
    }

    if (
      (fieldName === 'maximumAttemps' || fieldName === 'subsetSize') &&
      value
    ) {
      value = parseInt(value, 10);
    }

    if (fieldName === 'quiz') {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        {
          ...item,
          quiz: value,
          maximumAttemps: '',
          unlimited: false,
          subsetSize: '',
          allQuestions: false,
          passingScore: '',
        },
        ...quizes.slice(index + 1, quizes.length),
      ]);
    } else {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        { ...item, [fieldName]: value },
        ...quizes.slice(index + 1, quizes.length),
      ]);
    }
  };

  const handleQuizChange = createInputChangeHandler('quiz');
  const handleLocaleChange = createInputChangeHandler('locale');
  const handleMaximumAttempsChange = createInputChangeHandler('maximumAttemps');
  const handleUnlimitedChange = createInputChangeHandler('unlimited');
  const handleSubsetSizeChange = createInputChangeHandler('subsetSize');
  const handleAllQuestionsChange = createInputChangeHandler('allQuestions');
  const handlePassingScoreChange = createInputChangeHandler('passingScore');

  return {
    handleAddInternalQuiz,
    handleRemoveInternalQuiz,
    handleQuizChange,
    handleLocaleChange,
    handleMaximumAttempsChange,
    handleUnlimitedChange,
    handleSubsetSizeChange,
    handleAllQuestionsChange,
    handlePassingScoreChange,
    quizesOptions,
    selectedQuizes,
    localeOptions,
  };
}

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      subSteps: (values.quizes || []).map((quiz) => ({
        attributes: [
          {
            name: 'localeCode',
            attributeOrder: null,
            value: quiz.locale,
          },
          {
            name: 'quizId',
            attributeOrder: null,
            value: quiz.quiz,
          },
          {
            name: 'passingScore',
            attributeOrder: null,
            value: `${quiz.passingScore}`.replace('%', ''),
          },
          {
            name: 'maxAttempts',
            attributeOrder: null,
            value: quiz.unlimited ? '-1' : quiz.maximumAttemps,
          },
          {
            name: 'subsetSize',
            attributeOrder: null,
            value: quiz.allQuestions ? '-1' : quiz.subsetSize,
          },
        ],
      })),
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

function mapPropsToValues(props) {
  const {
    card: { subSteps },
  } = props;

  if (subSteps && subSteps.length) {
    return {
      quizes: subSteps.map((step) => {
        const maximumAttemps = get(
          step.attributes.find((att) => att.name === 'maxAttempts'),
          'value',
          '',
        );
        const subsetSize = get(
          step.attributes.find((att) => att.name === 'subsetSize'),
          'value',
          '',
        );

        return {
          quiz: parseInt(
            get(
              step.attributes.find((att) => att.name === 'quizId'),
              'value',
              -1,
            ),
            10,
          ),
          locale: get(
            step.attributes.find((att) => att.name === 'localeCode'),
            'value',
            -1,
          ),
          maximumAttemps: maximumAttemps === '-1' ? '' : maximumAttemps,
          unlimited: maximumAttemps === '-1',
          subsetSize: subsetSize === '-1' ? '' : subsetSize,
          allQuestions: subsetSize === '-1',
          passingScore: get(
            step.attributes.find((att) => att.name === 'passingScore'),
            'value',
            null,
          ),
          key: shortid.generate(),
        };
      }),
    };
  }

  return initialFormValues;
}

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
      quizes: Yup.array().test(
        'internalLocaleBasedQuizArray',
        '',
        validateArray,
      ),
    }),
  }),
  withHooks(useHooksAsProps),
)(InternalLocaleBasedQuizStep);
