import * as Yup from 'yup';
import { requiredMessage } from '../../../../../pages/ProjectSetup/Steps/shared';

export default Yup.object()
  .strict()
  .shape({
    localeId: Yup.number().required(requiredMessage`Locale`),
    maxUsersPerDay: Yup.number()
      .min(1, 'Max users per day must be greater than or equal to 1')
      .required(requiredMessage`Max users per day`),
    maxActiveUsers: Yup.number()
      .min(1, 'Max active users must be greater than or equal to 1')
      .required(requiredMessage`Max active users`),
  });
