/* eslint-disable */
import createEntityStateProvider from '../../helpers/createEntityStateProvider';
import ENDPOINTS from '../../../constants/endpoints';
import shortid from 'shortid';

function getUrl() {
  return ENDPOINTS.V2.DEMOGRAPHIC_REQUIREMENTS_PARAMETERS();
}

function transform(requirements) {
  return requirements.map((requirement) => ({
    id: requirement.name,
    name: requirement.name,
    groups: requirement.values.map((group) => ({
      id: group.value,
      name: group.value,
      label: group.label,
    })),
  }));
}

const [
  {
    reducer,
    actions: {
      fetchAll,
      fetchFailed,
      setAll,
      addOne,
      addMany,
      removeOne,
      removeMany,
      removeAll,
      updateOne,
      updateMany,
      upsertOne,
      upsertMany,
    },
  },
  {
    selectIds,
    selectEntities,
    selectAll,
    selectTotal,
    selectById,
    selectLoading,
    selectError,
  },
  demographicRequirementsSaga,
] = createEntityStateProvider(
  'demographicRequirements',
  getUrl,
  'id',
  transform,
);

export default reducer;

function createRequirementGroupsSelector(requirementID) {
  return (state) => {
    const r = selectById(state, requirementID);

    return r && r.groups ? r.groups : [];
  };
}

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
  selectLoading,
  selectError,
  demographicRequirementsSaga,
  createRequirementGroupsSelector,
};

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.demographicRequirements = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
