/* eslint-disable no-param-reassign */
import { createSlice, createSelector } from '@reduxjs/toolkit';
import { takeLeading, put, call, select } from 'redux-saga/effects';
import axios from '../../../services/axios';
import ENDPOINTS from '../../../constants/endpoints';

const slice = createSlice({
  name: 'tenantsByCountry',

  initialState: {
    data: {},
    loading: false,
    error: null,
  },

  reducers: {
    fetchAll: (state /* , action = { payload: country } */) => ({
      ...state,
      loading: true,
    }),

    setAll: (state, action) => {
      const {
        payload: { country, data },
      } = action;

      state.loading = false;
      state.data[country] = data;
    },

    fetchFailed: (state, action) => ({
      ...state,
      loading: false,
      error: action.payload,
    }),
  },
});

const {
  actions: { fetchAll, fetchFailed, setAll },
  reducer,
} = slice;

export function* tenantsByCountrySagaFn(action) {
  const { payload: country } = action;

  const tenants = yield select((state) => state.tenantsByCountry.data[country]);

  if (tenants) {
    yield put(setAll({ country, data: tenants }));

    return;
  }

  try {
    const response = yield call(
      axios,
      ENDPOINTS.V2.TENANTS_FOR_COUNTRY({
        query: {
          country,
        },
      }),
    );

    yield put(setAll({ country, data: response.data }));
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

reducer.key = 'tenantsByCountry';

export default reducer;

export { fetchAll, fetchFailed, setAll };

export const tenantsByCountrySaga = takeLeading(
  fetchAll.type,
  tenantsByCountrySagaFn,
);

tenantsByCountrySaga.fn = tenantsByCountrySagaFn;

function selectTenantsState(state) {
  return state[reducer.key];
}

export function selectAll(state) {
  const tenantsByCountry = selectTenantsState(state);

  return [].concat(
    ...Object.keys(tenantsByCountry.data).map(
      (key) => tenantsByCountry.data[key],
    ),
  );
}

export const selectLocaleDialects = createSelector(selectTenantsState);

export function createSelectByCountry(country) {
  return (state) => selectTenantsState(state).data[country] || [];
}

export function selectLoading(state) {
  return selectTenantsState(state).loading;
}

export function selectError(state) {
  return selectTenantsState(state).error;
}

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.tenantsByCountry = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
