import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { Label, Section } from '../../../../styles';
import { StepActionContainer } from '../styles';
import {
  QuizContainer,
  Img,
  PreviewItem,
  QuizTitle,
  QuizRemoveColumn,
  QuizContentColumn,
  RemoveQuizButton,
  PreviewContainer,
} from './styles';
import Select from '../../../Select';
import ActionFooter from '../../../ActionFooter';
import handleKeyDown from '../../../../pages/ProjectSetup/helpers/handleKeyDown';
import ArrowRightIcon from '../../../../assets/icons/icon-right-arrow.svg';
import close from '../../../../assets/icons/close.svg';

export default function TranslationQuizStep(props) {
  const {
    values: { quizzes = [] },
    submitCount,
    card: { subSteps },
    submitForm,
    localeOptions,
    languageQuizOptions,
    collapsed,
    errors,
    selectedQuizzes,
    handleAddQuiz,
    handleRemoveQuiz,
    handleQuizChange,
    handleLocaleChange,
    cancelEditing,
  } = props;

  const hasAttributes = subSteps && subSteps.length;

  if (collapsed && hasAttributes) {
    return (
      <PreviewContainer>
        {selectedQuizzes.map(({ fromLocale, toLocale, languageQuiz }) => (
          <PreviewItem>
            {`${fromLocale} to ${toLocale}`}
            <Img src={ArrowRightIcon} alt="arrow right icon" />
            {`${languageQuiz}`}
          </PreviewItem>
        ))}
      </PreviewContainer>
    );
  }

  return (
    <>
      {quizzes.map((quiz, index) => (
        <QuizContainer key={quiz.key}>
          <QuizContentColumn>
            {quizzes.length > 1 && (
              <QuizTitle>Translation Quiz {index + 1}</QuizTitle>
            )}
            <Section marginBottom="32px">
              <Row>
                <Col col={6}>
                  <Label htmlFor="documentToSign">LOCALE</Label>
                  <Select
                    name={`fromLocale${index}`}
                    options={localeOptions}
                    onChange={(value) =>
                      handleLocaleChange(index, value, 'fromLocale')
                    }
                    value={quizzes[index].fromLocale}
                    placeholder="Select locale"
                    error={submitCount >= 1 && errors[`fromLocale${index}`]}
                  />
                </Col>
                <Col col={6}>
                  <Label htmlFor="documentToSign">TRANSLATE TO LOCALE</Label>
                  <Select
                    name={`toLocale${index}`}
                    options={localeOptions}
                    onChange={(value) =>
                      handleLocaleChange(index, value, 'toLocale')
                    }
                    value={quizzes[index].toLocale}
                    placeholder="Select locale"
                    error={submitCount >= 1 && errors[`toLocale${index}`]}
                  />
                </Col>
              </Row>
            </Section>
            <Section marginBottom="45px">
              <Row>
                <Col col={12}>
                  <Label htmlFor="documentToSign">TRANSLATION QUIZ</Label>
                  <Select
                    name={`languageQuiz${index}`}
                    options={languageQuizOptions}
                    onChange={(value) => handleQuizChange(index, value)}
                    value={quizzes[index].languageQuiz}
                    placeholder="Select existing quiz"
                    error={submitCount >= 1 && errors[`languageQuiz${index}`]}
                  />
                </Col>
              </Row>
            </Section>
          </QuizContentColumn>
          <QuizRemoveColumn>
            {quizzes.length > 1 && index !== 0 && (
              <RemoveQuizButton
                role="button"
                onClick={() => handleRemoveQuiz(index)}
                onKeyDown={handleKeyDown(() => handleRemoveQuiz(index))}
                tabIndex={0}
              >
                <img src={close} alt="close icon" />
              </RemoveQuizButton>
            )}
          </QuizRemoveColumn>
        </QuizContainer>
      ))}
      <StepActionContainer>
        <ActionFooter
          hideDivider
          leftButtons={[
            {
              type: 'secondary',
              text: 'Add Other Quiz',
              onClick: handleAddQuiz,
            },
          ]}
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !hasAttributes ? 'Add & Close' : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

TranslationQuizStep.propTypes = {
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  localeOptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
    }),
  ).isRequired,
  languageQuizOptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
    }),
  ).isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        attributeOrder: PropTypes.number,
      }),
    ),
    subSteps: PropTypes.arrayOf(
      PropTypes.shape({
        attributes: PropTypes.arrayOf(
          PropTypes.shape({
            value: PropTypes.string,
            attributeOrder: PropTypes.number,
          }),
        ),
      }),
    ),
  }).isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  selectedQuizzes: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  handleAddQuiz: PropTypes.func.isRequired,
  handleRemoveQuiz: PropTypes.func.isRequired,
  handleQuizChange: PropTypes.func.isRequired,
  handleLocaleChange: PropTypes.func.isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
