import { useState, useCallback } from 'react';
import shortid from 'shortid';
import PropTypes from 'prop-types';
import withHooks from 'hook-hoc';
import SuccessPopover from './SuccessPopover';

export function useSuccessPopover() {
  const [successShown, setSuccessShown] = useState(false);
  const [success, setSuccess] = useState(null);

  // Key is used to force component re-render.
  const [key, setKey] = useState(shortid.generate());

  const showSuccess = useCallback((nextSuccess) => {
    setSuccess(nextSuccess);
    setSuccessShown(true);
    setKey(shortid.generate());
  }, []);

  const hideSuccess = useCallback(() => {
    setSuccess(false);
    setSuccess(null);
    setKey(shortid.generate());
  }, []);

  const spreadableProps = {
    successShown,
    hideSuccess,
    success,
    key,
  };

  return [showSuccess, hideSuccess, spreadableProps];
}

export const withErrorPopover = withHooks(() => {
  const [showSuccess, hideSuccess, successPopoverProps] = useSuccessPopover();

  return {
    showSuccess,
    hideSuccess,
    successPopoverProps,
  };
});

export const SuccessPopoverProps = PropTypes.shape({
  successShown: PropTypes.bool.isRequired,
  hideSuccess: PropTypes.func.isRequired,
  success: PropTypes.oneOfType([PropTypes.instanceOf(Error), PropTypes.bool]),
  key: PropTypes.string.isRequired,
});

export default SuccessPopover;
