import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import * as yup from 'yup';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { projectOverviewYupValidations } from '../../../../../components/ProjectForms/ProjectOverviewForm';
import { projectToolsYupValidations } from '../../../../../components/ProjectForms/ProjectToolsForm';
import {
  patchProject,
  selectProject,
} from '../../../../../store/ducks/project';
import {
  selectProjectOverviewFromProject,
  selectProjectToolsFromProject,
} from '../../../../../store/ducks/project/selectors';
import isSWFH from '../../../../ProjectSetup/helpers/isSWFH';
import OverviewAndToolsForm from './OverviewAndToolsForm';

function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {
    name: values.projectName,
    alias: values.projectAlias,
    description: values.projectDescription,
    projectType: values.projectType,
    workType: values.taskType,
    taskVolume: values.taskVolume,
  };

  if (values.hasExternalSystem) {
    patch.externalSystemId = values.externalSystem;

    if (values.externalSystem) {
      if (isSWFH(values.externalSystem)) {
        patch.swfhProjectExternalUrl = values.swfhProjectExternalUrl || null;
      } else {
        patch.swfhProjectExternalUrl = null;
      }
    }
  } else {
    patch.externalSystemId = null;
    patch.swfhProjectExternalUrl = null;
  }

  if (values.hasIPRestrictions) {
    patch.allowedIps = (values.ipAddresses || []).map((ip) => ip.ipAddress);
  } else {
    patch.allowedIps = null;
  }

  dispatch(patchProject({ changes: patch }));
}

function mapStateToProps(state) {
  const project = selectProject(state);

  const projectOverview = selectProjectOverviewFromProject(project);
  const projectTools = selectProjectToolsFromProject(project);

  return {
    projectOverview,
    projectTools,
  };
}

function mapPropsToValues(props) {
  const { projectOverview, projectTools } = props;

  return {
    projectName: projectOverview.name,
    projectAlias: projectOverview.alias,
    projectDescription: projectOverview.description,
    projectType: projectOverview.type,
    taskType: projectOverview.taskType,
    taskVolume: projectOverview.taskVolume,

    ...projectTools,
  };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: yup.object().shape({
      ...projectOverviewYupValidations(selectProject),
      ...projectToolsYupValidations,
    }),
  }),
)(OverviewAndToolsForm);
