import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import {
  Container,
  Option,
  Circle,
  CircleContour,
  CircleCenter,
  Label,
} from './styles';
import handleKeyDown from '../../pages/ProjectSetup/helpers/handleKeyDown';
import MessageError from '../MessageError';

export default function Radio(props) {
  const {
    name,
    options,
    disabled,
    value,
    onChange,
    vertical,
    error,
    errorIncreasesHeight,
  } = props;

  const handleChange = useCallback(
    (index) => (event) => {
      if (typeof onChange === 'function') {
        event.target.name = name;
        event.target.value = options[index].value;

        onChange(event);
      }
    },
    [name, onChange, options],
  );

  return (
    <Container isDisabled={disabled}>
      {options.map((option, index) => (
        <Option
          key={option.label}
          role="radio"
          onClick={handleChange(index)}
          onKeyDown={handleKeyDown(handleChange(index))}
          tabIndex={0}
          isDisabled={option.disabled}
          isVertical={vertical}
          isFirst={index === 0}
        >
          <Circle>
            <CircleContour hasError={!!error} isActive={value === option.value}>
              <CircleCenter isActive={value === option.value} />
            </CircleContour>
          </Circle>
          <Label>{option.label}</Label>
        </Option>
      ))}
      <MessageError
        isVisible={!!error}
        message={error}
        increasesHeight={errorIncreasesHeight}
      />
    </Container>
  );
}

Radio.propTypes = {
  name: PropTypes.string.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.any.isRequired,
      label: PropTypes.string.isRequired,
      disabled: PropTypes.bool,
    }),
  ).isRequired,
  disabled: PropTypes.bool,
  // eslint-disable-next-line react/forbid-prop-types
  value: PropTypes.any,
  onChange: PropTypes.func,
  vertical: PropTypes.bool,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([false])]),
  errorIncreasesHeight: PropTypes.bool,
};

Radio.defaultProps = {
  disabled: false,
  value: undefined,
  onChange: undefined,
  vertical: false,
  error: undefined,
  errorIncreasesHeight: false,
};
