import React, { useMemo } from 'react';
import { capitalize, get } from 'lodash';
import PropTypes from 'prop-types';
import getFraudByType from '../../helpers/fraudSignals';
import Modal from '../../../Modal';
import Table from '../../../Table';
import { Popover } from '../../../ErrorPopover/styles';
import ActionFooter from '../../../ActionFooter';
import { Label } from '../../../../styles';
import {
  Resume,
  Footer,
  TableHeader,
  TableBody,
  Spacer,
  PopoverWrapper,
} from '../styles';
import { MaliciousTag } from '../../styles';

export default function Maliciousness(props) {
  const {
    // Modal
    modalProps: { open, onExited },
    modalControls: { closeModal },
    // Data
    data,
  } = props;

  const columns = [
    {
      Header: 'Type',
      accessor: 'type',
      key: 'type',
      width: 90,
    },
    {
      Header: 'Signals',
      accessor: 'signals',
      key: 'signals',
      width: 436,
    },
  ];

  const actionButtons = [
    {
      type: 'secondary',
      text: 'Close',
      onClick: closeModal,
    },
  ];

  const keys = Object.keys(get(data, 'flags', {}));

  const flags = keys.map((key) => {
    return {
      type: getFraudByType(get(data, ['flags', key, 'type'], null)),
      signals: get(
        data,
        ['flags', key, 'description'],
        'No description provided',
      ),
    };
  });

  const suspiciousCounts = get(data, 'suspicious_trigger_counts', 0);

  const maliciousnessLevel = capitalize(
    get(data, 'maliciousness_level', 'low'),
  );

  const signalsText = useMemo(
    () => (suspiciousCounts > 1 ? 'signals' : 'signal'),
    [suspiciousCounts],
  );

  return (
    <Modal
      open={open}
      onExited={onExited}
      onClose={closeModal}
      title="Fraud Check"
    >
      <div>
        <Label>Maliciousness</Label>
        <Resume>
          {suspiciousCounts} {signalsText}
        </Resume>
        <MaliciousTag level={maliciousnessLevel} margin={'8px 0 0 0'}>
          {maliciousnessLevel}
        </MaliciousTag>
        <Spacer />
        <TableHeader>Fraud report</TableHeader>
        <TableBody>
          <Table columns={columns} data={flags} />
        </TableBody>
        {maliciousnessLevel === 'High' && (
          <PopoverWrapper>
            <Popover>
              This user exceeds the amount of fraud signals considered safe.
              Rejection is highly recommended to prevent fraud.
            </Popover>
          </PopoverWrapper>
        )}
      </div>
      <Footer>
        <ActionFooter hideDivider rightButtons={actionButtons} />
      </Footer>
    </Modal>
  );
}

Maliciousness.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  data: PropTypes.shape({
    user_id: PropTypes.string,
    flags: PropTypes.objectOf(PropTypes.any),
    num_locales: PropTypes.number,
    num_self_reported_langauges: PropTypes.number,
    suspicious_trigger_counts: PropTypes.number,
    num_of_flags: PropTypes.number,
    maliciousness_level: PropTypes.string,
    flag_stats: PropTypes.objectOf(PropTypes.any),
  }).isRequired,
};
