import { createSlice } from '@reduxjs/toolkit';

export const INVOICE_TYPES = {
  USER_SELF_REPORTED: 'userSelfReported',
  SYSTEM_GENERATED: 'systemGenerated',
  USER_SELF_REPORTED_AND_SYSTEM_APPROVED: 'userSelfReportedAndSystemApproved',
};

export const INVOICE_TYPES_NAMES = {
  userSelfReported: 'User self-reported',
  systemGenerated: 'System generated',
  userSelfReportedAndSystemApproved: 'User self-reported and System approved ',
};

export const invoiceTypes = [
  {
    label: INVOICE_TYPES_NAMES[INVOICE_TYPES.USER_SELF_REPORTED],
    value: INVOICE_TYPES.USER_SELF_REPORTED,
  },
  {
    label: INVOICE_TYPES_NAMES[INVOICE_TYPES.SYSTEM_GENERATED],
    value: INVOICE_TYPES.SYSTEM_GENERATED,
  },
  {
    label:
      INVOICE_TYPES_NAMES[INVOICE_TYPES.USER_SELF_REPORTED_AND_SYSTEM_APPROVED],
    value: INVOICE_TYPES.USER_SELF_REPORTED_AND_SYSTEM_APPROVED,
  },
];

const initialState = {
  configurations: {},
  loading: true,
  saving: false,
  error: null,

  modalOpen: false,
  modalData: null,
};

const slice = createSlice({
  name: 'invoiceConfiguration',
  initialState,
  reducers: {
    fetchInvoiceConfiguration(state) {
      state.loading = true;
    },

    fetchSucceeded(state, action) {
      state.loading = false;

      const {
        payload: { projectId, configuration },
      } = action;

      state.configurations[projectId] = configuration;
    },

    fetchFailed(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    openModal(state, action) {
      state.modalOpen = true;
      state.modalData = action.payload || null;
    },

    closeModal(state) {
      state.modalOpen = false;
      state.modalData = null;
      state.error = null;
    },

    setConfiguration(state) {
      state.saving = true;
      state.error = null;
    },

    mutationSucceeded(state, action) {
      const {
        payload: { projectId, configuration },
      } = action;

      state.configurations[projectId] = configuration;
      state.modalOpen = false;
      state.modalData = null;
      state.saving = false;
    },

    mutationFailed(state, action) {
      state.saving = false;
      state.error = action.payload;
    },
  },
});

const {
  reducer,
  actions: {
    fetchInvoiceConfiguration,
    fetchFailed,
    fetchSucceeded,
    openModal,
    closeModal,
    setConfiguration,
    updateConfiguration,
    mutationFailed,
    mutationSucceeded,
  },
} = slice;

reducer.key = slice.name;

export {
  fetchInvoiceConfiguration,
  fetchFailed,
  fetchSucceeded,
  openModal,
  closeModal,
  setConfiguration,
  updateConfiguration,
  mutationFailed,
  mutationSucceeded,
};

export default reducer;

export function selectInvoiceConfigurationSlice(state) {
  return state[slice.name];
}

export function selectInvoiceConfiguration(state, projectId) {
  const currentSlice = selectInvoiceConfigurationSlice(state);

  return {
    configuration: currentSlice.configurations[projectId] || null,
    loading: currentSlice.loading,
    saving: currentSlice.saving,
    error: currentSlice.error,
  };
}

export function selectLoading(state) {
  return selectInvoiceConfigurationSlice(state).loading;
}

export function selectSaving(state) {
  return selectInvoiceConfigurationSlice(state).saving;
}

export function selectError(state) {
  return selectInvoiceConfigurationSlice(state).error;
}

export function selectModalState(state) {
  const currentSlice = selectInvoiceConfigurationSlice(state);

  return {
    modalOpen: currentSlice.modalOpen,
    modalData: currentSlice.modalData,
  };
}

export function createSelectInvoiceConfiguration(projectId) {
  return (state) => selectInvoiceConfiguration(state, projectId);
}
