import React, { useEffect, useState, useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import { Row, Col } from 'styled-bootstrap-grid';
import {
  Header,
  DemographicDistribution,
  DemographicDistributionInModal,
  CloseButton,
} from './styles';
import { Section } from '../../../styles';
import closeIcon from '../../../assets/icons/close.svg';
import DemographicData from './DemographicData';
import { selectProject } from '../../../store/ducks/project';
import { selectTotal as selectTotalHiringTargets } from '../../../store/ducks/hiringTargets';
import { useModal } from '../../../components/Modal';
import {
  fetchDemographics,
  selectLoading,
  selectError,
  setMetricsType,
  hasAnyDemographicData,
  selectHasCustomMetrics,
  selectMetricsType,
  DEMOGRAPHIC_METRICS_TYPE,
  setHasCustomMetrics,
  selectRequirements,
  selectDemographicType,
  DEMOGRAPHIC_TYPE,
} from '../../../store/ducks/projectDemographicMetrics';
import { createKeydownHandler } from '../../../helpers/a11y';
import TabSwitcher from '../../../components/TabSwitcher';
import BottomControls from './BottomControls';
import Loading from '../Loading';
import Select from '../../../components/Select';
import legacyIntegration from '../../../helpers/legacy-integration';

const viewAsNumberOrPercentTabs = [
  {
    label: 'NUMBER',
    'data-testid': 'demographics-modal--tab-as-number',
  },
  {
    label: 'PERCENTAGE',
    'data-testid': 'demographics-modal--tab-as-percentage',
  },
];

const viewAsOptions = [
  { value: 1, label: 'Custom demographics' },
  { value: 2, label: 'Total by category' },
];

export default function Demographics(props) {
  const { inModal } = props;

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchDemographics({ loadCustomMetrics: true }));
  }, [dispatch]);

  const [viewAs, setviewAs] = useState(1);
  const [viewAsPercent, setViewAsPercent] = useState(0);

  const handleViewMetricsChange = useCallback(
    ({ value }) => {
      setMetricsType(
        value === 1
          ? DEMOGRAPHIC_METRICS_TYPE.CUSTOM
          : DEMOGRAPHIC_METRICS_TYPE.GENERAL,
      );
      if (value !== viewAs) {
        dispatch(
          fetchDemographics({
            ...(value === 1
              ? { loadCustomMetrics: true }
              : { loadGeneralMetrics: true }),
          }),
        );
      }
    },
    [dispatch, viewAs],
  );

  const handleViewAsPercentChange = useCallback(
    (value) => {
      setViewAsPercent(value);
      if (value !== viewAsPercent) {
        const params = {
          ...(viewAs === 1
            ? { loadCustomMetrics: true }
            : { loadGeneralMetrics: true }),
          viewAsPercent: value,
        };
        dispatch(fetchDemographics(params));
      }
    },
    [dispatch, viewAsPercent, setViewAsPercent, viewAs],
  );
  const project = useSelector(selectProject);

  const loadingDemographics = useSelector(selectLoading);
  const hasDemographics = useSelector(hasAnyDemographicData);
  const [, modalControls] = useModal(true);
  const metricsType = useSelector(selectMetricsType);
  const hasCustomMetrics = useSelector(selectHasCustomMetrics);
  const demographicsError = useSelector(selectError);
  const totalHiringTargets = useSelector(selectTotalHiringTargets);

  const requirements = useSelector(selectRequirements)
    .map((r) => r.label)
    .join(', ');
  const demographicType =
    useSelector(selectDemographicType) === DEMOGRAPHIC_TYPE.BY_GROUP
      ? 'Requirements by group'
      : 'Aggregated requirements';

  useEffect(() => {
    if (
      !loadingDemographics &&
      !hasDemographics &&
      !demographicsError &&
      totalHiringTargets
    ) {
      dispatch(fetchDemographics({ loadGeneralMetrics: true }));
      setHasCustomMetrics(false);
    }
  }, [
    dispatch,
    loadingDemographics,
    hasDemographics,
    demographicsError,
    totalHiringTargets,
  ]);

  useEffect(() => {
    setviewAs(metricsType === DEMOGRAPHIC_METRICS_TYPE.CUSTOM ? 1 : 2);
  }, [metricsType]);

  const close = useCallback(() => {
    legacyIntegration.informDemographicsClose(project);
  }, [project]);

  const Wrapper = useMemo(() => {
    if (inModal) {
      return DemographicDistributionInModal;
    }

    return DemographicDistribution;
  }, [inModal]);

  return (
    <>
      <Wrapper>
        {!inModal && (
          <Header>
            <h2>Demographic Distribution</h2>
            <CloseButton
              onClick={close}
              onKeyDown={createKeydownHandler(close)}
              role="button"
              tabIndex={0}
            >
              <img src={closeIcon} alt="Remove current step from process" />
            </CloseButton>
          </Header>
        )}
        {!loadingDemographics && (
          <Section marginBottom="30px">
            <Row>
              <Col col={6}>
                {hasCustomMetrics
                  ? `${demographicType || 'Demographic Requirements'} ${
                      requirements ? `(${requirements})` : ''
                    }.`
                  : 'No custom requirements configured'}
              </Col>
            </Row>
          </Section>
        )}
        {hasCustomMetrics && (
          <Section marginBottom="24px">
            <Row>
              <Col col={6}>
                <Select
                  value={viewAs}
                  name="viewAsOptions"
                  id="viewAsOptions"
                  options={viewAsOptions}
                  onChange={handleViewMetricsChange}
                />
              </Col>
              <Col col={6}>
                <TabSwitcher
                  tabs={viewAsNumberOrPercentTabs}
                  value={viewAsPercent}
                  onChange={handleViewAsPercentChange}
                />
              </Col>
            </Row>
          </Section>
        )}
        {loadingDemographics ? (
          <Loading />
        ) : (
          <DemographicData
            closeModal={modalControls.closeModal}
            openModal={modalControls.openModal}
            modalData={modalControls.modalData}
            viewAsPercent={viewAsPercent}
          />
        )}
        {!inModal && (
          <BottomControls
            currentStep={2}
            exportLabel="Export Data"
            exportLoading={false}
            exportDisabled
            onExport={() => {}}
            closeLabel="Close"
            onClose={close}
          />
        )}
      </Wrapper>
    </>
  );
}
Demographics.propTypes = {
  inModal: PropTypes.bool,
};

Demographics.defaultProps = {
  inModal: false,
};
