import React from 'react';
import PropTypes from 'prop-types';

import InformationTooltip, {
  LabelWrapper,
} from '../../ProcessBuilder/InformationTooltip';
import { Label } from '../../../styles';
import PostMessageLink from '../../PostMessageLink';

export default function LabelGroupComponent({ index, customerId }) {
  return (
    <LabelWrapper>
      <Label>User Group {index || ''}</Label>
      <InformationTooltip
        title={
          <>
            User groups need to be assigned to
            <br />
            specific customers to appear on this
            <br />
            dropdown list.
            <br />
            <br />
            If you need to create new user groups
            <br />
            for this customer,{' '}
            <PostMessageLink to={`/qrp/core/customer/view/${customerId}`}>
              click here
            </PostMessageLink>
          </>
        }
      />
    </LabelWrapper>
  );
}

LabelGroupComponent.propTypes = {
  customerId: PropTypes.number.isRequired,
  index: PropTypes.number,
};

LabelGroupComponent.defaultProps = {
  index: undefined,
};
