import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { v4 as uuid } from 'uuid';
import { CardInfo, CardTitle, CardDescription, ChevronWrapper } from './styles';
import { ReactComponent as Chevron } from '../../../../assets/icons/double-chevron-right.svg';

export default function CardViewMode(props) {
  const { topics } = props;

  return (
    <Fragment key={uuid()}>
      {topics.map(({ name, question, parentQuestionId }) => (
        <CardInfo key={uuid()}>
          {!!parentQuestionId && (
            <ChevronWrapper>
              <Chevron />
            </ChevronWrapper>
          )}
          <CardTitle>{name}</CardTitle>
          <CardDescription>{question}</CardDescription>
        </CardInfo>
      ))}
    </Fragment>
  );
}

CardViewMode.propTypes = {
  topics: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      question: PropTypes.string,
      parentQuestionId: PropTypes.number,
    }),
  ),
};

CardViewMode.defaultProps = {
  topics: [],
};
