import { withFormik } from 'formik';
import * as Yup from 'yup';
import shortId from 'shortid';
import { compose, bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PayratesModal from './PayratesModal';
import * as payratesActions from '../../../../store/ducks/payrates';
import { selectById as selectDefaultPayrateById } from '../../../../store/ducks/defaultPayrates';
import {
  selectById,
  selectByMatchingProps,
} from '../../../../store/ducks/hiringTargets';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import resetForm from '../../../../pages/ProjectSetup/helpers/resetForm';

const WORKDAY_TASK_ID_PATTERN = /(^PROJECT_PLAN_TASK-(\d{1})-(\d{1,}))|(^(P|LR|CR)(\d{5}))$/;

function handleSubmit(values, formikBag) {
  const {
    props: {
      payrates,
      modalControls: { closeModal },
      showError,
      hideError,
    },
  } = formikBag;

  const {
    hiringTarget: hiringTargetId,
    customPayrate,
    selectedPayrates,
  } = values;

  return new Promise((resolve) => {
    const state = store.getState();

    function onSuccess() {
      hideError();
      closeModal();

      setTimeout(() => {
        resetForm(
          formikBag,
          {
            hiringTarget: -1,
          },
          resolve,
        );
      }, 225);
    }

    function onError(message) {
      showError(new Error(message));

      // Error is shown one tick before button is clickable
      setImmediate(() => resolve());
    }

    if (!customPayrate && selectedPayrates && selectedPayrates.length) {
      const selectedPayratesList = selectedPayrates.map((selectedPayRateId) => {
        const defaultPayrate = selectDefaultPayrateById(
          state,
          selectedPayRateId,
        );
        const hiringTarget = selectById(state, defaultPayrate.hiringTarget);

        return {
          id: selectedPayRateId,
          enabled: true,
          checked: false,
          ...defaultPayrate,
          groupId: defaultPayrate.defaultRate ? 1 : defaultPayrate.userGroup,
          taskType: defaultPayrate.defaultRate
            ? 'DEFAULT'
            : defaultPayrate.taskType,
          defaultRate: defaultPayrate.defaultRate || false,
          state: defaultPayrate.state || null,
          restrictToCountry: hiringTarget.restrictToCountry,
          localeLanguage: hiringTarget.localeLanguage,
          localeDialect: hiringTarget.localeDialect,
          localeLanguageTo: hiringTarget.localeLanguageTo,
          localeDialectTo: hiringTarget.localeDialectTo,
        };
      });

      payrates.addMany(selectedPayratesList, { onSuccess, onError });
    } else {
      // We are creating or editing a Custom Pay Rate

      const hiringTarget = selectById(store.getState(), hiringTargetId);

      const newPayrate = {
        id: values.id || shortId.generate(),
        writtenFluency: values.writtenFluency,
        spokenFluency: values.spokenFluency,
        certifiedFluency: values.certifiedFluency,
        userGroup: values.userGroup,
        userGroupId: values.userGroup,
        taskType: values.taskType,
        workdayTaskId: values.workdayTaskId,
        rate: values.rate,
        defaultRate: !customPayrate,
        restrictToCountry: hiringTarget.restrictToCountry,
        localeLanguage: hiringTarget.localeLanguage,
        localeDialect: hiringTarget.localeDialect,
        localeLanguageTo: hiringTarget.localeLanguageTo,
        localeDialectTo: hiringTarget.localeDialectTo,
      };

      if (!values.id) {
        // We are creating a new Custom Pay Rate
        payrates.addOne(
          {
            id: newPayrate.id,
            enabled: true,
            checked: false,
            ...newPayrate,
          },
          { onSuccess, onError },
        );
      } else {
        // We are updating an existing Custom Pay Rate
        payrates.updateOne(
          {
            id: values.id,
            changes: newPayrate,
          },
          { onSuccess, onError },
        );
      }
    }
  });
}

function mapPropsToValues(props) {
  const {
    modalControls: { modalData },
    rateType,
    businessUnit,
  } = props;

  if (modalData) {
    if (modalData.restoring) {
      return modalData.values;
    }

    const hiringTarget = selectByMatchingProps(store.getState(), {
      localeLanguage: modalData.localeLanguage,
      localeDialect: modalData.localeDialect,
      localeLanguageTo: modalData.localeLanguageTo,
      localeDialectTo: modalData.localeDialectTo,
      restrictToCountry: modalData.restrictToCountry,
    });

    return {
      id: modalData.id,
      rate: modalData.rate,
      hiringTarget: hiringTarget && hiringTarget.id,
      writtenFluency: modalData.writtenFluency,
      spokenFluency: modalData.spokenFluency,
      userGroup: modalData.userGroupId,
      certifiedFluency: modalData.certifiedFluency || false,
      taskType: modalData.taskTypeValue,
      workdayTaskId: modalData.workdayTaskId,
      localeLanguage: modalData.localeLanguage,
      localeDialect: modalData.localeDialect,
      localeLanguageTo: modalData.localeLanguageTo,
      localeDialectTo: modalData.localeDialectTo,
      restrictToCountry: modalData.restrictToCountry,
      // modal specific values
      mode: modalData.id ? 'edit' : 'copy',
      customPayrate: modalData.defaultRate && modalData.id ? 0 : 1,
    };
  }

  return {
    customPayrate: rateType === 'PIECERATE' ? 1 : 0,
    rateType,
    businessUnit,
  };
}

function bindDispatch(dispatch) {
  return {
    payrates: bindActionCreators(payratesActions, dispatch),
  };
}

const validationSchema = Yup.object().shape({
  hiringTarget: Yup.string().required(requiredMessage`Hiring Target`),
  customPayrate: Yup.number().oneOf([0, 1]).required(),
  workdayTaskId: Yup.string()
    .matches(WORKDAY_TASK_ID_PATTERN, 'Incorrect format type')
    .nullable(),
  writtenFluency: Yup.string().when('customPayrate', {
    is: 1,
    then: Yup.string().required(requiredMessage`Written Fluency`),
  }),

  spokenFluency: Yup.string().when('customPayrate', {
    is: 1,
    then: Yup.string().required(requiredMessage`Spoken Fluency`),
  }),

  userGroup: Yup.string().when('customPayrate', {
    is: 1,
    then: Yup.string().required(requiredMessage`User Group`),
  }),

  taskType: Yup.string().when('customPayrate', {
    is: 1,
    then: Yup.string().required(requiredMessage`Task Type`),
  }),

  rate: Yup.number().when('customPayrate', {
    is: 1,
    then: Yup.number().required(requiredMessage`Rate`),
  }),
});

export default compose(
  connect(null, bindDispatch),
  withFormik({
    handleSubmit,
    validateOnMount: true,
    validateOnBlur: true,
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
)(PayratesModal);
