import { withFormik } from 'formik';
import { v4 as uuidv4 } from 'uuid';

import { findLast } from 'lodash';
import { selectAll as selectAllHiringTargets } from '../../../../../../store/ducks/hiringTargets';
import { selectMarketAggregation } from '../../../../../../store/ducks/metricsAndGoals';

import store from '../../../../../../store';
import { dateFormat, aggregatedList } from '../../helpers';
import QualityGoalsForm from './QualityGoalsForm';

const formatGoalsValues = ({ hiringTargets, aggregation, goals, goalType }) => {
  const allMarkets = aggregatedList(hiringTargets, aggregation);
  const goal = { ...findLast(goals, (el) => el.goalType === goalType) };
  goal.qualityMarketGoals = allMarkets.map((market) => {
    const empty = {
      market,
      goalValue: 0,
    };
    let value = {};
    if (goal.qualityMarketGoals) {
      value = goal.qualityMarketGoals.find((g) => g.market === market) || {};
    }

    return { ...empty, ...value };
  });

  if (goal.goalValueType === null) goal.goalValueType = 'EQUAL';

  if (goal.goalValueType === 'PER_MARKET') {
    goal.goalValue = null;
    goal.productivityMarketGoals = null;
  }

  return goal;
};

const mapPropsToValues = ({
  goals,
  metricTitle,
  metricKey,
  isQuizBased,
  metric,
}) => {
  const hiringTargets = selectAllHiringTargets(store.getState());
  const aggregation = selectMarketAggregation(store.getState());
  const versions = [];
  const uniqGoals = goals.map((g) => ({ ...g, key: uuidv4() }));
  let sVersion = null;
  let sDate = null;
  let isEdit = false;

  const projectGoals = formatGoalsValues({
    hiringTargets,
    aggregation,
    goals: uniqGoals,
    goalType: 'PROJECT',
  });

  const workerGoals = formatGoalsValues({
    hiringTargets,
    aggregation,
    goals: uniqGoals,
    goalType: 'WORKER',
  });

  if (isQuizBased) {
    for (let i = uniqGoals.length - 1; i > 0; i -= 2) {
      const pGoals = formatGoalsValues({
        hiringTargets,
        aggregation,
        goals: [uniqGoals[i], uniqGoals[i - 1]],
        goalType: 'PROJECT',
      });
      const wGoals = formatGoalsValues({
        hiringTargets,
        aggregation,
        goals: [uniqGoals[i], uniqGoals[i - 1]],
        goalType: 'WORKER',
      });

      const version = {
        value: uuidv4(),
        label: dateFormat(uniqGoals[i].quizDate),
        projectGoals: pGoals,
        workerGoals: wGoals,
      };
      versions.push(version);
    }

    if (versions.length > 1 && versions[0].label !== '') {
      versions[0].label = `Current quiz - ${versions[0].label}`;
      isEdit = true;
      sVersion = versions[0].value;
      sDate = goals[0].quizDate;
    }
  }

  return {
    metric,
    metricTitle,
    projectGoals,
    workerGoals,
    key: metricKey,
    versions,
    sVersion,
    sDate,
    goals: uniqGoals,
    isQuizBased,
    isEdit,
    metricId: metric.id,
  };
};

export default withFormik({
  mapPropsToValues,
})(QualityGoalsForm);
