import React, { useEffect, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import {
  EDITABLE_ENTITIES,
  editor,
  useCloseEditor,
} from '../../../../../store/ducks/editableEntities';
import { ENTITY_METADATA } from '../../../../../store/ducks/options';
import { selectProjectId } from '../../../../../store/ducks/project';
import { Modal } from '../../../../Modal/Modal';
import AcademyResourceForm from './AcademyResourceForm';
import DocumentResourceForm from './DocumentResourceForm';
import QuizResourceForm from './QuizResourceForm';
import ResourceTypePicker from './ResourceTypePicker';
import { FormContainer } from './styles';
import SurveyResourceForm from './SurveyResourceForm';

export default function ResourceEditorModal() {
  const editorState = useSelector(
    editor.selectors.createSelectEditorSliceFor(
      EDITABLE_ENTITIES.PROJECT_RESOURCES,
    ),
  );

  const projectId = useSelector(selectProjectId);

  const closeModal = useCloseEditor(EDITABLE_ENTITIES.PROJECT_RESOURCES);

  const modalTitle = useMemo(() => {
    if (editorState.operation === 'create') {
      return 'Add Project Resource';
    }

    if (editorState.operation === 'copy') {
      return 'Copy Project Resource';
    }

    return 'Edit Project Resource';
  }, [editorState.operation]);

  const [resourceType, setResourceType] = useState(
    ENTITY_METADATA.projectResources.types.quiz,
  );

  const ActiveForm = useMemo(() => {
    switch (resourceType) {
      case ENTITY_METADATA.projectResources.types.quiz:
        return QuizResourceForm;

      case ENTITY_METADATA.projectResources.types.survey:
        return SurveyResourceForm;

      case ENTITY_METADATA.projectResources.types.academy:
        return AcademyResourceForm;

      case ENTITY_METADATA.projectResources.types.document:
        return DocumentResourceForm;

      default:
        return QuizResourceForm;
    }
  }, [resourceType]);

  useEffect(() => {
    if (editorState.data && editorState.data.type) {
      const newResourceType =
        editorState.operation === 'create'
          ? ENTITY_METADATA.projectResources.types.quiz
          : editorState.data.type;
      setResourceType(newResourceType);
    }
  }, [editorState.data, editorState.operation]);

  return (
    <Modal open={editorState.open} onClose={closeModal} title={modalTitle}>
      <ResourceTypePicker
        setResourceType={setResourceType}
        resourceType={resourceType}
        operation={editorState.operation}
      />
      <FormContainer>
        <ActiveForm closeModal={closeModal} projectId={projectId} />
      </FormContainer>
    </Modal>
  );
}
