import React, { useMemo } from 'react';
import PropTypes from 'prop-types';

export default function RepeatCell(props) {
  const {
    row: { original: masterProfileQuestionData },
  } = props;

  const { timer } = masterProfileQuestionData;

  const text = useMemo(
    () =>
      ({
        '-1': 'No lock-up',
        '1': 'Answer once',
        null: 'Not defined',
      }[timer] || `${timer} months`),
    [timer],
  );

  return <>{text}</>;
}

RepeatCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      masterProfileQuestionData: PropTypes.shape({
        timer: PropTypes.number,
      }),
    }),
  }).isRequired,
};

RepeatCell.defaultProps = {};
