import React from 'react';
import PropTypes from 'prop-types';
import { Container, Popover, ErrorTitle, Img, Wrapper } from './styles';
import closeRed from '../../assets/icons/close-red.svg';

export default function ErrorPopover(props) {
  const {
    title,
    errorShown,
    hideError,
    error,
    keepInDocumentFlow,
    fixedToFooter,
  } = props;

  if (!error || !errorShown) {
    return null;
  }

  return (
    <Container
      fixedToFooter={fixedToFooter}
      keepInDocumentFlow={keepInDocumentFlow}
    >
      <Wrapper keepInDocumentFlow={keepInDocumentFlow}>
        <Popover>
          <ErrorTitle>{title || 'Error'}</ErrorTitle>
          {!keepInDocumentFlow && (
            <Img
              role="button"
              onClick={() => hideError()}
              onKeyDown={({ keyCode }) => keyCode === 13 && hideError()}
              tabIndex={0}
              src={closeRed}
              alt="hide error"
            />
          )}
          {error.message}
        </Popover>
      </Wrapper>
    </Container>
  );
}

ErrorPopover.propTypes = {
  title: PropTypes.string,
  errorShown: PropTypes.bool.isRequired,
  hideError: PropTypes.func.isRequired,
  error: PropTypes.oneOfType([PropTypes.instanceOf(Error), PropTypes.bool]),
  keepInDocumentFlow: PropTypes.bool,
  fixedToFooter: PropTypes.bool,
};

ErrorPopover.defaultProps = {
  title: '',
  error: null,
  keepInDocumentFlow: false,
  fixedToFooter: false,
};
