import React from 'react';
import PropTypes from 'prop-types';
import HeaderCheckboxContainer from './styles';
import Checkbox from '../Checkbox';

export default function CheckboxHeader({ getToggleAllRowsSelectedProps }) {
  const checkboxProps = getToggleAllRowsSelectedProps();

  /*
    // This function unchecks all checkboxes when intederminate state checkbox is toggled.
    // We are not sure what is the best behaviour before testing with users.
    const handleChange = useCallback((event) => {
      if (checkboxProps.indeterminate) {
        event.target.checked = false;
      }

      return checkboxProps.onChange(event);
    }, [checkboxProps]);
  */

  return (
    <HeaderCheckboxContainer>
      <Checkbox
        onChange={checkboxProps.onChange}
        checked={checkboxProps.checked}
        title={checkboxProps.title}
        indeterminate={checkboxProps.indeterminate}
        style={checkboxProps.style}
      />
    </HeaderCheckboxContainer>
  );
}

CheckboxHeader.propTypes = {
  getToggleAllRowsSelectedProps: PropTypes.func.isRequired,
};
