import { compose } from 'recompose';
import { connect } from 'react-redux';
import { withFormik } from 'formik';
import withHooks from 'hook-hoc';
import { withErrorPopover } from '../../../components/ErrorPopover';
import { saveCategory } from '../../../store/ducks/profileQuestions';
import { PAGINATED_ENTITIES, setFilters } from '../../../store/ducks/paginated';
import CategoryModal from './CategoryModal';
import validationSchema from './validations';

function mapStateToProps() {
  return {};
}

function mapPropsToValues(props) {
  const {
    modalControls: { modalData },
  } = props;

  return modalData || {};
}

function useHooksAsProps() {
  return {};
}

function handleSubmit(values, { props }) {
  const {
    dispatch,
    modalControls: { closeModal },
  } = props;

  const { id = null, title, description } = values;

  const payload = {
    id,
    title,
    description,
  };

  dispatch(
    saveCategory({
      changes: payload,
      thenPut:
        setFilters({
          forKey: PAGINATED_ENTITIES.profileQuestions,
          loading: false,
        }) && closeModal(),
    }),
  );
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema,
  }),
  withHooks(useHooksAsProps),
)(CategoryModal);
