import React from 'react';
import PropTypes from 'prop-types';
import Icon from '../../../../Icon';
import ChevronDown from '../../../../../assets/icons/chevron-down-gray.svg';

export default function ExpanderCell({ row }) {
  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <span {...row.getToggleRowExpandedProps()}>
      {row.isExpanded ? (
        <Icon
          inheritColor={false}
          iconButton
          style={{
            lineHeight: '25px',
            textAlign: 'right',
            height: '22px',
          }}
          icon={<img src={ChevronDown} alt="Toggle" />}
        />
      ) : (
        <Icon
          inheritColor={false}
          iconButton
          style={{
            lineHeight: '25px',
            textAlign: 'right',
            height: '22px',
          }}
          icon={<img src={ChevronDown} alt="Toggle" />}
        />
      )}
    </span>
  );
}

ExpanderCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    getToggleRowExpandedProps: PropTypes.func.isRequired,
  }).isRequired,
};
