import React, { useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import { setDirty, setMode } from '../../../../../store/ducks/projectView';
import ActionFooter from '../../../../../components/ActionFooter';
import { themePropTypes } from '../../../../../styles/themes/default';
import * as projectPages from '../../../../../store/ducks/projectPages';
import useFirstChange from '../../../../ProjectSetup/helpers/useFirstChange';
import { lockLeave } from '../../../../../store/ducks/pageLeave';
import { CardContentContainer, CardFooter } from '../../../styles';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../../components/ErrorPopover';
import ProjectUserProjectPageForm from '../../../../../components/ProjectForms/ProjectUserProjectPageForm';
import { selectSaving, selectError } from '../../../../../store/ducks/project';

export default function UserProjectPageForm(props) {
  const {
    dirty,
    submitCount,
    submitForm,
    showError,
    errorPopoverProps,
    values,
    handleChange,
    theme,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setDirty(dirty));
  }, [dirty, dispatch]);

  const cancelEditing = useCallback(() => dispatch(setMode('view')), [
    dispatch,
  ]);

  const saving = useSelector(selectSaving);
  const error = useSelector(selectError);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  useEffect(() => {
    dispatch(projectPages.fetchAll());
  }, [dispatch]);

  useFirstChange(
    useCallback(() => dispatch(lockLeave()), [dispatch]),
    values,
  );
  return (
    <>
      <CardContentContainer>
        <Row>
          <Col col={8}>
            <ProjectUserProjectPageForm
              values={values}
              handleChange={handleChange}
              theme={theme}
            />
          </Col>
        </Row>
      </CardContentContainer>
      <CardFooter>
        <ErrorPopover {...errorPopoverProps} />
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              text: 'Cancel',
              type: 'secondary',
              disabled: saving,
              onClick: cancelEditing,
            },
            {
              text: 'Save Changes',
              type: 'primary',
              loading: saving,
              onClick: submitForm,
            },
          ]}
        />
      </CardFooter>
    </>
  );
}

UserProjectPageForm.propTypes = {
  dirty: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  submitForm: PropTypes.func.isRequired,
  showError: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  theme: themePropTypes.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
};
