import { useFormikContext } from 'formik';
import withHooks from 'hook-hoc';
import { useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import shortid from 'shortid';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
  selectors,
} from '../../../../store/ducks/options';
import BlockingRules from './BlockingRules';

const FIELD_NAME = 'blockingRules';

function useHooksAsProps() {
  const dispatch = useDispatch();

  const { values, setFieldValue } = useFormikContext();

  const {
    [FIELD_NAME]: blockingRules = [
      { key: shortid.generate(), projectId: null, permanent: false },
    ],
  } = values;

  const handleChange = useCallback(
    (value) => {
      setFieldValue('blockingRules', value);
    },
    [setFieldValue],
  );

  useEffect(() => {
    dispatch(
      fetch({
        optionsFor: ENTITIES.projects,
        nestedKey: ENTITY_METADATA.projects.nestedKeys.ENABLED,
      }),
    );
  }, [dispatch]);

  const enabledProjects = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.projects,
      ENTITY_METADATA.projects.nestedKeys.ENABLED,
    ),
  );

  const handleAddLine = useCallback(() => {
    handleChange([
      ...(blockingRules || []),
      {
        projectId: null,
        permanent: false,
        key: shortid.generate(),
      },
    ]);
  }, [handleChange, blockingRules]);

  const handleRemove = useCallback(
    (index) => {
      handleChange([
        ...blockingRules.slice(0, index),
        ...blockingRules.slice(index + 1, blockingRules.length),
      ]);
    },
    [handleChange, blockingRules],
  );

  const handleToggle = useCallback(
    (status, index) => {
      const item = blockingRules[index];

      const { value } = status && status.target ? status.target : {};

      handleChange([
        ...blockingRules.slice(0, index),
        { ...item, permanent: !!value },
        ...blockingRules.slice(index + 1, blockingRules.length),
      ]);
    },
    [handleChange, blockingRules],
  );

  const handleProjectChange = useCallback(
    ({ value: newValue }, index) => {
      const item = blockingRules[index];

      handleChange([
        ...blockingRules.slice(0, index),
        { ...item, projectId: newValue },
        ...blockingRules.slice(index + 1, blockingRules.length),
      ]);
    },
    [handleChange, blockingRules],
  );

  const selectedProjects = useMemo(
    () => blockingRules.map((br) => br.projectId),
    [blockingRules],
  );

  const projectOptions = useMemo(
    () => mapToOptions('id', 'name')(enabledProjects.data),
    [enabledProjects.data],
  );

  const notSelectedProjectOptions = projectOptions.filter(
    (p) => !selectedProjects.includes(p.value),
  );

  return {
    blockingRules,
    handleProjectChange,
    handleToggle,
    handleRemove,
    handleAddLine,
    projectOptions,
    notSelectedProjectOptions,
    enabledProjects,
  };
}

export default withHooks(useHooksAsProps)(BlockingRules);
