import { call, put, takeLatest } from 'redux-saga/effects';
import {
  fetchAccessHolder,
  fetchAccessHolderError,
  fetchAccessHolderSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchAccessHolderSagaFn(action) {
  const {
    payload: { projectId, hasExternalSystem },
  } = action;

  try {
    const response = yield call(
      axios,
      ENDPOINTS.V2.ACCESS_HOLDER({
        query: {
          projectId,
          accessHolder: hasExternalSystem,
        },
      }),
    );

    yield put(fetchAccessHolderSuccess(response.data));
  } catch (error) {
    yield put(fetchAccessHolderError(error));
  }
}

export default takeLatest(fetchAccessHolder.type, fetchAccessHolderSagaFn);
