import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { ENTITIES, selectors } from '../../../../store/ducks/options';
import TitleTooltip from '../../../Table/TitleTooltip';
import Modal from '../../../Modal';
import ActionFooter from '../../../ActionFooter';
import { Label } from '../../../../styles';
import { findByValue } from '../../../../helpers/find';
import {
  DoubleColumnsContainer,
  Resume,
  VerifiedBy,
  Text,
  Group,
  Footer,
} from '../styles';
import { IpTag } from '../../styles';

export default function IpCheck(props) {
  const {
    // Modal
    modalProps: { open, onExited },
    modalControls: { closeModal },
    // Data
    data,
  } = props;

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const actionButtons = [
    {
      type: 'secondary',
      text: 'Close',
      onClick: closeModal,
    },
  ];

  const ipQuality = get(data, 'ip_quality', {});

  const ipTag = useMemo(() => (ipQuality.failed ? 'Failed' : 'Passed'), [
    ipQuality,
  ]);

  const vpnCheck = useMemo(
    () => (ipQuality.vpn ? 'VPN Provider | Not detected' : '-'),
    [ipQuality],
  );

  const detectedCountry =
    useMemo(
      () => get(findByValue(countries, ipQuality.country_code2), 'label', ''),
      [countries, ipQuality.country_code2],
    ) || '-';

  const declaredCountry =
    useMemo(
      () => get(findByValue(countries, ipQuality.country_code2), 'label', ''),
      [countries, ipQuality.country_code2],
    ) || '-';

  const ipqsInfo = (
    <>
      IP: {ipQuality.ip}
      <br />
      City: {ipQuality.city_name}
      <br />
      Region: {ipQuality.region_name}
      <br />
      Country: {detectedCountry}
      <br />
      VPN: {ipQuality.vpn ? 'True' : 'False'}
      <br />
      Proxy: {ipQuality.proxy ? 'True' : 'False'}
      <br />
      <br />
      <b>IPQS fraud score: {ipQuality.fraud_score}</b>
    </>
  );

  const detectedCity = ipQuality.city_name || '';
  const detectedRegion =
    detectedCity && detectedCountry
      ? `${detectedCity}, ${detectedCountry}`
      : detectedCountry;

  return (
    <Modal
      open={open}
      onExited={onExited}
      onClose={closeModal}
      title="IP Check"
    >
      <DoubleColumnsContainer>
        <div>
          <Label>Detected IP</Label>
          <Resume>{ipQuality.ip}</Resume>
          <IpTag failed={ipQuality.failed} margin={'8px 0 0 0'}>
            {ipTag}
          </IpTag>
          {ipQuality.failed && (
            <VerifiedBy>
              <TitleTooltip title={ipqsInfo} placement="bottom" trigger="hover">
                Verified by IPQS
              </TitleTooltip>
            </VerifiedBy>
          )}
        </div>
        <div>
          <Group>
            <Label>Detected IP Location</Label>
            <Text>{detectedRegion || '-'}</Text>
          </Group>
          <Group>
            <Label>Declared Location</Label>
            <Text>{declaredCountry}</Text>
          </Group>
          <Group>
            <Label>VPN Check</Label>
            <Text>{vpnCheck}</Text>
          </Group>
        </div>
      </DoubleColumnsContainer>
      <Footer>
        <ActionFooter hideDivider rightButtons={actionButtons} />
      </Footer>
    </Modal>
  );
}

IpCheck.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  data: PropTypes.shape({
    flags: PropTypes.shape({
      ip_country_mismatch: PropTypes.shape({
        description: PropTypes.string,
        type: PropTypes.string,
        importance_flag: PropTypes.any,
      }),
    }),
    user_id: PropTypes.string,
    num_locales: PropTypes.number,
    num_self_reported_langauges: PropTypes.number,
    suspicious_trigger_counts: PropTypes.number,
    num_of_flags: PropTypes.number,
    maliciousness_level: PropTypes.any,
    flag_stats: PropTypes.objectOf(PropTypes.any),
    ip_quality: {
      failed: PropTypes.bool,
      ip: PropTypes.string,
      vpn: PropTypes.bool,
      proxy: PropTypes.bool,
      location: PropTypes.objectOf(PropTypes.any),
      city_name: PropTypes.string,
      region_name: PropTypes.string,
      country_code2: PropTypes.string,
      fraud_score: PropTypes.any,
    },
    self_reported_country: PropTypes.string,
  }).isRequired,
};
