import { put, takeLatest, call } from 'redux-saga/effects';
import {
  fetchQuestionCategories,
  setQuestionCategories,
  setQuestionCategoriesError,
} from '.';
import { profileBuilderAPI } from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* fetchQuestionCategoriesSaga() {
  const endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER_QUESTION_CATEGORIES();

  try {
    const response = yield call(profileBuilderAPI.get, endpoint);

    yield put(setQuestionCategories(response.data));
  } catch (error) {
    yield put(setQuestionCategoriesError(error));
  }
}

export default takeLatest(
  fetchQuestionCategories.type,
  fetchQuestionCategoriesSaga,
);
