import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import { Col, Row } from 'styled-bootstrap-grid';
import Button from '../../../components/Button';
import Select from '../../../components/Select';
import Input from '../../../components/Input';
import { Label, Section } from '../../../styles';
import ActionFooter from '../../../components/ActionFooter';
import close from '../../../assets/icons/close.svg';
import {
  ContentFormGeneratorModalContainer,
  SectionHeaderFlex,
  SectionTitle,
} from './styles';
import { RemoveIPAddressButton } from '../../ProjectSetup/Steps/ProjectTools/styles';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../components/ErrorPopover';

const GENDER_OPTIONS = [
  { value: 'Female', label: 'Female' },
  { value: 'Male', label: 'Male' },
  { value: 'Other', label: 'Other' },
];

function isAllowed({ floatValue }) {
  return (
    typeof floatValue === 'undefined' || (floatValue >= 0 && floatValue <= 120)
  );
}

function preventNonNumbers(fn) {
  return (event) => {
    const { value } = event.target;

    if (/\D/g.test(value)) {
      return;
    }

    fn(event);
  };
}

export default function ContentFormGeneratorModal(props) {
  const {
    values,
    handleChange,
    submitCount,
    errors,
    setFieldValue,
    closeModal,
    submitForm,
    errorPopoverProps,
  } = props;

  const { thirdParties } = values;

  const addThirdParty = useCallback(() => {
    setFieldValue('thirdParties', [
      ...thirdParties,
      { key: shortid.generate() },
    ]);
  }, [setFieldValue, thirdParties]);

  const removeThirdParty = useCallback(
    (index) => {
      setFieldValue('thirdParties', [
        ...thirdParties.slice(0, index),
        ...thirdParties.slice(index + 1, thirdParties.length),
      ]);
    },
    [setFieldValue, thirdParties],
  );

  return (
    <>
      <ContentFormGeneratorModalContainer>
        {thirdParties.map((thirdParty, index) => {
          const fieldBaseName = `thirdParties[${index}]`;
          const { firstName, lastName, email, gender, age } = thirdParty;
          const entryErrors =
            (errors.thirdParties && errors.thirdParties[index]) || {};

          return (
            <div key={thirdParty.key}>
              <Section>
                <SectionHeaderFlex>
                  <SectionTitle>
                    Third Party Consent Form {index + 1}
                  </SectionTitle>
                  <RemoveIPAddressButton
                    onClick={() => removeThirdParty(index)}
                    tabIndex={index > 0 ? 0 : -1}
                    disabled={index === 0}
                    data-testid={`generate-third-party-consent-form-delete-${index}`}
                  >
                    {index > 0 && <img src={close} alt="remove ip address" />}
                  </RemoveIPAddressButton>
                </SectionHeaderFlex>
              </Section>
              <Section>
                <Row>
                  <Col col={4}>
                    <Label htmlFor="firstName">First name</Label>
                    <Input
                      name={`${fieldBaseName}.firstName`}
                      onChange={handleChange}
                      id={`${fieldBaseName}.firstName`}
                      placeholder="Enter first name"
                      value={firstName}
                      error={
                        submitCount >= 1 && entryErrors && entryErrors.firstName
                      }
                      data-testid={`generate-third-party-consent-form-first-name-${index}`}
                    />
                  </Col>
                  <Col col={4}>
                    <Label htmlFor="lastName">Last name</Label>
                    <Input
                      name={`${fieldBaseName}.lastName`}
                      onChange={handleChange}
                      id={`${fieldBaseName}.lastName`}
                      placeholder="Enter last name"
                      value={lastName}
                      error={
                        submitCount >= 1 && entryErrors && entryErrors.lastName
                      }
                      data-testid={`generate-third-party-consent-form-last-name-${index}`}
                    />
                  </Col>
                  <Col col={3}>
                    <Label htmlFor="age">Age</Label>
                    <Input
                      allowNegative={false}
                      isAllowed={isAllowed}
                      name={`${fieldBaseName}.age`}
                      id={`${fieldBaseName}.age`}
                      onChange={preventNonNumbers(handleChange)}
                      placeholder="Enter age"
                      value={age}
                      error={submitCount >= 1 && entryErrors && entryErrors.age}
                      data-testid={`generate-third-party-consent-form-age-${index}`}
                    />
                  </Col>
                </Row>
              </Section>
              <Section>
                <Row>
                  <Col col={8}>
                    <Label htmlFor="email">
                      {age && age < 18 && 'Parent or Legal Guardian '}
                      Email address
                    </Label>
                    <Input
                      name={`${fieldBaseName}.email`}
                      onChange={handleChange}
                      id={`${fieldBaseName}.email`}
                      placeholder="Enter email address"
                      value={email}
                      error={
                        submitCount >= 1 && entryErrors && entryErrors.email
                      }
                      data-testid={`generate-third-party-consent-form-email-${index}`}
                    />
                  </Col>
                  <Col col={4}>
                    <Label htmlFor="gender">Gender</Label>
                    <Select
                      value={gender}
                      name={`${fieldBaseName}.gender`}
                      options={GENDER_OPTIONS}
                      error={
                        submitCount >= 1 && entryErrors && entryErrors.gender
                      }
                      id={`${fieldBaseName}.gender`}
                      onChange={({ value }) => {
                        setFieldValue(`${fieldBaseName}.gender`, value);
                      }}
                      data-testid={`generate-third-party-consent-form-gender-${index}`}
                    />
                  </Col>
                </Row>
              </Section>
            </div>
          );
        })}

        <Button type="secondary" onClick={addThirdParty}>
          Add Other Form
        </Button>
        <ErrorPopover {...errorPopoverProps} />
      </ContentFormGeneratorModalContainer>
      <ActionFooter
        rightButtons={[
          {
            type: 'secondary',
            text: 'Cancel',
            onClick: closeModal,
          },
          {
            type: 'primary',
            text: 'Submit',
            onClick: submitForm,
          },
        ]}
      />
    </>
  );
}

ContentFormGeneratorModal.propTypes = {
  values: PropTypes.shape({
    thirdParties: PropTypes.array,
  }).isRequired,
  handleChange: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  errors: PropTypes.shape({
    thirdParties: PropTypes.array,
  }).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
};
