import React from 'react';
import PropTypes from 'prop-types';
import { isEmpty } from 'lodash';
import { Modal, ModalHeader, ModalBody, ModalFooter, ROLE } from 'baseui/modal';
import { H1, Description } from '../../styles/index';
import Button from '../Button';
import { RowWrapper, Divider, NumbersContainer, Spacing } from './styles';

const ScreeningNumbersModal = (props) => {
  const { open, setOpen, metrics, handleStatusChange } = props;

  const handleCancel = () => {
    setOpen(false);
  };

  return (
    <Modal
      isOpen={open}
      onClose={() => setOpen(false)}
      closeable
      size={624}
      role={ROLE.dialog}
      unstable_ModalBackdropScroll
      overrides={{
        Dialog: {
          style: {
            borderBottomLeftRadius: '4px',
            borderBottomRightRadius: '4px',
            borderTopLeftRadius: '4px',
            borderTopRightRadius: '4px',
          },
        },
        Close: {
          style: {
            color: '#A2AAAD',
          },
        },
      }}
    >
      <ModalHeader>Screening Numbers</ModalHeader>
      <ModalBody>
        <Spacing />
        <RowWrapper>
          <NumbersContainer>
            <H1>{!isEmpty(metrics) ? metrics.waitingToBeScreened : 0}</H1>
            <Description>Users Waiting To Be Screened</Description>
          </NumbersContainer>
          <Button
            onClick={() => {
              handleStatusChange({ value: 'SCREENED' });
              handleCancel();
            }}
          >
            Use As Filter
          </Button>
        </RowWrapper>
        <RowWrapper>
          <NumbersContainer>
            <H1>{!isEmpty(metrics) ? metrics.staged : 0}</H1>
            <Description>Users Staged</Description>
          </NumbersContainer>
          <Button
            onClick={() => {
              handleStatusChange({ value: 'STAGED' });
              handleCancel();
            }}
          >
            Use As Filter
          </Button>
        </RowWrapper>
        <RowWrapper>
          <NumbersContainer>
            <H1>{!isEmpty(metrics) ? metrics.requiringActivation : 0}</H1>
            <Description>Users Requiring Activation</Description>
          </NumbersContainer>
          <Button
            onClick={() => {
              handleStatusChange({ value: 'CONTRACT_PENDING' });
              handleCancel();
            }}
          >
            Use As Filter
          </Button>
        </RowWrapper>
        <Divider />
      </ModalBody>

      <ModalFooter>
        <Button type="secondary" onClick={() => handleCancel()}>
          Close
        </Button>
      </ModalFooter>
    </Modal>
  );
};

ScreeningNumbersModal.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired,
  metrics: PropTypes.shape({
    requiringActivation: PropTypes.number,
    staged: PropTypes.number,
    waitingToBeScreened: PropTypes.number,
  }).isRequired,
  handleStatusChange: PropTypes.func.isRequired,
};

ScreeningNumbersModal.defaultProps = {};

export default ScreeningNumbersModal;
