import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { Container } from './styles';
import { Placeholder } from '../Placeholder/styles';

export default function MultiValue(props) {
  const {
    selectProps: { value, placeholder, getMultiCount },
    index,
  } = props;

  const count = useMemo(() => {
    if (Array.isArray(value)) {
      if (typeof getMultiCount === 'function') {
        return getMultiCount(value);
      }

      return value.length;
    }

    return '0';
  }, [getMultiCount, value]);

  if (index !== 0) {
    return null;
  }

  return (
    <>
      <Container hasPlaceholder={!!placeholder}>{count}</Container>
      <Placeholder>{placeholder}</Placeholder>
    </>
  );
}

MultiValue.propTypes = {
  selectProps: PropTypes.shape({
    value: PropTypes.array,
    placeholder: PropTypes.string,
    getMultiCount: PropTypes.func,
  }).isRequired,
  index: PropTypes.number.isRequired,
};
