export function formatMacroLanguageLabel(langObj) {
  if (langObj && langObj.label) {
    if (langObj.macroLanguage) {
      const languageLabel = langObj.label.split(' (')[0];
      return `${languageLabel} (All ${languageLabel} locales)`;
    }
    return langObj.label;
  }

  return '';
}

export function mapToMacroLanguages(
  array,
  disableAllLocales = false,
  overwriteLocaleCodeById = false,
) {
  if (!array || !Array.isArray(array) || !array.length) return [];
  const transformedLanguages = [];
  array.forEach((lang) => {
    if (lang.macroLanguage) {
      const options = [];
      const languageLabel = lang.label.split(' (')[0];

      options.push({
        ...lang,
        label: `${languageLabel} (All ${languageLabel} locales)`,
        disabled: disableAllLocales,
      });

      const locales = lang.locales.map((locale) => ({
        ...locale,
        ...(overwriteLocaleCodeById && { value: locale.id }),
      }));

      options.push(...locales);

      transformedLanguages.push({
        ...lang,
        value: `macro-${lang.value}`,
        macroLanguageLabel: true,
        label: languageLabel,
        options,
      });
    } else {
      transformedLanguages.push(lang);
    }
  });

  return transformedLanguages;
}
