import React, { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Table from '../../../Table';
import Button from '../../../Button';
import { useModal } from '../../../Modal';
import { SectionTitle, SectionDescription, TableContainer } from './styles';
import AddTenantModal from './AddTenantModal';
import { Section } from '../../../../styles';
import NoEntries from '../../../NoEntries';
import {
  selectById as selectLocaleTenantById,
  removeOne as removeOneLocaleTenant,
  selectAll as selectAllRawLocaleTenants,
  selectError,
} from '../../../../store/ducks/localeTenants';
import { selectAllEager as selectAllPayRates } from '../../../../store/ducks/payrates';
import SettingsCell from './SettingsCell';
import store from '../../../../store';
import { selectAll as selectAllHiringTargets } from '../../../../store/ducks/hiringTargets';
import ErrorPopover, { useErrorPopover } from '../../../ErrorPopover';
import { useLocaleTenants } from './helpers';

const columns = [
  {
    key: 'country',
    accessor: 'country',
    Header: 'COUNTRY',
    disableSortBy: true,
    width: 160,
  },
  {
    key: 'tenant',
    accessor: 'tenant',
    Header: 'TENANT',
    disableSortBy: true,
    width: 300,
  },
  {
    key: 'tenantType',
    accessor: 'tenantType',
    Header: 'TENANT TYPE',
    disableSortBy: true,
    width: 120,
  },
  {
    Header: '',
    accessor: 'id',
    key: 'settings',
    maxWidth: 44,
    disableSortBy: true,
    Cell: SettingsCell,
  },
];

function hasHiringTargetOrPayrateAttached(id) {
  const state = store.getState();
  const allLocaleTenants = selectAllRawLocaleTenants(state);
  const hiringTargets = selectAllHiringTargets(state);
  const allPayrates = selectAllPayRates(state);
  const localeTenant = selectLocaleTenantById(state, id);

  if (hiringTargets && allLocaleTenants) {
    const allLocaleTenantsCountries = new Set();
    allLocaleTenants.forEach((localeTente) =>
      allLocaleTenantsCountries.add(localeTente.country),
    );

    const allHiringTargetsSet = new Set();
    hiringTargets.forEach((hiringTarget) =>
      allHiringTargetsSet.add(hiringTarget.restrictToCountry),
    );

    const allPayratesSet = new Set();
    allPayrates.forEach((payRate) =>
      allPayratesSet.add(payRate.restrictToCountry),
    );

    const hiringTargetsWithoutSpecificCountry = new Set();
    const payRatesWithoutSpecificCountry = new Set();

    allHiringTargetsSet.forEach((localeTenantItem) => {
      if (!allLocaleTenantsCountries.has(localeTenantItem)) {
        hiringTargetsWithoutSpecificCountry.add(localeTenantItem);
      }
    });

    allPayratesSet.forEach((payRate) => {
      if (!allLocaleTenantsCountries.has(payRate)) {
        payRatesWithoutSpecificCountry.add(payRate);
      }
    });

    if (
      localeTenant.country === '*' &&
      (hiringTargetsWithoutSpecificCountry.length >= 1 ||
        payRatesWithoutSpecificCountry.length >= 1)
    ) {
      return true;
    }
  }

  return false;
}

function canDelete(id) {
  const state = store.getState();
  const hiringTargets = selectAllHiringTargets(state);
  const localeTenant = selectLocaleTenantById(state, id);

  if (!localeTenant) {
    return false;
  }

  if (hasHiringTargetOrPayrateAttached(id)) {
    return false;
  }

  const matches = hiringTargets.filter((hiringTarget) => {
    return hiringTarget.restrictToCountry === localeTenant.country;
    // Logic used to use allCountries as fallback
    // return (
    //   hiringTarget.localeDialect === localeTenant.country ||
    //   localeTenant.country === '*'
    // );
  });

  if (matches.length) {
    const localeTenants = selectAllRawLocaleTenants(state);

    if (localeTenant.country === '*') {
      const tenantWithoutCurrent = localeTenants.filter(
        (lt) => lt.id !== localeTenant.id,
      );

      const hasReplacement = matches.filter(
        (hiringTarget) =>
          !!tenantWithoutCurrent.find(
            (lt) => lt.country === hiringTarget.restrictToCountry,
          ),
      );

      return matches.length === hasReplacement.length;
    }

    return false;
  }

  return true;
}

// const localeTenantsRequired = new Error('You must add at least one Locale Tenant.');

export default function LocaleTenants() {
  const [modalProps, modalControls] = useModal();

  const tenants = useLocaleTenants();

  // Delete Locale Tenant Logic
  const dispatch = useDispatch();

  const deleteLocaleTenant = useCallback(
    (id) => {
      if (!canDelete(id, store.getState())) {
        return;
      }

      dispatch(removeOneLocaleTenant(id));
    },
    [dispatch],
  );

  const editLocaleTenant = useCallback(
    (id) => {
      if (!hasHiringTargetOrPayrateAttached(id)) {
        modalControls.openModal(selectLocaleTenantById(store.getState(), id));
      }
    },
    [modalControls],
  );

  const [showError, , errorPopoverProps] = useErrorPopover();

  const currentError = useSelector(selectError);

  useEffect(() => {
    if (currentError) {
      showError(currentError);
    }
  }, [currentError, showError]);

  return (
    <>
      <Section marginBottom="46px">
        <SectionTitle>Locale Tenants</SectionTitle>
        <SectionDescription>
          The project locale tenants for each locale.
        </SectionDescription>

        {tenants.length ? (
          <TableContainer>
            <Table
              columns={columns}
              data={tenants}
              initialState={{ deleteLocaleTenant, editLocaleTenant, canDelete }}
            />
          </TableContainer>
        ) : (
          <NoEntries />
        )}

        <ErrorPopover {...errorPopoverProps} />

        <Button
          data-testid="project-setup--recruitment--add-tenant-button"
          onClick={() => {
            modalControls.openModal();
          }}
        >
          Add Tenant
        </Button>
      </Section>

      <AddTenantModal
        data={modalControls.modalData}
        modalProps={modalProps}
        canDelete={canDelete}
        modalControls={modalControls}
      />
    </>
  );
}

