import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import NumberFormat from 'react-number-format';
import { Label, Section } from '../../../styles';
import Select from '../../Select';
import Input from '../../Input';
import Checkbox, { CheckGroup, CheckAltMessage } from '../../Checkbox';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip/InformationTooltip';
import { LabelWrapper } from '../../ProcessBuilder/InformationTooltip/styles';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import Disabled from '../../Disabled/Disabled';
import {
  invoiceTypes,
  INVOICE_TYPES,
  selectError,
  selectSaving,
} from '../../../store/ducks/invoiceConfiguration';
import ErrorPopover, { ErrorPopoverProps } from '../../ErrorPopover';
import ActionFooter from '../../ActionFooter';

function isPercentageAllowed(formatedPayload) {
  if (typeof formatedPayload.floatValue !== 'number') {
    return true;
  }

  return formatedPayload.floatValue <= 100;
}

const DISABLED_WHEN_NO_PRODUCTIVITY_DATA =
  'Enabled only when Available Productivity Data is checked.';

export default function InvoiceTemplateModal(props) {
  const {
    values,
    errors,
    handleChange,
    setFieldValue,
    submitCount,
    hasProductivityData,
    showError,
    errorPopoverProps,
    submitForm,
    closeModal,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.invoiceGenerationTypes));
  }, [dispatch]);

  const saving = useSelector(selectSaving);

  const invoiceGenerationTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.invoiceGenerationTypes),
  );

  const invoiceTypesOptions = useMemo(
    () =>
      invoiceTypes.map((invoiceType) => ({
        ...invoiceType,
        disabled:
          invoiceType.value === INVOICE_TYPES.USER_SELF_REPORTED
            ? false
            : !hasProductivityData,
        disabledMessage: DISABLED_WHEN_NO_PRODUCTIVITY_DATA,
      })),
    [hasProductivityData],
  );

  const baseAutoGenerationOnOptions = useMemo(
    () => invoiceGenerationTypes.data.filter((item) => item.value !== 'BOTH'),
    [invoiceGenerationTypes.data],
  );

  const submitError = useSelector(selectError);

  useEffect(() => {
    if (submitCount > 0 && submitError) {
      showError(submitError);
    }
  }, [submitCount, showError, submitError]);

  return (
    <>
      <LabelWrapper>
        <Label htmlFor="invoiceType">Invoice type</Label>
        <InformationTooltip
          lineLength={42}
          title={
            <>
              The type of invoice that a<br /> project will use.
            </>
          }
        />
      </LabelWrapper>

      <Section>
        <Select
          value={values.invoiceType}
          name="invoiceType"
          options={invoiceTypesOptions}
          loading={invoiceTypes.loading}
          error={invoiceTypes.error || (submitCount >= 1 && errors.invoiceType)}
          onChange={({ value }) => {
            setFieldValue('invoiceType', value);
            setFieldValue('baseAutoGenerationOn', null);
          }}
          data-testid="invoice-template-setup-modal-invoice-type-select"
        />
      </Section>

      {values.invoiceType &&
        values.invoiceType !== INVOICE_TYPES.USER_SELF_REPORTED && (
          <Section marginLeft="34px">
            <Row>
              <Col col={6}>
                <Label htmlFor="baseAutoGenerationOn">
                  Base auto-generation on
                </Label>
                <Select
                  value={values.baseAutoGenerationOn}
                  name="baseAutoGenerationOn"
                  options={baseAutoGenerationOnOptions}
                  loading={invoiceGenerationTypes.loading}
                  error={
                    invoiceGenerationTypes.error ||
                    (submitCount >= 1 && errors.baseAutoGenerationOn)
                  }
                  onChange={({ value }) =>
                    setFieldValue('baseAutoGenerationOn', value)
                  }
                  data-testid="invoice-template-setup-modal-base-autogeneration-on-select"
                />
              </Col>
              <Col col={6}>
                <Section marginBottom="0px" marginTop="24px">
                  <Disabled
                    when
                    message={
                      values.invoiceType === INVOICE_TYPES.SYSTEM_GENERATED
                        ? 'Enabled only for User self-reported and System approved invoices.'
                        : 'Always enabled for User self-reported and System approved  invoices.'
                    }
                  >
                    <Checkbox
                      name="allowSelfReportingTime"
                      onChange={handleChange}
                      checked={
                        values.invoiceType ===
                        INVOICE_TYPES.USER_SELF_REPORTED_AND_SYSTEM_APPROVED
                      }
                      data-testid="invoice-template-setup-modal-allow-self-reporting-checkbox "
                      bold={false}
                    >
                      Allow self-reporting time
                    </Checkbox>
                  </Disabled>
                </Section>
              </Col>
            </Row>
          </Section>
        )}

      <Disabled
        when={!hasProductivityData}
        message={DISABLED_WHEN_NO_PRODUCTIVITY_DATA}
      >
        <Section marginBottom={16}>
          <CheckGroup>
            <Checkbox
              name="autoApproveInvoices"
              onChange={(event) => {
                setFieldValue('autoApproveInvoices', event.target.value);
                setFieldValue('autoApproveInvoicesBy', null);
                setFieldValue('varianceThreshold', '');
              }}
              checked={values.autoApproveInvoices}
              label="Auto-approve Invoices"
              data-testid="invoice-template-setup-modal-auto-approve-invoices"
            />
            <CheckAltMessage>
              Invoice will be auto-approved by the system.
            </CheckAltMessage>
          </CheckGroup>
        </Section>
      </Disabled>

      {values.autoApproveInvoices && (
        <Section marginLeft="34px" marginTop="30px">
          <Row>
            <Col col={6}>
              <Label htmlFor="autoApproveInvoicesBy">
                Base auto-approval on
              </Label>
              <Select
                value={values.autoApproveInvoicesBy}
                name="autoApproveInvoicesBy"
                options={invoiceGenerationTypes.data}
                loading={invoiceGenerationTypes.loading}
                error={
                  invoiceGenerationTypes.error ||
                  (submitCount >= 1 && errors.autoApproveInvoicesBy)
                }
                onChange={({ value }) =>
                  setFieldValue('autoApproveInvoicesBy', value)
                }
                data-testid="invoice-template-setup-modal-invoice-base-auto-approve-on-select"
              />
            </Col>
            <Col col={6}>
              <LabelWrapper>
                <Label htmlFor="autoApprovePercent">
                  Acceptable Var. Threshold
                </Label>
                <InformationTooltip
                  lineLength={42}
                  title={
                    <>
                      Acceptable variant % to
                      <br />
                      auto generate or approve
                      <br />
                      an invoice.
                    </>
                  }
                />
              </LabelWrapper>

              <NumberFormat
                suffix="%"
                isAllowed={isPercentageAllowed}
                allowNegative={false}
                customInput={Input}
                name="varianceThreshold"
                onChange={(event) =>
                  setFieldValue(
                    'varianceThreshold',
                    parseInt(`${event.target.value}`.replace('%', ''), 10),
                  )
                }
                id="varianceThreshold"
                placeholder="Suggested: 10% to 20%"
                value={values.varianceThreshold}
                error={submitCount >= 1 && errors.varianceThreshold}
              />
            </Col>
          </Row>
        </Section>
      )}

      <Section marginBottom={16}>
        <CheckGroup>
          <Checkbox
            name="pmApprovalAtLineLevel"
            onChange={handleChange}
            checked={values.pmApprovalAtLineLevel}
            label="Project manager approval at line item level"
            data-testid="invoice-template-setup-modal-auto-approve-invoices"
          />
          <CheckAltMessage>
            PM approval is required at the line item of an invoice.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      <ErrorPopover {...errorPopoverProps} />

      <ActionFooter
        rightButtons={[
          {
            type: 'secondary',
            text: 'Cancel',
            onClick: closeModal,
            disabled: saving,
          },
          {
            type: 'primary',
            text: 'Save',
            onClick: submitForm,
            loading: saving,
          },
        ]}
      />
    </>
  );
}

InvoiceTemplateModal.propTypes = {
  // This is used in the container.
  // eslint-disable-next-line react/no-unused-prop-types
  projectId: PropTypes.number.isRequired,
  handleChange: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  hasProductivityData: PropTypes.bool.isRequired,
  configuration: PropTypes.shape({}),
  values: PropTypes.shape({
    invoiceType: PropTypes.string,
    baseAutoGenerationOn: PropTypes.string,
    allowSelfReportingTime: PropTypes.bool,
    autoApproveInvoices: PropTypes.bool,
    autoApproveInvoicesBy: PropTypes.string,
    varianceThreshold: PropTypes.string,
    pmApprovalAtLineLevel: PropTypes.bool,
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  submitForm: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
};

InvoiceTemplateModal.defaultProps = {
  configuration: null,
};
