import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const KEY = 'userScreeningFalsePositives';
const UNDO_TYPE = `${KEY}/undo`;

const slice = createSlice({
  name: KEY,

  initialState: {
    data: null,
    loading: {},
    fetchError: null,
    saving: false,
    saveError: null,
  },

  reducers: {
    saveFalsePositive: (state) => {
      state.fetchError = null;
    },

    saveFalsePositiveSuccess: (state, action) => {
      state.fetchError = null;
      state.data = action.payload;
    },

    saveFalsePositiveError: (state, action) => {
      state.fetchError = action.payload;
    },

    setLoading(state, { payload }) {
      const { userId, status } = payload;
      if (userId) state.loading = { ...state.loading, [userId]: status };
    },
  },
});

const {
  actions: {
    saveFalsePositive,
    saveFalsePositiveSuccess,
    saveFalsePositiveError,
    setLoading,
  },
  reducer,
} = slice;

reducer.key = KEY;

export default reducer;

export {
  saveFalsePositive,
  saveFalsePositiveSuccess,
  saveFalsePositiveError,
  setLoading,
};

export function selectFalsePositiveSlice(state) {
  return state[reducer.key];
}

export function selectFalsePositive(state) {
  return get(selectFalsePositiveSlice(state), 'data', null);
}

export function selectLoading(state, user) {
  const { userId } = user;
  if (!userId) return false;
  const loading = get(selectFalsePositiveSlice(state), 'loading', false);
  return get(loading, userId, false);
}

export function selectFetchError(state) {
  return get(selectFalsePositiveSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectFalsePositiveSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectFalsePositiveSlice(state), 'saveError', null);
}

export function selectFalsePositiveById(state, id) {
  const falsePositives = selectFalsePositive(state);
  return falsePositives.find((config) => config.id === id);
}

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}
