import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useMemo } from 'react';
import withHooks from 'hook-hoc';
import sum from 'lodash/sum';
import sortBy from 'lodash/sortBy';
import RegistrationAndQualification from './RegistrationAndQualification';
import {
  selectSteps as selectRegistrationSteps,
  fetchSteps as fetchRegistrationSteps,
  clearStep as clearRegistrationStep,
} from '../../../../store/ducks/registrationProcesses';
import {
  selectSteps as selectQualificationSteps,
  fetchSteps as fetchQualificationSteps,
  clearStep as clearQualificationStep,
} from '../../../../store/ducks/qualificationProcesses';
import { selectRegistrationAndQualification } from '../../../../store/ducks/projectView';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import { selectProject } from '../../../../store/ducks/project';

function mapToMetrics(processMetricsRaw = {}, processStepsRaw = []) {
  const metrics = {
    abandoned: 0,
    inProgress: 0,
    registered: 0,
    rejected: 0,
    total: 0,
    steps: [],
  };

  if (processMetricsRaw) {
    Object.entries(processMetricsRaw).forEach(([key, value]) => {
      metrics[key] = value;
    });

    metrics.total = sum(Object.values(processMetricsRaw));
  }

  if (processStepsRaw.length) {
    metrics.steps = sortBy(processStepsRaw, 'order').map((currentProcess) => {
      return {
        id: currentProcess.id,
        passed: currentProcess.metricsCount || 0,
      };
    });
  }

  return metrics;
}

function useHooksAsProps(outerProps) {
  const { mode } = outerProps;
  const dispatch = useDispatch();

  const registrationAndQualification = useSelector(
    selectRegistrationAndQualification,
  );

  const project = useSelector(selectProject);

  useEffect(() => {
    if (registrationAndQualification.registrationProcessTemplate) {
      dispatch(
        fetchRegistrationSteps(
          registrationAndQualification.registrationProcessTemplate,
        ),
      );

      dispatch(
        fetch({
          optionsFor: ENTITIES.registrationMetrics,
          nestedKey: registrationAndQualification.registrationProcessTemplate,
          projectId: project.id,
        }),
      );
    }

    return () => dispatch(clearRegistrationStep());
  }, [
    dispatch,
    registrationAndQualification.registrationProcessTemplate,
    mode,
    project.id,
  ]);

  useEffect(() => {
    if (registrationAndQualification.qualificationProcessTemplate) {
      dispatch(
        fetchQualificationSteps(
          registrationAndQualification.qualificationProcessTemplate,
        ),
      );

      dispatch(
        fetch({
          optionsFor: ENTITIES.qualificationMetrics,
          nestedKey: registrationAndQualification.qualificationProcessTemplate,
          projectId: project.id,
        }),
      );
    }

    return () => dispatch(clearQualificationStep());
  }, [
    dispatch,
    registrationAndQualification.qualificationProcessTemplate,
    mode,
    project.id,
  ]);

  const registrationSteps = useSelector(selectRegistrationSteps);
  const qualificationSteps = useSelector(selectQualificationSteps);

  const registrationMetricsRaw = useSelector(
    selectors.createSelectDataFor(
      ENTITIES.registrationMetrics,
      registrationAndQualification.registrationProcessTemplate,
    ),
  );

  const qualificationMetricsRaw = useSelector(
    selectors.createSelectDataFor(
      ENTITIES.qualificationMetrics,
      registrationAndQualification.qualificationProcessTemplate,
    ),
  );

  const registrationMetrics = useMemo(
    () =>
      mapToMetrics(
        registrationMetricsRaw.metrics,
        registrationMetricsRaw.processSteps,
        registrationMetricsRaw.instantiated,
      ),
    [registrationMetricsRaw],
  );

  const qualificationMetrics = useMemo(
    () =>
      mapToMetrics(
        qualificationMetricsRaw.metrics,
        qualificationMetricsRaw.processSteps,
        qualificationMetricsRaw.instantiated,
      ),
    [qualificationMetricsRaw],
  );

  return {
    registrationSteps,
    qualificationSteps,
    registrationMetrics,
    qualificationMetrics,
  };
}

export default withHooks(useHooksAsProps)(RegistrationAndQualification);
