import React, { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import ProjectAccessPreview from '../../../../../../components/ProjectPreviews/ProjectAccessPreview';
import {
  selectProjectTools,
  selectProjectId,
} from '../../../../../../store/ducks/project';
import { fetchAccessHolder } from '../../../../../../store/ducks/experimentGroups';

export default function ProjectAccess(props) {
  const { navigateTo, renderedIn } = props;

  const dispatch = useDispatch();

  const projectId = useSelector(selectProjectId);
  const { hasExternalSystem } = useSelector(selectProjectTools);

  useEffect(() => {
    dispatch(fetchAccessHolder({ projectId, hasExternalSystem }));
  }, [dispatch, projectId, hasExternalSystem]);

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.PROJECT_ACCESS);
  }, [navigateTo]);

  return (
    <ProjectAccessPreview
      stepNumber={STEP_NUMBER.PROJECT_ACCESS + 1}
      editStep={editStep}
      renderedIn={renderedIn}
    />
  );
}

ProjectAccess.propTypes = {
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectAccess.defaultProps = {
  renderedIn: 'project-setup',
};
