import React from 'react';
import PropTypes from 'prop-types';
import { Verified } from './styles';

export default function VerifiedCell({ row }) {
  const {
    original: { verified },
  } = row;

  return verified ? <Verified>Verified</Verified> : null;
}

VerifiedCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({
      verified: PropTypes.bool,
    }),
  }).isRequired,
};
