/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import { STEPS } from '../../../constants/projectSetup';

const initialState = {
  currentStep: 0,
  dirty: false,
};

const availableSteps = STEPS.filter((step) => step.enabled);

const slice = createSlice({
  name: 'projectSetup',

  initialState,

  reducers: {
    setDirty(state, action) {
      state.dirty =
        typeof action.payload !== 'undefined' ? !!action.payload : true;
    },

    navigate(state, action) {
      const { payload: toStep } = action;

      if (typeof toStep !== 'number') {
        return;
      }

      if (state.dirty) {
        if (
          // eslint-disable-next-line no-alert
          !window.confirm('Changes you made may not be saved.')
        ) {
          return;
        }
      }

      state.dirty = false;

      if (toStep <= 0) {
        state.currentStep = 0;
        return;
      }

      if (toStep > availableSteps.length - 1) {
        state.currentStep = availableSteps.length - 1;
        return;
      }

      state.currentStep = toStep;
    },
  },
});

const {
  reducer,
  actions: { setDirty, navigate },
} = slice;

reducer.key = 'projectSetup';

export default reducer;

export { setDirty, navigate };

function selectProjectSetup(state) {
  return state[reducer.key];
}

export function selectDirty(state) {
  return get(selectProjectSetup(state), 'dirty', false);
}

export function selectCurrentStep(state) {
  return get(selectProjectSetup(state), 'currentStep', 0);
}
