/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import orderBy from 'lodash/orderBy';

const initialState = {
  options: [],
  selectedProcess: null,
  steps: [],
  optionsLoading: true,
  optionsError: null,
  stepsLoading: false,
  stepsError: null,
  createdBy: '',
  dateCreated: null,
  processName: null,
};

const slice = createSlice({
  name: 'registrationProcesses',
  initialState,
  reducers: {
    fetchOptions(state) {
      state.optionsLoading = true;
    },
    fetchOptionsSucceded(state, action) {
      state.optionsLoading = false;
      state.options = action.payload;
    },
    fetchOptionsFailed(state, action) {
      state.optionsLoading = false;
      state.optionsError = action.payload;
    },
    fetchSteps(state, action) {
      state.stepsLoading = true;
      state.selectedProcess = action.payload;
    },
    fetchStepsSucceded(state, action) {
      state.stepsLoading = false;
      state.steps = orderBy(action.payload.processSteps || [], 'order');
      state.createdBy = action.payload.createdBy;
      state.dateCreated = action.payload.dateCreated;
      state.stepsError = null;
      state.processName = action.payload.name;
    },
    fetchStepsFailed(state, action) {
      state.stepsLoading = false;
      state.stepsError = action.payload;
      state.steps = [];
      state.createdBy = 'null';
      state.dateCreated = null;
    },
    clearStep(state) {
      state.steps = [];
      state.processName = '';
    },
  },
});

const {
  reducer,
  name,
  actions: {
    fetchOptions,
    fetchOptionsFailed,
    fetchOptionsSucceded,
    fetchSteps,
    fetchStepsFailed,
    fetchStepsSucceded,
    clearStep,
  },
} = slice;

reducer.key = name;

export default reducer;

export {
  fetchOptions,
  fetchOptionsFailed,
  fetchOptionsSucceded,
  fetchSteps,
  fetchStepsFailed,
  fetchStepsSucceded,
  clearStep,
};

// SELECTORS

export function selectState(state) {
  return state[reducer.key] || {};
}

export function selectOptions(state) {
  return get(selectState(state), 'options', []);
}

export function selectOptionsLoading(state) {
  return get(selectState(state), 'optionsLoading', false);
}

export function selectOptionsError(state) {
  return get(selectState(state), 'optionsError', null);
}

export function selectOptionsState(state) {
  return {
    options: selectOptions(state),
    loading: selectOptionsLoading(state),
    error: selectOptionsError(state),
  };
}

export function selectSteps(state) {
  const stateSlice = selectState(state);
  const selectedProcessId = get(stateSlice, 'selectedProcess', null);
  const selectedProcess = selectOptions(state).find(
    (process) => process.id === selectedProcessId,
  );

  return {
    processId: selectedProcessId,
    processName:
      get(selectedProcess, 'name', '') || get(stateSlice, 'processName', ''),
    steps: get(stateSlice, 'steps', []),
    createdBy: get(stateSlice, 'createdBy', []),
    dateCreated: get(stateSlice, 'dateCreated', []),
    loading: get(stateSlice, 'stepsLoading', false),
    error: get(stateSlice, 'stepsError', null),
  };
}

export function selectStepsLoading(state) {
  return get(selectState(state), 'stepsLoading', false);
}

export function selectStepsError(state) {
  return get(selectState(state), 'stepsError', null);
}
