import { createSlice, createSelector } from '@reduxjs/toolkit';

const initialState = {
  saving: false,
  loading: true,
  isBlocked: true,
  isAnswered: false,
  documentVersion: null,
};

const slice = createSlice({
  name: 'externalConsentForm',

  initialState,

  reducers: {
    submitUserResponse(state) {
      return state;
    },

    submitUserResponseSuccess(state) {
      state.loading = false;
      state.isBlocked = true;
      state.isAnswered = true;
    },

    submitUserResponseError(state, action) {
      state.error = action.payload;
      state.loading = false;
      state.isBlocked = false;
    },

    fetchConsentForm(state) {
      return state;
    },

    fetchConsentFormSuccess(state, action) {
      const { payload } = action;

      state.form = payload;
      state.loading = false;
      state.isBlocked = true;
    },

    fetchConsentFormError(state, action) {
      state.error = action.payload;
      state.loading = false;
      state.isBlocked = true;
    },

    setIsBlocked(state, action) {
      state.isBlocked = action.payload;
    },

    setSaving(state, action) {
      state.saving = action.payload;
    },

    setLoading(state, action) {
      state.loading = action.payload;
    },

    setIsAnswered(state, action) {
      state.isAnswered = action.payload;
    },

    reset() {
      return initialState;
    },
  },
});

const {
  name,
  reducer,
  actions: {
    submitUserResponse,
    submitUserResponseSuccess,
    submitUserResponseError,
    fetchConsentForm,
    fetchConsentFormSuccess,
    fetchConsentFormError,
    setIsBlocked,
    setSaving,
    setLoading,
    reset,
  },
} = slice;

reducer.key = name;

export default reducer;

export {
  submitUserResponse,
  submitUserResponseSuccess,
  submitUserResponseError,
  fetchConsentForm,
  fetchConsentFormSuccess,
  fetchConsentFormError,
  setIsBlocked,
  setSaving,
  setLoading,
  reset,
};

function selectConsentFormFn(state) {
  return state[reducer.key];
}

export const selectConsentForm = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.form,
);

export const selectError = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.error,
);

export const selectIsBlocked = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.isBlocked,
);

export const selectIsAnswered = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.isAnswered,
);

export const selectLoading = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.loading,
);

export const selectSaving = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.saving,
);

export const selectDocumentVersion = createSelector(
  selectConsentFormFn,
  (consentForm) => consentForm.documentVersion,
);
