import React, { useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import noop from 'lodash/noop';
import Modal from '../../../LargeModal';
import Table from '../../../Table';
import ActionFooter from '../../../ActionFooter';
import Pagination from '../../../Pagination';
import ProgressBar from '../../../ProgressBar';
import Switch from '../../../Switch';
import AttributeCell from './Cells/AttributeCell';
import ExpanderCell from './Cells/ExpanderCell';
import ProfileCell from './Cells/ProfileCell';
import SignalCell from './Cells/SignalCell';
import StatusCell from './Cells/StatusCell';
import VerifiedCell from './Cells/VerifiedCell';
import { Label } from '../../../../styles';
import convertToCamelCase from '../../../../helpers/snake-case-to-camel-case';
import {
  DoubleColumnsContainer,
  ProfileInfo,
  ProfileName,
  Resume,
  SocialMedia,
  Spacer,
  TableHeader,
  TableBody,
  PaginationContainer,
  FloatContainer,
  FloatLeft,
  FloatRight,
  ShowVerified,
} from '../styles';

export default function Duplicates(props) {
  const {
    // Modal
    modalProps: { open, onExited },
    modalControls: { closeModal },
    // Data
    data,
  } = props;

  const style = { padding: '15px 0 0 0' };

  const columns = [
    {
      Header: 'Signals',
      accessor: 'numSignals',
      key: 'numSignals',
      width: 15,
      Cell: SignalCell,
    },
    {
      Header: 'ID',
      accessor: 'userId',
      key: 'userId',
      width: 15,
      disableSortBy: true,
      Cell: AttributeCell,
    },
    {
      Header: 'Duplicated profiles',
      accessor: 'fullName',
      key: 'fullName',
      width: 40,
      disableSortBy: true,
      Cell: ProfileCell,
    },
    {
      Header: 'Status',
      accessor: 'status',
      key: 'status',
      width: 30,
      disableSortBy: true,
      Cell: StatusCell,
    },
    {
      Header: '',
      accessor: 'verified',
      key: 'verified',
      width: 10,
      disableSortBy: true,
      Cell: VerifiedCell,
    },
    {
      id: 'expander',
      key: 'expander',
      accessor: 'expander',
      width: 7,
      Header: () => null,
      disableSortBy: true,
      Cell: ExpanderCell,
      style,
    },
  ];

  const actionButtons = [
    {
      type: 'secondary',
      text: 'Close',
      onClick: closeModal,
    },
  ];

  const [showVerified, setShowVerified] = useState(false);

  const detailsData = convertToCamelCase(get(data, 'details', {}));

  const duplicatesData = get(detailsData, 'duplicates', {});
  const potentialDuplicates = get(duplicatesData, 'potentialDuplicates', []);
  const total = get(duplicatesData, 'total', 0);

  const name = get(data, 'name', null) || '-';
  const email = get(detailsData, 'email', null) || '-';
  const phone = get(detailsData, 'mobilePhone', null) || '-';
  const address = get(detailsData, 'address', null) || '-';
  const country = get(detailsData, 'country', null) || '-';
  const similarityScore = get(duplicatesData, 'similarityScore', 0) * 100;
  const referenceWorkerId = get(duplicatesData, 'referenceWorkerId', 0);

  const duplicates = potentialDuplicates.map((duplicate) => ({
    ...get(duplicate, 'attributes', {}),
    ...duplicate,
    referenceWorkerId,
  }));

  const filteredDuplicates = useMemo(() => {
    if (showVerified) {
      return (duplicates || []).filter((duplicate) => duplicate.verified);
    }
    return duplicates;
  }, [duplicates, showVerified]);

  const photoId = '-';
  const registrationIp = '-';

  const arrSessionIps = [];
  const userSessions = arrSessionIps.map((ip) => ip.value).join(', ');

  const fullName = name
    .split(' ')
    .map((word) => {
      return word[0].toUpperCase() + word.substring(1);
    })
    .join(' ');

  const totalDuplicates = useMemo(() => {
    if (total === 0) {
      return '-';
    }

    return total > 1 ? `${total} users` : `${total} user`;
  }, [total]);

  const sidebar = (
    <>
      <DoubleColumnsContainer>
        <div>
          <Label>Total duplicates</Label>
          <Resume>{totalDuplicates}</Resume>
        </div>
        <div>
          <Label>Similarity score</Label>
          <ProgressBar
            progressshowAsNumeric
            progressPercentage={similarityScore}
            progressLabelColor={'#BA4745'}
            progressColor={'#BA4745'}
            width={'66%'}
          />
        </div>
      </DoubleColumnsContainer>
      <Spacer />
      <Label>Main Profile info</Label>
      <ProfileName>{fullName}</ProfileName>
      <ProfileInfo>
        Full name: {fullName} <br />
        Email: {email} <br />
        Mobile phone: {phone} <br />
        Address: {address} <br />
        Country: {country} <br />
      </ProfileInfo>
      <ProfileInfo>
        Registration IP: {registrationIp} <br />
        User Session IP: {userSessions} <br />
        Photo ID: {photoId} <br />
        Social media profile:{' '}
        <SocialMedia>facebook, twitter, linkedin</SocialMedia>
      </ProfileInfo>
    </>
  );

  const footer = <ActionFooter hideDivider rightButtons={actionButtons} />;

  return (
    <Modal
      open={open}
      onExited={onExited}
      onClose={closeModal}
      title="Potential Duplicates"
      sidebar={sidebar}
      footer={footer}
    >
      <div>
        <TableHeader>Duplicates Verification</TableHeader>
        <TableBody>
          <Table
            columns={columns}
            data={filteredDuplicates}
            renderExpandedComponent={noop}
          />
          <PaginationContainer>
            <Pagination
              current={1}
              pageSize={10}
              total={10}
              hideAccessoryRight
            />
            <FloatContainer>
              <FloatLeft>
                <ShowVerified>Show verified</ShowVerified>
              </FloatLeft>
              <FloatRight>
                <Switch
                  name="showVerified"
                  value={showVerified}
                  onChange={() => setShowVerified(!showVerified)}
                />
              </FloatRight>
            </FloatContainer>
          </PaginationContainer>
        </TableBody>
      </div>
    </Modal>
  );
}

Duplicates.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  data: PropTypes.objectOf(PropTypes.any).isRequired,
};
