import React, { useEffect, useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import Select from '../../../../Select';
import Input from '../../../../Input';
import { CancelButton, SelectGroup, HiringTargetLink } from './styles';
import { Section, Label } from '../../../../../styles';
import FloatContainer, { FloatRight } from '../../../../Float';
import Button from '../../../../Button';
import DateInput from '../../../../DateInput';
import Modal from '../../../../Modal';
// import { buildInternalUserName } from '../helpers';
import ErrorPopover, { ErrorPopoverProps } from '../../../../ErrorPopover';
import { ENTITIES, fetch, selectors } from '../../../../../store/ducks/options';
import { userHasAnyRole } from '../../../../../store/ducks/security';
import ROLES from '../../../../../constants/roles';
import InformationTooltip from '../../../../ProcessBuilder/InformationTooltip';
import { hiringTargetsGuidelineLink } from '../../../../../constants/externalLinks';
// import { userHasAnyRole } from '../../../../../helpers/security-tools';

const tomorrow = new Date();

tomorrow.setDate(new Date().getDate() + 1);

function filterDate(date) {
  return new Date(date).valueOf() > new Date().setHours(0, 0, 0, 0);
}

function preventNonNumbers(fn) {
  return (event) => {
    const {
      target: { value },
    } = event;

    if (/\D/g.test(value)) {
      return;
    }

    fn(event);
  };
}

function renderDialectError(submitCount, error) {
  if (typeof error === 'string' && error && `${error}`.includes('required')) {
    return submitCount >= 1 ? error : null;
  }

  return error;
}

const hiringTargetLinkTooltip = (
  <>
    Check hiring target guidelines{' '}
    <HiringTargetLink href={hiringTargetsGuidelineLink} target="blank">
      here
    </HiringTargetLink>
    .
  </>
);

export default function HiringTargetsModal(props) {
  const {
    setFieldValue,
    values,
    submitCount,
    errors,
    modalProps,
    modalControls,
    handleChange,
    submitForm,
    isSubmitting,
    errorPopoverProps,
    isTranslationProject,
  } = props;

  const dispatch = useDispatch();

  // Fetch Data
  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.localeLanguages));
  }, [dispatch]);

  useEffect(() => {
    if (values.language) {
      dispatch(
        fetch({
          optionsFor: ENTITIES.localeDialects,
          nestedKey: values.language,
        }),
      );
    }
  }, [dispatch, values.language]);

  useEffect(() => {
    if (values.languageTo) {
      dispatch(
        fetch({
          optionsFor: ENTITIES.localeDialects,
          nestedKey: values.languageTo,
        }),
      );
    }
  }, [dispatch, values.languageTo]);

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const localeLanguages = useSelector(
    selectors.createSelectSliceFor(ENTITIES.localeLanguages),
  );

  const localeDialectsTo = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.localeDialects,
      values.languageTo || 'no-language',
    ),
  );

  const localeDialectsToOptions = useMemo(() => {
    return localeDialectsTo.data.filter((locale) => locale.value !== '*');
  }, [localeDialectsTo.data]);

  const localeDialects = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.localeDialects,
      values.language || 'no-language',
    ),
  );

  // Submitting Error (Duplicate Hiring Target)
  const submittingError = useMemo(
    () => (errors.submittionError ? new Error(errors.submittionError) : null),
    [errors.submittionError],
  );

  const submittingErrorTitle = useMemo(() => {
    if (
      submittingError &&
      submittingError.message.includes('You have a very similar')
    ) {
      return 'Duplicate Hiring Target';
    }

    return 'Problem while saving';
  }, [submittingError]);

  const handleLanguageChange = useCallback(
    ({ value }) => {
      setFieldValue('language', value);
      setFieldValue('dialect', '');
      dispatch(
        fetch({
          optionsFor: ENTITIES.localeDialects,
          nestedKey: value,
        }),
      );
    },
    [dispatch, setFieldValue],
  );

  const handleLanguageToChange = useCallback(
    ({ value }) => {
      setFieldValue('languageTo', value);
      setFieldValue('dialectTo', '');
      dispatch(
        fetch({
          optionsFor: ENTITIES.localeDialects,
          nestedKey: value,
        }),
      );
    },
    [dispatch, setFieldValue],
  );

  const internalUsers = useSelector(
    selectors.createSelectSliceFor(ENTITIES.internalUsers),
  );

  const internalUsersOptions = internalUsers.data.map((user) => ({
    value: user.id,
    label: `${user.lastName} (${user.firstName})`,
  }));

  const isRecruitingTeam = useSelector(
    userHasAnyRole(ROLES.RECRUITING, ROLES.RECRUITING_SUPER),
  );

  return (
    <Modal
      open={modalProps.open}
      onClose={modalProps.onClose}
      onExited={modalProps.onExited}
      title="Add Hiring Target"
      closeOnOverlayClick={!isSubmitting}
      closeOnEsc={!isSubmitting}
    >
      <Section>
        <Row>
          <Col col="6">
            <Label htmlFor="language">
              Locale Language
              {isTranslationProject ? ' From' : ''}
            </Label>
            <Select
              value={values.language}
              name="language"
              id="language"
              onChange={handleLanguageChange}
              options={localeLanguages.data}
              loading={localeLanguages.loading}
              error={
                localeLanguages.error || (submitCount >= 1 && errors.language)
              }
              data-testid="project-setup--recruitment--hiring-targets-modal-language-select"
              placeholder="Select language"
              containerStyles={SelectGroup}
            />
          </Col>
          <Col col="6">
            <Label htmlFor="dialect">
              Language Region
              {isTranslationProject ? ' From' : ''}
            </Label>
            <Select
              value={values.dialect}
              name="dialect"
              onChange={({ value }) => setFieldValue('dialect', value)}
              options={localeDialects.data}
              loading={localeDialects.loading}
              data-testid="project-setup--recruitment--hiring-targets-modal-dialec-select"
              error={renderDialectError(
                submitCount,
                localeDialects.error || errors.dialect,
              )}
              placeholder="Select region"
            />
          </Col>
        </Row>
      </Section>

      {isTranslationProject && (
        <Section>
          <Row>
            <Col col="6">
              <Label htmlFor="languageTo">Locale Language To</Label>
              <Select
                value={values.languageTo}
                name="languageTo"
                id="languageTo"
                onChange={handleLanguageToChange}
                options={localeLanguages.data}
                loading={localeLanguages.loading}
                error={
                  localeLanguages.error ||
                  (submitCount >= 1 && errors.languageTo)
                }
                data-testid="project-setup--recruitment--hiring-targets-modal-language-to-select"
                placeholder="Select language"
              />
            </Col>
            <Col col="6">
              <Label htmlFor="dialectTo">Language Region To</Label>
              <Select
                value={values.dialectTo}
                name="dialectTo"
                onChange={({ value }) => setFieldValue('dialectTo', value)}
                options={localeDialectsToOptions}
                loading={localeDialectsTo.loading}
                error={
                  localeDialectsTo.error ||
                  (submitCount >= 1 && errors.dialectTo)
                }
                data-testid="project-setup--recruitment--hiring-targets-modal-dialect-to-select"
                placeholder="Select region"
              />
            </Col>
          </Row>
        </Section>
      )}

      <Section>
        <Row>
          <Col col="6">
            <Label htmlFor="country">Restrict to Country</Label>
            <Select
              value={values.country}
              name="country"
              onChange={({ value }) => setFieldValue('country', value)}
              options={countries.data}
              loading={countries.loading}
              error={countries.error || (submitCount >= 1 && errors.country)}
              placeholder="Select country"
              data-testid="project-setup--recruitment--hiring-targets-modal-country-select"
            />
          </Col>
          <Col col="6">
            <Label htmlFor="owner">Assign Owner</Label>
            <Select
              disabled={!isRecruitingTeam}
              value={values.owner}
              name="owner"
              onChange={({ value }) => setFieldValue('owner', value)}
              options={internalUsersOptions}
              loading={internalUsers.loading}
              error={internalUsers.error || (submitCount >= 1 && errors.owner)}
              placeholder="Select owner"
            />
          </Col>
        </Row>
      </Section>

      <Section>
        <Row>
          <Col col="6">
            <Label htmlFor="hiringDeadline">Hiring Deadline</Label>
            <DateInput
              value={values.hiringDeadline}
              minDate={tomorrow}
              id="hiringDeadline"
              name="hiringDeadline"
              onChange={(value) => setFieldValue('hiringDeadline', value)}
              filterDate={filterDate}
              error={submitCount >= 1 && errors.hiringDeadline}
              placeholder="Select deadline"
              data-testid="project-setup--recruitment--hiring-targets-modal-hiring-deadline"
            />
          </Col>
          <Col col="4">
            <Label htmlFor="target">
              Target
              <InformationTooltip
                tooltipIconStyle={{ bottom: 0 }}
                title={hiringTargetLinkTooltip}
              />
            </Label>
            <Input
              name="target"
              id="target"
              placeholder="Number"
              value={values.target || ''}
              onChange={preventNonNumbers(handleChange)}
              data-testid="project-setup--recruitment--hiring-targets-modal-target"
              error={submitCount >= 1 && errors.target}
            />
          </Col>
        </Row>
      </Section>

      <ErrorPopover title={submittingErrorTitle} {...errorPopoverProps} />

      <FloatContainer>
        <FloatRight>
          <CancelButton
            disabled={isSubmitting}
            type="secondary"
            onClick={modalControls.closeModal}
            data-testid="project-setup--recruitment--hiring-targets-modal-cancel-button"
          >
            Cancel
          </CancelButton>
          <Button
            loading={isSubmitting}
            type="primary"
            onClick={submitForm}
            data-testid="project-setup--recruitment--hiring-targets-modal-save-button"
          >
            Save
          </Button>
        </FloatRight>
      </FloatContainer>
    </Modal>
  );
}

HiringTargetsModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  submitForm: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  isTranslationProject: PropTypes.bool.isRequired,
};
