import capitalize from 'lodash/capitalize';

export default function mapToOptions(valueKey, labelKey) {
  return (array) =>
    Array.isArray(array)
      ? array.map((entity) => ({
          value:
            typeof valueKey === 'function'
              ? valueKey(entity)
              : entity[valueKey],
          label:
            typeof labelKey === 'function'
              ? labelKey(entity)
              : entity[labelKey],
        }))
      : [];
}

export function mapOptionCapitalizeLabels(option) {
  return {
    ...option,
    label: capitalize(`${option.label}`.toLowerCase()),
  };
}
