import { useEffect, useState, useCallback, useMemo } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as Yup from 'yup';
import shortId from 'shortid';
import get from 'lodash/get';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import TranslationQuizStep from './TranslationQuizStep';
import {
  loadStepById,
  setProcessAttributes,
  createSelectStepDefinition,
} from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import createArrayValidation from '../../../../helpers/array-validation';
import { findByValue, findByNestedValue } from '../../../../helpers/find';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import {
  formatMacroLanguageLabel,
  mapToMacroLanguages,
} from '../../helpers/macroLanguages';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';

const fields = [
  {
    name: 'fromLocale',
    order: 1,
  },
  {
    name: 'toLocale',
    order: 2,
  },
  {
    name: 'languageQuiz',
    order: 3,
  },
];

const initialFormValues = {
  quizzes: [
    {
      fromLocale: null,
      toLocale: null,
      languageQuiz: null,
      key: shortId.generate(),
    },
  ],
};

function transformError(error, value, index) {
  if (error.path === 'fromLocale') {
    return new Yup.ValidationError(
      requiredMessage`Locale`,
      value,
      `fromLocale${index}`,
    );
  }

  if (error.path === 'toLocale') {
    return new Yup.ValidationError(
      requiredMessage`Translate to locale`,
      value,
      `toLocale${index}`,
    );
  }

  return new Yup.ValidationError(
    requiredMessage`Translation quiz`,
    value,
    `languageQuiz${index}`,
  );
}

function useHooksAsProps(props) {
  const {
    setFieldValue,
    card: { stepDefinitionId },
    values: { quizzes = [] },
  } = props;

  const [formCount, setFormCount] = useState(1);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
    dispatch(
      fetch({
        optionsFor: ENTITIES.locales,
        includeWildcard: true,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  const addForm = useCallback(() => {
    setFormCount(formCount + 1);
  }, [formCount]);

  const stepDefinition = useSelector(
    createSelectStepDefinition(stepDefinitionId),
  );

  const locales = useSelector(selectors.createSelectSliceFor(ENTITIES.locales));
  const localeOptions = mapToMacroLanguages(locales.data);

  const languageQuizFieldOrder = fields.find((f) => f.name === 'languageQuiz')
    .order;
  const languageQuizOptions = useMemo(() => {
    return mapToOptions(
      'id',
      'name',
    )(
      get(
        get(stepDefinition, 'attributes', []).find(
          (s) => s.attributeOrder === languageQuizFieldOrder,
        ),
        'options',
        [],
      ),
    );
  }, [languageQuizFieldOrder, stepDefinition]);

  const selectedQuizzes = useMemo(() => {
    return quizzes
      .filter((quiz) => quiz.fromLocale && quiz.toLocale && quiz.languageQuiz)
      .map((quiz) => ({
        fromLocale: formatMacroLanguageLabel(
          findByNestedValue(localeOptions, quiz.fromLocale, 'options'),
        ),
        toLocale: formatMacroLanguageLabel(
          findByNestedValue(localeOptions, quiz.toLocale, 'options'),
        ),
        languageQuiz: get(
          findByValue(
            languageQuizOptions,
            parseInt(quiz.languageQuiz, 10) || 0,
          ),
          'label',
          '',
        ),
      }));
  }, [localeOptions, quizzes, languageQuizOptions]);

  const handleAddQuiz = useCallback(() => {
    setFieldValue('quizzes', [
      ...quizzes,
      {
        fromLocale: null,
        toLocale: null,
        languageQuiz: null,
        key: shortId.generate(),
      },
    ]);
  }, [quizzes, setFieldValue]);

  const handleRemoveQuiz = useCallback(
    (index) => {
      setFieldValue('quizzes', [
        ...quizzes.slice(0, index),
        ...quizzes.slice(index + 1, quizzes.length),
      ]);
    },
    [quizzes, setFieldValue],
  );

  const handleQuizChange = (index, { value }) => {
    const item = quizzes[index];

    setFieldValue('quizzes', [
      ...quizzes.slice(0, index),
      { ...item, languageQuiz: value },
      ...quizzes.slice(index + 1, quizzes.length),
    ]);
  };

  const handleLocaleChange = (index, { value }, name) => {
    const item = quizzes[index];

    setFieldValue('quizzes', [
      ...quizzes.slice(0, index),
      { ...item, [name]: value },
      ...quizzes.slice(index + 1, quizzes.length),
    ]);
  };

  return {
    localeOptions,
    languageQuizOptions,
    formCount,
    addForm,
    selectedQuizzes,
    handleAddQuiz,
    handleRemoveQuiz,
    handleQuizChange,
    handleLocaleChange,
  };
}

function mapPropsToValues(props) {
  const {
    card: { subSteps },
  } = props;

  if (subSteps && subSteps.length) {
    return {
      quizzes: subSteps.map((step) => {
        const fromLocale = get(
          step.attributes.find((att) => att.name === 'fromLocaleCode'),
          'value',
          '',
        );

        const toLocale = get(
          step.attributes.find((att) => att.name === 'toLocaleCode'),
          'value',
          '',
        );

        const quizId = get(
          step.attributes.find((att) => att.name === 'quizId'),
          'value',
          '',
        );

        return {
          fromLocale,
          toLocale,
          languageQuiz: parseInt(quizId, 10) || 0,
          key: shortId.generate(),
        };
      }),
    };
  }

  return initialFormValues;
}

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  const subSteps =
    values && values.quizzes
      ? values.quizzes.map((quiz) => ({
          attributes: [
            {
              name: 'fromLocaleCode',
              value: quiz.fromLocale,
              attributeOrder: 1,
            },
            {
              name: 'toLocaleCode',
              value: quiz.toLocale,
              attributeOrder: 2,
            },
            {
              name: 'quizId',
              value: `${quiz.languageQuiz}`,
              attributeOrder: 3,
            },
          ],
        }))
      : null;

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      subSteps,
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

const validateArray = createArrayValidation(
  {
    fromLocale: Yup.string().required('Locale is required'),
    toLocale: Yup.string().required('Translate to locale is required'),
    languageQuiz: Yup.number().required('Language Quiz is required'),
  },
  transformError,
);

const validationSchema = Yup.object()
  .strict()
  .shape({
    quizzes: Yup.array().test('localeAndLanguageQuiz', '', validateArray),
  });

export default compose(
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(TranslationQuizStep);
