import React from 'react';
import PropTypes from 'prop-types';
import DemographicSettings from './DemographicSettings';
import DemographicData from './DemographicData';

export default function DemographicsModal(props) {
  const {
    nextStep,
    currentStep,
    closeModal,
    goBack,
    isGoingBack,
    openModal,
    modalData,
  } = props;

  return (
    <>
      {currentStep === 1 && (
        <DemographicSettings
          nextStep={nextStep}
          isGoingBack={isGoingBack}
          closeModal={closeModal}
        />
      )}
      {currentStep === 2 && (
        <DemographicData
          goBack={goBack}
          closeModal={closeModal}
          openModal={openModal}
          modalData={modalData}
        />
      )}
    </>
  );
}

DemographicsModal.propTypes = {
  nextStep: PropTypes.func.isRequired,
  currentStep: PropTypes.number.isRequired,
  closeModal: PropTypes.func.isRequired,
  openModal: PropTypes.func.isRequired,
  goBack: PropTypes.func.isRequired,
  isGoingBack: PropTypes.bool.isRequired,
  modalData: PropTypes.objectOf(
    PropTypes.shape({
      requirementsByGroup: PropTypes.array,
      aggregatedRequirements: PropTypes.object,
    }),
  ),
};

DemographicsModal.defaultProps = {
  modalData: undefined,
};
