import createEntityStateProvider from '../../helpers/createEntityStateProvider';
import ENDPOINTS from '../../../constants/endpoints';
import { selectProject } from '../project';

function getUrl(state) {
  const project = selectProject(state);
  return ENDPOINTS.V2.PROJECT_PAGES({
    query:
      project && project.id
        ? {
            projectId: project.id,
          }
        : {},
  });
}
function transform(projectPages = []) {
  return Array.from(projectPages || []).map((projectPage) => ({
    ...projectPage,
    id: projectPage.id || 'DEFAULT',
  }));
}

const [
  {
    reducer,
    actions: {
      fetchAll,
      fetchFailed,
      setAll,
      addOne,
      addMany,
      removeOne,
      removeMany,
      removeAll,
      updateOne,
      updateMany,
      upsertOne,
      upsertMany,
    },
  },
  {
    selectIds,
    selectEntities,
    selectAll,
    selectTotal,
    selectById,
    selectLoading,
    selectError,
  },
  projectPagesSaga,
] = createEntityStateProvider('projectPages', getUrl, 'id', transform);

export default reducer;

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
  selectLoading,
  selectError,
  projectPagesSaga,
};

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.projectPages = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
