import { useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector, batch } from 'react-redux';
import withHooks from 'hook-hoc';
import { compose, withProps } from 'recompose';
import { useFormikContext } from 'formik';
import orderBy from 'lodash/orderBy';
import TargetScreening from './TargetScreening';
import {
  fetchModules,
  selectTargetBuilderModules,
  requestOpenTargetBuilder,
  selectTargetBuilderOpen,
} from '../../../../store/ducks/targetBuilder';
import { closeProcessBuilder } from '../../../../store/ducks/processBuilder';
import {
  fetchTargetScreening,
  selectTargetScreening,
  selectLoading,
} from '../../../../store/ducks/targetScreening';
import * as registrationProcesses from '../../../../store/ducks/registrationProcesses';
import { selectRegistrationAndQualification } from '../../../../store/ducks/project';
import { getCurrentUser } from '../../../../store/ducks/security';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';

const existingOrNewTabs = [
  { label: 'Use existing target group' },
  { label: 'Create new target Group' },
];

const createProps = withProps({
  existingOrNewTabs,
});

function useHooksAsProps() {
  const { setFieldValue } = useFormikContext();

  const dispatch = useDispatch();

  const currentUser = useSelector(getCurrentUser());
  const targetBuilderOpen = useSelector(selectTargetBuilderOpen);
  const targetGroups = useSelector(selectTargetScreening);
  const modules = useSelector(selectTargetBuilderModules);
  const loading = useSelector(selectLoading);
  const registrationSteps = useSelector(registrationProcesses.selectSteps);
  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );
  const languages = useSelector(
    selectors.createSelectSliceFor(ENTITIES.localeLanguages),
  );
  const registrationAndQualification = useSelector(
    selectRegistrationAndQualification,
  );

  const { registrationProcessTemplate } = registrationAndQualification;

  let hasCompleteAdditionalAttributes = false;

  if (registrationSteps.steps.length) {
    const steps = registrationSteps.steps.map(
      (step) =>
        step.stepDefinition.sequenceFlow === 'completeProjectAttributesFlow',
    );
    hasCompleteAdditionalAttributes = steps.includes(true);
  }

  const targetGroupOptions = useMemo(
    () =>
      orderBy(
        targetGroups.map((target) => ({
          label: target.name,
          value: target.id,
          lowerCaseLabel: target.name.toLowerCase(),
        })),
        ['lowerCaseLabel'],
        ['asc'],
      ),
    [targetGroups],
  );

  useEffect(() => {
    batch(() => {
      dispatch(fetchTargetScreening());
      dispatch(fetchModules());
      dispatch(registrationProcesses.fetchOptions());
      dispatch(fetch(ENTITIES.countries));
      dispatch(fetch(ENTITIES.localeLanguages));
      if (registrationProcessTemplate) {
        dispatch(registrationProcesses.fetchSteps(registrationProcessTemplate));
      }
    });
  }, [dispatch, registrationProcessTemplate]);

  const handleTabChange = useCallback(
    (nextTab) => setFieldValue('currentTab', nextTab),
    [setFieldValue],
  );

  const handleProfileTargetIdChange = useCallback(
    ({ value }) => setFieldValue('profileTargetId', value),
    [setFieldValue],
  );

  const handleCreateTargetBuilder = useCallback(() => {
    dispatch(requestOpenTargetBuilder({}));
  }, [dispatch]);

  const handleCancel = useCallback(() => {
    dispatch(closeProcessBuilder());
  }, [dispatch]);

  const handleEdit = useCallback(
    ({ id }) => {
      dispatch(
        requestOpenTargetBuilder({
          processID: id,
        }),
      );
    },
    [dispatch],
  );

  const handleDelete = useCallback(() => {
    setFieldValue('profileTargetId', null);
  }, [setFieldValue]);

  const handleProcessChange = useCallback(
    (changedProcessType, processId, isNewProcess) => {
      // set Formik value
      handleProfileTargetIdChange({
        value: processId,
        isNewProcess,
      });
      setFieldValue('currentTab', 0);
    },
    [handleProfileTargetIdChange, setFieldValue],
  );

  return {
    currentUser,
    hasCompleteAdditionalAttributes,
    targetGroupOptions,
    targetBuilderOpen,
    targetGroups,
    modules,
    countries,
    languages,
    loading,
    handleEdit,
    handleDelete,
    handleTabChange,
    handleProfileTargetIdChange,
    handleCreateTargetBuilder,
    handleProcessChange,
    handleCancel,
  };
}

export default compose(
  createProps,
  withHooks(useHooksAsProps),
)(TargetScreening);
