import { useEffect } from 'react';
import { connect, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as Yup from 'yup';
import get from 'lodash/get';
import QuestionLoader from './QuestionLoader';
import store from '../../../store';
import {
  setProcessAttributes,
  loadQuestionById,
  createSelectStepDefinition,
} from '../../../store/ducks/targetBuilder';
import {
  checkboxValidator,
  inputValidator,
  selectValidator,
  selectGroupValidator,
  selectDatePickerValidator,
} from './validations';
import { findByIgnoringCase } from '../../../helpers/find';

const actionOptions = [
  { value: 'passThrough', label: 'Pass Through' },
  { value: 'reject', label: 'Reject' },
];

const multipleChoiceTypes = [
  'CHECKBOX_GROUP',
  'MULTIPLE_CHOICE',
  'DATE_PICKER',
  'YES_NO',
];

function handleSubmit(values, { props }) {
  const { setCollapsed, stepDefinitionId, cardKey, items = [] } = props;

  const updatedItems = items.map((item) => ({
    ...item,
    attributes: [
      {
        ...get(item.attributes, [0], {}),
        enabled: { [item.id]: values.enabled[item.id] },
      },
    ],
  }));

  store.dispatch(
    setProcessAttributes({
      id: stepDefinitionId,
      key: cardKey,
      dirty: false,
      attributes: [{ ...values }],
      items: updatedItems,
    }),
  );

  setCollapsed(true);
}

function useHooksAsProps(outerProps) {
  const dispatch = useDispatch();
  const { stepDefinitionId, items = [] } = outerProps;

  useEffect(() => {
    dispatch(
      loadQuestionById({
        stepDefinitionId,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  useEffect(() => {
    items.map((item) =>
      dispatch(
        loadQuestionById({
          stepDefinitionId: item.id,
        }),
      ),
    );
  }, [dispatch, items]);

  return {
    actionOptions,
  };
}

function mapStateToProps(state, outerProps) {
  const { stepDefinitionId } = outerProps;

  const data = createSelectStepDefinition(state, stepDefinitionId) || {};

  return {
    data,
  };
}

function mapPropsToValues(props) {
  const { attributes, data, items = [] } = props;

  if (data) {
    const { id, answerList = [], logic, questionType } = data;

    if (attributes && attributes.length) {
      return get(attributes, 0, {});
    }

    const answers = {};
    const enabled = {};

    const countryOrLanguage = answerList.filter((answer) =>
      findByIgnoringCase(['COUNTRY LIST', 'LANGUAGE LIST'], answer.answer),
    );

    enabled[id] = true;
    answers[id] =
      countryOrLanguage.length === 0
        ? answerList.map((answer) => ({
            ...answer,
            questionType,
          }))
        : null;

    items.forEach(
      ({
        id: childrenId,
        answerList: childrenAnswerList,
        questionType: childrenQuestionType,
      }) => {
        enabled[childrenId] = true;
        answers[childrenId] = childrenAnswerList.map((answer) => ({
          ...answer,
          questionType: childrenQuestionType,
        }));
      },
    );

    return {
      answers,
      logic,
      enabled,
      questionType,
    };
  }

  return {};
}

const validationSchema = (props) => {
  const {
    data: { id, questionType },
    items = [],
  } = props;

  return Yup.lazy(({ enabled }) => {
    const validators =
      {
        CHECKBOX_GROUP: checkboxValidator,
        MULTIPLE_CHOICE: checkboxValidator,
        OPEN_TEXT: inputValidator,
        OPEN_TEXT_PARAGRAPH: inputValidator,
        YES_NO: selectGroupValidator,
        DATE_PICKER: selectDatePickerValidator,
      } || null;

    const schema = {};
    schema[`firstOption${id}`] = selectValidator;
    schema[`secondOption${id}`] = selectValidator;

    const openTexts = ['OPEN_TEXT', 'OPEN_TEXT_PARAGRAPH'];

    if (openTexts.includes(questionType)) {
      delete schema[`firstOption${id}`];
      delete schema[`secondOption${id}`];
    }

    if (multipleChoiceTypes.includes(questionType)) {
      if (questionType !== 'YES_NO') {
        schema.answers = {
          [id]: validators[questionType],
        };
      }
      items.forEach((item) => {
        const isEnabled = enabled[item.id];

        if (item.questionType !== 'YES_NO') {
          schema.answers = {
            ...schema.answers,
            [item.id]: isEnabled
              ? validators[item.questionType]
              : Yup.mixed().notRequired(),
          };
        }
        schema[`firstOption${item.id}`] = isEnabled
          ? selectValidator
          : Yup.mixed().notRequired();
        schema[`secondOption${item.id}`] = isEnabled
          ? selectValidator
          : Yup.mixed().notRequired();

        if (openTexts.includes(item.questionType)) {
          delete schema[`firstOption${item.id}`];
          delete schema[`secondOption${item.id}`];
        }
      });
      schema.answers = Yup.object(schema.answers);
    }

    return Yup.object(schema);
  });
};

export default compose(
  withHooks(useHooksAsProps),
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema,
  }),
)(QuestionLoader);
