import React, { useCallback, useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useFormikContext } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import mapToOptions from '../../../../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  ENTITIES,
  fetch,
  selectors,
} from '../../../../../../store/ducks/options';
import { Section, Label } from '../../../../../../styles';
import Checkbox from '../../../../../Checkbox';
import Select from '../../../../../Select';
import { CheckGroup } from '../../../../ProjectToolsForm/styles';
import ResourceFormContainer from '../ResourceFormContainer';

export default function AcademyResourceForm(props) {
  const { closeModal } = props;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.academies));
  }, [dispatch]);

  const { data: academies, error, loading } = useSelector(
    selectors.createSelectSliceFor(ENTITIES.academies),
  );

  const academiesOptions = useMemo(
    () => mapToOptions('id', 'name')(academies),
    [academies],
  );

  const { setFieldValue, values, submitCount, errors } = useFormikContext();

  const handleAllowQualifyingApplicantsAccessChange = useCallback(
    (event) => {
      if (!event.target.value && !values.allowActiveUserAccess) {
        setFieldValue('allowActiveUserAccess', true);
      }

      setFieldValue('allowQualifyingApplicantAccess', event.target.value);
    },
    [setFieldValue, values.allowActiveUserAccess],
  );

  const handleAllowActiveUsersAccess = useCallback(
    (event) => {
      if (!event.target.value && !values.allowQualifyingApplicantAccess) {
        setFieldValue('allowQualifyingApplicantAccess', true);
      }

      setFieldValue('allowActiveUserAccess', event.target.value);
    },
    [setFieldValue, values.allowQualifyingApplicantAccess],
  );

  return (
    <ResourceFormContainer closeModal={closeModal}>
      <Section>
        <Label htmlFor="resourceId">Academy</Label>
        <Select
          name="resourceId"
          id="resourceId"
          options={academiesOptions}
          onChange={({ value }) => setFieldValue('resourceId', value)}
          value={values.resourceId}
          placeholder="Select existing academy"
          error={error || (submitCount >= 1 && errors.resourceId)}
          loading={loading}
        />
      </Section>

      <Section>
        <Section marginBottom={24}>
          <CheckGroup>
            <Checkbox
              name="allowQualifyingApplicantAccess"
              onChange={handleAllowQualifyingApplicantsAccessChange}
              checked={
                typeof values.allowQualifyingApplicantAccess === 'undefined'
                  ? true
                  : values.allowQualifyingApplicantAccess
              }
              label="Allow Qualifying Applicants Access"
              bold={false}
            />
          </CheckGroup>
        </Section>
        <Section>
          <CheckGroup>
            <Checkbox
              name="allowActiveUserAccess"
              onChange={handleAllowActiveUsersAccess}
              checked={values.allowActiveUserAccess}
              label="Allow Active Users Access"
              bold={false}
            />
          </CheckGroup>
        </Section>
      </Section>
    </ResourceFormContainer>
  );
}

AcademyResourceForm.propTypes = {
  closeModal: PropTypes.func.isRequired,
};
