import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { Portal } from 'react-portal';

import { useDispatch, useSelector } from 'react-redux';
import { useFormikContext } from 'formik';
import { useWindowHeight } from '@react-hook/window-size';
import { SectionTitle, Label } from '../../../styles';
import {
  FlexItem,
  InputWrapper,
  InputSection,
  ProcessDescription,
  ProcessBuilderWindow,
} from './styles';
import * as registrationProcesses from '../../../store/ducks/registrationProcesses';
import * as qualificationProcesses from '../../../store/ducks/qualificationProcesses';
import ProcessSteps from '../../ProcessSteps';
import {
  closeProcessBuilder,
  PROCESS_TYPE,
  requestOpenProcessBuilder,
  selectProcessBuilderOpen,
  selectProcessModules,
  selectProcessType,
} from '../../../store/ducks/processBuilder';
import NoEntries from '../../NoEntries';
import TabSwitcher from '../../TabSwitcher';
import Button from '../../Button';
import Select from '../../Select';
import mapToOptions from '../../../pages/ProjectSetup/helpers/mapToOptions';
import ProcessBuilder from '../../ProcessBuilder';
import { PreviewProvider } from '../../Builder/helpers/preview-context';
import PreviewModal from '../../ProcessBuilder/PreviewModal';

const viewAsTabs = [
  { label: 'Use existing process' },
  { label: 'Create new process' },
];

export default function RegistrationAndQualificationForm() {
  const { setFieldValue, errors, values, submitCount } = useFormikContext();
  const dispatch = useDispatch();

  // # Data Fetching
  useEffect(() => {
    dispatch(registrationProcesses.fetchOptions());
    dispatch(qualificationProcesses.fetchOptions());
  }, [dispatch]);

  useEffect(() => {
    if (values.registrationProcessTemplate) {
      dispatch(
        registrationProcesses.fetchSteps(values.registrationProcessTemplate),
      );
    }

    return () => dispatch(registrationProcesses.clearStep());
  }, [dispatch, values.registrationProcessTemplate]);

  useEffect(() => {
    if (values.qualificationProcessTemplate) {
      dispatch(
        qualificationProcesses.fetchSteps(values.qualificationProcessTemplate),
      );
    }

    return () => dispatch(qualificationProcesses.clearStep());
  }, [dispatch, values.qualificationProcessTemplate]);

  // # Selectors

  const registrationProcessesSlice = useSelector(
    registrationProcesses.selectOptionsState,
  );

  const qualificationProcessesSlice = useSelector(
    qualificationProcesses.selectOptionsState,
  );

  const registrationTemplatesOptions = useMemo(() => {
    return mapToOptions('id', 'name')(registrationProcessesSlice.options);
  }, [registrationProcessesSlice.options]);

  const qualificationTemplatesOptions = useMemo(() => {
    return mapToOptions('id', 'name')(qualificationProcessesSlice.options);
  }, [qualificationProcessesSlice.options]);

  const registrationSteps = useSelector(registrationProcesses.selectSteps);
  const qualificationSteps = useSelector(qualificationProcesses.selectSteps);

  // State managment for TabSwitcher

  const [creatingNewRegProcess, setCreatingNewRegProcess] = useState(0);
  const [createdNewRegProcess, setCreatedNewRegProcess] = useState(false);

  const [creatingNewQuaProcess, setCreatingNewQuaProcess] = useState(0);
  const [createdNewQuaProcess, setCreatedNewQuaProcess] = useState(false);

  const handleRegisTabChange = useCallback(
    (nextTab) => {
      if (creatingNewRegProcess && createdNewRegProcess && !nextTab) {
        setFieldValue('registrationProcessTemplate', null);
        dispatch(registrationProcesses.clearStep());
        setCreatedNewRegProcess(false);
      }

      setCreatingNewRegProcess(nextTab);
    },
    [createdNewRegProcess, creatingNewRegProcess, dispatch, setFieldValue],
  );

  const handleQualiTabChange = useCallback(
    (nextTab) => {
      if (creatingNewQuaProcess && createdNewQuaProcess && !nextTab) {
        setFieldValue('qualificationProcessTemplate', null);
        dispatch(qualificationProcesses.clearStep());
        setCreatedNewQuaProcess(false);
      }

      setCreatingNewQuaProcess(nextTab);
    },
    [createdNewQuaProcess, creatingNewQuaProcess, dispatch, setFieldValue],
  );

  const handleRegistrationProcessChange = useCallback(
    ({ value, isNewProcess }) => {
      if (isNewProcess) {
        setCreatedNewRegProcess(true);
      } else {
        setCreatedNewRegProcess(false);
        setCreatingNewRegProcess(0);
      }

      setFieldValue('registrationProcessTemplate', value);
    },
    [setFieldValue],
  );

  const handleQualificationProcessChange = useCallback(
    ({ value, isNewProcess }) => {
      if (isNewProcess) {
        setCreatedNewQuaProcess(true);
      } else {
        setCreatedNewQuaProcess(false);
        setCreatingNewQuaProcess(0);
      }

      setFieldValue('qualificationProcessTemplate', value);
    },
    [setFieldValue],
  );

  const windowHeight = useWindowHeight();

  const processBuilderOpen = useSelector(selectProcessBuilderOpen);
  const processType = useSelector(selectProcessType);

  const createRegistrationProcess = useCallback(() => {
    dispatch(
      requestOpenProcessBuilder({
        processType: PROCESS_TYPE.REGISTRATION,
      }),
    );
  }, [dispatch]);

  const createQualificationProcess = useCallback(() => {
    dispatch(
      requestOpenProcessBuilder({
        processType: PROCESS_TYPE.QUALIFICATION,
      }),
    );
  }, [dispatch]);

  const editExistingProcess = useCallback(
    (processId, thisProcessType) => {
      dispatch(
        requestOpenProcessBuilder({
          processType: thisProcessType,
          processID: processId,
        }),
      );
    },
    [dispatch],
  );

  const copyExistingProcess = useCallback(
    (processId, thisProcessType) => {
      dispatch(
        requestOpenProcessBuilder({
          processType: thisProcessType,
          copyFromProcessID: processId,
        }),
      );
    },
    [dispatch],
  );

  const handleProcessChange = useCallback(
    (changedProcessType, processId, isNewProcess) => {
      if (changedProcessType === PROCESS_TYPE.REGISTRATION) {
        // set Formik value
        handleRegistrationProcessChange({
          value: processId,
          isNewProcess,
        });

        // Refetch List
        dispatch(registrationProcesses.fetchOptions());

        // Refetch Step
        dispatch(registrationProcesses.fetchSteps(processId));
      } else {
        handleQualificationProcessChange({
          value: processId,
          isNewProcess,
        });
        dispatch(qualificationProcesses.fetchOptions());
        dispatch(qualificationProcesses.fetchSteps(processId));
      }
    },
    [
      dispatch,
      handleQualificationProcessChange,
      handleRegistrationProcessChange,
    ],
  );

  const handleCancel = useCallback(() => {
    dispatch(closeProcessBuilder());
  }, [dispatch]);

  const processModules = useSelector(selectProcessModules);

  return (
    <PreviewProvider processModules={processModules} processType={processType}>
      <InputSection>
        <SectionTitle>Registration Process</SectionTitle>
        <ProcessDescription>
          Process workers will go through in order to register to a project.
        </ProcessDescription>
        <TabSwitcher
          style={{ maxWidth: '220px' }}
          tabs={viewAsTabs}
          value={creatingNewRegProcess}
          onChange={handleRegisTabChange}
        />
        {creatingNewRegProcess && !createdNewRegProcess ? (
          <>
            <NoEntries label="No process created" />
            <Button onClick={createRegistrationProcess}>Create Process</Button>
          </>
        ) : (
          <>
            <InputWrapper marginTop="30px">
              <FlexItem>
                <Label htmlFor="registrationProcessTemplate">
                  Select Registration process
                </Label>
                <InputWrapper>
                  <FlexItem>
                    <Select
                      name="registrationProcessTemplate"
                      id="registrationProcessTemplate"
                      data-testid="project-setup--reg-qua--reg-process-template"
                      options={registrationTemplatesOptions}
                      loading={registrationProcessesSlice.loading}
                      value={values.registrationProcessTemplate}
                      error={
                        registrationProcessesSlice.error ||
                        (submitCount > 0 && errors.registrationProcessTemplate)
                      }
                      onChange={handleRegistrationProcessChange}
                    />
                  </FlexItem>
                </InputWrapper>
              </FlexItem>
            </InputWrapper>
            <ProcessSteps
              processName={registrationSteps.processName}
              steps={registrationSteps.steps}
              creatorName={registrationSteps.createdBy}
              createdAt={registrationSteps.dateCreated}
              loading={registrationSteps.loading}
              error={registrationSteps.error}
              processId={registrationSteps.processId}
              processType={PROCESS_TYPE.REGISTRATION}
              editExistingProcess={editExistingProcess}
              copyExistingProcess={copyExistingProcess}
            />
          </>
        )}
      </InputSection>

      <InputSection>
        <SectionTitle>Qualification Process</SectionTitle>
        <ProcessDescription>
          Process workers will go through in order to qualify to a project.
        </ProcessDescription>
        <TabSwitcher
          style={{ maxWidth: '220px' }}
          tabs={viewAsTabs}
          value={creatingNewQuaProcess}
          onChange={handleQualiTabChange}
        />
        {creatingNewQuaProcess && !createdNewQuaProcess ? (
          <>
            <NoEntries label="No process created" />
            <Button onClick={createQualificationProcess}>Create Process</Button>
          </>
        ) : (
          <>
            <InputWrapper marginTop="30px">
              <FlexItem>
                <Label htmlFor="qualificationProcessTemplate">
                  Select Qualification process
                </Label>
                <InputWrapper>
                  <FlexItem>
                    <Select
                      name="qualificationProcessTemplate"
                      id="qualificationProcessTemplate"
                      data-testid="project-setup--reg-qua--qua-process-template"
                      options={qualificationTemplatesOptions}
                      loading={qualificationProcessesSlice.loading}
                      value={values.qualificationProcessTemplate}
                      error={
                        qualificationProcessesSlice.error ||
                        (submitCount > 0 && errors.qualificationProcessTemplate)
                      }
                      onChange={handleQualificationProcessChange}
                    />
                  </FlexItem>
                </InputWrapper>
              </FlexItem>
            </InputWrapper>
            <ProcessSteps
              processName={qualificationSteps.processName}
              steps={qualificationSteps.steps}
              creatorName={qualificationSteps.createdBy}
              createdAt={qualificationSteps.dateCreated}
              loading={qualificationSteps.loading}
              error={qualificationSteps.error}
              processId={qualificationSteps.processId}
              processType={PROCESS_TYPE.QUALIFICATION}
              editExistingProcess={editExistingProcess}
              copyExistingProcess={copyExistingProcess}
            />
          </>
        )}
      </InputSection>

      {processBuilderOpen && (
        <Portal>
          <ProcessBuilderWindow style={{ height: windowHeight }}>
            <ProcessBuilder
              processType={processType}
              onProcessChange={handleProcessChange}
              onCancel={handleCancel}
            />
          </ProcessBuilderWindow>
        </Portal>
      )}

      <PreviewModal />
    </PreviewProvider>
  );
}
