export const StandInTable = styled.div`
  display: flex;
  flex-direction: row;

  thead th {
    height: 45px;
    line-height: 26px;
    padding-top: 9px;
    padding-left: 8px;
  }

  thead tr {
    border-top: 1px solid ${(props) => props.theme.colors.lightPeriwinkle};
    background-color: ${(props) => props.theme.colors.paleGrey};
  }

  tbody tr {
    height: ${(props) => enforcePx(props.multiplier * 52, '52px')};
  }

  tbody td {
    padding: 8px 16px 8px 8px;
    border-right: 1px solid
      ${(props) => props.theme.colors.lightPeriwinkle}!important;

    &:first-child {
      line-height: 34px;
      line-height: ${(props) => enforcePx(props.multiplier * 52 - 27, '16px')};
    }
  }
`;

export const Flex = styled.div`
  flex: 1;
  max-width: ${(props) => {
    if (props.columns === 0) {
      return '740px';
    }

    if (props.columns === 1) {
      return '600px';
    }

    return '100%';
  }};

  tbody td {
    padding-right: 16px;
  }

  thead th {
    padding-left: 5px;
  }

  th:first-child,
  td:first-child {
    padding-left: 10px;
  }
`;
