import React, { useContext } from 'react';
import { ThemeContext } from 'styled-components';
import PropTypes from 'prop-types';
// components
import MessageError from '../MessageError';
// styles
import {
  InputBase,
  InputContainer,
  IconContainer,
  IconBeforeContainer,
  IconAfterContainer,
} from './styles';

export default function Input(props) {
  const {
    className,
    'data-testid': dataTestId,
    disabled,
    error,
    errorIncreasesHeight,
    iconAfter,
    iconBefore,
    id,
    name,
    onChange,
    onBlur,
    onKeyDown,
    placeholder,
    showCheck,
    type,
    value,
    maxLength,
  } = props;

  const styledTheme = useContext(ThemeContext);

  return (
    <>
      <InputContainer>
        {iconBefore && <IconBeforeContainer>{iconBefore}</IconBeforeContainer>}
        <InputBase
          className={className}
          data-testid={dataTestId}
          disabled={disabled}
          error={error}
          hasIconAfter={!!iconAfter}
          hasIconBefore={!!iconBefore}
          id={id}
          name={name}
          onChange={onChange}
          onBlur={onBlur}
          onKeyDown={onKeyDown}
          placeholder={placeholder}
          type={type}
          value={value || ''}
          maxLength={maxLength}
        />
        {iconAfter && <IconAfterContainer>{iconAfter}</IconAfterContainer>}
        {showCheck && (
          <IconContainer>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="13"
              height="10"
              viewBox="0 0 13 10"
              data-testid="input--tick-icon"
            >
              <path
                fill={styledTheme.colors.lightGreyBlue}
                fillRule="nonzero"
                d="M11.581 1.24l.178.179a1 1 0 0 1 0 1.414L5.211 9.381l-.052.05a1.126 1.126 0 0 1-1.644.055L.33 6.3a1.126 1.126 0 0 1 1.592-1.592l2.389 2.388 5.856-5.855a1 1 0 0 1 1.414 0z"
              />
            </svg>
          </IconContainer>
        )}
      </InputContainer>
      <MessageError
        isVisible={!!error}
        message={error}
        increasesHeight={errorIncreasesHeight}
      />
    </>
  );
}

Input.propTypes = {
  name: PropTypes.string.isRequired,
  type: PropTypes.string,
  className: PropTypes.string,
  error: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
    PropTypes.instanceOf(Error),
  ]),
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  onKeyDown: PropTypes.func,
  placeholder: PropTypes.string,
  showCheck: PropTypes.bool,
  errorIncreasesHeight: PropTypes.bool,
  disabled: PropTypes.bool,
  iconBefore: PropTypes.node,
  iconAfter: PropTypes.node,
  id: PropTypes.string,
  maxLength: PropTypes.number,
  'data-testid': PropTypes.string,
};

Input.defaultProps = {
  type: undefined,
  className: undefined,
  value: undefined,
  onChange: () => {},
  onBlur: () => {},
  placeholder: undefined,
  error: false,
  onKeyDown: undefined,
  showCheck: false,
  errorIncreasesHeight: true,
  disabled: false,
  iconBefore: undefined,
  iconAfter: undefined,
  id: undefined,
  maxLength: Infinity,
  'data-testid': undefined,
};
