import { withFormik } from 'formik';
import { compose } from 'recompose';
import { connect } from 'react-redux';
import InvoicePayment from './InvoicePayment';
import { invoiceAndPaymentYupValidations } from '../../../../components/ProjectForms/InvoiceAndPaymentForm';
import {
  patchProject,
  selectInvoiceAndPayment,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  return {
    initialValues: selectInvoiceAndPayment(state),
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  payload.workdayTaskId = values.workdayTaskId;

  payload.rateType = values.rateType;
  payload.businessUnit = values.businessUnit;

  payload.productivityData = !!values.hasProductivityData;

  if (values.hasNotesToFinance) {
    payload.financeNotes = values.notesToFinance;
  }

  const nextStep = STEP_NUMBER.INVOICE_PAYMENT + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    enableReinitialize: true,
    validateOnMount: true,
    validationSchema: invoiceAndPaymentYupValidations,
  }),
)(InvoicePayment);
