import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const KEY = 'userScreeningDetails';
const UNDO_TYPE = `${KEY}/undo`;

const slice = createSlice({
  name: KEY,

  initialState: {
    data: null,
    loading: {},
    fetchError: null,
    saving: false,
    saveError: null,
  },

  reducers: {
    fetchUserScreeningDetails: (state) => {
      state.fetchError = null;
    },

    fetchUserScreeningDetailsSuccess: (state, action) => {
      state.fetchError = null;
      state.data = action.payload;
    },

    fetchUserScreeningDetailsError: (state, action) => {
      state.fetchError = action.payload;
    },

    updateCurrentUserState: (state, action) => {
      const { duplicateUserId } = action.payload;

      const potencialDuplicates = get(
        state.data,
        ['details', 'duplicates', 'potential_duplicates'],
        null,
      );

      if (potencialDuplicates.length > 0) {
        const duplicates = potencialDuplicates.map((duplicate) => {
          const currentUserId = get(duplicate, ['attributes', 'user_id'], null);
          if (currentUserId === duplicateUserId) {
            duplicate.attributes.verified = true;
          }
          return duplicate;
        });

        state.data.details.duplicates.potential_duplicates = duplicates;
      }
    },

    setLoading(state, { payload }) {
      const { userId, status } = payload;
      if (userId) state.loading = { ...state.loading, [userId]: status };
    },
  },
});

const {
  actions: {
    fetchUserScreeningDetails,
    fetchUserScreeningDetailsSuccess,
    fetchUserScreeningDetailsError,
    updateCurrentUserState,
    setLoading,
  },
  reducer,
} = slice;

reducer.key = KEY;

export default reducer;

export {
  fetchUserScreeningDetails,
  fetchUserScreeningDetailsSuccess,
  fetchUserScreeningDetailsError,
  updateCurrentUserState,
  setLoading,
};

export function selectUserScreeningDetailsSlice(state) {
  return state[reducer.key];
}

export function selectUserScreeningDetails(state) {
  return get(selectUserScreeningDetailsSlice(state), 'data', null);
}

export function selectLoading(state, user) {
  const { userId } = user;
  if (!userId) return false;
  const loading = get(selectUserScreeningDetailsSlice(state), 'loading', false);
  return get(loading, userId, false);
}

export function selectFetchError(state) {
  return get(selectUserScreeningDetailsSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectUserScreeningDetailsSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectUserScreeningDetailsSlice(state), 'saveError', null);
}

export function selectUserScreeningDetailsById(state, id) {
  const userScreeningDetailss = selectUserScreeningDetails(state);
  return userScreeningDetailss.find((config) => config.id === id);
}

export function selectIpCheck(state) {
  return get(selectUserScreeningDetails(state), ['details', 'ipCheck'], null);
}

export function selectMaliciousness(state) {
  return get(selectUserScreeningDetails(state), ['details', 'malicious'], null);
}

export function selectDuplicates(state) {
  return get(
    selectUserScreeningDetails(state),
    ['details', 'duplicates'],
    null,
  );
}

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}
