import * as yup from 'yup';
import store from '../../../store';
import createRemoteFieldValidation from '../../../pages/ProjectSetup/helpers/remoteFieldValidation';
import ENDPOINTS from '../../../constants/endpoints';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

function getProjectNameUrl(value) {
  return ENDPOINTS.V2.PROJECTS({
    query: {
      name: value,
    },
  });
}

function getProjectAliasUrl(value) {
  return ENDPOINTS.V2.PROJECTS({
    query: {
      alias: value,
    },
  });
}

function createValidateData(selectProject) {
  return (data) => {
    const project = selectProject(store.getState());

    if (project && project.id && data.length > 0 && project.id === data[0].id) {
      return true;
    }

    // if there is no projects for the given query, it's valid!
    return Array.isArray(data) && !data.length;
  };
}

export default (selectProject) => {
  const validateData = createValidateData(selectProject);

  return {
    projectName: yup
      .string()
      .test(
        'projectName',
        'Project Name already in use.',
        createRemoteFieldValidation(getProjectNameUrl, validateData),
      )
      .required(requiredMessage`Project Name`),
    projectAlias: yup
      .string()
      .notOneOf(
        [yup.ref('projectName'), null],
        'Project Alias must be different from Project Name.',
      )
      .test(
        'projectAlias',
        'Project Alias already in use.',
        createRemoteFieldValidation(getProjectAliasUrl, validateData),
      )
      .required(requiredMessage`Project Alias`),
    projectDescription: yup
      .string()
      .required(requiredMessage`Project Description`),
    projectType: yup.string().required(requiredMessage`Project Type`),
    taskType: yup.string().required(requiredMessage`Task Type`),
    taskVolume: yup.string().required(requiredMessage`Task Volume`),
  };
};
