import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import { withTheme } from 'styled-components';
import {
  patchProject,
  selectUserProjectPage,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import UserProjectPage from './UserProjectPage';
import { projectUserProjectPageValidations } from '../../../../components/ProjectForms/ProjectUserProjectPageForm';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  return {
    initialValues: selectUserProjectPage(state),
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  payload.defaultPageId =
    values.defaultPageTask === 'DEFAULT' ? null : values.defaultPageTask;

  payload.skillsReview = !!values.hasSkillReview;
  payload.acanProject = !!values.isACANProject;

  const nextStep = STEP_NUMBER.USER_PROJECT_PAGE + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    validationSchema: projectUserProjectPageValidations,
  }),
  withTheme,
)(UserProjectPage);
