import React from 'react';
import PropTypes from 'prop-types';
import { useTheme } from 'styled-components';
import { getLabel } from './helpers';
import Button from '../../../../../../components/Button';
import QuestionSetupModal from '../../../QuestionSetupModal';
import {
  CardInfo,
  CardLabel,
  CardTitle,
  CardDescription,
  ChevronWrapper,
  ButtonContainer,
  ButtonContent,
  ButtonIcon,
} from '../../../CardViewMode/styles';
import { LabelWrapper, Answer, Notes } from './styles';
import { Label } from '../../../../../../styles';
import { ReactComponent as Chevron } from '../../../../../../assets/icons/double-chevron-right.svg';
import pencil from '../../../../../../assets/icons/pencil-blue.svg';
import close from '../../../../../../assets/icons/close-blue.svg';

export default function TopicsCell(props) {
  const {
    modalProps,
    modalControls,
    openModal,
    masterProfileQuestionData: {
      name,
      question,
      answerList = [],
      questionType,
      parentQuestionId,
    },
    row: { isExpanded },
  } = props;

  const theme = useTheme();

  const renderAnswers = (list) => {
    return list.map(({ id, answer }, index) => {
      const lastIndex = index === list.length - 1;
      return (
        <Answer paddingBottom={lastIndex ? 16 : 8} key={id}>
          {answer}
        </Answer>
      );
    });
  };

  const renderLabel = (label, Icon, margin) => {
    const style = {
      verticalAlign: 'middle',
      margin: margin || '-5px 7px 0 0',
    };
    return (
      <Label>
        <Icon style={style} />
        {label}
      </Label>
    );
  };

  const [label, Icon, margin] = getLabel(questionType);

  return (
    <>
      <CardInfo paddingBottom={0}>
        {!!parentQuestionId && (
          <>
            <ChevronWrapper>
              <Chevron />
            </ChevronWrapper>
            <CardLabel>IF: SELECTED</CardLabel>
          </>
        )}
        <CardTitle bold>{name}</CardTitle>
        <CardDescription color={theme.colors.charcoal}>
          {question}
        </CardDescription>
      </CardInfo>
      {isExpanded && (
        <>
          <LabelWrapper>{renderLabel(label, Icon, margin)}</LabelWrapper>
          {renderAnswers(answerList)}
          <Notes>Exclude countries: None</Notes>
          <ButtonContainer>
            <Button
              type="secondary"
              disabled
              onClick={() => {}}
              marginRight={16}
            >
              <ButtonContent>
                <ButtonIcon src={close} alt={'delete icon'} padding={2} />
                Delete
              </ButtonContent>
            </Button>
            <Button type="secondary" onClick={openModal}>
              <ButtonContent>
                <ButtonIcon src={pencil} alt={'edit icon'} /> Edit
              </ButtonContent>
            </Button>
          </ButtonContainer>
        </>
      )}
      <QuestionSetupModal
        modalProps={modalProps}
        modalControls={modalControls}
      />
    </>
  );
}

TopicsCell.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  openModal: PropTypes.func.isRequired,
  masterProfileQuestionData: PropTypes.shape({
    name: PropTypes.string,
    question: PropTypes.string,
    answerList: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number,
        answer: PropTypes.string,
      }),
    ),
    questionType: PropTypes.string,
    parentQuestionId: PropTypes.number,
  }).isRequired,
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
  }),
};

TopicsCell.defaultProps = {
  row: [],
};
