import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import Tooltip from 'rc-tooltip';
import linewrap from 'linewrap';
import Styles, { TooltipChildren } from './styles';
// import information from '../../../assets/icons/information.svg';

export default function InformationTooltip(props) {
  const { trigger, placement, title, lineLength, children } = props;

  const wrapLines = useMemo(
    () => linewrap(lineLength, { lineBreakScheme: 'html' }),
    [lineLength],
  );

  return (
    <>
      <Styles />
      <Tooltip
        placement={placement}
        trigger={Array.isArray(trigger) ? trigger : [trigger]}
        overlay={
          typeof title === 'string' ? (
            // eslint-disable-next-line react/no-danger
            <span dangerouslySetInnerHTML={{ __html: wrapLines(title) }} />
          ) : (
            title
          )
        }
        prefixCls="information_tooltip"
      >
        <TooltipChildren>
          {children || (
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M8.86834 0.730893C5.95849 -0.852521 2.3159 0.221965 0.731627 3.13115C-0.852639 6.04035 0.221182 9.68293 3.13026 11.2678C6.03935 12.8526 9.68246 11.7797 11.2681 8.87127C12.0301 7.47369 12.2053 5.83068 11.7552 4.30387C11.3051 2.77707 10.2666 1.49176 8.86834 0.730893ZM6.66667 8.66661C6.66667 9.0348 6.3682 9.33332 6.00001 9.33332C5.63182 9.33332 5.33335 9.0348 5.33335 8.66661V5.99995C5.33335 5.63177 5.63182 5.33324 6.00001 5.33324C6.3682 5.33324 6.66667 5.63177 6.66667 5.99995V8.66661ZM6.00001 4.00002C5.63182 4.00002 5.33335 3.70149 5.33335 3.3333C5.33335 2.96511 5.63182 2.66659 6.00001 2.66659C6.3682 2.66659 6.66667 2.96511 6.66667 3.3333C6.66667 3.70149 6.3682 4.00002 6.00001 4.00002Z" />
            </svg>
          )}
        </TooltipChildren>
      </Tooltip>
    </>
  );
}

const triggers = ['hover', 'focus', 'click'];

InformationTooltip.propTypes = {
  trigger: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.oneOf(triggers)),
    PropTypes.oneOf(triggers),
  ]),
  placement: PropTypes.oneOf([
    'top',
    'left',
    'right',
    'bottom',
    'topLeft',
    'topRight',
    'bottomLeft',
    'bottomRight',
    'leftTop',
    'leftBottom',
    'rightTop',
    'rightBottom',
  ]),
  title: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]),
  lineLength: PropTypes.number,
  children: PropTypes.element,
};

InformationTooltip.defaultProps = {
  title: '',
  placement: 'bottom',
  trigger: ['hover'],
  lineLength: 28,
  children: null,
};
