import { useDispatch, useSelector } from 'react-redux';
import { useCallback, useEffect, useMemo } from 'react';
import { useFormikContext } from 'formik';
import shortid from 'shortid';
import withHooks from 'hook-hoc';
import LinkedProjects from './LinkedProjects';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
  selectors,
} from '../../../../store/ducks/options';

const FIELD_NAME = 'linkedProjects';

function useHooksAsProps() {
  const { values, setFieldValue } = useFormikContext();

  const {
    [FIELD_NAME]: linkedProjects = [
      { key: shortid.generate(), projectId: null, permanent: false },
    ],
  } = values;

  const handleChange = useCallback(
    (value) => {
      setFieldValue('linkedProjects', value);
    },
    [setFieldValue],
  );

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      fetch({
        optionsFor: ENTITIES.projects,
        nestedKey: ENTITY_METADATA.projects.nestedKeys.ALL,
      }),
    );
  }, [dispatch]);

  const projects = useSelector(
    selectors.createSelectSliceFor(
      ENTITIES.projects,
      ENTITY_METADATA.projects.nestedKeys.ALL,
    ),
  );

  const handleAddLine = useCallback(() => {
    handleChange([
      ...(linkedProjects || []),
      {
        projectId: null,
        permanent: false,
        key: shortid.generate(),
      },
    ]);
  }, [handleChange, linkedProjects]);

  const handleRemove = useCallback(
    (index) => {
      handleChange([
        ...linkedProjects.slice(0, index),
        ...linkedProjects.slice(index + 1, linkedProjects.length),
      ]);
    },
    [handleChange, linkedProjects],
  );

  const handleProjectChange = useCallback(
    ({ value: newValue }, index) => {
      const item = linkedProjects[index];

      handleChange([
        ...linkedProjects.slice(0, index),
        { ...item, projectId: newValue, permanent: false },
        ...linkedProjects.slice(index + 1, linkedProjects.length),
      ]);
    },
    [handleChange, linkedProjects],
  );

  const selectedProjects = useMemo(
    () => linkedProjects.map((br) => br.projectId),
    [linkedProjects],
  );

  const projectOptions = useMemo(
    () => mapToOptions('id', 'name')(projects.data),
    [projects.data],
  );

  const filteredProjectOptions = projectOptions.filter(
    (p) => !selectedProjects.includes(p.value),
  );

  return {
    linkedProjects,
    handleProjectChange,
    handleRemove,
    handleAddLine,
    projectOptions,
    filteredProjectOptions,
    projects,
  };
}

export default withHooks(useHooksAsProps)(LinkedProjects);
