import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import NumberFormat from 'react-number-format';
import Input from '../../../Input';
import { Label, Section } from '../../../../styles';
import RichTextEditor, {
  useEditorChangeHandler,
} from '../../../RichTextEditor';
import { EmailDefaultText, RadioContainer } from './styles';
import ActionFooter from '../../../ActionFooter';
import {
  StepContentContainer,
  StepActionContainer,
  CollapsedViewContainer,
} from '../styles';
import InformationTooltip, { LabelWrapper } from '../../InformationTooltip';
import LabeledSwitch from '../../../LabeledSwitch';
import Radio from '../../../Radio/Radio';

const EmailHeader = (
  <EmailDefaultText>
    Dear (Users full name),
    <br />
    Regarding project (Project Name)
    <br />
    {"Welcome to the Project (The project's alias) qualification exam!"}
  </EmailDefaultText>
);

const EmailFooter = (
  <EmailDefaultText>
    Best wishes for your success,
    <br />
    The Appen team.
  </EmailDefaultText>
);

const needsExamBatchOptions = [
  { label: 'Start Exam Immediately', value: false },
  { label: 'Load the User into Exam Batch', value: true },
];

export default function SendEmailStep(props) {
  const {
    handleChange,
    values,
    errors,
    submitCount,
    collapsed,
    submitForm,
    cancelEditing,
    card: { attributes },
  } = props;

  const handleEditorChange = useEditorChangeHandler('emailContent');

  if (collapsed && attributes && attributes.length) {
    return (
      <CollapsedViewContainer>
        {values.emailTitle || ''}
        {values.needsExamBatch ? ' (Load into Batch)' : ' (Start Immediately)'}
      </CollapsedViewContainer>
    );
  }

  return (
    <>
      <StepContentContainer>
        <Section>
          <Label htmlFor="emailTitle">Email Title</Label>
          <Input
            name="emailTitle"
            onChange={handleChange}
            id="emailTitle"
            placeholder="Enter email title"
            value={values.emailTitle}
            error={submitCount >= 1 && errors.emailTitle}
          />
        </Section>
        <Section>
          <Label htmlFor="emailContent">Email Content</Label>
          <RichTextEditor
            name="emailContent"
            onChange={handleEditorChange}
            id="emailContent"
            placeholder="Write email here"
            value={values.emailContent}
            error={submitCount >= 1 && errors.emailContent}
            accessoryHeader={EmailHeader}
            accessoryFooter={EmailFooter}
          />
        </Section>
        <Section>
          <RadioContainer>
            <Radio
              onChange={handleChange}
              value={values.needsExamBatch}
              name="needsExamBatch"
              options={needsExamBatchOptions}
              error={submitCount >= 1 && errors.needsExamBatch}
            />
          </RadioContainer>
        </Section>
        <Section>
          <Row>
            <Col col={4}>
              <LabelWrapper>
                <Label htmlFor="maximumAttemps">Maximum attempts</Label>
                <InformationTooltip
                  lineLength={42}
                  title="The max number of times an applicant may attempt to take the specified external exam."
                />
              </LabelWrapper>
              <NumberFormat
                customInput={Input}
                allowNegative={false}
                name="maximumAttemps"
                onChange={handleChange}
                id="maximumAttemps"
                placeholder={values.unlimited ? '' : 'Enter number'}
                value={values.unlimited ? '' : values.maximumAttemps}
                disabled={values.unlimited}
                error={submitCount >= 1 && errors.maximumAttemps}
                errorIncreasesHeight
              />
              <Section marginTop={12} marginBottom={0}>
                <LabeledSwitch
                  name="unlimited"
                  onChange={handleChange}
                  value={values.unlimited}
                  label="UNLIMITED"
                />
              </Section>
            </Col>
          </Row>
        </Section>
      </StepContentContainer>
      <StepActionContainer>
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !attributes.length ? 'Add & Close' : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

SendEmailStep.propTypes = {
  handleChange: PropTypes.func.isRequired,
  values: PropTypes.shape({
    emailTitle: PropTypes.string,
    emailContent: PropTypes.any,
    unlimited: PropTypes.bool,
    maximumAttemps: PropTypes.string,
    needsExamBatch: PropTypes.bool,
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        attributeOrder: PropTypes.number,
      }),
    ),
  }).isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
