import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { v4 as uuid } from 'uuid';
import ExpandedCell from './ExpandedCell';
import ConfirmationModal from './ConfirmationModal';
import { useModal } from '../../../../components/Modal';
import {
  ActionButton,
  ActionContainer,
  Container,
  Info,
  Resume,
  Title,
} from './styles';

export default function NameCell({ row }) {
  const {
    isExpanded,
    original: { name = '' },
  } = row;

  const [modalProps, modalControls] = useModal();

  const handleOpenModal = useCallback(
    (operation) => {
      modalControls.openModal({ key: uuid(), operation });
    },
    [modalControls],
  );

  return (
    <Container>
      <Title isExpanded={isExpanded}>{name}</Title>
      <ExpandedCell isExpanded={isExpanded} label="Profile info">
        <Info>Email: alina.roberts@appen.com</Info>
        <Info>Mobile phone: +1 585 475-5263</Info>
        <Info>Address: 169 Lomb Memorial Drive, Rochester, 14623</Info>
        <Info>Country: Canada</Info>
        <Resume>View Resume</Resume>
        <ActionContainer>
          <ActionButton onClick={() => handleOpenModal('stage')}>
            Stage
          </ActionButton>
          <ActionButton onClick={() => handleOpenModal('reject')}>
            Reject
          </ActionButton>
          <ActionButton onClick={() => handleOpenModal('complete')}>
            Complete
          </ActionButton>
        </ActionContainer>
      </ExpandedCell>
      <ConfirmationModal
        modalProps={modalProps}
        modalControls={modalControls}
      />
    </Container>
  );
}

NameCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({ name: PropTypes.string }).isRequired,
  }).isRequired,
};
