import { call, put, takeLeading, select } from 'redux-saga/effects';
import { createSlice, createEntityAdapter } from '@reduxjs/toolkit';
import axios from '../../services/axios';

/* eslint-disable no-param-reassign */
// Redux toolkit user immer internally to check for param reasignement
// and do a non-mutating update to the redux store!
export default function createEntityStateProvider(
  name,
  getUrl,
  identifier,
  transform,
  lazy = false,
) {
  const adapter = createEntityAdapter({
    selectId: (entity) => {
      if (typeof type === 'function') {
        return identifier(entity);
      }

      return entity[identifier || 'value'];
    },
  });

  const initialState = adapter.getInitialState({
    loading: true,
    error: null,
  });

  const selectors = {
    ...adapter.getSelectors((state) => state[name]),
    selectLoading: (state) => state[name].loading,
    selectError: (state) => state[name].error,
  };

  const slice = createSlice({
    name,
    initialState,
    reducers: {
      fetchAll(state) {
        if (lazy && state.ids.length) {
          return;
        }

        state.loading = true;
      },

      fetchFailed(state, action) {
        state.error = action.payload;
        state.loading = false;
      },

      setAll(state, action) {
        if (typeof transform === 'function') {
          adapter.setAll(state, transform(action.payload));
        } else {
          adapter.setAll(state, action.payload);
        }

        state.error = null;
        state.loading = false;
      },

      addOne: adapter.addOne,
      addMany: adapter.addMany,
      removeOne: adapter.removeOne,
      removeMany: adapter.removeMany,
      removeAll: adapter.removeAll,
      updateOne: adapter.updateOne,
      updateMany: adapter.updateMany,
      upsertOne: adapter.upsertOne,
      upsertMany: adapter.upsertMany,
    },
  });

  const {
    actions: { fetchFailed, fetchAll, setAll },
  } = slice;

  function* saga(action) {
    const state = yield select();

    if (lazy && selectors.selectIds(state).length) {
      return;
    }

    try {
      const url = getUrl(state, action);

      const response = yield call(axios, url);

      yield put(setAll(response.data));
    } catch (err) {
      yield put(fetchFailed(err));
    }
  }

  slice.reducer.key = name;

  const sagaCreator = takeLeading(fetchAll.type, saga);

  sagaCreator.fn = saga;

  return [slice, selectors, sagaCreator];
}
