import React, { useCallback, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';

import AnswerSubmitted from './AnswerSubmitted';

import ActionFooter from '../../components/ActionFooter';
import Loader from '../../components/Loader';
import ErrorPopover, { useErrorPopover } from '../../components/ErrorPopover';
import RichDescription from '../../components/RichDescription';

import useOnScreen from '../../helpers/visibility-sensor';

import {
  fetchConsentForm,
  selectConsentForm,
  selectDocumentVersion,
  selectError,
  selectIsAnswered,
  selectIsBlocked,
  selectLoading,
  setIsBlocked,
  submitUserResponse,
} from '../../store/ducks/externalConsentForm';

import {
  FloatingFooter,
  ConsentFormContainer,
  AccessoryLeft,
  Content,
  Logo,
} from './styles';

import appen from '../../assets/images/small-logo.svg';

import './styles.css';

export default function ConsentForm() {
  const dispatch = useDispatch();
  const match = useRouteMatch();
  const ref = useRef();
  const [showError, , errorPopoverProps] = useErrorPopover();

  const consentForm = useSelector(selectConsentForm);
  const documentVersion = useSelector(selectDocumentVersion);
  const loading = useSelector(selectLoading);
  const currentError = useSelector(selectError);
  const isBlocked = useSelector(selectIsBlocked);
  const isAnswered = useSelector(selectIsAnswered);

  const isVisible = useOnScreen(ref);

  const { token } = match.params;

  useEffect(() => {
    if (currentError) {
      showError(currentError);
    }
  }, [currentError, showError]);

  useEffect(() => {
    if (token) {
      dispatch(fetchConsentForm(token));
    }
  }, [dispatch, token]);

  const handleDenyButton = useCallback(() => {
    dispatch(
      submitUserResponse({
        status: 'DENIED',
        consentFormToken: token,
      }),
    );
  }, [dispatch, token]);

  const handleAcknowledgeButton = useCallback(() => {
    dispatch(
      submitUserResponse({
        status: 'ACCEPTED',
        consentFormToken: token,
      }),
    );
  }, [dispatch, token]);

  const handleVisibility = useCallback(() => {
    dispatch(setIsBlocked(false));
  }, [dispatch]);

  const buttons = [
    {
      text: 'Deny',
      type: 'secondary',
      disabled: loading || isBlocked || isAnswered,
      onClick: handleDenyButton,
    },
    {
      text: 'Accept',
      type: 'primary',
      disabled: loading || isBlocked || isAnswered,
      marginRight: '30px',
      onClick: handleAcknowledgeButton,
    },
  ];

  return (
    <ConsentFormContainer>
      <Logo src={appen} alt="Appen illustration" />
      {isAnswered ? (
        <AnswerSubmitted />
      ) : (
        <>
          <Content>
            {!currentError && loading && <Loader text="Loading..." />}
            <RichDescription html={consentForm} sanitize={false} />
            <div ref={ref}>
              {!currentError &&
                !loading &&
                isBlocked &&
                isVisible &&
                handleVisibility()}
            </div>
          </Content>
          <FloatingFooter>
            <ActionFooter
              rightButtons={buttons}
              accessoryLeft={
                <AccessoryLeft>
                  Confidential Appen Information Document version:{' '}
                  {documentVersion}
                </AccessoryLeft>
              }
            />
          </FloatingFooter>
          <ErrorPopover {...errorPopoverProps} />
        </>
      )}
    </ConsentFormContainer>
  );
}
