import get from 'lodash/get';
import jsonToFormData from '../../../../helpers/json-to-formdata';
import endpoints, { safeUrl } from '../../../../constants/endpoints';

function toEntriesArray(formData) {
  return Array.from(formData.entries());
}

export function getPostPayload(processModule, processId) {
  if (processModule.sequenceFlow === 'sendEmailFlow') {
    return toEntriesArray(
      jsonToFormData({
        title: get(
          (processModule.attributes || []).find(
            (attr) => attr.attributeOrder === 1,
          ),
          'value',
          '',
        ),
        content: get(
          (processModule.attributes || []).find(
            (attr) => attr.attributeOrder === 2,
          ),
          'value',
          '',
        ),
        processId,
      }),
    );
  }

  if (processModule.sequenceFlow === 'signDocumentFlow') {
    const hasAdditionalAttributes = !!get(
      processModule,
      'attributes[0].additionalAttributes',
      false,
    );

    if (hasAdditionalAttributes) {
      return toEntriesArray(
        jsonToFormData({
          title: get(
            processModule,
            'attributes[0].additionalAttributes.documentTitle',
            '',
          ),
          content: get(
            processModule,
            'attributes[0].additionalAttributes.documentBody',
            '',
          ),
        }),
      );
    }
  }

  if (processModule.sequenceFlow === 'takeExternalExamFlow') {
    return toEntriesArray(
      jsonToFormData({
        title: get(
          (processModule.attributes || []).find(
            (attr) => attr.attributeOrder === 1,
          ),
          'value',
          '',
        ),
        content: get(
          (processModule.attributes || []).find(
            (attr) => attr.attributeOrder === 2,
          ),
          'value',
          '',
        ),
        processId,
      }),
    );
  }

  return [];
}

const METHODS = {
  GET: 'GET',
  POST: 'POST',
};

function getMethodAndActionPath(processModule, projectId) {
  if (processModule.sequenceFlow === 'signDocumentFlow') {
    const hasAdditionalAttributes = !!get(
      processModule,
      'attributes[0].additionalAttributes',
      false,
    );

    if (hasAdditionalAttributes) {
      return [METHODS.POST, endpoints.CORE.PROCESS_PREVIEW.SIGN_DOCUMENT()];
    }

    return [
      METHODS.GET,
      endpoints.CORE.PROCESS_PREVIEW.SIGN_DOCUMENT({
        query: {
          documentId: get(processModule, 'attributes[0].value', undefined),
        },
      }),
    ];
  }

  // TODO: There are some missing steps.
  switch (processModule.sequenceFlow) {
    case 'completeProjectAttributesFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.ADDITIONAL_ATTRIBUTES({
          query: {
            projectId,
          },
        }),
      ];

    case 'sendEmailFlow':
      return [METHODS.POST, endpoints.CORE.PROCESS_PREVIEW.SEND_EMAIL()];

    case 'manualWorkFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.PERFORM_MANUAL_WORK({
          query: {
            projectId,
          },
        }),
      ];

    case 'takeInternalQuizFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.QUIZ({
          query: {
            quizId: get(
              (processModule.attributes || []).find(
                (attr) => attr.attributeOrder === 1,
              ),
              'value',
              undefined,
            ),
            subsetSize: get(
              (processModule.attributes || []).find(
                (attr) => attr.attributeOrder === 4,
              ),
              'value',
              undefined,
            ),
          },
        }),
      ];

    case 'takeInternalLocaleBasedQuizFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.QUIZ({
          query: {
            quizId: get(
              (processModule.attributes || []).find(
                (attr) => attr.name === 'quizId',
              ),
              'value',
              undefined,
            ),
            subsetSize: get(
              (processModule.attributes || []).find(
                (attr) => attr.name === 'subsetSize',
              ),
              'value',
              undefined,
            ),
          },
        }),
      ];
    case 'takeLanguageCertificationQuizFlow':
    case 'takeTranslationQuizFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.QUIZ({
          query: {
            quizId: get(
              (processModule.attributes || []).find(
                (attr) => attr.name === 'quizId',
              ),
              'value',
              undefined,
            ),
          },
        }),
      ];

    case 'takeExternalExamFlow':
      return [METHODS.POST, endpoints.CORE.PROCESS_PREVIEW.EXTERNAL_EXAM()];

    case 'completeAcademyCourseFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.ACADEMY_COURSE({
          query: {
            academyId: get(processModule, 'attributes[0].value', false),
          },
        }),
      ];

    case 'downloadSecureBrowserFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.DOWNLOAD_SECURE_BROWSER(),
      ];

    case 'faceRegisterFlow':
      return [METHODS.GET, endpoints.CORE.PROCESS_PREVIEW.FACE_REGISTER()];

    case 'dataCollectionPiiConsentFormFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.DATA_COLLECTION({
          query: {
            projectId,
          },
        }),
      ];

    case 'takeSurveyFlow':
      return [
        METHODS.GET,
        endpoints.CORE.PROCESS_PREVIEW.QUIZ({
          query: {
            quizId: get(
              (processModule.attributes || []).find(
                (attr) => attr.attributeOrder === 1,
              ),
              'value',
              undefined,
            ),
          },
        }),
      ];

    default:
      break;
  }

  return [METHODS.GET, '/'];
}

export function getMethodAndActionURL(processModule, projectId) {
  const BACKEND_URL = process.env.REACT_APP_QRP_URL;

  const [method, path] = getMethodAndActionPath(processModule, projectId);

  return [method, safeUrl(`${BACKEND_URL}${path}`)];
}
