import React, { useEffect, useMemo, useCallback } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';

import InformationTooltip from '../../../../ProcessBuilder/InformationTooltip';
import Select from '../../../../Select';
import Input from '../../../../Input';
import Checkbox from '../../../../Checkbox';
import { CheckboxDescription, FormSection, InnerSection } from './styles';
import { Section, Label, Li } from '../../../../../styles';
import {
  selectAll as selectAllHiringTargets,
  selectById as selectHiringtargetById,
} from '../../../../../store/ducks/hiringTargets';
import { selectAllEager as selectAllPayRates } from '../../../../../store/ducks/payrates';
import { findById } from '../../../../../helpers/find';
import mapToOptions, {
  mapOptionCapitalizeLabels,
} from '../../../../../pages/ProjectSetup/helpers/mapToOptions';
import { ENTITIES, fetch, selectors } from '../../../../../store/ducks/options';
import useFetchUserGroups from '../../../../../store/ducks/options/helpers/fetchUserGroups';

function createSelectHiringTarget(id) {
  return (state) => selectHiringtargetById(state, id) || {};
}

const toCurrency = (number) => {
  if (!number) {
    return '';
  }

  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });
  return formatter.format(number);
};

export default function CustomPayrateForm(props) {
  const {
    values,
    handleChange,
    setFieldValue,
    errors,
    hiringTarget,
    rateType,
    handleBlur,
    submitCount,
  } = props;

  const dispatch = useDispatch();

  useFetchUserGroups();

  // Fetch data for DropDowns
  useEffect(() => {
    setTimeout(() => {
      // give React (generous) 8 ticks to update the UI before firing sagas
      // to prevent UI lock
      dispatch(fetch(ENTITIES.taskTypes));
      dispatch(fetch(ENTITIES.fluencies));
    }, 128);
  }, [dispatch]);

  const userGroups = useSelector(
    selectors.createSelectSliceFor(ENTITIES.userGroups),
  );

  const taskTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.taskTypes),
  );

  const fluencies = useSelector(
    selectors.createSelectSliceFor(ENTITIES.fluencies),
  );

  // Make sure "ALL USERS" group is always present:
  const unfilteredUserGroupsOptions = mapToOptions(
    'id',
    'name',
  )(userGroups.data);

  const allUsersUserGroup = useMemo(
    () =>
      unfilteredUserGroupsOptions.find((userGroup) =>
        `${userGroup.label}`.match(/all users/i),
      ) || {},
    [unfilteredUserGroupsOptions],
  );

  const selectHiringTarget = useMemo(
    () => createSelectHiringTarget(hiringTarget),
    [hiringTarget],
  );

  const hiringTargets = useSelector(selectAllHiringTargets);
  const selectedHiringTarget = useSelector(selectHiringTarget);
  const payrates = useSelector(selectAllPayRates);

  const payratesWithCountries = useMemo(
    () =>
      payrates.map((pr) => {
        const htForCurrentPayrate =
          findById(hiringTargets, pr.hiringTarget) || {};

        return {
          ...pr,
          country: htForCurrentPayrate.country,
        };
      }),
    [hiringTargets, payrates],
  );

  const userGroupsOptions = useMemo(() => {
    const payratesForCurrentHiringTargetsCountry = payratesWithCountries.filter(
      (pr) => pr.country === selectedHiringTarget.country,
    );

    if (payratesForCurrentHiringTargetsCountry.length) {
      const hasAllUsersUserGroup = !!payratesForCurrentHiringTargetsCountry.find(
        // !pr.userGroups means its a default payrate for given country.
        (pr) => pr.userGroup === allUsersUserGroup.value || !pr.userGroup,
      );

      if (hasAllUsersUserGroup) {
        return unfilteredUserGroupsOptions.map(mapOptionCapitalizeLabels);
      }

      return [allUsersUserGroup].map(mapOptionCapitalizeLabels);
    }

    return [allUsersUserGroup]
      .filter((option) => option.value)
      .map(mapOptionCapitalizeLabels);
  }, [
    allUsersUserGroup,
    payratesWithCountries,
    selectedHiringTarget.country,
    unfilteredUserGroupsOptions,
  ]);

  const taskTypesOptions = useMemo(() => {
    let options = taskTypes.data.map(mapOptionCapitalizeLabels);
    if (rateType === 'HOURLY') {
      options = options.filter((taskType) => taskType.value === 'DEFAULT');
    }
    return options;
  }, [taskTypes.data, rateType]);

  const handleWorkdayTaskIdChange = useCallback(
    ({ target: { value } }) => {
      const formattedValue = value ? value.toUpperCase() : '';

      if (
        (formattedValue.startsWith('CR') || formattedValue.startsWith('LR')) &&
        formattedValue.length > 7
      ) {
        return;
      }

      setFieldValue('workdayTaskId', formattedValue);
    },
    [setFieldValue],
  );

  return (
    <FormSection>
      <Section marginBottom="50px">
        <Row>
          <Col col="6">
            <Label htmlFor="spokenFluency">Spoken Fluency</Label>
            <Select
              value={values.spokenFluency}
              name="spokenFluency"
              onChange={({ value }) => setFieldValue('spokenFluency', value)}
              options={fluencies.data}
              loading={fluencies.loading}
              error={submitCount >= 1 && errors.spokenFluency}
              placeholder="Select fluency"
              data-testid="payrates-modal--select-spoken-fluency"
              errorIncreasesHeight
            />
          </Col>
          <Col col="6">
            <Label htmlFor="writtenFluency">Written Fluency</Label>
            <Select
              value={values.writtenFluency}
              name="writtenFluency"
              onChange={({ value }) => setFieldValue('writtenFluency', value)}
              options={fluencies.data}
              loading={fluencies.loading}
              error={submitCount >= 1 && errors.writtenFluency}
              placeholder="Select fluency"
              data-testid="payrates-modal--select-written-fluency"
              errorIncreasesHeight
            />
          </Col>
        </Row>
        <InnerSection marginTop="10px">
          <Row>
            <Col col="6">
              <Checkbox
                name="certifiedFluency"
                onChange={handleChange}
                checked={values.certifiedFluency}
                bold
                disabled={!!values.id}
                data-testid="payrates-modal--certified-fluency-toggle"
              >
                <CheckboxDescription isDisabled={!!values.id}>
                  Require certified fluency
                </CheckboxDescription>
              </Checkbox>
            </Col>
          </Row>
        </InnerSection>
      </Section>

      <Section>
        <Row>
          <Col col="6">
            <Label htmlFor="userGroup">User Group</Label>
            <Select
              value={values.userGroup}
              name="userGroup"
              onChange={({ value }) => setFieldValue('userGroup', value)}
              options={userGroupsOptions}
              loading={userGroups.loading}
              error={submitCount >= 1 && errors.userGroup}
              placeholder="Select group"
              data-testid="payrates-modal--select-user-group"
              errorIncreasesHeight
            />
          </Col>
          <Col col="6">
            <Label htmlFor="taskType">Task Type</Label>
            <Select
              value={values.taskType}
              name="taskType"
              onChange={({ value }) => setFieldValue('taskType', value)}
              options={taskTypesOptions}
              loading={taskTypes.loading}
              error={submitCount >= 1 && errors.taskType}
              placeholder="Select type"
              disabled={!!values.id && !values.customPayrate}
              data-testid="payrates-modal--select-task-type"
              errorIncreasesHeight
            />
          </Col>
        </Row>
        <InnerSection marginTop="20px">
          <Row>
            <Col col="6">
              <Label htmlFor="workdayTaskId">
                WORKDAY TASK ID (OPTIONAL)
                <InformationTooltip
                  title={
                    <ul>
                      Format Options: <br />
                      <Li>P##### (5 digits)</Li>
                      <Li>CR##### (5 digits) </Li>
                      <Li>LR##### (5 digits) </Li>
                      <Li>
                        PROJECT_PLAN_TASK-#-### (last digits variable length)
                      </Li>
                    </ul>
                  }
                />
              </Label>
              <Input
                value={values.workdayTaskId}
                name="workdayTaskId"
                onChange={handleWorkdayTaskIdChange}
                id="workdayTaskId"
                placeholder="Enter Task Code"
                onBlur={handleBlur}
                error={submitCount >= 1 && errors.workdayTaskId}
                data-testid="payrates-modal--workday-task-id-input"
                errorIncreasesHeight
              />
            </Col>
            <Col col="4">
              <Label htmlFor="rate">Rate</Label>
              <Input
                value={toCurrency(values.rate)}
                name="rate"
                onChange={({ target: { value } }) => {
                  setFieldValue(
                    'rate',
                    parseFloat(value.replace(/\D/g, '')) / 100,
                  );
                }}
                id="rate"
                placeholder="$ 0,00"
                error={submitCount >= 1 && errors.rate}
                disabled={!values.customPayrate}
                data-testid="payrates-modal--rate-input"
                errorIncreasesHeight
              />
            </Col>
          </Row>
        </InnerSection>
      </Section>
    </FormSection>
  );
}

CustomPayrateForm.propTypes = {
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  hiringTarget: PropTypes.number,
  rateType: PropTypes.string.isRequired,
};

CustomPayrateForm.defaultProps = {
  hiringTarget: -1,
};
