/* eslint-disable import/no-named-as-default */
import { spawn, all } from 'redux-saga/effects';

import { projectPagesSaga } from './projectPages';
import fetchRegistrationOptionsSaga from './registrationProcesses/fetchOptionsSaga';
import fetchRegistrationStepsSaga from './registrationProcesses/fetchStepsSaga';
import fetchQualificationOptionsSaga from './qualificationProcesses/fetchOptionsSaga';
import fetchQualificationStepsSaga from './qualificationProcesses/fetchStepsSaga';
import { teamTypesSaga } from './teamTypes';
import preventLeaveSaga from './pageLeave/preventLeaveSaga';
import { defaultPayratesSaga } from './defaultPayrates';
import { tenantsByCountrySaga } from './tenantsByCountry';

import fetchHiringTargetsSaga from './hiringTargets/fetchHiringTargetsSaga';
import createHiringTargetSaga from './hiringTargets/createHiringTargetSaga';
import updateHiringTargetSaga from './hiringTargets/updateHiringTargetSaga';
import removeHiringTargetSaga from './hiringTargets/removeHiringTargetSaga';

import fetchPayRatesSaga from './payrates/fetchPayRatesSaga';
import createPayRateSaga from './payrates/createPayRateSaga';
import createManyPayRatesSaga from './payrates/createManyPayRatesSaga';
import updatePayRateSaga from './payrates/updatePayRateSaga';
import updateManyPayRatesSaga from './payrates/updateManyPayRatesSaga';
import removePayRateSaga from './payrates/removePayRateSaga';

import fetchLocaleTenantsSaga from './localeTenants/fetchLocaleTenantsSaga';
import createLocaleTenantSaga from './localeTenants/createLocaleTenantSaga';
import updateLocaleTenantSaga from './localeTenants/updateLocaleTenantSaga';
import removeLocaleTenantSaga from './localeTenants/removeLocaleTenantSaga';

import { demographicRequirementsSaga } from './demographicRequirements';
import fetchDemographicsSaga from './projectDemographicRequirements/fetchDemographicsSaga';
import saveDemographicsSaga from './projectDemographicRequirements/saveDemographicsSaga';
import removeDemographicsSaga from './projectDemographicRequirements/removeDemographicsSaga';

import fetchDemographicsMetricsSaga from './projectDemographicMetrics/fetchDemographicsMetricsSaga';

import fetchStepDefinitionSaga from './processBuilder/fetchStepDefinitionSaga';
import fetchModulesSaga from './processBuilder/fetchModulesSaga';
import fetchProcessSaga from './processBuilder/fetchProcessSaga';
import saveOrCreateProcessSaga from './processBuilder/saveOrCreateProcessSaga';

import { saga as optionsSaga } from './options';
import { saga as securitySaga } from './security';
import { saga as paginatedSaga } from './paginated';
import { saga as editableEntitiesSaga } from './editableEntities';

import fetchProjectSaga from './project/fetchProjectSaga';
import patchProjectSaga from './project/patchProjectSaga';
import putProjectStatusSaga from './project/putProjectStatusSaga';
import postProjectSaga from './project/postProjectSaga';
import cloneProjectSaga from './project/cloneProjectSaga';

import setInvoiceConfiguration from './invoiceConfiguration/setInvoiceConfiguration';
import fetchInvoiceConfiguration from './invoiceConfiguration/fetchInvoiceConfiguration';
import resendPartnerDataSaga from './resendPartnerData/resendPartnerDataSaga';

import navigateSaga from './projectSetup/navigateSaga';

import fetchExperimentGroupsSaga from './experimentGroups/fetchExperimentGroupsSaga';
import fetchAccessHolderSaga from './experimentGroups/fetchAccessHolderSaga';
import saveExperimentGroupsSaga from './experimentGroups/saveExperimentGroupsSaga';
import saveAccessHolderSaga from './experimentGroups/saveAccessHolderSaga';
import toggleExperimentGroupSaga from './experimentGroups/toggleExperimentGroupSaga';

import fetchConsentFormSaga from './externalConsentForm/fetchConsentFormSaga';
import submitUserResponseSaga from './externalConsentForm/submitUserResponseSaga';

import fetchUserThirdPartyListSaga from './userThirdPartyList/fetchUserThirdPartyListSaga';
import saveUserThirdPartyListSaga from './userThirdPartyList/saveUserThirdPartyListSaga';
import resendEmailSaga from './userThirdPartyList/resendEmailSaga';
import saveDataConsentFormSaga from './dataConsentForm/saveDataConsentFormSaga';
import fetchDataConsentFormSaga from './dataConsentForm/fetchDataConsentFormSaga';
import deleteDataConsentFormSaga from './dataConsentForm/deleteDataConsentFormSaga';

import fetchTargetBuilderSaga from './targetBuilder/fetchTargetBuilderSaga';
import fetchTargetBuilderModulesSaga from './targetBuilder/fetchModulesSaga';
import fetchTargetBuilderQuestionsSaga from './targetBuilder/fetchQuestionsSaga';
import saveOrCreateTargetBuilderSaga from './targetBuilder/saveOrCreateTargetBuilderSaga';
import fetchQuestionsCategoriesSaga from './targetBuilder/fetchQuestionsCategoriesSaga';

import fetchMetricsAndGoalsSaga from './metricsAndGoals/fetchMetricsAndGoalsSaga';
import saveMetricsAndGoalsSaga from './metricsAndGoals/saveMetricsAndGoalsSaga';
import fetchMetricChangelogsSaga from './metricsAndGoals/fetchMetricChangelogsSaga';

import saveProfileQuestionsSaga from './profileQuestions/saveProfileQuestionsSaga';
import saveCategorySaga from './profileQuestions/saveCategorySaga';
import fetchTargetScreeningSaga from './targetScreening/fetchTargetScreeningSaga';
import fetchScreeningMetricsSaga from './screeningMetrics/fetchScreeningMetricsSaga';

import fetchAutoScreeningSaga from './autoScreening/fetchAutoScreeningSaga';
import saveAutoScreeningSaga from './autoScreening/saveAutoScreeningSaga';
import deleteAutoScreeningSaga from './autoScreening/deleteAutoScreeningSaga';

import fetchUserScreeningDetailsSaga from './userScreeningDetails/fetchUserScreeningDetailsSaga';
import saveFalsePositiveSaga from './userScreeningFalsePositives/saveFalsePositiveSaga';

export default function* rootSaga() {
  const sagas = [
    // Generic Data Fetching Sagas
    projectPagesSaga,
    teamTypesSaga,

    // Registration and Qualification
    fetchRegistrationOptionsSaga,
    fetchRegistrationStepsSaga,
    fetchQualificationOptionsSaga,
    fetchQualificationStepsSaga,

    // Scoped Data Fetching Sagas
    tenantsByCountrySaga,
    defaultPayratesSaga,

    // Project Setup Sagas
    preventLeaveSaga,

    // Locale Tenant Sagas for within Project Setup
    fetchLocaleTenantsSaga,
    createLocaleTenantSaga,
    updateLocaleTenantSaga,
    removeLocaleTenantSaga,

    // Hiring Target Sagas for within Project Setup
    fetchHiringTargetsSaga,
    createHiringTargetSaga,
    updateHiringTargetSaga,
    removeHiringTargetSaga,

    // Pay Rates Sagas for within Project Setup
    fetchPayRatesSaga,
    createPayRateSaga,
    createManyPayRatesSaga,
    updatePayRateSaga,
    updateManyPayRatesSaga,
    removePayRateSaga,

    // Demographic Requirements Sagas
    demographicRequirementsSaga,
    fetchDemographicsSaga,
    saveDemographicsSaga,
    removeDemographicsSaga,

    // Demographic Metrics Sagas
    fetchDemographicsMetricsSaga,

    // Process Builder Sagas
    fetchStepDefinitionSaga,
    fetchModulesSaga,
    fetchProcessSaga,
    saveOrCreateProcessSaga,

    // Project Sagas
    fetchProjectSaga,
    patchProjectSaga,
    putProjectStatusSaga,
    postProjectSaga,
    cloneProjectSaga,

    optionsSaga,
    paginatedSaga,
    securitySaga,
    editableEntitiesSaga,

    // Invoice Configuration
    setInvoiceConfiguration,
    fetchInvoiceConfiguration,

    // Resend Partner Data
    resendPartnerDataSaga,
    navigateSaga,

    // Experiment Groups
    fetchExperimentGroupsSaga,
    fetchAccessHolderSaga,
    saveExperimentGroupsSaga,
    saveAccessHolderSaga,
    toggleExperimentGroupSaga,

    // User Third Party Lists
    fetchUserThirdPartyListSaga,
    saveUserThirdPartyListSaga,
    resendEmailSaga,

    // Consent Form
    saveDataConsentFormSaga,
    fetchDataConsentFormSaga,
    deleteDataConsentFormSaga,

    // External Consent Form
    fetchConsentFormSaga,
    submitUserResponseSaga,

    // Target Builder
    fetchTargetBuilderSaga,
    fetchTargetBuilderModulesSaga,
    fetchTargetBuilderQuestionsSaga,
    saveOrCreateTargetBuilderSaga,
    fetchQuestionsCategoriesSaga,

    // Metrics & Goals
    fetchMetricsAndGoalsSaga,
    saveMetricsAndGoalsSaga,
    fetchMetricChangelogsSaga,

    // Profile Questions
    saveProfileQuestionsSaga,
    saveCategorySaga,

    // Target Screening
    fetchTargetScreeningSaga,
    fetchScreeningMetricsSaga,

    // Auto Screening
    fetchAutoScreeningSaga,
    saveAutoScreeningSaga,
    deleteAutoScreeningSaga,
    fetchUserScreeningDetailsSaga,
    saveFalsePositiveSaga,
  ];

  yield all(
    sagas.map((saga) => {
      let loops = 0;

      return spawn(function* genericSaga() {
        while (true) {
          if (loops > 10) {
            // Even in a big session, if a saga restarts more than 10 times,
            // we are probably in a eternal loop. Breaking out and killing app.

            if (process.env.NODE_ENV === 'development') {
              // eslint-disable-next-line no-console
              console.error(saga);
            }

            throw new Error('Eternal Saga loop detected.');
          }

          loops += 1;

          try {
            // instantiate our saga.
            yield saga;

            // if it completes successfuly (call() returns) we terminate the thread.
            break;
          } catch (error) {
            // if it errors (call() errors), re-instantiate the saga
            if (process.env.NODE_ENV === 'development') {
              // eslint-disable-next-line no-console
              console.log(error);
            }
            // TODO: How should we handle/report this errors?
          }
        }
      });
    }),
  );
}
