import { useState, useCallback } from 'react';
import shortid from 'shortid';
import PropTypes from 'prop-types';
import withHooks from 'hook-hoc';
import ErrorPopover from './ErrorPopover';

export function useErrorPopover() {
  const [errorShown, setErrorShown] = useState(false);
  const [error, setError] = useState(null);

  // Key is used to force component re-render.
  const [key, setKey] = useState(shortid.generate());

  const showError = useCallback((nextError) => {
    setError(nextError);
    setErrorShown(true);
    setKey(shortid.generate());
  }, []);

  const hideError = useCallback(() => {
    setError(false);
    setError(null);
    setKey(shortid.generate());
  }, []);

  const spreadableProps = {
    errorShown,
    hideError,
    error,
    key,
  };

  return [showError, hideError, spreadableProps];
}

export const withErrorPopover = withHooks(() => {
  const [showError, hideError, errorPopoverProps] = useErrorPopover();

  return {
    showError,
    hideError,
    errorPopoverProps,
  };
});

export const ErrorPopoverProps = PropTypes.shape({
  errorShown: PropTypes.bool.isRequired,
  hideError: PropTypes.func.isRequired,
  error: PropTypes.oneOfType([PropTypes.instanceOf(Error), PropTypes.bool]),
  key: PropTypes.string.isRequired,
});

export default ErrorPopover;
