import withHooks from 'hook-hoc';
import { useSelector } from 'react-redux';
import { selectSupportTeam } from '../../../../store/ducks/projectView';
import { findById, findByValue } from '../../../../helpers/find';
import * as teamTypes from '../../../../store/ducks/teamTypes';
import SupportTeam from './SupportTeam';
import { buildInternalUserName } from '../../../../components/ProjectForms/RecruitmentForm/HiringTargets/helpers';
import { ENTITIES, selectors } from '../../../../store/ducks/options';

function useHooksAsProps() {
  const { supportTeam } = useSelector(selectSupportTeam);
  const teamTypesData = useSelector(teamTypes.selectAll);

  const internalUsersData = useSelector(
    selectors.createSelectDataFor(ENTITIES.internalUsers),
  );

  const supportTeamData = (supportTeam || []).map((member) => {
    const { member: memberId } = member;
    const userData = findById(internalUsersData, memberId);
    return {
      id: memberId,
      role: (findByValue(teamTypesData, member.role) || {}).label || '-',
      member: buildInternalUserName(userData),
      email: userData ? userData.email : '-',
      phone: userData ? userData.phone : '-',
    };
  });

  return {
    supportTeam: supportTeamData,
  };
}

export default withHooks(useHooksAsProps)(SupportTeam);
