import { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import moment from 'moment';
import get from 'lodash/get';
import OverviewAndTools from './OverviewAndTools';
import { selectOverviewAndTools } from '../../../../store/ducks/projectView';
import { findById, findByValue } from '../../../../helpers/find';
import isSWFH from '../../../ProjectSetup/helpers/isSWFH';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import ENDPOINTS from '../../../../constants/endpoints';

import { ReactComponent as Express } from '../../../../assets/work-type-icons/express.svg';
import { ReactComponent as Linguistics } from '../../../../assets/work-type-icons/linguistics.svg';
import { ReactComponent as SearchEvaluation } from '../../../../assets/work-type-icons/search-evaluation.svg';
import { ReactComponent as SocialMedia } from '../../../../assets/work-type-icons/social-media.svg';
import { ReactComponent as Transcription } from '../../../../assets/work-type-icons/transcription.svg';
import { ReactComponent as Translation } from '../../../../assets/work-type-icons/translation.svg';
import { ReactComponent as WebResearch } from '../../../../assets/work-type-icons/web-research.svg';

function useHooksAsProps() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.externalSystems));
    dispatch(fetch(ENTITIES.workTypes));
    dispatch(fetch(ENTITIES.taskVolumes));
    dispatch(fetch(ENTITIES.customers));
    dispatch(fetch(ENTITIES.projectStatus));
  }, [dispatch]);

  const overviewAndTools = useSelector(selectOverviewAndTools);

  // Project Overview

  const workTypes = useSelector(
    selectors.createSelectDataFor(ENTITIES.workTypes),
  );

  const taskVolumes = useSelector(
    selectors.createSelectDataFor(ENTITIES.taskVolumes),
  );

  const status = useSelector(
    selectors.createSelectById(
      ENTITIES.projectStatus,
      null,
      overviewAndTools.status,
    ),
  );

  const taskType =
    (findByValue(workTypes, overviewAndTools.workType) || {}).label || '-';

  const taskVolume =
    (findByValue(taskVolumes, overviewAndTools.taskVolume) || {}).label || '-';

  const statusLabel = (status || {}).label || '-';

  // Project Tools
  const externalSystems = useSelector(
    selectors.createSelectDataFor(ENTITIES.externalSystems),
  );

  const externalSystem = overviewAndTools.externalSystemId
    ? (findById(externalSystems, overviewAndTools.externalSystemId) || {}).name
    : '-';

  const ipAddresses = Array.isArray(overviewAndTools.allowedIps)
    ? overviewAndTools.allowedIps.join(', ')
    : '';

  const isSWFHProject = externalSystem ? isSWFH(externalSystem) : false;

  const externalSystemUrl =
    process.env.REACT_APP_QRP_URL +
    ENDPOINTS.V2.PROJECT_EXTERNAL_URL({
      pathVariables: {
        id: get(overviewAndTools, 'projectId', null),
      },
    });

  // Cards
  const statusDescription = useMemo(() => {
    switch (overviewAndTools.status) {
      case 'DRAFT':
        return 'Finish project setup to start recruiting workers for this project.';

      case 'READY':
        return 'Your project is ready to be enabled. Click on Enable Project to start recruitment.';

      case 'ENABLED':
        return 'Your project is now running and recruitment is in progress.';

      case 'DISABLED':
      default:
        return 'Your project is currently disabled. Click on Enable Project to resume recruitment.';
    }
  }, [overviewAndTools.status]);

  const HighlightIcon = useMemo(() => {
    switch (overviewAndTools.workType) {
      case 'DATA_COLLECTION':
        return Express;

      case 'LINGUISTICS':
        return Linguistics;

      case 'SEARCH_EVALUATION':
        return SearchEvaluation;

      case 'SOCIAL_MEDIA':
        return SocialMedia;

      case 'TRANSCRIPTION':
        return Transcription;

      case 'TRANSLATION':
        return Translation;

      case 'WEB_RESEARCH':
      default:
        return WebResearch;
    }
  }, [overviewAndTools.workType]);

  const customers = useSelector(
    selectors.createSelectDataFor(ENTITIES.customers),
  );

  const customer = (findById(customers, overviewAndTools.customerId) || {})
    .name;

  const createdAt = useMemo(
    () =>
      overviewAndTools.createdAt
        ? moment(overviewAndTools.createdAt).format('MMMM DD[,] YYYY HH:mm')
        : 'Not set',
    [overviewAndTools.createdAt],
  );

  return {
    ...overviewAndTools,
    externalSystem,
    externalSystemUrl,
    taskType,
    taskVolume,
    ipAddresses,
    isSWFHProject,
    status: statusLabel,
    statusDescription,
    HighlightIcon,
    customer,
    createdAt,
  };
}

export default withHooks(useHooksAsProps)(OverviewAndTools);
