import { withFormik } from 'formik';
import { compose } from 'recompose';
import { connect } from 'react-redux';
import * as yup from 'yup';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';
import DataConsentModal from './DataConsentModal';
import { ENTITIES, selectors, fetch } from '../../../store/ducks/options';
import {
  saveDataConsentForm,
  selectData as selectDataConsentForm,
} from '../../../store/ducks/dataConsentForm';
import { withErrorPopover } from '../../ErrorPopover';
import { EEA_COUNTRY_CODES } from '../constants';

function mapStateToProps(state) {
  const consentForm = selectDataConsentForm(state);
  const countries = selectors.selectSliceFor(ENTITIES.countries, state);

  return {
    consentForm,
    countries: countries.data,
  };
}

function mapPropsToValues(props) {
  const { consentForm, countries } = props;

  if (Object.keys(consentForm).length) {
    const collectThirdPartyPIIFrom = {
      ADULT: '0',
      MINOR: '1',
      ADULT_AND_MINOR: '2',
    }[consentForm.thirdPartyAgeType];

    const countriesCollectingData = countries
      .filter((country) =>
        consentForm.countries.includes(country.value) ? country : null,
      )
      .filter((country) => country);

    const isEEACountry =
      countriesCollectingData.length > 0
        ? countriesCollectingData.every((candidate) =>
            EEA_COUNTRY_CODES.includes(candidate.value),
          )
        : false;

    if (isEEACountry) {
      countriesCollectingData.push({
        value: 'EEA',
        label: 'European Economic Area (EEA)',
        alpha3: 'EEA',
      });
    }

    const pIIDataCollected = consentForm.piiData.map((pii) => {
      return {
        ...pii,
        label: pii.name,
        value: pii.id,
      };
    });

    const collectThirdPartyPII = collectThirdPartyPIIFrom
      ? collectThirdPartyPIIFrom.toString().length
      : null;

    const appenOwnsSomeDataset = {
      CUSTOMER_OWNS_ALL: 0,
      APPEN_OWNS_SOME_OR_ALL: 1,
      CUSTOMER_TEMPLATE: 2,
    }[consentForm.datasetOwnerType];

    return {
      id: consentForm.id,
      appenEntity: consentForm.appenEntityId,
      projectContactEmail: consentForm.contactEmail,
      customerDPOEmail: consentForm.customerDpoEmail,
      customerFullLegalName: consentForm.customerFullLegalName,
      customerCountry: consentForm.customerCountry,
      collectThirdPartyPII,
      collectThirdPartyPIIFrom,
      appenOwnsSomeDataset,
      countriesCollectingData,
      pIIDataCollected,
      version: consentForm.version,
      documentId: consentForm.documentId,
    };
  }

  return {
    appenOwnsSomeDataset: false,
  };
}

function handleSubmit(values, { props }) {
  const { dispatch, projectId, closeModal } = props;

  const datasetOwnerType = {
    0: 'CUSTOMER_OWNS_ALL',
    1: 'APPEN_OWNS_SOME_OR_ALL',
    2: 'CUSTOMER_TEMPLATE',
  }[values.appenOwnsSomeDataset || 0];

  let countries = values.countriesCollectingData
    ? values.countriesCollectingData.reduce((acc, country) => {
        acc.push(country.value);
        return acc;
      }, [])
    : [];
  countries = countries.filter((country) => country !== 'EEA');
  countries = countries.find((country) => country === '*') ? ['*'] : countries;

  const piiData = values.pIIDataCollected
    ? values.pIIDataCollected.reduce((acc, pii) => {
        acc.push({ id: pii.id });
        return acc;
      }, [])
    : [];
  const thirdPartyAgeType = {
    0: 'ADULT',
    1: 'MINOR',
    2: 'ADULT_AND_MINOR',
  }[values.collectThirdPartyPIIFrom];

  const dataConsentForm = {
    projectId,
    appenEntityId: values.appenEntity,
    datasetOwnerType,
    contactEmail: values.projectContactEmail,
    countries,
    piiData,
    thirdPartyAgeType,
    customerDpoEmail: values.customerDPOEmail,
    customerFullLegalName: values.customerFullLegalName,
    customerCountry: values.customerCountry,
    customerTemplate: datasetOwnerType === 'CUSTOMER_TEMPLATE',
    documentId: values.documentId,
  };

  dispatch(
    saveDataConsentForm({
      changes: { dataConsentForm, version: values.id || null },
      thenPut: fetch({
        optionsFor: ENTITIES.projectLevelDataConsentForm,
        nestedKey: projectId,
        projectId,
      }),
      closeModal,
    }),
  );
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: yup.object().shape({
      projectContactEmail: yup.mixed().when('appenOwnsSomeDataset', {
        is: (value) => value < 2, // Customer owns datasets and Appen owns datasets
        then: yup
          .string()
          .email('E-mail must be a valid e-mail address')
          .required(requiredMessage`Email`),
      }),
      appenEntity: yup.mixed().when('appenOwnsSomeDataset', {
        is: (value) => value < 2, // Customer owns datasets and Appen owns datasets
        then: yup
          .string()
          .required(requiredMessage`Appen entity`)
          .nullable(),
      }),
      pIIDataCollected: yup.mixed().when('appenOwnsSomeDataset', {
        is: (value) => value < 2, // Customer owns datasets and Appen owns datasets
        then: yup
          .string()
          .required(requiredMessage`PII Data collected`)
          .nullable(),
      }),
      countriesCollectingData: yup.mixed().when('appenOwnsSomeDataset', {
        is: (value) => value < 2, // Customer owns datasets and Appen owns datasets
        then: yup
          .string()
          .required(requiredMessage`Countries`)
          .nullable(),
      }),
      customerDPOEmail: yup.mixed().when('displayCustomerRealData', {
        is: true,
        then: yup
          .string()
          .email('E-mail must be a valid e-mail address')
          .required(
            requiredMessage`When "Display customer real data" is checked this`,
          ),
      }),
      documentId: yup.mixed().when('appenOwnsSomeDataset', {
        is: (value) => value === 2, // Customer Template
        then: yup
          .string()
          .required(requiredMessage`Customer consent form`)
          .nullable(),
      }),
    }),
    enableReinitialize: true,
  }),
)(DataConsentModal);
