import React from 'react';
import PropTypes from 'prop-types';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../../Popover';
import copy from '../../../../../assets/icons/copy.svg';
import pencil from '../../../../../assets/icons/pencil.svg';
import { Container } from './styles';
import SettingsHoverable from '../../../../Icons/SettingsHoverable';

const actions = [
  { code: 'copy', src: copy, alt: 'copy payrate', text: 'Copy' },
  { code: 'edit', src: pencil, alt: 'edit payrate', text: 'Edit' },
];

export default function SettingsCell(props) {
  const {
    row: {
      original: { id },
    },
    initialState: { copyPayrate, editPayrate },
  } = props;

  const getStandardActions = () => {
    const out = [
      {
        ...actions[0],
        fn: copyPayrate,
        'data-testid': `payrates--actions-copy-${id}`,
      },
      {
        ...actions[1],
        fn: editPayrate,
        'data-testid': `payrates--actions-edit-${id}`,
      },
    ];

    return out;
  };

  const actionFns = getStandardActions()
    .filter((sa) => actions.find((pra) => pra.code === sa.code))
    .map((action) => ({
      ...action,
      clickFn: () => action.fn(id),
    }))
    .map((action) => ({
      ...action,
      keyDownFn: ({ keyCode }) => keyCode === 13 && action.fn(id),
    }));

  const tooltipContent = actionFns.map((action) => (
    <PopoverItem
      onClick={action.clickFn}
      onKeyDown={action.keyDownFn}
      role="button"
      tabIndex={0}
      key={action.text}
      data-testid={action['data-testid']}
    >
      <PopoverIcon src={action.src} alt={action.alt} />
      <PopoverText>{action.text}</PopoverText>
    </PopoverItem>
  ));

  return (
    <Container
      className="settings-icon-container"
      data-testid="payrates--actions-hoverable"
    >
      <Popover content={tooltipContent} placement="bottom-end">
        <SettingsHoverable />
      </Popover>
    </Container>
  );
}

SettingsCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      defaultRate: PropTypes.bool,
    }),
  }).isRequired,
  initialState: PropTypes.shape({
    disableDelete: PropTypes.bool,
    copyPayrate: PropTypes.func.isRequired,
    editPayrate: PropTypes.func.isRequired,
  }).isRequired,
};
