import { withFormik } from 'formik';
import * as yup from 'yup';
import { compose } from 'recompose';
import { connect } from 'react-redux';
import ProjectTools from './ProjectTools';
import { projectToolsYupValidations } from '../../../../components/ProjectForms/ProjectToolsForm';
import {
  patchProject,
  selectProjectTools,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  return {
    initialValues: {
      ...selectProjectTools(state),
      hasExternalSystem: true,
    },
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  if (values.hasExternalSystem) {
    payload.externalSystemId = values.externalSystem;

    if (values.externalSystem) {
      payload.swfhProjectExternalUrl = values.swfhProjectExternalUrl || null;
    }
  } else {
    payload.externalSystemId = null;
    payload.swfhProjectExternalUrl = null;
  }

  if (values.hasIPRestrictions) {
    payload.allowedIps = (values.ipAddresses || []).map((ip) => ip.ipAddress);
  }

  const nextStep = STEP_NUMBER.PROJECT_TOOLS + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    validationSchema: yup.object().shape(projectToolsYupValidations),
  }),
)(ProjectTools);
