import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import * as yup from 'yup';
import { requiredMessage } from '../../../../../../pages/ProjectSetup/Steps/shared';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../../store/ducks/editableEntities';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
} from '../../../../../../store/ducks/options';
import AcademyResourceForm from './AcademyResourceForm';

export default compose(
  connect((state) => ({
    resource: editor.selectors.selectEditorDataFor(
      EDITABLE_ENTITIES.PROJECT_RESOURCES,
      state,
    ),
  })),
  withFormik({
    mapPropsToValues: ({ resource }) =>
      resource || {
        allowQualifyingApplicantAccess: true,
      },
    handleSubmit: async (values, { props: { dispatch, projectId } }) => {
      dispatch(
        editor.actions.save(
          EDITABLE_ENTITIES.PROJECT_RESOURCES,
          {
            type: ENTITY_METADATA.projectResources.types.academy,
            ...values,
          },
          {
            projectId,
            thenPut: fetch({
              optionsFor: ENTITIES.projectResources,
              nestedKey: projectId,
            }),
          },
        ),
      );
    },
    validationSchema: yup.object().shape({
      resourceId: yup.number().required(requiredMessage`Academy`),
    }),
  }),
)(AcademyResourceForm);
