import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import * as projectPages from '../../../../../../store/ducks/projectPages';
import ProjectUserProjectPagePreview from '../../../../../../components/ProjectPreviews/ProjectUserProjectPagePreview';

export default function UserProjectPage(props) {
  const {
    defaultPageTask,
    hasSkillReview,
    isACANProject,
    navigateTo,
    renderedIn,
  } = props;

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.USER_PROJECT_PAGE);
  }, [navigateTo]);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(projectPages.fetchAll());
  }, [dispatch]);

  return (
    <ProjectUserProjectPagePreview
      defaultPageTask={defaultPageTask}
      hasSkillReview={hasSkillReview}
      isACANProject={isACANProject}
      editStep={editStep}
      renderedIn={renderedIn}
      stepNumber={STEP_NUMBER.USER_PROJECT_PAGE + 1}
    />
  );
}

UserProjectPage.propTypes = {
  defaultPageTask: PropTypes.string,
  hasSkillReview: PropTypes.bool,
  isACANProject: PropTypes.bool,
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf([
    'project-view',
    'project-setup',
    'project-preview-modal',
  ]),
};

UserProjectPage.defaultProps = {
  defaultPageTask: '',
  hasSkillReview: false,
  isACANProject: false,
  renderedIn: 'project-setup',
};
