import React from 'react';
import PropTypes from 'prop-types';
import { useDrag } from 'react-dnd';
import { useSelector } from 'react-redux';
import noop from 'lodash/noop';
import {
  Modules,
  Module,
  ModuleTitle,
  ChevronWrapper,
  Wrapper,
} from './styles';
import { CARD_FROM_SIDEBAR } from '../../../ProcessBuilder/constants';
import { findByStepDefinitionId } from '../../../../helpers/find';
import { ReactComponent as Chevron } from '../../../../assets/icons/double-chevron-right.svg';

export default function DragableItem(props) {
  const {
    id,
    name,
    sequenceFlow,
    description,
    parentId,
    children,
    canDrag,
    canBeDraggedMultipleTimes,
    items,
    nested,
    selectStepEditorDirty,
    selectProcessModules,
  } = props;

  const dirty = useSelector(selectStepEditorDirty);
  const processModules = useSelector(selectProcessModules);

  const [{ isDragging }, dragRef] = useDrag({
    item: {
      stepDefinitionId: id,
      parentId,
      name,
      type: CARD_FROM_SIDEBAR,
      sequenceFlow,
      description,
      items,
    },
    canDrag: canDrag !== undefined ? canDrag : !dirty,
    collect: (monitor) => ({ isDragging: monitor.isDragging() }),
  });

  let dropped = findByStepDefinitionId(processModules, id) ? 'dropped' : '';
  dropped = canBeDraggedMultipleTimes ? '' : dropped;
  const ref = !dropped ? dragRef : noop;

  return (
    <Modules
      ref={ref}
      isDragging={isDragging}
      isDirty={dirty}
      className={dropped}
    >
      <Wrapper>
        {parentId && (
          <ChevronWrapper>
            <Chevron />
          </ChevronWrapper>
        )}
        <Module
          isDragging={isDragging}
          isDirty={dirty}
          className={dropped}
          title={
            dirty
              ? "You can't add new steps while there are unsaved changes."
              : undefined
          }
        >
          <ModuleTitle className={nested && 'nested'}>{name}</ModuleTitle>
        </Module>
      </Wrapper>
      {children}
    </Modules>
  );
}

DragableItem.defaultProps = {
  children: undefined,
  parentId: undefined,
  canDrag: undefined,
  canBeDraggedMultipleTimes: false,
  items: [],
  nested: false,
};

DragableItem.propTypes = {
  id: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  sequenceFlow: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  children: PropTypes.node,
  canDrag: PropTypes.bool,
  canBeDraggedMultipleTimes: PropTypes.bool,
  parentId: PropTypes.number,
  items: PropTypes.arrayOf(PropTypes.object),
  nested: PropTypes.bool,
  selectStepEditorDirty: PropTypes.func.isRequired,
  selectProcessModules: PropTypes.func.isRequired,
};
