import { combineReducers } from 'redux';

// Generic Data Fetching Reducers
import projectPagesReducer from './projectPages';
import registrationProcessesReducer from './registrationProcesses';
import qualificationProcessesReducer from './qualificationProcesses';
import teamTypesReducer from './teamTypes';
import demographicRequirements from './demographicRequirements';

// Non-generic Data Fetching Reducers
import tenantsByCountryReducer from './tenantsByCountry';
import defaultPayratesReducer from './defaultPayrates';

// Local state Reducers
import projectSetupReducer from './projectSetup';
import projectViewReducer from './projectView';
import hiringTargetsReducer from './hiringTargets';
import localeTenantsReducer from './localeTenants';
import payratesReducer from './payrates';

import projectDemographicRequirementsReducer from './projectDemographicRequirements';
import projectDemographicMetricsReducer from './projectDemographicMetrics';
import processBuilderReducer from './processBuilder';
import targetBuilderReducer from './targetBuilder';

import options from './options';
import paginated from './paginated';
import security from './security';
import editableEntities from './editableEntities';

import projectReducer from './project';
import invoiceConfigurationReducer from './invoiceConfiguration';
import resendPartnerData from './resendPartnerData';
import experimentGroups from './experimentGroups';
import dataConsentFormReducer from './dataConsentForm';
import externalConsentFormReducer from './externalConsentForm';
import userThirdPartyList from './userThirdPartyList';
import metricsAndGoalsReducer from './metricsAndGoals';
import profileQuestionsReducer from './profileQuestions';
import targetScreeningReducer from './targetScreening';
import autoScreeningReducer from './autoScreening';
import userScreeningDetailsReducer from './userScreeningDetails';
import screeningMetricsReducer from './screeningMetrics';
import userScreeningFalsePositivesReducer from './userScreeningFalsePositives';

export default combineReducers({
  // For simple data fetching
  [projectPagesReducer.key]: projectPagesReducer,
  [registrationProcessesReducer.key]: registrationProcessesReducer,
  [qualificationProcessesReducer.key]: qualificationProcessesReducer,
  [teamTypesReducer.key]: teamTypesReducer,
  [demographicRequirements.key]: demographicRequirements,

  // For conditional data fetching
  [tenantsByCountryReducer.key]: tenantsByCountryReducer,
  [defaultPayratesReducer.key]: defaultPayratesReducer,

  // For Project State
  [projectSetupReducer.key]: projectSetupReducer,
  // For Project View
  [projectViewReducer.key]: projectViewReducer,
  // For local Hiring Targets State
  [hiringTargetsReducer.key]: hiringTargetsReducer,
  // For local locale Tenants State
  [localeTenantsReducer.key]: localeTenantsReducer,
  // For local custom payrates State
  // For selected payrates (both custom and local)
  [payratesReducer.key]: payratesReducer,

  [projectDemographicRequirementsReducer.key]: projectDemographicRequirementsReducer,
  [projectDemographicMetricsReducer.key]: projectDemographicMetricsReducer,

  [processBuilderReducer.key]: processBuilderReducer,
  [targetBuilderReducer.key]: targetBuilderReducer,

  [projectReducer.key]: projectReducer,

  [options.key]: options,
  [paginated.key]: paginated,
  [security.key]: security,
  [editableEntities.key]: editableEntities,

  [invoiceConfigurationReducer.key]: invoiceConfigurationReducer,
  [resendPartnerData.key]: resendPartnerData,

  [experimentGroups.key]: experimentGroups,

  [externalConsentFormReducer.key]: externalConsentFormReducer,
  [dataConsentFormReducer.key]: dataConsentFormReducer,
  [userThirdPartyList.key]: userThirdPartyList,
  [metricsAndGoalsReducer.key]: metricsAndGoalsReducer,

  [profileQuestionsReducer.key]: profileQuestionsReducer,
  [targetScreeningReducer.key]: targetScreeningReducer,
  [autoScreeningReducer.key]: autoScreeningReducer,
  [userScreeningDetailsReducer.key]: userScreeningDetailsReducer,
  [screeningMetricsReducer.key]: screeningMetricsReducer,
  [userScreeningFalsePositivesReducer.key]: userScreeningFalsePositivesReducer,
});
