import React from 'react';
import PropTypes from 'prop-types';
import {
  PreviewSectionItem,
  PreviewSection,
  PreviewSectionText,
} from '../../styles';
import { Container, TextContainer, NoTextContainer } from './styles';
import { Label } from '../../../../styles';

export default function Demographics(props) {
  const { demographicType, requirements, header } = props;

  return (
    <PreviewSection marginBottom={30}>
      {header ? (
        <>
          {header}

          <Container
            isLoading={false}
            data-testid="demographics-modal--action-hoverable"
          >
            {requirements ? (
              <TextContainer>
                <Label>{demographicType}</Label>
                <p>{requirements}</p>
              </TextContainer>
            ) : (
              <NoTextContainer>Not Set</NoTextContainer>
            )}
          </Container>
        </>
      ) : (
        <>
          <PreviewSectionItem>
            {requirements ? demographicType : 'Demographic Requirements'}
          </PreviewSectionItem>

          <PreviewSectionText>
            <p>{requirements || 'Not available'}</p>
          </PreviewSectionText>
        </>
      )}
    </PreviewSection>
  );
}

Demographics.propTypes = {
  demographicType: PropTypes.string.isRequired,
  requirements: PropTypes.string.isRequired,
  header: PropTypes.element,
};

Demographics.defaultProps = {
  header: null,
};
