import uniq from 'lodash/uniq';
import { all, call, put, takeLatest } from 'redux-saga/effects';
import { fetchAll, fetchFailed, setAll } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { ENTITIES, fetch } from '../options';
import { tenantsByCountrySagaFn } from '../tenantsByCountry';

export function* fetchLocaleTenantsFn(action) {
  const {
    payload: { projectId },
  } = action;

  try {
    const { data: localeTenants } = yield call(
      axios.get,
      ENDPOINTS.V2.LOCALE_TENANTS({
        query: {
          projectId,
        },
      }),
    );

    const countries = uniq(localeTenants.map((lt) => lt.country));

    yield all(
      countries.map((country) => tenantsByCountrySagaFn({ payload: country })),
    );

    yield put(fetch(ENTITIES.countries));

    yield put(
      setAll(
        localeTenants.map((lt) => ({
          id: lt.id,
          tenant: lt.tenantId,
          country: lt.country,
        })),
      ),
    );
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

export default takeLatest(fetchAll.type, fetchLocaleTenantsFn);
