import React from 'react';
import PropTypes from 'prop-types';
import { DEMOGRAPHIC_TYPE } from '../../../../store/ducks/projectDemographicRequirements';
import DemographicsGroupTable from './DemographicsGroupTable';
import AggregatedRequirements from './AggregatedDemographicsTable';

export default function DemographicData(props) {
  const {
    requirementHeaders,
    demographicType,
    values,
    setRequirementsByGroupValue,
    setAggregatedRequirementsValue,
    viewAs,
    viewAsPercent,
  } = props;

  return (
    <div>
      {demographicType === DEMOGRAPHIC_TYPE.BY_GROUP ? (
        requirementHeaders.map((rh) => (
          <DemographicsGroupTable
            key={rh.id}
            tableName={rh.name}
            requirementID={rh.id}
            groups={rh.groups}
            value={values.requirementsByGroup}
            setRequirementsByGroupValue={setRequirementsByGroupValue}
            viewAs={viewAs}
            viewAsPercent={viewAsPercent}
          />
        ))
      ) : (
        <AggregatedRequirements
          value={values.aggregatedRequirements}
          setAggregatedRequirementsValue={setAggregatedRequirementsValue}
          viewAs={viewAs}
          viewAsPercent={viewAsPercent}
        />
      )}
    </div>
  );
}

DemographicData.propTypes = {
  requirementHeaders: PropTypes.arrayOf(PropTypes.object).isRequired,
  demographicType: PropTypes.oneOf(Object.values(DEMOGRAPHIC_TYPE)).isRequired,
  values: PropTypes.shape({
    requirementsByGroup: PropTypes.array,
    aggregatedRequirements: PropTypes.object,
    distributedEvenly: PropTypes.bool,
  }).isRequired,
  setRequirementsByGroupValue: PropTypes.func.isRequired,
  setAggregatedRequirementsValue: PropTypes.func.isRequired,
  viewAs: PropTypes.string.isRequired,
  viewAsPercent: PropTypes.string.isRequired,
};
