import React from 'react';
import PropTypes from 'prop-types';
import ExpandedCell from './ExpandedCell';
import CheckFrame from '../../../../components/CheckFrame';
import { DUPLICATES } from '../../../../components/CheckFrame/helpers/checkFrameTypes';
import { ReactComponent as CheckedIcon } from '../../../../assets/icons/checkmark.svg';
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close-circle.svg';
import { Center } from './styles';

export default function IpCheckCell({ row }) {
  const {
    isExpanded,
    original: { ipCheck },
  } = row;

  const Icon = ipCheck ? CheckedIcon : CloseIcon;

  return (
    <>
      <Center>
        <Icon style={{ margin: '-2px 0 0 -15px' }} height={18} />
      </Center>
      <ExpandedCell isExpanded={isExpanded}>
        <CheckFrame type={DUPLICATES} user={row.original} />
      </ExpandedCell>
    </>
  );
}

IpCheckCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({ ipCheck: PropTypes.bool }).isRequired,
  }).isRequired,
};
