import { call, put, takeLatest } from 'redux-saga/effects';
import {
  toggleExperimentGroupSuccess,
  toggleExperimentGroupError,
  toggleExperimentGroup,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* toggleExperimentGroupSagaFn(action) {
  const {
    payload: { experimentGroup, projectId },
  } = action;

  try {
    const response = yield call(
      axios.put,
      ENDPOINTS.V2.EXPERIMENT_GROUPS({
        query: {
          projectId,
        },
        pathVariables: {
          id: experimentGroup.id,
        },
      }),
      experimentGroup,
    );

    yield put(toggleExperimentGroupSuccess(response.data));
  } catch (error) {
    yield put(toggleExperimentGroupError(error));
  }
}

export default takeLatest(
  toggleExperimentGroup.type,
  toggleExperimentGroupSagaFn,
);
