import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useFormikContext } from 'formik';
import NumberFormat from 'react-number-format';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';
import mapToOptions from '../../../../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  ENTITIES,
  fetch,
  selectors,
} from '../../../../../../store/ducks/options';
import { Section, Label } from '../../../../../../styles';
import Checkbox from '../../../../../Checkbox';
import Input from '../../../../../Input';
import LabeledSwitch from '../../../../../LabeledSwitch';
import InformationTooltip, {
  LabelWrapper,
} from '../../../../../ProcessBuilder/InformationTooltip';
import Select from '../../../../../Select';
import { CheckGroup } from '../../../../ProjectToolsForm/styles';
import ResourceFormContainer from '../ResourceFormContainer';

export default function SurveyResourceForm(props) {
  const { closeModal } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.surveys));
  }, [dispatch]);

  const { data: surveys, error, loading } = useSelector(
    selectors.createSelectSliceFor(ENTITIES.surveys),
  );

  const surveysOptions = useMemo(() => mapToOptions('id', 'name')(surveys), [
    surveys,
  ]);

  const {
    setFieldValue,
    values,
    submitCount,
    errors,
    handleChange,
  } = useFormikContext();

  return (
    <ResourceFormContainer closeModal={closeModal}>
      <Section>
        <Label htmlFor="resourceId">Survey</Label>
        <Select
          name="resourceId"
          id="resourceId"
          options={surveysOptions}
          onChange={({ value }) => setFieldValue('resourceId', value)}
          value={values.resourceId}
          placeholder="Select existing survey"
          error={error || (submitCount >= 1 && errors.resourceId)}
          loading={loading}
        />
      </Section>

      <Section marginBottom={42}>
        <Row>
          <Col col={4}>
            <LabelWrapper>
              <Label htmlFor="maxQuizAttempts">Maximum attempts</Label>
              <InformationTooltip title="The max number of times an applicant may attempt to take the specified internal screening quiz." />
            </LabelWrapper>
            <NumberFormat
              customInput={Input}
              allowNegative={false}
              name="maxQuizAttempts"
              onChange={(e) =>
                setFieldValue('maxQuizAttempts', parseInt(e.target.value, 10))
              }
              id="maxQuizAttempts"
              placeholder={values.unlimited ? '' : 'Enter number'}
              value={values.unlimited ? '' : values.maxQuizAttempts}
              disabled={values.unlimited}
              error={submitCount >= 1 && errors.maxQuizAttempts}
              errorIncreasesHeight
            />
            <Section marginTop={12} marginBottom={0}>
              <LabeledSwitch
                name="unlimited"
                onChange={(e) => {
                  setFieldValue('unlimited', e.target.value);
                  if (e.target.value) {
                    setFieldValue('maxQuizAttempts', -1);
                  } else {
                    setFieldValue('maxQuizAttempts', '');
                  }
                }}
                value={values.unlimited}
                label="UNLIMITED"
              />
            </Section>
          </Col>
          <Col col={4}>
            <LabelWrapper>
              <Label htmlFor="subsetSize">Subset size</Label>
              <InformationTooltip title="The number of questions in the quiz to be displayed when an applicant takes the specified screening quiz." />
            </LabelWrapper>
            <NumberFormat
              customInput={Input}
              allowNegative={false}
              name="subsetSize"
              onChange={(e) =>
                setFieldValue('subsetSize', parseInt(e.target.value, 10))
              }
              id="subsetSize"
              placeholder={
                values.allQuestions ? 'All questions' : 'Enter number'
              }
              disabled={values.allQuestions}
              value={values.allQuestions ? '' : values.subsetSize}
              error={submitCount >= 1 && errors.subsetSize}
              errorIncreasesHeight
            />
            <Section marginTop={12} marginBottom={0}>
              <LabeledSwitch
                name="allQuestions"
                onChange={(e) => {
                  setFieldValue('allQuestions', e.target.value);
                  if (e.target.value) {
                    setFieldValue('subsetSize', -1);
                  } else {
                    setFieldValue('subsetSize', '');
                  }
                }}
                value={values.allQuestions}
                label="ALL QUESTIONS"
              />
            </Section>
          </Col>
        </Row>
      </Section>
      <Section>
        <Section>
          <CheckGroup>
            <Checkbox
              name="questionsShuffled"
              onChange={handleChange}
              checked={values.questionsShuffled}
              label="Shuffle Questions"
              bold={false}
            />
          </CheckGroup>
        </Section>
      </Section>
    </ResourceFormContainer>
  );
}

SurveyResourceForm.propTypes = {
  closeModal: PropTypes.func.isRequired,
};
