/* eslint-disable import/no-cycle */
import { useCallback } from 'react';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import shortid from 'shortid';
import DatePicker from './DatePicker';

const logicOptions = [
  { value: 'BEFORE', label: 'Before' },
  { value: 'EQUAL_OR_BEFORE', label: 'Equal or before' },
  { value: 'EQUAL', label: 'Equal' },
  { value: 'EQUAL_OR_AFTER', label: 'Equal or after' },
  { value: 'AFTER', label: 'After' },
  { value: 'BETWEEN', label: 'Between' },
];

function useHooksAsProps(outerProps) {
  const {
    data: { id },
    setFieldValue,
    values: { answers },
  } = outerProps;

  const fieldBaseName = `answers[${id}]`;
  const datePickerAnswers = answers[id];

  const handleAddLine = useCallback(() => {
    setFieldValue(fieldBaseName, [
      ...datePickerAnswers,
      { key: shortid.generate() },
    ]);
  }, [setFieldValue, datePickerAnswers, fieldBaseName]);

  const handleRemove = useCallback(
    (index) => {
      setFieldValue(fieldBaseName, [
        ...datePickerAnswers.slice(0, index),
        ...datePickerAnswers.slice(index + 1, datePickerAnswers.length),
      ]);
    },
    [setFieldValue, datePickerAnswers, fieldBaseName],
  );

  const handleLogic = useCallback(
    (key, value, betweenKey) => {
      setFieldValue(key, value);
      setFieldValue(betweenKey, value === 'BETWEEN');
    },
    [setFieldValue],
  );

  return {
    handleAddLine,
    handleRemove,
    handleLogic,
    logicOptions,
  };
}

export default compose(withHooks(useHooksAsProps))(DatePicker);
