import { call, put, takeEvery } from 'redux-saga/effects';
import { fetchFailed, fetchInvoiceConfiguration, fetchSucceeded } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { fromDTOToLocalState } from './converter';

function* fetchInvoiceConfigurationSaga(action) {
  const {
    payload: { projectId },
  } = action;

  try {
    const response = yield call(
      axios.get,
      ENDPOINTS.V2.INVOICE_CONFIGURATION({
        pathVariables: {
          id: projectId,
        },
      }),
    );

    yield put(
      fetchSucceeded({
        projectId,
        configuration: fromDTOToLocalState(response.data),
      }),
    );
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

export default takeEvery(
  fetchInvoiceConfiguration.type,
  fetchInvoiceConfigurationSaga,
);
