/* eslint-disable react/jsx-props-no-spreading */
import React, { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import { useStyletron } from '@connect-ui/styles';
import { Block } from 'baseui/block';

import Button from '../../../components/ConnectButton';
import Icon from '../../../components/ConnectIcon';
import Input from '../../../components/ConnectInput';
import Select from '../../../components/ConnectSelect';

import {
  PAGINATED_ENTITIES,
  setFilters,
  selectors,
} from '../../../store/ducks/paginated';
import {
  fetch,
  ENTITIES,
  selectors as optionsSelectors,
} from '../../../store/ducks/options';
import HorizontalFlex from '../../../components/HorizontalFlex';
import { Spacer } from './styles';
import legacyIntegration from '../../../helpers/legacy-integration';
import STATUSES from '../../../helpers/project-statuses';
import {
  REQUEST_TYPES,
  requestInformation,
} from '../../../services/postMessage';

export default function Filters() {
  const [, theme] = useStyletron();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.customers));
  }, [dispatch]);

  const handleInputChange = useCallback(
    (event) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.projects,
          filters: {
            name: event.target.value,
          },
        }),
      );
    },
    [dispatch],
  );

  const handleCustomerChange = useCallback(
    ({ value: { id } }) => {
      legacyIntegration.informSwitchCustomer(id);
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.projects,
          filters: {
            customer: id,
          },
        }),
      );
    },
    [dispatch],
  );

  const handleStatusChange = useCallback(
    ({ value }) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.projects,
          filters: {
            status: value.value,
          },
        }),
      );
    },
    [dispatch],
  );

  const customers = useSelector(
    optionsSelectors.createSelectSliceFor(ENTITIES.customers),
  );

  const filters = useSelector(
    selectors.createSelectFiltersFor(PAGINATED_ENTITIES.projects),
  );

  useEffect(() => {
    requestInformation(REQUEST_TYPES.currentCustomer, ({ currentCustomer }) => {
      if (currentCustomer && currentCustomer.length !== 0) {
        handleCustomerChange({ value: parseInt(currentCustomer, 10) });
      }
    });
  }, [handleCustomerChange]);

  const name = get(filters, 'name', '');
  const customer = get(filters, 'customer', undefined);
  const status = get(filters, 'status', undefined);

  return (
    <HorizontalFlex
      hideDivider
      leftNodes={
        <>
          <Block minWidth="320px">
            <Input
              id="filterByText"
              name="filterByText"
              placeholder="Search Projects"
              value={name}
              onChange={handleInputChange}
              inputSize="100%"
              startEnhancer
              clearable
              clearOnEscape
              overrides={{
                ClearIcon: {
                  component: function closeIcon(p) {
                    return (
                      <span
                        style={{ display: 'flex', alignItems: 'center' }}
                        {...p}
                      >
                        <Icon
                          name="close"
                          color={theme.colors.inputPlaceholder}
                        />
                      </span>
                    );
                  },
                },
                StartEnhancer: {
                  component: function searchIcon() {
                    return (
                      <span style={{ display: 'flex', alignItems: 'center' }}>
                        <Icon
                          name="search"
                          color={theme.colors.inputPlaceholder}
                        />
                      </span>
                    );
                  },
                },
              }}
            />
          </Block>
          <Spacer />
          <Block minWidth="240px">
            <Select
              labelKey="name"
              placeholder="Select customer"
              options={customers.data}
              loading={customers.loading}
              error={customers.error}
              onChange={handleCustomerChange}
              value={{ id: customer }}
            />
          </Block>
          <Spacer />
          <Block minWidth="160px">
            <Select
              placeholder="Select project status"
              options={STATUSES}
              onChange={handleStatusChange}
              value={{ value: status }}
              valueKey="value"
            />
          </Block>
        </>
      }
      rightNodes={
        <Button onClick={() => legacyIntegration.informCreateProject()}>
          Create New Project
        </Button>
      }
    />
  );
}
