import React, { useEffect, useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import { selectError, selectSaving, setError } from '../../store/ducks/project';
import { selectSaving as selectExperimentGroupsSaving } from '../../store/ducks/experimentGroups';
import ErrorPopover, { useErrorPopover } from '../ErrorPopover';
import ActionFooter, { AccessoryText } from '../ActionFooter';
import { SavingText } from '../../styles';

export default function StepFooter(props) {
  const { backAction, saveAction, nextAction, nextLabel } = props;

  const projectError = useSelector(selectError);
  const projectSaving = useSelector(selectSaving);

  const experimentGroupsSaving = useSelector(selectExperimentGroupsSaving);

  const saving = !!(projectSaving || experimentGroupsSaving);

  const [showError, _hideError, errorProps] = useErrorPopover();

  useEffect(() => {
    if (projectError) {
      showError(projectError);
    } else {
      _hideError();
    }
  }, [_hideError, projectError, showError]);

  const dispatch = useDispatch();

  useEffect(
    () => () => {
      dispatch(setError(null));
    },
    [dispatch],
  );

  const hideError = useCallback(() => {
    _hideError();
    dispatch(setError(null));
  }, [_hideError, dispatch]);

  const handleBack = useCallback(() => {
    hideError();
    backAction();
  }, [backAction, hideError]);

  const handleSave = useCallback(() => {
    hideError();
    saveAction();
  }, [hideError, saveAction]);

  const handleNext = useCallback(() => {
    hideError();
    nextAction();
  }, [hideError, nextAction]);

  const leftButtons = useMemo(() => {
    return backAction
      ? [
          {
            type: 'secondary',
            onClick: handleBack,
            text: 'Back',
            disabled: saving,
            testId: 'step-footer--back-button',
          },
        ]
      : [];
  }, [backAction, handleBack, saving]);

  const rightButtons = useMemo(() => {
    const buttons = [
      {
        onClick: handleNext,
        text: nextLabel,
        disabled: saving,
        testId: 'step-footer--next-button',
      },
    ];

    if (saveAction) {
      buttons.unshift({
        type: 'secondary',
        onClick: handleSave,
        text: 'Save',
        disabled: saving,
        testId: 'step-footer--save-button',
      });
    }
    return buttons;
  }, [handleNext, handleSave, nextLabel, saving, saveAction]);

  return (
    <div>
      <ActionFooter
        hideDivider
        leftButtons={leftButtons}
        accessoryRight={
          saving && (
            <AccessoryText data-testid="step-footer--saving-indicator">
              <SavingText>Saving...</SavingText>
            </AccessoryText>
          )
        }
        rightButtons={rightButtons}
      />
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <ErrorPopover {...errorProps} hideError={hideError} />
    </div>
  );
}

StepFooter.propTypes = {
  backAction: PropTypes.func,
  saveAction: PropTypes.func,
  nextAction: PropTypes.func.isRequired,
  nextLabel: PropTypes.string.isRequired,
};

StepFooter.defaultProps = {
  backAction: undefined,
  saveAction: undefined,
};
