/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback } from 'react';
import PropTypes from 'prop-types';

// styles
import { Check, Label, Text } from './styles';
import { themePropTypes } from '../../styles/themes/default';

export default function Checkbox(props) {
  const {
    children,
    name,
    onChange,
    checked,
    label,
    theme,
    'data-testid': dataTestId,
    disabled,
    bold = true,
  } = props;

  const handleChange = useCallback(
    (event) => {
      if (!disabled) {
        // eslint-disable-next-line no-param-reassign
        event.target = {
          ...event.target,
          name,
          value: !checked,
        };
        onChange(event);
      }
    },
    [checked, disabled, name, onChange],
  );

  const handleKeyDown = useCallback(
    (event) => {
      if (disabled) {
        return;
      }

      const { keyCode } = event;

      if (keyCode === 32) {
        event.preventDefault();
        onChange({ target: { name, value: !checked } });
      }
    },
    [checked, disabled, name, onChange],
  );

  return (
    <Label data-testid={dataTestId} tabIndex={0} onKeyDown={handleKeyDown}>
      <Check
        name={name}
        onChange={handleChange}
        checked={checked}
        tabIndex={-1}
        disabled={disabled}
      />
      <span className="overlay">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="13"
          height="9"
          viewBox="0 0 13 9"
          className="icon"
        >
          <path
            fill={theme.colors.white}
            fillRule="nonzero"
            d="M11.89.61a1 1 0 0 1 0 1.415L5.524 8.39a1 1 0 0 1-1.414 0l-.03-.032a.996.996 0 0 1-.374-.236L.88 5.293a1 1 0 1 1 1.414-1.414L4.75 6.335 10.475.611a1 1 0 0 1 1.414 0z"
          />
        </svg>
      </span>
      <Text isBold={bold} isDisabled={disabled}>
        {children || label}
      </Text>
    </Label>
  );
}

Checkbox.propTypes = {
  name: PropTypes.string.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]),
  checked: PropTypes.bool,
  onChange: PropTypes.func,
  label: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.object,
  ]),
  theme: themePropTypes.isRequired,
  'data-testid': PropTypes.string,
  disabled: PropTypes.bool,
  bold: PropTypes.bool,
};
Checkbox.defaultProps = {
  checked: false,
  onChange: undefined,
  label: undefined,
  children: null,
  'data-testid': '',
  disabled: false,
  bold: true,
};
