import styled from 'styled-components';
import { transparentize } from 'polished';

export const Radio = styled.input.attrs(() => ({
  type: 'checkbox',
}))``;

export const Label = styled.label`
  position: relative;
  margin-left: 15px;
  margin-right: 30px;

  input {
    position: absolute;
    opacity: 0;
  }

  input:focus ~ .overlay {
    outline: 1px dotted #212121;
    outline: 5px auto -webkit-focus-ring-color;
  }

  cursor: ${(props) => (props.isDisabled ? 'default' : 'pointer')};

  ${(props) =>
    props.hasTooltip &&
    `
    display: flex;
    flex-flow: row;
  `}
`;

export const Overlay = styled.div`
  position: absolute;
  top: 5px;
  left: 0px;
  height: 20px;
  width: 20px;
  background-color: ${(props) =>
    props.checked
      ? props.theme.colors.azure
      : props.theme.colors.lightBlueGrey};
  border-radius: 50%;
  transition: all 0.3s;
`;

export const InsideCircle = styled.div`
  position: relative;
  top: 4px;
  left: 4px;
  height: 12px;
  width: 12px;
  border: 3px solid white;
  border-radius: 50%;
  background-color: ${(props) =>
    props.checked ? props.theme.colors.azure : props.theme.colors.white};
`;

export const Text = styled.div`
  margin-left: 34px;
  line-height: 1.6;
  color: ${(props) =>
    props.isDisabled
      ? transparentize(0.4, '#999')
      : props.theme.colors.charcoal};
  &:hover {
    color: ${(props) =>
      props.isDisabled
        ? transparentize(0.6, '#999')
        : props.theme.colors.charcoal};
  }
  font-size: ${(props) => props.theme.sizes.p};
  font-weight: ${(props) => (props.bold ? 'bold' : 'normal')};
  padding-top: 4px;
`;

export const TooltipWrapper = styled.div`
  height: 20px;
  margin-top: 7px;
`;
