import React from 'react';
import PropTypes from 'prop-types';
import noop from 'lodash/noop';
import { useSelector } from 'react-redux';
import { Label, Section } from '../../../../../styles';
import RichTextEditor, {
  useEditorChangeHandler,
} from '../../../../RichTextEditor';
import Input from '../../../../Input';
import { CheckGroup } from '../../../../../pages/ProjectSetup/Steps/ProjectTools/styles';
import Checkbox from '../../../../Checkbox';
import Textarea from '../../../../Textarea';
import { FlexContainer, CheckboxContainer, CheckboxLabel } from '../styles';
import Switch from '../../../../Switch';
import DocumentInformation from '../DocumentInformation';
import ActionFooter from '../../../../ActionFooter';
import { selectors, ENTITIES } from '../../../../../store/ducks/options';
import ErrorPopover, { ErrorPopoverProps } from '../../../../ErrorPopover';

export default function DocumentEditor(props) {
  const {
    documentId,
    updateDocument,
    cancelEditing,
    handleChange,
    submitCount,
    values,
    errors,
    isSubmitting,
    errorProps,
  } = props;

  const handleEditorChange = useEditorChangeHandler('documentBody');

  const loading = useSelector(
    selectors.createSelectLoadingFor(ENTITIES.documents, documentId),
  );

  return (
    <>
      <Section>
        <Label htmlFor="documentTitle">Document Title</Label>
        <Input
          name="documentTitle"
          onChange={handleChange}
          id="documentTitle"
          placeholder="Enter document title"
          value={values.documentTitle}
          error={submitCount >= 1 && errors.documentTitle}
          disabled={loading || documentId !== -1}
        />
      </Section>
      <Section>
        <Label htmlFor="documentBody">Document Content</Label>
        <RichTextEditor
          name="documentBody"
          onChange={handleEditorChange}
          id="documentBody"
          placeholder="Write document here"
          value={values.documentBody}
          error={submitCount >= 1 && errors.documentBody}
          disabled={loading}
        />
      </Section>

      <Section marginBottom="8px">
        <FlexContainer>
          <CheckboxContainer>
            <CheckboxLabel>Required</CheckboxLabel>
            <Switch
              name="required"
              onChange={handleChange}
              value={values.required}
              disabled={loading || documentId !== -1}
            />
          </CheckboxContainer>
        </FlexContainer>
      </Section>

      <Section>
        <CheckGroup>
          <Checkbox
            name="hasInternalNotes"
            onChange={handleChange}
            checked={values.hasInternalNotes}
            label="Add Internal Notes"
            disabled={loading || documentId !== -1}
          />
        </CheckGroup>
      </Section>

      {values.hasInternalNotes && (
        <Section marginBottom="12px" marginTop="30px" marginLeft="34px">
          <Label>Internal Notes</Label>
          <Textarea
            onChange={handleChange}
            name="internalNotes"
            placeholder="Write internal notes"
            rows={6}
            error={submitCount >= 1 && errors.internalNotes}
            value={values.internalNotes}
            disabled={loading || documentId !== -1}
          />
        </Section>
      )}

      {documentId !== -1 && (
        <>
          <Section>
            <Label>Change log</Label>
            <Textarea
              onChange={handleChange}
              name="changeLog"
              placeholder="Describe in a few words"
              rows={4}
              error={submitCount >= 1 && errors.changeLog}
              value={values.changeLog}
              disabled={loading}
            />
          </Section>

          <Section>
            <DocumentInformation documentId={documentId} overviewMode />
          </Section>

          <ActionFooter
            rightButtons={[
              {
                type: 'secondary',
                text: 'Cancel',
                onClick: cancelEditing,
                disabled: isSubmitting,
              },
              {
                type: 'primary',
                text: 'Update & Save',
                onClick: updateDocument,
                disabled: loading,
                loading: isSubmitting,
              },
            ]}
          />
        </>
      )}
      <ErrorPopover {...errorProps} />
    </>
  );
}

DocumentEditor.propTypes = {
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  handleChange: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  documentId: PropTypes.number,
  updateDocument: PropTypes.func,
  cancelEditing: PropTypes.func,
  errorProps: ErrorPopoverProps.isRequired,
};

DocumentEditor.defaultProps = {
  documentId: -1,
  updateDocument: noop,
  cancelEditing: noop,
};
