import React, { useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import { themePropTypes } from '../../../../styles/themes/default';
import * as projectPages from '../../../../store/ducks/projectPages';
import useFirstChange from '../../helpers/useFirstChange';
import { setDirty } from '../../../../store/ducks/projectSetup';
import StepFooter from '../../../../components/StepFooter';
import ProjectUserProjectPageForm from '../../../../components/ProjectForms/ProjectUserProjectPageForm';
import { useNavigationContext } from '../../helpers/navigationContext';

export default function UserProjectPage(props) {
  const { values, handleChange, setFieldValue, submitForm, theme } = props;

  function submitAndNavigate() {
    setFieldValue('navigate', true);
    submitForm();
  }

  const { goBack } = useNavigationContext();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(projectPages.fetchAll());
  }, [dispatch]);

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    values,
  );

  return (
    <div>
      <WizardTitle>User Project Page</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <ProjectUserProjectPageForm
        values={values}
        handleChange={handleChange}
        theme={theme}
      />

      <WizardFooter>
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: Support Team"
        />
      </WizardFooter>
    </div>
  );
}

UserProjectPage.propTypes = {
  submitForm: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  theme: themePropTypes.isRequired,
};
