import { useEffect, useCallback, useMemo } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as Yup from 'yup';
import shortId from 'shortid';
import get from 'lodash/get';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import AcademyCourseStep from './AcademyCourseStep';
import {
  loadStepById,
  setProcessAttributes,
  createSelectStepDefinition,
} from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import createArrayValidation from '../../../../helpers/array-validation';
import { findByValue } from '../../../../helpers/find';

const fields = [
  {
    name: 'academyCourse',
    order: 1,
  },
];

function transformError(error, value, index) {
  return new Yup.ValidationError(
    'Academy course is required',
    value,
    `academyCourse${index}`,
  );
}

function useHooksAsProps(props) {
  const {
    setFieldValue,
    card: { stepDefinitionId },
    values: { academyCourses = [] },
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  const stepDefinition = useSelector(
    createSelectStepDefinition(stepDefinitionId),
  );

  const academyCourseFieldOrder = fields.find((f) => f.name === 'academyCourse')
    .order;
  const academyCourseOptions =
    stepDefinition && stepDefinition.attributes
      ? mapToOptions(
          'id',
          'name',
        )(
          stepDefinition.attributes.find(
            (s) => s.attributeOrder === academyCourseFieldOrder,
          ).options,
        )
      : [];

  const selectedAcademyCourses = useMemo(() => {
    return academyCourses
      .filter((course) => course.academyCourse)
      .map((course) => ({
        academyCourse: get(
          findByValue(academyCourseOptions, course.academyCourse),
          'label',
          '',
        ),
      }));
  }, [academyCourseOptions, academyCourses]);

  const handleAddCourse = useCallback(() => {
    setFieldValue('academyCourses', [
      ...academyCourses,
      { academyCourse: null, key: shortId.generate() },
    ]);
  }, [academyCourses, setFieldValue]);

  const handleRemoveCourse = useCallback(
    (index) => {
      setFieldValue('academyCourses', [
        ...academyCourses.slice(0, index),
        ...academyCourses.slice(index + 1, academyCourses.length),
      ]);
    },
    [academyCourses, setFieldValue],
  );

  const handleAcademyCourseChange = (index, { value }) => {
    const item = academyCourses[index];

    setFieldValue('academyCourses', [
      ...academyCourses.slice(0, index),
      { ...item, academyCourse: value },
      ...academyCourses.slice(index + 1, academyCourses.length),
    ]);
  };

  return {
    academyCourseOptions,
    handleAcademyCourseChange,
    handleAddCourse,
    handleRemoveCourse,
    selectedAcademyCourses,
  };
}

function mapPropsToValues(props) {
  const {
    card: { attributes },
  } = props;

  if (attributes && attributes.length) {
    return {
      academyCourses: [
        {
          academyCourse: parseInt(
            get(
              attributes.find((att) => att.attributeOrder === 1),
              'value',
              '',
            ),
            10,
          ),
          key: shortId.generate(),
        },
      ],
    };
  }

  return {
    academyCourses: [{ academyCourse: null, key: shortId.generate() }],
  };
}

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  // const subSteps =
  //   values && values.academyCourses
  //     ? values.academyCourses.map((course) => ({
  //         attributes: [
  //           {
  //             name: 'Academy',
  //             value: course.academyCourse,
  //             attributeOrder: 1,
  //           },
  //         ],
  //       }))
  //     : null;

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      // subSteps,
      attributes: [
        {
          name: 'Academy',
          value: get(values, 'academyCourses[0].academyCourse', null),
          attributeOrder: 1,
        },
      ],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

const validateArray = createArrayValidation(
  {
    academyCourse: Yup.number().required('Academy course is required'),
  },
  transformError,
);

const validationSchema = Yup.object()
  .strict()
  .shape({
    academyCourses: Yup.array().test('academyCourses', '', validateArray),
  });

export default compose(
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(AcademyCourseStep);
