import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import Modal from '../../../../../components/Modal';
import ActionFooter from '../../../../../components/ActionFooter';
import { Container, Footer } from './styles';

export default function ConfirmationModal(props) {
  const {
    // Modal
    modalProps: { open, onExited },
    modalControls: { closeModal, modalData },
  } = props;

  const title = useMemo(
    () =>
      ({
        stage: 'Stage',
        reject: 'Reject',
        complete: 'Complete',
      }[modalData && modalData.operation] || 'Stage'),
    [modalData],
  );

  const nextStatus = useMemo(
    () =>
      ({
        stage: 'staged',
        reject: 'rejected',
        complete: 'screened',
      }[modalData && modalData.operation] || 'staged'),
    [modalData],
  );

  const actionButtons = [
    {
      type: 'secondary',
      text: 'Cancel',
      onClick: closeModal,
    },
    {
      type: 'primary',
      text: 'Proceed',
      onClick: closeModal,
    },
  ];

  return (
    <Modal open={open} onExited={onExited} onClose={closeModal} title={title}>
      <Container>
        By clicking on Proceed this user will be moved to the {nextStatus}
        user status.
        <br />
        <br />
        Are you sure you want to proceed?
      </Container>
      <Footer>
        <ActionFooter hideDivider rightButtons={actionButtons} />
      </Footer>
    </Modal>
  );
}

ConfirmationModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
    modalData: PropTypes.shape({
      operation: PropTypes.string,
    }),
  }).isRequired,
};
