import { connect } from 'react-redux';
import Recruitment from './Recruitment';
import { selectAll as selectAllHiringTargets } from '../../../../store/ducks/hiringTargets';
import { selectProject } from '../../../../store/ducks/project';

function mapStateToProps(state) {
  const hiringTargets = selectAllHiringTargets(state);
  const { status } = selectProject(state);

  const hasStatistics = ['ENABLED', 'DISABLED'].includes(status);

  let averageProgress = 0;

  if (hasStatistics) {
    let totalTarget = 0;
    let totalActive = 0;

    hiringTargets.forEach((hiringTarget) => {
      totalTarget += hiringTarget.target;
      totalActive += hiringTarget.metrics.active;
    });

    averageProgress = totalActive / (totalTarget || 1);
  }

  return {
    averageProgress,
    hasStatistics,
  };
}

export default connect(mapStateToProps)(Recruitment);
