import React from 'react';
import PropTypes from 'prop-types';

import { ThemeProvider } from 'styled-components';

export const theme = {
  colors: {
    azure: '#0091ff',
    blueGrey: '#848899',
    charcoal: '#2f3838',
    error: '#ba4745',
    errorLight: '#F8ECEC',
    green: '#339b5e',
    lightGreen: '#EBF5EF',
    warning: '#FACE70',
    lightBlueGrey: '#c7cad4',
    lightGreyBlue: '#a6a9b5',
    lightPeriwinkle: '#dfe0e6',
    paleGrey: '#fafafc',
    white: '#ffffff',
    lightYellow: '#FFFAF1',
  },
  sizes: {
    h1: '1.625rem',
    h2: '1.125rem',
    p: '0.9375rem',
  },
};

theme.buttonTypes = {
  primary: {
    backgroundColor: theme.colors.azure,
    color: theme.colors.white,
    borderRadius: '4px',
    border: `solid 1px ${theme.colors.azure}`,
  },
  secondary: {
    backgroundColor: theme.colors.white,
    borderRadius: '4px',
    border: `solid 1px ${theme.colors.azure}`,
    padding: '8px 16px',
    color: theme.colors.azure,
  },
};

const Theme = ({ children }) =>
  React.createElement(ThemeProvider, { theme }, children);

export default Theme;

Theme.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
};

export const themePropTypes = PropTypes.shape({
  colors: PropTypes.objectOf(PropTypes.string).isRequired,
  sizes: PropTypes.objectOf(PropTypes.string).isRequired,
  buttonTypes: PropTypes.objectOf(
    PropTypes.objectOf(
      PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    ),
  ),
});
