import { put, takeEvery, call, select } from 'redux-saga/effects';
import orderBy from 'lodash/orderBy';
import {
  setProcessModules,
  PROCESS_TYPE,
  setProcessLoading,
  setProcessError,
  openProcessBuilder,
  requestOpenProcessBuilder,
  setAffectedProjects,
  setAffectedProjectsError,
  setProcessDetails,
  isProcessModulePristine,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import { createDTOToProcessModuleMapper } from './helpers';

/**
 * type OpenProcessBuilderAction = {
 *   payload: {
 *     processType: PROCESS_TYPE,
 *     processID?: number,
 *     copyFromProcessID?: number,
 *   }
 * }
 */
function* fetchProcessSaga(action /* : OpenProcessBuilderAction */) {
  const {
    payload: {
      processType,
      processID,
      copyFromProcessID,
      processName,
      processDescription,
    },
    meta,
  } = action;

  if (meta && meta.preventFetch) {
    return;
  }

  if (!processID && !copyFromProcessID) {
    // Starting an empty project
    yield put(
      openProcessBuilder({
        processType,
      }),
    );

    return;
  }

  const isPristine = yield select(isProcessModulePristine);

  if (!isPristine) {
    return;
  }

  const endpoint =
    processType === PROCESS_TYPE.REGISTRATION
      ? endpoints.V2.REGISTRATION_PROCESSES({
          pathVariables: {
            id: processID || copyFromProcessID,
          },
        })
      : endpoints.V2.QUALIFICATION_PROCESSES({
          pathVariables: {
            id: processID || copyFromProcessID,
          },
        });

  const isCopying = !processID && !!copyFromProcessID;

  try {
    const response = yield call(axios.get, endpoint);

    if (!isCopying) {
      const affectedProjectsEndpoint =
        processType === PROCESS_TYPE.REGISTRATION
          ? endpoints.V2.REGISTRATION_PROCESS_ASSOCIATED_PROJECTS({
              pathVariables: {
                id: processID || copyFromProcessID,
              },
            })
          : endpoints.V2.QUALIFICATION_PROCESS_ASSOCIATED_PROJECTS({
              pathVariables: {
                id: processID || copyFromProcessID,
              },
            });

      try {
        const { data: affectedProjects } = yield call(
          axios.get,
          affectedProjectsEndpoint,
        );

        yield put(setAffectedProjects(affectedProjects));
      } catch (error) {
        yield put(setAffectedProjectsError(error));
        throw error;
      }
    }

    yield put(
      openProcessBuilder({
        processType,
        processName: processName || response.data.name,
        processDescription: processDescription || response.data.description,
        processID: isCopying ? null : processID,
        copyFromProcessID: isCopying ? copyFromProcessID : null,
        instantiated: response.data.instantiated,
      }),
    );

    yield put(
      setProcessModules(
        orderBy(response.data.processSteps || [], 'order').map(
          createDTOToProcessModuleMapper(),
        ),
        { isPristine: true },
      ),
    );
    yield put(setProcessLoading(false));
  } catch (error) {
    yield put(setProcessError(error));
  }
}

export default takeEvery(
  [requestOpenProcessBuilder.type, setProcessDetails.type],
  fetchProcessSaga,
);
