export const productiveMetricsMap = (rateType) => {
  if (rateType === 'PIECERATE') {
    return [
      {
        ...PRODUCTIVITY_UNITS_WEEK_METRIC,
        disabled: false,
        required: true,
        isCollapsed: false,
      },
      {
        ...PRODUCTIVITY_UNITS_WORK_HOUR_METRIC,
        disabled: false,
        required: true,
        isCollapsed: false,
      },
      PRODUCTIVITY_HOURS_WEEK_METRIC,
      PRODUCTIVITY_DAYS_WEEK_METRIC,
    ];
  }
  return [
    {
      ...PRODUCTIVITY_HOURS_WEEK_METRIC,
      disabled: false,
      required: true,
      isCollapsed: false,
    },
    PRODUCTIVITY_UNITS_WEEK_METRIC,
    PRODUCTIVITY_UNITS_WORK_HOUR_METRIC,
    PRODUCTIVITY_DAYS_WEEK_METRIC,
  ];
};

export function createAgregationList(hiringTargets, agregationType) {
  const aux = {};
  switch (agregationType) {
    case 'DIAL_LG_COUNTRY':
      hiringTargets.forEach((item) => {
        const market = `${item.localeLanguage}-${item.localeDialect}-${item.restrictToCountry}`.toUpperCase();

        aux[market] = {
          market,
          goalValue: 0,
        };
      });

      return _.sortBy(_.values(aux), ['market']);

    case 'LANGUAGE_COUNTRY':
      hiringTargets.forEach((item) => {
        const market = `${item.localeLanguage}-${item.restrictToCountry}`.toUpperCase();

        aux[market] = {
          market,
          goalValue: 0,
        };
      });

      return _.sortBy(_.values(aux), ['market']);

    case 'LANGUAGE':
      hiringTargets.forEach((item) => {
        const market = `${item.localeLanguage}`.toUpperCase();

        aux[market] = {
          market,
          goalValue: 0,
        };
      });

      return _.sortBy(_.values(aux), ['market']);

    case 'COUNTRY':
      hiringTargets.forEach((item) => {
        const market = `${item.restrictToCountry}`.toUpperCase();

        aux[market] = {
          market,
          goalValue: 0,
        };
      });

      return _.sortBy(_.values(aux), ['market']);

    default:
      hiringTargets.forEach((item) => {
        const market = `${item.localeLanguage}-${item.localeDialect}-${item.restrictToCountry}`.toUpperCase();

        aux[market] = {
          market,
          goalValue: 0,
        };
      });

      return _.sortBy(_.values(aux), ['market']);
  }
}

