import { call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction, addOne } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

export function* createHiringTargetSaga(action) {
  const {
    payload,
    meta: { onSuccess, onError },
  } = action;

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can create a Hiring Target.');
    return;
  }

  try {
    const { data: hiringTarget } = yield call(
      axios.post,
      ENDPOINTS.V2.HIRING_TARGETS(),
      {
        id: null,
        language: payload.localeLanguage,
        languageCountry: payload.localeDialect,
        languageTo: payload.localeLanguageTo,
        languageCountryTo: payload.localeDialectTo,
        restrictToCountry: payload.restrictToCountry,
        ownerId: payload.assignedOwner,
        deadline: payload.hiringDeadline,
        target: payload.target,
        projectId: payload.projectId || project.id,
      },
    );

    yield put(
      updateOne(
        {
          id: payload.id,
          changes: { id: hiringTarget.id },
        },
        { bypassUpdateSaga: true },
      ),
    );

    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error creating this Hiring Target. Please try again.',
    );

    yield put(undoLastAction());
  }
}

export default takeEvery(addOne.type, createHiringTargetSaga);
