import { useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as Yup from 'yup';
import get from 'lodash/get';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';
import SignDocumentStep from './SignDocumentStep';
import {
  loadStepById,
  setProcessAttributes,
  createSelectStepDefinition,
} from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import { draftToHTML, HTMLToDraft } from '../../../RichTextEditor';

const fields = [
  {
    name: 'documentToSign',
    order: 1,
  },
];

function useHooksAsProps(props) {
  const {
    card: { stepDefinitionId },
  } = props;

  const dispatch = useDispatch();

  const fetchStepDefinition = useCallback(() => {
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  useEffect(() => {
    fetchStepDefinition();
  }, [fetchStepDefinition]);

  const stepDefinition = useSelector(
    createSelectStepDefinition(stepDefinitionId),
  );

  const documentsFieldOrder = fields.find((f) => f.name === 'documentToSign')
    .order;
  const documentOptions =
    stepDefinition && stepDefinition.attributes
      ? mapToOptions(
          'id',
          'name',
        )(
          stepDefinition.attributes.find(
            (s) => s.attributeOrder === documentsFieldOrder,
          ).options,
        )
      : [];

  return {
    documentOptions,
    fetchStepDefinition,
  };
}

function mapPropsToValues(props) {
  const {
    card: { attributes = [] },
  } = props;

  const document = get(attributes, '0.additionalAttributes', null);

  if (document) {
    return {
      isNewDocument: 1,
      documentTitle: document.documentTitle,
      documentBody: HTMLToDraft(document.documentBody),
      required: document.required,
      hasInternalNotes: document.hasInternalNotes,
      internalNotes: document.internalNotes,
    };
  }

  return {
    isNewDocument: 0,
    documentToSign: attributes[0] ? parseInt(attributes[0].value, 10) : null,
  };
}

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey, sequenceFlow },
    controls: { setDirty, setCollapsed },
  } = props;

  const { editingExistingDocument } = values;

  if (editingExistingDocument) {
    return;
  }

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      attributes: [
        {
          value: values.documentToSign || null,
          attributeOrder: 1,
          sequenceFlow,
          additionalAttributes: values.isNewDocument
            ? {
                documentTitle: values.documentTitle,
                documentBody: draftToHTML(values.documentBody),
                required: values.required,
                hasInternalNotes: values.hasInternalNotes,
                internalNotes: values.internalNotes,
              }
            : null,
        },
      ],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

const validationSchema = Yup.object().shape({
  documentToSign: Yup.mixed().when('isNewDocument', {
    is: 0,
    then: Yup.string()
      .nullable()
      .required(requiredMessage`Document`),
  }),
  documentTitle: Yup.mixed().when('isNewDocument', {
    is: 1,
    then: Yup.string().required(requiredMessage`Document title`),
  }),
  documentBody: Yup.mixed().when('isNewDocument', {
    is: 1,
    then: Yup.mixed().required(requiredMessage`Document content`),
  }),
  internalNotes: Yup.mixed().when('isNewDocument', {
    is: 1,
    then: Yup.mixed()
      .nullable()
      .when('hasInternalNotes', {
        is: true,
        then: Yup.string().required(requiredMessage`Internal notes`),
      }),
  }),
  changeLog: Yup.mixed().when('editingExistingDocument', {
    is: true,
    then: Yup.string().required(requiredMessage`Change log`),
  }),
});

export default compose(
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(SignDocumentStep);
