import { call, put, takeLeading, delay } from 'redux-saga/effects';
import axios from '../../../services/axios';
import ENDPOINTS from '../../../constants/endpoints';
import { resend, setReseting, setError } from '.';

function* resendPartnerDataSaga(action) {
  try {
    yield call(
      axios.post,
      ENDPOINTS.V2.RESEND_PARTNER_DATA({
        pathVariables: {
          id: action.payload,
        },
      }),
    );
    yield put(setReseting(2));

    yield delay(1000);

    yield put(setReseting(0));

    setTimeout(() => {}, 1800);
  } catch (error) {
    yield put(setError(error.message));
  }
}

export default takeLeading(resend.type, resendPartnerDataSaga);
