import styled from 'styled-components';
import { transparentize, darken } from 'polished';

const Base = styled.button((props) => {
  const {
    theme: { sizes, colors, buttonTypes },
    fontWeight,
    styleType,
    className,
    disabled,
  } = props;

  const sharedStyle = {
    fontFamily: 'Lato, sans-serif',
    fontSize: sizes.p,
    fontWeight: fontWeight || '600',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: 1.33,
    letterSpacing: 'normal',
    textAlign: 'center',
    padding: '8px 24px',
    borderRadius: '4px',
    backgroundColor: colors.azure,
    color: colors.white,
    marginLeft: `${props.marginLeft || 0}px`,
    marginRight: `${props.marginRight || 0}px`,
    cursor: 'pointer',
    width: props.width ? `${props.width}px` : 'auto',
  };

  if (disabled) {
    sharedStyle.opacity = 0.6;
    sharedStyle.cursor = 'initial';
  }

  if (styleType) {
    return {
      ...sharedStyle,
      ...(buttonTypes[styleType] || {}),
    };
  }
  if (className && className.length > 0) {
    const classNames = className.split(' ');
    const buttonType = classNames.find((c) => buttonTypes[c]);

    return {
      ...sharedStyle,
      ...(buttonType || {}),
    };
  }
  return sharedStyle;
});

export default styled(Base)`
  transition: background 160ms ease;

  &:hover {
    background-color: ${(props) =>
      props.styleType === 'primary'
        ? transparentize(0.15, props.theme.colors.azure)
        : darken(0.05, props.theme.colors.white)};
  }

  &:active {
    background-color: ${(props) =>
      props.styleType === 'primary'
        ? darken(0.05, props.theme.colors.azure)
        : darken(0.15, props.theme.colors.white)};
  }

  &:disabled {
    pointer-events: none;
  }

  ${(props) =>
    props.responsive &&
    `@media only screen and (max-width: 768px) {
    font-size: 40px;
  }`}
`;
