function validateIPv4(input) {
  const ipV4RegexPattern =
    '((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)';
  const ipV4CIDRRegexPattern = '(/(3[0-2]|2[0-9]|1[0-9]|[0-9]))?';

  return !!input.match(`^${ipV4RegexPattern}${ipV4CIDRRegexPattern}$`);
}

function validateIPv6(input) {
  const ipV6RegexPattern = [
    '([0-9a-fA-F]{1,4}:){7,7}[0-9a-fA-F]{1,4}|', // 1:2:3:4:5:6:7:8
    '([0-9a-fA-F]{1,4}:){1,7}:|', // 1:: and 1:2:3:4:5:6:7::
    '([0-9a-fA-F]{1,4}:){1,6}:[0-9a-fA-F]{1,4}|', // 1::8 and 1:2:3:4:5:6::8 and 1:2:3:4:5:6::8
    '([0-9a-fA-F]{1,4}:){1,5}(:[0-9a-fA-F]{1,4}){1,2}|', // 1::7:8 and 1:2:3:4:5::7:8 and 1:2:3:4:5::8
    '([0-9a-fA-F]{1,4}:){1,4}(:[0-9a-fA-F]{1,4}){1,3}|', // 1::6:7:8 and 1:2:3:4::6:7:8 and 1:2:3:4::8
    '([0-9a-fA-F]{1,4}:){1,3}(:[0-9a-fA-F]{1,4}){1,4}|', // 1::5:6:7:8 and 1:2:3::5:6:7:8 and 1:2:3::8
    '([0-9a-fA-F]{1,4}:){1,2}(:[0-9a-fA-F]{1,4}){1,5}|', // 1::4:5:6:7:8 and 1:2::4:5:6:7:8 and 1:2::8
    '[0-9a-fA-F]{1,4}:((:[0-9a-fA-F]{1,4}){1,6})|', // 1::3:4:5:6:7:8 and 1::3:4:5:6:7:8 and 1::8
    ':((:[0-9a-fA-F]{1,4}){1,7}|:)|', // ::2:3:4:5:6:7:8  ::2:3:4:5:6:7:8 ::8 and ::
    'fe80:(:[0-9a-fA-F]{0,4}){0,4}%[0-9a-zA-Z]{1,}|', // fe80::7:8%eth0 and fe80::7:8%1
    '::(ffff(:0{1,4}){0,1}:){0,1}',
    '((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\\.){3,3}',
    '(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])|', // ::255.255.255.255 and ::ffff:255.255.255.255 and ::ffff:0:255.255.255.255 (IPv4-mapped)
    '([0-9a-fA-F]{1,4}:){1,4}:',
    '((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\\.){3,3}',
    '(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])', // 2001:db8:3:4::192.0.2.33 and 64:ff9b::192.0.2.33 (IPv4-Embedded)
  ].join('');
  const ipV6CIDRRegexPattern = '(/(12[0-8]|1[01][0-9]|[1-9]?[0-9]))?';

  return !!input.match(`^${ipV6RegexPattern}${ipV6CIDRRegexPattern}$`);
}

export default function ipValidation(input) {
  return validateIPv4(`${input}`) || validateIPv6(`${input}`);
}
