import React from 'react';
import PropTypes from 'prop-types';
import StatusDistribution from './StatusDistribution';
import { QualificationsGraphContainer } from './styles';
import DropRate from './DropRate';
import { PROCESS_TYPE } from '../../store/ducks/processBuilder';
import { DropRateStepPropTypes } from './DropRate/DropRate';

export default function QualificationsGraph(props) {
  const {
    processType = PROCESS_TYPE.REGISTRATION,
    total = 1,
    inProgress = 0,
    registered = 0,
    rejected = 0,
    abandoned = 0,
    steps = [],
  } = props;

  return (
    <QualificationsGraphContainer>
      <StatusDistribution
        processType={processType}
        total={total}
        inProgress={inProgress}
        registered={registered}
        rejected={rejected}
        abandoned={abandoned}
      />

      <DropRate total={total} steps={steps} />
    </QualificationsGraphContainer>
  );
}

QualificationsGraph.propTypes = {
  processType: PropTypes.oneOf(Object.values(PROCESS_TYPE)).isRequired,
  total: PropTypes.number.isRequired,
  inProgress: PropTypes.number.isRequired,
  registered: PropTypes.number.isRequired,
  rejected: PropTypes.number.isRequired,
  abandoned: PropTypes.number.isRequired,
  steps: PropTypes.arrayOf(DropRateStepPropTypes).isRequired,
};
