import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { capitalize, get } from 'lodash';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import { ENTITIES, selectors } from '../../store/ducks/options';
import ProgressBar from '../ProgressBar';
import { IP_CHECK, MALICIOUSNESS, DUPLICATES } from './helpers/checkFrameTypes';
import IpCheckModal from './Modals/IpCheck';
import MaliciousnessModal from './Modals/Maliciousness';
import DuplicatesModal from './Modals/Duplicates';
import { Label } from '../../styles';
import { findByValue } from '../../helpers/find';
import {
  Frame,
  Text,
  IpTag,
  MaliciousTag,
  ProgressBarWrapper,
  Review,
  ShimmerSpacer,
} from './styles';

export default function CheckFrame(props) {
  const {
    duplicates: duplicatesData,
    ipCheck: ipCheckData,
    maliciousness: maliciousnessData,
    originalData,
    loading,
    type,
    modalProps,
    modalControls,
    handleOpenModal,
  } = props;

  const ipQuality = get(ipCheckData, 'ip_quality', {});

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const detectedCountry =
    useMemo(
      () => get(findByValue(countries, ipQuality.country_code2), 'label', ''),
      [countries, ipQuality.country_code2],
    ) || '-';

  const detectedCity = ipQuality.city_name || '';
  const detectedRegion =
    detectedCity && detectedCountry
      ? `${detectedCity}, ${detectedCountry}`
      : detectedCountry;

  const ipTag = useMemo(
    () => ([true, undefined].includes(ipQuality.failed) ? 'Failed' : 'Passed'),
    [ipQuality],
  );

  const suspiciousCounts = get(
    maliciousnessData,
    'suspicious_trigger_counts',
    0,
  );

  const maliciousnessLevel = capitalize(
    get(maliciousnessData, 'maliciousness_level', 'low'),
  );

  const signalsText = useMemo(
    () => (suspiciousCounts > 1 ? 'signals' : 'signal'),
    [suspiciousCounts],
  );

  const similarityScore = useMemo(
    () => get(duplicatesData, 'similarity_score', 0) * 100,
    [duplicatesData],
  );

  const total = get(duplicatesData, 'total', 0);

  const totalDuplicates = useMemo(() => {
    if (total === 0) {
      return 'No duplicates found';
    }

    return total > 1 ? `${total} duplicates found` : `${total} duplicate found`;
  }, [total]);

  const renderLoadingState = (title) => {
    return (
      <>
        <Frame>
          <Label>{title}</Label>
          <Shimmer width={120} height={16} />
          <ShimmerSpacer />
          <Shimmer width={50} height={16} />
          <ShimmerSpacer />
          <Review>Review</Review>
        </Frame>
      </>
    );
  };

  const renderIpCheck = () => {
    if (loading) {
      return renderLoadingState('IP Check');
    }

    return (
      <>
        <Frame>
          <Label>IP check</Label>
          <Text>{detectedRegion || 'No data found'}</Text>
          <IpTag failed={ipQuality.failed}>{ipTag}</IpTag>
          <Review onClick={handleOpenModal}>Review</Review>
        </Frame>
        <IpCheckModal
          modalProps={modalProps}
          modalControls={modalControls}
          data={ipCheckData}
        />
      </>
    );
  };

  const renderMaliciousness = () => {
    if (loading) {
      return renderLoadingState('Maliciousness');
    }

    return (
      <>
        <Frame>
          <Label>Maliciousness</Label>
          <Text>
            {suspiciousCounts} {signalsText} found
          </Text>
          <MaliciousTag level={maliciousnessLevel}>
            {maliciousnessLevel}
          </MaliciousTag>
          <Review onClick={handleOpenModal}>Review</Review>
        </Frame>
        <MaliciousnessModal
          modalProps={modalProps}
          modalControls={modalControls}
          data={maliciousnessData}
        />
      </>
    );
  };

  const renderDuplicates = () => {
    if (loading) {
      return renderLoadingState('Duplicates');
    }

    return (
      <>
        <Frame width={200}>
          <Label>Duplicates</Label>
          <Text>{totalDuplicates}</Text>
          <ProgressBarWrapper>
            <ProgressBar
              progressshowAsNumeric
              progressPercentage={similarityScore}
              progressColor={'#BA4745'}
              width={'80%'}
            />
          </ProgressBarWrapper>
          <Review onClick={handleOpenModal}>Review</Review>
        </Frame>
        <DuplicatesModal
          modalProps={modalProps}
          modalControls={modalControls}
          data={originalData}
        />
      </>
    );
  };

  const ipCheck = renderIpCheck();
  const maliciousness = renderMaliciousness();
  const duplicates = renderDuplicates();

  return { ipCheck, maliciousness, duplicates }[type] || <></>;
}

CheckFrame.propTypes = {
  duplicates: PropTypes.objectOf(PropTypes.any),
  ipCheck: PropTypes.objectOf(PropTypes.any),
  maliciousness: PropTypes.objectOf(PropTypes.any),
  originalData: PropTypes.objectOf(PropTypes.any),
  loading: PropTypes.bool.isRequired,
  type: PropTypes.oneOf([IP_CHECK, MALICIOUSNESS, DUPLICATES]).isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  handleOpenModal: PropTypes.func.isRequired,
};

CheckFrame.defaultProps = {
  duplicates: {},
  ipCheck: {},
  maliciousness: {},
  originalData: {},
};
