import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { useModal } from '../../Modal';

const PreviewContext = React.createContext({});

export function PreviewProvider(props) {
  const { startOpen = false, initialData = {}, children } = props;
  const [modalProps, modalControls] = useModal(startOpen, initialData);

  return (
    <PreviewContext.Provider
      value={{
        modalProps,
        modalControls,
      }}
    >
      {children}
    </PreviewContext.Provider>
  );
}

export const PreviewConsumer = PreviewContext.Consumer;

export function usePreviewContext() {
  return useContext(PreviewContext);
}

export function usePreviewModalControls() {
  return get(usePreviewContext(), 'modalControls', {});
}

export function usePreviewModalProps() {
  return get(usePreviewContext(), 'modalProps', {});
}

PreviewProvider.propTypes = {
  startOpen: PropTypes.bool,
  // eslint-disable-next-line react/forbid-prop-types
  initialData: PropTypes.any,
  children: PropTypes.node.isRequired,
};

PreviewProvider.defaultProps = {
  startOpen: false,
  initialData: {},
};
