import { useMemo, useCallback, useState } from 'react';
import Paginator from 'paginator';
import shortid from 'shortid';
import withHooks from 'hook-hoc';
import PropTypes from 'prop-types';
import { setPropTypes, compose } from 'recompose';
import { getButtonStatus } from './helpers';
import Pagination from './Pagination';
import { BUTTON_TYPES } from './styles';

const BUTTONS_TO_SHOW = 5;

export function usePagination({ defaultCurrentPage, defaultPageSize } = {}) {
  const [currentPage, setCurrentPage] = useState(defaultCurrentPage || 1);
  const [pageSize, setPageSize] = useState(defaultPageSize || 20);

  return {
    currentPage,
    setCurrentPage,
    pageSize,
    setPageSize: useCallback((nextPageSize) => {
      setPageSize(nextPageSize);
      setCurrentPage(1);
    }, []),
  };
}

function useHooksAsProps(outerProps) {
  const {
    current,
    pageSize,
    total,
    onChange,
    onPageSizeChange,

    hideDisabled,
    hideGoToButtons,
    hideGoToAdjacentButtons,
    hideGoToNextPage,
    hideGoToPrevPage,
    hideGoToBorderButtons,
    hideGoToFirstPage,
    hideGoToLastPage,
  } = outerProps;

  const [viewOffset, setViewOffset] = useState(0);

  const handleChange = useCallback(
    (page) => {
      if (typeof onChange === 'function') {
        onChange(page);
      }

      setViewOffset(0);
    },
    [onChange],
  );

  const handlePageSizeChange = useCallback(
    ({ value }) => {
      if (typeof onPageSizeChange === 'function') {
        onPageSizeChange(value);
      }

      setViewOffset(0);
    },
    [onPageSizeChange],
  );

  const [pages, firstResult, lastResult] = useMemo(() => {
    const paginationInfo = new Paginator(pageSize, BUTTONS_TO_SHOW).build(
      total,
      current + viewOffset,
    );

    const buttons = new Array(BUTTONS_TO_SHOW)
      .fill(paginationInfo.first_page)
      .map((firstPage, index) => firstPage + index)
      .filter((page) => page <= paginationInfo.last_page)
      .map((page) => ({
        type: BUTTON_TYPES.number,
        key: shortid.generate(),
        label: `${page}`,
        active: current === page,
        disabled: false,
        onChange: () => {
          handleChange(page);
        },
      }));

    const showLeftSeparator =
      buttons.length && !buttons.find((button) => button.label === '1');

    const showRightSeparator =
      buttons.length &&
      !buttons.find(
        (button) => button.label === `${paginationInfo.total_pages}`,
      );

    const buttonStatus = getButtonStatus({
      hasPrevPage: paginationInfo.has_previous_page,
      hasNextPage: paginationInfo.has_next_page,
      hideDisabled,
      hideGoToAdjacentButtons,
      hideGoToBorderButtons,
      hideGoToButtons,
      hideGoToFirstPage: hideGoToFirstPage || !showLeftSeparator,
      hideGoToLastPage: hideGoToLastPage || !showRightSeparator,
      hideGoToNextPage,
      hideGoToPrevPage,
    });

    if (showLeftSeparator && !buttons.find((button) => button.label === '2')) {
      buttons.unshift({
        type: BUTTON_TYPES.separator,
        key: shortid.generate(),
        onClick: () => {
          setViewOffset((offset) => offset - BUTTONS_TO_SHOW);
        },
      });
    }

    if (buttonStatus.firstPageVisible || !buttons.length) {
      buttons.unshift({
        type: BUTTON_TYPES.firstPage,
        key: BUTTON_TYPES.firstPage,
        label: '1',
        number: 1,
        disabled: !paginationInfo.has_previous_page,
        onChange: () => {
          handleChange(1);
        },
      });
    }

    if (buttonStatus.prevPageVisible) {
      buttons.unshift({
        type: BUTTON_TYPES.prevPage,
        key: BUTTON_TYPES.prevPage,
        disabled: !paginationInfo.has_previous_page,
        onChange: () => {
          handleChange(paginationInfo.previous_page);
        },
      });
    }
    if (
      showRightSeparator &&
      !buttons.find(
        (button) => button.label === `${paginationInfo.total_pages - 1}`,
      )
    ) {
      buttons.push({
        type: BUTTON_TYPES.separator,
        key: shortid.generate(),
        onClick: () => {
          setViewOffset((offset) => offset + BUTTONS_TO_SHOW);
        },
      });
    }

    if (buttonStatus.lastPageVisible) {
      buttons.push({
        type: BUTTON_TYPES.lastPage,
        key: BUTTON_TYPES.lastPage,
        label: `${paginationInfo.total_pages}`,
        number: paginationInfo.total_pages,
        disabled: paginationInfo.current_page === paginationInfo.total_pages,
        onChange: () => {
          handleChange(paginationInfo.total_pages);
        },
      });
    }

    if (buttonStatus.nextPageVisible) {
      buttons.push({
        type: BUTTON_TYPES.nextPage,
        key: BUTTON_TYPES.nextPage,
        disabled: !paginationInfo.has_next_page,
        onChange: () => {
          handleChange(paginationInfo.next_page);
        },
      });
    }

    return [buttons, paginationInfo.first_result, paginationInfo.last_result];
  }, [
    pageSize,
    total,
    current,
    viewOffset,
    hideDisabled,
    hideGoToAdjacentButtons,
    hideGoToBorderButtons,
    hideGoToButtons,
    hideGoToFirstPage,
    hideGoToLastPage,
    hideGoToNextPage,
    hideGoToPrevPage,
    handleChange,
  ]);

  return {
    pages,
    firstResult,
    lastResult,
    handlePageSizeChange,
  };
}

export default compose(
  // This prop-types are the outer-props the user must/can provide
  setPropTypes({
    current: PropTypes.number.isRequired,
    pageSize: PropTypes.number.isRequired,
    total: PropTypes.number.isRequired,
    onChange: PropTypes.func,
    onPageSizeChange: PropTypes.func,

    hideDisabled: PropTypes.bool,
    hideGoToButtons: PropTypes.bool,
    hideGoToAdjacentButtons: PropTypes.bool,
    hideGoToNextPage: PropTypes.bool,
    hideGoToPrevPage: PropTypes.bool,
    hideGoToBorderButtons: PropTypes.bool,
    hideGoToFirstPage: PropTypes.bool,
    hideGoToLastPage: PropTypes.bool,
    hideAccessoryRight: PropTypes.bool,
  }),
  withHooks(useHooksAsProps),
)(Pagination);
