export default function getNormalizedLabel(questionType) {
  switch (questionType) {
    case 'CHECKBOX_GROUP':
      return 'Checkbox Group';
    case 'MULTIPLE_CHOICE':
      return 'Multiple Choice';
    case 'OPEN_TEXT':
      return 'Open Text';
    case 'OPEN_TEXT_PARAGRAPH':
      return 'Open Text Paragraph';
    case 'YES_NO':
      return 'Yes/No';
    case 'DROPDOWN_SELECTION':
    case 'LANGUAGE_LIST':
    case 'COUNTRY_LIST':
      return 'Dropdown Selection';
    case 'DATE_PICKER':
      return 'Date Picker';
    default:
      return null;
  }
}
