import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import ActionFooter from '../../../../../components/ActionFooter';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../../components/ErrorPopover';
import InvoicesAndPaymentFormBase from '../../../../../components/ProjectForms/InvoiceAndPaymentForm';
import {
  selectSaving,
  selectProject,
} from '../../../../../store/ducks/project';
import { setDirty, setMode } from '../../../../../store/ducks/projectView';
import { CardFooter, CardContentContainer } from '../../../styles';

export default function InvoicesAndPaymentForm(props) {
  const {
    dirty,
    submitForm,
    submitCount,
    showError,
    errorPopoverProps,
    errors,
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setDirty(dirty));
  }, [dirty, dispatch]);

  const cancelEditing = useCallback(() => dispatch(setMode('view')), [
    dispatch,
  ]);

  const saving = useSelector(selectSaving);

  useEffect(() => {
    if (submitCount > 0) {
      Object.values(errors).forEach((error) => {
        showError(new Error(error));
      });
    }
  }, [submitCount, errors, showError]);

  const project = useSelector(selectProject);

  return (
    <>
      <CardContentContainer>
        <InvoicesAndPaymentFormBase
          projectId={project ? project.id : undefined}
        />
      </CardContentContainer>
      <CardFooter>
        <ErrorPopover {...errorPopoverProps} />
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              text: 'Cancel',
              type: 'secondary',
              disabled: saving,
              onClick: cancelEditing,
            },
            {
              text: 'Save Changes',
              type: 'primary',
              loading: saving,
              onClick: submitForm,
            },
          ]}
        />
      </CardFooter>
    </>
  );
}

InvoicesAndPaymentForm.propTypes = {
  dirty: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  submitForm: PropTypes.func.isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  errors: PropTypes.objectOf(PropTypes.any).isRequired,
};
