import React, { useCallback, useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import SelectComponent from 'react-select';
import MessageError from '../MessageError';
import { themePropTypes } from '../../styles/themes/default';
import Option from './Option';
import Placeholder from './Placeholder';
import MultiValue from './MultiValue';
import Control from './Control';

const components = {
  IndicatorSeparator() {
    return null;
  },
  Option,
  Placeholder,
  MultiValue,
  Control,
};

function menuPortal(styles) {
  return {
    ...styles,
    zIndex: 1010, // this makes sure zIndex is bigger than Modals.
  };
}

export default function Select(props) {
  const {
    name,
    value,
    options,
    placeholder,
    onChange,
    disabled,
    error,
    classNamePrefix,
    containerStyles,
    theme,
    loading,
    errorIncreasesHeight,
    isMulti,
    hideSelectedOptions,
    closeMenuOnSelect,
    multiMaxSelectedOptions,
    multiMaxSelectedMessage,
    id,
    'data-testid': dataTestId,
    getMultiCount,
  } = props;

  const [placeholderState, setPlaceholderState] = useState(placeholder);

  const selectTheme = useCallback(
    (defaultTheme) => ({
      ...defaultTheme,
      colors: {
        ...defaultTheme.colors,
        primary: theme.colors.azure,
      },
    }),
    [theme.colors.azure],
  );

  const placeholderStyles = useCallback(
    (styles) => ({
      ...styles,
      color: theme.colors.lightGreyBlue,
      fontFamily: "'Lato', sans-serif",
      fontSize: 15,
    }),
    [theme.colors.lightGreyBlue],
  );

  const control = useCallback(
    (styles, state) => ({
      ...styles,
      border: `1px solid ${
        error ? theme.colors.error : theme.colors.lightPeriwinkle
      }`,
      color: theme.colors.charcoal,
      '&:hover': { borderColor: 'none' },
      paddingLeft: '6px',
      backgroundColor: state.isDisabled
        ? 'rgba(166, 169, 181, 0.1)'
        : styles.backgroundColor,
    }),
    [error, theme.colors],
  );

  const singleValue = useCallback(
    (styles) => ({
      ...styles,
      overflow: 'initial',
      color: theme.colors.charcoal,
    }),
    [theme.colors.charcoal],
  );

  const colourStyles = useMemo(
    () => ({
      menuPortal,
      control,
      singleValue,
      placeholder: placeholderStyles,
    }),
    [control, placeholderStyles, singleValue],
  );

  const handleChange = useCallback(
    (selectedOption) => {
      if (typeof onChange === 'function') {
        if (
          Array.isArray(selectedOption) &&
          isMulti &&
          multiMaxSelectedOptions > 0 &&
          selectedOption.length > multiMaxSelectedOptions
        ) {
          return;
        }
        onChange(selectedOption);
      }
    },
    [isMulti, multiMaxSelectedOptions, onChange],
  );

  const findInputValue = useCallback(
    (v) => {
      if (v) {
        if (v.value) {
          return v;
        }

        const option = (options || []).find(
          (o1) =>
            o1.value === v || (o1.options || []).find((o2) => o2.value === v),
        );

        if (option) {
          return option.value === v
            ? option
            : option.options.find((o) => o.value === v);
        }
      }

      return '';
    },
    [options],
  );

  const inputValue = useMemo(() => {
    if (isMulti) {
      if (Array.isArray(value)) {
        return value.map((v) => findInputValue(v));
      }

      return [];
    }

    return findInputValue(value);
  }, [findInputValue, isMulti, value]);

  const onInputChange = useCallback(
    (str) => {
      setPlaceholderState(str.length > 0 ? '' : placeholder);
    },
    [placeholder],
  );

  return (
    <>
      <SelectComponent
        name={name}
        value={inputValue}
        onChange={handleChange}
        placeholder={placeholderState}
        options={options || []}
        isDisabled={disabled}
        isSearchable
        isLoading={loading}
        components={components}
        classNamePrefix={classNamePrefix}
        styles={{ ...colourStyles, ...containerStyles }}
        theme={selectTheme}
        // Positioning
        menuPosition="fixed"
        // Multi select options
        isMulti={isMulti}
        hideSelectedOptions={isMulti ? false : hideSelectedOptions}
        closeMenuOnSelect={isMulti ? false : closeMenuOnSelect}
        multiMaxSelectedOptions={multiMaxSelectedOptions}
        multiMaxSelectedMessage={multiMaxSelectedMessage}
        input={id}
        data-testid={dataTestId}
        getMultiCount={getMultiCount}
        onInputChange={onInputChange}
      />
      <MessageError
        isVisible={!!error}
        message={error}
        increasesHeight={errorIncreasesHeight}
      />
    </>
  );
}

Select.propTypes = {
  name: PropTypes.string.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
        .isRequired,
      label: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
    }),
  ),
  onChange: PropTypes.func,
  placeholder: PropTypes.string,
  disabled: PropTypes.bool,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.arrayOf(
      PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
        PropTypes.shape({
          value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
          label: PropTypes.any,
        }),
      ]),
    ),
  ]),
  error: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
    PropTypes.instanceOf(Error),
  ]),
  classNamePrefix: PropTypes.string,
  containerStyles: PropTypes.objectOf(PropTypes.any),
  theme: themePropTypes.isRequired,
  loading: PropTypes.bool,
  errorIncreasesHeight: PropTypes.bool,
  isMulti: PropTypes.bool,
  hideSelectedOptions: PropTypes.bool,
  closeMenuOnSelect: PropTypes.bool,
  multiMaxSelectedOptions: PropTypes.number,
  multiMaxSelectedMessage: PropTypes.string,
  getMultiCount: PropTypes.func,
  id: PropTypes.string,
  'data-testid': PropTypes.string,
};

Select.defaultProps = {
  options: [],
  onChange: () => {},
  disabled: false,
  placeholder: '',
  value: '',
  error: '',
  classNamePrefix: '',
  containerStyles: {},
  loading: false,
  errorIncreasesHeight: false,
  isMulti: false,
  hideSelectedOptions: false,
  closeMenuOnSelect: true,
  multiMaxSelectedOptions: 0,
  multiMaxSelectedMessage: '',
  id: undefined,
  'data-testid': undefined,
  getMultiCount: undefined,
};
