import React, { useMemo, useCallback, useEffect, useRef } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';
import PropTypes from 'prop-types';
import Wizard from '../../components/Wizard';
import ProgressBar from '../../components/ProgressBar';
import { Container, Header, Title } from './styles';
import {
  fetchProject,
  selectLoading,
  setLoading,
  selectCompletedPercentage,
  selectProjectStatus,
  selectStepsCompletedStatus,
  selectProject,
} from '../../store/ducks/project';
import { STEPS } from '../../constants/projectSetup';
import ActiveComponent from './ActiveComponent';
import SetupComplete from './SetupComplete';
import { updateParentHeight } from '../../services/postMessage';
import Loading from './Loading';
import { selectCurrentStep, navigate } from '../../store/ducks/projectSetup';
import legacyIntegration from '../../helpers/legacy-integration';
import { useClone } from './helpers/cloneContext';

const blockedStatus = ['ENABLED', 'DISABLED'];

export default function ProjectSetup(props) {
  const project = useSelector(selectProject);
  const projectStatus = useSelector(selectProjectStatus);
  const projectLoading = useSelector(selectLoading);
  const completePercent = useSelector(selectCompletedPercentage);
  const stepsCompletedStatus = useSelector(selectStepsCompletedStatus);
  const currentStep = useSelector(selectCurrentStep);

  const { cloneProject } = props;

  const { setIsCloning, isCloning } = useClone();

  const projectCompleted = useMemo(
    () => projectStatus === 'READY' && !isCloning,
    [projectStatus, isCloning],
  );

  const sidebarSteps = useMemo(
    () =>
      STEPS.map((s, i) =>
        s.enabled
          ? {
              ...s,
              complete: stepsCompletedStatus[i],
              loading: projectLoading,
            }
          : null,
      ).filter((step) => step),
    [projectLoading, stepsCompletedStatus],
  );

  const dispatch = useDispatch();
  const match = useRouteMatch();

  useEffect(() => {
    const {
      params: { projectID },
    } = match;

    if (projectID) {
      dispatch(fetchProject({ projectId: match.params.projectID }));
    } else {
      dispatch(setLoading({ loading: false }));
    }
  }, [dispatch, match, match.params, match.params.projectID]);

  useEffect(() => {
    if (project && blockedStatus.includes(projectStatus) && !cloneProject) {
      legacyIntegration.informViewProjectList();
    }
  });

  useEffect(() => (setIsCloning ? setIsCloning(cloneProject) : false), [
    cloneProject,
    setIsCloning,
  ]);

  const navigateFn = useCallback(
    (toStep = 0) => {
      dispatch(navigate(toStep));
    },
    [dispatch],
  );

  const navigationRecoveryHandled = useRef(false);

  const lastEditableStep = useMemo(() => {
    const index = Object.values(stepsCompletedStatus).lastIndexOf(true);
    return index > 0 ? index : 0;
  }, [stepsCompletedStatus]);

  useEffect(() => {
    if (!projectLoading && !navigationRecoveryHandled.current) {
      navigationRecoveryHandled.current = true;

      navigateFn(lastEditableStep);
    }
  }, [lastEditableStep, navigateFn, projectLoading, stepsCompletedStatus]);

  updateParentHeight();

  return (
    <Container>
      <Header>
        <Title>Project Setup</Title>
        <ProgressBar progressPercentage={completePercent} />
      </Header>
      <Wizard
        steps={sidebarSteps}
        currentStep={currentStep}
        lastEditableStep={match.params.projectID ? Infinity : lastEditableStep}
        navigateTo={navigateFn}
      >
        {projectLoading ? (
          <Loading />
        ) : (
          <>{projectCompleted ? <SetupComplete /> : <ActiveComponent />}</>
        )}
      </Wizard>
    </Container>
  );
}

ProjectSetup.propTypes = {
  cloneProject: PropTypes.bool,
};

ProjectSetup.defaultProps = {
  cloneProject: false,
};
