import { call, put, takeLatest } from 'redux-saga/effects';
import { SVC_ENDPOINTS } from '../../../constants/endpoints';
import { svcAPI } from '../../../services/axios';
import {
  getMetricsAndGoals,
  setMetricsAndGoals,
  setMetricsAndGoalsError,
} from '.';

function* fetchMetricsAndGoalsSagaFn(action) {
  const { payload } = action;
  try {
    const response = yield call(
      svcAPI.get,
      SVC_ENDPOINTS.V1.METRICS({
        pathVariables: {
          id: payload.projectId,
        },
      }),
    );
    const metricsAndGoals = response.data;
    // Adjust payload to save data
    const { projectInfo } = metricsAndGoals;
    const projectId = projectInfo.id;
    delete metricsAndGoals.projectInfo;
    delete metricsAndGoals.success;
    delete metricsAndGoals.error;
    const newMetric = { ...metricsAndGoals, projectId };

    yield put(
      setMetricsAndGoals({ data: newMetric, rateType: payload.rateType }),
    );
  } catch (error) {
    const { response } = error;
    // eslint-disable-next-line no-console
    console.error(response);
    yield put(setMetricsAndGoalsError({ fatalError: true }));
  }
}

export default takeLatest(getMetricsAndGoals.type, fetchMetricsAndGoalsSagaFn);
