/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const initialState = {
  reseting: 0, // can be 0 (false), 1 (saving), 2 (just saved, temporary)
  error: null,
};

const slice = createSlice({
  name: 'resendPartnerData',
  initialState,
  reducers: {
    resend(state) {
      state.reseting = 1;
      state.error = null;
    },
    setReseting(state, action) {
      state.reseting = action.payload;
    },
    setError(state, action) {
      state.error = action.payload;
      state.reseting = 0;
    },
  },
});

const {
  reducer,
  name,
  actions: { resend, setReseting, setError },
} = slice;

reducer.key = name;

export default reducer;

export { resend, setReseting, setError };

// SELECTORS

export function selectState(state) {
  return state[reducer.key] || {};
}

export function selectReseting(state) {
  return get(selectState(state), 'reseting', 0);
}

export function selectError(state) {
  return get(selectState(state), 'error', 0);
}
