import { withFormik } from 'formik';
import shortId from 'shortid';
import { withTheme } from 'styled-components';
import { compose } from 'recompose';
import { connect } from 'react-redux';
import SupportTeam from './SupportTeam';
import { projectSupportTeamValidations } from '../../../../components/ProjectForms/ProjectSupportTeamForm';
import {
  patchProject,
  selectSupportTeam,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  const { supportTeam } = selectSupportTeam(state);

  return {
    initialValues: {
      supportTeam: supportTeam || [
        {
          supportTeam: [{ key: shortId.generate(), role: null, member: null }],
        },
      ],
    },
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  payload.supportTeam = (values.supportTeam || []).map((st) => ({
    userId: st.member,
    role: st.role,
  }));

  const nextStep = STEP_NUMBER.SUPPORT_TEAM + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    validationSchema: projectSupportTeamValidations,
  }),
  withTheme,
)(SupportTeam);
