import SendEmailStep from '../../ProcessBuilder/Steps/SendEmailStep';
import SignDocumentStep from '../../ProcessBuilder/Steps/SignDocumentStep';
import LanguageCertificationQuizStep from '../../ProcessBuilder/Steps/LanguageCertificationQuizStep';
import TranslationQuizStep from '../../ProcessBuilder/Steps/TranslationQuizStep';
import InternalQuizStep from '../../ProcessBuilder/Steps/InternalQuizStep';
import InternalLocaleBasedQuizStep from '../../ProcessBuilder/Steps/InternalLocaleBasedQuizStep';
import ExternalExamStep from '../../ProcessBuilder/Steps/ExternalExamStep';
import AcademyCourseStep from '../../ProcessBuilder/Steps/AcademyCourseStep';
import ConsentFormStep from '../../ProcessBuilder/Steps/ConsentFormStep';
import SurveyQuestionsStep from '../../ProcessBuilder/Steps/SurveyQuestionsStep';

export default function getStepComponent(sequenceFlow) {
  // TODO: There are some missing steps.
  switch (sequenceFlow) {
    case 'completeProjectAttributesFlow':
    case 'manualWorkFlow':
      return null;

    case 'sendEmailFlow':
      return SendEmailStep;

    case 'signDocumentFlow':
      return SignDocumentStep;

    case 'takeInternalQuizFlow':
      return InternalQuizStep;

    case 'takeInternalLocaleBasedQuizFlow':
      return InternalLocaleBasedQuizStep;

    case 'takeExternalExamFlow':
      return ExternalExamStep;

    case 'takeLanguageCertificationQuizFlow':
      return LanguageCertificationQuizStep;

    case 'takeTranslationQuizFlow':
      return TranslationQuizStep;

    case 'completeAcademyCourseFlow':
      return AcademyCourseStep;

    case 'dataCollectionPiiConsentFormFlow':
      return ConsentFormStep;

    case 'takeSurveyFlow':
      return SurveyQuestionsStep;

    default:
      return null;
  }
}

export function hasStepComponent(sequenceFlow) {
  // TODO: There are some missing steps.
  switch (sequenceFlow) {
    case 'completeProjectAttributesFlow':
    case 'manualWorkFlow':
    case 'dataCollectionPiiConsentFormFlow':
    case 'takeSimulationFlow':
      return false;

    case 'sendEmailFlow':
    case 'signDocumentFlow':
    case 'takeInternalQuizFlow':
    case 'takeInternalLocaleBasedQuizFlow':
    case 'takeExternalExamFlow':
    case 'takeLanguageCertificationQuizFlow':
    case 'takeTranslationQuizFlow':
    case 'completeAcademyCourseFlow':
    case 'takeSurveyFlow':
      return true;

    default:
      return true;
  }
}
