import { useEffect } from 'react';
import { withFormik } from 'formik';
import withHooks from 'hook-hoc';
import { withTheme } from 'styled-components';
import { connect, useSelector, useDispatch } from 'react-redux';
import { compose } from 'recompose';
import * as Yup from 'yup';
import UserProjectPageForm from './UserProjectPageForm';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { selectUserProjectPage } from '../../../../../store/ducks/projectView';
import { requiredMessage } from '../../../../ProjectSetup/Steps/shared';
import {
  patchProject,
  selectRegistrationAndQualification,
} from '../../../../../store/ducks/project';
import * as registrationProcesses from '../../../../../store/ducks/registrationProcesses';

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {
    defaultPageId: values.defaultPageTask,
    skillsReview: !!values.hasSkillReview,
    acanProject: !!values.isACANProject,
  };

  dispatch(patchProject({ changes: patch }));
}

function mapStateToProps(state) {
  const userProjectPage = selectUserProjectPage(state);
  const registrationAndQualification = selectRegistrationAndQualification(
    state,
  );
  const { registrationProcessTemplate } = registrationAndQualification;

  return {
    userProjectPage,
    registrationProcessTemplate,
  };
}

function useHooksAsProps({ registrationProcessTemplate }) {
  const dispatch = useDispatch();

  const registrationSteps = useSelector(registrationProcesses.selectSteps);

  useEffect(() => {
    dispatch(registrationProcesses.fetchOptions());
    if (registrationProcessTemplate) {
      dispatch(registrationProcesses.fetchSteps(registrationProcessTemplate));
    }
  }, [dispatch, registrationProcessTemplate]);

  return {
    registrationSteps,
  };
}

function mapPropsToValues(props) {
  const { userProjectPage, registrationSteps } = props;

  const steps = registrationSteps.steps.map(
    (step) =>
      step.stepDefinition.sequenceFlow === 'dataCollectionPiiConsentFormFlow',
  );

  return {
    ...userProjectPage,
    dataCollectionPiiConsentFormFlow: steps.includes(true),
  };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withHooks(useHooksAsProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: Yup.object()
      .strict()
      .shape({
        defaultPageTask: Yup.number()
          .nullable()
          .required(requiredMessage`Default Page Task`),
      }),
  }),
)(withTheme(UserProjectPageForm));
