import * as Yup from 'yup';

const { ValidationError } = Yup;

/* HOW TO USE
 *
 * // [{ id: 2 }, { id: null }]
 *
 * function transformError(error, value, index) {
 *   return new Yup.ValidationError('ID is required', value, `field${index}`);
 * }
 *
 * const validateArray = createArrayValidation(
 *   { id: Yup.number().required() },
 *   transformError,
 * );
 *
 * Yup.object().shape({
 *   array: Yup.array().test('arrayName', 'canBeEmpty', validateArray),
 * });
 *
 */

export default function createArrayValidation(schemaOrObject, transformError) {
  let schema = schemaOrObject;

  if (!Yup.isSchema(schemaOrObject)) {
    schema = Yup.object(schemaOrObject).strict();
  }

  return function validateArray(value) {
    if (!value) {
      return true;
    }

    if (!Array.isArray(value)) {
      throw new TypeError(`Expected array but got ${value}`);
    }

    let errors = [];

    value.forEach((item, index) => {
      try {
        schema.validateSync(item, { abortEarly: false });
      } catch (rawError) {
        let fieldErrors = [];

        if (
          rawError instanceof ValidationError &&
          typeof transformError === 'function'
        ) {
          rawError.inner.forEach((e) => {
            fieldErrors = [...fieldErrors, transformError(e, item, index)];
          });
        }
        errors = [...errors, ...fieldErrors];
      }
    });

    const finalError = new ValidationError('', '', '');
    finalError.inner = errors;

    throw finalError;
  };
}
