import React from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import {
  PreviewSectionTitle,
  PreviewSectionItem,
  PreviewSectionText,
  PreviewSection,
  EditButtonContainer,
  EditStepButton,
  SectionTitle,
  SectionDescription,
} from '../styles';
import ProjectResources from '../../ProjectForms/ProjectUserProjectPageForm/ProjectResources';
import DataConsentForm from '../../DataConsentForm';
import { selectProjectId } from '../../../store/ducks/project';

export default function ProjectUserProjectPagePreview(props) {
  const {
    defaultPageTask,
    hasSkillReview,
    isACANProject,
    editStep,
    renderedIn,
    stepNumber,
  } = props;

  const projectId = useSelector(selectProjectId);

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: User Project Pages
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}

      <PreviewSection>
        <Row>
          <Col col={4}>
            <PreviewSectionItem>Default Page Task</PreviewSectionItem>
            <PreviewSectionText>{defaultPageTask}</PreviewSectionText>
          </Col>
          <Col col={4}>
            <PreviewSectionItem>Available Skill Review</PreviewSectionItem>
            {hasSkillReview ? 'Available' : 'Not available'}
          </Col>
          <Col col={4}>
            <PreviewSectionItem>ACAN Project</PreviewSectionItem>
            {isACANProject ? 'Yes' : 'No'}
          </Col>
        </Row>
      </PreviewSection>

      <ProjectResources projectId={projectId} mode="view" />

      <div>
        <SectionTitle>Data Collection Consent Form Template</SectionTitle>
        <SectionDescription>
          Template configuration for the primary or third party users.
        </SectionDescription>
      </div>
      <DataConsentForm projectId={projectId} mode="view" />
    </>
  );
}

ProjectUserProjectPagePreview.propTypes = {
  defaultPageTask: PropTypes.string,
  hasSkillReview: PropTypes.bool,
  isACANProject: PropTypes.bool,
  editStep: PropTypes.func,
  renderedIn: PropTypes.oneOf([
    'project-view',
    'project-setup',
    'project-preview-modal',
  ]),
  stepNumber: PropTypes.number,
};

ProjectUserProjectPagePreview.defaultProps = {
  defaultPageTask: '',
  hasSkillReview: false,
  isACANProject: false,
  editStep: undefined,
  renderedIn: 'project-setup',
  stepNumber: 7,
};
