import { call, put, takeLatest } from 'redux-saga/effects';
import { fetchAll, fetchFailed, setAll } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchUserThirdPartyListSagaFn(action) {
  const {
    payload: { userId, projectId },
  } = action;

  try {
    const { data: userThirdParty } = yield call(
      axios.get,
      ENDPOINTS.V2.THIRD_PARTIES({
        query: {
          userId,
          projectId,
        },
      }),
    );

    yield put(setAll(userThirdParty));
  } catch (error) {
    yield put(fetchFailed(error));
  }
}

export default takeLatest(fetchAll.type, fetchUserThirdPartyListSagaFn);
