import { call, put, takeLatest } from 'redux-saga/effects';
import {
  fetchAutoScreening,
  fetchAutoScreeningError,
  fetchAutoScreeningSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

export function* fetchAutoScreeningSagaFn(action) {
  const {
    payload: { projectId },
  } = action;

  try {
    const { data: autoScreenings } = yield call(
      axios.get,
      ENDPOINTS.V2.AUTO_SCREENING({
        pathVariables: {
          projectId,
        },
      }),
    );

    yield put(fetchAutoScreeningSuccess(autoScreenings));
  } catch (error) {
    yield put(fetchAutoScreeningError(error));
  }
}

export default takeLatest(fetchAutoScreening.type, fetchAutoScreeningSagaFn);
