import { withFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useMemo } from 'react';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import ProcessDetailsModal from './ProcessDetailsModal';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';
import * as registrationTemplates from '../../../store/ducks/registrationProcesses';
import * as qualificationTemplates from '../../../store/ducks/qualificationProcesses';
import store from '../../../store';
import {
  setProcessDetails,
  PROCESS_TYPE,
} from '../../../store/ducks/processBuilder';
import mapToOptions from '../../../pages/ProjectSetup/helpers/mapToOptions';
import stripHTMLTags from '../../../helpers/strip-html-tags';

function useHooksAsProps(outerProps) {
  const { processType } = outerProps;

  const dispatch = useDispatch();

  useEffect(() => {
    if (processType === PROCESS_TYPE.REGISTRATION) {
      dispatch(registrationTemplates.fetchOptions());
    } else {
      dispatch(qualificationTemplates.fetchOptions());
    }
  }, [dispatch, processType]);

  const rawOptions = useSelector(
    useMemo(() => {
      if (processType === PROCESS_TYPE.REGISTRATION) {
        return registrationTemplates.selectOptions;
      }

      return qualificationTemplates.selectOptions;
    }, [processType]),
  );

  const loading = useSelector(
    useMemo(() => {
      if (processType === PROCESS_TYPE.REGISTRATION) {
        return registrationTemplates.selectOptionsLoading;
      }

      return qualificationTemplates.selectOptionsLoading;
    }, [processType]),
  );

  const error = useSelector(
    useMemo(() => {
      if (processType === PROCESS_TYPE.REGISTRATION) {
        return registrationTemplates.selectOptionsError;
      }

      return qualificationTemplates.selectOptionsError;
    }, [processType]),
  );

  const options = useMemo(() => {
    return mapToOptions('id', 'name')(rawOptions);
  }, [rawOptions]);

  return {
    options,
    loading,
    error,
  };
}

function handleSubmit(values, { props }) {
  const { processType, closeModal } = props;

  if (typeof closeModal === 'function') {
    closeModal();
  }

  store.dispatch(
    setProcessDetails({
      processType,
      processName: values.processName,
      processDescription: values.processDescription,
      copyFromProcessID: values.copyModulesFrom || null,
    }),
  );
}

function mapPropsToValues(props) {
  const { initialValues } = props;

  if (initialValues) {
    return {
      processName: initialValues.processName,
      processDescription: stripHTMLTags(initialValues.processDescription),
      copyModulesFrom: initialValues.copyFromProcessID,
      showCopyFromProcessSelect: !!initialValues.copyFromProcessID,
    };
  }

  return {};
}

const validationSchema = yup.object().shape({
  processName: yup.string().required(requiredMessage`Process name`),
  processDescription: yup
    .string()
    .required(requiredMessage`Process description`),
  copyModulesFrom: yup.mixed().when('showCopyFromProcessSelect', {
    is: true,
    then: yup
      .mixed()
      .required(
        requiredMessage`When "Copy Modules From an Existing Process" is checked this`,
      ),
  }),
});

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(ProcessDetailsModal);
