/* eslint-disable no-param-reassign */
// allActionsWillBe {
//   type: 'fetch|fetchSuccess|fetchFailed'
//   meta: {
//     key: 'to fetch/refetch'
//   }
//   action?: { [key: string]: any }
// }

import { call, put, select, takeEvery, all } from 'redux-saga/effects';
import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import memoize from 'lodash/memoize';
import axios from '../../../services/axios';

export const OPTIONS_TYPE = {
  OBJECT: 'object',
  ARRAY: 'array',
  NESTED_OBJECT: 'nested_object',
  NESTED_ARRAY: 'nested_array',
};

export const OPTIONS_FETCH_MODES = {
  EAGER: 'eager',
  LAZY: 'lazy',
};

const requestsInFlight = {};

function getInitialStateForType(stateType) {
  if (stateType === OPTIONS_TYPE.OBJECT) {
    return {
      loading: false,
      error: null,
      data: null,
    };
  }

  if (stateType === OPTIONS_TYPE.ARRAY) {
    return {
      loading: false,
      error: null,
      data: [],
    };
  }

  return {
    loading: false,
    error: null,
    data: {},
  };
}

function getCacheKey(stateKey, nestedKey) {
  if (nestedKey) {
    return `${stateKey}${nestedKey}`;
  }

  return `${stateKey}`;
}

// type EntityConfig<T> = {
//   type: OPTIONS_TYPE;
//   entityPK: (entity: T) => string | string;
//   url: (state: any, action: any) => string | string;
//   transform?: (entity: T): any;
//   fetchMode?: 'eager' | 'regular' | 'lazy' // undefined means regular (fetch everytime its called)
// }

export default function createEntitiesSlice(
  keys /*: { [key: string]: EntityConfig } */,
  reducerKeyParam,
) {
  const reducerKey = reducerKeyParam || 'options';
  const initialState = {};

  Object.keys(keys).forEach((key) => {
    initialState[key] = getInitialStateForType(
      keys[key].type || OPTIONS_TYPE.ARRAY,
    );
  });

  function getEntityConfig(key) {
    return keys[key] || {};
  }

  // Selecting
  function selectSliceFor(stateKey, state) {
    const path = [reducerKey, stateKey];

    return get(state, path, {});
  }

  function selectDataFor(stateKey, nestedKey, state) {
    const path = ['data'];
    const config = keys[stateKey];

    if (nestedKey) {
      path.push(nestedKey);
    }

    return get(
      selectSliceFor(stateKey, state),
      path,
      config.type === OPTIONS_TYPE.ARRAY ||
        config.type === OPTIONS_TYPE.NESTED_ARRAY
        ? []
        : {},
    );
  }

  function selectLoadingFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'loading', false);
  }

  function selectErrorFor(stateKey, state) {
    return get(selectSliceFor(stateKey, state), 'error', null);
  }

  function selectById(stateKey, nestedKey, id, state) {
    const config = getEntityConfig(stateKey);
    const data = selectDataFor(stateKey, nestedKey, state);

    if (!data) {
      return null;
    }

    if (Array.isArray(data)) {
      return (
        data.find(
          (candidate) => candidate && candidate[config.entityPK || 'id'] === id,
        ) || null
      );
    }

    return data[id] || null;
  }

  const createSelectSliceFor = memoize(
    (stateKey, nestedKey) => (state) => {
      const sliceData = selectSliceFor(stateKey, state);

      if (!nestedKey) {
        return sliceData;
      }

      return {
        ...sliceData,
        data: selectDataFor(stateKey, nestedKey, state),
      };
    },
    getCacheKey,
  );

  const createSelectDataFor = (stateKey, nestedKey) => (state) =>
    selectDataFor(stateKey, nestedKey, state);

  const createSelectLoadingFor = (stateKey) => (state) =>
    selectLoadingFor(stateKey, state);

  const createSelectErrorFor = (stateKey) => (state) =>
    selectErrorFor(stateKey, state);

  const createSelectById = (stateKey, nestedKey, id) => (state) =>
    selectById(stateKey, nestedKey, id, state);

  // Helpers

  function hasData(key, nestedKey, state) {
    const config = getEntityConfig(key);
    const possibleData = selectDataFor(key, nestedKey, state);

    if (
      !config.type ||
      config.type === OPTIONS_TYPE.ARRAY ||
      config.type === OPTIONS_TYPE.NESTED_ARRAY
    ) {
      return !!possibleData.length;
    }

    return !!Object.keys(possibleData).length;
  }

  function getNestedKey(key, fetchAction) {
    // Get nested key for nested types
    const config = getEntityConfig(key);
    let nestedKey = null;

    if (
      (config.type === OPTIONS_TYPE.NESTED_ARRAY ||
        config.type === OPTIONS_TYPE.NESTED_OBJECT) &&
      typeof fetchAction.payload !== 'string'
    ) {
      nestedKey = fetchAction.payload.nestedKey;
    }

    return nestedKey;
  }

  // Managment
  const slice = createSlice({
    name: reducerKey,
    initialState,
    reducers: {
      /**
       * action is a string or an object:
       * { optionsFor: string, metadata: any, nestedKey?: string }
       * Usage: fetch('businessUnis'); or
       * fetch({ optionsFor: 'businessUnits' });
       */
      fetch() {},

      /**
       * // Action here is: {
       *  payload: {
       *    data: any;
       *    stateKey: string;
       *    nestedKey?: string;
       *  }
       * }
       */
      fetchSucceded(state, action) {
        const key = action.payload.stateKey;
        const nestedKey = getNestedKey(key, action);
        const config = getEntityConfig(key);

        const data =
          typeof config.transform === 'function'
            ? config.transform(action.payload.data)
            : action.payload.data;

        state[key].loading = false;
        state[key].error = null;

        switch (config.type || OPTIONS_TYPE.ARRAY) {
          case OPTIONS_TYPE.OBJECT:
            state[key].data = data || null;
            break;

          case OPTIONS_TYPE.ARRAY:
            state[key].data = data || [];
            break;

          case OPTIONS_TYPE.NESTED_OBJECT:
            state[key].data[nestedKey] = data || null;
            break;

          case OPTIONS_TYPE.NESTED_ARRAY:
            state[key].data[nestedKey] = data || [];
            break;

          default:
        }
      },

      /**
       * // Action here is: {
       *  payload: {
       *    error: Error;
       *    stateKey: string;
       *  }
       * }
       */
      fetchFailed(state, action) {
        state[action.payload.stateKey].loading = false;
        state[action.payload.stateKey].error = action.payload.error;
      },

      setLoading(state, action) {
        const key =
          typeof action.payload === 'string'
            ? action.payload
            : action.payload.optionsFor;

        state[key].loading = true;
      },
    },
  });

  slice.reducer.key = reducerKey;

  if (process.env.NODE_ENV === 'development' && window.Cypress) {
    window.optionsActions = {
      ...slice.actions,
    };
  }

  // Fetching
  function* genericFetchSaga(action) {
    const state = yield select();

    const stateKey =
      typeof action.payload === 'string'
        ? action.payload
        : action.payload.optionsFor;

    const config = getEntityConfig(stateKey);

    // Get nested key for nested types
    const nestedKey = getNestedKey(stateKey, action);

    // prevent fetch if in lazy mode and already have data available
    if (
      config.fetchMode === OPTIONS_FETCH_MODES.LAZY &&
      hasData(stateKey, nestedKey, state)
    ) {
      return;
    }

    yield put(slice.actions.setLoading(action.payload));

    try {
      const url =
        typeof config.url === 'function'
          ? config.url(state, action)
          : config.url;

      let request = requestsInFlight[url];

      if (!request) {
        const connection = config.connection ? config.connection : axios;
        request = connection.get(url);
        requestsInFlight[url] = request;
      }

      const response = yield request;

      delete requestsInFlight[url];

      yield put(
        slice.actions.fetchSucceded({
          stateKey,
          nestedKey,
          data: response.data,
        }),
      );
    } catch (err) {
      yield put(
        slice.actions.fetchFailed({
          stateKey,
          error: err,
        }),
      );
    }
  }

  const eagerKeys = Object.keys(keys).filter(
    (key) => getEntityConfig(key).fetchMode === OPTIONS_FETCH_MODES.EAGER,
  );

  let saga;

  if (eagerKeys.length) {
    saga = call(function* fetchEagerEntities() {
      yield takeEvery(slice.actions.fetch.type, genericFetchSaga);
      yield all(eagerKeys.map((key) => put(slice.actions.fetch(key))));
    });
  } else {
    saga = takeEvery(slice.actions.fetch.type, genericFetchSaga);
  }

  return {
    reducer: slice.reducer,
    fetch: slice.actions.fetch,
    saga,
    selectors: {
      createSelectSliceFor,
      createSelectDataFor,
      createSelectLoadingFor,
      createSelectErrorFor,
      createSelectById,
      selectSliceFor,
      selectDataFor,
      selectLoadingFor,
      selectErrorFor,
      selectById,
    },
  };
}
