import React from 'react';
import PropTypes from 'prop-types';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import useDimensions from 'react-use-dimensions';
import {
  EditorContainer,
  AccessoryHeaderContainer,
  AccessoryFooterContainer,
} from './styles';
import MessageError from '../MessageError';

const toolbarOptions = {
  options: [
    'inline',
    'blockType',
    'list',
    'textAlign',
    'colorPicker',
    'link',
    'emoji',
    'image',
    'remove',
    'history',
  ],
  inline: {
    options: ['bold', 'italic', 'underline', 'strikethrough'],
  },
};

export default function RichTextEditor(props) {
  const {
    value,
    onChange,
    accessoryHeader,
    accessoryFooter,
    onBlur,
    placeholder,
    error,
    errorIncreasesHeight,
  } = props;

  const [headerRef, { height: headerHeight }] = useDimensions();
  const [footerRef, { height: footerHeight }] = useDimensions();

  return (
    <>
      <EditorContainer
        headerHeight={headerHeight}
        footerHeight={footerHeight}
        hasError={!!error}
      >
        <Editor
          editorState={value}
          onEditorStateChange={onChange}
          toolbarClassName="components__RichTextEditor__Toolbar"
          editorClassName="components__RichTextEditor__Editor"
          onBlur={onBlur}
          placeholder={placeholder}
          toolbar={toolbarOptions}
        />
        {accessoryHeader && (
          <AccessoryHeaderContainer ref={headerRef} tabIndex={-1}>
            {accessoryHeader}
          </AccessoryHeaderContainer>
        )}
        {accessoryFooter && (
          <AccessoryFooterContainer ref={footerRef} tabIndex={-1}>
            {accessoryFooter}
          </AccessoryFooterContainer>
        )}
      </EditorContainer>
      <MessageError
        isVisible={!!error}
        message={error}
        increasesHeight={errorIncreasesHeight}
      />
    </>
  );
}

RichTextEditor.propTypes = {
  // Value is DraftJS EditorState Immutable object.
  // eslint-disable-next-line react/forbid-prop-types
  value: PropTypes.any,
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  accessoryHeader: PropTypes.node,
  accessoryFooter: PropTypes.node,
  placeholder: PropTypes.string,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([false])]),
  errorIncreasesHeight: PropTypes.bool,
};

RichTextEditor.defaultProps = {
  value: undefined,
  onChange: undefined,
  onBlur: undefined,
  accessoryHeader: null,
  accessoryFooter: null,
  placeholder: undefined,
  error: undefined,
  errorIncreasesHeight: false,
};
