import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import get from 'lodash/get';
import { Label, Section } from '../../../../styles';
import Select from '../../../Select';
import TabSwitcher from '../../../TabSwitcher';
import ActionFooter from '../../../ActionFooter';
import Modal, { useModal } from '../../../Modal';
import {
  StepContentContainer,
  StepActionContainer,
  CollapsedViewContainer,
} from '../styles';
import DocumentEditor from './DocumentEditor';
import DocumentInformation from './DocumentInformation';
import { EditModalContentContainer } from './styles';
import { usePreviewModalControls } from '../../../Builder/helpers/preview-context';

const existingOrNewTabs = [
  { label: 'USE EXISTING DOCUMENT' },
  { label: 'CREATE NEW DOCUMENT' },
];

export default function SignDocumentStep(props) {
  const {
    values,
    submitCount,
    setFieldValue,
    card: { attributes },
    submitForm,
    documentOptions,
    collapsed,
    errors,
    handleChange,
    cancelEditing,
    card,
    fetchStepDefinition,
  } = props;

  const previewModalControls = usePreviewModalControls();

  const previewStep = () => {
    previewModalControls.openModal({
      only: {
        ...card,
        index: card.cardIndex,
        attributes: [
          {
            value: values.documentToSign || null,
            attributeOrder: 1,
            sequenceFlow: card.sequenceFlow,
            additionalAttributes: null,
          },
        ],
      },
    });
  };

  const handleTabChange = useCallback(
    (nextTab) => {
      setFieldValue('isNewDocument', nextTab);
    },
    [setFieldValue],
  );

  const [modalProps, modalControls] = useModal(false, {
    documentToEdit: -1,
  });

  const editDocument = useCallback(
    (documentId) => {
      modalControls.openModal({ documentToEdit: documentId });
    },
    [modalControls],
  );

  if (collapsed && attributes.length) {
    if (values.isNewDocument) {
      return (
        <CollapsedViewContainer>{values.documentTitle}</CollapsedViewContainer>
      );
    }

    const option = (documentOptions || []).find(
      (doc) => doc.value === values.documentToSign,
    );

    return (
      <CollapsedViewContainer>
        {option ? option.label : ''}
      </CollapsedViewContainer>
    );
  }

  return (
    <>
      <Modal {...modalProps} title="Edit Document">
        <EditModalContentContainer>
          <DocumentEditor
            documentId={get(modalControls, 'modalData.documentToEdit', -1)}
            closeModal={modalControls.closeModal}
            modalOpen={modalProps.open}
            refetchStepDefinition={fetchStepDefinition}
            handleChange={handleChange}
            submitCount={submitCount}
            values={values}
            errors={errors}
          />
        </EditModalContentContainer>
      </Modal>

      <StepContentContainer>
        <Section marginBottom="32px">
          <Row>
            <Col col={10}>
              <TabSwitcher
                tabs={existingOrNewTabs}
                value={values.isNewDocument || 0}
                onChange={handleTabChange}
              />
            </Col>
          </Row>
        </Section>
        {!values.isNewDocument ? (
          <Section marginBottom="32px">
            <Row>
              <Col col={12}>
                <Label htmlFor="documentToSign">SELECT DOCUMENT</Label>
                <Select
                  name="documentToSign"
                  id="documentToSign"
                  options={documentOptions}
                  onChange={({ value }) =>
                    setFieldValue('documentToSign', value)
                  }
                  value={values.documentToSign}
                  placeholder="Select existing document"
                  error={submitCount >= 1 && errors.documentToSign}
                />
              </Col>
            </Row>
            {values.documentToSign && (
              <DocumentInformation
                documentId={values.documentToSign}
                editDocument={editDocument}
              />
            )}
          </Section>
        ) : (
          <>
            <DocumentEditor
              handleChange={handleChange}
              submitCount={submitCount}
              values={values}
              errors={errors}
              documentId={-1}
            />
          </>
        )}
      </StepContentContainer>
      <StepActionContainer>
        <ActionFooter
          hideDivider
          leftButtons={
            !values.isNewDocument || values.isNewDocument === 0
              ? [
                  {
                    type: 'secondary',
                    text: 'Preview Document',
                    onClick: previewStep,
                  },
                ]
              : []
          }
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !attributes.length ? 'Add & Close' : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

SignDocumentStep.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  documentOptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
    }),
  ).isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    cardIndex: PropTypes.number.isRequired,
    sequenceFlow: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.number,
        attributeOrder: PropTypes.number,
      }),
    ),
  }).isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  cancelEditing: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  fetchStepDefinition: PropTypes.func.isRequired,
};
