/* eslint-disable react/jsx-props-no-spreading, import/no-cycle */
import React, { useMemo } from 'react';
import { Field } from 'formik';
import PropTypes from 'prop-types';
import ActionBuilder from '../../ActionBuilder';
import Select from '../../../../Select';
import CloseableTag from '../../../../CloseableTag';
import SecondaryQuestions from '../../SecondaryQuestions';
import getNormalizedLabel from '../../helpers/getNormalizedLabel';
import { Label } from '../../../../../styles';
import { Container } from './styles';
import { ReactComponent as Icon } from '../../../../../assets/icons/dropdown.svg';

export default function MultiSelect(props) {
  const {
    data: { id, questionType },
    values: { answers },
    setFieldValue,
    errors,
    submitCount,
    options,
    calculateCount,
    handleRemove,
    items,
  } = props;

  const label = useMemo(() => getNormalizedLabel(questionType), [questionType]);
  const title = useMemo(
    () =>
      ({
        COUNTRY_LIST: 'Select Country',
        LANGUAGE_LIST: 'Select Language',
        DROPDOWN_SELECTION: 'Select Answers',
      }[questionType]),
    [questionType],
  );

  return (
    <>
      <Label>
        <Icon style={{ verticalAlign: 'middle', margin: '-3px 7px 0 0' }} />{' '}
        {label}
      </Label>
      <Field
        {...props}
        component={ActionBuilder}
        id={id}
        firstOption={
          <Container>
            <Label htmlFor="answers">{title}</Label>
            <Select
              getMultiCount={calculateCount}
              id="answers"
              name="answers"
              options={options}
              value={answers[id]}
              onChange={(value) => setFieldValue(`answers[${id}]`, value)}
              placeholder="Selects the options"
              error={submitCount > 0 && errors.answers}
              isMulti
            />
            {answers[id] &&
              answers[id].map((answer) =>
                answer.label ? (
                  <CloseableTag
                    key={answer.label}
                    label={answer.label}
                    onClose={() => handleRemove(id, answer.value)}
                  />
                ) : null,
              )}
          </Container>
        }
        secondOption={'All others.'}
      />
      {items.map((item) => (
        <Field
          {...props}
          data={item}
          component={SecondaryQuestions}
          id={id}
          item={item}
          key={item.id}
        />
      ))}
    </>
  );
}

MultiSelect.propTypes = {
  data: PropTypes.shape({
    id: PropTypes.number,
    questionType: PropTypes.string,
  }).isRequired,
  values: PropTypes.shape({
    answers: PropTypes.object,
  }).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  errors: PropTypes.PropTypes.shape({
    answers: PropTypes.array,
  }).isRequired,
  submitCount: PropTypes.number.isRequired,
  handleRemove: PropTypes.func.isRequired,
  options: PropTypes.arrayOf(PropTypes.object).isRequired,
  items: PropTypes.arrayOf(PropTypes.object),
  calculateCount: PropTypes.func.isRequired,
};

MultiSelect.defaultProps = {
  items: [],
};
