import { useSelector } from 'react-redux';
import { useCallback, useMemo } from 'react';
import withHooks from 'hook-hoc';
import cloneDeep from 'lodash/cloneDeep';
import DemographicsGroupTable from './DemographicsGroupTable';
import { selectAll as selectAllHiringTargets } from '../../../../../../../store/ducks/hiringTargets';
import HiringTargetHeader from './HiringTargetHeader';
import InputCell from './InputCell';

function useHooksAsProps(outerProps) {
  const {
    tableName,
    groups,
    requirementID,
    setRequirementsByGroupValue,
    value,
  } = outerProps;

  // Getters and Setters
  const getFieldValue = useCallback(
    (groupID, hiringTargetID) => {
      if (Array.isArray(value)) {
        const field = value.find(
          (item) =>
            item.requirementID === requirementID && item.groupID === groupID,
        );

        if (
          field &&
          field.hiringTargets &&
          field.hiringTargets[hiringTargetID]
        ) {
          return `${field.hiringTargets[hiringTargetID]}`;
        }
      }

      return '0';
    },
    [requirementID, value],
  );

  const handleFieldChange = useCallback(
    (groupID, hiringTargetID, nextFieldValue) => {
      const index = value.findIndex(
        (item) =>
          item.requirementID === requirementID && item.groupID === groupID,
      );

      if (index < 0) {
        return;
      }

      const nextValue = [...value].map(cloneDeep);

      if (nextFieldValue.length) {
        const nextFieldNumber = parseInt(nextFieldValue, 10);

        if (Number.isNaN(nextFieldNumber)) {
          return;
        }

        nextValue[index].hiringTargets[hiringTargetID] = `${nextFieldNumber}`;
      } else {
        nextValue[index].hiringTargets[hiringTargetID] = '';
      }

      setRequirementsByGroupValue(nextValue);
    },
    [requirementID, setRequirementsByGroupValue, value],
  );

  // Columns
  const hiringTargets = useSelector(selectAllHiringTargets);

  const columns = useMemo(
    () => [
      {
        key: 'name',
        accessor: 'label',
        disableSortBy: true,
        Header: tableName,
      },
      ...hiringTargets.map((hiringTarget) => ({
        key: 'hiringTargetId',
        accessor: `${hiringTarget.id}`,
        disableSortBy: true,
        Header: HiringTargetHeader,
        Cell: InputCell,
        hiringTarget,
      })),
    ],
    [hiringTargets, tableName],
  );

  // Table data
  const tableData = useMemo(() => {
    return groups.map((group) => {
      const row = {
        label: group.label,
        groupID: group.id,
        handleFieldChange,
      };

      hiringTargets.forEach((hiringTarget) => {
        row[hiringTarget.id] = {
          hiringTargetID: hiringTarget.id,
          value: getFieldValue(group.id, hiringTarget.id),
          ...hiringTarget,
        };
      });

      return row;
    });
  }, [getFieldValue, groups, handleFieldChange, hiringTargets]);

  return {
    columns,
    tableData,
  };
}

export default withHooks(useHooksAsProps)(DemographicsGroupTable);
