import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveExperimentGroupsSuccess,
  saveExperimentGroupsError,
  saveExperimentGroups,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* saveExperimentGroupsSagaFn(action) {
  const {
    payload: {
      changes: { experimentGroups, experimentId },
      thenPut,
    },
  } = action;

  try {
    const response = yield call(
      axios.put,
      ENDPOINTS.V2.EXPERIMENT_GROUPS({
        pathVariables: {
          id: experimentId,
        },
      }),
      experimentGroups,
    );

    const groupsByExperiment = {};
    groupsByExperiment[experimentId] = [];

    response.data.forEach((group) =>
      groupsByExperiment[experimentId].push(group),
    );

    yield put(saveExperimentGroupsSuccess(groupsByExperiment));

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }
  } catch (error) {
    yield put(saveExperimentGroupsError(error));
  }
}

export default takeLatest(
  saveExperimentGroups.type,
  saveExperimentGroupsSagaFn,
);
