import { withFormik } from 'formik';
import * as Yup from 'yup';
import shortId from 'shortid';
import { compose } from 'recompose';
import HiringTargetsModal from './HiringTargetsModal';
import * as hiringTargets from '../../../../../store/ducks/hiringTargets';
import { selectAll as selectAllLocaleTenants } from '../../../../../store/ducks/localeTenants';
import { selectProject } from '../../../../../store/ducks/project';
import store from '../../../../../store';
import resetForm from '../../../../../pages/ProjectSetup/helpers/resetForm';
import { requiredMessage } from '../../../../../pages/ProjectSetup/Steps/shared';

function validateDialect(dialect) {
  if (dialect) {
    const localeTenantCountries = selectAllLocaleTenants(store.getState()).map(
      (tenant) => tenant.country,
    );

    if (localeTenantCountries.includes('*')) {
      return true;
    }

    if (localeTenantCountries.includes(dialect)) {
      return true;
    }

    return false;
  }

  return true;
}

function isTranslationProject() {
  const project = selectProject(store.getState());
  const { workType } = project;

  return workType === 'TRANSLATION';
}

// This is a workaround for the LanguageTo validation since
// it gets lost because of the enableReinitialize flag on formik.
function createLanguageToValidator() {
  let lastValue = null;

  return (value) => {
    // This makes sure that if the field is valid once,
    // it remains valid during the same JS tick
    const prevValue = lastValue;
    lastValue = value;

    // Schedule cleanup for next tick
    Promise.resolve().then(() => {
      lastValue = null;
    });

    if (value || prevValue) {
      return true;
    }

    return !isTranslationProject();
  };
}

function handleSubmit(values, formikBag) {
  const {
    props: {
      modalControls: { closeModal },
      showError,
      hideError,
    },
  } = formikBag;

  const {
    language,
    dialect,
    languageTo,
    dialectTo,
    country,
    owner,
    id,
  } = values;

  const similar = hiringTargets.selectAll(store.getState()).find((ht) => {
    const hasMatch =
      ht.localeLanguage === language &&
      ht.localeDialect === dialect &&
      ht.restrictToCountry === country;

    if (isTranslationProject()) {
      return (
        hasMatch &&
        ht.localeLanguageTo === languageTo &&
        ht.localeDialectTo === dialectTo
      );
    }

    return hasMatch;
  });

  const existsSimilar = !!similar && similar.id !== id;

  if (existsSimilar) {
    showError(
      new Error(
        'You have a very similar target already included. If you need to increase a target, consider editing it.',
      ),
    );

    return Promise.resolve();
  }

  return new Promise((resolve) => {
    const data = {
      id: id || shortId.generate(),
      localeLanguage: language,
      localeDialect: dialect,
      localeLanguageTo: languageTo,
      localeDialectTo: dialectTo,
      restrictToCountry: country,
      assignedOwner: owner,
      hiringDeadline: new Date(values.hiringDeadline).toISOString(),
      target: values.target,
      metrics: {
        qualifying: 0,
        active: 0,
        progress: 0,
      },
    };

    function onSuccess() {
      hideError();
      closeModal();

      setTimeout(() => {
        resetForm(
          formikBag,
          {
            lenguage: '',
            dialect: '',
            country: '',
            owner: '',
          },
          resolve,
        );
      }, 250);
    }

    function onError(message) {
      showError(new Error(message));

      // Error is shown one tick before button is clickable
      setImmediate(resolve());
    }

    if (id) {
      store.dispatch(
        hiringTargets.updateOne(
          { id, changes: data },
          {
            onSuccess,
            onError,
          },
        ),
      );
    } else {
      store.dispatch(
        hiringTargets.addOne(data, {
          onSuccess,
          onError,
        }),
      );
    }
  });
}

const today = new Date(Date.now());
function mapPropsToValues(props) {
  const {
    modalControls: { modalData },
  } = props;

  if (modalData) {
    return {
      id: modalData.id,
      language: modalData.localeLanguage,
      dialect: modalData.localeDialect,
      languageTo: modalData.localeLanguageTo,
      dialectTo: modalData.localeDialectTo,
      country: modalData.restrictToCountry,
      owner: modalData.assignedOwner,
      hiringDeadline:
        modalData.hiringDeadline !== null
          ? new Date(modalData.hiringDeadline)
          : today,
      target: modalData.target,
    };
  }

  return {
    writtenFluency: 6,
    spokenFluency: 6,
    userGroup: 1,
    taskType: 1,
  };
}

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validateOnMount: true,
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
      language: Yup.string().required(requiredMessage`Locale Language`),
      dialect: Yup.string().required(requiredMessage`Locale Region`),
      country: Yup.string()
        .test(
          'country',
          'You must have a Locale Tenant for this country.',
          validateDialect,
        )
        .required(requiredMessage`Restrict to Country`),
      // owner: Yup.number().required(requiredMessage`Assign Owner`),
      languageTo: Yup.mixed().test(
        'hiringTargetsModal_languageTo',
        requiredMessage`Locale Language To`,
        createLanguageToValidator(),
      ),
      dialectTo: Yup.mixed().test(
        'hiringTargetsModal_dialectTo',
        requiredMessage`Locale Region To`,
        (value) => (value ? true : !isTranslationProject()),
      ),

      target: Yup.number().required(requiredMessage`Target`),
      hiringDeadline: Yup.mixed().test(
        'date',
        requiredMessage`Hiring Deadline`,
        (value) => value instanceof Date,
      ),
    }),
  }),
)(HiringTargetsModal);
