import { call, put, takeLeading } from 'redux-saga/effects';
import { resendEmail, resendEmailError, resendEmailSuccess } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* resendEmailSagaFn(action) {
  const {
    payload: {
      changes: { id },
      thenPut,
      closeModal,
    },
  } = action;

  try {
    const response = yield call(
      axios.post,
      ENDPOINTS.V2.RESEND_EMAIL({
        pathVariables: {
          id,
        },
      }),
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    if (typeof closeModal === 'function') {
      closeModal();
    }

    yield put(resendEmailSuccess(response.data));
  } catch (error) {
    yield put(resendEmailError(error));
  }
}

export default takeLeading(resendEmail.type, resendEmailSagaFn);
