/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';
import { Paragraph } from '../../components/ProcessBuilder/AffectedProjectsModal/styles';
import { Section, SectionDescription, SectionTitle } from '../../styles';
import StatusCell from './Cells/StatusCell';
import ResendCell from './Cells/ResendCell';
import { UserThirdPartyListContainer } from './styles';
import SignedAtCell from './Cells/SignedAtCell';
import Table from '../../components/Table';
import BoldCell from './Cells/BoldCell';
import Button from '../../components/Button';
import Modal from '../../components/Modal';
import ContentFormGeneratorModal from './ContentFormGeneratorModal';
import {
  fetchAll as fetchUserThirdPartyList,
  selectAll,
  selectResendSuccess,
  selectResendError,
} from '../../store/ducks/userThirdPartyList';
import {
  EDITABLE_ENTITIES,
  editor,
  useCloseEditor,
} from '../../store/ducks/editableEntities';
import ErrorPopover, { useErrorPopover } from '../../components/ErrorPopover';
import SuccessPopover, {
  useSuccessPopover,
} from '../../components/SuccessPopover';

const columns = [
  {
    Header: 'THIRD PARTY NAME',
    accessor: 'firstName',
    key: 'firstName',
    Cell: BoldCell,
    width: 200,
  },
  {
    Header: 'EMAIL',
    accessor: 'email',
    key: 'email',
    width: 200,
  },
  {
    Header: 'AGE',
    accessor: 'age',
    key: 'age',
    style: {
      textAlign: 'center',
    },
  },
  {
    Header: 'GENDER',
    accessor: 'gender',
    key: 'gender',
    disableSortBy: true,
  },
  {
    Header: 'SIGNED',
    accessor: 'completedAt',
    key: 'completedAt',
    Cell: SignedAtCell,
    disableSortBy: true,
  },
  {
    Header: 'STATUS',
    accessor: 'status',
    key: 'status',
    Cell: StatusCell,
    disableSortBy: true,
  },
  {
    Header: 'RESEND',
    accessor: 'lastSentAt',
    key: 'lastSentAt',
    Cell: ResendCell,
    disableSortBy: true,
    tooltip:
      'You can resend generated consent forms up to 10 times. If you reach this limit, this action will be blocked.',
    tooltipIcon: true,
  },
];

export default function UserThirdPartyList() {
  const editorState = useSelector(
    editor.selectors.createSelectEditorSliceFor(
      EDITABLE_ENTITIES.THIRD_PARTIES,
    ),
  );
  const elementRef = useRef();

  const dispatch = useDispatch();
  const match = useRouteMatch();

  const {
    params: { projectId, userId },
  } = match;

  const openModal = useCallback(() => {
    dispatch(editor.actions.create(EDITABLE_ENTITIES.THIRD_PARTIES));
  }, [dispatch]);

  useEffect(() => {
    dispatch(fetchUserThirdPartyList({ userId, projectId }));
  }, [dispatch, userId, projectId]);

  const thirdParties = useSelector(selectAll);
  const resendError = useSelector(selectResendError);
  const resendSuccess = useSelector(selectResendSuccess);

  const closeModal = useCloseEditor(EDITABLE_ENTITIES.THIRD_PARTIES);
  const [showError, , errorPopoverProps] = useErrorPopover();
  const [showSuccess, , successPopoverProps] = useSuccessPopover();

  useEffect(() => {
    if (resendError) {
      showError(resendError);
    }
  }, [showError, resendError]);

  useEffect(() => {
    if (resendSuccess) {
      showSuccess({
        message: 'Consent form has been resent',
      });
    }
  }, [showSuccess, resendSuccess]);

  return (
    <UserThirdPartyListContainer>
      <Section>
        <SectionTitle>Third Party Data Collection Consent Forms</SectionTitle>
        <SectionDescription>
          Generate data collection consent forms for third parties to sign.
        </SectionDescription>
      </Section>
      <Section>
        <Paragraph>
          This project will be collecting data from users outside {"Appen's"}{' '}
          crowd. Add third parties contact information in order to generate and
          collect consent forms.
          <br />
          <br />
          If the individuals participating are under the age of 18 years old,
          consent forms must be acknowledged by their parent or legal
          representative.
        </Paragraph>
      </Section>
      <Section>
        {thirdParties.error && (
          <ErrorPopover
            title="Failed to load"
            errorShown
            error={thirdParties.error}
            keepInDocumentFlow
          />
        )}
        {!thirdParties.loading &&
          !thirdParties.error &&
          thirdParties.length > 0 && (
            <Table ref={elementRef} columns={columns} data={thirdParties} />
          )}
        <ErrorPopover
          title="Failed to resend consent form"
          {...errorPopoverProps}
          fixedToFooter
        />
        <SuccessPopover
          title="Success"
          {...successPopoverProps}
          fixedToFooter
        />
      </Section>
      <Section>
        <Button onClick={() => openModal()}>
          Generate Third Party Consent Form
        </Button>
      </Section>

      <Modal
        open={editorState.open}
        onClose={closeModal}
        title="Generate Third Party Consent Form"
      >
        <ContentFormGeneratorModal
          projectId={projectId}
          userId={userId}
          closeModal={closeModal}
        />
      </Modal>
    </UserThirdPartyListContainer>
  );
}
