import React, { useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import capitalize from 'lodash/capitalize';
import SyledTable from './styles';
import Table from '../../../../Table';
import RateCell from './RateCell';
import * as defaultPayrates from '../../../../../store/ducks/defaultPayrates';

const defaultColumns = [
  {
    key: 'state',
    accessor: 'state',
    Header: 'STATE',
    width: 90,
  },
  {
    key: 'tenant',
    accessor: 'tenant',
    Header: 'TENANT TYPE',
    width: 250,
    Cell: ({ cell: { value } }) => value.employmentType.label,
  },
  {
    key: 'businessUnit',
    accessor: 'businessUnit',
    Header: 'BUS. UNIT',
    width: 120,
  },
  {
    key: 'rateType',
    accessor: 'rateType',
    Header: 'RATE TYPE',
    width: 100,
    Cell: ({ cell: { value } }) => capitalize(`${value}`.toLowerCase()),
  },
  {
    key: 'rateValue',
    accessor: 'rateValue',
    Header: 'RATE',
    Cell: RateCell,
    width: 100,
    style: {
      textAlign: 'center',
    },
  },
];

export default function DefaultPayrateForm(props) {
  const { setFieldValue, hiringTarget } = props;

  const selectByHiringTarget = useMemo(
    () => defaultPayrates.selectByHiringTarget(hiringTarget),
    [hiringTarget],
  );

  const payrates = useSelector(selectByHiringTarget);

  const handleSelect = useCallback(
    (keys) => {
      const ids = keys.map((key) => payrates[key].countryPayRateId);
      setFieldValue('selectedPayrates', ids);
    },
    [payrates, setFieldValue],
  );

  const columns = useMemo(() => {
    if (payrates.findIndex((x) => x.state) === -1) {
      return defaultColumns.filter((c) => c.key !== 'state');
    }

    return defaultColumns;
  }, [payrates]);

  return (
    <SyledTable>
      <Table columns={columns} data={payrates} onSelect={handleSelect} />
    </SyledTable>
  );
}

DefaultPayrateForm.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  hiringTarget: PropTypes.number,
};

DefaultPayrateForm.defaultProps = {
  hiringTarget: '',
};
