import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import { v4 as uuid } from 'uuid';
import CheckFrame from './CheckFrame';
import { useModal } from '../Modal';
import {
  fetchUserScreeningDetails,
  selectDuplicates,
  selectIpCheck,
  selectMaliciousness,
  selectUserScreeningDetails,
  selectLoading,
} from '../../store/ducks/userScreeningDetails';
import store from '../../store';
import { IP_CHECK } from './helpers/checkFrameTypes';

function useHooksAsProps(outerProps) {
  const dispatch = useDispatch();
  const [modalProps, modalControls] = useModal();
  const { user, type } = outerProps;

  const ipCheck = useSelector(selectIpCheck);
  const maliciousness = useSelector(selectMaliciousness);
  const duplicates = useSelector(selectDuplicates);
  const originalData = useSelector(selectUserScreeningDetails);
  const loading = selectLoading(store.getState(), {
    userId: get(user, 'id', null),
  });

  useEffect(() => {
    if (user && type === IP_CHECK) {
      dispatch(fetchUserScreeningDetails({ userId: user.id }));
    }
  }, [dispatch, user, type]);

  const handleOpenModal = useCallback(() => {
    modalControls.openModal({ key: uuid() });
  }, [modalControls]);

  return {
    duplicates,
    ipCheck,
    maliciousness,
    originalData,
    loading,
    modalProps,
    modalControls,
    handleOpenModal,
  };
}

export default withHooks(useHooksAsProps)(CheckFrame);
