import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useFormikContext } from 'formik';
import { useSelector } from 'react-redux';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../../store/ducks/editableEntities';
import ActionFooter from '../../../../../ActionFooter';
import ErrorPopover, { useErrorPopover } from '../../../../../ErrorPopover';

export default function ResourceFormContainer(props) {
  const { closeModal, children } = props;

  const { values, isSubmitting, submitForm } = useFormikContext();

  const [showError, hideError, errorPopoverProps] = useErrorPopover();

  const { saving, saveError } = useSelector(
    editor.selectors.createSelectEditorSliceFor(
      EDITABLE_ENTITIES.PROJECT_RESOURCES,
    ),
  );

  useEffect(() => {
    if (saveError) {
      showError(new Error(saveError));
    }
  }, [saveError, showError]);

  useEffect(() => {
    hideError();
  }, [hideError, values]);

  return (
    <>
      {children}

      <ErrorPopover {...errorPopoverProps} />

      <ActionFooter
        rightButtons={[
          {
            type: 'secondary',
            text: 'Cancel',
            onClick: closeModal,
            disabled: isSubmitting || saving,
          },
          {
            type: 'primary',
            text: 'Save',
            onClick: submitForm,
            loading: isSubmitting || saving,
          },
        ]}
      />
    </>
  );
}
ResourceFormContainer.propTypes = {
  closeModal: PropTypes.func.isRequired,
  children: PropTypes.element.isRequired,
};
