import { call, put, takeLeading } from 'redux-saga/effects';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import {
  fetchConsentForm,
  fetchConsentFormSuccess,
  fetchConsentFormError,
} from '.';

function* fetchConsentFormSaga(action) {
  const { payload: consentFormToken } = action;

  try {
    const { data } = yield call(
      axios.get,
      endpoints.V2.CONSENT_FORM({
        query: {
          consentFormToken,
        },
      }),
    );

    yield put(fetchConsentFormSuccess(data));
  } catch (err) {
    yield put(fetchConsentFormError(err));
  }
}

export default takeLeading(fetchConsentForm.type, fetchConsentFormSaga);
