/* eslint-disable no-param-reassign */
import { createEntityAdapter, createSlice } from '@reduxjs/toolkit';
import undoable from 'redux-undo';
import { selectAll as selectAllDefaultPayrates } from '../defaultPayrates';
import { findById } from '../../../helpers/find';

const payratesAdapter = createEntityAdapter({
  selectId: (e) => e.id,
});

const slice = createSlice({
  name: 'payrates',
  initialState: payratesAdapter.getInitialState(),
  reducers: {
    fetchAll(state) {
      state.loading = true;
    },
    fetchFailed(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    setAll(state, action) {
      state.loading = false;

      return payratesAdapter.setAll(state, action);
    },
    addOne: {
      reducer: payratesAdapter.addOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    addMany: {
      reducer: payratesAdapter.addMany,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    removeOne: {
      reducer: payratesAdapter.removeOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    removeMany: payratesAdapter.removeMany,
    removeAll: payratesAdapter.removeAll,
    updateOne: {
      reducer: payratesAdapter.updateOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    updateMany: {
      reducer: payratesAdapter.updateMany,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    upsertOne: payratesAdapter.upsertOne,
    upsertMany: payratesAdapter.upsertMany,
    undoLastAction: () => {
      // no-op reducer used to call redux-undo
    },
  },
});

const {
  reducer,
  actions: {
    fetchAll,
    fetchFailed,
    setAll,
    addOne,
    addMany,
    removeOne,
    removeMany,
    removeAll,
    updateOne,
    updateMany,
    upsertOne,
    upsertMany,
    undoLastAction,
  },
} = slice;

const undoableReducer = undoable(reducer, {
  limit: 1,
  undoType: undoLastAction.type,
});

undoableReducer.key = 'payrates';

const {
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
} = payratesAdapter.getSelectors((state) => state[undoableReducer.key].present);

export default undoableReducer;

export function selectAllEager(state) {
  const defaultPayrates = selectAllDefaultPayrates(state);
  const payrates = selectAll(state);

  return payrates.map((rawPayrate) => ({
    ...rawPayrate,
    ...(findById(defaultPayrates, rawPayrate.id) || rawPayrate),
  }));
}

export function hasPayRates(state) {
  const ids = selectIds(state);

  return !!ids.length;
}

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
  undoLastAction,
};

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.payrates = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
