import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import * as Yup from 'yup';
import DemographicSettings from './DemographicSettings';
import {
  updateDemographicType,
  selectDemographicType,
  updateViewAs,
  selectViewAs,
  DEMOGRAPHIC_VIEW_AS,
  updateRequirements,
  selectRequirements,
  DEMOGRAPHIC_TYPE,
  hasRequirementsByGroup,
  hasAggregatedRequirements,
} from '../../../../../../store/ducks/projectDemographicRequirements';
import store from '../../../../../../store';
import {
  fetchAll as fetchAllRequirements,
  selectAll as selectAllRequirements,
  selectLoading as selectLoadingRequirements,
  selectError as selectErrorRequirements,
} from '../../../../../../store/ducks/demographicRequirements';
import mapToOptions from '../../../../../../pages/ProjectSetup/helpers/mapToOptions';

function useHooksAsProps(props) {
  const dispatch = useDispatch();
  const { values, setFieldValue, nextStep, isGoingBack } = props;

  // Fetch Requirements onMount
  useEffect(() => {
    dispatch(fetchAllRequirements());
  }, [dispatch]);

  // Get RequirementOptions
  const requirementOptions = useSelector(selectAllRequirements);
  const requirementOptionsLoading = useSelector(selectLoadingRequirements);
  const requirementOptionsError = useSelector(selectErrorRequirements);

  const setDemographicsType = useCallback(
    ({ value }) => {
      dispatch(updateDemographicType(value));
      if (
        value === DEMOGRAPHIC_TYPE.AGGREGATED &&
        Array.isArray(values.requirements) &&
        values.requirements.length > 3
      ) {
        setFieldValue('requirements', []);
      }
    },
    [dispatch, setFieldValue, values.requirements],
  );

  const setViewAs = useCallback(
    (viewAs) => {
      dispatch(
        updateViewAs(
          viewAs === 0
            ? DEMOGRAPHIC_VIEW_AS.PERCENTAGE
            : DEMOGRAPHIC_VIEW_AS.NUMBER,
        ),
      );
    },
    [dispatch],
  );

  const handleChange = useCallback(
    (value) => {
      setFieldValue('requirements', value);
    },
    [setFieldValue],
  );

  const handleRemove = useCallback(
    (id) => () => {
      setFieldValue(
        'requirements',
        values.requirements.filter((r) => r.value !== id),
      );
    },
    [setFieldValue, values.requirements],
  );

  const requirementsByGroup = useSelector(hasRequirementsByGroup);
  const aggregatedRequirements = useSelector(hasAggregatedRequirements);

  useEffect(() => {
    if (!isGoingBack && (requirementsByGroup || aggregatedRequirements)) {
      nextStep();
    }
    // We only want to run this on mount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return {
    setDemographicsType,
    demographicsType: useSelector(selectDemographicType),

    setViewAs,
    viewAs:
      useSelector(selectViewAs) === DEMOGRAPHIC_VIEW_AS.PERCENTAGE ? 0 : 1,

    requirementOptions: mapToOptions('id', 'name')(requirementOptions),
    requirementOptionsLoading,
    requirementOptionsError,

    handleChange,
    handleRemove,
  };
}

function mapPropsToValues() {
  return {
    requirements: selectRequirements(store.getState()),
  };
}

function handleSubmit(values, formikBag) {
  const {
    props: { nextStep },
  } = formikBag;

  store.dispatch(updateRequirements(values.requirements));

  nextStep();
}

const validationSchema = Yup.object().shape({
  requirements: Yup.array().min(1, 'Select at least one requirement'),
});

export default compose(
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema,
  }),
  withHooks(useHooksAsProps),
)(DemographicSettings);
