import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import useFirstChange from '../../helpers/useFirstChange';
import { setDirty } from '../../../../store/ducks/projectSetup';
import StepFooter from '../../../../components/StepFooter';
import RegistrationAndQualificationForm from '../../../../components/ProjectForms/RegistrationAndQualificationForm/RegistrationAndQualificationForm';
import { useNavigationContext } from '../../helpers/navigationContext';

export default function RegistrationAndQualification(props) {
  const { setFieldValue, values, submitForm } = props;

  const { goBack } = useNavigationContext();

  const dispatch = useDispatch();

  function submitAndNavigate() {
    setFieldValue('navigate', true);
    submitForm();
  }

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    values,
  );

  return (
    <>
      <WizardTitle>Registration and Qualification</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <RegistrationAndQualificationForm />

      <WizardFooter>
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: Recruitment"
        />
      </WizardFooter>
    </>
  );
}

RegistrationAndQualification.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
};
