import shortid from 'shortid';
import { get } from 'lodash';

export function createDTOToProcessModuleMapper(processModules) {
  return function mapDTOToProcessModule(step, index) {
    return {
      id: step.id,
      stepDefinitionId: step.stepDefinition.id,
      name: step.stepDefinition.name,
      sequenceFlow: step.stepDefinition.sequenceFlow,
      description: step.stepDefinition.description,
      key: get(processModules, `${index}.key`, shortid.generate()),
      attributes: step.attributes || [],
      subSteps: step.subSteps || [],
    };
  };
}

export function mapProcessModuleToDTO(processStep, index) {
  return {
    ...(processStep.id ? { id: processStep.id } : {}),
    order: index + 1,
    stepDefinition: {
      id: processStep.stepDefinitionId,
      sequenceFlow: processStep.sequenceFlow,
    },
    attributes: processStep.attributes || [],
    subSteps: processStep.subSteps || [],
  };
}
