import { withFormik } from 'formik';
import { compose } from 'recompose';
import { connect } from 'react-redux';
import RegistrationAndQualification from './RegistrationAndQualification';
import { registrationAndQualificationYupValidations } from '../../../../components/ProjectForms/RegistrationAndQualificationForm';
import {
  patchProject,
  selectRegistrationAndQualification,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  const registrationAndQualification = selectRegistrationAndQualification(
    state,
  );

  return {
    initialValues: registrationAndQualification,
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  payload.registrationProcessTemplateId = values.registrationProcessTemplate;
  payload.qualificationProcessTemplateId = values.qualificationProcessTemplate;

  const nextStep = STEP_NUMBER.REGISTRATION_AND_QUALIFICATION + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues: ({ initialValues }) => initialValues,
    enableReinitialize: true,
    validationSchema: registrationAndQualificationYupValidations,
  }),
)(RegistrationAndQualification);
