import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from 'react-router-dom';
import ProjectSetup from './ProjectSetup';
import Demographics from './Demographics';
import Projects from './Projects';
import ProjectView from './ProjectView';
import ConsentForm from './ConsentForm';
import ProfileQuestions from './ProfileQuestions';
import TargetBuilder from '../components/TargetBuilder/Page';
import { Provider as DemographicsViewTypeProvider } from '../helpers/demographics-view-type-context';
import CloneProvider from './ProjectSetup/helpers/cloneContext';
import UserThirdPartyList from './UserThirdPartyList';
import RecruitingAutomation from './RecruitingAutomation';

function ViewDemographics(routerProps) {
  return (
    <DemographicsViewTypeProvider value={{ viewType: 'edit' }}>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <Demographics {...routerProps} />
    </DemographicsViewTypeProvider>
  );
}

function DemographicMetrics(routerProps) {
  return (
    <DemographicsViewTypeProvider value={{ viewType: 'metrics' }}>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <Demographics {...routerProps} />
    </DemographicsViewTypeProvider>
  );
}

function CloneProject(routerProps) {
  return (
    <CloneProvider>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <ProjectSetup {...routerProps} cloneProject />
    </CloneProvider>
  );
}

export default function Pages() {
  return (
    <>
      <Router>
        <Switch>
          <Route
            exact
            path="/third-party-list/:projectId/:userId"
            component={UserThirdPartyList}
          />
          <Route exact path="/wizard" component={ProjectSetup} />
          <Route exact path="/wizard/:projectID" component={ProjectSetup} />
          <Route exact path="/clone/:projectID" component={CloneProject} />
          <Route
            exact
            path="/demographics/:projectID"
            component={ViewDemographics}
          />
          <Route
            exact
            path="/demographics-metrics/:projectID"
            component={DemographicMetrics}
          />
          <Route exact path="/projects" component={Projects} />
          <Route
            exact
            path="/projects/view/:projectID"
            component={ProjectView}
          />
          <Route exact path="/consent-form/:token" component={ConsentForm} />
          <Route exact path="/target-builder" component={TargetBuilder} />
          <Route
            exact
            path="/master-profile-admin"
            component={ProfileQuestions}
          />
          <Route
            exact
            path="/user-screening"
            component={RecruitingAutomation}
          />
          <Redirect exact from="/" to="/wizard" />
        </Switch>
      </Router>
    </>
  );
}
