import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { InputBase } from '../../../../../../../Input/styles';

export default function InputCell({
  cell: {
    value: { hiringTargetID, value },
  },
  row: {
    original: { groupID, handleFieldChange },
  },
}) {
  const onChange = useCallback(
    (e) => {
      handleFieldChange(groupID, hiringTargetID, `${e.target.value}`);
    },
    [groupID, handleFieldChange, hiringTargetID],
  );

  return (
    <InputBase
      key={hiringTargetID}
      placeholder="0"
      onChange={onChange}
      value={value || ''}
      data-testid={`demographics-modal--input-${groupID}-${hiringTargetID}`}
    />
  );
}

InputCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.shape({
      hiringTargetID: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      value: PropTypes.string,
    }).isRequired,
  }).isRequired,
  row: PropTypes.shape({
    original: PropTypes.shape({
      groupID: PropTypes.string.isRequired,
      handleFieldChange: PropTypes.func.isRequired,
    }).isRequired,
  }).isRequired,
};
