import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useCallback } from 'react';
import withHooks from 'hook-hoc';
import {
  fetchDemographics,
  selectDemographicType,
  selectRequirements,
  selectLoading,
  DEMOGRAPHIC_TYPE,
  removeRequirements,
  hasAnyDemographicData,
} from '../../../../store/ducks/projectDemographicRequirements';
import Demographics from './Demographics';
import { useModal } from '../../../Modal';

function useHooksAsProps(props) {
  const {
    shouldHaveDemographics, // checkbox is checked?
    hasDemographics, // user actually added some demographics?
    submitCount, // did the user tried to submit the form?
  } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchDemographics());
  }, [dispatch]);

  const [modalProps, modalControls] = useModal();

  const removeDemographicRequirements = useCallback(() => {
    dispatch(removeRequirements());
  }, [dispatch]);

  return {
    requirements: useSelector(selectRequirements)
      .map((r) => r.label)
      .join(', '),
    demographicType:
      useSelector(selectDemographicType) === DEMOGRAPHIC_TYPE.BY_GROUP
        ? 'REQUIREMENTS BY GROUP'
        : 'AGGREGATED REQUIREMENTS',
    loading: useSelector(selectLoading),
    renderError: shouldHaveDemographics && !hasDemographics && submitCount >= 1,
    modalControls,
    modalProps,
    hasDemographics: useSelector(hasAnyDemographicData),
    removeDemographicRequirements,
  };
}

export default withHooks(useHooksAsProps)(Demographics);
