import { put, select, take, race } from 'redux-saga/effects';

export default function* fetchEntity(
  fetchActionCreator,
  onSuccessActionCreator,
  onErrorActionCreators,
  selector,
) {
  // Puts the fetch action
  yield put(fetchActionCreator());

  // wait for it to resolve
  const { reject } = yield race({
    resolve: take(onSuccessActionCreator),
    reject: take(onErrorActionCreators),
  });

  // If an error occured, we break the saga
  if (reject) {
    throw new ReferenceError(
      'Failed to fetch demographic requirements from server.',
    );
  }

  // We use the selector because their might be transformations
  // aplied by the request success action
  const result = yield select(selector);

  return result;
}
