import React from 'react';
import get from 'lodash/get';
import PropTypes from 'prop-types';
// import Button from '../../../components/Button';
import HorizontalFlex from '../../../components/HorizontalFlex';
import Input from '../../../components/Input';
import Select from '../../../components/Select';
import Icon from '../../../components/Icon';
import { ReactComponent as SearchIcon } from '../../../assets/icons/search.svg';
import { ReactComponent as CloseInput } from '../../../assets/icons/close-input.svg';
import { InputContainer, Spacer } from './styles';
import { Label } from '../../../styles';
import CategoryModal from '../CategoryModal';

export default function Filters(props) {
  const {
    handleInputChange,
    handleInputClear,
    handleInputClearKeydown,
    handleCategoryChange,
    categories,
    categoryOptions,
    filters,
    profileQuestions,
    // handleOpenModal,
    modalProps,
    modalControls,
  } = props;

  const topic = get(filters, 'topic', '');
  const category = get(filters, 'category', '*');

  const resultText = profileQuestions.length === 1 ? 'RESULT' : 'RESULTS';

  return (
    <HorizontalFlex
      hideDivider
      leftNodes={
        <>
          <InputContainer minWidth={320}>
            <Input
              iconBefore={
                <Icon
                  icon={<SearchIcon />}
                  inheritColor={false}
                  style={{ position: 'relative', top: 2 }}
                />
              }
              iconAfter={
                topic.length > 0 && (
                  <Icon
                    icon={<CloseInput />}
                    inheritColor={false}
                    iconButton
                    onClick={handleInputClear}
                    onKeyDown={handleInputClearKeydown}
                    role="button"
                    tabIndex={0}
                  />
                )
              }
              name="topics"
              placeholder="Search topics"
              onChange={handleInputChange}
              value={topic}
            />
          </InputContainer>
          <Spacer />
          <InputContainer minWidth={220}>
            <Select
              name="category"
              placeholder="All Categories"
              options={categoryOptions}
              loading={categories.loading}
              error={categories.error}
              onChange={handleCategoryChange}
              value={category}
            />
          </InputContainer>
          <Spacer width={24} />
          <InputContainer minWidth={220} paddingTop={12}>
            <Label>
              {profileQuestions.length} {resultText}
            </Label>
          </InputContainer>
          <Spacer width={158} />
          <InputContainer minWidth={220}>
            {/* <Button type="secondary" marginRight={16} onClick={handleOpenModal}>
              Add New Category
            </Button> */}
          </InputContainer>
          <CategoryModal
            modalProps={modalProps}
            modalControls={modalControls}
          />
        </>
      }
    />
  );
}

Filters.propTypes = {
  handleInputChange: PropTypes.func.isRequired,
  handleInputClear: PropTypes.func.isRequired,
  handleInputClearKeydown: PropTypes.func.isRequired,
  handleCategoryChange: PropTypes.func.isRequired,
  categories: PropTypes.shape({
    loading: PropTypes.bool,
    error: PropTypes.oneOfType([
      PropTypes.oneOf([null]),
      PropTypes.instanceOf(Error),
    ]),
  }).isRequired,
  categoryOptions: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      label: PropTypes.string,
    }),
  ).isRequired,
  filters: PropTypes.shape({
    topic: PropTypes.string,
    category: PropTypes.string,
  }).isRequired,
  profileQuestions: PropTypes.arrayOf(PropTypes.object).isRequired,
  // handleOpenModal: PropTypes.func.isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
};
