/* eslint-disable react/jsx-props-no-spreading, import/no-cycle */
import React, { useMemo } from 'react';
import { Field } from 'formik';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import QuestionBuilder from '../QuestionBuilder';
import Separator from './Separator';
import Switch from '../../../Switch';
import { Label } from '../../../../styles';
import {
  SecondaryContainer,
  SecondarySwitchWrapper,
  SecondaryTextWrapper,
} from './styles';

export default function SecondaryQuestions(props) {
  const {
    data,
    values: { enabled },
    setFieldValue,
    item,
  } = props;

  const fieldBaseName = `enabled[${data.id}]`;

  const logic = get(data, ['parentQuestion', 'subQuestionLogic'], 'IF');
  const answerValue = get(data, ['parentQuestion', 'parentAnswerValue'], 'Yes');

  const label = useMemo(
    () => (logic === 'IF' ? `IF: ${answerValue}` : `IF NOT: ${answerValue}`),
    [answerValue, logic],
  );

  return (
    <>
      <Separator name={item.name} description={item.question} label={label} />
      <SecondaryContainer>
        <SecondarySwitchWrapper>
          <Switch
            name={fieldBaseName}
            value={!enabled[data.id]}
            onChange={({ target: { value } }) =>
              setFieldValue(fieldBaseName, !value)
            }
          />
        </SecondarySwitchWrapper>
        <SecondaryTextWrapper>
          <Label>Disable secondary question</Label>
        </SecondaryTextWrapper>
      </SecondaryContainer>
      {enabled[data.id] && (
        <Field
          {...props}
          component={QuestionBuilder}
          data={data}
          items={item.items}
        />
      )}
    </>
  );
}

SecondaryQuestions.propTypes = {
  id: PropTypes.number.isRequired,
  data: PropTypes.shape({
    id: PropTypes.number,
    questionType: PropTypes.string,
  }).isRequired,
  values: PropTypes.shape({
    enabled: PropTypes.object,
  }).isRequired,
  setFieldValue: PropTypes.func.isRequired,
  item: PropTypes.shape({
    name: PropTypes.string,
    question: PropTypes.string,
    items: PropTypes.array,
  }).isRequired,
};

SecondaryQuestions.defaultProps = {};
