import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import get from 'lodash/get';
import { capitalizeWordsOrDot } from '../CellWithDefaultDot';
import { UserGroupCellContainer } from './styles';
import { ENTITIES, selectors } from '../../../../../store/ducks/options';

export default function UserGroupCell(props) {
  const {
    row: {
      original: { userGroup: defaultUserGroup, userGroupId },
    },
  } = props;

  const userGroup = useSelector(
    selectors.createSelectById(ENTITIES.userGroups, null, userGroupId || 1),
  );

  const groupName = useMemo(() => get(userGroup, 'name', defaultUserGroup), [
    defaultUserGroup,
    userGroup,
  ]);

  return (
    <UserGroupCellContainer>
      {capitalizeWordsOrDot(groupName)}
    </UserGroupCellContainer>
  );
}

UserGroupCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      userGroupId: PropTypes.number.isRequired,
      userGroup: PropTypes.string,
    }).isRequired,
  }).isRequired,
};
