import React from 'react';
import noop from 'lodash/noop';
import PropTypes from 'prop-types';
import Table from '../../../../components/Table';
import TopicsCell from './Cells/TopicsCell';
import RepeatCell from './Cells/RepeatCell';
import ActionsCell from './Cells/ActionsCell';
import ExpanderCell from './Cells/ExpanderCell';

export default function CardEditMode(props) {
  const { topics } = props;

  const style = { padding: '21px 0 19px 0' };

  const columns = [
    {
      key: 'topics',
      accessor: 'topics',
      Header: 'Topics',
      disableSortBy: true,
      width: 80,
      Cell: TopicsCell,
      style,
    },
    {
      key: 'timer',
      accessor: 'timer',
      Header: 'Repeat',
      disableSortBy: true,
      width: 10,
      Cell: RepeatCell,
      style,
    },
    {
      key: 'actions',
      accessor: 'actions',
      Header: () => null,
      disableSortBy: true,
      width: 4,
      Cell: ActionsCell,
      style,
    },
    {
      id: 'expander',
      key: 'expander',
      accessor: 'expander',
      width: 4,
      Header: () => null,
      disableSortBy: true,
      Cell: ExpanderCell,
      style,
    },
  ];

  return (
    <>
      <Table columns={columns} data={topics} renderExpandedComponent={noop} />
    </>
  );
}

CardEditMode.propTypes = {
  topics: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      description: PropTypes.string,
      answers: PropTypes.arrayOf(PropTypes.object),
      questionType: PropTypes.string,
      parentId: PropTypes.number,
    }),
  ),
};

CardEditMode.defaultProps = {
  topics: [],
};
