import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import StepFooter from '../../../../components/StepFooter';
import {
  WizardDescription,
  WizardFooter,
  WizardTitle,
} from '../../../../components/Wizard';
import { useNavigationContext } from '../../helpers/navigationContext';
import { selectProjectTools } from '../../../../store/ducks/project';
import ForExternalProject from './ForExternalProject';
import ForInternalProject from './ForInternalProject';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../components/ErrorPopover';
import { selectSaveError } from '../../../../store/ducks/experimentGroups';

export default function ProjectAccess(props) {
  const {
    setFieldValue,
    submitForm,
    submitCount,
    showError,
    errorPopoverProps,
    experimentId,
  } = props;

  const { goBack } = useNavigationContext();

  const submitAndNavigate = useCallback(() => {
    setFieldValue('navigate', true);
    submitForm();
  }, [setFieldValue, submitForm]);

  const { hasExternalSystem } = useSelector(selectProjectTools);

  const error = useSelector(selectSaveError);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  return (
    <div>
      <WizardTitle>Project Access</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      {hasExternalSystem ? (
        <ForExternalProject experimentId={experimentId} />
      ) : (
        <ForInternalProject />
      )}

      <WizardFooter>
        <ErrorPopover {...errorPopoverProps} />
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: User Project Page"
        />
      </WizardFooter>
    </div>
  );
}

ProjectAccess.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  submitCount: PropTypes.number.isRequired,
  showError: PropTypes.func.isRequired,
  experimentId: PropTypes.number.isRequired,
};
