import { connect } from 'react-redux';
import SupportTeam from './SupportTeam';
import * as teamTypes from '../../../../../../store/ducks/teamTypes';
import { findById, findByValue } from '../../../../../../helpers/find';
import { buildInternalUserName } from '../../../../../../components/ProjectForms/RecruitmentForm/HiringTargets/helpers';
import { ENTITIES, selectors } from '../../../../../../store/ducks/options';
import { selectSupportTeam } from '../../../../../../store/ducks/project';

function mapSupportMember(state) {
  const internalUsersData = selectors.selectDataFor(
    ENTITIES.internalUsers,
    null,
    state,
  );

  const teamTypesData = teamTypes.selectAll(state);

  return ({ role, member }) =>
    `${
      (findByValue(teamTypesData, role) || {}).label || ''
    }: ${buildInternalUserName(findById(internalUsersData, member))}`;
}

function mapStateToProps(state) {
  const supportTeam = selectSupportTeam(state);

  const { supportTeam: supportTeamData } = supportTeam;

  return {
    supportTeam: (supportTeamData || []).map(mapSupportMember(state)),
  };
}

export default connect(mapStateToProps)(SupportTeam);
