import { call, put, takeLatest } from 'redux-saga/effects';
import {
  fetchScreeningMetrics,
  fetchScreeningMetricsError,
  fetchScreeningMetricsSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import { userSvcAPI } from '../../../services/axios';

function* fetchScreeningMetricsSagaFn() {
  try {
    const response = yield call(
      userSvcAPI.get,
      ENDPOINTS.USER_SCREENING.USER_SCREENING_NUMBERS(),
    );

    yield put(fetchScreeningMetricsSuccess(response.data));
  } catch (error) {
    yield put(fetchScreeningMetricsError(error));
  }
}

export default takeLatest(
  fetchScreeningMetrics.type,
  fetchScreeningMetricsSagaFn,
);
