/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const slice = createSlice({
  name: 'userThirdPartyList',

  initialState: {
    data: {},
    loading: false,
    error: null,
  },

  reducers: {
    fetchAll: (state) => ({
      ...state,
      loading: true,
    }),

    setAll: (state, action) => {
      state.loading = false;
      state.data = action.payload;
    },

    fetchFailed: (state, action) => ({
      ...state,
      loading: false,
      error: action.payload,
    }),

    saveUserThirdPartyList: (state) => {
      state.saving = true;
      state.saveError = null;
      state.error = null;
    },

    saveUserThirdPartyListSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.error = null;
      state.data = action.payload;
    },

    saveUserThirdPartyListError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
      state.error = action.payload;
    },

    resendEmail: (state) => {
      state.saving = true;
      state.resendSuccess = null;
      state.resendError = null;
    },

    resendEmailSuccess: (state, action) => {
      state.saving = false;
      state.resendError = null;
      state.resendSuccess = action.payload.id;

      const updatedIndex = state.data.findIndex(
        (tpc) => tpc.id === action.payload.id,
      );

      if (updatedIndex !== -1) {
        state.data = [
          ...state.data.slice(0, updatedIndex),
          action.payload,
          ...state.data.slice(updatedIndex + 1),
        ];
      }
    },

    resendEmailError: (state, action) => {
      state.saving = false;
      state.resendSuccess = null;
      state.resendError = action.payload;
    },
  },
});

const {
  actions: {
    fetchAll,
    fetchFailed,
    setAll,
    saveUserThirdPartyList,
    saveUserThirdPartyListSuccess,
    saveUserThirdPartyListError,
    resendEmail,
    resendEmailSuccess,
    resendEmailError,
  },
  reducer,
} = slice;

reducer.key = 'userThirdPartyList';

export default reducer;

export {
  fetchAll,
  fetchFailed,
  setAll,
  saveUserThirdPartyList,
  saveUserThirdPartyListSuccess,
  saveUserThirdPartyListError,
  resendEmail,
  resendEmailSuccess,
  resendEmailError,
};

function selectUserThirdPartyList(state) {
  return state[reducer.key];
}

export function selectAll(state) {
  const userThirdPartyList = selectUserThirdPartyList(state);

  return [].concat(
    ...Object.keys(userThirdPartyList.data).map(
      (key) => userThirdPartyList.data[key],
    ),
  );
}

export function selectLoading(state) {
  return selectUserThirdPartyList(state).loading;
}

export function selectError(state) {
  return selectUserThirdPartyList(state).error;
}

export function selectById(state, id) {
  return selectAll(state).find(
    (userThirdPartyList) => userThirdPartyList.id === id,
  );
}

export function selectSaving(state) {
  return selectUserThirdPartyList(state).saving;
}

export function selectSaveError(state) {
  return selectUserThirdPartyList(state).saveError;
}

export function selectResendError(state) {
  return selectUserThirdPartyList(state).resendError;
}

export function selectResendSuccess(state) {
  return selectUserThirdPartyList(state).resendSuccess;
}

export function selectSaveSuccess(state) {
  const { saving, saveError } = selectUserThirdPartyList(state) || {};
  return !saving && !saveError;
}

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.userThirdPartyList = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
