/* eslint-disable react/require-default-props, import/no-unresolved */
import React from 'react';
import PropTypes from 'prop-types';
import { Slide, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { ReactComponent as CloseIcon } from '../../assets/icons/close.svg';
import { CloseButtonStyle, ToastWrapper } from './styles';

const CloseButton = ({ closeToast }) => (
  <CloseButtonStyle className="close-button">
    <CloseIcon onClick={closeToast} alt="Close alert" />
  </CloseButtonStyle>
);

CloseButton.propTypes = {
  closeToast: PropTypes.func.isRequired,
};

export const alert = toast;

export const alertSuccess = (message, props) => {
  return alert.success(message, {
    toastId: Math.random(),
    ...props,
  });
};

export const alertError = (message, props) => {
  return alert.error(message, {
    toastId: Math.random(),
    ...props,
  });
};

export const alertWarning = (message, props) => {
  return alert.warning(message, {
    toastId: Math.random(),
    ...props,
  });
};

export function Alert(props) {
  const {
    position,
    autoClose,
    closeButton,
    hideProgressBar,
    pauseOnHover,
    closeOnClick,
    newestOnTop,
    className,
    style,
    toastClassName,
    bodyClassName,
    progressClassName,
    progressStyle,
    transition,
    rtl,
    draggable,
    draggablePercent,
    pauseOnFocusLoss,
    enableMultiContainer,
    containerId,
    role,
    onClick,
  } = props;

  return (
    <ToastWrapper>
      <ToastContainer
        position={position}
        autoClose={autoClose}
        closeButton={closeButton}
        hideProgressBar={hideProgressBar}
        pauseOnHover={pauseOnHover}
        closeOnClick={closeOnClick}
        newestOnTop={newestOnTop}
        className={className}
        style={style}
        toastClassName={toastClassName}
        bodyClassName={bodyClassName}
        progressClassName={progressClassName}
        progressStyle={progressStyle}
        transition={transition}
        rtl={rtl}
        draggable={draggable}
        draggablePercent={draggablePercent}
        pauseOnFocusLoss={pauseOnFocusLoss}
        enableMultiContainer={enableMultiContainer}
        containerId={containerId}
        role={role}
        onClick={onClick}
      />
    </ToastWrapper>
  );
}

Alert.propTypes = {
  position: PropTypes.oneOf([
    'top-left',
    'top-right',
    'top-center',
    'bottom-left',
    'bottom-right',
    'bottom-center',
  ]),
  autoClose: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),
  closeButton: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.bool,
    PropTypes.func,
  ]),
  hideProgressBar: PropTypes.bool,
  pauseOnHover: PropTypes.bool,
  closeOnClick: PropTypes.bool,
  newestOnTop: PropTypes.bool,
  className: PropTypes.string,
  // eslint-disable-next-line react/forbid-prop-types
  style: PropTypes.object,
  toastClassName: PropTypes.string,
  bodyClassName: PropTypes.string,
  progressClassName: PropTypes.string,
  // eslint-disable-next-line react/forbid-prop-types
  progressStyle: PropTypes.object,
  transition: PropTypes.func,
  rtl: PropTypes.bool,
  draggable: PropTypes.bool,
  draggablePercent: PropTypes.number,
  pauseOnFocusLoss: PropTypes.bool,
  enableMultiContainer: PropTypes.bool,
  containerId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  role: PropTypes.string,
  onClick: PropTypes.func,
};

Alert.defaultProps = {
  position: 'top-right',
  transition: Slide,
  rtl: false,
  autoClose: 5000,
  hideProgressBar: false,
  closeButton: CloseButton,
  pauseOnHover: true,
  pauseOnFocusLoss: true,
  closeOnClick: true,
  newestOnTop: false,
  draggable: false,
  draggablePercent: 80,
  role: 'alert',
};
