import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import * as yup from 'yup';
import { requiredMessage } from '../../../../../../pages/ProjectSetup/Steps/shared';
import {
  EDITABLE_ENTITIES,
  editor,
} from '../../../../../../store/ducks/editableEntities';
import {
  ENTITIES,
  ENTITY_METADATA,
  fetch,
} from '../../../../../../store/ducks/options';
import SurveyResourceForm from './SurveyResourceForm';

export default compose(
  connect((state) => ({
    resource: editor.selectors.selectEditorDataFor(
      EDITABLE_ENTITIES.PROJECT_RESOURCES,
      state,
    ),
  })),
  withFormik({
    mapPropsToValues: ({ resource }) => {
      if (resource) {
        return {
          ...resource,
          unlimited: resource.maxQuizAttempts === -1,
          allQuestions: resource.subsetSize === -1,
        };
      }

      return {};
    },
    handleSubmit: async (values, { props: { dispatch, projectId } }) => {
      dispatch(
        editor.actions.save(
          EDITABLE_ENTITIES.PROJECT_RESOURCES,
          {
            type: ENTITY_METADATA.projectResources.types.survey,
            ...values,
          },
          {
            projectId,
            thenPut: fetch({
              optionsFor: ENTITIES.projectResources,
              nestedKey: projectId,
            }),
          },
        ),
      );
    },
    validationSchema: yup.object().shape({
      resourceId: yup.number().required(requiredMessage`Survey`),
      maxQuizAttempts: yup.string().when('unlimited', {
        is: (value) => !value,
        then: yup
          .string()
          .min(1)
          .required(requiredMessage`Maximum Attemps`),
        otherwise: yup.string().notRequired(),
      }),
      subsetSize: yup.string().when('allQuestions', {
        is: (value) => !value,
        then: yup
          .string()
          .min(1)
          .required(requiredMessage`Subset size`),
        otherwise: yup.string().notRequired(),
      }),
    }),
  }),
)(SurveyResourceForm);
