import React from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import {
  Container,
  Sidebar,
  Content,
  SidebarItem,
  Tick,
  StepNumber,
  SidebarStepTitle,
  Loading,
} from './styles';
import { themePropTypes } from '../../styles/themes/default';

export default function Wizard(props) {
  const {
    steps,
    currentStep,
    lastEditableStep,
    children,
    navigateTo,
    theme,
  } = props;

  function renderItem(item, index) {
    const active = index === currentStep;
    const isClickable = index <= lastEditableStep;

    return (
      <SidebarItem
        key={item.title}
        active={active}
        isFirst={index === 0}
        isClickable={isClickable}
        onClick={() => isClickable && navigateTo(index)}
        data-testid={`wizard-step-active-${active}`}
        // isLast={index === (length - 1)}
      >
        <StepNumber>{`0${index + 1}`}</StepNumber>
        <SidebarStepTitle>{item.title}</SidebarStepTitle>

        {item.loading && (
          <Loading>
            <Shimmer width={20} height={20} />
          </Loading>
        )}

        {!item.loading && item.complete && (
          <Tick data-testid={`wizard-tick--${index}`}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="13"
              height="10"
              viewBox="0 0 13 10"
              data-testid="tick"
            >
              <path
                fill={theme.colors.azure}
                fillRule="nonzero"
                d="M11.581 1.24l.178.179a1 1 0 0 1 0 1.414L5.211 9.381l-.052.05a1.126 1.126 0 0 1-1.644.055L.33 6.3a1.126 1.126 0 0 1 1.592-1.592l2.389 2.388 5.856-5.855a1 1 0 0 1 1.414 0z"
              />
            </svg>
          </Tick>
        )}
      </SidebarItem>
    );
  }

  return (
    <Container>
      <Sidebar>{(steps || []).map(renderItem)}</Sidebar>

      <Content>{children}</Content>
    </Container>
  );
}

Wizard.propTypes = {
  steps: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      complete: PropTypes.bool.isRequired,
      loading: PropTypes.bool,
    }),
  ).isRequired,

  currentStep: PropTypes.number.isRequired,
  lastEditableStep: PropTypes.number.isRequired,
  navigateTo: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.node])),
  ]).isRequired,
  theme: themePropTypes.isRequired,
};
