import React from 'react';
import PropTypes from 'prop-types';

// styles
import {
  ProgressBar as ProgressBarContainer,
  ProgressFiller,
  ProgressLabel,
} from './styles';

export default function ProgressBar(props) {
  const {
    progressshowAsNumeric,
    progressPercentage,
    progressLabelColor,
    progressColor,
    width,
  } = props;
  return (
    <ProgressBarContainer width={width}>
      <ProgressFiller
        progress={Math.min(Math.max(progressPercentage, 0), 100)}
        progressColor={progressColor}
      />
      {progressPercentage >= 0 && (
        <ProgressLabel
          emptyState={progressPercentage === 0}
          color={progressLabelColor}
        >
          {progressshowAsNumeric
            ? parseFloat(progressPercentage / 100).toFixed(1)
            : `${Math.ceil(progressPercentage)}%`}
        </ProgressLabel>
      )}
    </ProgressBarContainer>
  );
}

ProgressBar.propTypes = {
  progressshowAsNumeric: PropTypes.bool,
  progressPercentage: PropTypes.number,
  progressColor: PropTypes.string,
  progressLabelColor: PropTypes.string,
  width: PropTypes.string,
};

ProgressBar.defaultProps = {
  progressshowAsNumeric: false,
  progressPercentage: 0,
  progressColor: undefined,
  progressLabelColor: undefined,
  width: undefined,
};
