import styled from 'styled-components';
import { darken } from 'polished';
import { Label } from '../../../styles';

function shouldHide(props) {
  if (props.isHidden) {
    return props.isHovered ? 1 : 0;
  }

  return 1;
}

export const Container = styled.div`
  min-height: 474px;
  padding: 0 32px;
  background-color: ${(props) =>
    props.isHovered
      ? darken(0.02, props.theme.colors.paleGrey)
      : props.theme.colors.paleGrey};

  padding-bottom: ${(props) => (props.isHovered ? '150px' : '0px')};
  transition: padding 225ms ease;
`;

export const AddStepBox = styled(Label)`
  border: ${(props) =>
    props.isHovered
      ? `solid 1px ${props.theme.colors.azure}`
      : `dashed 1px ${props.theme.colors.lightBlueGrey}`};
  color: ${(props) =>
    props.isHovered ? props.theme.colors.azure : props.theme.colors.blueGrey};
  opacity: ${(props) => shouldHide(props)};
  height: 102px;
  line-height: 102px;
  text-align: center;
  cursor: initial;
  border-radius: 4px;
  max-width: 624px;
`;

export const Spacer = styled.div`
  height: 24px;
`;

export const PopoverContainer = styled.div``;

export const Wrapper = styled.div`
  min-height: 76px;
  width: 100%;
  padding: 139px 0 30px 0;
`;

export const Popover = styled.div`
  border-radius: 4px;
  box-shadow: 1px 1px 5px 0 ${(props) => props.theme.colors.lightPeriwinkle};
  border: solid 1px ${(props) => props.theme.colors.warning};
  padding: 16px;
  background-color: rgba(250, 206, 112, 0.1);
  color: #2f3838;
  position: relative;
  line-height: 1.5;
`;

export const ErrorTitle = styled(Label)`
  color: #2f3838;
  cursor: default;
`;

export const Img = styled.img`
  position: absolute;
  right: 16px;
  top: 16px;
  cursor: pointer;
`;
