import { call, put, takeLatest } from 'redux-saga/effects';
import {
  fetchDataConsentForm,
  fetchDataConsentFormError,
  fetchDataConsentFormSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* fetchDataConsentFormSagaFn(action) {
  const {
    payload: { projectId },
  } = action;

  try {
    const response = yield call(
      axios.get,
      ENDPOINTS.V2.PROJECT_LEVEL_DATA_CONSENT_FORM({
        query: {
          projectId,
        },
      }),
    );

    yield put(fetchDataConsentFormSuccess(response.data));
  } catch (error) {
    yield put(fetchDataConsentFormError(error));
  }
}

export default takeLatest(
  fetchDataConsentForm.type,
  fetchDataConsentFormSagaFn,
);
