/* eslint-disable import/no-cycle */
import { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import { compose } from 'recompose';
import get from 'lodash/get';
import MultiSelect from './MultiSelect';
import { ENTITIES, fetch, selectors } from '../../../../../store/ducks/options';
import mapToMacroLocaleLanguages from './helpers';

const getFormattedOptions = (questionType, answerList, options) => {
  try {
    const candidates = options || [];
    const id = get(answerList, [0, 'id'], null);

    if (questionType === 'COUNTRY_LIST') {
      return [
        { value: '*', label: 'All Countries', id, questionType },
        ...candidates.filter(({ value }) => value !== '*'),
      ].map((country) => ({ ...country, id, questionType }));
    }
    if (questionType === 'LANGUAGE_LIST') {
      return mapToMacroLocaleLanguages(
        id,
        questionType,
        candidates.filter(({ value }) => value !== '*'),
      );
    }
    return answerList.map((answer) => ({
      ...answer,
      id: answer.id,
      value: answer.id,
      label: answer.answer,
      questionType,
    }));
  } catch (e) {
    return [];
  }
};

function useHooksAsProps(outerProps) {
  const {
    data: { questionType, answerList },
    setFieldValue,
    values: { answers },
  } = outerProps;

  const dispatch = useDispatch();

  const stateKey = {
    COUNTRY_LIST: ENTITIES.countries,
    LANGUAGE_LIST: ENTITIES.localeLanguages,
    DROPDOWN_SELECTION: null,
  }[questionType];

  useEffect(() => {
    if (stateKey) {
      dispatch(fetch(stateKey));
    }
  }, [dispatch, stateKey]);

  const data = useSelector(selectors.createSelectSliceFor(stateKey));
  const options = getFormattedOptions(questionType, answerList, data.data);

  const handleRemove = useCallback(
    (answerId, value) => {
      setFieldValue(
        `answers[${answerId}]`,
        answers[answerId].filter((answer) => answer.value !== value),
      );
    },
    [setFieldValue, answers],
  );

  const calculateCount = useCallback((value) => value.length, []);

  return {
    options,
    calculateCount,
    handleRemove,
  };
}

export default compose(withHooks(useHooksAsProps))(MultiSelect);
