import { useEffect } from 'react';
import { connect, useDispatch } from 'react-redux';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';

import SideModules from './SideModules';

import {
  fetchModules,
  selectTemplateProcessModules,
} from '../../../store/ducks/processBuilder';

import {
  fetchModules as fetchTargetBuilderModules,
  fetchQuestionCategories,
  selectTargetBuilderModules,
  selectQuestionCategories,
} from '../../../store/ducks/targetBuilder';

function useHooksAsProps(props) {
  const dispatch = useDispatch();

  const { processType } = props;

  useEffect(() => {
    if (processType) {
      dispatch(fetchModules({ processType }));
    } else {
      dispatch(fetchTargetBuilderModules());
      dispatch(fetchQuestionCategories());
    }
  }, [dispatch, processType]);
}

function mapStateToProps(state, outerProps) {
  const { processType } = outerProps;

  const modules = processType
    ? selectTemplateProcessModules(state)
    : selectTargetBuilderModules(state);

  let categories = selectQuestionCategories(state);

  categories = categories.reduce(
    (acc, category) => ({
      ...acc,
      [category.value]: category.label,
    }),
    {},
  );

  const groups = modules.reduce(
    (acc, item) => ({
      ...acc,
      [item.category]: [...(acc[item.category] || []), item],
    }),
    {},
  );

  return {
    modules,
    groups,
    categories,
  };
}

export default compose(
  withHooks(useHooksAsProps),
  connect(mapStateToProps),
)(SideModules);
