import { call, put, takeLatest } from 'redux-saga/effects';
import {
  fetchTargetScreening,
  fetchTargetScreeningError,
  fetchTargetScreeningSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import { profileBuilderAPI } from '../../../services/axios';

function* fetchTargetScreeningSagaFn() {
  try {
    const response = yield call(
      profileBuilderAPI.get,
      ENDPOINTS.PROFILE_BUILDER.TARGET_SCREENING(),
    );

    yield put(fetchTargetScreeningSuccess(response.data));
  } catch (error) {
    yield put(fetchTargetScreeningError(error));
  }
}

export default takeLatest(
  fetchTargetScreening.type,
  fetchTargetScreeningSagaFn,
);
