import { useCallback } from 'react';

export function getKeyboardFocusableElements(element = document) {
  return [
    ...element.querySelectorAll(
      'a, button, input, textarea, select, details,[tabindex]:not([tabindex="-1"])',
    ),
  ].filter((el) => !el.hasAttribute('disabled'));
}

export function focusNext(element, offset = 1) {
  const allElements = getKeyboardFocusableElements();
  const index = allElements.indexOf(element);

  const nextElement = allElements[index + offset];

  if (index !== -1 && nextElement && nextElement.focus) {
    nextElement.focus();
  }
}

export function createKeydownHandler(callback, keyCode = 13) {
  return (event) => {
    if (event && event.keyCode === keyCode) {
      callback(event);
    }
  };
}

export function useAccessibleCallback(callback, dependencies) {
  const handler = useCallback(callback, dependencies);

  const keyDownHandler = useCallback(
    (...args) => {
      if (typeof args[0].keyCode === 'number' && args[0].keyCode === 13) {
        handler();
      }
    },
    [handler],
  );

  return [handler, keyDownHandler];
}
