function isFirstPageVisible(
  { hideGoToButtons, hideGoToBorderButtons, hideGoToFirstPage, hideDisabled },
  hasPrevPage,
) {
  if (
    hideGoToButtons ||
    hideGoToBorderButtons ||
    hideGoToFirstPage ||
    (hideDisabled && !hasPrevPage)
  ) {
    return false;
  }

  return true;
}

function isPrevPageVisible(
  { hideGoToButtons, hideGoToAdjacentButtons, hideGoToPrevPage, hideDisabled },
  hasPrevPage,
) {
  if (
    hideGoToButtons ||
    hideGoToAdjacentButtons ||
    hideGoToPrevPage ||
    (hideDisabled && !hasPrevPage)
  ) {
    return false;
  }

  return true;
}

function isNextPageVisible(
  { hideGoToButtons, hideGoToAdjacentButtons, hideGoToNextPage, hideDisabled },
  hasNextPage,
) {
  if (
    hideGoToButtons ||
    hideGoToAdjacentButtons ||
    hideGoToNextPage ||
    (hideDisabled && !hasNextPage)
  ) {
    return false;
  }

  return true;
}

function isLastPageVisible(
  { hideGoToButtons, hideGoToBorderButtons, hideGoToLastPage, hideDisabled },
  hasNextPage,
) {
  if (
    hideGoToButtons ||
    hideGoToBorderButtons ||
    hideGoToLastPage ||
    (hideDisabled && !hasNextPage)
  ) {
    return false;
  }

  return true;
}

// eslint-disable-next-line import/prefer-default-export
export function getButtonStatus({
  hasPrevPage,
  hasNextPage,
  hideDisabled,
  hideGoToAdjacentButtons,
  hideGoToBorderButtons,
  hideGoToButtons,
  hideGoToFirstPage,
  hideGoToLastPage,
  hideGoToNextPage,
  hideGoToPrevPage,
}) {
  return {
    firstPageVisible: isFirstPageVisible(
      {
        hideGoToButtons,
        hideGoToBorderButtons,
        hideGoToFirstPage,
        hideDisabled,
      },
      hasPrevPage,
    ),
    prevPageVisible: isPrevPageVisible(
      {
        hideGoToButtons,
        hideGoToAdjacentButtons,
        hideGoToPrevPage,
        hideDisabled,
      },
      hasPrevPage,
    ),
    nextPageVisible: isNextPageVisible(
      {
        hideGoToButtons,
        hideGoToAdjacentButtons,
        hideGoToNextPage,
        hideDisabled,
      },
      hasNextPage,
    ),
    lastPageVisible: isLastPageVisible(
      {
        hideGoToButtons,
        hideGoToBorderButtons,
        hideGoToLastPage,
        hideDisabled,
      },
      hasNextPage,
    ),
  };
}
