import { call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction, addOne } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

export function* createLocaleTenantSaga(action) {
  const {
    payload,
    meta: { onSuccess, onError },
  } = action;

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can create a Locale Tenant.');

    return;
  }

  try {
    const { data } = yield call(axios.post, ENDPOINTS.V2.LOCALE_TENANTS(), {
      country: payload.country,
      tenantId: payload.tenant,
      projectId: payload.projectId || project.id,
    });

    yield put(
      updateOne(
        {
          id: payload.id,
          changes: {
            id: data.id,
          },
        },
        { onSuccess, onError, bypassUpdateSaga: true },
      ),
    );
    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error creating this Locale Tenant. Please try again.',
    );

    yield put(undoLastAction());
  }
}
export default takeEvery(addOne.type, createLocaleTenantSaga);
