import { connect } from 'react-redux';
import ProjectTools from './ProjectTools';
import isSWFH from '../../../../helpers/isSWFH';
import { ENTITIES, selectors } from '../../../../../../store/ducks/options';
import { selectProjectTools } from '../../../../../../store/ducks/project';

function mapStateToProps(state) {
  const projectTools = selectProjectTools(state);

  const {
    hasExternalSystem,
    externalSystem,
    externalSystemUrl,
    hasIPRestrictions,
    ipAddresses,
    swfhProjectExternalUrl,
  } = projectTools;

  const externalSystemsData = selectors.selectById(
    ENTITIES.externalSystems,
    null,
    externalSystem,
    state,
  );

  return {
    externalSystem: hasExternalSystem ? (externalSystemsData || {}).name : '',
    externalSystemUrl: hasExternalSystem ? externalSystemUrl : '',
    ipAddresses: hasIPRestrictions
      ? (ipAddresses || []).map((ip) => ip.ipAddress).join(', ')
      : '',

    hasExternalSystem,
    hasIPRestrictions,
    isSWFHProject: hasExternalSystem ? isSWFH(externalSystem) : false,
    swfhProjectExternalUrl,
  };
}

export default connect(mapStateToProps)(ProjectTools);
