import { useCallback, useMemo } from 'react';
import { connect, useSelector } from 'react-redux';
import { withFormik } from 'formik';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import ConfigurationModal from './ConfigurationModal';
import validationSchema from './validations';
import { withErrorPopover } from '../../../../ErrorPopover';
import { ENTITIES, selectors } from '../../../../../store/ducks/options';
import { mapToMacroLanguages } from '../../../../ProcessBuilder/helpers/macroLanguages';
import { saveAutoScreening } from '../../../../../store/ducks/autoScreening';
import { selectProjectId } from '../../../../../store/ducks/project';
import store from '../../../../../store';

function handleSubmit(values, { props }) {
  const {
    projectId,
    modalControls: { closeModal },
  } = props;

  const { dispatch } = store;

  const { id, localeId, maxUsersPerDay, maxActiveUsers } = values;

  const payload = {
    ...(id && { id }),
    projectId,
    localeId,
    maxUsersPerDay,
    maxActiveUsers,
  };

  dispatch(
    saveAutoScreening({
      changes: payload,
      thenPut: closeModal(),
    }),
  );
}

function mapStateToProps(state) {
  const projectId = selectProjectId(state);
  const initialValues = {
    localeId: undefined,
    maxUsersPerDay: undefined,
    maxActiveUsers: undefined,
  };

  return {
    projectId,
    initialValues,
  };
}

function mapPropsToValues(props) {
  const {
    initialValues,
    modalControls: { modalData },
  } = props;

  return modalData || initialValues;
}

function useHooksAsProps(props) {
  const { setFieldValue } = props;

  const locales = useSelector(selectors.createSelectSliceFor(ENTITIES.locales));

  const macroLanguages = useMemo(
    () => mapToMacroLanguages(locales.data, true, true),
    [locales.data],
  );

  const localeOptions = useMemo(
    () => macroLanguages.map((locale) => ({ ...locale, value: locale.id })),
    [macroLanguages],
  );

  const handleLocaleChange = useCallback(
    ({ value }) => setFieldValue('localeId', value),
    [setFieldValue],
  );

  const handleMaxActiveUsersChange = useCallback(
    ({ target: { value } }) =>
      setFieldValue('maxActiveUsers', parseInt(value.replace(/\D/g, ''), 10)),
    [setFieldValue],
  );

  const handleMaxUsersPerDayChange = useCallback(
    ({ target: { value } }) =>
      setFieldValue('maxUsersPerDay', parseInt(value.replace(/\D/g, ''), 10)),
    [setFieldValue],
  );

  return {
    localeOptions,
    handleLocaleChange,
    handleMaxActiveUsersChange,
    handleMaxUsersPerDayChange,
  };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(ConfigurationModal);
