import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import * as teamTypes from '../../../../../../store/ducks/teamTypes';
import ProjectSupportTeam from '../../../../../../components/ProjectPreviews/ProjectSupportTeam';
import { ENTITIES, fetch } from '../../../../../../store/ducks/options';

export default function SupportTeam(props) {
  const { supportTeam, navigateTo, renderedIn } = props;

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.SUPPORT_TEAM);
  }, [navigateTo]);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(teamTypes.fetchAll());
    dispatch(fetch(ENTITIES.internalUsers));
  }, [dispatch]);

  return (
    <ProjectSupportTeam
      supportTeam={supportTeam}
      editStep={editStep}
      renderedIn={renderedIn}
      stepNumber={STEP_NUMBER.SUPPORT_TEAM + 1}
    />
  );
}

SupportTeam.propTypes = {
  supportTeam: PropTypes.arrayOf(PropTypes.string).isRequired,
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

SupportTeam.defaultProps = {
  renderedIn: 'project-setup',
};
