import withHooks from 'hook-hoc';
import { useTheme } from 'styled-components';
import StatusDistribution from './StatusDistribution';

const { useMemo } = require('react');

function useHooksAsProps(outerProps) {
  const { total, inProgress, registered, rejected, abandoned } = outerProps;

  const theme = useTheme();

  const graphData = useMemo(
    () =>
      total === 0
        ? [{ angle: 1, color: theme.colors.lightBlueGrey }]
        : [
            { angle: registered / (total || 1), color: theme.colors.azure },
            { angle: rejected / (total || 1), color: theme.colors.error },
            { angle: abandoned / (total || 1), color: theme.colors.warning },
            {
              angle: inProgress / (total || 1),
              color: theme.colors.lightBlueGrey,
            },
          ].filter((item) => item.angle !== 0),
    [
      abandoned,
      inProgress,
      rejected,
      registered,
      theme.colors.azure,
      theme.colors.error,
      theme.colors.lightBlueGrey,
      theme.colors.warning,
      total,
    ],
  );

  return {
    graphData,
  };
}

export default withHooks(useHooksAsProps)(StatusDistribution);
