import React, { useContext, useEffect, useMemo } from 'react';
import { useFeatures } from 'flagged';
import moment from 'moment';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import {
  PreviewSectionTitle,
  PreviewSection,
  PreviewSectionItem,
  PreviewSectionText,
  EditButtonContainer,
  EditStepButton,
} from '../styles';
import Table from '../../Table';
import NoEntries from '../../NoEntries';
import { Target as OriginalTarget } from '../../ProjectForms/RecruitmentForm/LocaleTenants/styles';
import { selectProject } from '../../../store/ducks/project';
import Demographics from './Demographics';
import AutoScreening from '../../ProjectForms/RecruitmentForm/AutoScreening';
import DemographicsPage from '../../../pages/Demographics';
import { ENTITIES, ENTITY_METADATA, fetch } from '../../../store/ducks/options';
import LegacyIntelligentAttributes from '../../LegacyIntelligentAttributes';
import Modal, { useModal } from '../../Modal';
import { ButtonLink } from '../../../pages/ProjectView/styles';
import { Provider as DemographicsViewTypeProvider } from '../../../helpers/demographics-view-type-context';
import { PreviewContext } from '../components/PreviewSectionText';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip';
import RichDescription from '../../RichDescription';
import CellWithDefaultDot from '../../ProjectForms/InvoiceAndPaymentForm/PayratesSetup/CellWithDefaultDot';
import {
  fetchTargetScreening,
  selectTargetScreening,
} from '../../../store/ducks/targetScreening';

const NumberCell = styled(OriginalTarget)`
  color: ${(props) => props.theme.colors.charcoal};
`;

const PercentCell = styled(OriginalTarget)`
  color: ${(props) => props.theme.colors.azure};
`;

function renderFormatedDate({ cell: { value } }) {
  return value === null ? '.' : moment(value).format('MMM Do[,] YYYY');
}

// Not a Functional Component!
// eslint-disable-next-line react/prop-types
export function renderTarget({ cell: { value } }) {
  return <NumberCell>{value || '0'}</NumberCell>;
}
// Not a Functional Component!
// eslint-disable-next-line react/prop-types
export function renderTargetPercent({ cell: { value } }) {
  if (value && value > Math.floor(value)) {
    // eslint-disable-next-line no-param-reassign,react/prop-types
    value = value.toFixed(2);
  }
  return <PercentCell>{value || '0'}%</PercentCell>;
}

const getHiringTargetsColumns = (
  isTranslationProject,
  renderedIn,
  hasStatistics = false,
) => [
  {
    Header: 'LOC. LANGUAGE',
    accessor: 'localeLanguage',
    key: 'localeLanguage',
    disableSortBy: true,
    width: 200,
  },
  {
    Header: 'LOC. DIALECT',
    accessor: 'localeDialect',
    key: 'localeDialect',
    disableSortBy: true,
    width: 200,
  },
  ...(isTranslationProject
    ? [
        {
          Header: 'LOC. LANGUAGE To',
          accessor: 'localeLanguageTo',
          key: 'localeLanguageTo',
          disableSortBy: true,
          width: 200,
        },
        {
          Header: 'LOC. DIALECT TO',
          accessor: 'localeDialectTo',
          key: 'localeDialectTo',
          disableSortBy: true,
          width: 200,
        },
      ]
    : []),
  {
    Header: 'REST. TO COUNTRY',
    accessor: 'restrictToCountry',
    key: 'restrictToCountry',
    disableSortBy: true,
    width: 180,
  },
  ...(renderedIn === 'project-view'
    ? [
        {
          Header: 'ASSIGNED OWNER',
          accessor: 'assignedOwner',
          key: 'assignedOwner',
          disableSortBy: true,
          Cell: CellWithDefaultDot,
        },
      ]
    : []),
  {
    Header: 'HIRING DEADLINE',
    accessor: 'hiringDeadline',
    key: 'hiringDeadline',
    Cell: renderFormatedDate,
    disableSortBy: true,
    width: 180,
  },
  {
    Header: 'TARGET',
    accessor: 'target',
    key: 'target',
    tooltip: 'Actual hiring target.',
    Cell: renderTarget,
    maxWidth: 72,
    disableSortBy: true,
    style: {
      textAlign: 'center',
    },
    width: 180,
  },
  ...(hasStatistics
    ? [
        {
          Header: 'QUALIFYING',
          accessor: 'metrics.qualifying',
          key: 'qualifying',
          tooltip:
            'Number of workers going through a registration or qualification process.',
          Cell: renderTarget,
          maxWidth: 90,
          disableSortBy: true,
          style: {
            textAlign: 'center',
          },
        },
        {
          Header: 'ACTIVE',
          accessor: 'metrics.active',
          key: 'active',
          tooltip:
            'Number of workers who finished both registration and qualification processes.',
          Cell: renderTarget,
          maxWidth: 90,
          disableSortBy: true,
          style: {
            textAlign: 'center',
          },
        },
        {
          Header: 'PROGRESS',
          accessor: 'metrics.progress',
          key: 'progress',
          tooltip: 'Percentage of the hiring target achieved.',
          Cell: renderTargetPercent,
          maxWidth: 90,
          disableSortBy: true,
          style: {
            textAlign: 'center',
          },
        },
      ]
    : []),
];

const localeTenantsColumns = [
  {
    Header: 'COUNTRY',
    key: 'country',
    accessor: 'country',
    disableSortBy: true,
  },
  {
    Header: 'TENANT',
    accessor: 'tenant',
    key: 'tenant',
    disableSortBy: true,
  },
  {
    Header: 'TENANT TYPE',
    accessor: 'tenantType',
    key: 'tenantType',
    disableSortBy: true,
    width: 48,
  },
];

export default function Recruitment(props) {
  const {
    localeTenants,
    hiringTargets,
    loadingHiringTargets,
    // hasBlockingRules,
    // hasIntelligentAttributes,
    // hasLinkedProjects,
    // hasNotesToRecruiting,
    blockingRules,
    profileTargetId,
    linkedProjects,
    notesToRecruiting,
    editStep,
    renderedIn,
    localeTenantsHeader,
    hiringTargetsHeader,
    demographicsHeader,
    autoScreenHeader,
    stepNumber,
  } = props;

  const dispatch = useDispatch();

  const { targetScreening, autoScreening } = useFeatures();

  const project = useSelector(selectProject);
  const targets = useSelector(selectTargetScreening);

  const isTranslationProject = useMemo(
    () => get(project, 'workType', '') === 'TRANSLATION',
    [project],
  );

  const projectId = useMemo(() => get(project, 'id', null), [project]);

  const hasStatistics = useMemo(
    () => ['ENABLED', 'DISABLED'].includes(get(project, 'status', '')),
    [project],
  );

  useEffect(() => {
    dispatch(fetch(ENTITIES.countries));
    dispatch(fetch(ENTITIES.localeLanguages));
    dispatch(fetch(ENTITIES.internalUsers));
    dispatch(fetchTargetScreening());

    dispatch(
      fetch({
        optionsFor: ENTITIES.projects,
        nestedKey: ENTITY_METADATA.projects.nestedKeys.ALL,
      }),
    );
  }, [dispatch, projectId]);

  const [modalProps, modalControls] = useModal();

  const { validateEmpty } = useContext(PreviewContext);

  const targetName = useMemo(() => {
    const selectedTarget = targets.filter((t) => t.id === profileTargetId);
    return get(selectedTarget, [0, 'name'], '');
  }, [targets, profileTargetId]);

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: Recruitment
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}

      <PreviewSection
        marginBottom={
          (!localeTenants || !localeTenants.length) && validateEmpty ? 72 : 36
        }
      >
        {localeTenantsHeader || (
          <PreviewSectionItem>Locale Tenants</PreviewSectionItem>
        )}
        {localeTenants && localeTenants.length ? (
          <Table data={localeTenants} columns={localeTenantsColumns} />
        ) : (
          <NoEntries error={validateEmpty ? 'Required field is empty' : null} />
        )}
      </PreviewSection>

      <PreviewSection
        marginBottom={
          (!hiringTargets || !hiringTargets.length) && validateEmpty ? 72 : 36
        }
      >
        {hiringTargetsHeader || (
          <PreviewSectionItem>Hiring Targets</PreviewSectionItem>
        )}
        {hiringTargets && hiringTargets.length ? (
          <Table
            data={hiringTargets}
            columns={getHiringTargetsColumns(
              isTranslationProject,
              renderedIn,
              hasStatistics,
            )}
          />
        ) : (
          <NoEntries
            label={loadingHiringTargets ? 'Loading Hiring Targets...' : null}
            error={validateEmpty ? 'Required field is empty' : null}
          />
        )}
      </PreviewSection>

      {autoScreening && (
        <PreviewSection marginBottom={28} marginTop={12}>
          <AutoScreening header={autoScreenHeader} mode="view" />
        </PreviewSection>
      )}

      {targetScreening && (
        <PreviewSection>
          <PreviewSectionItem>
            Target Screening and Data Requirements
          </PreviewSectionItem>
          <PreviewSectionText>{targetName || '-'}</PreviewSectionText>
        </PreviewSection>
      )}

      <PreviewSection marginBottom={28} marginTop={12}>
        <Demographics header={demographicsHeader} />
        {!editStep && (
          <PreviewSection marginBottom={48}>
            View project{' '}
            <ButtonLink
              type="button"
              onClick={() => {
                modalControls.openModal();
              }}
            >
              Demographic Distribution
            </ButtonLink>
            .
          </PreviewSection>
        )}
      </PreviewSection>

      <PreviewSection>
        <PreviewSectionItem>
          Blocking Rules{' '}
          <InformationTooltip
            title={
              <>
                If marked as permanent, any raters working on this project will
                not be able to work on the blocked projects even if they are
                revoked from this project.
                <br />
                <br />
                You can only add blocking rules to enabled projects.
              </>
            }
          />
        </PreviewSectionItem>
        <PreviewSectionText>{blockingRules || '-'}</PreviewSectionText>
      </PreviewSection>

      {/*
      {hasIntelligentAttributes && (
        <PreviewSection>
          <PreviewSectionItem>Intelligent Attributes</PreviewSectionItem>
        </PreviewSection>
      )} */}

      <PreviewSection>
        <PreviewSectionItem>Linked Projects</PreviewSectionItem>
        <PreviewSectionText>{linkedProjects || '-'}</PreviewSectionText>
      </PreviewSection>

      <PreviewSection>
        <PreviewSectionItem>Notes to Recruiting Team</PreviewSectionItem>
        <PreviewSectionText>
          <RichDescription html={notesToRecruiting || '-'} />
        </PreviewSectionText>
      </PreviewSection>

      {renderedIn === 'project-view' && (
        <PreviewSection>
          <LegacyIntelligentAttributes projectId={projectId} />
        </PreviewSection>
      )}

      <Modal {...modalProps} title="Demographic Distribution">
        <DemographicsViewTypeProvider value={{ viewType: 'metrics' }}>
          <DemographicsPage inModal />
        </DemographicsViewTypeProvider>
      </Modal>
    </>
  );
}

Recruitment.propTypes = {
  localeTenants: PropTypes.arrayOf(PropTypes.object),
  hiringTargets: PropTypes.arrayOf(PropTypes.object),
  loadingHiringTargets: PropTypes.bool,
  hasBlockingRules: PropTypes.bool,
  hasIntelligentAttributes: PropTypes.bool,
  hasLinkedProjects: PropTypes.bool,
  blockingRules: PropTypes.string,
  profileTargetId: PropTypes.number,
  linkedProjects: PropTypes.string,
  hasNotesToRecruiting: PropTypes.bool,
  notesToRecruiting: PropTypes.string,
  editStep: PropTypes.func,
  renderedIn: PropTypes.oneOf([
    'project-view',
    'project-setup',
    'project-preview-modal',
  ]),
  localeTenantsHeader: PropTypes.element,
  hiringTargetsHeader: PropTypes.element,
  demographicsHeader: PropTypes.element,
  autoScreenHeader: PropTypes.element,
  stepNumber: PropTypes.number,
};

Recruitment.defaultProps = {
  hasBlockingRules: false,
  hasIntelligentAttributes: false,
  hasLinkedProjects: false,
  hasNotesToRecruiting: false,
  blockingRules: '',
  profileTargetId: null,
  linkedProjects: '',
  notesToRecruiting: '',
  localeTenants: '',
  hiringTargets: '',
  loadingHiringTargets: false,
  editStep: undefined,
  renderedIn: 'project-setup',
  localeTenantsHeader: null,
  hiringTargetsHeader: null,
  demographicsHeader: null,
  autoScreenHeader: null,
  stepNumber: 3,
};
