import React, { useState } from 'react';
import PropTypes from 'prop-types';
import noop from 'lodash/noop';
import CardViewMode from './CardViewMode';
import CardEditMode from './CardEditMode';
import Button from '../../../components/Button';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../components/Popover';
import QuestionSetupModal from './QuestionSetupModal';
import CategoryModal from '../CategoryModal';
import EllipsesHoverable from '../../../components/Icons/EllipsesHoverable';
import Disabled from '../../../components/Disabled';
import {
  CardContainer,
  CardHeader,
  CardTitle,
  CardFooter,
  CardDescription,
  CardContent,
  CardExpandButton,
  CardOptions,
} from './styles';
import pencil from '../../../assets/icons/pencil.svg';
import close from '../../../assets/icons/close.svg';

export default function Card(props) {
  const {
    title,
    description,
    topics,
    category,
    handleNewTopic,
    handleEditCategory,
    modalProps,
    modalControls,
    modalPropsCategory,
    modalControlsCategory,
  } = props;
  const [collapsed, setCollapsed] = useState(false);

  const tooltipContent = (
    <>
      <Disabled when message="Category can't be edited.">
        <PopoverItem
          onClick={handleEditCategory}
          onKeyDown={noop}
          role="button"
          tabIndex={0}
        >
          <PopoverIcon src={pencil} alt={'edit icon'} />
          <PopoverText>Edit</PopoverText>
        </PopoverItem>
      </Disabled>
      <Disabled when message="Category can't be deleted.">
        <PopoverItem
          onClick={noop}
          onKeyDown={noop}
          role="button"
          tabIndex={-1}
        >
          <PopoverIcon src={close} alt={'clear icon'} />
          <PopoverText>Delete</PopoverText>
        </PopoverItem>
      </Disabled>
    </>
  );

  return (
    <CardContainer>
      <CardHeader>
        <CardTitle>
          {title}
          <CardDescription>{description}</CardDescription>
        </CardTitle>
        <CardOptions>
          {!collapsed && (
            <CardExpandButton onClick={() => setCollapsed(!collapsed)}>
              Expand
            </CardExpandButton>
          )}
          <Popover placement="bottom" content={tooltipContent}>
            <EllipsesHoverable />
          </Popover>
        </CardOptions>
      </CardHeader>
      <CardContent increasePadding={collapsed}>
        {collapsed ? (
          <CardEditMode topics={topics} category={category} />
        ) : (
          <CardViewMode topics={topics} />
        )}
      </CardContent>
      {collapsed && (
        <CardFooter>
          <Button type="secondary" marginRight={16} onClick={handleNewTopic}>
            Add New Topic
          </Button>
          <Button type="primary" onClick={() => setCollapsed(!collapsed)}>
            Collapse
          </Button>
        </CardFooter>
      )}
      <QuestionSetupModal
        modalProps={modalProps}
        modalControls={modalControls}
      />
      <CategoryModal
        modalProps={modalPropsCategory}
        modalControls={modalControlsCategory}
      />
    </CardContainer>
  );
}

Card.propTypes = {
  title: PropTypes.string,
  description: PropTypes.string,
  topics: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      description: PropTypes.string,
    }),
  ),
  category: PropTypes.string.isRequired,
  handleNewTopic: PropTypes.func.isRequired,
  handleEditCategory: PropTypes.func.isRequired,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
  modalPropsCategory: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControlsCategory: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
};

Card.defaultProps = {
  title: null,
  description: null,
  topics: [],
};
