import React from 'react';
import PropTypes from 'prop-types';

// style
import { ErrorContainer, ErrorMessage } from './styles';

export default function MessageError(props) {
  const { isVisible, increasesHeight } = props;
  let { message } = props;

  if (isVisible) {
    if (message instanceof Error) {
      message = message.message;
    }

    if (typeof message !== 'string') {
      message = '';
    }

    return (
      <ErrorContainer
        increasesHeight={increasesHeight}
        data-testid="message-error--container"
      >
        <ErrorMessage increasesHeight={increasesHeight}>{message}</ErrorMessage>
      </ErrorContainer>
    );
  }

  return null;
}

MessageError.propTypes = {
  increasesHeight: PropTypes.bool,
  message: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
    PropTypes.instanceOf(Error),
  ]),
  isVisible: PropTypes.bool,
};

MessageError.defaultProps = {
  message: false,
  isVisible: false,
  increasesHeight: false,
};
