import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const KEY = 'autoScreening';
const UNDO_TYPE = `${KEY}/undo`;

const slice = createSlice({
  name: KEY,

  initialState: {
    data: [],
    loading: false,
    fetchError: null,
    saving: false,
    saveError: null,
  },

  reducers: {
    fetchAutoScreening: (state) => {
      state.loading = true;
      state.fetchError = null;
    },

    fetchAutoScreeningSuccess: (state, action) => {
      state.loading = false;
      state.fetchError = null;
      state.data = action.payload;
    },

    fetchAutoScreeningError: (state, action) => {
      state.loading = false;
      state.fetchError = action.payload;
    },

    saveAutoScreening: (state) => {
      state.saving = true;
      state.saveError = null;
    },

    saveAutoScreeningSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;

      const updatedIndex = state.data.findIndex(
        (config) => config.id === action.payload.id,
      );

      if (updatedIndex !== -1) {
        state.data = [
          ...state.data.slice(0, updatedIndex),
          action.payload,
          ...state.data.slice(updatedIndex + 1),
        ];
      } else {
        state.data = [...state.data, action.payload];
      }
    },

    saveAutoScreeningError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
    },

    deleteAutoScreening: (state) => {
      state.saving = true;
      state.saveError = null;
    },

    deleteAutoScreeningSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.data = state.data.filter(
        (candidate) => candidate.id !== action.payload.id,
      );
    },

    deleteAutoScreeningError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
    },
  },
});

const {
  actions: {
    fetchAutoScreening,
    fetchAutoScreeningSuccess,
    fetchAutoScreeningError,
    saveAutoScreening,
    saveAutoScreeningSuccess,
    saveAutoScreeningError,
    deleteAutoScreening,
    deleteAutoScreeningSuccess,
    deleteAutoScreeningError,
  },
  reducer,
} = slice;

reducer.key = KEY;

export default reducer;

export {
  fetchAutoScreening,
  fetchAutoScreeningSuccess,
  fetchAutoScreeningError,
  saveAutoScreening,
  saveAutoScreeningSuccess,
  saveAutoScreeningError,
  deleteAutoScreening,
  deleteAutoScreeningSuccess,
  deleteAutoScreeningError,
};

export function selectAutoScreeningSlice(state) {
  return state[reducer.key];
}

export function selectAutoScreenings(state) {
  return get(selectAutoScreeningSlice(state), 'data', []);
}

export function selectLoading(state) {
  return get(selectAutoScreeningSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectAutoScreeningSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectAutoScreeningSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectAutoScreeningSlice(state), 'saveError', null);
}

export function selectAutoScreeningById(state, id) {
  const autoScreenings = selectAutoScreenings(state);
  return autoScreenings.find((config) => config.id === id);
}

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}
