import { call, put, select, takeEvery } from 'redux-saga/effects';
import { updateOne, undoLastAction } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';
import { selectProject } from '../project';

function* saveHiringTargetsFn(action) {
  const {
    payload: { changes, id },
    meta: { onSuccess, onError, bypassUpdateSaga },
  } = action;

  if (bypassUpdateSaga) {
    return;
  }

  const project = yield select(selectProject);

  if (!project || !project.id) {
    onError('You must create a project before you can update a Hiring Target.');
    return;
  }

  try {
    yield call(
      axios.put,
      ENDPOINTS.V2.HIRING_TARGETS({
        pathVariables: {
          id,
        },
      }),
      {
        id,
        language: changes.localeLanguage,
        languageCountry: changes.localeDialect,
        languageTo: changes.localeLanguageTo,
        languageCountryTo: changes.localeDialectTo,
        restrictToCountry: changes.restrictToCountry,
        ownerId: changes.assignedOwner,
        deadline: changes.hiringDeadline,
        target: changes.target,
        projectId: project.id,
      },
    );

    onSuccess();
  } catch (error) {
    onError(
      error.message ||
        'There was an error updating a Hiring Target. Please try again.',
    );

    yield put(undoLastAction());
  }
}

export default takeEvery(updateOne.type, saveHiringTargetsFn);
