import React, { useCallback, useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { partition, uniq } from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Row } from 'styled-bootstrap-grid';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import { selectSaveError } from '../../../store/ducks/dataConsentForm';
import { Label, Section } from '../../../styles';
import ActionFooter from '../../ActionFooter';
import Checkbox from '../../Checkbox';
import CloseableTag from '../../CloseableTag';
import Disabled from '../../Disabled';
import Input from '../../Input';
import Select from '../../Select';
import TabSwitcher from '../../TabSwitcher';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip';
import ErrorPopover, { ErrorPopoverProps } from '../../ErrorPopover';
import {
  StepActionContainer,
  StepContentContainer,
} from '../../ProcessBuilder/Steps/styles';
import { ModalContainer, DataCollectedTypeLabel, LabelWrapper } from './styles';
import Radio from '../../Radio';
import {
  EEA_COUNTRY_CODES,
  THIRD_PARTY_OPTIONS,
  DATA_OWNERSHIP_TOGGLE_OPTIONS,
} from '../constants';
import {
  PopoverContainer,
  PopoverWarning,
  ErrorTitle,
  Wrapper,
} from '../styles';

const PIITooltip = (
  <>
    <strong>{'"Personal data"'}</strong> or <strong>{'"PII"'}</strong> means any
    <br />
    information relating to an identified or
    <br />
    identifiable natural person {'("data subject");'}
    <br />
    <br />
    An <strong>Identifiable Natural Person</strong> is one
    <br />
    who can be identified, directly or
    <br />
    indirectly, in particular by reference to
    <br />
    an identifier such as a name, an
    <br />
    identification number, location data, an
    <br />
    online identifier or to one or more
    <br />
    factors specific to the physical,
    <br />
    physiological, genetic, mental,
    <br />
    economic, cultural or social identity of
    <br />
    that natural person.
  </>
);

export default function DataConsentModal(props) {
  const {
    values,
    submitCount,
    setFieldValue,
    submitForm,
    showError,
    errors,
    handleChange,
    closeModal,
    errorPopoverProps,
    hasDataCollectionPiiConsentForm,
  } = props;

  const dispatch = useDispatch();

  const appenEntities = useSelector(
    selectors.createSelectSliceFor(ENTITIES.appenEntities),
  );

  const piiDataTypes = useSelector(
    selectors.createSelectSliceFor(ENTITIES.piiDataTypes),
  );

  const countries = useSelector(
    selectors.createSelectSliceFor(ENTITIES.countries),
  );

  const electronicDocuments = useSelector(
    selectors.createSelectDataFor(ENTITIES.electronicDocuments),
  );

  const error = useSelector(selectSaveError);

  useEffect(() => {
    dispatch(fetch(ENTITIES.appenEntities));
    dispatch(fetch(ENTITIES.piiDataTypes));
    dispatch(fetch(ENTITIES.countries));
    dispatch(
      fetch({
        optionsFor: ENTITIES.electronicDocuments,
        activitiName: 'Sign document',
      }),
    );
  }, [dispatch]);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  const piiDataTypesOptions = useMemo(
    () =>
      piiDataTypes.data.map((data) => {
        const options = data.options.map((option) => {
          return {
            ...option,
            value: option.id,
          };
        });
        return {
          label: data.label,
          options,
        };
      }),
    [piiDataTypes.data],
  );

  const appenEntitiesOptions = useMemo(
    () =>
      appenEntities.data.map((data) => {
        return {
          value: data.id,
          label: data.name,
          country: data.country,
        };
      }),
    [appenEntities.data],
  );

  const countriesOptions = useMemo(
    () => [
      { value: '*', label: 'All Countries' },
      { value: 'EEA', label: 'European Economic Area (EEA)' },
      ...countries.data.filter((candidate) => candidate.value !== '*'),
    ],
    [countries],
  );

  const documentOptions = useMemo(
    () =>
      electronicDocuments.map((document) => ({
        label: document.title,
        value: document.id,
      })),
    [electronicDocuments],
  );

  const handleDocumentId = useCallback(
    (e) => {
      setFieldValue('documentId', e.value);
    },
    [setFieldValue],
  );

  const handlePIIDataChange = useCallback(
    (value) => {
      setFieldValue('pIIDataCollected', value);
    },
    [setFieldValue],
  );

  const handleCountriesChange = useCallback(
    (nextValue) => {
      if (!nextValue.length) {
        setFieldValue('countriesCollectingData', nextValue);
      }

      const { countriesCollectingData = [] } = values;

      const hasAllCountries = nextValue.find(
        (candidate) => candidate.value === '*',
      );

      const hadAllCountries = countriesCollectingData.find(
        (candidate) => candidate.value === '*',
      );

      if (!hadAllCountries && hasAllCountries) {
        // Adding "All countries"
        setFieldValue('countriesCollectingData', [
          { value: 'EEA', label: 'European Economic Area (EEA)' },
          ...countries.data,
        ]);

        return;
      }

      if (hadAllCountries && !hasAllCountries) {
        // Removing "All countries"
        setFieldValue('countriesCollectingData', []);

        return;
      }

      const hasEEA = nextValue.find((candidate) => candidate.value === 'EEA');
      const hadEEa = countriesCollectingData.find(
        (candidate) => candidate.value === 'EEA',
      );

      if (hasEEA && !hadEEa) {
        // Adding EEA
        setFieldValue(
          'countriesCollectingData',
          uniq([
            { value: 'EEA', label: 'European Economic Area (EEA)' },
            ...countriesCollectingData,
            ...countries.data.filter((candidate) =>
              EEA_COUNTRY_CODES.includes(candidate.value),
            ),
          ]),
        );

        return;
      }

      if (hadEEa && !hasEEA) {
        // Removing EEA
        setFieldValue(
          'countriesCollectingData',
          countriesCollectingData.filter(
            (candidate) => !EEA_COUNTRY_CODES.includes(candidate.value),
          ),
        );

        return;
      }

      if (
        hasEEA &&
        !EEA_COUNTRY_CODES.every((countryCode) =>
          nextValue.includes(countryCode),
        )
      ) {
        setFieldValue(
          'countriesCollectingData',
          nextValue.filter((item) => item.value !== 'EEA'),
        );
        return;
      }

      setFieldValue('countriesCollectingData', nextValue);
    },
    [countries.data, setFieldValue, values],
  );

  const handleCountryRemoval = useCallback(
    (value) => {
      const { countriesCollectingData = [] } = values;

      if (value === '*') {
        setFieldValue('countriesCollectingData', []);
      } else if (value === 'EEA') {
        setFieldValue(
          'countriesCollectingData',
          countriesCollectingData.filter(
            (item) => !EEA_COUNTRY_CODES.includes(item.value),
          ),
        );
      } else if (
        EEA_COUNTRY_CODES.includes(value) &&
        countriesCollectingData.find((item) => item.value === 'EEA')
      ) {
        setFieldValue(
          'countriesCollectingData',
          countriesCollectingData.filter(
            (item) => item.value !== 'EEA' && item.value !== value,
          ),
        );
      } else {
        setFieldValue(
          'countriesCollectingData',
          countriesCollectingData.filter((item) => item.value !== value),
        );
      }
    },
    [setFieldValue, values],
  );

  const countryTags = useMemo(() => {
    const { countriesCollectingData = [] } = values;

    if (countriesCollectingData.find((candidate) => candidate.value === '*')) {
      return [
        {
          value: '*',
          label: 'All countries',
        },
      ];
    }

    const isEEACountry =
      countriesCollectingData.length === EEA_COUNTRY_CODES.length
        ? countriesCollectingData.every((candidate) =>
            EEA_COUNTRY_CODES.includes(candidate.value),
          )
        : false;

    if (isEEACountry) {
      return [
        {
          value: 'EEA',
          label: 'European Economic Area (EEA)',
        },
        ...countriesCollectingData.filter(
          (candidate) =>
            candidate.value !== 'EEA' &&
            !EEA_COUNTRY_CODES.includes(candidate.value),
        ),
      ];
    }

    return countriesCollectingData.filter(
      (candidate) => candidate.value !== 'EEA',
    );
  }, [values]);

  const [sensitivePIIs, nonSensitivePIIs] = useMemo(
    () =>
      partition(
        values.pIIDataCollected || [],
        (candidate) => !!candidate.sensitive,
      ),
    [values.pIIDataCollected],
  );

  const entityLocation = useMemo(() => {
    if (appenEntitiesOptions) {
      const entity = appenEntitiesOptions.filter(
        (e) => e.value === values.appenEntity,
      );
      if (entity.length) {
        setFieldValue('entityLocation', entity[0].country);
      }
    }
  }, [appenEntitiesOptions, setFieldValue, values.appenEntity]);

  const handleRemovePII = useCallback(
    (value) => {
      setFieldValue(
        'pIIDataCollected',
        values.pIIDataCollected.filter((candidate) => candidate.id !== value),
      );
    },
    [setFieldValue, values.pIIDataCollected],
  );

  const handleDisplayCustomerRealDataChange = useCallback(
    (value) => {
      if (!value) {
        setFieldValue('displayCustomerRealData', false);
        setFieldValue('customerDPOEmail', '');
        setFieldValue('customerFullLegalName', '');
        setFieldValue('customerCountry', '');
      } else {
        setFieldValue('displayCustomerRealData', true);
      }
    },
    [setFieldValue],
  );

  const calculateMultiValueCount = useCallback((value) => {
    if (value.find((item) => item.value === 'EEA')) {
      return value.length - 1;
    }

    return value.length;
  }, []);

  const calculateMultiPIIValueCount = useCallback((value) => value.length, []);

  const showCustomerRealData = useMemo(() => {
    const { countriesCollectingData = [] } = values;

    const hasSensitivePIIs = sensitivePIIs.length > 0;
    const hasEEACountry = countriesCollectingData.find((candidate) =>
      EEA_COUNTRY_CODES.includes(candidate.value),
    );
    const hasBrazilCountry = countriesCollectingData.find(
      (candidate) => candidate.value === 'BRA',
    );
    const displayCustomerRealData =
      hasSensitivePIIs || hasEEACountry || hasBrazilCountry;

    handleDisplayCustomerRealDataChange(displayCustomerRealData);

    return displayCustomerRealData;
  }, [values, sensitivePIIs, handleDisplayCustomerRealDataChange]);

  const isCustomerTemplate = useMemo(
    () => Number(values.appenOwnsSomeDataset) === 2,
    [values.appenOwnsSomeDataset],
  );

  return (
    <ModalContainer>
      <StepContentContainer>
        <Section>
          <TabSwitcher
            tabs={DATA_OWNERSHIP_TOGGLE_OPTIONS}
            value={Number(values.appenOwnsSomeDataset)}
            onChange={(index) => setFieldValue('appenOwnsSomeDataset', index)}
          />
        </Section>
        {isCustomerTemplate && (
          <Section>
            <LabelWrapper>
              <Label htmlFor="documentId">Existing customer consent form</Label>
            </LabelWrapper>
            <Select
              name="documentId"
              id="documentId"
              options={documentOptions}
              onChange={handleDocumentId}
              value={values.documentId}
              placeholder="Select existing document"
              error={submitCount >= 1 && errors.documentId}
            />
          </Section>
        )}
        {!isCustomerTemplate && (
          <>
            <Section>
              <Row>
                <Col col={12}>
                  <Label htmlFor="projectContactEmail">
                    Project Contact email
                  </Label>
                  <Input
                    name="projectContactEmail"
                    onChange={handleChange}
                    id="projectContactEmail"
                    placeholder="Enter email"
                    value={values.projectContactEmail}
                    data-testid="icf-step-project-contact-email"
                    error={submitCount >= 1 && errors.projectContactEmail}
                  />
                </Col>
              </Row>
            </Section>
            <Section>
              <Row>
                <Col col={6}>
                  <Label htmlFor="appenEntity">Appen Entity</Label>
                  <Select
                    id="appenEntity"
                    name="appenEntity"
                    data-testid="icf-step-appen-entity"
                    placeholder="Select entity"
                    options={appenEntitiesOptions}
                    value={values.appenEntity}
                    onChange={(entity) => {
                      setFieldValue('appenEntity', entity.value);
                      setFieldValue('entityLocation', entity.country);
                    }}
                    loading={appenEntities.loading}
                    error={
                      appenEntities.error ||
                      (submitCount > 0 && errors.appenEntity)
                    }
                  />
                </Col>
                <Col col={6}>
                  <Label htmlFor="entityLocation">Entity Location</Label>
                  <Select
                    id="entityLocation"
                    name="entityLocation"
                    data-testid="icf-step-entity-location"
                    placeholder="Location"
                    options={countries.data}
                    value={values.entityLocation || entityLocation}
                    onChange={({ value }) =>
                      setFieldValue('entityLocation', value)
                    }
                    disabled
                    loading={appenEntities.loading}
                    error={
                      appenEntities.error ||
                      (submitCount > 0 && errors.entityLocation)
                    }
                  />
                </Col>
              </Row>
            </Section>
            <Section>
              <LabelWrapper>
                <Label htmlFor="pIIDataCollected">PII Data collected</Label>
                <InformationTooltip
                  tooltipIconStyle={{ bottom: 0 }}
                  title={PIITooltip}
                />
              </LabelWrapper>
              <Select
                getMultiCount={calculateMultiPIIValueCount}
                id="pIIDataCollected"
                name="pIIDataCollected"
                data-testid="icf-step-pii-data-collected"
                options={piiDataTypesOptions || []}
                value={values.pIIDataCollected}
                onChange={handlePIIDataChange}
                loading={piiDataTypes.loading}
                placeholder="Select PII data"
                error={
                  piiDataTypes.error ||
                  (submitCount > 0 && errors.pIIDataCollected)
                }
                isMulti
              />{' '}
            </Section>

            {sensitivePIIs.length > 0 && (
              <Section>
                <DataCollectedTypeLabel>Sensitive PIIs</DataCollectedTypeLabel>
                {sensitivePIIs.map((r) => (
                  <CloseableTag
                    key={r.label}
                    label={r.label}
                    onClose={() => handleRemovePII(r.value)}
                  />
                ))}
              </Section>
            )}

            {nonSensitivePIIs.length > 0 && (
              <Section>
                <DataCollectedTypeLabel>
                  Non-Sensitive PIIs
                </DataCollectedTypeLabel>
                {nonSensitivePIIs.map((r) => (
                  <CloseableTag
                    key={r.label}
                    label={r.label}
                    onClose={() => handleRemovePII(r.value)}
                  />
                ))}
              </Section>
            )}

            <Section>
              <Label htmlFor="countriesCollectingData">
                Countries collecting data
              </Label>
              <Select
                getMultiCount={calculateMultiValueCount}
                id="countriesCollectingData"
                name="countriesCollectingData"
                data-testid="icf-step-countries-collecting-data"
                options={countriesOptions}
                value={values.countriesCollectingData}
                onChange={handleCountriesChange}
                loading={countries.loading}
                placeholder="Select Countries"
                error={
                  countries.error ||
                  (submitCount > 0 && errors.countriesCollectingData)
                }
                isMulti
              />
              {countryTags.map((r) => (
                <CloseableTag
                  key={r.label}
                  label={r.label}
                  onClose={() => handleCountryRemoval(r.value)}
                />
              ))}
            </Section>
          </>
        )}
        <Section marginBottom="0px">
          <Checkbox
            name="collectThirdPartyPII"
            onChange={handleChange}
            checked={values.collectThirdPartyPII}
            bold={false}
            label={
              <LabelWrapper>
                Project will also collect PII from third parties{' '}
                <InformationTooltip
                  title={
                    <>
                      This is usually crowd {"worker's"} friends,
                      <br />
                      family and acquaintances.
                    </>
                  }
                />
              </LabelWrapper>
            }
            data-testid="icf-step-collect-third-party-pii"
          />
        </Section>
        {values.collectThirdPartyPII && (
          <Section marginLeft="34px">
            <Radio
              options={THIRD_PARTY_OPTIONS}
              name="collectThirdPartyPIIFrom"
              value={values.collectThirdPartyPIIFrom}
              onChange={handleChange}
            />
          </Section>
        )}
        {!isCustomerTemplate && (
          <>
            <Section marginBottom="0px">
              <Disabled
                when={showCustomerRealData}
                message="Required if sensitive PII is collected or if any selected country is part of EEA or Brazil. "
              >
                <Checkbox
                  name="displayCustomerRealData"
                  onChange={handleDisplayCustomerRealDataChange}
                  checked={showCustomerRealData}
                  bold={false}
                  label={
                    <LabelWrapper>
                      Display customer real data{' (recommended) '}
                      <InformationTooltip
                        title={
                          <>
                            Customer full legal name, country and
                            <br />
                            contact info will be displayed on the
                            <br />
                            consent form. This is required under
                            <br />
                            some data privacy laws.
                            <br />
                            <br />
                            Required if collecting sensitive PII data
                            <br />
                            or if any selected country is part of EEA
                            <br />
                            or Brazil.
                            <br />
                            <br />
                            If this is impossible, you need to seek
                            <br />
                            Legal approval for override.
                          </>
                        }
                      />
                    </LabelWrapper>
                  }
                  data-testid="icf-step-collect-third-party-pii"
                />
              </Disabled>
            </Section>
            {showCustomerRealData && (
              <Section marginLeft="34px">
                <Section>
                  <Label htmlFor="customerDPOEmail">
                    Customer DPO Contact email
                  </Label>
                  <Input
                    name="customerDPOEmail"
                    onChange={handleChange}
                    id="customerDPOEmail"
                    placeholder="Enter customer email"
                    value={values.customerDPOEmail}
                    data-testid="icf-step-customer-dpo-email"
                    error={submitCount >= 1 && errors.customerDPOEmail}
                  />
                </Section>
                <Section>
                  <Row>
                    <Col col={7}>
                      <Label htmlFor="customerFullLegalName">
                        Customer full legal name
                      </Label>
                      <Input
                        name="customerFullLegalName"
                        onChange={handleChange}
                        id="customerFullLegalName"
                        placeholder="Enter customer legal name"
                        value={values.customerFullLegalName}
                        data-testid="icf-step-customer-full-legal-name"
                        error={submitCount >= 1 && errors.customerFullLegalName}
                      />
                    </Col>
                    <Col col={5}>
                      <Label htmlFor="customerCountry">Customer country</Label>
                      <Select
                        id="customerCountry"
                        name="customerCountry"
                        data-testid="icf-step-customer-country"
                        placeholder="Select country"
                        options={countries.data}
                        value={values.customerCountry}
                        onChange={(option) =>
                          setFieldValue('customerCountry', option.value)
                        }
                        loading={countries.loading}
                        error={
                          countries.error ||
                          (submitCount > 0 && errors.customerCountry)
                        }
                      />
                    </Col>
                  </Row>
                </Section>
              </Section>
            )}
          </>
        )}
        {isCustomerTemplate && hasDataCollectionPiiConsentForm && (
          <PopoverContainer width={576}>
            <Wrapper>
              <PopoverWarning>
                <ErrorTitle>Attention</ErrorTitle>
                You have a Data Collection PII Consent Form module added into
                your registration process. This means that whatever document you
                select here will also be sent to the primary worker to sign
                during registration. Make sure there is no other sign document
                module with the same content added to the registration process
                or primary workers will have to sign the same document twice.
              </PopoverWarning>
            </Wrapper>
          </PopoverContainer>
        )}
      </StepContentContainer>
      <ErrorPopover
        title="Failed to save Data Consent Form"
        {...errorPopoverProps}
        errorShown={!!error}
      />
      <StepActionContainer>
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: closeModal,
            },
            {
              text: 'Save',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </ModalContainer>
  );
}

DataConsentModal.propTypes = {
  handleChange: PropTypes.func.isRequired,
  values: PropTypes.shape({
    appenOwnsSomeDataset: PropTypes.number,
    projectContactEmail: PropTypes.string,
    appenEntity: PropTypes.number,
    pIIDataCollected: PropTypes.arrayOf(PropTypes.number),
    countriesCollectingData: PropTypes.arrayOf(PropTypes.string),
    collectThirdPartyPII: PropTypes.bool,
    collectThirdPartyPIIFrom: PropTypes.string,
    displayCustomerRealData: PropTypes.bool,
    customerDPOEmail: PropTypes.string,
    customerFullLegalName: PropTypes.string,
    customerCountry: PropTypes.string,
    entityLocation: PropTypes.string,
    documentId: PropTypes.number,
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        attributeOrder: PropTypes.number,
      }),
    ),
  }).isRequired,
  closeModal: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  showError: PropTypes.func.isRequired,
  hasDataCollectionPiiConsentForm: PropTypes.bool.isRequired,
};
