import React, { createContext, useContext, useState } from 'react';
import PropTypes from 'prop-types';

const CloneContext = createContext();

export default function CloneProvider({ children }) {
  const [isCloning, setIsCloning] = useState(false);
  const [isCloneCreated, setIsCloneCreated] = useState(false);
  const [
    blockingRulesInconsistencies,
    setBlockingRulesInconsistencies,
  ] = useState(false);

  return (
    <CloneContext.Provider
      value={{
        isCloning,
        setIsCloning,
        isCloneCreated,
        setIsCloneCreated,
        blockingRulesInconsistencies,
        setBlockingRulesInconsistencies,
      }}
    >
      {children}
    </CloneContext.Provider>
  );
}

export function useClone() {
  const context = useContext(CloneContext);
  if (!context) return {};
  const {
    isCloning,
    setIsCloning,
    isCloneCreated,
    setIsCloneCreated,
    blockingRulesInconsistencies,
    setBlockingRulesInconsistencies,
  } = context;
  return {
    isCloning,
    setIsCloning,
    isCloneCreated,
    setIsCloneCreated,
    blockingRulesInconsistencies,
    setBlockingRulesInconsistencies,
  };
}

CloneProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

CloneProvider.defaultProps = {};
