import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import shortId from 'shortid';
import { ThemeContext } from 'styled-components';
import { useFormikContext } from 'formik';
import {
  FlexContainer,
  InputContainer,
  RestContainer,
  DeleteButtonContainer,
  DeleteButtom,
} from './styles';
import Select from '../Select';
import Button from '../Button';
import { useModal } from '../Modal';
import { FloatLeft } from '../Float';
import { Label, Section } from '../../styles';
import InformationTooltip, {
  LabelWrapper,
} from '../ProcessBuilder/InformationTooltip';
import LabelGroupComponent from './LabelGroupComponent';
import PostMessageLink from '../PostMessageLink';
import Table from '../Table';
import NoEntries from '../NoEntries';
import DeleteModal from './DeleteModal';

export default function UserGroupPicker(props) {
  const {
    userGroups,
    handleProjectChange,
    handleRemove,
    handleAddLine,
    projectOptions,
    notSelectedProjectOptions,
    allUserGroups,
    hasExternalSystem,
    customerId,
    mode,
  } = props;

  const COLUMNS = [
    {
      Header: (
        <LabelWrapper>
          <Label>User Group</Label>
          <InformationTooltip
            title={
              <>
                User groups need to be assigned to
                <br />
                specific customers to appear on this
                <br />
                dropdown list.
                <br />
                <br />
                If you need to create new user groups
                <br />
                for this customer,{' '}
                <PostMessageLink to={`/qrp/core/customer/view/${customerId}`}>
                  click here
                </PostMessageLink>
              </>
            }
          />
        </LabelWrapper>
      ),
      accessor: 'name',
      key: 'name',
      disableSortBy: true,
    },
  ];

  const [modalProps, modalControls] = useModal();
  const { submitCount, errors } = useFormikContext();
  const theme = useContext(ThemeContext);
  const isSelected = (groups, index) => {
    const { groupId } = groups[index];
    return groupId !== null;
  };

  if (mode === 'view') {
    const groupList = [];

    if (allUserGroups.data && userGroups) {
      userGroups.forEach((group) => {
        allUserGroups.data.forEach((el) => {
          if (el.id === group.groupId) {
            groupList.push({
              ...el,
              experimentId: group.experimentId,
            });
          }
        });
      });
    }

    return (
      <Section maxWidth="680px">
        {groupList.length === 0 ? (
          <NoEntries
            error={!hasExternalSystem ? 'Required field is empty' : null}
            label="Not set"
          />
        ) : (
          <Table columns={COLUMNS} data={groupList} />
        )}
      </Section>
    );
  }

  return (
    <>
      {userGroups.length === 0 && (
        <>
          <LabelGroupComponent customerId={customerId} />
          <Section maxWidth="680px" marginTop="-32px">
            <NoEntries label="Not set" />
          </Section>
        </>
      )}

      {userGroups.map((userGroup, index) => (
        <>
          <LabelGroupComponent customerId={customerId} index={index + 1} />
          <FlexContainer key={userGroup.key}>
            <InputContainer>
              <Select
                name={`userGroup${index}group`}
                onChange={(value) => handleProjectChange(value, index)}
                placeholder="Select user group"
                value={userGroup.groupId}
                options={
                  userGroup.groupId ? projectOptions : notSelectedProjectOptions
                }
                loading={allUserGroups.loading}
                error={
                  allUserGroups.error ||
                  (submitCount >= 1 &&
                    get(errors, [`userGroup${index}group`], false))
                }
                data-testid={`project-setup--project-access-user-groups--select-${index}`}
                errorIncreasesHeight
              />
            </InputContainer>
            <RestContainer>
              <FloatLeft>
                <DeleteButtonContainer>
                  <DeleteButtom
                    onClick={() =>
                      isSelected(Object.values(userGroups), index)
                        ? modalControls.openModal({ index, userGroup })
                        : handleRemove(index)
                    }
                    data-testid={`project-setup--project-access-user-groups--delete-${index}`}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      xmlnsXlink="http://www.w3.org/1999/xlink"
                      width="14"
                      height="14"
                      viewBox="0 0 14 14"
                    >
                      <defs>
                        <path
                          id="a"
                          d="M1.742.312L7 5.569 12.258.312a1 1 0 0 1 1.414 0l.016.016a1 1 0 0 1 0 1.414L8.431 7l5.257 5.258a1 1 0 0 1 0 1.414l-.016.016a1 1 0 0 1-1.414 0L7 8.431l-5.258 5.257a1 1 0 0 1-1.414 0l-.016-.016a1 1 0 0 1 0-1.414L5.569 7 .312 1.742a1 1 0 0 1 0-1.414L.328.312a1 1 0 0 1 1.414 0z"
                        />
                      </defs>
                      <use
                        fill={theme.colors.lightGreyBlue}
                        fillRule="nonzero"
                        xlinkHref="#a"
                      />
                    </svg>
                  </DeleteButtom>
                </DeleteButtonContainer>
              </FloatLeft>
              {/* <FloatRight>
              <FloatContainer>
                <FloatLeft>
                  <Switch
                    name={`userGroup${index}disabled`}
                    onChange={(value) => handleToggle(value, index)}
                    value={userGroup.disabled}
                    data-testid={`project-setup--project-access-user-groups--switch-${index}`}
                  />
                </FloatLeft>
                <FloatRight>
                  <PermanentText>Enabled</PermanentText>
                </FloatRight>
              </FloatContainer>
            </FloatRight> */}
            </RestContainer>
          </FlexContainer>
        </>
      ))}

      <Section marginTop={24} marginBotton={24}>
        <Button
          data-testid="project-setup--project-access-user-groups--add"
          onClick={handleAddLine}
        >
          Add {userGroups.length !== 0 && 'Other'} User Group
        </Button>
      </Section>

      <DeleteModal
        data={modalControls.modalData}
        handleRemove={handleRemove}
        modalProps={modalProps}
        modalControls={modalControls}
      />
    </>
  );
}

UserGroupPicker.propTypes = {
  userGroups: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
      group: PropTypes.number,
      enabled: PropTypes.bool,
    }),
  ),
  handleProjectChange: PropTypes.func.isRequired,
  handleRemove: PropTypes.func.isRequired,
  handleAddLine: PropTypes.func.isRequired,
  projectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  notSelectedProjectOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }).isRequired,
  ).isRequired,
  allUserGroups: PropTypes.shape({
    data: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
      }).isRequired,
    ).isRequired,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.instanceOf(Error),
  }).isRequired,
  mode: PropTypes.oneOf(['view', 'edit']),
  hasExternalSystem: PropTypes.bool.isRequired,
  customerId: PropTypes.number.isRequired,
};

UserGroupPicker.defaultProps = {
  userGroups: [{ key: shortId.generate(), group: null, enabled: false }],
  mode: 'view',
};
