import React from 'react';
import { useSelector } from 'react-redux';
import { Container, Image, MessageTitle, Message } from './styles';
import projectCompleted from '../../../assets/images/project-completed.svg';
import legacyIntegration from '../../../helpers/legacy-integration';
import { WizardFooter } from '../../../components/Wizard';
import { FloatRight } from '../../../components/Float';
import Button from '../../../components/Button';
import { selectProject } from '../../../store/ducks/project';

export default function SetupComplete() {
  const project = useSelector(selectProject);

  return (
    <>
      <Container>
        <Image
          src={projectCompleted}
          alt="Project setup completed illustration"
        />
        <MessageTitle>Setup Complete!</MessageTitle>
        <Message>
          Your project is ready to be enabled. Until then, all project
          information can be seen and edited on the project page.
        </Message>
      </Container>
      <WizardFooter>
        <FloatRight>
          <Button
            onClick={() => legacyIntegration.informProjectCreation(project)}
          >
            Go to Project Page
          </Button>
        </FloatRight>
      </WizardFooter>
    </>
  );
}
