import React from 'react';
import styled from 'styled-components';
import { transparentize } from 'polished';
import { CellPropTypes } from './helpers';
import ConnectTag from '../../../components/ConnectTag';

function getColorForStatus(target, status, theme) {
  if (target === 'bg') {
    switch (status) {
      case 'READY':
      case 'ACCEPTED':
        return transparentize(0.9, theme.colors.green);

      case 'ENABLED':
      case 'PENDING':
        return transparentize(0.9, theme.colors.azure);

      case 'DISABLED':
      case 'DENIED':
      case 'INVALID':
      case 'FAILED':
        return transparentize(0.9, theme.colors.error);

      case 'PAUSED':
        return transparentize(0.8, theme.colors.warning);

      case 'DRAFT':
      case 'EXPIRED':
      default:
        return transparentize(0.8, theme.colors.blueGrey);
    }
  }

  switch (status) {
    case 'READY':
    case 'ACCEPTED':
      return theme.colors.green;

    case 'ENABLED':
    case 'PENDING':
      return theme.colors.azure;

    case 'DISABLED':
    case 'DENIED':
    case 'INVALID':
    case 'FAILED':
      return theme.colors.error;

    case 'PAUSED':
      return '#EFA70E';

    case 'DRAFT':
    case 'EXPIRED':
    default:
      return theme.colors.blueGrey;
  }
}

export const Tag = styled.div`
  border-radius: 4px;
  display: inline-block;
  padding: 6px 12px;
  font-weight: bold;
  font-size: 12px;
  color: ${(props) =>
    getColorForStatus('text', props.currentStatus, props.theme)};
  background-color: ${(props) =>
    getColorForStatus('bg', props.currentStatus, props.theme)};
`;

const getKindTag = (status) => {
  switch (status) {
    case 'READY':
    case 'ACCEPTED':
      return 'accent';

    case 'ENABLED':
    case 'PENDING':
      return 'positive';

    case 'DISABLED':
    case 'DENIED':
    case 'INVALID':
    case 'FAILED':
      return 'negative';

    case 'PAUSED':
      return 'warning';

    case 'DRAFT':
    case 'EXPIRED':
    default:
      return 'neutral';
  }
};

export default function StatusCell({ row }) {
  const {
    original: { status },
  } = row;

  return (
    <ConnectTag kind={getKindTag(status.value)} variant="status">
      {status.label}
    </ConnectTag>
  );
}

StatusCell.propTypes = CellPropTypes;
