/* eslint-disable no-nested-ternary */
import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';
import EditDemographics from './Embedded';
import ViewDemographics from './Metrics';

import { fetchProject, selectProject } from '../../store/ducks/project';
import { useDemographicsViewTypeContext } from '../../helpers/demographics-view-type-context';

export default function Demographics(props) {
  const { inModal } = props;
  const { viewType } = useDemographicsViewTypeContext();

  const dispatch = useDispatch();
  const match = useRouteMatch();

  const project = useSelector(selectProject);

  useEffect(() => {
    if (!inModal && match.params.projectID) {
      dispatch(fetchProject({ projectId: match.params.projectID }));
    }
  }, [dispatch, inModal, match.params.projectID]);

  return (
    <>
      {project &&
        (viewType === 'edit' ? (
          <EditDemographics />
        ) : (
          <ViewDemographics inModal={inModal} />
        ))}
    </>
  );
}

Demographics.propTypes = {
  inModal: PropTypes.bool,
};

Demographics.defaultProps = {
  inModal: false,
};
