import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { useSelector } from 'react-redux';
import Input from '../../Input';
import Select from '../../Select';
import Textarea from '../../Textarea';
import Checkbox from '../../Checkbox';
import { Label, Section } from '../../../styles';
import {
  CheckGroup,
  CheckAltMessage,
} from '../../../pages/ProjectSetup/Steps/ProjectTools/styles';
import ActionFooter from '../../ActionFooter';
import Disabled from '../../Disabled/Disabled';
import { isProcessModulePristine } from '../../../store/ducks/targetBuilder';
import { TextAreaCount } from './styles';

export default function ProcessDetailsModal(props) {
  const {
    handleChange,
    values,
    errors,
    submitCount,
    setFieldValue,
    submitForm,
    onCancel,
    isSubmitting,
    options,
    loading,
    error,
  } = props;

  const processDescription = get(values, 'processDescription', '');
  const [textAreaCount, setTextAreaCount] = useState(processDescription.length);

  const handleProcessDescriptionChange = useCallback(
    (e) => {
      const { value } = e.target;
      setFieldValue('processDescription', value);
      setTextAreaCount(value.length);
    },
    [setFieldValue],
  );

  const handleCheckboxChange = useCallback(() => {
    setFieldValue(
      'showCopyFromProcessSelect',
      !values.showCopyFromProcessSelect,
    );
  }, [setFieldValue, values.showCopyFromProcessSelect]);

  const handleSelectChange = useCallback(
    ({ value }) => {
      setFieldValue('copyModulesFrom', value);
    },
    [setFieldValue],
  );

  const isPristine = useSelector(isProcessModulePristine);

  return (
    <>
      <Section marginBottom="48px" marginRight="16px">
        <Section>
          <Label htmlFor="processName">Group Name</Label>
          <Input
            name="processName"
            onChange={handleChange}
            id="processName"
            placeholder="Name of the group"
            value={values.processName}
            maxLength={255}
            error={submitCount >= 1 && errors.processName}
          />
        </Section>
        <Section>
          <Label htmlFor="processDescription">Group Description</Label>
          <Textarea
            onChange={handleProcessDescriptionChange}
            name="processDescription"
            id="processDescription"
            value={values.processDescription}
            placeholder="Describe your target audience group in a few words"
            rows={6}
            maxLength={255}
            error={submitCount >= 1 && errors.processDescription}
          />
          <TextAreaCount>{textAreaCount}/255 Characters</TextAreaCount>
        </Section>
        {/* Temporary hack to hide copy feature */}
        <div style={{ display: 'none' }}>
          <Disabled
            when={!isPristine}
            message="You have unsaved changes in the target builder."
          >
            <CheckGroup>
              <Checkbox
                name="showCopyFromProcessSelect"
                onChange={handleCheckboxChange}
                checked={values.showCopyFromProcessSelect}
                label="Copy Questions From an Existing Group"
              />
              <CheckAltMessage>
                Start building your target audience group with the same question
                <br />
                configuration and rules used on an existing group.
              </CheckAltMessage>
            </CheckGroup>
            {values.showCopyFromProcessSelect && (
              <Section marginLeft="34px" marginTop="32px">
                <Label htmlFor="copyModulesFrom">Copy questions from</Label>
                <Select
                  name="copyModulesFrom"
                  placeholder="Select question"
                  options={options}
                  loading={loading}
                  value={values.copyModulesFrom}
                  error={error || (submitCount >= 1 && errors.copyModulesFrom)}
                  onChange={handleSelectChange}
                />
              </Section>
            )}
          </Disabled>
        </div>
      </Section>
      <ActionFooter
        rightButtons={[
          {
            text: 'Cancel',
            type: 'secondary',
            onClick: onCancel,
            disabled: isSubmitting,
          },
          { text: 'Save', onClick: submitForm, loading: isSubmitting },
        ]}
      />
    </>
  );
}

ProcessDetailsModal.propTypes = {
  handleChange: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  isSubmitting: PropTypes.bool.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.number,
      label: PropTypes.string,
    }),
  ).isRequired,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.oneOfType([
    PropTypes.oneOf([null]),
    PropTypes.instanceOf(Error),
  ]),
};

ProcessDetailsModal.defaultProps = {
  error: null,
};
