import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { ArrowContainer } from './styles';
import { createKeydownHandler } from '../../../../helpers/a11y';
import Icon from '../../../Icon';
import { ReactComponent as ChevronRight } from '../../../../assets/icons/chevron-right.svg';
import { ReactComponent as ChevronLeft } from '../../../../assets/icons/chevron-left.svg';

export default function Arrow(props) {
  const { onClick, direction, hasNext, hasPrev } = props;

  const handleKeyDown = useMemo(() => {
    return createKeydownHandler(onClick, 13);
  }, [onClick]);

  const isActive = direction === 'backward' ? hasPrev : hasNext;

  return (
    <ArrowContainer arrowDirection={direction}>
      <Icon
        icon={direction === 'backward' ? <ChevronLeft /> : <ChevronRight />}
        inheritColor={false}
        style={{
          opacity: isActive ? 1 : 0.4,
          pointerEvents: isActive ? 'all' : 'none',
        }}
        iconButton
        onClick={onClick}
        onKeyDown={handleKeyDown}
        role="button"
        tabIndex={0}
      />
    </ArrowContainer>
  );
}

Arrow.propTypes = {
  onClick: PropTypes.func.isRequired,
  direction: PropTypes.oneOf(['backward', 'forward']),
  hasNext: PropTypes.bool,
  hasPrev: PropTypes.bool,
};

Arrow.defaultProps = {
  direction: 'backward',
  hasNext: false,
  hasPrev: false,
};
