import React, { useCallback, useMemo, useEffect } from 'react';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { DndProvider } from 'react-dnd';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import noop from 'lodash/noop';
import { Helmet } from 'react-helmet';
import { Shimmer } from 'react-shimmer';
import {
  ProcessBuilderContainer,
  ProcessBuilderMainWindow,
  ProcessBuilderFooter,
  ProcessBuilderHeader,
  ProcessBuilderSidebar,
  ProcessBuilderProcess,
  ProcessBuilderTitle,
  ProcessBuilderDescription,
  SavingText,
  ProcessBuilderActions,
  ProcessDetails,
} from './styles';
import ProcessesColumn from '../Builder/ProcessesColumn';
import SideModules from '../Builder/SideModules';
import ActionFooter from '../ActionFooter';
import {
  selectProcessDetails,
  saveOrCreateProcess,
  selectProjectSaving,
  PROCESS_TYPE,
  resetState,
  selectProjectLoading,
  selectDetailsModalOpen,
  toggleDetailsModal,
  selectAffectedProjectsModalOpen,
  hasAnyDirtyProcessModule,
  setProcessModules,
  selectStepEditorDirty,
  selectProcessModules,
} from '../../store/ducks/processBuilder';
import Modal from '../Modal';
import ProcessDetailsModal from './ProcessDetailsModal';
import Popover, { PopoverItem, PopoverIcon, PopoverText } from '../Popover';
import pencil from '../../assets/icons/pencil.svg';
import close from '../../assets/icons/close.svg';
import { createKeydownHandler } from '../../helpers/a11y';
import SettingsHoverable, { SettingsStyled } from '../Icons/SettingsHoverable';
import { Spacer } from '../Builder/ProcessesColumn/styles';
import AffectedProjectsModal from './AffectedProjectsModal';
import ErrorPopover, { useErrorPopover } from '../ErrorPopover';
import stripHTMLTags from '../../helpers/strip-html-tags';
import {
  PreviewProvider,
  PreviewConsumer,
} from '../Builder/helpers/preview-context';
import PreviewModal from './PreviewModal';

const popoverModifiers = {
  offset: {
    offset: '0, -2',
  },
};

export default function ProcessBuilder(props) {
  const { processType, onProcessChange, onCancel, processModules } = props;

  const processDetails = useSelector(selectProcessDetails);
  const { processName, processDescription, processID } = processDetails;

  const isSaving = useSelector(selectProjectSaving);
  const isLoading = useSelector(selectProjectLoading);
  const detailsModalOpen = useSelector(selectDetailsModalOpen);
  const hasAnyDirtyModule = useSelector(hasAnyDirtyProcessModule);

  const hasModules = !!processModules.length;

  const affectedProjectsModalOpen = useSelector(
    selectAffectedProjectsModalOpen,
  );

  const [showError, hideError, spreadableProps] = useErrorPopover();

  useEffect(() => {
    hideError();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [processModules, hideError]);

  const handleProcessChange = useCallback(
    (pType, pID, justCreated) => {
      hideError();
      onProcessChange(pType, pID, justCreated);
    },
    [hideError, onProcessChange],
  );

  const dispatch = useDispatch();

  const createOrSaveProcess = useCallback(() => {
    dispatch(
      saveOrCreateProcess(null, {
        onProcessChange: handleProcessChange,
        onError: showError,
      }),
    );
  }, [dispatch, handleProcessChange, showError]);

  const handleCancelation = useCallback(() => {
    dispatch(resetState());
    onCancel();
  }, [dispatch, onCancel]);

  const openEditModal = useCallback(() => {
    dispatch(toggleDetailsModal(true));
  }, [dispatch]);

  const closeEditModal = useCallback(() => {
    dispatch(toggleDetailsModal(false));
  }, [dispatch]);

  const poppoverContent = (
    <>
      <PopoverItem
        onClick={openEditModal}
        onKeyDown={createKeydownHandler(openEditModal)}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={pencil} alt="Edit process details" />
        <PopoverText>Edit Details</PopoverText>
      </PopoverItem>
      <PopoverItem
        onClick={handleCancelation}
        onKeyDown={createKeydownHandler(handleCancelation)}
        role="button"
        tabIndex={0}
      >
        <PopoverIcon src={close} alt="Cancel editing" />
        <PopoverText>Cancel</PopoverText>
      </PopoverItem>
    </>
  );

  const parsedProcessDescription = useMemo(() => {
    return stripHTMLTags(processDescription);
  }, [processDescription]);

  return (
    <PreviewProvider processModules={processModules} processType={processType}>
      <Helmet>
        {/* Helmet uses style strings */}
        {/* eslint-disable-next-line react/style-prop-object */}
        <body style="overflow: hidden;" />
      </Helmet>
      <DndProvider backend={HTML5Backend}>
        <ProcessBuilderContainer>
          <ProcessBuilderMainWindow>
            <ProcessBuilderHeader>
              <ProcessDetails>
                <ProcessBuilderTitle>
                  {processType === PROCESS_TYPE.REGISTRATION
                    ? 'Registration'
                    : 'Qualification'}{' '}
                  Process Builder
                </ProcessBuilderTitle>
                <ProcessBuilderDescription title={parsedProcessDescription}>
                  {processName ||
                    `New ${
                      processType === PROCESS_TYPE.REGISTRATION
                        ? 'registration'
                        : 'qualification'
                    } process`}
                </ProcessBuilderDescription>
              </ProcessDetails>
              <ProcessBuilderActions>
                <Popover
                  content={poppoverContent}
                  placement="bottom-end"
                  modifiers={popoverModifiers}
                >
                  <SettingsStyled>
                    <SettingsHoverable />
                  </SettingsStyled>
                </Popover>
              </ProcessBuilderActions>
            </ProcessBuilderHeader>
            <ProcessBuilderProcess>
              <ProcessesColumn
                isLoading={isLoading}
                openPreview={() => {}}
                instantiated={processDetails.instantiated}
                processModules={processModules}
                label="Drag modules to build your process"
                setProcessModules={setProcessModules}
              />
            </ProcessBuilderProcess>
            <ProcessBuilderFooter>
              <PreviewConsumer>
                {({ modalControls }) => (
                  <ActionFooter
                    hideDivider
                    leftButtons={[
                      {
                        text: 'Preview Process',
                        type: 'secondary',
                        onClick: () => modalControls.openModal(),
                      },
                    ]}
                    accessoryRight={
                      isSaving && <SavingText>Saving...</SavingText>
                    }
                    rightButtons={[
                      {
                        text: processID ? 'Exit' : 'Cancel',
                        type: 'secondary',
                        onClick: handleCancelation,
                        disabled: isSaving,
                      },
                      {
                        text: processID
                          ? 'Save Updates'
                          : `Create ${
                              processType === PROCESS_TYPE.REGISTRATION
                                ? 'Registration'
                                : 'Qualification'
                            }  Process`,
                        onClick: createOrSaveProcess,
                        disabled: hasAnyDirtyModule || !hasModules,
                      },
                    ]}
                  />
                )}
              </PreviewConsumer>
              <ErrorPopover {...spreadableProps} />
            </ProcessBuilderFooter>
          </ProcessBuilderMainWindow>
          <ProcessBuilderSidebar>
            <SideModules
              isLoading={isLoading}
              processType={processType}
              title={'Select Modules'}
              selectStepEditorDirty={selectStepEditorDirty}
              selectProcessModules={selectProcessModules}
            />
          </ProcessBuilderSidebar>
        </ProcessBuilderContainer>
      </DndProvider>

      <Modal
        open={detailsModalOpen}
        title="Process Details"
        onClose={processID ? closeEditModal : noop}
        hideCloseButton={!processID}
      >
        {isLoading ? (
          <>
            {/* bug in focus trap where it needs a focusable element to render the modal */}
            {/* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */}
            <div tabIndex={0} aria-hidden />
            <Shimmer width={320} height={40} />
            <Spacer />
            <Shimmer width={500} height={250} />
            <Spacer />
            <Shimmer width={410} height={40} />
          </>
        ) : (
          <ProcessDetailsModal
            closeModal={closeEditModal}
            onCancel={processID ? closeEditModal : handleCancelation}
            processType={processType}
            initialValues={processDetails}
          />
        )}
      </Modal>
      <Modal
        open={affectedProjectsModalOpen}
        title="Edit Registration Process"
        onClose={onCancel}
      >
        <AffectedProjectsModal
          closeModal={closeEditModal}
          onCancel={handleCancelation}
          processType={processType}
        />
      </Modal>
      <PreviewModal />
    </PreviewProvider>
  );
}

ProcessBuilder.propTypes = {
  processType: PropTypes.oneOf(Object.values(PROCESS_TYPE)).isRequired,
  onCancel: PropTypes.func.isRequired,
  onProcessChange: PropTypes.func.isRequired,
  processModules: PropTypes.arrayOf(PropTypes.object).isRequired,
};
