import React from 'react';
import PropTypes from 'prop-types';
import { Center, Duplicates } from './styles';

export default function DuplicatesCell({ row }) {
  const {
    original: { duplicates },
  } = row;

  return (
    <Center>
      <Duplicates count={duplicates}>{duplicates}</Duplicates>
    </Center>
  );
}

DuplicatesCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({ duplicates: PropTypes.number }).isRequired,
  }).isRequired,
};
