import { useFormikContext } from 'formik';
import React, { useCallback, useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import isSWFH from '../../../pages/ProjectSetup/helpers/isSWFH';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';
import useIPAddressMultilineInput from './useIPAddressMultilineInput';
import { selectAll as selectAllTenantsByCountry } from '../../../store/ducks/tenantsByCountry';
import close from '../../../assets/icons/close.svg';
import { findById } from '../../../helpers/find';
import { Label, Section } from '../../../styles';
import Checkbox from '../../Checkbox';
import Select from '../../Select';
import Input from '../../Input';
import Disabled from '../../Disabled';
import Button from '../../Button';
import { selectAll as selectAllLocaleTenants } from '../../../store/ducks/localeTenants';
import {
  AllowedIpsInputContainer,
  CheckAltMessage,
  CheckGroup,
  DynamicButtonContainer,
  RemoveIPAddressButton,
} from './styles';

export default function ProjectToolsForm() {
  const {
    handleChange,
    setFieldValue,
    errors,
    values,
    submitCount,
  } = useFormikContext();

  const { ipAddresses = [] } = values;

  const handleCheckExternalProject = useCallback(() => {
    setFieldValue('hasExternalSystem', !values.hasExternalSystem);
  }, [setFieldValue, values.hasExternalSystem]);

  const {
    handleAddLine,
    handleRemove,
    handleLineChange,
    handleToggleSecureIps,
    handleKeyDown,
  } = useIPAddressMultilineInput();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.externalSystems));
  }, [dispatch]);

  const externalSystems = useSelector(
    selectors.createSelectSliceFor(ENTITIES.externalSystems),
  );

  const externalSystemsOptions = useMemo(
    () =>
      (externalSystems.data || [])
        .filter((externalSystem) => externalSystem.externalSystemIsDisplayed)
        .map((externalSystem) => ({
          value: externalSystem.id,
          label: externalSystem.name,
        })),
    [externalSystems.data],
  );

  // SWFH edge case business rule
  const hasSWFH = useMemo(() => isSWFH(values.externalSystem), [
    values.externalSystem,
  ]);

  // Only enable Secure IP if we have a secure facility tenant
  const localeTenants = useSelector(selectAllLocaleTenants);
  const allTenants = useSelector(selectAllTenantsByCountry);

  const enableSecureIP = useMemo(
    () =>
      !!localeTenants.find((lt) => {
        const tenant = findById(allTenants, lt.tenant);
        return (
          tenant &&
          tenant.employmentType &&
          tenant.employmentType.value === 'FACILITY_EMPLOYEE'
        );
      }),
    [localeTenants, allTenants],
  );

  return (
    <>
      <Section marginBottom={16}>
        <CheckGroup>
          <Checkbox
            name="hasExternalSystem"
            onChange={handleCheckExternalProject}
            checked={values.hasExternalSystem}
            label="External System"
            bold
            data-testid="project-setup--project-tools--has-external-system-toggle"
          />
          <CheckAltMessage>
            If checked, vendors will need to acquire tasks from an external
            source that is not part of our task platform.
          </CheckAltMessage>
          {values.hasExternalSystem && (
            <>
              <Section
                marginLeft={32}
                marginTop={16}
                marginBottom={hasSWFH ? 16 : 32}
              >
                <Label>External System</Label>
                <Select
                  name="externalSystem"
                  placeholder="Select external system"
                  value={values.externalSystem}
                  options={externalSystemsOptions}
                  loading={externalSystems.loading}
                  data-testid="project-setup--project-tools--select-external-system"
                  onChange={({ value }) =>
                    setFieldValue('externalSystem', value)
                  }
                  error={
                    (submitCount >= 1 && errors.externalSystem) ||
                    externalSystems.error
                  }
                />
              </Section>
              {hasSWFH && (
                <Section marginLeft={32}>
                  <Label>SWFH Url</Label>
                  <Input
                    name="swfhProjectExternalUrl"
                    placeholder="Input SWFH url"
                    value={values.swfhProjectExternalUrl}
                    onChange={handleChange}
                    error={submitCount >= 1 && errors.swfhProjectExternalUrl}
                    data-testid="project-setup--project-tools--swfh-project"
                  />
                </Section>
              )}
            </>
          )}
        </CheckGroup>
      </Section>

      <Disabled
        when={!enableSecureIP}
        message={
          'Option available only when a Secure Facility Tenant is configured.'
        }
      >
        <Section>
          <CheckGroup>
            <Checkbox
              name="hasIPRestrictions"
              onChange={handleToggleSecureIps}
              checked={values.hasIPRestrictions}
              label="Secure IP Access"
              data-testid="project-setup--project-tools--has-secure-ip-toggle"
            />
            <CheckAltMessage>
              If checked, only selected IPs will be allowed access
            </CheckAltMessage>
          </CheckGroup>
          {values.hasIPRestrictions && (
            <Section marginLeft={32} marginBottom={0} marginTop={12}>
              <Label>Allowed IPs</Label>
              {ipAddresses.map((ipAddress, index) => (
                <Section marginBottom={8} key={ipAddress.key}>
                  <AllowedIpsInputContainer>
                    <div>
                      <Input
                        name={`ipAddress${index}`}
                        onChange={handleLineChange(index)}
                        placeholder="Input allowed IP"
                        value={ipAddress.ipAddress}
                        error={submitCount >= 1 && errors[`ipAddress${index}`]}
                        onKeyDown={handleKeyDown}
                        data-testid={`project-setup--project-tools--ip-address-${index}-input`}
                      />
                    </div>
                    <RemoveIPAddressButton
                      onClick={() => handleRemove(index)}
                      tabIndex={index > 0 ? 0 : -1}
                      disabled={index === 0}
                      data-testid={`project-setup--project-tools--ip-address-${index}-delete-button`}
                    >
                      {index > 0 && <img src={close} alt="remove ip address" />}
                    </RemoveIPAddressButton>
                  </AllowedIpsInputContainer>
                </Section>
              ))}
            </Section>
          )}
          {values.hasIPRestrictions && (
            <DynamicButtonContainer>
              <Button
                data-testid="project-setup--project-tools--add-ip-address-button"
                htmlType="button"
                onClick={() => handleAddLine()}
              >
                Add Other IP
              </Button>
            </DynamicButtonContainer>
          )}
        </Section>
      </Disabled>
    </>
  );
}
