import createEntitiesSlice from './engine';
import ENDPOINTS from '../../../constants/endpoints';
import { profileBuilderAPI, userSvcAPI } from '../../../services/axios';

export const PAGINATED_ENTITIES = {
  projects: 'projects',
  profileQuestions: 'profileQuestions',
  userScreening: 'userScreening',
};

const ENTITIES_CONFIG = {
  [PAGINATED_ENTITIES.projects]: {
    entityPK: 'id',
    lazyFetch: true, // dont refetch pages
    initialFilters: {
      status: 'ALL_STATUSES',
      customer: -1,
    },
    buildURL: (slice) => {
      let disabled = null;

      if (slice.filters.status === 'ENABLED') {
        disabled = false;
      } else if (slice.filters.status === 'DISABLED') {
        disabled = true;
      }

      let customer = null;

      if (typeof slice.filters.customer === 'number') {
        if (slice.filters.customer > 0) {
          customer = slice.filters.customer;
        }
      }

      let status = null;

      if (slice.filters.status !== 'ALL_STATUSES') {
        status = slice.filters.status;
      }

      return ENDPOINTS.V2.PROJECTS({
        query: {
          disabled,
          viewType: 'full',
          nameOrAlias: slice.filters.name || '',
          customerId: customer,
          page: slice.currentPage,
          size: slice.pageSize,
          sort: slice.filters.sort,
          direction: slice.filters.direction,
          ...(status ? { status } : {}),
        },
      });
    },
  },
  [PAGINATED_ENTITIES.profileQuestions]: {
    entityPK: 'id',
    connection: profileBuilderAPI,
    lazyFetch: true, // dont refetch pages
    initialFilters: {
      topic: '',
      category: '*',
    },
    buildURL: (slice) => {
      const {
        filters: { topic: name, category },
      } = slice;

      return ENDPOINTS.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS({
        query: {
          name,
          category,
        },
      });
    },
  },
  [PAGINATED_ENTITIES.userScreening]: {
    entityPK: 'id',
    connection: userSvcAPI,
    lazyFetch: true, // dont refetch pages
    initialFilters: {
      pageSize: 10,
    },
    buildURL: (slice) => {
      const {
        sort,
        direction,
        name,
        languages,
        locales,
        countries,
        ageGroupMax,
        ageGroupMin,
        directPay,
        genderFemale,
        ipCheckPassed,
        ipFailureMismatch,
        ipFailureVPN,
        maliciousnessLevelHigh,
        maliciousnessLevelMedium,
        optInA9,
        similarityScoreMax,
        similarityScoreMin,
      } = slice.filters;

      const { currentPage: page, pageSize: size } = slice;

      let status = null;

      if (slice.filters.status !== 'ALL_STATUSES') {
        status = slice.filters.status;
      }

      return ENDPOINTS.USER_SCREENING.ADVANCED_SEARCH({
        query: {
          ...(page ? { page } : {}),
          ...(size ? { size } : {}),
          ...(name ? { name } : {}),
          ...(status ? { status } : {}),
          ...(languages ? { languages } : {}),
          ...(countries ? { countries } : {}),
          ...(locales ? { locales } : {}),
          ...(ageGroupMax ? { ageGroupMax } : {}),
          ...(ageGroupMin ? { ageGroupMin } : {}),
          ...(directPay ? { directPay } : {}),
          ...(genderFemale ? { genderFemale } : {}),
          ...(ipCheckPassed ? { ipCheckPassed } : {}),
          ...(ipFailureMismatch ? { ipFailureMismatch } : {}),
          ...(ipFailureVPN ? { ipFailureVPN } : {}),
          ...(maliciousnessLevelHigh ? { maliciousnessLevelHigh } : {}),
          ...(maliciousnessLevelMedium ? { maliciousnessLevelMedium } : {}),
          ...(optInA9 ? { optInA9 } : {}),
          ...(similarityScoreMax ? { similarityScoreMax } : {}),
          ...(similarityScoreMin ? { similarityScoreMin } : {}),
          sort: sort ? `${sort},${direction}` : undefined,
          allowComma: true,
        },
      });
    },
  },
};

const options = createEntitiesSlice(ENTITIES_CONFIG);

export default options.reducer;

const {
  fetchPaginated,
  nextPage,
  prevPage,
  setFilters,
  setPageSize,
  resetFor,
  saga,
  selectors,
} = options;

export {
  fetchPaginated,
  nextPage,
  prevPage,
  setFilters,
  setPageSize,
  resetFor,
  saga,
  selectors,
};

export const SORT_DIRECTIONS = {
  DESC: 'DESC',
  ASC: 'ASC',
};
