import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveUserThirdPartyList,
  saveUserThirdPartyListError,
  saveUserThirdPartyListSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* saveUserThirdPartyListSagaFn(action) {
  const {
    payload: {
      changes: { thirdParties },
      thenPut,
      closeModal,
      onError,
    },
  } = action;

  try {
    const response = yield call(
      axios.post,
      ENDPOINTS.V2.BULK_THIRD_PARTIES(),
      thirdParties,
    );

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    if (typeof closeModal === 'function') {
      closeModal();
    }

    yield put(saveUserThirdPartyListSuccess(response.data));
  } catch (error) {
    onError(
      error.message ||
        'There was an error creating the Third Party Consent form. Please try again.',
    );
    yield put(saveUserThirdPartyListError(error));
  }
}

export default takeLatest(
  saveUserThirdPartyList.type,
  saveUserThirdPartyListSagaFn,
);
