import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useFormikContext } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import mapToOptions from '../../../../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  ENTITIES,
  fetch,
  selectors,
} from '../../../../../../store/ducks/options';
import { Section, Label } from '../../../../../../styles';
import Checkbox from '../../../../../Checkbox';
import LabeledSwitch from '../../../../../LabeledSwitch';
import Radio from '../../../../../Radio';
import Select from '../../../../../Select';
import Textarea from '../../../../../Textarea';
import { CheckGroup } from '../../../../ProjectToolsForm/styles';
import ResourceFormContainer from '../ResourceFormContainer';

const DOCUMENT_TYPES = [
  {
    label: 'Upskilling',
    value: 'TRAINING',
  },
  {
    label: 'Guideline',
    value: 'GUIDELINE',
  },
];

export default function DocumentResourceForm(props) {
  const { closeModal } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.documents));
  }, [dispatch]);

  const { data: documents, error, loading } = useSelector(
    selectors.createSelectSliceFor(ENTITIES.documents),
  );

  const documentsOptions = useMemo(
    () => mapToOptions('id', 'name')(documents),
    [documents],
  );

  const {
    setFieldValue,
    values,
    submitCount,
    errors,
    handleChange,
  } = useFormikContext();

  return (
    <ResourceFormContainer closeModal={closeModal}>
      <Section>
        <Radio
          name="usageType"
          onChange={handleChange}
          options={DOCUMENT_TYPES}
          value={values.usageType || DOCUMENT_TYPES[0].value}
        />
      </Section>

      <Section>
        <Label htmlFor="resourceId">Document</Label>
        <Section marginBottom={14}>
          <Select
            name="resourceId"
            id="resourceId"
            options={documentsOptions}
            onChange={({ value }) => setFieldValue('resourceId', value)}
            value={values.resourceId}
            placeholder="Select existing document"
            error={error || (submitCount >= 1 && errors.resourceId)}
            loading={loading}
            errorIncreasesHeight
          />
        </Section>
        {values.usageType === DOCUMENT_TYPES[1].value && (
          <LabeledSwitch
            name="requireAcknowledgement"
            onChange={handleChange}
            value={values.requireAcknowledgement}
            label="REQUIRE ACKNOWLEDGEMENT"
          />
        )}
      </Section>

      <Section>
        <Section marginBottom={24}>
          <CheckGroup>
            <Checkbox
              name="allowQualifyingApplicantAccess"
              onChange={handleChange}
              checked={values.allowQualifyingApplicantAccess}
              label="Allow Qualifying Applicants Access"
              bold={false}
            />
          </CheckGroup>
        </Section>
        <Section marginBottom={16}>
          <CheckGroup>
            <Checkbox
              name="hasNotesToRaters"
              onChange={handleChange}
              checked={values.hasNotesToRaters}
              label="Add Notes To Raters"
              bold={false}
            />
          </CheckGroup>
        </Section>

        {values.hasNotesToRaters && (
          <>
            <Label htmlFor="noteToRater">Notes to Raters</Label>
            <Textarea
              onChange={handleChange}
              value={values.noteToRater}
              name="noteToRater"
              placeholder="Describe in a few words"
              rows={6}
              error={submitCount >= 1 && errors.noteToRater}
            />
          </>
        )}
      </Section>
    </ResourceFormContainer>
  );
}

DocumentResourceForm.propTypes = {
  closeModal: PropTypes.func.isRequired,
};
