import { createEntityAdapter, createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';
import undoable from 'redux-undo';
import { findById, findByValue } from '../../../helpers/find';
import { selectAll as selectAllTenantsByCountries } from '../tenantsByCountry';
import { ENTITIES, selectors } from '../options';

const localeTenantAdapter = createEntityAdapter({
  selectId: (e) => e.id || e.tenant,
});

const slice = createSlice({
  name: 'localeTenants',
  initialState: localeTenantAdapter.getInitialState({
    loading: false,
    error: null,
  }),
  reducers: {
    fetchAll(state) {
      state.loading = true;
    },
    fetchFailed(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    setAll(state, action) {
      state.loading = false;

      return localeTenantAdapter.setAll(state, action);
    },
    addOne: {
      reducer: localeTenantAdapter.addOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    addMany: localeTenantAdapter.addMany,
    removeOne: localeTenantAdapter.removeOne,
    removeMany: localeTenantAdapter.removeMany,
    removeAll: localeTenantAdapter.removeAll,
    updateOne: {
      reducer: localeTenantAdapter.updateOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    updateMany: localeTenantAdapter.updateMany,
    upsertOne: localeTenantAdapter.upsertOne,
    upsertMany: localeTenantAdapter.upsertMany,
    setError(state, action) {
      state.error = action.payload;
    },
  },
});

const {
  reducer,
  actions: {
    fetchAll,
    fetchFailed,
    setAll,
    addOne,
    addMany,
    removeOne,
    removeMany,
    removeAll,
    updateOne,
    updateMany,
    upsertOne,
    upsertMany,
    setError,
  },
} = slice;

const UNDO_TYPE = 'localeTenants/undo';

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}

const localeTenantsReducer = undoable(reducer, {
  limit: 1,
  undoType: UNDO_TYPE,
});

localeTenantsReducer.key = 'localeTenants';

const {
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
} = localeTenantAdapter.getSelectors(
  (state) => state[localeTenantsReducer.key].present,
);

export function selectLocaleTentansLoading(state) {
  return state[localeTenantsReducer.key].present.loading;
}

export function hasLocaleTenants(state) {
  const ids = selectIds(state);

  return !!ids.length;
}

export default localeTenantsReducer;

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
  setError,
};

export function selectError(state) {
  const currentSlice = state[localeTenantsReducer.key];

  return currentSlice.present.error || null;
}

export const selectAllEager = (state) => {
  const localeTenants = selectAll(state);
  const allCountries = selectors.selectDataFor(ENTITIES.countries, null, state);
  const allTenantsByCountries = selectAllTenantsByCountries(state);

  return localeTenants.map((t) => {
    const tenant = findById(allTenantsByCountries, t.tenant);
    const country = findByValue(allCountries, t.country);

    return {
      ...t,
      tenant: get(tenant, 'name', ''),
      country: get(country, 'label', ''),
      tenantType: get(tenant, ['employmentType', 'label'], ''),
    };
  });
};

export function selectByCountry(state) {
  return (country) => {
    const localeTenants = selectAll(state) || [];
    const localeTenantByCountry = localeTenants.find(
      (lt) => lt.country === country,
    );
    const localeTenantAllCountries = localeTenants.find(
      (lt) => lt.country === '*',
    );
    return localeTenantByCountry || localeTenantAllCountries;
  };
}

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.localeTenants = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}
