import { withFormik } from 'formik';
import { compose } from 'recompose';
import DeleteModal from './DeleteModal';
import { withErrorPopover } from '../../ErrorPopover';

async function handleSubmit(values, formikBag) {
  const {
    data: { index },
    handleRemove,
  } = values;
  const {
    props: {
      modalControls: { closeModal },
    },
  } = formikBag;

  handleRemove(index);
  closeModal();
}

function mapPropsToValues(props) {
  const { data, handleRemove } = props;

  if (data) {
    return {
      data,
      handleRemove,
    };
  }

  return {
    data: {},
    handleRemove: () => {},
  };
}

export default compose(
  withErrorPopover,
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
  }),
)(DeleteModal);
