import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';

import { compose } from 'recompose';
import withHooks from 'hook-hoc';

import ProjectAccessPreview from './ProjectAccessPreview';
import {
  fetchExperimentGroups,
  selectData as selectExperimentGroups,
  selectAccessHolder,
} from '../../../store/ducks/experimentGroups';
import { ENTITIES, selectors } from '../../../store/ducks/options';

function useHooksAsProps() {
  const dispatch = useDispatch();

  const accessHolders = useSelector(selectAccessHolder);
  const userGroups = useSelector(selectExperimentGroups);

  const allUserGroups = useSelector(
    selectors.createSelectSliceFor(ENTITIES.userGroups),
  );

  const accessHolder = (accessHolders || [{}])[0];

  const [userGroupsByExperimentId, setUserGroupsByExperimentId] = useState([]);

  useEffect(() => {
    const experimentId = accessHolder ? accessHolder.id : null;
    setUserGroupsByExperimentId(get(userGroups, experimentId, []));
  }, [accessHolder, userGroups]);

  useEffect(() => {
    if (accessHolder !== undefined) {
      dispatch(fetchExperimentGroups({ experimentId: accessHolder.id }));
    }
  }, [dispatch, accessHolder]);

  return {
    accessHolder,
    userGroups: userGroupsByExperimentId,
    allUserGroups,
  };
}

export default compose(withHooks(useHooksAsProps))(ProjectAccessPreview);
