import React from 'react';
import PropTypes from 'prop-types';
import Input from '../../../Input';
import { Label, Section } from '../../../../styles';
import RichTextEditor, {
  useEditorChangeHandler,
} from '../../../RichTextEditor';
import { EmailDefaultText } from './styles';
import ActionFooter from '../../../ActionFooter';
import {
  StepContentContainer,
  StepActionContainer,
  CollapsedViewContainer,
} from '../styles';

const EmailHeader = (
  <EmailDefaultText>
    Dear (Users full name),
    <br />
    Regarding project (Project Name)
  </EmailDefaultText>
);

const EmailFooter = (
  <EmailDefaultText>
    Best Regards,
    <br />
    The Appen team.
  </EmailDefaultText>
);

export default function SendEmailStep(props) {
  const {
    handleChange,
    values,
    errors,
    submitCount,
    collapsed,
    submitForm,
    cancelEditing,
    card: { attributes },
  } = props;

  const handleEditorChange = useEditorChangeHandler('emailContent');

  if (collapsed && attributes && attributes.length) {
    return (
      <CollapsedViewContainer>{values.emailTitle || ''}</CollapsedViewContainer>
    );
  }

  return (
    <>
      <StepContentContainer>
        <Section>
          <Label htmlFor="emailTitle">Email Title</Label>
          <Input
            name="emailTitle"
            onChange={handleChange}
            id="emailTitle"
            placeholder="Name of your project"
            value={values.emailTitle}
            error={submitCount >= 1 && errors.emailTitle}
          />
        </Section>
        <Section>
          <Label htmlFor="emailContent">Email Content</Label>
          <RichTextEditor
            name="emailContent"
            onChange={handleEditorChange}
            id="emailContent"
            placeholder="Write email here"
            value={values.emailContent}
            error={submitCount >= 1 && errors.emailContent}
            accessoryHeader={EmailHeader}
            accessoryFooter={EmailFooter}
          />
        </Section>
      </StepContentContainer>
      <StepActionContainer>
        <ActionFooter
          hideDivider
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !attributes.length ? 'Add & Close' : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

SendEmailStep.propTypes = {
  handleChange: PropTypes.func.isRequired,
  values: PropTypes.shape({
    emailTitle: PropTypes.string,
    emailContent: PropTypes.any,
  }).isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.string,
        attributeOrder: PropTypes.number,
      }),
    ),
  }).isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
