import { call, put, takeLatest } from 'redux-saga/effects';
import {
  saveProfileQuestionsSuccess,
  saveProfileQuestionsError,
  saveProfileQuestions,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import { profileBuilderAPI } from '../../../services/axios';

function* saveProfileQuestionsSagaFn(action) {
  const {
    payload: { changes: payload, thenPut, closeModal, operation },
  } = action;

  try {
    const create = operation === 'create';
    let endpoint = ENDPOINTS.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS();

    if (!create) {
      endpoint = ENDPOINTS.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS({
        pathVariables: {
          id: payload.id,
        },
      });
    }

    const method = create ? profileBuilderAPI.post : profileBuilderAPI.put;
    const response = yield call(method, endpoint, payload);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    if (typeof closeModal === 'function') {
      closeModal();
    }

    yield put(saveProfileQuestionsSuccess(response.data));
  } catch (error) {
    yield put(saveProfileQuestionsError(error));
  }
}

export default takeLatest(
  saveProfileQuestions.type,
  saveProfileQuestionsSagaFn,
);
