import { call, put, takeLeading } from 'redux-saga/effects';
import axios from '../../../services/axios';
import ENDPOINTS from '../../../constants/endpoints';
import { fetchStepsSucceded, fetchStepsFailed, fetchSteps } from '.';

function* fetchStepsSaga(action) {
  try {
    const response = yield call(
      axios,
      ENDPOINTS.V2.REGISTRATION_PROCESSES({
        pathVariables: {
          id: action.payload,
        },
      }),
    );
    yield put(fetchStepsSucceded(response.data));
  } catch (error) {
    yield put(fetchStepsFailed(error));
  }
}

export default takeLeading(fetchSteps.type, fetchStepsSaga);
