import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import { withErrorPopover } from '../../../../../components/ErrorPopover';
import { invoiceAndPaymentYupValidations } from '../../../../../components/ProjectForms/InvoiceAndPaymentForm';
import {
  patchProject,
  selectProject,
} from '../../../../../store/ducks/project';
import { selectInvoiceAndPaymentFromProject } from '../../../../../store/ducks/project/selectors';
import InvoicesAndPaymentForm from './InvoicesAndPaymentForm';

function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const patch = {};

  patch.workdayTaskId = values.workdayTaskId;

  patch.rateType = values.rateType;
  patch.businessUnit = values.businessUnit;

  patch.productivityData = !!values.hasProductivityData;

  if (values.hasNotesToFinance) {
    patch.financeNotes = values.notesToFinance;
  } else {
    patch.financeNotes = null;
  }

  dispatch(patchProject({ changes: patch }));
}

function mapStateToProps(state) {
  const project = selectProject(state);
  const invoicePayment = selectInvoiceAndPaymentFromProject(project);

  return {
    invoicePayment,
  };
}

function mapPropsToValues(props) {
  const { invoicePayment } = props;

  return {
    ...invoicePayment,
  };
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    mapPropsToValues,
    handleSubmit,
    validationSchema: invoiceAndPaymentYupValidations,
  }),
)(InvoicesAndPaymentForm);
