export function find(idProp) {
  return (array, idValue) =>
    Array.from(array || []).find((item) => item[idProp] === idValue) || null;
}

export function checkPropIdAndValueNull(idProp, item, idValue) {
  return item[idProp] !== null && idValue !== null;
}

export function findIgnoringCase(idProp) {
  return (array, idValue) =>
    Array.from(array || []).find(
      (item) =>
        checkPropIdAndValueNull(idProp, item, idValue) &&
        item[idProp].toLowerCase() === idValue.toLowerCase(),
    ) || null;
}

export function findByValue(array, value) {
  return find('value')(array, value);
}

export function findByValueIgnoringCase(array, value) {
  try {
    return findIgnoringCase('value')(array, value);
  } catch (e) {
    return null;
  }
}

export function findByNestedValue(array, value, nestedProp = 'options') {
  const result = Array.from(array || []).find(
    (item) =>
      item.value === value ||
      (item[nestedProp] && find('value')(item[nestedProp], value)),
  );

  if (result) {
    return result.value === value
      ? result
      : find('value')(result[nestedProp], value);
  }
  return null;
}

export function findById(array, id) {
  return find('id')(array, id);
}

export function findByLocalID(array, localId) {
  return find('localId')(array, localId);
}

export function findByUserGroupId(array, userGroupId) {
  return find('userGroupId')(array, userGroupId);
}

export function findByGroupId(array, groupId) {
  return find('groupId')(array, groupId);
}

export function findByStepDefinitionId(array, stepDefinitionId) {
  return find('stepDefinitionId')(array, stepDefinitionId);
}

export function findByIgnoringCase(array, value) {
  const matches = array.filter((str) =>
    str.toLowerCase().includes(value.toLowerCase()),
  );
  return matches.length > 0;
}
