import React from 'react';
import PropTypes from 'prop-types';
import { IconContainer } from './styles';

function getTabIndex(tabIndex, isIconButton) {
  if (typeof tabIndex !== 'undefined') {
    return tabIndex;
  }

  if (isIconButton) {
    return 0;
  }

  return undefined;
}

export default function Icon(props) {
  const {
    icon,
    inheritColor,
    style,
    iconButton,
    onClick,
    onKeyDown,
    role,
    tabIndex,
    height,
  } = props;

  return (
    <IconContainer
      $inheritColor={inheritColor}
      $isIconButton={iconButton}
      style={style}
      onClick={onClick}
      onKeyDown={onKeyDown}
      role={role}
      height={height}
      tabIndex={getTabIndex(tabIndex, iconButton)}
    >
      {icon}
    </IconContainer>
  );
}

Icon.propTypes = {
  icon: PropTypes.element.isRequired,
  inheritColor: PropTypes.bool,
  style: PropTypes.objectOf(PropTypes.any),
  iconButton: PropTypes.bool,
  onClick: PropTypes.func,
  onKeyDown: PropTypes.func,
  role: PropTypes.string,
  tabIndex: PropTypes.number,
  height: PropTypes.string,
};

Icon.defaultProps = {
  inheritColor: true,
  iconButton: false,
  style: undefined,
  onClick: undefined,
  onKeyDown: undefined,
  role: undefined,
  tabIndex: undefined,
  height: null,
};
