import { withFormik } from 'formik';
import { convertToRaw, EditorState, ContentState } from 'draft-js';
import htmlToDraft from 'html-to-draftjs';
import draftToHtml from 'draftjs-to-html';
import * as Yup from 'yup';
import get from 'lodash/get';
import ExternalExamStep from './ExternalExamStep';
import { setProcessAttributes } from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      attributes: [
        {
          value: values.emailTitle,
          attributeOrder: 1,
        },
        {
          value: draftToHtml(
            convertToRaw(values.emailContent.getCurrentContent()),
          ),
          attributeOrder: 2,
        },
        {
          value: values.needsExamBatch,
          attributeOrder: 3,
        },
        {
          value: values.unlimited ? '-1' : values.maximumAttemps,
          attributeOrder: 4,
        },
      ],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

function mapPropsToValues(props) {
  const {
    card: { attributes },
  } = props;

  if (attributes && attributes.length) {
    const maximumAttemps = parseInt(
      get(
        attributes.find((att) => att.attributeOrder === 4),
        'value',
        0,
      ),
      10,
    );

    const blocksFromHtml = htmlToDraft(
      get(
        attributes.find((att) => att.attributeOrder === 2),
        'value',
        '',
      ),
    );

    const { contentBlocks, entityMap } = blocksFromHtml;

    const contentState = ContentState.createFromBlockArray(
      contentBlocks,
      entityMap,
    );

    return {
      emailTitle: get(
        attributes.find((att) => att.attributeOrder === 1),
        'value',
        '',
      ),
      emailContent: EditorState.createWithContent(contentState),
      needsExamBatch:
        get(
          attributes.find((att) => att.attributeOrder === 3),
          'value',
          '',
        ) === true,
      maximumAttemps: maximumAttemps === -1 ? '' : maximumAttemps,
      unlimited: maximumAttemps === -1,
    };
  }

  return {};
}

export default withFormik({
  handleSubmit,
  mapPropsToValues,
  enableReinitialize: true,
  validationSchema: Yup.object({
    emailTitle: Yup.string().required(requiredMessage`Email title`),
    emailContent: Yup.mixed().required(requiredMessage`Email content`),
    needsExamBatch: Yup.bool().required('Please check one of the options.'),
    maximumAttemps: Yup.string().when('unlimited', {
      is: (value) => !value,
      then: Yup.string()
        .matches(/\d+/g)
        .required(requiredMessage`Maximum attempts`),
      otherwise: Yup.string().notRequired(),
    }),
  }),
})(ExternalExamStep);
