import React from 'react';
import PropTypes from 'prop-types';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../../Popover';
import copy from '../../../../../assets/icons/copy.svg';
import pencil from '../../../../../assets/icons/pencil.svg';
import close from '../../../../../assets/icons/close.svg';
import { Container } from './styles';
import SettingsHoverable from '../../../../Icons/SettingsHoverable';

const actions = [
  { src: copy, alt: 'copy hiring target', text: 'Copy' },
  { src: pencil, alt: 'edit hiring target', text: 'Edit' },
  { src: close, alt: 'delete hiring target', text: 'Delete' },
];

export default function SettingsCell(props) {
  const {
    row: {
      values: { id },
    },
    initialState: { copyHiringTarget, editHiringTarget, deleteHiringTarget },
  } = props;

  const actionFns = [copyHiringTarget, editHiringTarget, deleteHiringTarget];

  const clickFns = actionFns.map((fn) => () => fn(id));
  const keyDownFns = actionFns.map((fn) => ({ keyCode }) =>
    keyCode === 13 && fn(id),
  );

  const tooltipContent = actions.map((action, index) => (
    <PopoverItem
      onClick={clickFns[index]}
      onKeyDown={keyDownFns[index]}
      role="button"
      tabIndex={0}
      data-testid={`project-setup--recruitment--hiring-targets-action-${index}`}
      key={action.text}
    >
      <PopoverIcon src={action.src} alt={action.alt} />
      <PopoverText>{action.text}</PopoverText>
    </PopoverItem>
  ));

  return (
    <Container
      className="settings-icon-container"
      data-testid={`project-setup--recruitment--hiring-targets-actions-${id}`}
    >
      <Popover content={tooltipContent} placement="bottom-end">
        <SettingsHoverable />
      </Popover>
    </Container>
  );
}

SettingsCell.propTypes = {
  row: PropTypes.shape({
    values: PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  }).isRequired,
  initialState: PropTypes.shape({
    copyHiringTarget: PropTypes.func.isRequired,
    editHiringTarget: PropTypes.func.isRequired,
    deleteHiringTarget: PropTypes.func.isRequired,
  }).isRequired,
};
