import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { Label, Section } from '../../../../styles';
import { StepActionContainer } from '../styles';
import {
  QuizContainer,
  QuizContentColumn,
  QuizRemoveColumn,
  RemoveQuizButton,
  QuizTitle,
  PreviewItem,
  PreviewContainer,
} from './styles';
import Select from '../../../Select';
import ActionFooter from '../../../ActionFooter';
import handleKeyDown from '../../../../pages/ProjectSetup/helpers/handleKeyDown';
import close from '../../../../assets/icons/close.svg';

export default function AcademyCourseStep(props) {
  const {
    values: { academyCourses = [] },
    submitCount,
    card: { attributes /* subSteps */ },
    submitForm,
    academyCourseOptions,
    /* handleAddCourse, */
    handleRemoveCourse,
    handleAcademyCourseChange,
    collapsed,
    errors,
    selectedAcademyCourses,
    cancelEditing,
  } = props;

  if (collapsed && attributes && attributes.length) {
    return (
      <PreviewContainer>
        {selectedAcademyCourses.map(({ academyCourse }) => (
          <PreviewItem key={academyCourse}>{`${academyCourse}`}</PreviewItem>
        ))}
      </PreviewContainer>
    );
  }

  return (
    <>
      {academyCourses.map((quiz, index) => (
        <QuizContainer key={quiz.key}>
          <QuizContentColumn>
            {academyCourses.length > 1 && (
              <QuizTitle>Academy Course {index + 1}</QuizTitle>
            )}
            <Section marginBottom="32px">
              <Row>
                <Col col={12}>
                  <Label htmlFor="documentToSign">SELECT ACADEMY COURSE</Label>
                  <Select
                    name={`academyCourse${index}`}
                    options={academyCourseOptions}
                    onChange={(value) =>
                      handleAcademyCourseChange(index, value)
                    }
                    value={academyCourses[index].academyCourse}
                    placeholder="Select course"
                    error={submitCount >= 1 && errors[`academyCourse${index}`]}
                  />
                </Col>
              </Row>
            </Section>
          </QuizContentColumn>
          <QuizRemoveColumn>
            {academyCourses.length > 1 && index !== 0 && (
              <RemoveQuizButton
                role="button"
                onClick={() => handleRemoveCourse(index)}
                onKeyDown={handleKeyDown(() => handleRemoveCourse(index))}
                tabIndex={0}
              >
                <img src={close} alt="close icon" />
              </RemoveQuizButton>
            )}
          </QuizRemoveColumn>
        </QuizContainer>
      ))}
      <StepActionContainer>
        <ActionFooter
          hideDivider
          // leftButtons={[
          //   {
          //     text: 'Add Other Academy Course',
          //     type: 'secondary',
          //     onClick: handleAddCourse,
          //   },
          // ]}
          rightButtons={[
            {
              type: 'secondary',
              text: 'Cancel',
              onClick: cancelEditing,
            },
            {
              text: !attributes.length ? 'Add & Close' : 'Update & Close',
              onClick: submitForm,
            },
          ]}
        />
      </StepActionContainer>
    </>
  );
}

AcademyCourseStep.propTypes = {
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  submitCount: PropTypes.number.isRequired,
  academyCourseOptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
    }),
  ).isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  card: PropTypes.shape({
    id: PropTypes.number,
    cardKey: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(
      PropTypes.shape({
        value: PropTypes.number,
        attributeOrder: PropTypes.number,
      }),
    ),
    subSteps: PropTypes.arrayOf(
      PropTypes.shape({
        attributes: PropTypes.arrayOf(
          PropTypes.shape({
            value: PropTypes.string,
            attributeOrder: PropTypes.number,
          }),
        ),
      }),
    ),
  }).isRequired,
  collapsed: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  /* handleAddCourse: PropTypes.func.isRequired, */
  handleRemoveCourse: PropTypes.func.isRequired,
  handleAcademyCourseChange: PropTypes.func.isRequired,
  selectedAcademyCourses: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  ).isRequired,
  cancelEditing: PropTypes.func.isRequired,
};
