import React from 'react';
import PropTypes from 'prop-types';
import Cell from './styles';

export function wordsOrDot(value = '') {
  if (!value) {
    return '.';
  }

  return `${value || ''}`.toUpperCase();
}

export default function WordayTaskIdCell(props) {
  const {
    cell: { value },
  } = props;

  return <Cell>{wordsOrDot(value)}</Cell>;
}

WordayTaskIdCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.string,
  }).isRequired,
};
