import { useCallback, useState } from 'react';
import { useDispatch } from 'react-redux';
import { compose } from 'recompose';
import { withFormik, useFormikContext } from 'formik';
import withHooks from 'hook-hoc';
import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';
import * as yup from 'yup';
import Filters from './Filters';
import { requiredMessage } from '../../../ProjectSetup/Steps/shared';
import {
  PAGINATED_ENTITIES,
  setFilters,
} from '../../../../store/ducks/paginated';

const statusOptions = [
  { value: 'ALL_STATUSES', label: 'All Statuses' },
  { value: 'APPLICATION_RECEIVED', label: 'Application Received' },
  { value: 'SCREENED', label: 'Screened' },
  { value: 'STAGED', label: 'Staged' },
  { value: 'ARCHIVED', label: 'Archived' },
  { value: 'CONTRACT_PENDING', label: 'Contract Pending' },
  { value: 'IN_ACTIVATION_QUEUE', label: 'In Activation Queue' },
  { value: 'ACTIVE', label: 'Active' },
  { value: 'REJECTED', label: 'Rejected' },
  { value: 'ABANDONED', label: 'Abandoned' },
  { value: 'SUSPENDED', label: 'Suspended' },
  { value: 'TERMINATED', label: 'Terminated' },
  { value: 'EXPIRED', label: 'Expired Contract' },
  { value: 'ON_HOLD', label: 'On Hold' },
  { value: 'REACTIVATION_REQUESTED', label: 'Reactivation Requested' },
  { value: 'PAYONEER_SETUP', label: 'Payoneer Setup' },
  { value: 'EXPRESS_QUALIFYING', label: 'Express Qualifying' },
  { value: 'EXPRESS_ACTIVE', label: 'Express Active' },
  { value: 'EXPRESS_TO_BE_CONVERTED', label: 'Express To Be Converted' },
  { value: 'INTERNAL', label: 'Internal User' },
];

function mapPropsToValues() {
  return {};
}

function useHooksAsProps() {
  const dispatch = useDispatch();
  const { setFieldValue } = useFormikContext();

  const [filters, setAdvancedFilters] = useState({});
  const [countSelectedFilters, setCountSelectedFilters] = useState(0);
  const [resetFilters, setResetFilters] = useState(false);
  const [showAdvancedFilters, setShowAdvancedFilters] = useState(false);
  const [filterSummary, setFilterSummary] = useState(null);
  const [bulkOption, setBulkOption] = useState(null);
  const [openBulkModal, setOpenBulkModal] = useState(false);
  const [openNumbersModal, setOpenNumbersModal] = useState(false);
  const [showMenu, setShowMenu] = useState(false);

  const verify = useCallback(
    debounce((name) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.userScreening,
          filters: {
            name,
          },
        }),
      );
    }, 500),
    [dispatch, setFilters],
  );

  const handleNameChange = useCallback(
    ({ target: { value } }) => {
      setFieldValue('name', value);
      verify(value);
    },
    [setFieldValue, verify],
  );

  const handleStatusChange = useCallback(
    ({ value: status }) => {
      setFieldValue('status', status);
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.userScreening,
          filters: {
            status,
          },
        }),
      );
    },
    [dispatch, setFieldValue],
  );

  const handleFilterCountChange = useCallback((count) => {
    setCountSelectedFilters(count);
  }, []);

  const handleResetFilters = useCallback(
    (status) => {
      const advancedFilter = {};
      Object.keys(filters).forEach((i) => {
        advancedFilter[i] = null;
      });

      setResetFilters(status);
      setAdvancedFilters(advancedFilter);
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.userScreening,
          filters: advancedFilter,
        }),
      );
    },
    [dispatch, filters],
  );

  const handleApplyFilters = useCallback(() => {
    setShowAdvancedFilters(!showAdvancedFilters);
    if (!isEmpty(filters)) {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.userScreening,
          filters,
        }),
      );
    }
  }, [dispatch, filters, showAdvancedFilters]);

  const handleShowAdvancedFilters = useCallback(() => {
    setShowAdvancedFilters(!showAdvancedFilters);
  }, [showAdvancedFilters]);

  const handleFilterSummary = useCallback((summary) => {
    setFilterSummary(summary);
  }, []);

  const handleBulkOption = useCallback((key) => {
    setShowMenu(false);
    setOpenBulkModal(true);
    setBulkOption(key);
  }, []);

  const handleShowMenu = useCallback(() => {
    setShowMenu(!showMenu);
  }, [showMenu]);

  const handleAdvancedFiltersChange = useCallback(
    (values) => {
      setAdvancedFilters(values);
    },
    [setAdvancedFilters],
  );

  return {
    resetFilters,
    countSelectedFilters,
    statusOptions,
    showAdvancedFilters,
    filterSummary,
    bulkOption,
    openBulkModal,
    openNumbersModal,
    showMenu,
    setOpenNumbersModal,
    setOpenBulkModal,
    handleNameChange,
    handleStatusChange,
    handleResetFilters,
    handleBulkOption,
    handleApplyFilters,
    handleFilterCountChange,
    handleShowAdvancedFilters,
    handleFilterSummary,
    handleAdvancedFiltersChange,
    setShowAdvancedFilters,
    handleShowMenu,
  };
}

const validationSchema = yup.object().shape({
  processName: yup.string().required(requiredMessage`Process name`),
  processDescription: yup
    .string()
    .required(requiredMessage`Process description`),
  copyModulesFrom: yup.mixed().when('showCopyFromProcessSelect', {
    is: true,
    then: yup
      .mixed()
      .required(
        requiredMessage`When "Copy Modules From an Existing Process" is checked this`,
      ),
  }),
});

export default compose(
  withFormik({
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(Filters);
