import { withFormik } from 'formik';
import shortid from 'shortid';
import { useCallback, useEffect, useMemo } from 'react';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import * as Yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import SurveyQuestionsStep from './SurveyQuestionsStep';
import {
  setProcessAttributes,
  loadStepById,
} from '../../../../store/ducks/processBuilder';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import createArrayValidation from '../../../../helpers/array-validation';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';

const initialFormValues = {
  surveys: [
    {
      survey: null,
      key: shortid.generate(),
    },
  ],
};

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  const survey = values.surveys[0];

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      attributes: [
        {
          attributeOrder: 1,
          name: 'Survey',
          value: survey.survey,
        },
      ],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

function useHooksAsProps(outerProps) {
  const {
    setFieldValue,
    values: { surveys = [] },
    card: { stepDefinitionId },
  } = outerProps;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.screening_survey));
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  const options = useSelector(
    selectors.createSelectDataFor(ENTITIES.screening_survey),
  );

  const surveysOptions = useMemo(() => {
    return mapToOptions('id', 'name')(options);
  }, [options]);

  const selectedSurveys = useMemo(() => {
    return surveys
      .map((survey) => survey.survey)
      .filter(Boolean)
      .map((surveyId) => surveysOptions.find((q) => q.value === surveyId))
      .map((survey) => ({ ...survey, key: shortid.generate() }));
  }, [surveys, surveysOptions]);

  const handleAddSurvey = useCallback(() => {
    setFieldValue('surveys', [
      ...surveys,
      { ...initialFormValues.surveys[0], key: shortid.generate() },
    ]);
  }, [surveys, setFieldValue]);

  const handleRemoveSurvey = useCallback(
    (index) => {
      setFieldValue('surveys', [
        ...surveys.slice(0, index),
        ...surveys.slice(index + 1, surveys.length),
      ]);
    },
    [surveys, setFieldValue],
  );

  const createInputChangeHandler = (fieldName) => (index) => (changeEvent) => {
    const item = surveys[index];

    let value;

    if (changeEvent.target) {
      value = changeEvent.target.value;
    } else {
      value = changeEvent.value;
    }

    if (fieldName === 'survey') {
      setFieldValue('surveys', [
        ...surveys.slice(0, index),
        {
          ...item,
          survey: value,
        },
        ...surveys.slice(index + 1, surveys.length),
      ]);
    } else {
      setFieldValue('surveys', [
        ...surveys.slice(0, index),
        { ...item, [fieldName]: value },
        ...surveys.slice(index + 1, surveys.length),
      ]);
    }
  };

  const handleSurveyChange = createInputChangeHandler('survey');

  return {
    handleAddSurvey,
    handleRemoveSurvey,
    handleSurveyChange,
    surveysOptions,
    selectedSurveys,
  };
}

function transformError(error, value, index) {
  switch (error.path) {
    case 'survey':
      return new Yup.ValidationError(
        requiredMessage`Survey`,
        value,
        `survey${index}`,
      );

    default:
      return new Yup.ValidationError(
        requiredMessage`Survey`,
        value,
        `survey${index}`,
      );
  }
}

const validateArray = createArrayValidation(
  {
    survey: Yup.number().required(),
  },
  transformError,
);

function mapPropsToValues(props) {
  const {
    card: { attributes },
  } = props;

  if (attributes && attributes.length) {
    return {
      surveys: [
        {
          survey: parseInt(
            get(
              attributes.find((att) => att.attributeOrder === 1),
              'value',
              -1,
            ),
            10,
          ),
          key: shortid.generate(),
        },
      ],
    };
  }

  return initialFormValues;
}

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
      surveys: Yup.array().test('surveyArray', '', validateArray),
    }),
  }),
  withHooks(useHooksAsProps),
)(SurveyQuestionsStep);
