export default function mapToMacroLocaleLanguages(id, questionType, array) {
  if (!array || !Array.isArray(array) || !array.length) return [];
  const transformedLanguages = [];
  array.forEach((lang) => {
    if (lang.macroLanguage) {
      const options = [];

      options.push({
        ...lang,
        disabled: true,
      });

      const languages = lang.languages.map((locale) => ({
        ...locale,
        id,
        questionType,
      }));

      options.push(...languages);

      transformedLanguages.push({
        ...lang,
        macroLanguageLabel: true,
        options,
        id,
        questionType,
      });
    } else {
      transformedLanguages.push({
        ...lang,
        id,
        questionType,
      });
    }
  });

  return transformedLanguages;
}
