import { withFormik } from 'formik';
import shortId from 'shortid';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import Recruitment from './Recruitment';
import { recruitmentYupValidations } from '../../../../components/ProjectForms/RecruitmentForm';
import {
  patchProject,
  selectRecruitment,
} from '../../../../store/ducks/project';
import { navigate, setDirty } from '../../../../store/ducks/projectSetup';
import { selectAutoScreenings } from '../../../../store/ducks/autoScreening';
import STEP_NUMBER from '../../../../constants/stepNumber';

function mapStateToProps(state) {
  const recruiting = selectRecruitment(state);
  const autoScreenings = selectAutoScreenings(state);

  return {
    initialValues: {
      ...recruiting,
      autoScreenings,
      blockingRules: recruiting.blockingRules || [
        { key: shortId.generate(), projectId: null, permanent: false },
      ],
      linkedProjects: recruiting.linkedProjects || [
        { key: shortId.generate(), projectId: null },
      ],
      currentTab: 0,
    },
  };
}

async function handleSubmit(values, { props }) {
  const { dispatch } = props;

  const payload = {};

  if (values.hasNotesToRecruiting) {
    payload.recruitingTeamNotes = values.notesToRecruiting;
  }

  if (values.hasBlockingRules) {
    payload.blockingRules = (values.blockingRules || []).map((br) => ({
      blockedProjectId: br.projectId,
      permanent: br.permanent,
    }));
  } else {
    payload.blockingRules = null;
  }

  if (values.hasLinkedProjects) {
    payload.linkedProjectsIds = (values.linkedProjects || []).map(
      (lp) => lp.projectId,
    );
  } else {
    payload.linkedProjectsIds = null;
  }

  if (values.hasTargetScreening) {
    payload.profileTargetId = values.profileTargetId || null;
  } else {
    payload.profileTargetId = null;
  }

  const nextStep = STEP_NUMBER.RECRUITMENT + 1;

  dispatch(setDirty(false));

  dispatch(
    patchProject({
      changes: payload,
      thenPut: values.navigate ? navigate(nextStep) : undefined,
    }),
  );
}

function mapPropsToValues({ initialValues }) {
  const { autoScreenings = [] } = initialValues || {};

  const hasAutoScreening = autoScreenings.length > 0;

  return {
    ...initialValues,
    hasAutoScreening,
  };
}

export default compose(
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: recruitmentYupValidations,
  }),
)(Recruitment);
