import { select, put, call, takeEvery, all } from 'redux-saga/effects';
import orderBy from 'lodash/orderBy';
import {
  PROCESS_TYPE,
  setProcessError,
  setProcessDetails,
  selectProcessDetails,
  selectProcessModules,
  setProcessSaving,
  saveOrCreateProcess,
  setProcessModules,
  loadStepById,
} from '.';
import axios from '../../../services/axios';
import endpoints from '../../../constants/endpoints';
import { fetchStepDefinitionFn } from './fetchStepDefinitionSaga';
import {
  mapProcessModuleToDTO,
  createDTOToProcessModuleMapper,
} from './helpers';

function isNewDocumentStep(processModule) {
  return (
    processModule.sequenceFlow &&
    processModule.sequenceFlow === 'signDocumentFlow' &&
    processModule.attributes &&
    processModule.attributes[0] &&
    processModule.attributes[0].additionalAttributes
  );
}

function getPayloadForNewDocuments(processModules = []) {
  return processModules.filter(isNewDocumentStep).map((processModule) => ({
    key: processModule.key,
    data: processModule.attributes[0].additionalAttributes,
  }));
}

function* createDocument(documentPayload) {
  const { key, data } = documentPayload;

  const response = yield call(axios.post, endpoints.V2.ELECTRONIC_DOCUMENTS(), {
    title: data.documentTitle,
    content: data.documentBody,
    internalDescription: data.hasInternalNotes ? data.internalNotes : '',
    optional: !data.required,
  });

  return {
    id: response.data.id,
    key,
  };
}

function replaceDocumentDataForId(documentResponses) {
  return (processModule) => {
    if (isNewDocumentStep(processModule)) {
      return {
        ...processModule,
        attributes: [
          {
            value: documentResponses.find((d) => d.key === processModule.key)
              .id,
            attributeOrder: 1,
          },
        ],
      };
    }

    return processModule;
  };
}

function getEndpoint(processType, processId) {
  if (processId) {
    return processType === PROCESS_TYPE.REGISTRATION
      ? endpoints.V2.REGISTRATION_PROCESSES({
          pathVariables: {
            id: processId,
          },
        })
      : endpoints.V2.QUALIFICATION_PROCESSES({
          pathVariables: {
            id: processId,
          },
        });
  }

  return processType === PROCESS_TYPE.REGISTRATION
    ? endpoints.V2.REGISTRATION_PROCESSES()
    : endpoints.V2.QUALIFICATION_PROCESSES();
}

function* saveOrCreateProcessSaga(action) {
  const processDetails = yield select(selectProcessDetails);
  let processModules = yield select(selectProcessModules);

  // Create new documents
  const payloadForNewDocuments = getPayloadForNewDocuments(processModules);

  // for all "signDocument" steps where the user is creating a new document
  if (payloadForNewDocuments.length) {
    try {
      // we create the documents
      const responses = yield all(
        payloadForNewDocuments.map((documentPayload) =>
          call(createDocument, documentPayload),
        ),
      );

      const signDocumentProcess = processModules.find(
        (process) => process.sequenceFlow === 'signDocumentFlow',
      );

      // update the list of documents in redux
      yield call(
        fetchStepDefinitionFn,
        loadStepById({
          stepDefinitionID: signDocumentProcess.stepDefinitionId,
        }),
      );

      // and add the id of the new document in the step
      processModules = processModules.map(replaceDocumentDataForId(responses));
    } catch (error) {
      yield put(setProcessError(error));

      if (action && action.meta) {
        if (typeof action.meta.onError === 'function') {
          action.meta.onError(error);
        }
      }

      yield put(setProcessSaving(false));
      return;
    }
  }

  try {
    const endpoint = getEndpoint(
      processDetails.processType,
      processDetails.processID,
    );

    const response = yield call(
      processDetails.processID ? axios.put : axios.post,
      endpoint,
      {
        name: processDetails.processName,
        description: processDetails.processDescription,
        type: processDetails.processType,
        processSteps: processModules.map((processModule, index) =>
          mapProcessModuleToDTO(
            {
              ...processModule,
              id: !processDetails.processID ? null : processModule.id,
            },
            index,
          ),
        ),
      },
    );

    yield put(
      setProcessModules(
        orderBy(response.data.processSteps || [], 'order').map(
          createDTOToProcessModuleMapper(processModules),
        ),
        { isPristine: true },
      ),
    );

    if (!processDetails.processID) {
      yield put(
        setProcessDetails(
          {
            processID: response.data.id,
          },
          { preventFetch: true },
        ),
      );
    }

    if (
      action &&
      action.meta &&
      typeof action.meta.onProcessChange === 'function'
    ) {
      action.meta.onProcessChange(
        processDetails.processType,
        response.data.id,
        !processDetails.processID,
      );
    }
  } catch (error) {
    yield put(setProcessError(error));

    if (action && action.meta) {
      if (typeof action.meta.onError === 'function') {
        action.meta.onError(error);
      }
    }
  } finally {
    yield put(setProcessSaving(false));
  }
}

export default takeEvery(saveOrCreateProcess.type, saveOrCreateProcessSaga);
