import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import HiringTargetHeader from './HiringTargetHeader';
import { fromPercent } from '../../../../helpers';
import {
  selectViewAs,
  DEMOGRAPHIC_VIEW_AS,
} from '../../../../../../../../store/ducks/projectDemographicRequirements';

function renderCountryAbbre(abbrev) {
  if (abbrev === '*') {
    return 'All';
  }

  return abbrev;
}

function useHooksAsProps(outerProps) {
  const {
    column: { hiringTarget },
    data,
  } = outerProps;

  const sum = (data || [])
    .map((item) => parseInt(item[hiringTarget.id].value, 10))
    .filter((item) => !Number.isNaN(item))
    .reduce((current, accumulated) => accumulated + current, 0);

  const target = useMemo(() => parseInt(hiringTarget.target, 10), [
    hiringTarget.target,
  ]);

  const viewAs = useSelector(selectViewAs);

  const hasTooMany = useMemo(
    () =>
      viewAs === DEMOGRAPHIC_VIEW_AS.NUMBER
        ? sum > target
        : fromPercent(sum, target) > target,
    [sum, target, viewAs],
  );

  const percentChar = useMemo(
    () => (viewAs === DEMOGRAPHIC_VIEW_AS.NUMBER ? '' : '%'),
    [viewAs],
  );

  return {
    sum,
    target,
    percentChar,
    hasTooMany,
    isRounded: sum === Math.round(sum),
    restrictToCountry: renderCountryAbbre(hiringTarget.restrictToCountry),
    localeDialect: renderCountryAbbre(hiringTarget.localeDialect),
    localeLanguage: hiringTarget.localeLanguage,
  };
}

export default withHooks(useHooksAsProps)(HiringTargetHeader);
