import React, { useMemo, Fragment } from 'react';
import PropTypes from 'prop-types';
import TabSwitcher from '../../components/TabSwitcher';
import UserScreening from './UserScreening';
import { Container, Header, Tabs, Title } from './styles';

export default function RecruitingAutomation(props) {
  const { tabsOptions, currentTab, handleTabChange } = props;

  const CurrentPage = useMemo(
    () => ({ 0: UserScreening, 1: Fragment }[currentTab || 0]),
    [currentTab],
  );

  const CurrentTitle = useMemo(
    () =>
      ({ 0: 'User Screening Dashboard', 1: 'Project Qualification' }[
        currentTab || 0
      ]),
    [currentTab],
  );

  return (
    <Container>
      <Header>
        <Title>{CurrentTitle}</Title>
        <Tabs>
          <TabSwitcher
            tabs={tabsOptions}
            value={currentTab}
            onChange={handleTabChange}
            width={'100%'}
          />
        </Tabs>
      </Header>
      <CurrentPage />
    </Container>
  );
}

RecruitingAutomation.propTypes = {
  tabsOptions: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
    }),
  ).isRequired,
  currentTab: PropTypes.number.isRequired,
  handleTabChange: PropTypes.func.isRequired,
};
