export default function ordinalSuffix(dayOfTheMonth) {
  const j = dayOfTheMonth % 10;
  const k = dayOfTheMonth % 100;

  if (j === 1 && k !== 11) {
    return `${dayOfTheMonth}st`;
  }
  if (j === 2 && k !== 12) {
    return `${dayOfTheMonth}nd`;
  }
  if (j === 3 && k !== 13) {
    return `${dayOfTheMonth}rd`;
  }
  return `${dayOfTheMonth}th`;
}
