import React, { useRef, useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import { Shimmer } from 'react-shimmer';
import throttle from 'lodash/throttle';
import { PreviewModalWrapper } from './styles';
import { getPostPayload, getMethodAndActionURL } from './helpers';
import { selectProjectId } from '../../../../store/ducks/project';

// Prevent all pages from rendering at once
const renderQueue = [];

renderQueue.next = throttle(function next() {
  const nextFn = this.shift();

  if (typeof nextFn === 'function') {
    nextFn();
  }
}, 200);

export default function IFrame(props) {
  const { id, processModule } = props;

  const frameRef = useRef();
  const projectId = useSelector(selectProjectId);
  const [ready, setReady] = useState(false);

  const onFrameLoad = useCallback(() => {
    if (frameRef.current.handled) {
      renderQueue.next();

      setReady(true);
      return;
    }

    renderQueue.push(() => {
      if (frameRef.current && frameRef.current.contentWindow) {
        try {
          const [method, action] = getMethodAndActionURL(
            processModule,
            projectId,
          );

          if (method === 'GET') {
            frameRef.current.setAttribute('src', action);
            frameRef.current.handled = true;
            return;
          }

          const formEl = document.createElement('form');

          formEl.setAttribute('target', '_self');
          formEl.setAttribute('method', method);
          formEl.setAttribute('action', action);

          const payload = getPostPayload(processModule, projectId);

          payload.forEach(([key, value]) => {
            const inputEl = document.createElement('input');
            inputEl.setAttribute('name', key);
            inputEl.setAttribute('value', value);
            inputEl.setAttribute('hidden', 'true');

            formEl.appendChild(inputEl);
          });

          frameRef.current.contentWindow.document.body.appendChild(formEl);
          frameRef.current.handled = true;

          formEl.submit();
        } catch (error) {
          // will error as soon as child frame redirects because of CORS
        }
      }
    });

    // Quickstart the queue
    renderQueue.next();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <PreviewModalWrapper>
      {!ready && (
        <div>
          <Shimmer width={672} height={550} />
        </div>
      )}
      <iframe
        ref={frameRef}
        title="Process Preview"
        name={id}
        scrolling="auto"
        onLoad={onFrameLoad}
      />
    </PreviewModalWrapper>
  );
}

IFrame.propTypes = {
  id: PropTypes.string.isRequired,
  processModule: PropTypes.objectOf(PropTypes.any).isRequired,
};
