/* eslint-disable react/jsx-props-no-spreading */
import React, { useEffect } from 'react';
import { Field } from 'formik';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { selectStepEditorDirty } from '../../../store/ducks/targetBuilder';
import usePrevious from '../../Builder/helpers/usePrevious';
import ActionFooter from '../../ActionFooter';
import QuestionBuilder from '../QuestionBuilder';
import { Section } from '../../../styles';
import {
  FormContainer,
  FooterContainer,
  CollapsedViewContainer,
} from './styles';

export default function QuestionLoader(props) {
  const {
    submitForm,
    isSecondary,

    // Controls
    collapsed,
    setCollapsed,
    cancelEditing,
    attributes,
  } = props;

  const dirty = useSelector(selectStepEditorDirty);
  const wasDirty = usePrevious(dirty);

  useEffect(() => {
    // Was there a same operation?
    if (wasDirty && !dirty) {
      // If so, close the current card too.
      setCollapsed(true);
    }
    setCollapsed(true);
  }, [dirty, setCollapsed, wasDirty]);

  if (collapsed && attributes && attributes.length) {
    return <CollapsedViewContainer />;
  }

  const margin = isSecondary ? 0 : 32;

  return (
    <>
      <Section marginLeft={margin} marginTop={margin} marginBottom={margin}>
        <FormContainer>
          <Field {...props} component={QuestionBuilder} />
        </FormContainer>
      </Section>
      {!isSecondary && (
        <FooterContainer>
          <ActionFooter
            hideDivider
            rightButtons={[
              {
                type: 'secondary',
                text: 'Cancel',
                onClick: cancelEditing,
              },
              {
                text: !attributes.length ? 'Add & Close' : 'Update & Close',
                onClick: submitForm,
              },
            ]}
          />
        </FooterContainer>
      )}
    </>
  );
}

QuestionLoader.propTypes = {
  submitForm: PropTypes.func.isRequired,
  collapsed: PropTypes.bool.isRequired,
  setCollapsed: PropTypes.func.isRequired,
  cancelEditing: PropTypes.func.isRequired,
  attributes: PropTypes.arrayOf(PropTypes.object),
  isSecondary: PropTypes.bool.isRequired,
};

QuestionLoader.defaultProps = {
  attributes: [],
};
