import React, { useEffect, useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import {
  selectStepEditorDirty,
  setStepEditorDirty,
} from '../../../../store/ducks/processBuilder';
import usePrevious from '../../helpers/usePrevious';
import getStepComponent from '../../helpers/getStepComponent';

export default function StepSwitcher(props) {
  const {
    // Card info
    id,
    sequenceFlow,
    stepDefinitionId,
    cardKey,
    cardIndex,

    // Controls
    mode,
    collapsed,
    setCollapsed,
    removeModule,
    cancelEditing,
    attributes,
    subSteps,
  } = props;

  const dirty = useSelector(selectStepEditorDirty);
  const dispatch = useDispatch();

  const wasDirty = usePrevious(dirty);

  useEffect(() => {
    // Was there a same operation?
    if (wasDirty && !dirty) {
      // If so, close the current card too.
      setCollapsed(true);
    }
  }, [dirty, setCollapsed, wasDirty]);

  const setDirty = useCallback(
    (nextDirty) => {
      dispatch(setStepEditorDirty(nextDirty));
    },
    [dispatch],
  );

  const [hasComponent, Component] = useMemo(() => {
    const PossibleComponent = getStepComponent(sequenceFlow);

    return [!!PossibleComponent, PossibleComponent];
  }, [sequenceFlow]);

  const card = useMemo(
    () => ({
      id,
      sequenceFlow,
      cardKey,
      stepDefinitionId,
      attributes,
      subSteps,
      cardIndex,
    }),
    [
      attributes,
      cardIndex,
      cardKey,
      id,
      sequenceFlow,
      stepDefinitionId,
      subSteps,
    ],
  );

  const controls = useMemo(
    () => ({
      mode,
      collapsed,
      setCollapsed,
      removeModule,
      setDirty,
    }),
    [collapsed, mode, removeModule, setCollapsed, setDirty],
  );

  return (
    hasComponent && (
      <Component
        mode={mode}
        collapsed={collapsed}
        setDirty={setDirty}
        removeModule={removeModule}
        setCollapsed={setCollapsed}
        card={card}
        controls={controls}
        cancelEditing={cancelEditing}
      />
    )
  );
}

StepSwitcher.propTypes = {
  id: PropTypes.number,
  cardKey: PropTypes.string.isRequired,
  sequenceFlow: PropTypes.string.isRequired,
  cardIndex: PropTypes.number.isRequired,
  stepDefinitionId: PropTypes.number.isRequired,
  mode: PropTypes.oneOf(['edit', 'create']).isRequired,
  collapsed: PropTypes.bool.isRequired,
  setCollapsed: PropTypes.func.isRequired,
  removeModule: PropTypes.func.isRequired,
  cancelEditing: PropTypes.func.isRequired,
  attributes: PropTypes.arrayOf(PropTypes.object),
  subSteps: PropTypes.arrayOf(PropTypes.object),
};

StepSwitcher.defaultProps = {
  id: null,
  attributes: [],
  subSteps: [],
};
