import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import {
  PreviewSectionTitle,
  PreviewSectionItem,
  PreviewSectionText,
  PreviewSection,
  EditButtonContainer,
  EditStepButton,
} from '../styles';
import RichDescription from '../../RichDescription';

export default function ProjectOverviewPreview(props) {
  const {
    projectName,
    projectAlias,
    projectDescription,
    projectType,
    taskType,
    taskVolume,
    editStep,
    stepNumber,
    renderedIn,
  } = props;

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: Project Overview
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}
      {(projectName || projectAlias) && (
        <PreviewSection>
          <Row>
            {projectName && (
              <Col col={4}>
                <PreviewSectionItem>Project Name</PreviewSectionItem>
                <PreviewSectionText>{projectName}</PreviewSectionText>
              </Col>
            )}
            {projectAlias && (
              <Col col={8}>
                <PreviewSectionItem>Project Alias</PreviewSectionItem>
                <PreviewSectionText>{projectAlias}</PreviewSectionText>
              </Col>
            )}
          </Row>
        </PreviewSection>
      )}

      <PreviewSection>
        <PreviewSectionItem>Project Description</PreviewSectionItem>
        <PreviewSectionText>
          <RichDescription html={projectDescription} />
        </PreviewSectionText>
      </PreviewSection>

      <PreviewSection>
        <Row>
          <Col col={4}>
            <PreviewSectionItem>Type of Project</PreviewSectionItem>
            <PreviewSectionText>{projectType}</PreviewSectionText>
          </Col>
          <Col col={4}>
            <PreviewSectionItem>Type of Task</PreviewSectionItem>
            <PreviewSectionText>{taskType}</PreviewSectionText>
          </Col>
          <Col col={4}>
            <PreviewSectionItem>Task Volume</PreviewSectionItem>
            <PreviewSectionText>{taskVolume}</PreviewSectionText>
          </Col>
        </Row>
      </PreviewSection>
    </>
  );
}

ProjectOverviewPreview.propTypes = {
  projectName: PropTypes.string,
  projectAlias: PropTypes.string,
  projectDescription: PropTypes.string.isRequired,
  projectType: PropTypes.string.isRequired,
  taskType: PropTypes.string.isRequired,
  taskVolume: PropTypes.string.isRequired,
  editStep: PropTypes.func,
  stepNumber: PropTypes.number,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectOverviewPreview.defaultProps = {
  projectName: undefined,
  projectAlias: undefined,
  editStep: undefined,
  stepNumber: 1,
  renderedIn: 'project-setup',
};
