import React, { useState, useCallback, useRef } from 'react';
import DatePicker from 'react-datepicker';
import PropTypes from 'prop-types';
import useClickOutside from 'use-click-outside';
import InputBase, { Icon } from './styles';
import styles from './styles.css';
import MessageError from '../MessageError';

const popperPositionModifiers = {
  flip: {
    enabled: false,
  },
  preventOverflow: {
    enabled: true,
    // force popper to stay in viewport (even when input is scrolled out of view)
    escapeWithReference: false,
  },
};

export default function DateInput(props) {
  const {
    value,
    onChange,
    name,
    id,
    filterDate,
    minDate,
    error,
    placeholder,
    'data-testid': dataTestid,
    maxDate,
    disabled,
  } = props;

  const [focused, setFocused] = useState(null);

  const handleFocus = useCallback(() => setFocused(true), []);
  const handleBlur = useCallback(() => setFocused(false), []);

  const inputRef = useRef();
  useClickOutside(inputRef, handleBlur);

  const onClickLabel = useCallback((event) => {
    if (event.nativeEvent.target !== inputRef.current) {
      event.preventDefault();
    }
  }, []);

  return (
    <>
      <label htmlFor={name || id} onClick={onClickLabel} role="presentation">
        <InputBase
          error={error}
          focused={focused}
          ref={inputRef}
          data-testid={dataTestid}
        >
          <DatePicker
            selected={value}
            onChange={onChange}
            minDate={minDate}
            placeholderText={placeholder}
            id={name || id}
            name={name}
            popperClassName={styles.popper}
            className={styles.inputComponent}
            popperPlacement="bottom-end"
            popperModifiers={popperPositionModifiers}
            filterDate={filterDate}
            autoComplete="off"
            onFocus={handleFocus}
            onBlur={handleBlur}
            disabled={disabled}
            maxDate={maxDate}
          />
          <Icon error={error} focused={focused} disabled={disabled}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="17"
              viewBox="0 0 16 17"
            >
              <path d="M13.566 1.698h-1.489V1.03a.754.754 0 00-1.508 0v.668H5.372V1.03a.754.754 0 00-1.508 0v.668H2.375A2.263 2.263 0 00.112 3.961v10.21a2.263 2.263 0 002.263 2.263h11.19a2.263 2.263 0 002.264-2.263V3.961a2.263 2.263 0 00-2.263-2.263zm-1.754 7.614L7.516 13.42a.754.754 0 01-1.054-.011l-2.338-2.337a.755.755 0 111.067-1.067l1.814 1.81 3.772-3.6a.754.754 0 111.042 1.09l-.007.006zM14.32 5.49H1.62V3.963c0-.417.338-.755.754-.755h1.489v.668a.754.754 0 001.508 0v-.67h5.197v.668a.754.754 0 001.508 0v-.667h1.489c.416 0 .754.337.754.754v1.53z" />
            </svg>
          </Icon>
        </InputBase>
      </label>
      <MessageError isVisible={!!error} message={error} />
    </>
  );
}

DateInput.propTypes = {
  value: PropTypes.instanceOf(Date),
  id: PropTypes.string,
  onChange: PropTypes.func,
  name: PropTypes.string.isRequired,
  filterDate: PropTypes.func,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
  minDate: PropTypes.instanceOf(Date),
  maxDate: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
  placeholder: PropTypes.string,
  'data-testid': PropTypes.string,
};

DateInput.defaultProps = {
  value: undefined,
  id: undefined,
  minDate: undefined,
  maxDate: undefined,
  disabled: false,
  error: false,
  placeholder: 'Select Deadline',
  onChange: () => {},
  filterDate: () => true,
  'data-testid': '',
};
