import { useEffect, useCallback } from 'react';
import { batch, useDispatch, useSelector, useStore } from 'react-redux';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import { v4 as uuid } from 'uuid';
import AutoScreening from './AutoScreening';
import { useModal } from '../../../Modal';
import {
  deleteAutoScreening,
  fetchAutoScreening,
  selectAutoScreenings,
  selectAutoScreeningById,
  selectLoading,
} from '../../../../store/ducks/autoScreening';
import {
  selectProjectId,
  selectRegistrationAndQualification,
} from '../../../../store/ducks/project';
import { ENTITIES, fetch, selectors } from '../../../../store/ducks/options';
import * as registrationProcesses from '../../../../store/ducks/registrationProcesses';

function useHooksAsProps() {
  const [modalProps, modalControls] = useModal();
  const dispatch = useDispatch();
  const store = useStore();

  const projectId = useSelector(selectProjectId);
  const autoScreenings = useSelector(selectAutoScreenings);
  const loadingConfigs = useSelector(selectLoading);
  const registrationSteps = useSelector(registrationProcesses.selectSteps);
  const loadingLocales = useSelector(
    selectors.createSelectLoadingFor(ENTITIES.locales),
  );
  const registrationAndQualification = useSelector(
    selectRegistrationAndQualification,
  );

  const { registrationProcessTemplate } = registrationAndQualification;

  let hasCompleteAdditionalAttributes = false;

  if (registrationSteps.steps.length) {
    const steps = registrationSteps.steps.map(
      (step) =>
        step.stepDefinition.sequenceFlow === 'completeProjectAttributesFlow',
    );
    hasCompleteAdditionalAttributes = steps.includes(true);
  }

  useEffect(() => {
    batch(() => {
      dispatch(fetchAutoScreening({ projectId }));
      dispatch(fetch(ENTITIES.locales));
      dispatch(registrationProcesses.fetchOptions());
      if (registrationProcessTemplate) {
        dispatch(registrationProcesses.fetchSteps(registrationProcessTemplate));
      }
    });
  }, [dispatch, projectId, registrationProcessTemplate]);

  const handleAddConfiguration = useCallback(() => {
    modalControls.openModal({ key: uuid() });
  }, [modalControls]);

  const handleEditConfiguration = useCallback(
    (id) =>
      modalControls.openModal({
        ...selectAutoScreeningById(store.getState(), id),
        key: uuid(),
      }),
    [modalControls, store],
  );

  const handleDeleteConfiguration = useCallback(
    (id) =>
      dispatch(
        deleteAutoScreening({
          changes: {
            id,
            projectId,
          },
        }),
      ),
    [dispatch, projectId],
  );

  return {
    autoScreenings,
    loadingConfigs,
    loadingLocales,
    modalProps,
    modalControls,
    hasCompleteAdditionalAttributes,
    handleAddConfiguration,
    handleEditConfiguration,
    handleDeleteConfiguration,
  };
}

export default compose(withHooks(useHooksAsProps))(AutoScreening);
