import React from 'react';
import PropTypes from 'prop-types';
import { v4 as uuidv4 } from 'uuid';

import Modal from '../../../../../components/Modal';

import { Container } from './styles';
import { SectionDescription } from '../styles';

import ProductivityGoalsForm from './ProductivityGoalsForm';
import QualityGoalsForm from './QualityGoalsForm';

import { PRODUCTIVITY_METRICS_TYPE, QUALITY_METRICS_TYPE } from '../constants';

export default function GoalsModal({
  modalProps,
  metric,
  panelType,
  handleCancel,
}) {
  return (
    <Modal
      key={uuidv4()}
      open={modalProps.open}
      onClose={handleCancel}
      onExited={modalProps.onExited}
      title="Edit Goals"
    >
      <Container>
        <SectionDescription className="description">
          Input the values for your metrics
        </SectionDescription>

        {panelType === PRODUCTIVITY_METRICS_TYPE ? (
          <ProductivityGoalsForm
            metricId={metric.id}
            metricTitle={metric.metricName}
            metricKey={metric.key}
            metricRequired={metric.required}
            goals={metric.productivityGoals}
            closeModal={modalProps.onClose}
          />
        ) : (
          <QualityGoalsForm
            metric={metric}
            metricTitle={metric.metricName}
            isQuizBased={metric.quizBased}
            metricKey={metric.key}
            goals={metric.qualityGoals}
            closeModal={handleCancel}
          />
        )}
      </Container>
    </Modal>
  );
}

GoalsModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  metric: PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    metricName: PropTypes.string,
    quizBased: PropTypes.bool,
    required: PropTypes.bool,
    key: PropTypes.string,
    productivityGoals: PropTypes.arrayOf(PropTypes.shape({})),
    qualityGoals: PropTypes.arrayOf(PropTypes.shape({})),
  }).isRequired,
  panelType: PropTypes.oneOf([PRODUCTIVITY_METRICS_TYPE, QUALITY_METRICS_TYPE])
    .isRequired,
  handleCancel: PropTypes.func.isRequired,
};
