import React, { useMemo, useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import linewrap from 'linewrap';
import { useDispatch, useStore, useSelector } from 'react-redux';
import { TableContainer, Target, HiringTargetLink } from './styles';
import Table from '../../../Table';
import { SectionTitle, SectionDescription } from '../LocaleTenants/styles';
import { Section } from '../../../../styles';
import Button from '../../../Button';
import HiringTargetsModal from './HiringTargetsModal';
import { useModal } from '../../../Modal';
import NoEntries from '../../../NoEntries';
import {
  removeOne as removeOneHiringTarget,
  selectById as selectHiringTargetById,
  selectLoading as selectHiringTargetLoading,
} from '../../../../store/ducks/hiringTargets';
import { useHiringTargets } from './helpers';
import SettingsCell from './SettingsCell';
import { useErrorPopover } from '../../../ErrorPopover';
import { useLocaleTenants } from '../LocaleTenants/helpers';
import CellWithDefaultDot from '../../InvoiceAndPaymentForm/PayratesSetup/CellWithDefaultDot';
import { hiringTargetsGuidelineLink } from '../../../../constants/externalLinks';

const wrapLines = linewrap(28, { lineBreakScheme: 'html' });

function renderMultilineTooltip(string) {
  // We must inject html in order to have line breaks!
  /* eslint-disable react/no-danger */
  return <span dangerouslySetInnerHTML={{ __html: wrapLines(string) }} />;
}

function renderFormatedDate({ cell: { value } }) {
  return value === null ? '.' : moment(value).format('MMM Do[,] YYYY');
}

function sortByDate(rowA, rowB) {
  if (rowA.values.hiringDeadline && rowB.values.hiringDeadline) {
    return moment(rowA.values.hiringDeadline).diff(
      moment(rowB.values.hiringDeadline),
    );
  }

  return null;
}

// Not a Functional Component!
// eslint-disable-next-line react/prop-types
function renderTarget({ cell: { value } }) {
  return <Target>{value}</Target>;
}

const hiringTargetsErr = new Error(
  'Must fill Locale Tenants table in order to add Hiring Targets.',
);

export default function HiringTargets(props) {
  const { isTranslationProject, projectId } = props;

  const [modalProps, modalControls] = useModal();

  const dispatch = useDispatch();
  const store = useStore();

  const hiringTargets = useHiringTargets(projectId);
  const localeTenants = useLocaleTenants();

  const [showError, setShowError] = useState(false);

  const handleAdd = useCallback(() => {
    if (localeTenants.length === 0) {
      setShowError(true);
      return;
    }

    modalControls.openModal();
  }, [localeTenants.length, modalControls]);

  const loading = useSelector(selectHiringTargetLoading);

  // Delete Hiring Target Logic
  // const payrates = useSelector(selectAllEager);

  const deleteHiringTarget = useCallback(
    (id) => {
      // const hasPayrate = !!payrates.find((pr) => pr.hiringTarget === id);
      dispatch(removeOneHiringTarget(id));

      // TODO: enable check for hiring target.
      // Also, what should we do when there is a hiring target for this payrate?
      // if (!hasPayrate) {
      //   dispatch(hiringTargets.removeOne(id));
      // }
    },
    [dispatch],
  );

  const finalColumns = useMemo(() => {
    return [
      ...(isTranslationProject
        ? [
            {
              Header: 'LOC. LANGUAGE FROM',
              accessor: 'localeLanguage',
              key: 'localeLanguage',
              tooltip: renderMultilineTooltip(
                'Language that workers speak in their locale.',
              ),
              width: 200,
            },
            {
              Header: 'LOC. DIALECT FROM',
              accessor: 'localeDialect',
              key: 'localeDialect',
              tooltip: 'Region for the language.',
              width: 200,
            },
            {
              Header: 'LOC. LANGUAGE To',
              accessor: 'localeLanguageTo',
              key: 'localeLanguageTo',
              tooltip: renderMultilineTooltip(
                'Language that workers speak in their locale.',
              ),
              width: 200,
            },
            {
              Header: 'LOC. DIALECT TO',
              accessor: 'localeDialectTo',
              key: 'localeDialectTo',
              tooltip: 'Region for the language.',
              width: 200,
            },
          ]
        : [
            {
              Header: 'LOC. LANGUAGE',
              accessor: 'localeLanguage',
              key: 'localeLanguage',
              tooltip: renderMultilineTooltip(
                'Language that workers speak in their locale.',
              ),
            },
            {
              Header: 'LOC. DIALECT',
              accessor: 'localeDialect',
              key: 'localeDialect',
              tooltip: 'Region for the language.',
            },
          ]),
      {
        Header: 'REST. TO COUNTRY',
        accessor: 'restrictToCountry',
        key: 'restrictToCountry',
        tooltip: 'Country workers live in.',
      },
      {
        Header: 'ASSIGNED OWNER',
        accessor: 'assignedOwner',
        key: 'assignedOwner',
        disableSortBy: true,
        tooltip: 'Recruiter for the hiring target.',
        Cell: CellWithDefaultDot,
      },
      {
        Header: 'HIRING DEADLINE',
        accessor: 'hiringDeadline',
        key: 'hiringDeadline',
        Cell: renderFormatedDate,
        sortType: sortByDate,
        tooltip: renderMultilineTooltip(
          'Hiring deadline for the recruiting process.',
        ),
      },
      {
        Header: 'TARGET',
        accessor: 'target',
        key: 'target',
        tooltip: 'Actual hiring target.',
        Cell: renderTarget,
        maxWidth: 72,
        style: {
          textAlign: 'center',
        },
      },
      {
        Header: '',
        accessor: 'id',
        key: 'settings',
        maxWidth: 44,
        disableSortBy: true,
        Cell: SettingsCell,
      },
    ].filter(Boolean);
  }, [isTranslationProject]);

  const editHiringTarget = useCallback(
    (id) => {
      modalControls.openModal(selectHiringTargetById(store.getState(), id));
    },
    [modalControls, store],
  );

  const copyHiringTarget = useCallback(
    (id) => {
      const hiringTarget = {
        ...(selectHiringTargetById(store.getState(), id) || {}),
      };

      delete hiringTarget.id;

      modalControls.openModal(hiringTarget);
    },
    [modalControls, store],
  );

  const [showModalError, hideModalError, errorPopoverProps] = useErrorPopover();

  return (
    <>
      <Section marginBottom="46px">
        <SectionTitle>Hiring Targets</SectionTitle>
        <SectionDescription>
          The project hiring targets for each locate. Please check{' '}
          <HiringTargetLink href={hiringTargetsGuidelineLink} target="blank">
            hiring target guidelines
          </HiringTargetLink>
        </SectionDescription>

        {hiringTargets.length ? (
          <TableContainer>
            <Table
              columns={finalColumns}
              data={hiringTargets}
              initialState={{
                deleteHiringTarget,
                editHiringTarget,
                copyHiringTarget,
              }}
            />
          </TableContainer>
        ) : (
          <NoEntries
            label={loading ? 'Loading Hiring Targets...' : null}
            error={showError && hiringTargetsErr}
          />
        )}

        <Button
          onClick={handleAdd}
          data-testid="project-setup--recruitment--add-hiring-target-button"
        >
          Add Target
        </Button>
      </Section>

      <HiringTargetsModal
        modalProps={modalProps}
        modalControls={modalControls}
        showError={showModalError}
        hideError={hideModalError}
        errorPopoverProps={errorPopoverProps}
        isTranslationProject={isTranslationProject}
      />
    </>
  );
}

HiringTargets.propTypes = {
  isTranslationProject: PropTypes.bool.isRequired,
  projectId: PropTypes.number.isRequired,
};
