import React, { useEffect, useState, useMemo } from 'react';
import PropTypes from 'prop-types';

import { isEmpty } from 'lodash';
import { Col, Row } from 'styled-bootstrap-grid';

import Button from '../../../../../components/Button';
import FloatContainer, { FloatRight } from '../../../../../components/Float';
import Input from '../../../../../components/Input';
import Modal from '../../../../../components/Modal';
import Select from '../../../../../components/Select';

import { Label } from '../../../../../styles';

import { Container } from './styles';
import { SectionDescription } from '../styles';

import {
  METRIC_LIST,
  CALCULATION_LIST,
  generateEmptyQualityMetric,
} from '../constants';

const QualityMetricModal = (props) => {
  const { metric, onChange, modalProps, modalControls } = props;
  const [selectedMetric, setSelectedMetric] = useState(
    generateEmptyQualityMetric(),
  );

  const isEdit = useMemo(() => metric.id !== undefined, [metric.id]);

  const validate = () => {
    let valid = true;
    const transformed = { ...selectedMetric };

    if (isEmpty(transformed.metric)) {
      valid = false;
      transformed.metricError = 'is required';
    }
    if (isEmpty(transformed.metricName)) {
      valid = false;
      transformed.metricNameError = 'is required';
    }
    if (isEmpty(transformed.calculation)) {
      valid = false;
      transformed.calculationError = 'is required';
    }

    setSelectedMetric(transformed);

    return valid;
  };

  const handleSubmit = () => {
    if (validate()) {
      onChange(selectedMetric);
      modalControls.closeModal();
      setSelectedMetric(generateEmptyQualityMetric());
    }
  };

  useEffect(() => {
    if (!isEmpty(metric)) {
      setSelectedMetric(metric);
    }
  }, [metric]);

  return (
    <Modal
      open={modalProps.open}
      onClose={modalProps.onClose}
      onExited={modalProps.onExited}
      title="Create New Quality Metric"
    >
      <Container>
        <SectionDescription className="description">
          Select a metric category that best fit your project, choose a name and
          calculation for it.
        </SectionDescription>
        <Row style={{ marginBottom: 24 }}>
          <Col col={12}>
            <Label htmlFor="metric-category">Metric category</Label>
            <Select
              name="metric-category"
              placeholder="Select"
              options={METRIC_LIST}
              value={selectedMetric.metric}
              disabled={isEdit}
              error={selectedMetric.metricError}
              errorIncreasesHeight
              onChange={({ value, quizBased }) =>
                setSelectedMetric({
                  ...selectedMetric,
                  metric: value,
                  quizBased,
                  metricError: null,
                })
              }
            />
          </Col>
        </Row>
        <Row style={{ marginBottom: 60 }}>
          <Col col={6}>
            <Label htmlFor="metric-name">Metric Name</Label>
            <Input
              name="metric-name"
              placeholder="Enter Name"
              value={selectedMetric.metricName}
              error={selectedMetric.metricNameError}
              errorIncreasesHeight
              onChange={({ target: { value } }) =>
                setSelectedMetric({
                  ...selectedMetric,
                  metricName: value,
                  metricNameError: null,
                })
              }
            />
          </Col>
          <Col col={6}>
            <Label htmlFor="calculation">Calculation</Label>
            <Select
              name="calculation"
              placeholder="Select"
              options={CALCULATION_LIST}
              value={selectedMetric.calculation}
              disabled={isEdit}
              error={selectedMetric.calculationError}
              errorIncreasesHeight
              onChange={({ value }) =>
                setSelectedMetric({
                  ...selectedMetric,
                  calculation: value,
                  calculationError: null,
                })
              }
            />
          </Col>
        </Row>
        <FloatContainer className="actions">
          <FloatRight>
            <Button type="secondary" onClick={modalControls.closeModal}>
              Cancel
            </Button>
            <Button onClick={() => handleSubmit()}>Create Metric</Button>
          </FloatRight>
        </FloatContainer>
      </Container>
    </Modal>
  );
};

QualityMetricModal.propTypes = {
  metric: PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  }),
  onChange: PropTypes.func,
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,
};

QualityMetricModal.defaultProps = {
  metric: {},
  onChange: () => null,
};
export default QualityMetricModal;
