import * as yup from 'yup';
import createArrayValidation from '../../../helpers/array-validation';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';

function transformBlockigRuleError(error, value, index) {
  return new yup.ValidationError(
    requiredMessage`Project`,
    value,
    `blockingRule${index}projectId`,
  );
}

function transformLinkedProjectError(error, value, index) {
  return new yup.ValidationError(
    requiredMessage`Project`,
    value,
    `linkedProject${index}projectId`,
  );
}

const projectItemSchema = { projectId: yup.number().required() };

const validateBlockingRules = createArrayValidation(
  projectItemSchema,
  transformBlockigRuleError,
);

const validateLinkedProjects = createArrayValidation(
  projectItemSchema,
  transformLinkedProjectError,
);

export default yup
  .object()
  .strict()
  .shape({
    blockingRules: yup.mixed().when('hasBlockingRules', {
      is: true,
      then: yup
        .mixed()
        .test('blockingRules', '', validateBlockingRules)
        .required(requiredMessage`Project`),
    }),

    linkedProjects: yup.mixed().when('hasLinkedProjects', {
      is: true,
      then: yup
        .mixed()
        .test('linkedProjects', '', validateLinkedProjects)
        .required(requiredMessage`Project`),
      otherwise: yup.mixed(),
    }),

    notesToRecruiting: yup.mixed().when('hasNotesToRecruiting', {
      is: true,
      then: yup
        .string()
        .required(
          requiredMessage`When "Add Notes to Recruiting Team" is checked this`,
        ),
    }),
  });
