import { useState, useCallback } from 'react';
import { connect, useDispatch } from 'react-redux';
import { compose } from 'recompose';
import {
  selectProcessModules,
  resetState,
} from '../../store/ducks/processBuilder';
import ProcessBuilder from './ProcessBuilder';

function mapStateToProps(state) {
  const processModules = selectProcessModules(state);

  return {
    processModules,
  };
}

export default compose(connect(mapStateToProps))(ProcessBuilder);

export function useProcessBuilder() {
  const [_projectID, setProjestID] = useState(null);
  const [_processID, setProcessID] = useState(null);
  const [_copyFromProcessID, setCopyFromProcessID] = useState(null);
  const [_processType, setProcessType] = useState(null);
  const [windowOpen, setWindowOpen] = useState(false);

  const openProcessBuilder = useCallback(
    (projectId, { processType, processId, copyFromProcessID }) => {
      setProjestID(projectId || null);
      setProcessType(processType);
      setProcessID(processId);
      setWindowOpen(true);
      setCopyFromProcessID(copyFromProcessID || null);
    },
    [],
  );

  const dispatch = useDispatch();

  const onCancel = useCallback(() => {
    dispatch(resetState());
    setProcessType(null);
    setWindowOpen(false);
  }, [dispatch]);

  const onProcessCreated = useCallback((id) => {
    setProcessID(id);
  }, []);

  return [
    openProcessBuilder,
    windowOpen,
    {
      projectId: _projectID,
      processId: _processID,
      processType: _processType,
      copyFromProcessID: _copyFromProcessID,
      onCancel,
      onProcessCreated,
    },
  ];
}
