import { call, all, put, delay, takeEvery } from 'redux-saga/effects';
import {
  saveAccessHolder,
  saveAccessHolderError,
  saveAccessHolderSuccess,
} from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* accessHolderRequest({ name, status, id }) {
  let requestError = false;

  try {
    const response = yield call(
      axios.put,
      ENDPOINTS.V2.ACCESS_HOLDER_UPDATE({
        pathVariables: {
          id,
        },
      }),
      {
        name,
        status,
      },
    );
    yield put(saveAccessHolderSuccess(response.data));
  } catch (error) {
    requestError = error;
  }

  return requestError;
}

function* saveAccessHolderSagaFn(action) {
  const {
    payload: { changes, thenPut },
  } = action;

  const errors = yield all(
    changes.map((accessHolderItem) =>
      call(accessHolderRequest, accessHolderItem),
    ),
  );

  const error = errors.find((result) => result);

  if (typeof thenPut === 'object') {
    yield put(thenPut);
  }

  if (error) {
    yield delay(50);
    yield put(saveAccessHolderError(error));
  }
}

export default takeEvery(saveAccessHolder.type, saveAccessHolderSagaFn);
