import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import { useFeatures } from 'flagged';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import { FormLayout } from './styles';
import { setDirty } from '../../../../store/ducks/projectSetup';
import useFirstChange from '../../helpers/useFirstChange';
import StepFooter from '../../../../components/StepFooter';
import ProjectToolsForm from '../../../../components/ProjectForms/ProjectToolsForm';
import { useNavigationContext } from '../../helpers/navigationContext';

export default function ProjectTools(props) {
  const { setFieldValue, values, submitForm, handleSubmit } = props;

  const { goBack } = useNavigationContext();

  const submitAndNavigate = useCallback(() => {
    setFieldValue('navigate', true);
    submitForm();
  }, [setFieldValue, submitForm]);

  const dispatch = useDispatch();
  const features = useFeatures();

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    values,
  );

  const nextPage = features.experimentGroups
    ? 'Project Access'
    : 'User Project Page';

  return (
    <FormLayout onSubmit={handleSubmit}>
      <WizardTitle>Project Tools</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <ProjectToolsForm />

      <WizardFooter>
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel={`Next: ${nextPage}`}
        />
      </WizardFooter>
    </FormLayout>
  );
}

ProjectTools.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
};
