import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { Label, Section } from '../../../../../../styles';
import Select from '../../../../../Select';
import TabSwitcher from '../../../../../TabSwitcher';
import { DEMOGRAPHIC_TYPE } from '../../../../../../store/ducks/projectDemographicRequirements';
import CloseableTag from '../../../../../CloseableTag';
import { InputContainer } from './styles';
import BottomControls from '../BottomControls';

const demographicsTypeOptions = [
  { value: DEMOGRAPHIC_TYPE.BY_GROUP, label: 'Requirements by group' },
  { value: DEMOGRAPHIC_TYPE.AGGREGATED, label: 'Aggregated requirements' },
];

const viewAsTabs = [
  {
    label: 'PERCENTAGE',
    'data-testid': 'demographics-modal--tab-as-percentage',
  },
  { label: 'NUMBER', 'data-testid': 'demographics-modal--tab-as-number' },
];

export default function DemographicSettings(props) {
  const {
    demographicsType,
    setDemographicsType,
    viewAs,
    setViewAs,
    requirementOptions,
    requirementOptionsLoading,
    requirementOptionsError,
    handleChange,
    values,
    handleRemove,
    submitForm,
    closeModal,
    submitCount,
    errors,
  } = props;

  return (
    <>
      <Section marginBottom="32px">
        <Row>
          <Col col={6}>
            <Label htmlFor="demographicsType">DEMOGRAPHICS TYPE</Label>
            <Select
              name="demographicsType"
              id="demographicsType"
              options={demographicsTypeOptions}
              onChange={setDemographicsType}
              value={demographicsType}
              data-testid="demographics-modal--select-type"
            />
          </Col>
          <Col col={6}>
            <Label htmlFor="businessUnit">VIEW AS</Label>
            <TabSwitcher
              tabs={viewAsTabs}
              value={viewAs}
              onChange={setViewAs}
            />
          </Col>
        </Row>
      </Section>
      <Section marginBottom="32px">
        <InputContainer>
          <Label htmlFor="demographicsType">SELECT PARAMETERS</Label>
          <Select
            id="requirements"
            data-testid="demographics-modal--select-parameters"
            loading={requirementOptionsLoading}
            name="requirements"
            onChange={handleChange}
            options={requirementOptions}
            placeholder="Select options to add parameters"
            value={values.requirements}
            error={
              requirementOptionsError ||
              (submitCount > 0 && errors.requirements)
            }
            isMulti
            multiMaxSelectedOptions={
              demographicsType === DEMOGRAPHIC_TYPE.AGGREGATED ? 3 : 0
            }
            multiMaxSelectedMessage="You cant select more than 3 parameters for aggregated requirements."
          />
          {(values.requirements || []).map((r) => (
            <CloseableTag
              key={r.label}
              label={r.label}
              onClose={handleRemove(r.value)}
            />
          ))}
        </InputContainer>
      </Section>

      <BottomControls
        currentStep={1}
        nextLabel="Next: Distribution"
        onNext={submitForm}
        backLabel="Cancel"
        onBack={closeModal}
      />
    </>
  );
}

DemographicSettings.propTypes = {
  demographicsType: PropTypes.string,
  setDemographicsType: PropTypes.func.isRequired,
  viewAs: PropTypes.number,
  setViewAs: PropTypes.func.isRequired,
  requirementOptions: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      label: PropTypes.string,
    }),
  ).isRequired,
  requirementOptionsLoading: PropTypes.bool.isRequired,
  requirementOptionsError: PropTypes.instanceOf(Error),
  handleChange: PropTypes.func.isRequired,
  handleRemove: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  errors: PropTypes.objectOf(PropTypes.any).isRequired,
  submitCount: PropTypes.number.isRequired,
};

DemographicSettings.defaultProps = {
  demographicsType: undefined,
  viewAs: undefined,
  requirementOptionsError: null,
};
