/* eslint-disable react/jsx-props-no-spreading, import/no-cycle */
import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import getFields from './helpers/getFields';
import withField from './helpers/withField';

const QuestionBuilder = (props) => {
  const {
    data: { questionType },
  } = props;

  const Component = useMemo(() => getFields(questionType), [questionType]);

  return <Component {...props} />;
};

export default withField(QuestionBuilder);

QuestionBuilder.propTypes = {
  data: PropTypes.shape({
    questionType: PropTypes.string,
  }).isRequired,
};

QuestionBuilder.defaultProps = {};
