import { createSlice } from '@reduxjs/toolkit';
import get from 'lodash/get';

const key = 'dataConsentForm';

const slice = createSlice({
  name: key,

  initialState: {
    data: [],
    loading: false,
    fetchError: null,
    saving: 0,
    saveError: null,
  },

  reducers: {
    fetchDataConsentForm: (state) => {
      state.loading = true;
      state.error = null;
      state.saveError = null;
    },

    fetchDataConsentFormSuccess: (state, action) => {
      state.loading = false;
      state.error = null;
      state.saveError = null;
      state.data = action.payload;
    },

    fetchDataConsentFormError: (state, action) => {
      state.loading = false;
      state.error = action.payload;
      state.saveError = null;
    },

    saveDataConsentForm: (state) => {
      state.saving = true;
      state.saveError = null;
      state.error = null;
    },

    saveDataConsentFormSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.error = null;
      state.data = action.payload;
    },

    saveDataConsentFormError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
      state.error = action.payload;
    },

    deleteDataConsentForm: (state) => {
      state.saving = true;
      state.saveError = null;
      state.error = null;
    },

    deleteDataConsentFormSuccess: (state, action) => {
      state.saving = false;
      state.saveError = null;
      state.error = null;
      state.data = action.payload;
    },

    deleteDataConsentFormError: (state, action) => {
      state.saving = false;
      state.saveError = action.payload;
      state.error = action.payload;
    },
  },
});

const {
  actions: {
    fetchDataConsentForm,
    fetchDataConsentFormSuccess,
    fetchDataConsentFormError,
    saveDataConsentForm,
    saveDataConsentFormSuccess,
    saveDataConsentFormError,
    deleteDataConsentForm,
    deleteDataConsentFormSuccess,
    deleteDataConsentFormError,
  },
  reducer,
} = slice;

reducer.key = key;

export default reducer;

export {
  fetchDataConsentForm,
  fetchDataConsentFormSuccess,
  fetchDataConsentFormError,
  saveDataConsentForm,
  saveDataConsentFormSuccess,
  saveDataConsentFormError,
  deleteDataConsentForm,
  deleteDataConsentFormSuccess,
  deleteDataConsentFormError,
};

export function selectDataConsentFormSlice(state) {
  return state[reducer.key];
}

export function selectData(state) {
  return get(selectDataConsentFormSlice(state), 'data', []);
}

export function selectLoading(state) {
  return get(selectDataConsentFormSlice(state), 'loading', false);
}

export function selectFetchError(state) {
  return get(selectDataConsentFormSlice(state), 'fetchError', null);
}

export function selectSaving(state) {
  return get(selectDataConsentFormSlice(state), 'saving', 0);
}

export function selectSaveError(state) {
  return get(selectDataConsentFormSlice(state), 'saveError', null);
}
