import { useState } from 'react';
// eslint-disable-next-line import/no-named-as-default
import Modal from './Modal';

export default Modal;

export function useModal(startOpen = false, initialData = null) {
  const [open, setOpen] = useState(startOpen);
  const [data, setData] = useState(initialData);

  function openModal(newData) {
    setOpen(true);

    // Ignore data if it is a React event
    if (
      newData &&
      typeof newData.persist === 'function' &&
      !!newData.nativeEvent
    ) {
      setData(null);
    } else {
      setData(newData);
    }
  }

  function closeModal(sync = false) {
    setOpen(false);

    if (sync) {
      // This will not wait for the modal UI to unmount before clearing modal data;
      setData(null);
    }
  }

  function handleExited() {
    setData(null);
  }

  const modalProps = {
    open,
    onExited: handleExited,
    onClose: closeModal,
  };

  const modalControls = {
    openModal,
    closeModal,
    modalData: data,
  };

  return [modalProps, modalControls];
}
