import React from 'react';
import PropTypes from 'prop-types';
import { Attribute, Metadata, Link } from './styles';

export default function AttributeCell({ row }) {
  const {
    original: { attribute, metadata, links = [] },
  } = row;

  return (
    <>
      <Attribute>{attribute}</Attribute>{' '}
      {links.map((link, i, { length }) => {
        return (
          <span key={link.url}>
            <Link href={link.url} target="_blank">
              {link.name}
            </Link>
            {i + 1 < length && ', '}
          </span>
        );
      })}
      <Metadata>{metadata}</Metadata>
    </>
  );
}

AttributeCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      attribute: PropTypes.string,
      metadata: PropTypes.string,
      links: PropTypes.arrayOf(
        PropTypes.shape({ name: PropTypes.string, url: PropTypes.string }),
      ),
    }),
  }).isRequired,
};
