import { useEffect, useMemo, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import Filters from './Filters';
import {
  PAGINATED_ENTITIES,
  setFilters,
  selectors,
  fetchPaginated,
} from '../../../store/ducks/paginated';
import {
  fetch,
  ENTITIES,
  selectors as optionsSelectors,
} from '../../../store/ducks/options';
import mapToOptions from '../../ProjectSetup/helpers/mapToOptions';
import { createKeydownHandler } from '../../../helpers/a11y';
import { useModal } from '../../../components/Modal';

function useHooksAsProps() {
  const dispatch = useDispatch();
  const [modalProps, modalControls] = useModal();

  useEffect(() => {
    dispatch(fetch(ENTITIES.masterProfileCategories));
    dispatch(fetchPaginated(PAGINATED_ENTITIES.profileQuestions));
  }, [dispatch]);

  const handleInputChange = useCallback(
    (event) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.profileQuestions,
          filters: {
            topic: event.target.value,
          },
        }),
      );
    },
    [dispatch],
  );

  const handleInputClear = useCallback(() => {
    dispatch(
      setFilters({
        forKey: PAGINATED_ENTITIES.profileQuestions,
        filters: {
          topic: '',
        },
      }),
    );
  }, [dispatch]);

  const handleInputClearKeydown = useMemo(
    () => createKeydownHandler(handleInputClear, 13),
    [handleInputClear],
  );

  const handleCategoryChange = useCallback(
    ({ value }) => {
      dispatch(
        setFilters({
          forKey: PAGINATED_ENTITIES.profileQuestions,
          filters: {
            category: value,
          },
        }),
      );
    },
    [dispatch],
  );

  const handleOpenModal = useCallback(() => {
    modalControls.openModal({});
  }, [modalControls]);

  const categories = useSelector(
    optionsSelectors.createSelectSliceFor(ENTITIES.masterProfileCategories),
  );

  const categoryOptions = useMemo(
    () => [
      { value: '*', label: 'All Categories' },
      ...mapToOptions('value', 'label')(categories.data),
    ],
    [categories],
  );

  const filters = useSelector(
    selectors.createSelectFiltersFor(PAGINATED_ENTITIES.profileQuestions),
  );

  const profileQuestions = useSelector(
    selectors.createSelectDataFor(PAGINATED_ENTITIES.profileQuestions),
  );

  const filterTopic = get(filters, 'topic', '');
  const filterCategory = get(filters, 'category', '*');

  let filteredQuestions = profileQuestions.filter(({ name }) =>
    name.toLowerCase().includes(filterTopic.toLowerCase()),
  );

  if (filterCategory !== '*') {
    filteredQuestions = profileQuestions.filter(
      ({ category }) => category === filterCategory,
    );
  }

  return {
    handleInputChange,
    handleInputClear,
    handleInputClearKeydown,
    handleCategoryChange,
    handleOpenModal,
    categories,
    categoryOptions,
    filters,
    profileQuestions: filteredQuestions,
    modalProps,
    modalControls,
  };
}

export default withHooks(useHooksAsProps)(Filters);
