import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { ThemeContext } from 'styled-components';
import SortArrowContainer from './styles';

export default function SortArrow({ column }) {
  const theme = useContext(ThemeContext);

  const arrowColor =
    column.canSort && column.isSorted
      ? theme.colors.azure
      : theme.colors.blueGrey;

  if (!column.canSort) {
    return null;
  }

  return (
    <SortArrowContainer flip={column.isSortedDesc}>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="8"
        height="5"
        viewBox="0 0 8 5"
      >
        <g fill="none" fillRule="evenodd">
          <path d="M0 0H14V14H0z" transform="translate(-3 -5)" />
          <path
            fill={arrowColor}
            fillRule="nonzero"
            d="M7.38 9.007L9.886 6.86a.583.583 0 00-.379-1.027H4.492a.583.583 0 00-.38 1.027l2.509 2.147c.218.186.54.186.758 0z"
            transform="translate(-3 -5)"
          />
        </g>
      </svg>
    </SortArrowContainer>
  );
}

SortArrow.propTypes = {
  column: PropTypes.shape({
    canSort: PropTypes.bool,
    isSorted: PropTypes.bool,
    isSortedDesc: PropTypes.bool,
  }).isRequired,
};
