import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import withHooks from 'hook-hoc';
import get from 'lodash/get';
import ProfileQuestions from './ProfileQuestions';
import { PAGINATED_ENTITIES, selectors } from '../../store/ducks/paginated';
import {
  fetch,
  ENTITIES,
  selectors as optionsSelectors,
} from '../../store/ducks/options';

function useHooksAsProps() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch(ENTITIES.masterProfileCategories));
  }, [dispatch]);

  const loading = useSelector(
    selectors.createSelectLoadingFor(PAGINATED_ENTITIES.profileQuestions),
  );

  const categories = get(
    useSelector(
      optionsSelectors.createSelectSliceFor(ENTITIES.masterProfileCategories),
    ),
    'data',
    [],
  );

  const profileQuestions = useSelector(
    selectors.createSelectDataFor(PAGINATED_ENTITIES.profileQuestions),
  );

  const filters = useSelector(
    selectors.createSelectFiltersFor(PAGINATED_ENTITIES.profileQuestions),
  );

  const filterTopic = get(filters, 'topic', '');
  const filterCategory = get(filters, 'category', '*');

  let filteredQuestions = profileQuestions.filter(({ name }) =>
    name.toLowerCase().includes(filterTopic.toLowerCase()),
  );

  if (filterCategory !== '*') {
    filteredQuestions = profileQuestions.filter(
      ({ category }) => category === filterCategory,
    );
  }

  const groups = filteredQuestions.reduce(
    (currentGroups, item) => ({
      ...currentGroups,
      [item.category]: [...(currentGroups[item.category] || []), item],
    }),
    {},
  );

  const maxQuestionId = Math.max(
    ...filteredQuestions.map((o) => {
      return o.id;
    }),
  );

  const answerIds = filteredQuestions.map((o) => {
    return o.answerList.map((a) => {
      return a.id;
    });
  });

  const maxAnswerId = Math.max(...[].concat(...answerIds));

  return {
    categories,
    maxAnswerId,
    maxQuestionId,
    profileQuestions: groups,
    loading,
  };
}

export default withHooks(useHooksAsProps)(ProfileQuestions);
