import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import { useFormikContext } from 'formik';
import isEmpty from 'lodash/isEmpty';
import { themePropTypes } from '../../../styles/themes/default';
import { Section, InputsContainer } from './styles';
import * as registrationProcesses from '../../../store/ducks/registrationProcesses';
import * as projectPages from '../../../store/ducks/projectPages';
import {
  selectProjectId,
  selectRegistrationAndQualification,
} from '../../../store/ducks/project';
import { selectData as selectDataConsentForm } from '../../../store/ducks/dataConsentForm';
import { Label } from '../../../styles';
import Select from '../../Select';
import mapToOptions from '../../../pages/ProjectSetup/helpers/mapToOptions';
import {
  CheckGroup,
  CheckAltMessage,
} from '../../../pages/ProjectSetup/Steps/ProjectTools/styles';
import Checkbox from '../../Checkbox';
import ProjectResources from './ProjectResources';
import DataConsentForm from '../../DataConsentForm';

export default function ProjectUserProjectPageForm(props) {
  const { values, handleChange, theme, renderedIn } = props;
  const { setFieldValue, errors, submitCount } = useFormikContext();

  const dispatch = useDispatch();

  const registrationAndQualification = useSelector(
    selectRegistrationAndQualification,
  );

  const { registrationProcessTemplate } = registrationAndQualification;

  useEffect(() => {
    dispatch(projectPages.fetchAll());
    dispatch(registrationProcesses.fetchOptions());
    if (registrationProcessTemplate) {
      dispatch(registrationProcesses.fetchSteps(registrationProcessTemplate));
    }
  }, [dispatch, registrationProcessTemplate]);

  const registrationSteps = useSelector(registrationProcesses.selectSteps);
  const consentForm = useSelector(selectDataConsentForm);
  const projectId = useSelector(selectProjectId);

  const projectPageOptions = mapToOptions(
    'id',
    'tabName',
  )(useSelector(projectPages.selectAll));

  let hasConsentForm = false;
  let hasDataCollectionPiiConsentForm = false;
  if (registrationSteps.steps.length) {
    const steps = registrationSteps.steps.map(
      (step) =>
        step.stepDefinition.sequenceFlow === 'dataCollectionPiiConsentFormFlow',
    );
    hasDataCollectionPiiConsentForm = steps.includes(true);
    hasConsentForm = hasDataCollectionPiiConsentForm || !isEmpty(consentForm);
  }

  return (
    <>
      <InputsContainer>
        <Section>
          <Row>
            <Col col="6">
              <Label htmlFor="defaultPageTask">Default Page Task</Label>
              <Select
                name="defaultPageTask"
                onChange={({ value }) =>
                  setFieldValue('defaultPageTask', value)
                }
                options={projectPageOptions}
                loading={useSelector(projectPages.selectLoading)}
                error={
                  useSelector(projectPages.selectError) ||
                  (submitCount >= 1 && errors.defaultPageTask)
                }
                value={values.defaultPageTask}
                data-testid="project-setup--user-project-page--select-default-page-task"
              />
            </Col>
          </Row>
        </Section>

        <Section marginBottom={16}>
          <CheckGroup>
            <Checkbox
              name="hasSkillReview"
              onChange={handleChange}
              checked={values.hasSkillReview}
              label="Available Skill Review"
              data-testid="project-setup--user-project-page--has-skill-review-toggle"
              theme={theme}
            />
            <CheckAltMessage>
              If checked, the skills review subtab will be added for the
              project.
            </CheckAltMessage>
          </CheckGroup>
        </Section>

        <Section>
          <CheckGroup>
            <Checkbox
              name="isACANProject"
              onChange={handleChange}
              checked={values.isACANProject}
              label="ACAN Project"
              data-testid="project-setup--user-project-page--is-acan-project-toggle"
              theme={theme}
            />
            <CheckAltMessage>
              If checked, the ACAN Timesheet subtab will be added for the
              project.
            </CheckAltMessage>
          </CheckGroup>
        </Section>
        <Section
          style={{ overflowX: renderedIn === 'project-view' ? 'none' : 'auto' }}
        >
          {/* <LegacyProjectMappings projectId={projectId} /> */}
        </Section>
      </InputsContainer>

      <ProjectResources projectId={projectId} mode="edit" />

      <Section marginBottom="30px">
        <CheckGroup>
          <Checkbox
            name="hasDataCollectionConsentForm"
            onChange={handleChange}
            checked={values.hasDataCollectionConsentForm || hasConsentForm}
            label="Data Collection Consent Form Template"
            data-testid="project-setup--user-project-page--consent-form--toggle"
          />
          <CheckAltMessage>
            Template configuration for the primary or third party users.
          </CheckAltMessage>
        </CheckGroup>
      </Section>

      {(values.hasDataCollectionConsentForm || hasConsentForm) && (
        <DataConsentForm
          projectId={projectId}
          hasConsentForm={hasConsentForm}
          hasDataCollectionPiiConsentForm={hasDataCollectionPiiConsentForm}
          mode="edit"
          showErrorWhenEmpty={
            submitCount >= 1 && !!errors.hasDataCollectionConsentForm
          }
        />
      )}
    </>
  );
}

ProjectUserProjectPageForm.propTypes = {
  values: PropTypes.objectOf(PropTypes.any).isRequired,
  handleChange: PropTypes.func.isRequired,
  theme: themePropTypes.isRequired,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
};

ProjectUserProjectPageForm.defaultProps = {
  renderedIn: 'project-setup',
};
