import React, { useCallback } from 'react';
import { Row, Col } from 'styled-bootstrap-grid';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import Select from '../../../Select';
import { ActionContainer, QuestionInfo } from './styles';
import { Label, Section } from '../../../../styles';

export default function ActionBuilder(props) {
  const {
    id,
    actionOptions,
    values,
    data,
    submitCount,
    errors,
    setFieldValue,
    firstOption,
    secondOption,
    firstOptionAccessory,
  } = props;

  const firstOptionBaseName = `firstOption[${id}].value`;
  const secondOptionBaseName = `secondOption[${id}].value`;

  const firstOptionErrorName = `firstOption${id}`;
  const secondOptionErrorName = `secondOption${id}`;

  const firstOptionValue = get(values.firstOption, id, null);
  const secondOptionValue = get(values.secondOption, id, null);

  const handleChange = useCallback(
    (currentField, secondaryField, value) => {
      setFieldValue(currentField, value);
      if (data.questionType !== 'YES_NO') {
        const inverseValue = get(
          actionOptions.filter((action) => action.value !== value),
          [0, 'value'],
          undefined,
        );
        // Prevents validation error on the first field when triggering the validateOnChange method
        setTimeout(() => setFieldValue(secondaryField, inverseValue), 50);
        setFieldValue(firstOptionErrorName, value);
        setFieldValue(secondOptionErrorName, value);
      }

      if (data.questionType === 'YES_NO') {
        setFieldValue(
          currentField.includes('firstOption')
            ? firstOptionErrorName
            : secondOptionErrorName,
          value,
        );
      }
    },
    [
      setFieldValue,
      firstOptionErrorName,
      secondOptionErrorName,
      data.questionType,
      actionOptions,
    ],
  );

  const firstOptionError = get(errors, firstOptionErrorName, null);
  const secondOptionError = get(errors, secondOptionErrorName, null);

  return (
    <>
      <ActionContainer>
        <Label>SELECT ACTION</Label>
        <Section marginBottom={submitCount > 0 && firstOptionError ? 30 : 10}>
          <Row>
            <Col col={3}>
              <Select
                id={firstOptionBaseName}
                name={firstOptionBaseName}
                data-testid="icf-step-appen-entity"
                placeholder="Select action"
                options={actionOptions}
                value={get(firstOptionValue, 'value', null)}
                onChange={({ value }) =>
                  handleChange(firstOptionBaseName, secondOptionBaseName, value)
                }
                error={submitCount > 0 && firstOptionError}
              />
            </Col>
            <Col col={9} style={{ margin: '9px 0 0 -16px' }}>
              {firstOption}
            </Col>
          </Row>
          {firstOptionAccessory}
        </Section>
        <Section marginBottom={24}>
          <Row>
            <Col col={3}>
              <Select
                id={secondOptionBaseName}
                name={secondOptionBaseName}
                data-testid="icf-step-appen-entity"
                placeholder="Select action"
                options={actionOptions}
                value={get(secondOptionValue, 'value', null)}
                onChange={({ value }) =>
                  handleChange(secondOptionBaseName, firstOptionBaseName, value)
                }
                error={submitCount > 0 && secondOptionError}
              />
            </Col>
            <Col col={9} style={{ margin: '8px 0 0 -16px' }}>
              {secondOption}
            </Col>
          </Row>
        </Section>
      </ActionContainer>
      <Section>
        <Row>
          <Col col={12}>
            <QuestionInfo>Exclude countries: None</QuestionInfo>
          </Col>
        </Row>
      </Section>
    </>
  );
}

ActionBuilder.propTypes = {
  firstOption: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  secondOption: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  firstOptionAccessory: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.object,
  ]),
  id: PropTypes.number.isRequired,
  actionOptions: PropTypes.arrayOf(PropTypes.object).isRequired,
  values: PropTypes.shape({
    firstOption: PropTypes.arrayOf(PropTypes.object).isRequired,
    secondOption: PropTypes.arrayOf(PropTypes.object).isRequired,
  }).isRequired,
  data: PropTypes.shape({
    questionType: PropTypes.string.isRequired,
  }).isRequired,
  submitCount: PropTypes.number.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  setFieldValue: PropTypes.func.isRequired,
};

ActionBuilder.defaultProps = {
  firstOption: 'all checked answers.',
  secondOption: 'all unchecked answers.',
  firstOptionAccessory: null,
};
