import { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withFormik } from 'formik';
import * as yup from 'yup';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import ProcessDetailsModal from './ProcessDetailsModal';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';
import store from '../../../store';
import { setProcessDetails } from '../../../store/ducks/targetBuilder';
import {
  fetchTargetScreening,
  selectTargetScreening,
} from '../../../store/ducks/targetScreening';

function useHooksAsProps() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchTargetScreening());
  }, [dispatch]);

  const targetGroups = useSelector(selectTargetScreening);

  const options = useMemo(
    () =>
      targetGroups.map((target) => ({
        label: target.name,
        value: target.id,
      })),
    [targetGroups],
  );

  return {
    options,
    loading: false,
    error: null,
  };
}

function handleSubmit(values, { props }) {
  const { closeModal } = props;

  if (typeof closeModal === 'function') {
    closeModal();
  }

  store.dispatch(
    setProcessDetails({
      processName: values.processName,
      processDescription: values.processDescription,
      copyFromProcessID: values.copyModulesFrom || null,
    }),
  );
}

function mapPropsToValues(props) {
  const { initialValues } = props;

  if (initialValues) {
    return {
      ...initialValues,
    };
  }

  return {};
}

const validationSchema = yup.object().shape({
  processName: yup.string().required(requiredMessage`Group name`),
  processDescription: yup.string().required(requiredMessage`Group description`),
  copyModulesFrom: yup.mixed().when('showCopyFromProcessSelect', {
    is: true,
    then: yup
      .mixed()
      .required(
        requiredMessage`When "Copy Questions From an Existing Group" is checked this`,
      ),
  }),
});

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema,
    enableReinitialize: true,
  }),
  withHooks(useHooksAsProps),
)(ProcessDetailsModal);
