import React from 'react';
import PropTypes from 'prop-types';
import HiringTargetOption from '../../HiringTargetOption';

export default function HiringTargetCell(props) {
  const {
    row: {
      original: {
        restrictToCountry,
        localeLanguage,
        localeDialect,
        localeLanguageTo,
        localeDialectTo,
      },
    },
  } = props;

  return (
    <HiringTargetOption
      restrictToCountry={restrictToCountry}
      localeLanguage={localeLanguage}
      localeDialect={localeDialect}
      localeLanguageTo={localeLanguageTo}
      localeDialectTo={localeDialectTo}
    />
  );
}

HiringTargetCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      localeLanguage: PropTypes.string.isRequired,
      localeDialect: PropTypes.string.isRequired,
      restrictToCountry: PropTypes.string.isRequired,
      localeLanguageTo: PropTypes.string,
      localeDialectTo: PropTypes.string,
      state: PropTypes.string,
    }).isRequired,
  }).isRequired,
};
