import styled from 'styled-components';

export const Check = styled.input.attrs(() => ({
  type: 'checkbox',
}))``;

export const Label = styled.label`
  position: relative;

  margin-right: 30px;
  input {
    position: absolute;
    opacity: 0;
  }

  .overlay {
    position: absolute;
    top: 5px;
    left: 0px;
    height: 20px;
    width: 20px;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid ${(props) => props.theme.colors.lightBlueGrey};
    transition: all 0.3s;
  }

  .icon {
    color: white;
    display: none;
    margin-left: 2.5px;
    margin-top: 5px;
  }

  input:checked ~ .overlay {
    background-color: ${(props) => props.theme.colors.azure};
    opacity: 1;
    border: 1px solid ${(props) => props.theme.colors.azure};
  }

  input:checked ~ .overlay .icon {
    display: block;
  }

  input:disabled ~ .overlay {
    pointer-events: none;
    opacity: 0.4;
  }
`;

export const Text = styled.div`
  margin-left: 34px;
  line-height: 1.6;
  color: ${(props) => props.theme.colors.charcoal};
  font-size: ${(props) => props.theme.sizes.p};
  font-weight: ${(props) => (props.isBold ? 'bold' : 'normal')};
  padding-top: 4px;
`;

export const CheckGroup = styled.div`
  display: flex;
  flex-direction: column;
`;

export const CheckAltMessage = styled(Text)`
  display: flex;
  flex-direction: row;
  margin-bottom: 0;
  margin-left: 34px;
  font-weight: normal;
  line-height: 1.6;
  font-size: ${(props) => props.theme.sizes.p};
  color: ${(props) => props.theme.colors.blueGrey};
`;
