import React, { useEffect } from 'react';
import { Row, Col } from 'styled-bootstrap-grid';
import { useDispatch, useSelector } from 'react-redux';
import { useFormikContext } from 'formik';
import shortId from 'shortid';
import { Section, RemoveButton, InputsContainer } from './styles';
import * as teamTypes from '../../../store/ducks/teamTypes';
import { themePropTypes } from '../../../styles/themes/default';
import { Label } from '../../../styles';
import Button from '../../Button';
import Select from '../../Select';
import { ENTITIES, fetch, selectors } from '../../../store/ducks/options';

const defaultValue = [{ key: shortId.generate(), role: null, member: null }];

export default function ProjectSupportTeamForm(props) {
  const { theme } = props;
  const {
    setFieldValue,
    errors,
    values: { supportTeam = defaultValue },
    submitCount,
  } = useFormikContext();

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(teamTypes.fetchAll());
    dispatch(fetch(ENTITIES.internalUsers));
  }, [dispatch]);

  function handleAddLine() {
    setFieldValue('supportTeam', [
      ...supportTeam,
      { role: null, member: null, key: shortId.generate() },
    ]);
  }

  function handleRemove(index) {
    setFieldValue('supportTeam', [
      ...supportTeam.slice(0, index),
      ...supportTeam.slice(index + 1, supportTeam.length),
    ]);
  }

  const teamTypesOptions = useSelector(teamTypes.selectAll);
  const teamTypesLoading = useSelector(teamTypes.selectLoading);
  const teamTypesError = useSelector(teamTypes.selectError);

  const internalUsers = useSelector(
    selectors.createSelectSliceFor(ENTITIES.internalUsers),
  );

  const internalUsersOptions = internalUsers.data.map((user) => ({
    value: user.id,
    label: `${user.lastName} (${user.firstName})`,
  }));

  function handleRoleChange(index, { value }) {
    const item = supportTeam[index];

    setFieldValue('supportTeam', [
      ...supportTeam.slice(0, index),
      { ...item, role: value, member: null },
      ...supportTeam.slice(index + 1, supportTeam.length),
    ]);
  }

  function handleMemberChange(index, { value }) {
    const item = supportTeam[index];

    setFieldValue('supportTeam', [
      ...supportTeam.slice(0, index),
      { ...item, member: value },
      ...supportTeam.slice(index + 1, supportTeam.length),
    ]);
  }

  return (
    <>
      <InputsContainer>
        <Section marginBottom={0}>
          <Row>
            <Col col={6}>
              <Label>Support User Role</Label>
            </Col>
            <Col col={6}>
              <Label>Member Name</Label>
            </Col>
          </Row>
        </Section>
        {supportTeam.map((supportMember, index) => (
          <Section key={supportMember.key} marginBottom={12}>
            <Row>
              <Col col={6}>
                <Select
                  name={`roleInput${index}`}
                  onChange={(value) => handleRoleChange(index, value)}
                  options={teamTypesOptions}
                  loading={teamTypesLoading}
                  error={
                    teamTypesError ||
                    (submitCount >= 1 && errors[`roleInput${index}`])
                  }
                  value={supportTeam[index].role}
                  placeholder="Select role"
                  errorIncreasesHeight
                  data-testid={`project-setup--support-team--select-role-${index}`}
                />
              </Col>
              <Col col={6}>
                <Select
                  name={`memberInput${index}`}
                  onChange={(value) => handleMemberChange(index, value)}
                  options={internalUsersOptions}
                  loading={internalUsers.loading}
                  disabled={!supportTeam[index].role}
                  error={
                    internalUsers.error ||
                    (submitCount >= 1 && errors[`memberInput${index}`])
                  }
                  value={supportTeam[index].member || supportTeam[index].userId}
                  placeholder="Select user"
                  data-testid={`project-setup--support-team--select-user-${index}`}
                  errorIncreasesHeight
                />
              </Col>
            </Row>
            {supportTeam.length > 1 && (
              <RemoveButton
                data-testid={`project-setup--support-team--remove-user-${index}`}
                onClick={() => handleRemove(index)}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  xmlnsXlink="http://www.w3.org/1999/xlink"
                  width="14"
                  height="14"
                  viewBox="0 0 14 14"
                >
                  <defs>
                    <path
                      id="a"
                      d="M1.742.312L7 5.569 12.258.312a1 1 0 0 1 1.414 0l.016.016a1 1 0 0 1 0 1.414L8.431 7l5.257 5.258a1 1 0 0 1 0 1.414l-.016.016a1 1 0 0 1-1.414 0L7 8.431l-5.258 5.257a1 1 0 0 1-1.414 0l-.016-.016a1 1 0 0 1 0-1.414L5.569 7 .312 1.742a1 1 0 0 1 0-1.414L.328.312a1 1 0 0 1 1.414 0z"
                    />
                  </defs>
                  <use
                    fill={theme.colors.lightGreyBlue}
                    fillRule="nonzero"
                    xlinkHref="#a"
                  />
                </svg>
              </RemoveButton>
            )}
          </Section>
        ))}
      </InputsContainer>
      <Button
        data-testid="project-setup--support-team--add-user"
        onClick={handleAddLine}
      >
        Add Member
      </Button>
    </>
  );
}

ProjectSupportTeamForm.propTypes = {
  theme: themePropTypes.isRequired,
};
