import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import {
  PreviewSectionTitle,
  PreviewSection,
  PreviewSectionItem,
  PreviewSectionText,
  EditButtonContainer,
  EditStepButton,
  Link,
} from '../styles';
import { PreviewContextProvider } from '../components/PreviewSectionText';
import { selectAllEager as selectAllPayRates } from '../../../store/ducks/payrates';

export default function ProjectToolsPreview(props) {
  const {
    hasExternalSystem,
    externalSystem,
    externalSystemUrl,
    hasIPRestrictions,
    ipAddresses,
    swfhProjectExternalUrl,
    workdayTaskId,
    externalLinkEnabled,
    hasExternalProjectSettings,
    isSWFHProject,
    editStep,
    stepNumber,
    renderedIn,
  } = props;

  const payrates = useSelector(selectAllPayRates);

  const renderExternalUrlComponent = (title, url, externalLink = false) => (
    <Col col={8}>
      <PreviewSectionItem>{title}</PreviewSectionItem>
      <PreviewSectionText>
        {externalLink ? (
          <Link href={url} target="_blank">
            Go to external system
          </Link>
        ) : (
          url
        )}
      </PreviewSectionText>
    </Col>
  );

  const renderCustomExternalUrlComponent = useCallback(
    (title, url) => (
      <Col col={8}>
        <PreviewSectionItem>{title}</PreviewSectionItem>
        <PreviewSectionText>
          {payrates.map((payrate) =>
            payrate.workdayTaskId ? (
              <>
                <Link
                  href={`${url}?workdayId=${payrate.workdayTaskId}`}
                  target="_blank"
                >
                  {payrate.workdayTaskId}
                </Link>
                <br />
              </>
            ) : null,
          )}
        </PreviewSectionText>
      </Col>
    ),
    [payrates],
  );

  const renderExternalURL = useCallback(() => {
    if (!isSWFHProject && !externalLinkEnabled && !hasExternalProjectSettings) {
      return null;
    }

    if (isSWFHProject) {
      return renderExternalUrlComponent(
        'SWFH Project URL',
        swfhProjectExternalUrl,
        true,
      );
    }

    if (externalLinkEnabled && hasExternalProjectSettings) {
      if (workdayTaskId) {
        return renderExternalUrlComponent(
          'External System Url',
          externalSystemUrl,
          true,
        );
      }
      return renderCustomExternalUrlComponent(
        'External System Url',
        externalSystemUrl,
      );
    }

    return null;
  }, [
    externalLinkEnabled,
    externalSystemUrl,
    hasExternalProjectSettings,
    isSWFHProject,
    renderCustomExternalUrlComponent,
    swfhProjectExternalUrl,
    workdayTaskId,
  ]);

  return (
    <>
      {editStep && (
        <PreviewSectionTitle>
          {`${stepNumber}`.padStart(2, '0')}: Project Tools
          <EditButtonContainer>
            {renderedIn === 'project-setup' && (
              <EditStepButton onClick={editStep}>Edit step</EditStepButton>
            )}
          </EditButtonContainer>
        </PreviewSectionTitle>
      )}

      <PreviewSection>
        <Row>
          <Col col={4}>
            <PreviewSectionItem>External Project</PreviewSectionItem>
            <PreviewContextProvider
              value={{ validateEmpty: hasExternalSystem }}
            >
              <PreviewSectionText>{externalSystem}</PreviewSectionText>
            </PreviewContextProvider>
          </Col>
          {renderExternalURL()}
        </Row>
      </PreviewSection>

      <PreviewSection>
        <PreviewSectionItem>Secure IP Access</PreviewSectionItem>
        {hasIPRestrictions ? (
          <PreviewSectionText>
            {'Allowed IPs: '}
            {ipAddresses}
          </PreviewSectionText>
        ) : (
          'Not enabled'
        )}
      </PreviewSection>
    </>
  );
}

ProjectToolsPreview.propTypes = {
  hasExternalSystem: PropTypes.bool,
  externalSystem: PropTypes.string,
  externalSystemUrl: PropTypes.string,
  hasIPRestrictions: PropTypes.bool,
  ipAddresses: PropTypes.string,
  swfhProjectExternalUrl: PropTypes.string,
  isSWFHProject: PropTypes.bool.isRequired,
  editStep: PropTypes.func,
  stepNumber: PropTypes.number,
  renderedIn: PropTypes.oneOf(['project-view', 'project-setup']),
  workdayTaskId: PropTypes.string,
  externalLinkEnabled: PropTypes.bool,
  hasExternalProjectSettings: PropTypes.bool,
};

ProjectToolsPreview.defaultProps = {
  hasExternalSystem: false,
  externalSystem: '',
  externalSystemUrl: '',
  hasIPRestrictions: false,
  ipAddresses: '',
  swfhProjectExternalUrl: '',
  editStep: undefined,
  stepNumber: 5,
  renderedIn: 'project-setup',
  workdayTaskId: '',
  externalLinkEnabled: false,
  hasExternalProjectSettings: false,
};
