import { connect } from 'react-redux';
import get from 'lodash/get';
import Recruitment from './RecruitmentPreview';
import * as localeTenants from '../../../store/ducks/localeTenants';
import {
  selectAll as selectAllHiringTargets,
  selectLoading,
} from '../../../store/ducks/hiringTargets';
import * as tenantsByCountry from '../../../store/ducks/tenantsByCountry';
import {
  findById,
  findByValue,
  findByNestedValue,
} from '../../../helpers/find';
import {
  ENTITIES,
  ENTITY_METADATA,
  selectors,
} from '../../../store/ducks/options';
import { selectRecruitment } from '../../../store/ducks/projectView';
import { buildInternalUserName } from '../../ProjectForms/RecruitmentForm/HiringTargets/helpers';

function mapLocaleTenant(state) {
  const countries = selectors.selectDataFor(ENTITIES.countries, null, state);
  const allTenants = tenantsByCountry.selectAll(state);

  return (t) => {
    const tenant = findById(allTenants, t.tenant);
    const country = findByValue(countries, t.country);

    return {
      ...t,
      tenant: get(tenant, 'name', ''),
      country: get(country, 'label', ''),
      tenantType: get(tenant, ['employmentType', 'label'], ''),
    };
  };
}

function mapHiringTarget(state) {
  const countries = selectors.selectDataFor(ENTITIES.countries, null, state);

  const localeLanguages = selectors.selectDataFor(
    ENTITIES.localeLanguages,
    null,
    state,
  );

  const internalUsers = selectors.selectDataFor(
    ENTITIES.internalUsers,
    null,
    state,
  );

  return (hiringTarget) => ({
    id: hiringTarget.id,
    hiringDeadline: hiringTarget.hiringDeadline,
    target: hiringTarget.target,
    metrics: hiringTarget.metrics,

    localeLanguage: (
      findByNestedValue(
        localeLanguages,
        hiringTarget.localeLanguage,
        'options',
      ) || {}
    ).label,

    localeDialect: (findByValue(countries, hiringTarget.localeDialect) || {})
      .label,

    localeLanguageTo: (
      findByNestedValue(
        localeLanguages,
        hiringTarget.localeLanguageTo,
        'options',
      ) || {}
    ).label,

    localeDialectTo: (
      findByValue(countries, hiringTarget.localeDialectTo) || {}
    ).label,

    restrictToCountry: (
      findByValue(countries, hiringTarget.restrictToCountry) || {}
    ).label,

    assignedOwner: buildInternalUserName(
      findById(internalUsers, hiringTarget.assignedOwner),
      8,
    ),
  });
}

function mapStateToProps(state) {
  const recruitment = selectRecruitment(state);

  const projects = selectors.selectDataFor(
    ENTITIES.projects,
    ENTITY_METADATA.projects.nestedKeys.ALL,
    state,
  );

  const {
    hasBlockingRules,
    blockingRules,
    hasIntelligentAttributes,
    hasLinkedProjects,
    linkedProjects,
    hasNotesToRecruiting,
    notesToRecruiting,
    profileTargetId,
  } = recruitment;

  return {
    localeTenants: localeTenants.selectAll(state).map(mapLocaleTenant(state)),

    hiringTargets: selectAllHiringTargets(state).map(mapHiringTarget(state)),
    loadingHiringTargets: selectLoading(state),

    hasBlockingRules,
    blockingRules: hasBlockingRules
      ? blockingRules
          .map((rule) => {
            const blockedProject = findById(projects, rule.projectId) || {};

            return {
              ...blockedProject,
              permanent: rule.permanent,
            };
          })
          .filter((project) => project.name && project.name.trim().length)
          .map(
            (project) =>
              `${project.name}${project.permanent ? ' (Permanent)' : ''}`,
          )
          .join(', ')
      : '',

    hasIntelligentAttributes,
    hasLinkedProjects,
    linkedProjects: hasLinkedProjects
      ? linkedProjects
          .map((link) => findById(projects, link.projectId) || {})
          .filter((project) => project.name && project.name.trim().length)
          .map((project) => project.name)
          .join(', ')
      : '',

    hasNotesToRecruiting,
    notesToRecruiting,
    profileTargetId,
  };
}

export default connect(mapStateToProps)(Recruitment);
