import React from 'react';
import PropTypes from 'prop-types';
import { Label } from '../../../styles';
import {
  BarIndicator,
  BarIndicatorContainer,
  ProcessStepContainer,
  ProcessStepsWrapper,
  ProcessStepsContainer,
  DropoffTooltipContent,
  StepNumber,
} from './styles';
// import { ReactComponent as ArrowDown } from '../../../assets/icons/arrow-down.svg';
import InformationTooltip from '../../ProcessBuilder/InformationTooltip';

function floatToPercent(floatNumber) {
  return parseFloat((floatNumber * 100).toFixed(2));
}

function getPercents(passed = 0, total = 1) {
  if (total === 0) {
    return { dropOff: '0%', conversion: '0%' };
  }

  const conversionRate = passed / total;
  const conversionRatePercent = floatToPercent(conversionRate);

  const dropOffRate = 1 - conversionRate;
  const dropOffRatePercent = floatToPercent(
    Math.min(Math.max(dropOffRate, 0), 100),
  );

  return {
    dropOff: `${dropOffRate > 100 ? '100' : dropOffRatePercent}%`,
    conversion: `${conversionRatePercent}%`,
  };
}

export default function DropRate(props) {
  const { steps, total } = props;

  return (
    <ProcessStepsWrapper>
      <Label>% passed per step</Label>

      <ProcessStepsContainer>
        {steps.map((step, index) => {
          // const currentTotal = index > 0 ? steps[index - 1].passed : total;

          const { /* dropOff, */ conversion } = getPercents(step.passed, total);

          return (
            <InformationTooltip
              title={
                <DropoffTooltipContent>
                  <span>
                    <strong>{`Passed: ${step.passed}`}</strong>
                    {` (of ${total})`}
                  </span>
                  <br />
                  Conversion: {conversion}
                  {/* <br />
                  Drop-off: {dropOff} */}
                </DropoffTooltipContent>
              }
            >
              <ProcessStepContainer key={step.id}>
                <StepNumber>Step {index + 1}</StepNumber>
                <BarIndicatorContainer>
                  <BarIndicator
                    style={{
                      width: conversion,
                    }}
                  />
                </BarIndicatorContainer>
                {/* <ArrowDown /> */}
                {conversion}
              </ProcessStepContainer>
            </InformationTooltip>
          );
        })}
      </ProcessStepsContainer>
    </ProcessStepsWrapper>
  );
}

export const DropRateStepPropTypes = PropTypes.shape({
  id: PropTypes.number.isRequired,
  passed: PropTypes.number.isRequired,
}).isRequired;

DropRate.propTypes = {
  steps: PropTypes.arrayOf(DropRateStepPropTypes).isRequired,
  total: PropTypes.number.isRequired,
};
