import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import Letter, { Img } from './styles';
import certificate from '../../../../../assets/icons/certificate.svg';
import { ENTITIES, selectors } from '../../../../../store/ducks/options';

export default function SpokenWrittenFluencyCell(props) {
  const {
    row: {
      original: {
        spokenFluency: spokenFluencyId,
        writtenFluency: writtenFluencyId,
        certifiedFluency,
      },
    },
  } = props;

  const writtenFluency = useSelector(
    selectors.createSelectById(ENTITIES.fluencies, null, writtenFluencyId),
  );

  const spokenFluency = useSelector(
    selectors.createSelectById(ENTITIES.fluencies, null, spokenFluencyId),
  );

  const writtenFluencyName = (writtenFluency || {}).label || '.';

  const spokenFluencyName = (spokenFluency || {}).label || '.';

  return (
    <div>
      {spokenFluency === writtenFluency ? (
        spokenFluencyName
      ) : (
        <>
          <Letter>S:</Letter>
          {`${spokenFluencyName}, `}
          <Letter>W:</Letter>
          {writtenFluencyName}
        </>
      )}
      {certifiedFluency && (
        <Img
          data-testid="payrates--certified-fluency-icon"
          src={certificate}
          alt="Requires certification"
        />
      )}
    </div>
  );
}

SpokenWrittenFluencyCell.propTypes = {
  row: PropTypes.shape({
    original: PropTypes.shape({
      spokenFluency: PropTypes.string.isRequired,
      writtenFluency: PropTypes.string.isRequired,
      certifiedFluency: PropTypes.bool,
    }).isRequired,
  }).isRequired,
};
