import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import useFirstChange from '../../helpers/useFirstChange';
import { setDirty } from '../../../../store/ducks/projectSetup';
import StepFooter from '../../../../components/StepFooter';
import RecruitmentForm from '../../../../components/ProjectForms/RecruitmentForm';
import { useNavigationContext } from '../../helpers/navigationContext';

export default function Recruitment(props) {
  const { values, submitForm, setFieldValue } = props;

  const { goBack } = useNavigationContext();

  function submitAndNavigate() {
    setFieldValue('navigate', true);
    submitForm();
  }

  const dispatch = useDispatch();

  useFirstChange(
    useCallback(() => dispatch(setDirty()), [dispatch]),
    values,
  );

  return (
    <div>
      <WizardTitle>Recruitment</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <RecruitmentForm />

      <WizardFooter>
        <StepFooter
          backAction={goBack}
          saveAction={submitForm}
          nextAction={submitAndNavigate}
          nextLabel="Next: Invoice & Payment"
        />
      </WizardFooter>
    </div>
  );
}

Recruitment.propTypes = {
  setFieldValue: PropTypes.func.isRequired,
  submitForm: PropTypes.func.isRequired,
  values: PropTypes.shape({
    hasBlockingRules: PropTypes.bool,
    blockingRules: PropTypes.arrayOf(PropTypes.object),
    hasIntelligentAttributes: PropTypes.bool,
    hasLinkedProjects: PropTypes.bool,
    linkedProjects: PropTypes.arrayOf(PropTypes.object),
    hasNotesToRecruiting: PropTypes.bool,
    notesToRecruiting: PropTypes.string,
    localeTenants: PropTypes.arrayOf(PropTypes.object),
    hiringTargets: PropTypes.arrayOf(PropTypes.object),
    showDemographics: PropTypes.bool,
    demographicsType: PropTypes.number,
    demographicsUnit: PropTypes.number,
    demographics: PropTypes.objectOf(PropTypes.any),
  }).isRequired,
};
