import { withFormik } from 'formik';
import { connect } from 'react-redux';
import { compose } from 'recompose';
import * as yup from 'yup';
import { requiredMessage } from '../../../pages/ProjectSetup/Steps/shared';
import {
  setConfiguration,
  selectInvoiceConfiguration,
  INVOICE_TYPES,
} from '../../../store/ducks/invoiceConfiguration';
import { withErrorPopover } from '../../ErrorPopover';
import InvoiceTemplateModal from './InvoiceTemplateModal';

function mapStateToProps(state, outerProps) {
  const { projectId } = outerProps;

  return {
    configuration: selectInvoiceConfiguration(state, projectId).configuration,
  };
}

function mapPropsToValues(outerProps) {
  const { configuration } = outerProps;

  if (configuration) {
    const {
      invoiceType,
      baseAutoGenerationOn,
      allowSelfReporting,
      autoApproveInvoicesBy,
      varianceThreshold,
      pmApprovalAtLineLevel,
    } = configuration;

    return {
      invoiceType,
      baseAutoGenerationOn,
      allowSelfReporting,
      autoApproveInvoicesBy,
      autoApproveInvoices: !!autoApproveInvoicesBy,
      varianceThreshold: varianceThreshold * 100,
      pmApprovalAtLineLevel,
    };
  }

  return {};
}

async function handleSubmit(values, { props }) {
  const { dispatch, projectId, configuration } = props;

  dispatch(
    setConfiguration({
      projectId,
      configuration: { ...configuration, ...values },
    }),
  );
}

export default compose(
  withErrorPopover,
  connect(mapStateToProps),
  withFormik({
    handleSubmit,
    mapPropsToValues,
    validationSchema: yup.object().shape({
      invoiceType: yup.string().required(requiredMessage`Invoice Type`),
      copyModulesFrom: yup.mixed().when('showCopyFromProcessSelect', {
        is: true,
        then: yup
          .mixed()
          .required(
            requiredMessage`When "Copy Modules From an Existing Process" is checked this`,
          ),
      }),
      baseAutoGenerationOn: yup.mixed().when('invoiceType', {
        is: (value) =>
          value === INVOICE_TYPES.SYSTEM_GENERATED ||
          value === INVOICE_TYPES.USER_SELF_REPORTED_AND_SYSTEM_APPROVED,
        then: yup
          .mixed()
          .required(
            requiredMessage`When "Invoice Type" is not "User self-reported" this`,
          ),
      }),
      autoApproveInvoicesBy: yup.mixed().when('autoApproveInvoices', {
        is: true,
        then: yup
          .mixed()
          .required(
            requiredMessage`When "Auto-approve Invoices" is checked this`,
          ),
      }),
      varianceThreshold: yup.mixed().when('autoApproveInvoices', {
        is: true,
        then: yup
          .mixed()
          .required(
            requiredMessage`When "Auto-approve Invoices" is checked this`,
          ),
      }),
    }),
  }),
)(InvoiceTemplateModal);
