import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import noop from 'lodash/noop';
import { useDispatch, useSelector } from 'react-redux';
import { Shimmer } from 'react-shimmer';
import { Label, Section } from '../../../../../styles';
import { HelpText, InternalNotes, EditLink } from './styles';
import { fetch, ENTITIES, selectors } from '../../../../../store/ducks/options';

export default function DocumentInformation(props) {
  const { overviewMode, documentId, editDocument } = props;

  // use documentId to fetch stuff

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetch({ optionsFor: ENTITIES.documents, nestedKey: documentId }));
  }, [dispatch, documentId]);

  const { loading: documentLoading, data: currentDocument } = useSelector(
    selectors.createSelectSliceFor(ENTITIES.documents, documentId),
  );

  const loading = useMemo(
    () => documentLoading && Object.keys(currentDocument).length === 0,
    [currentDocument, documentLoading],
  );

  const {
    createdBy: { firstName, lastName, email } = {},
    currentUserIsOwner,
    dateCreated,
    documentVersion,
    internalDescription,
    optional,
  } = currentDocument;

  const createdAt = useMemo(
    () => moment(dateCreated).format('MMMM D[,] YYYY [|] h:mm A'),
    [dateCreated],
  );

  return (
    <Section marginTop={overviewMode ? '' : '30px'} marginBottom="0px">
      <Label>
        {!loading && `Version ${documentVersion}`}
        {!loading && !optional ? ', REQUIRED' : ''}
        {loading && <Shimmer width={136} height={16} />}
      </Label>
      {!loading && !overviewMode && (
        <InternalNotes>{internalDescription}</InternalNotes>
      )}
      {loading && <Shimmer width={144} height={16} />}
      <HelpText>
        {!loading && (
          <>
            Created by{' '}
            <strong>
              {firstName} {lastName} ({email})
            </strong>{' '}
            on {createdAt}
          </>
        )}
        {loading && (
          <>
            <div style={{ height: 8 }} />
            <Shimmer width={500} height={12} />
          </>
        )}
      </HelpText>

      {!loading && !currentUserIsOwner && (
        <HelpText>
          Please contact document creator if you need to make any updates of the
          content of this electronic document.
        </HelpText>
      )}

      {loading && (
        <HelpText>
          <div style={{ height: 4 }} />
          <Shimmer width={440} height={12} />
        </HelpText>
      )}

      {!loading && !overviewMode && currentUserIsOwner && (
        <EditLink onClick={() => editDocument(documentId)}>
          Edit document
        </EditLink>
      )}
    </Section>
  );
}

DocumentInformation.propTypes = {
  overviewMode: PropTypes.bool,
  documentId: PropTypes.number.isRequired,
  editDocument: PropTypes.func,
};

DocumentInformation.defaultProps = {
  overviewMode: false,
  editDocument: noop,
};
