import { useCallback } from 'react';
import { useFormikContext } from 'formik';
import withHooks from 'hook-hoc';

import AccessHolderInput from './AccessHolderInput';

function useHooksAsProps() {
  const { values, setFieldValue } = useFormikContext();

  const handleChange = useCallback(
    (experimentGroupId, { value }) => {
      const { accessHolder } = values;

      const updatedAccessHolder = accessHolder.map((group) =>
        group.id === experimentGroupId
          ? {
              ...group,
              status: value,
            }
          : group,
      );

      setFieldValue('accessHolder', updatedAccessHolder);
    },
    [setFieldValue, values],
  );

  return {
    handleChange,
  };
}

export default withHooks(useHooksAsProps)(AccessHolderInput);
