import React from 'react';
import PropTypes from 'prop-types';
import Button from '../../../../components/Button';
import FloatContainer, { FloatRight } from '../../../../components/Float';
import Divider from '../../../../components/Divider/Divider';

export default function BottomControls(props) {
  const {
    exportLabel,
    exportLoading,
    exportDisabled,
    onExport,
    closeLabel,
    onClose,
  } = props;

  return (
    <>
      <Divider marginBottom={23} marginTop={56} />
      <FloatContainer>
        <FloatRight>
          <Button type="secondary" onClick={onClose}>
            {closeLabel}
          </Button>
          {!exportDisabled && (
            <Button
              onClick={onExport}
              disabled={exportDisabled}
              loading={exportLoading}
            >
              {exportLabel}
            </Button>
          )}
        </FloatRight>
      </FloatContainer>
    </>
  );
}

BottomControls.propTypes = {
  exportLabel: PropTypes.string.isRequired,
  onExport: PropTypes.func.isRequired,
  closeLabel: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired,
  exportDisabled: PropTypes.bool,
  exportLoading: PropTypes.bool,
};

BottomControls.defaultProps = {
  exportDisabled: true,
  exportLoading: false,
};
