import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { Shimmer } from 'react-shimmer';
import shortid from 'shortid';
import classNames from './styles.module.css';

export default function TabShimmer(props) {
  const { tabs } = props;

  const tabComponents = useMemo(
    () =>
      new Array(tabs || 4).fill(0).map(() => (
        <Shimmer
          key={shortid.generate()}
          className={classNames.tab_shimmer}
          height={40}
          /**
           *  Will be ignored because of max-width in className, here to
           * prevent prop-type error.
           */
          width={window.innerWidth}
        />
      )),
    [tabs],
  );

  return (
    <div className={classNames.tab_shimmer_container}>{tabComponents}</div>
  );
}

TabShimmer.propTypes = {
  tabs: PropTypes.number,
};

TabShimmer.defaultProps = {
  tabs: 4,
};
