import React from 'react';
import PropTypes from 'prop-types';
import { MetricCell, Count, Target } from './styles';

export default function InputCell(props) {
  const {
    cell: { value },
    row: {
      original: { viewAsPercent },
    },
  } = props;

  if (value) {
    const ratio = Math.min(((value.count || 0) / value.target) * 100, 100);
    const suffix = viewAsPercent ? '%' : '';
    const count = value.finalCount || 0;
    const target = value.finalTarget || 0;
    return (
      <MetricCell>
        <Count ratio={ratio}>{`${count}${suffix}`}</Count>
        <Target>{` of ${target}${suffix}`}</Target>
      </MetricCell>
    );
  }

  return <MetricCell>0</MetricCell>;

  // return (
  //   <InputBase
  //     key={hiringTargetID}
  //     placeholder="0"
  //     onChange={onChange}
  //     value={value || ''}
  //   />
  // );
}

InputCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  }).isRequired,
  row: PropTypes.shape({
    original: PropTypes.shape({
      handleFieldChange: PropTypes.func.isRequired,
      viewAs: PropTypes.string.isRequired,
      viewAsPercent: PropTypes.bool.isRequired,
    }).isRequired,
  }).isRequired,
  column: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
};
