import { call, put, takeEvery } from 'redux-saga/effects';
import { removeOne, undoLastAction } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import axios from '../../../services/axios';

function* removeHiringTargetsFn(action) {
  const { payload: id } = action;

  try {
    yield call(
      axios.delete,
      ENDPOINTS.V2.HIRING_TARGETS({
        pathVariables: {
          id,
        },
      }),
    );
  } catch (error) {
    yield put(undoLastAction());
  }
}

export default takeEvery(removeOne.type, removeHiringTargetsFn);
