import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import ExpandedCell from './ExpandedCell';
import CheckFrame from '../../../../components/CheckFrame';
import { MALICIOUSNESS } from '../../../../components/CheckFrame/helpers/checkFrameTypes';
import { Text } from './styles';

export default function LastUpdatedCell({ row }) {
  const {
    isExpanded,
    original: { dateUpdated = '' },
  } = row;

  const date = useMemo(
    () => moment(dateUpdated).format('MMM D[,] YYYY [|] h:mm A'),
    [dateUpdated],
  );

  return (
    <>
      <Text>{date}</Text>
      <ExpandedCell isExpanded={isExpanded}>
        <CheckFrame type={MALICIOUSNESS} user={row.original} />
      </ExpandedCell>
    </>
  );
}

LastUpdatedCell.propTypes = {
  row: PropTypes.shape({
    isExpanded: PropTypes.bool,
    original: PropTypes.shape({ dateUpdated: PropTypes.string }).isRequired,
  }).isRequired,
};
