import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import * as teamTypes from '../../../../store/ducks/teamTypes';
import { ReactComponent as MailIcon } from '../../../../assets/icons/mail.svg';
import { CardContentContainer, Link } from '../../styles';
import {
  TableHeader,
  SupportTeamTableRow,
  RowSpacer,
  TableContainer,
} from './styles';
import SupportTeamForm from './SupportTeamForm';
import NoEntries from '../../../../components/NoEntries';
import { ENTITIES, fetch } from '../../../../store/ducks/options';
import PostMessageLink from '../../../../components/PostMessageLink';

export default function SupportTeam(props) {
  const { mode, supportTeam } = props;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(teamTypes.fetchAll());
    dispatch(fetch(ENTITIES.internalUsers));
  }, [dispatch]);

  if (mode === 'edit') {
    return <SupportTeamForm />;
  }

  return (
    <>
      <CardContentContainer>
        {supportTeam.length <= 0 ? (
          <NoEntries label="No entries yet" />
        ) : (
          <TableContainer>
            <table style={{ width: '100%' }}>
              <thead>
                <tr>
                  <th>
                    <TableHeader>Support User Role</TableHeader>
                  </th>
                  <th>
                    <TableHeader>Member Name</TableHeader>
                  </th>
                  <th>
                    <TableHeader>Phone number</TableHeader>
                  </th>
                  <th>
                    <TableHeader>Email</TableHeader>
                  </th>
                  <th>{''}</th>
                </tr>
              </thead>
              <tbody>
                {supportTeam.map((member) => (
                  <>
                    <SupportTeamTableRow>
                      <td>{member.role}</td>
                      <td>
                        <PostMessageLink
                          to={`${process.env.REACT_APP_QRP_URL}/core/vendor/view/${member.id}`}
                        >
                          {member.member}
                        </PostMessageLink>
                      </td>
                      <td>{member.phone}</td>
                      <td>{member.email}</td>
                      <td>
                        <Link href={`mailto:${member.email}`} target="_blank">
                          <MailIcon />
                        </Link>
                      </td>
                    </SupportTeamTableRow>
                    <RowSpacer />
                  </>
                ))}
              </tbody>
            </table>
          </TableContainer>
        )}
      </CardContentContainer>
    </>
  );
}

SupportTeam.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  supportTeam: PropTypes.arrayOf(PropTypes.string).isRequired,
};
