import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import { Feature } from 'flagged';
import {
  WizardTitle,
  WizardDescription,
  WizardFooter,
} from '../../../../components/Wizard';
import ProjectOverview from './Sections/ProjectOverview';
import RegistrationAndQualification from './Sections/RegistrationAndQualification';
import ProjectTools from './Sections/ProjectTools';
import Recruitment from './Sections/Recruitment';
import InvoicePayment from './Sections/InvoicePayment';
import UserProjectPage from './Sections/UserProjectPage';
import SupportTeam from './Sections/SupportTeam';
import ProjectAccess from './Sections/ProjectAccess';
import { PreviewSectionDivider } from './styles';
import StepFooter from '../../../../components/StepFooter';
import Disabled from '../../../../components/Disabled';
import { selectCompletedPercentage } from '../../../../store/ducks/project';
import { hasExperimentGroups as selectHasExperimentGroups } from '../../../../store/ducks/experimentGroups';
import { useNavigationContext } from '../../helpers/navigationContext';
import ErrorPopover, {
  useErrorPopover,
} from '../../../../components/ErrorPopover';

export default function Preview(props) {
  const { submitForm } = props;

  const { navigate, goBack } = useNavigationContext();

  const percentCompleted = useSelector(selectCompletedPercentage);
  const hasExperimentGroups = useSelector(selectHasExperimentGroups);

  const [hideError] = useErrorPopover();

  return (
    <div>
      <WizardTitle>Preview</WizardTitle>
      <WizardDescription>Description</WizardDescription>

      <PreviewSectionDivider />
      <ProjectOverview navigateTo={navigate} />

      <PreviewSectionDivider />
      <RegistrationAndQualification navigateTo={navigate} />

      <PreviewSectionDivider />
      <Recruitment navigateTo={navigate} />

      <PreviewSectionDivider />
      <InvoicePayment navigateTo={navigate} />

      <PreviewSectionDivider />
      <ProjectTools navigateTo={navigate} />

      <Feature
        name="experimentGroups"
        render={
          <>
            <PreviewSectionDivider />
            <ProjectAccess navigateTo={navigate} />
          </>
        }
      />

      <PreviewSectionDivider />
      <UserProjectPage navigateTo={navigate} />

      <PreviewSectionDivider />
      <SupportTeam navigateTo={navigate} />

      <WizardFooter>
        {percentCompleted !== 100 && !hasExperimentGroups && (
          <ErrorPopover
            title="Some required fields are empty"
            hideError={hideError}
            errorShown
            error={{
              message:
                'You will need to create an Experiment Group for this project in order to configure internal access parameters.',
            }}
          />
        )}
        <Disabled
          when={percentCompleted !== 100}
          message="Some required fields are empty. Fill them to finish project setup."
          focusOffset={1}
        >
          <StepFooter
            backAction={goBack}
            nextAction={submitForm}
            nextLabel="Finish Project Setup"
          />
        </Disabled>
      </WizardFooter>
    </div>
  );
}

Preview.propTypes = {
  submitForm: PropTypes.func.isRequired,
};
