import React from 'react';
import PropTypes from 'prop-types';
import { RateContainer, DefaultPayrate, RateValue } from './styles';

export default function RateCell(props) {
  const {
    cell: { value },
    row: {
      original: { defaultRate },
    },
  } = props;

  return (
    <RateContainer>
      {defaultRate && <DefaultPayrate>D</DefaultPayrate>}
      <RateValue>{`$${(value || 1).toFixed(2)}`}</RateValue>
    </RateContainer>
  );
}

RateCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.number.isRequired,
  }).isRequired,
  row: PropTypes.shape({
    original: PropTypes.shape({
      defaultRate: PropTypes.bool.isRequired,
    }),
  }).isRequired,
};
