import { withFormik } from 'formik';
import shortid from 'shortid';
import { useCallback, useEffect, useMemo } from 'react';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import * as Yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import get from 'lodash/get';
import InternalQuizStep from './InternalQuizStep';
import {
  setProcessAttributes,
  loadStepById,
  createSelectStepDefinition,
} from '../../../../store/ducks/processBuilder';
import store from '../../../../store';
import { requiredMessage } from '../../../../pages/ProjectSetup/Steps/shared';
import createArrayValidation from '../../../../helpers/array-validation';
import mapToOptions from '../../../../pages/ProjectSetup/helpers/mapToOptions';

const initialFormValues = {
  quizes: [
    {
      quiz: null,
      maximumAttemps: '',
      unlimited: false,
      subsetSize: '',
      allQuestions: false,
      passingScore: '',
      key: shortid.generate(),
    },
  ],
};

function transformError(error, value, index) {
  switch (error.path) {
    case 'quiz':
      return new Yup.ValidationError(
        requiredMessage`Quiz`,
        value,
        `quiz${index}`,
      );

    case 'maximumAttemps':
      return new Yup.ValidationError(
        error.value === 0
          ? "Maximum attempts can't be zero"
          : requiredMessage`Maximum attempts`,
        value,
        `maximumAttemps${index}`,
      );

    case 'subsetSize':
      return new Yup.ValidationError(
        error.value === 0
          ? "Subset size can't be zero"
          : requiredMessage`Subset size`,
        value,
        `subsetSize${index}`,
      );

    case 'passingScore':
    default:
      return new Yup.ValidationError(
        requiredMessage`Passing score`,
        value,
        `passingScore${index}`,
      );
  }
}

const validateArray = createArrayValidation(
  {
    quiz: Yup.number().required(),
    maximumAttemps: Yup.number().when('unlimited', {
      is: (value) => !value,
      then: Yup.number().min(1).required(),
      otherwise: Yup.string().notRequired(),
    }),
    subsetSize: Yup.number().when('allQuestions', {
      is: (value) => !value,
      then: Yup.number().min(1).required(),
      otherwise: Yup.string().notRequired(),
    }),
    passingScore: Yup.string()
      .test('passingScore', 'error', (value) =>
        value ? !Number.isNaN(parseInt(value.replace('%', ''), 10)) : true,
      )
      .required(),
  },
  transformError,
);

function useHooksAsProps(outerProps) {
  const {
    setFieldValue,
    values: { quizes = [] },
    card: { stepDefinitionId },
  } = outerProps;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      loadStepById({
        stepDefinitionID: stepDefinitionId,
      }),
    );
  }, [dispatch, stepDefinitionId]);

  const stepDefinition = useSelector(
    createSelectStepDefinition(stepDefinitionId),
  );

  const quizesOptions = useMemo(() => {
    return mapToOptions(
      'id',
      'name',
    )(get(stepDefinition, 'attributes[0].options', []));
  }, [stepDefinition]);

  const selectedQuizes = useMemo(() => {
    return quizes
      .map((internalQuiz) => internalQuiz.quiz)
      .filter(Boolean)
      .map((quizId) => quizesOptions.find((q) => q.value === quizId))
      .map((quiz) => ({ ...quiz, key: shortid.generate() }));
  }, [quizes, quizesOptions]);

  const handleAddInternalQuiz = useCallback(() => {
    setFieldValue('quizes', [
      ...quizes,
      { ...initialFormValues.quizes[0], key: shortid.generate() },
    ]);
  }, [quizes, setFieldValue]);

  const handleRemoveInternalQuiz = useCallback(
    (index) => {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        ...quizes.slice(index + 1, quizes.length),
      ]);
    },
    [quizes, setFieldValue],
  );

  const createInputChangeHandler = (fieldName) => (index) => (changeEvent) => {
    const item = quizes[index];

    let value;

    if (changeEvent.target) {
      value = changeEvent.target.value;
    } else {
      value = changeEvent.value;
    }

    if (
      (fieldName === 'maximumAttemps' || fieldName === 'subsetSize') &&
      value &&
      Number.isNaN(parseInt(value, 10))
    ) {
      return;
    }

    if (
      (fieldName === 'maximumAttemps' || fieldName === 'subsetSize') &&
      value
    ) {
      value = parseInt(value, 10);
    }

    if (fieldName === 'quiz') {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        {
          ...item,
          quiz: value,
          maximumAttemps: '',
          unlimited: false,
          subsetSize: '',
          allQuestions: false,
          passingScore: '',
        },
        ...quizes.slice(index + 1, quizes.length),
      ]);
    } else {
      setFieldValue('quizes', [
        ...quizes.slice(0, index),
        { ...item, [fieldName]: value },
        ...quizes.slice(index + 1, quizes.length),
      ]);
    }
  };

  const handleQuizChange = createInputChangeHandler('quiz');
  const handleMaximumAttempsChange = createInputChangeHandler('maximumAttemps');
  const handleUnlimitedChange = createInputChangeHandler('unlimited');
  const handleSubsetSizeChange = createInputChangeHandler('subsetSize');
  const handleAllQuestionsChange = createInputChangeHandler('allQuestions');
  const handlePassingScoreChange = createInputChangeHandler('passingScore');

  return {
    handleAddInternalQuiz,
    handleRemoveInternalQuiz,
    handleQuizChange,
    handleMaximumAttempsChange,
    handleUnlimitedChange,
    handleSubsetSizeChange,
    handleAllQuestionsChange,
    handlePassingScoreChange,
    quizesOptions,
    selectedQuizes,
  };
}

function handleSubmit(values, { props }) {
  const {
    card: { id, cardKey },
    controls: { setDirty, setCollapsed },
  } = props;

  // TODO: Uncomment when backend support multiple quizes
  // store.dispatch(
  //   setProcessAttributes({
  //     id,
  //     key: cardKey,
  //     subSteps: (values.quizes || []).map((quiz) => ({
  //       attributes: [
  //         {
  //           attributeOrder: 1,
  //           value: quiz.quiz,
  //         },
  //         {
  //           attributeOrder: 2,
  //           value: quiz.unlimited ? '-1' : quiz.maximumAttemps,
  //         },
  //         {
  //           attributeOrder: 3,
  //           value: quiz.allQuestions ? '-1' : quiz.subsetSize,
  //         },
  //         { attributeOrder: 4, value: quiz.passingScore },
  //       ],
  //     })),
  //   }),
  // );

  const quiz = values.quizes[0];

  store.dispatch(
    setProcessAttributes({
      id,
      key: cardKey,
      dirty: false,
      attributes: [
        {
          attributeOrder: 1,
          value: quiz.quiz,
        },
        { attributeOrder: 2, value: `${quiz.passingScore}`.replace('%', '') },
        {
          attributeOrder: 3,
          value: quiz.unlimited ? '-1' : quiz.maximumAttemps,
        },
        {
          attributeOrder: 4,
          value: quiz.allQuestions ? '-1' : quiz.subsetSize,
        },
      ],
    }),
  );

  setDirty(false);
  setCollapsed(true);
}

function mapPropsToValues(props) {
  const {
    card: { attributes },
    // card: { subSteps },
  } = props;

  if (attributes && attributes.length) {
    const maximumAttemps = parseInt(
      get(
        attributes.find((att) => att.attributeOrder === 3),
        'value',
        0,
      ),
      10,
    );

    const subsetSize = parseInt(
      get(
        attributes.find((att) => att.attributeOrder === 4),
        'value',
        0,
      ),
      10,
    );

    return {
      quizes: [
        {
          quiz: parseInt(
            get(
              attributes.find((att) => att.attributeOrder === 1),
              'value',
              -1,
            ),
            10,
          ),
          maximumAttemps: maximumAttemps === -1 ? '' : maximumAttemps,
          unlimited: maximumAttemps === -1,
          subsetSize: subsetSize === -1 ? '' : subsetSize,
          allQuestions: subsetSize === -1,
          passingScore: `${get(
            attributes.find((att) => att.attributeOrder === 2),
            'value',
            null,
          )}`,
          key: shortid.generate(),
        },
      ],
    };
  }

  return initialFormValues;
}

export default compose(
  withFormik({
    handleSubmit,
    mapPropsToValues,
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
      quizes: Yup.array().test('internalQuizArray', '', validateArray),
    }),
  }),
  withHooks(useHooksAsProps),
)(InternalQuizStep);
