import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import EmbeddedDemographics from './EmbeddedDemographics';
import {
  fetchDemographics,
  selectRequirements,
  selectLoading,
} from '../../../store/ducks/projectDemographicRequirements';
import {
  fetchAll as fetchAllRequirements,
  selectAll,
} from '../../../store/ducks/demographicRequirements';
import { selectProject } from '../../../store/ducks/project';
import { useModal } from '../../../components/Modal';

function useHooksAsProps() {
  const [modalProps, modalControls] = useModal(true);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchDemographics());
    dispatch(fetchAllRequirements());
  }, [dispatch]);

  const parameters = useSelector(selectAll);
  const requirements = useSelector(selectRequirements);
  const loadingRequirements = useSelector(selectLoading);
  const project = useSelector(selectProject);

  return {
    modalControls,
    modalProps,
    project,
    parameters,
    requirements,
    loadingRequirements,
  };
}

export default withHooks(useHooksAsProps)(EmbeddedDemographics);
