import { call, put, takeLatest } from 'redux-saga/effects';
import { saveCategory, saveCategorySuccess, saveCategoryError } from '.';
import ENDPOINTS from '../../../constants/endpoints';
import { profileBuilderAPI } from '../../../services/axios';

function* saveCategorySagaFn(action) {
  const {
    payload: { changes: payload, thenPut },
  } = action;

  try {
    const update = !!payload.id;

    let endpoint = ENDPOINTS.PROFILE_BUILDER.TARGET_BUILDER_QUESTION_CATEGORIES();

    if (update) {
      endpoint = ENDPOINTS.PROFILE_BUILDER.TARGET_BUILDER_QUESTION_CATEGORIES({
        pathVariables: {
          id: payload.id,
        },
      });
    }

    const method = update ? profileBuilderAPI.put : profileBuilderAPI.post;
    const response = yield call(method, endpoint, payload);

    if (typeof thenPut === 'object') {
      yield put(thenPut);
    }

    yield put(saveCategorySuccess(response.data));
  } catch (error) {
    yield put(saveCategoryError(error));
  }
}

export default takeLatest(saveCategory.type, saveCategorySagaFn);
