/**
 * This file borrows most of it's code from axios-localstorage-adapter
 * See more: https://github.com/zphhhhh/axios-localstorage-adapter
 */
import axios from 'axios';

/**
 * @type {AxiosSessionStorageAdapterOptions}
 */
const options = {
  /** @type {string} cache name as sessionStorage's key's prefix */
  name: 'axios_cache',
  /** @type {number} default to 1 hour */
  maxAge: 60 * 60 * 1000,
  /** @type {string|RegExp|Function|(string|RegExp|Function)[]} urls to include */
  urlsToInclude: [],
  /** @type {boolean} if clear on sessionStorage's error */
  clearOnError: true,
  /** @type {AxiosAdapter} default adapter, default to axios.defaults.adapter */
  adapter: axios.defaults.adapter,
  /** @type {boolean} default to true to cache request, generally we may use it in development but not production */
  cache: true,
};

/**
 * @type {string} key for last-modified
 */
let KEY_LAST_MODIFIED = '';

/**
 * @type {string} key for url prefix
 */
let KEY_URL_PREFIX = '';

/**
 * @type {Object} like AxiosDefaultAdapter
 */
let adapter;

/**
 * if the url needs caching
 * @param {string} url request's url
 * @returns {boolean} return true if url needs cache
 */
function isUrlToInclude(url) {
  const { urlsToInclude } = options;
  if (urlsToInclude) {
    const urlList = Array.isArray(urlsToInclude)
      ? urlsToInclude
      : [urlsToInclude];
    const isMatched = urlList.some(
      (item) =>
        (typeof item === 'string' && url.startsWith(item)) ||
        (typeof item === 'function' && url.startsWith(item())) ||
        (Object.prototype.toString.call(item) === '[object RegExp]' &&
          item.test(url)),
    );
    return isMatched;
  }

  return false;
}

/**
 * clear the sessionStorage cache which prefix `options.name` to.
 */
function clearStorage() {
  Object.keys(sessionStorage)
    .filter((key) => key.startsWith(options.name))
    .forEach((key) => sessionStorage.removeItem(key));
}

/**
 * axios `get` with cache enhanced
 * @param {AxiosRequestConfig} config axiosConfig
 * @returns {AxiosPromise}
 */
async function requestGet(config) {
  try {
    const res = await options.adapter(config);
    const { data, headers, request } = res;
    // only cache it when request successful
    if (data) {
      let cachedData;
      let cachedHeaders;
      let cachedRequest;
      try {
        cachedData = JSON.parse(data);
        cachedHeaders = JSON.parse(headers);
        cachedRequest = JSON.parse(request);
      } catch (e) {
        cachedData = data;
        cachedHeaders = headers;
        cachedRequest = request;
      }

      if (typeof cachedData === 'string' || typeof cachedData === 'object') {
        try {
          const lastModified = new Date().getTime();
          const V_URL_NEW = JSON.stringify({
            lastModified,
            data: cachedData,
            headers: cachedHeaders,
            request: cachedRequest,
          });
          const V_LAST_MODIFIED_NEW = lastModified;
          sessionStorage.setItem(config.K_URL, V_URL_NEW);
          sessionStorage.setItem(KEY_LAST_MODIFIED, V_LAST_MODIFIED_NEW);
        } catch (e) {
          if (options.clearOnError) {
            clearStorage();
          }
        }
      }
    }
    return res;
  } catch (rej) {
    return rej;
  }
}

/**
 * @param {AxiosRequestConfig} config
 * @returns {AxiosPromise}
 */
async function cacheAdapter(config) {
  const { url, params = {}, method } = config;
  // only cache GET request, or return default adapter
  if (method.toLowerCase() !== 'get') {
    return options.adapter(config);
  }

  if (isUrlToInclude(url)) {
    // the key will be like `connect_web_url_/api/v2/countries`
    const K_URL = `${KEY_URL_PREFIX + url}?${Object.entries(params)
      .map(([k, v]) => `${k}=${v}`)
      .join('&')}`;
    const V_URL = sessionStorage.getItem(K_URL);

    config.K_URL = K_URL;

    // if cache none, just request continue
    if (!V_URL) {
      return requestGet(config);
    }

    try {
      // if there is a cache, determine whether cache could be used
      const lastRequest = JSON.parse(V_URL);
      if (lastRequest.lastModified + options.maxAge > new Date().getTime()) {
        // if cache not expired
        const response = {
          data: lastRequest.data,
          headers: lastRequest.headers,
          request: lastRequest.request,
          status: 200,
          statusText: 'OK',
          _fromSessionStorage: true,
          config,
        };
        return response;
      }
      // if cache expired
      sessionStorage.removeItem(K_URL);
    } catch (e) {
      // remove the item that is showing error
      sessionStorage.removeItem(K_URL);
    }

    return requestGet(config);
  }

  // return default adapter
  return options.adapter(config);
}

/**
 * Initial config
 */
function initAdapter() {
  if (!options.cache) {
    adapter = options.adapter;
    return;
  }

  try {
    if (typeof sessionStorage !== 'undefined') {
      sessionStorage.setItem('test', 't');
      sessionStorage.removeItem('test');
    }
  } catch (e) {
    adapter = options.adapter;
    return;
  }

  adapter = cacheAdapter;

  KEY_LAST_MODIFIED = `${options.name}_config_last_modified`;
  KEY_URL_PREFIX = `${options.name}_url_`;

  const V_LAST_MODIFIED = sessionStorage.getItem(KEY_LAST_MODIFIED);
  const expiredTime = parseInt(V_LAST_MODIFIED, 10) + options.maxAge;

  // clear all data if expired
  if (expiredTime < new Date().getTime()) {
    clearStorage();
  }
}

/**
 * init AxiosCacheAdapter and return the adapter
 * @param {AxiosSessionStorageAdapterOptions} opts custom options
 * @return {Object} axios adapter
 */
export default function AxiosCacheAdapter(opts) {
  Object.assign(options, opts);

  initAdapter();

  return adapter;
}
