import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Modal, ModalHeader, ModalBody, ModalFooter, ROLE } from 'baseui/modal';
import { H5, Body } from '@connect-ui/typography';
import Select from '../Select';
import Button from '../Button';
import Textarea from '../Textarea';
import { RowWrapper, Divider } from './styles';
import { REJECT_REASON, STAGE_REASON } from '../../constants/bulkUploadOptions';

const BulkModal = (props) => {
  const { open, setOpen, bulkOption } = props;
  const [selectedReason, setSelectedReason] = useState(undefined);
  const [notes, setNotes] = useState('');

  const handleCancel = () => {
    setOpen(false);
    setNotes('');
  };

  return (
    <Modal
      isOpen={open}
      onClose={() => setOpen(false)}
      closeable
      size={624}
      role={ROLE.dialog}
      unstable_ModalBackdropScroll
      overrides={{
        Dialog: {
          style: {
            borderBottomLeftRadius: '4px',
            borderBottomRightRadius: '4px',
            borderTopLeftRadius: '4px',
            borderTopRightRadius: '4px',
          },
        },
        Close: {
          style: {
            color: '#A2AAAD',
          },
        },
      }}
    >
      <ModalHeader>
        Bulk {bulkOption !== null ? bulkOption.label : 'modal'}
      </ModalHeader>
      <ModalBody>
        <RowWrapper>
          <Body color="GRAY_40">Action will be applied to</Body>
          <Body marginLeft="3px" weight="bold">
            3 workers
          </Body>
        </RowWrapper>
        {bulkOption && bulkOption.id !== 'complete' && (
          <>
            <H5
              style={{
                marginTop: '40px',
                marginBottom: '8px',
                color: '#4B4F54',
              }}
            >
              SELECT REASON
            </H5>
            <Select
              value={selectedReason}
              name="program"
              id="program"
              options={bulkOption.id === 'stage' ? STAGE_REASON : REJECT_REASON}
              placeholder="Select"
              onChange={({ value }) => {
                setSelectedReason(value);
              }}
            />
          </>
        )}
        <H5
          style={{
            marginTop: '30px',
            marginBottom: '8px',
            color: '#4B4F54',
          }}
        >
          INTERNAL NOTE (OPTIONAL)
        </H5>
        <Textarea
          onChange={(e) => setNotes(e.target.value)}
          name="notes"
          id="notes"
          value={notes}
          placeholder="Write note here "
          rows={5}
        />
        <Divider />
      </ModalBody>
      <ModalFooter>
        <Button type="secondary" onClick={() => handleCancel()}>
          Cancel
        </Button>
        <Button marginLeft={16} onClick={() => setOpen(false)}>
          Proceed
        </Button>
      </ModalFooter>
    </Modal>
  );
};

BulkModal.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired,
  bulkOption: PropTypes.shape({
    label: PropTypes.string,
    id: PropTypes.string,
    reasonOptions: PropTypes.arrayOf(
      PropTypes.shape({
        label: PropTypes.string,
        id: PropTypes.string,
      }),
    ),
  }).isRequired,
};

BulkModal.defaultProps = {};

export default BulkModal;
