import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { selectProjectTools } from '../../../../store/ducks/project';
import { Section } from '../../../../styles';
import { CardContentContainer, CardFooter } from '../../styles';
import AccessHolderInput from './AccessHolderInput';
import PostMessageLink from '../../../../components/PostMessageLink';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../../components/ErrorPopover';
import { setMode } from '../../../../store/ducks/projectView';
import {
  selectSaving,
  selectSaveError,
  hasExperimentGroups as selectHasExperimentGroups,
} from '../../../../store/ducks/experimentGroups';
import ActionFooter from '../../../../components/ActionFooter';

export default function ProjectAccess(props) {
  const { submitForm, showError, errorPopoverProps, mode } = props;

  const dispatch = useDispatch();

  const { hasExternalSystem } = useSelector(selectProjectTools);
  const hasExperimentGroups = useSelector(selectHasExperimentGroups);

  const cancelEditing = useCallback(() => dispatch(setMode('view')), [
    dispatch,
  ]);

  const saving = useSelector(selectSaving);
  const error = useSelector(selectSaveError);

  useEffect(() => {
    if (error) {
      showError(error);
    }
  }, [showError, error]);

  const externalMessage = 'View access holder on ';
  const internalMessage = hasExperimentGroups
    ? 'View experiment on '
    : 'Configure experiment on ';

  const accessHolderOnExperimentListLink = (
    <Section>
      {hasExternalSystem ? externalMessage : internalMessage}
      <PostMessageLink to="/qrp/core/partners/experiments">
        Experiment List
      </PostMessageLink>
      .
    </Section>
  );

  const editFooter = (
    <CardFooter>
      <ErrorPopover {...errorPopoverProps} errorShown={!!error} />
      <ActionFooter
        hideDivider
        rightButtons={[
          {
            text: 'Cancel',
            type: 'secondary',
            disabled: saving,
            onClick: cancelEditing,
          },
          {
            text: 'Save Changes',
            type: 'primary',
            loading: saving,
            onClick: submitForm,
          },
        ]}
      />
    </CardFooter>
  );

  if (hasExternalSystem) {
    return (
      <>
        <CardContentContainer>
          <Section marginBottom="56px">
            <AccessHolderInput mode={mode} />
          </Section>

          {accessHolderOnExperimentListLink}
        </CardContentContainer>
        {mode === 'edit' && editFooter}
      </>
    );
  }

  return (
    <>
      <CardContentContainer>
        <Section marginBottom="56px">
          <AccessHolderInput mode={mode} />
        </Section>
        {accessHolderOnExperimentListLink}
      </CardContentContainer>
      {mode === 'edit' && editFooter}
    </>
  );
}

ProjectAccess.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  dirty: PropTypes.bool.isRequired,
  submitCount: PropTypes.number.isRequired,
  submitForm: PropTypes.func.isRequired,
  showError: PropTypes.func.isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
};
