import React from 'react';
import capitalize from 'lodash/capitalize';
import get from 'lodash/get';
import ExpandedCell from './ExpandedCell';
import { CellPropTypes } from './helpers';

export default function AliasCell({ row }) {
  const {
    isExpanded,
    original: { workType, alias },
  } = row;

  return (
    <>
      {capitalize(get(workType, 'label', ''))}
      <ExpandedCell isExpanded={isExpanded} label="Alias">
        {alias}
      </ExpandedCell>
    </>
  );
}

AliasCell.propTypes = CellPropTypes;
