import React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import InvoicesPaymentPreview from '../../../../components/ProjectPreviews/InvoicePaymentPreviews';
import { CardContentContainer, StepName, Subtitle } from '../../styles';
import InvoiceAndPaymentForm from './InvoicesAndPaymentForm';
import { Section } from '../../../../styles';
import { selectValidateInvoiceConfig } from '../../../../store/ducks/projectView';

export default function InvoicesAndPayment(props) {
  const { mode } = props;

  const validateInvoiceConfig = useSelector(selectValidateInvoiceConfig);

  if (mode === 'edit') {
    return <InvoiceAndPaymentForm />;
  }

  return (
    <CardContentContainer>
      <InvoicesPaymentPreview
        renderedIn="project-view"
        validateInvoiceConfig={validateInvoiceConfig}
        payRatesHeader={
          <Section marginBottom={30}>
            <StepName>Pay Rates Setup</StepName>
            <Subtitle>
              The base rate per hour for work done in this project depending on
              Locale and Group.
            </Subtitle>
          </Section>
        }
        invoiceConfigurationHeader={
          <Section marginBottom={30}>
            <StepName>Invoice Configuration</StepName>
            <Subtitle>
              Configure invoice type, approval type, and payment.
            </Subtitle>
          </Section>
        }
      />
    </CardContentContainer>
  );
}

InvoicesAndPayment.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
};
