import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { DisabledContainer } from './styled';
import { focusNext } from '../../helpers/a11y';

export default function Disabled(props) {
  const {
    when,
    message,
    focusOffset,
    inLine,
    children,
    padding,
    opacity,
  } = props;

  const preventFocus = useCallback(
    (event) => {
      if (event && event.target) {
        focusNext(event.target, focusOffset);

        // remove itself from tabIndex until next re-render
        event.target.setAttribute('tabIndex', '-1');
      }
    },
    [focusOffset],
  );

  if (when) {
    return (
      <DisabledContainer
        inLine={inLine}
        onFocus={preventFocus}
        title={message}
        padding={padding}
        opacity={opacity}
      >
        {children}
      </DisabledContainer>
    );
  }
  return children;
}

Disabled.propTypes = {
  when: PropTypes.bool.isRequired,
  message: PropTypes.string,
  focusOffset: PropTypes.number,
  inLine: PropTypes.bool,
};

Disabled.defaultProps = {
  message: '',
  focusOffset: 2,
  inLine: false,
};
