import debounce from 'lodash/debounce';
import { useEffect, useState } from 'react';

function isAppenDomain(url) {
  return !!url.match(
    /^http(s)?:\/\/[A-Za-z0-9\-\\_\\.]+\.(appen\.com|integration\.cf3|sandbox\.cf3)/,
  );
}

export function getParentOrigin() {
  let url;

  if (process.env.NODE_ENV !== 'development') {
    url = isAppenDomain(document.referrer)
      ? document.referrer
      : process.env.REACT_APP_QRP_URL;
  } else {
    url = process.env.REACT_APP_QRP_URL;
  }

  const origin = `${url}`.match(/.+.(com|us)(:\d{2,4})?/g);

  if (origin && typeof origin[0] === 'string') {
    return origin[0];
  }

  return '';
}

function emitEvent(event, payload) {
  const parentOrigin = getParentOrigin();

  window.parent.postMessage(
    {
      type: event,
      payload,
    },
    parentOrigin,
  );
}

// EMITTERS

export function preventLeave() {
  emitEvent('PREVENT_LEAVE');
}

export function allowLeave() {
  emitEvent('ALLOW_LEAVE');
}

export function handleProjectCreated(projectId) {
  emitEvent('PROJECT_CREATED', projectId);
}

export function closeDemographics(projectId) {
  emitEvent('DEMOGRAPHICS_CLOSE', projectId);
}

export function deleteProject(projectId) {
  emitEvent('DELETE_PROJECT', { projectId });
}

export function cloneProject(projectId, customerId) {
  emitEvent('CLONE_PROJECT', {
    projectId,
    customerId,
  });
}

export function viewProject(projectId, disabled) {
  emitEvent('VIEW_PROJECT', {
    projectId,
    disabled,
  });
}

export function viewProjectExperiments(projectId) {
  emitEvent('VIEW_PROJECT_EXPERIMENTS', projectId);
}

export function createProject() {
  emitEvent('CREATE_PROJECT');
}

export function switchCustomer(customerId) {
  emitEvent('SWITCH_CUSTOMER', customerId);
}

export function loadProjectSetup(projectId) {
  emitEvent('LOAD_PROJECT_SETUP', { projectId });
}

export function openTracker(trackerId) {
  emitEvent('OPEN_TRACKER', { trackerId });
}

export function navigateTo(path = '') {
  emitEvent('NAVIGATE', path);
}

export function viewProjectList() {
  emitEvent('VIEW_PROJECT_LIST');
}

// Request Info from parent

export const REQUEST_TYPES = {
  currentCustomer: 'CURRENT_CUSTOMER',
  hasFinanceRole: 'HAS_FINANCE_ROLE',
};

const requests = {};

export function requestInformation(type, callback) {
  if (Array.isArray(requests[type])) {
    requests[type].push(callback);
  } else {
    requests[type] = [callback];
  }
  emitEvent('CURRENT_CUSTOMER', { type });
}

function handleResponse(event) {
  const { data } = event;

  if (!data || !data.type || !Array.isArray(requests[data.type])) {
    return;
  }

  requests[data.type].forEach((callback) =>
    setTimeout(() => callback(data.payload || data), 0),
  );

  delete requests[data.type];
}

window.addEventListener('message', handleResponse, false);

export function useParentValue(informationIdentifier) {
  const [value, setValue] = useState();
  const [loading, setLoading] = useState();
  const [error, setError] = useState();

  useEffect(() => {
    try {
      requestInformation(informationIdentifier, (data) => {
        setValue(data);
        setLoading(false);
      });
    } catch (err) {
      setLoading(false);
      setError(err);
    }
  }, [informationIdentifier]);

  return { value, loading, error };
}

// Send current height to parent

const root = document.getElementById('root');

export const updateParentHeight = debounce(
  () => {
    emitEvent('SET_HEIGHT', root.scrollHeight);
  },
  128,
  { leading: true, trailing: true },
);

if ('MutationObserver' in window) {
  new window.MutationObserver(updateParentHeight).observe(root, {
    attributes: false,
    childList: true,
    subtree: true,
  });
}
