import { createEntityAdapter, createSlice } from '@reduxjs/toolkit';
import undoable from 'redux-undo';

const hiringTargetAdapter = createEntityAdapter({
  selectId: (e) => e.id,
});

const slice = createSlice({
  name: 'hiringTargets',
  initialState: hiringTargetAdapter.getInitialState({
    loading: false,
    error: null,
  }),
  reducers: {
    fetchAll(state) {
      state.loading = true;
    },
    fetchFailed(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    setAll(state, action) {
      state.loading = false;

      return hiringTargetAdapter.setAll(state, action);
    },
    addOne: {
      reducer: hiringTargetAdapter.addOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    addMany: hiringTargetAdapter.addMany,
    removeOne: hiringTargetAdapter.removeOne,
    removeMany: hiringTargetAdapter.removeMany,
    removeAll: hiringTargetAdapter.removeAll,
    updateOne: {
      reducer: hiringTargetAdapter.updateOne,
      prepare: (payload, meta) => ({
        payload,
        meta,
      }),
    },
    updateMany: hiringTargetAdapter.updateMany,
    upsertOne: hiringTargetAdapter.upsertOne,
    upsertMany: hiringTargetAdapter.upsertMany,
  },
});

const {
  reducer,
  actions: {
    fetchAll,
    fetchFailed,
    setAll,
    addOne,
    addMany,
    removeOne,
    removeMany,
    removeAll,
    updateOne,
    updateMany,
    upsertOne,
    upsertMany,
  },
} = slice;

const UNDO_TYPE = 'hiringTargets/undo';

export function undoLastAction() {
  return {
    type: UNDO_TYPE,
  };
}

const undoableReducer = undoable(reducer, {
  limit: 1,
  undoType: UNDO_TYPE,
});

undoableReducer.key = 'hiringTargets';

export default undoableReducer;

const {
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
} = hiringTargetAdapter.getSelectors(
  (state) => state[undoableReducer.key].present,
);

export function selectLoading(state) {
  return state[undoableReducer.key].present.loading;
}

export function hasHiringTargets(state) {
  const ids = selectIds(state);

  return !!ids.length;
}

export function selectByMatchingProps(state, query = {}) {
  const all = selectAll(state);

  const hiringTarget = all.find((one) => {
    return Object.keys(query).every(
      (key) => !query[key] || query[key] === one[key],
    );
  });

  return hiringTarget;
}

export {
  fetchAll,
  fetchFailed,
  setAll,
  addOne,
  addMany,
  removeOne,
  removeMany,
  removeAll,
  updateOne,
  updateMany,
  upsertOne,
  upsertMany,
  selectIds,
  selectEntities,
  selectAll,
  selectTotal,
  selectById,
};

if (process.env.NODE_ENV === 'development' && window.Cypress) {
  window.hiringTargets = {
    setAll: (entries) => {
      if (window.ReduxStore) {
        window.ReduxStore.dispatch(setAll(entries));
      }
    },
  };
}

export function hasHiringStatistics(state) {
  const hiringTargets = selectAll(state);

  return hiringTargets.some(
    (hiringTarget) =>
      typeof hiringTarget.qualifying === 'number' &&
      typeof hiringTarget.active === 'number' &&
      typeof hiringTarget.progress === 'number',
  );
}
