import React from 'react';
import { Shimmer } from 'react-shimmer';
import PropTypes from 'prop-types';
import { Spacer } from './styles';

export default function ParagraphShimmer(props) {
  const { lines } = props;

  return (
    <div>
      {new Array(lines - 1).fill(0).map(() => (
        <div>
          <Shimmer width={420} height={14} />
          <Spacer />
        </div>
      ))}
      <Shimmer width={360} height={14} />
    </div>
  );
}

ParagraphShimmer.propTypes = {
  lines: PropTypes.number,
};

ParagraphShimmer.defaultProps = {
  lines: 3,
};
