import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'styled-bootstrap-grid';
import ProcessSteps from '../../../../components/ProcessSteps';
import { PROCESS_TYPE } from '../../../../store/ducks/processBuilder';
import { Section } from '../../../../styles';
import { CardContentContainer, StepName, Subtitle } from '../../styles';
import RegQuaProjectViewForm from './RegQuaProjectViewForm';
import PostMessageLink from '../../../../components/PostMessageLink';
import QualificationsGraph from '../../../../components/QualificationsGraph';
import { DropRateStepPropTypes } from '../../../../components/QualificationsGraph/DropRate/DropRate';
import PreviewModal from '../../../../components/ProcessBuilder/PreviewModal';
import { PreviewProvider } from '../../../../components/Builder/helpers/preview-context';

export default function RegistrationAndQualification(props) {
  const {
    mode,
    registrationSteps,
    qualificationSteps,
    registrationMetrics,
    qualificationMetrics,
  } = props;

  if (mode === 'edit') {
    return <RegQuaProjectViewForm />;
  }

  return (
    <CardContentContainer>
      <Section>
        <StepName>Registration Process</StepName>
        <Subtitle>
          Process workers will go through in order to register to a project.
        </Subtitle>
      </Section>

      <Section>
        <Row>
          <Col col={6}>
            <PreviewProvider processType={PROCESS_TYPE.REGISTRATION}>
              <ProcessSteps
                processName={registrationSteps.processName}
                steps={registrationSteps.steps}
                creatorName={registrationSteps.createdBy}
                createdAt={registrationSteps.dateCreated}
                loading={registrationSteps.loading}
                error={registrationSteps.error}
                processId={registrationSteps.processId}
                processType={PROCESS_TYPE.REGISTRATION}
              />
              <PreviewModal />
            </PreviewProvider>
          </Col>
          <Col col={6}>
            <QualificationsGraph
              processType={PROCESS_TYPE.REGISTRATION}
              total={registrationMetrics.total}
              inProgress={registrationMetrics.inProgress}
              registered={registrationMetrics.registered}
              rejected={registrationMetrics.rejected}
              abandoned={registrationMetrics.abandoned}
              steps={registrationMetrics.steps}
            />
          </Col>
        </Row>
      </Section>
      <Section marginBottom="124px">
        <p>
          Follow registration progress on{' '}
          <PostMessageLink to="/qrp/core/recruiting/project_qualifications">
            Project Qualifications
          </PostMessageLink>
          .{' '}
        </p>
      </Section>

      <Section>
        <StepName>Qualification Process</StepName>
        <Subtitle>
          Process workers will go through in order to qualify to a project.
        </Subtitle>
      </Section>

      <Section>
        <Row>
          <Col col={6}>
            <PreviewProvider processType={PROCESS_TYPE.QUALIFICATION}>
              <ProcessSteps
                processName={qualificationSteps.processName}
                steps={qualificationSteps.steps}
                creatorName={qualificationSteps.createdBy}
                createdAt={qualificationSteps.dateCreated}
                loading={qualificationSteps.loading}
                error={qualificationSteps.error}
                processId={qualificationSteps.processId}
                processType={PROCESS_TYPE.QUALIFICATION}
              />
              <PreviewModal />
            </PreviewProvider>
          </Col>
          <Col col={6}>
            <QualificationsGraph
              processType={PROCESS_TYPE.QUALIFICATION}
              total={qualificationMetrics.total}
              inProgress={qualificationMetrics.inProgress}
              registered={qualificationMetrics.registered}
              rejected={qualificationMetrics.rejected}
              abandoned={qualificationMetrics.abandoned}
              steps={qualificationMetrics.steps}
            />
          </Col>
        </Row>
      </Section>
      <Section marginBottom="124px">
        <p>
          Follow qualification progress on{' '}
          <PostMessageLink to="/qrp/core/recruiting/project_qualifications">
            Project Qualifications
          </PostMessageLink>
          .{' '}
        </p>
      </Section>
    </CardContentContainer>
  );
}

const stepsPropTypes = PropTypes.shape({
  processName: PropTypes.string,
  steps: PropTypes.arrayOf(PropTypes.shape({})),
  createdBy: PropTypes.string,
  dateCreated: PropTypes.number,
  loading: PropTypes.bool,
  error: PropTypes.instanceOf(Error),
  processId: PropTypes.number,
});

const metricsPropTypes = PropTypes.shape({
  abandoned: PropTypes.number.isRequired,
  inProgress: PropTypes.number.isRequired,
  registered: PropTypes.number.isRequired,
  rejected: PropTypes.number.isRequired,
  total: PropTypes.number.isRequired,
  steps: PropTypes.arrayOf(DropRateStepPropTypes).isRequired,
});

RegistrationAndQualification.propTypes = {
  mode: PropTypes.oneOf(['edit', 'view']).isRequired,
  registrationSteps: stepsPropTypes.isRequired,
  qualificationSteps: stepsPropTypes.isRequired,
  registrationMetrics: metricsPropTypes.isRequired,
  qualificationMetrics: metricsPropTypes.isRequired,
};
