import createEntitiesSlice, {
  OPTIONS_TYPE,
  OPTIONS_FETCH_MODES,
} from '../options/engine';
import ENDPOINTS from '../../../constants/endpoints';

export const DATA = {
  currentUser: 'currentUser',
};

const REDUCER_KEY = 'security';

const DATA_CONFIG = {
  // All customers signed up in the platform
  [DATA.currentUser]: {
    type: OPTIONS_TYPE.OBJECT,
    entityPK: 'id',
    url: ENDPOINTS.V2.AUTH_CURRENT_USER(),
    fetchMode: OPTIONS_FETCH_MODES.EAGER,
  },
};

const security = createEntitiesSlice(DATA_CONFIG, REDUCER_KEY);

export default security.reducer;

const { fetch, saga, selectors } = security;
export { fetch, saga, selectors };

export function userHasAnyRole(...roles) {
  return (state) => {
    const currentUser = selectors.selectDataFor(DATA.currentUser, null, state);
    if (
      currentUser &&
      currentUser.roles &&
      currentUser.roles.length &&
      roles &&
      roles.length
    ) {
      return roles.some((role) => currentUser.roles.includes(role));
    }
    return false;
  };
}

export function getCurrentUser() {
  return (state) => {
    return selectors.selectDataFor(DATA.currentUser, null, state);
  };
}
