import styled from 'styled-components';

export const Label = styled.label`
  position: relative;

  input {
    position: absolute;
    opacity: 0;
  }

  .overlay {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 20px;
    width: 20px;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid ${(props) => props.theme.colors.lightBlueGrey};
    transition: all 0.3s;
  }

  .icon {
    display: none;
    color: white;
    margin-left: 2.5px;
    margin-top: 5px;
  }

  .indeterminateIcon {
    display: none;
    color: white;
    position: relative;
    right: 1px;
  }

  input:indeterminate ~ .overlay {
    opacity: 1;
    background-color: ${(props) => props.theme.colors.lightBlueGrey};
    border: 1px solid ${(props) => props.theme.colors.lightBlueGrey};
  }

  input:indeterminate ~ .overlay .indeterminateIcon {
    display: block;
    transform: rotate(90deg);
  }

  input:checked ~ .overlay {
    opacity: 1;
    background-color: ${(props) => props.theme.colors.azure};
    border: 1px solid ${(props) => props.theme.colors.azure};
  }

  input:checked ~ .overlay .icon {
    display: block;
  }
`;

export const Checkbox = styled.input.attrs(() => ({
  type: 'checkbox',
}));
