import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import STEP_NUMBER from '../../../../../../constants/stepNumber';
import InvoicePaymentPreview from '../../../../../../components/ProjectPreviews/InvoicePaymentPreviews';

export default function InvoicePayment(props) {
  const { navigateTo, renderedIn } = props;

  const editStep = useCallback(() => {
    navigateTo(STEP_NUMBER.INVOICE_PAYMENT);
  }, [navigateTo]);

  return (
    <InvoicePaymentPreview
      stepNumber={STEP_NUMBER.INVOICE_PAYMENT + 1}
      editStep={editStep}
      renderedIn={renderedIn}
    />
  );
}

InvoicePayment.propTypes = {
  navigateTo: PropTypes.func.isRequired,
  renderedIn: PropTypes.oneOf([
    'project-preview-modal',
    'project-view',
    'project-setup',
  ]),
};

InvoicePayment.defaultProps = {
  renderedIn: 'project-setup',
};
