import styledEmotion from 'styled-components';
import { styled } from '@connect-ui/styles';

const Comp = styled('span', ({ $theme }) => ({
  fill: 'currentColor',
  color: $theme.colors.tickBorderDisabled,
  width: '36px',
  height: '36px',
  display: 'inline-block',
  lineHeight: '42px',
  textAlign: 'center',
  cursor: 'default',
  transition: 'color 170ms ease',
  outline: 'none',
  ':hover': {
    color: $theme.colors.tickBorderHover,
  },
}));
// eslint-disable-next-line import/prefer-default-export
export const IconContainer = styledEmotion(Comp)`
  fill: currentColor;
  color: ${(props) => props.$inheritColor && 'inherit'};
  width: 36px;
  height: ${(props) => (props.height ? props.height : '36px')};
  display: inline-block;
  line-height: 42px;
  text-align: center;
  cursor: ${(props) => (props.$isIconButton ? 'pointer' : 'default')};
  transition: color 170ms ease;
  outline: none;
  &:hover {
    color: ${(props) => {
      if (props.inheritColor) {
        return 'inherit';
      }
      return props.$isIconButton !== true && props.theme.colors.lightGreyBlue;
    }};
  }
`;
