import { put, takeLatest, call } from 'redux-saga/effects';
import { setModules, setModulesError, fetchModules } from '.';
import { profileBuilderAPI } from '../../../services/axios';
import endpoints from '../../../constants/endpoints';

function* fetchModulesSaga() {
  const endpoint = endpoints.PROFILE_BUILDER.TARGET_BUILDER_QUESTIONS();

  try {
    const response = yield call(profileBuilderAPI.get, endpoint);

    yield put(setModules(response.data));
  } catch (error) {
    yield put(setModulesError(error));
  }
}

export default takeLatest(fetchModules.type, fetchModulesSaga);
