import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'styled-bootstrap-grid';
import Modal from '../../../components/Modal';
import Input from '../../../components/Input';
import ActionFooter from '../../../components/ActionFooter';
import Textarea from '../../../components/Textarea';
import ErrorPopover, {
  ErrorPopoverProps,
} from '../../../components/ErrorPopover';
import { selectSaveError } from '../../../store/ducks/profileQuestions';
import { Section, Label } from '../../../styles';
import { Container, Footer } from './styles';

export default function CategoryModal(props) {
  const {
    modalProps,
    modalControls: { closeModal },

    // Formik
    values: { title, description },
    handleChange,
    submitForm,
    submitCount,
    showError,
    errors,
    errorPopoverProps,
  } = props;

  const error = useSelector(selectSaveError);

  useEffect(() => {
    if (submitCount > 0 && error) {
      showError(error);
    }
  }, [submitCount, error, showError]);

  const footerButtons = [
    {
      type: 'secondary',
      text: 'Cancel',
      onClick: closeModal,
    },
    {
      text: 'Save',
      onClick: submitForm,
    },
  ];

  return (
    <Modal
      open={modalProps.open}
      onExited={modalProps.onExited}
      onClose={closeModal}
      title="Add New Category"
    >
      <Container>
        <Section>
          <Row>
            <Col col={12}>
              <Label htmlFor="question">Category name</Label>
              <Input
                name="title"
                id="title"
                placeholder="Enter name"
                value={title}
                onChange={handleChange}
                error={submitCount >= 1 && errors.title}
              />
            </Col>
          </Row>
        </Section>
        <Section>
          <Row>
            <Col col={12}>
              <Label htmlFor="notes">Category Description</Label>
              <Textarea
                name="description"
                id="description"
                value={description}
                placeholder="Enter description"
                rows={3}
                onChange={handleChange}
                error={submitCount >= 1 && errors.description}
              />
            </Col>
          </Row>
        </Section>
      </Container>
      <ErrorPopover title="Problem while saving" {...errorPopoverProps} />
      <Footer>
        <ActionFooter hideDivider rightButtons={footerButtons} />
      </Footer>
    </Modal>
  );
}

CategoryModal.propTypes = {
  modalProps: PropTypes.shape({
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    onExited: PropTypes.func.isRequired,
  }).isRequired,
  modalControls: PropTypes.shape({
    closeModal: PropTypes.func.isRequired,
  }).isRequired,

  // Formik
  values: PropTypes.shape({
    title: PropTypes.string,
    description: PropTypes.string,
  }).isRequired,
  submitForm: PropTypes.func.isRequired,
  submitCount: PropTypes.number.isRequired,
  showError: PropTypes.func.isRequired,
  errors: PropTypes.objectOf(PropTypes.string).isRequired,
  errorPopoverProps: ErrorPopoverProps.isRequired,
  handleChange: PropTypes.func.isRequired,
};

CategoryModal.defaultProps = {};
