import { useMemo, useCallback } from 'react';
import { useSelector } from 'react-redux';
import withHooks from 'hook-hoc';
import set from 'lodash/setWith';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import shortid from 'shortid';
import AggregatedRequirements from './AggregatedDemographicsTable';
import { selectAll as selectAllHiringTargets } from '../../../../../../../store/ducks/hiringTargets';
import HiringTargetHeader from './HiringTargetHeader';
import InputCell from './InputCell';

function getLeafs(tree) {
  let leafs = [];
  const { groups } = tree;

  groups.forEach((group) => {
    if (group.leaf) {
      leafs.push(group);
    } else {
      leafs = [...leafs, ...getLeafs(group.value)];
    }
  });

  return leafs;
}

function useHooksAsProps(outerProps) {
  const { value, setAggregatedRequirementsValue } = outerProps;

  const hiringTargets = useSelector(selectAllHiringTargets);

  const handleFieldChange = useCallback(
    (path, groupID, hiringTargetId, nextFieldValue) => {
      let nextFieldNumber;

      if (nextFieldValue.length) {
        nextFieldNumber = parseInt(nextFieldValue, 10);

        if (Number.isNaN(nextFieldNumber)) {
          return;
        }
      }

      const nextValue = cloneDeep(value);
      let accessor = '';

      path.forEach(({ groupID: gID }) => {
        const requirement = accessor ? get(nextValue, accessor, {}) : nextValue;

        const groupIndex = requirement.groups.findIndex(
          (group) => group.id === gID,
        );

        accessor = `${
          accessor ? `${accessor}.` : ''
        }groups[${groupIndex}].value`;
      });

      const requirement = accessor ? get(nextValue, accessor, {}) : nextValue;

      const groupIndex = requirement.groups.findIndex(
        (group) => group.id === groupID,
      );

      accessor = accessor
        ? `${accessor}.groups[${groupIndex}].hiringTargets.${hiringTargetId}`
        : `groups[${groupIndex}].hiringTargets.${hiringTargetId}`;

      setAggregatedRequirementsValue(
        set(nextValue, accessor, `${nextFieldNumber || nextFieldValue || ''}`),
      );
    },
    [setAggregatedRequirementsValue, value],
  );

  const standInColumns = useMemo(() => {
    let searchTree = { ...value };
    const columns = [];
    const data = [];
    const lengths = [];
    let multiplier = 1;

    while (!searchTree.groups.find((g) => g.leaf)) {
      columns.push({
        key: searchTree.id,
        accessor: 'label',
        Header: searchTree.label,
        disableSortBy: true,
      });

      const columnData = [];
      const groups = [].concat(
        ...new Array(multiplier).fill(searchTree.groups),
      );

      groups.forEach((group) => {
        columnData.push({
          label: group.label,
          groupID: group.id,
        });
      });

      data.push(columnData);
      lengths.push(searchTree.groups.length);

      multiplier = searchTree.groups.length;
      searchTree = searchTree.groups[0].value;
    }

    return columns.map((column, index) => [
      [column],
      data[index],
      lengths[index],
      shortid.generate(),
    ]);
  }, [value]);

  const columns = useMemo(() => {
    let searchTree = { ...value };

    while (!searchTree.groups.find((g) => g.leaf)) {
      searchTree = searchTree.groups[0].value;
    }

    return [
      {
        key: 'colLabel3',
        accessor: 'label',
        Header: searchTree.label,
        maxWidth: 80,
        disableSortBy: true,
        sticky: true,
      },
      ...hiringTargets.map((hiringTarget) => ({
        key: 'hiringTargetId',
        accessor: `${hiringTarget.id}`,
        disableSortBy: true,
        Header: HiringTargetHeader,
        Cell: InputCell,
        hiringTarget,
      })),
    ];
  }, [hiringTargets, value]);

  const tableData = useMemo(() => {
    const searchTree = { ...value };
    const leafs = getLeafs(searchTree);

    return leafs.map((group, index) => {
      const path = standInColumns.map(([, rows]) => {
        const splitPoint = leafs.length / rows.length;
        return rows[Math.floor(index / splitPoint)];
      });

      return {
        id: group.id,
        label: group.label,
        handleFieldChange: (hiringTargetId, nextValue) =>
          handleFieldChange(path, group.id, hiringTargetId, nextValue),
        ...group.hiringTargets,
      };
    });
  }, [handleFieldChange, standInColumns, value]);

  return {
    standInColumns,
    columns,
    tableData,
  };
}

export default withHooks(useHooksAsProps)(AggregatedRequirements);
