import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import Switch from '../../../../Switch';
import Container from './styles';

export default function EnabledCell(props) {
  const {
    cell: { value },
    row: {
      values: { id },
      original: { canEditPayRate },
    },
    initialState: { toggleEnabled },
    toggleAllRowsSelected,
  } = props;

  const handleChange = useCallback(
    (newValue) => toggleEnabled(id, newValue, toggleAllRowsSelected),
    [id, toggleEnabled, toggleAllRowsSelected],
  );

  return (
    <Container>
      <Switch
        name={`${id}-switch`}
        value={value}
        key={id}
        onChange={handleChange}
        data-testid={`payrates-table--enabled-toggle-${id}`}
        disabled={!canEditPayRate}
      />
    </Container>
  );
}

EnabledCell.propTypes = {
  cell: PropTypes.shape({
    value: PropTypes.bool,
  }).isRequired,
  row: PropTypes.shape({
    values: PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
    original: PropTypes.shape({
      canEditPayRate: PropTypes.bool,
    }),
  }).isRequired,
  initialState: PropTypes.shape({
    toggleEnabled: PropTypes.func.isRequired,
  }).isRequired,
  toggleAllRowsSelected: PropTypes.func.isRequired,
};

EnabledCell.defaultProps = {};
