import * as Yup from 'yup';
import { requiredMessage } from '../../../ProjectSetup/Steps/shared';
import createArrayValidation from '../../../../helpers/array-validation';

function transformError(error, value, index) {
  return new Yup.ValidationError(
    'Answer item is required',
    value,
    `answerList${index}`,
  );
}

const validateArray = createArrayValidation(
  {
    answer: Yup.string().required(requiredMessage`Answer item`),
  },
  transformError,
);

export default Yup.object()
  .strict()
  .shape({
    name: Yup.string().required(requiredMessage`Topic name`),
    timer: Yup.number()
      .nullable()
      .required(requiredMessage`Lock-up period`),
    question: Yup.string().required(requiredMessage`Question text`),
    notes: Yup.mixed().when('addQuestionNotes', {
      is: true,
      then: Yup.string().required(requiredMessage`Notes`),
    }),
    questionType: Yup.string()
      .nullable()
      .required(requiredMessage`Answer type`),
    parentQuestionId: Yup.mixed().when('nestQuestion', {
      is: true,
      then: Yup.number()
        .nullable()
        .required(requiredMessage`Primary question`),
    }),
    logic: Yup.mixed().when('nestQuestion', {
      is: true,
      then: Yup.number()
        .nullable()
        .required(requiredMessage`Logic`),
    }),
    parentAnswerId: Yup.mixed().when('nestQuestion', {
      is: true,
      then: Yup.number()
        .nullable()
        .required(requiredMessage`Answer`),
    }),
    answerList: Yup.mixed().when('questionType', {
      is: (value) => ['MULTIPLE_CHOICE', 'CHECKBOX_GROUP'].includes(value),
      then: Yup.array().test('answerList', '', validateArray),
      otherwise: Yup.mixed().notRequired(),
    }),
    // TODO: Check if this field is required or not
    // answerStyle: Yup.mixed().when('questionType', {
    //   is: (value) => !['YES_NO', 'DATE_PICKER'].includes(value),
    //   then: Yup.string().required(requiredMessage`Answer`),
    //   otherwise: Yup.mixed().notRequired(),
    // }),
  });
