import React from 'react';
import PropTypes from 'prop-types';
import Popover, {
  PopoverItem,
  PopoverIcon,
  PopoverText,
} from '../../../../../Popover';
import SettingsHoverable from '../../../../../Icons/SettingsHoverable';
import { Container } from './styles';
import pencil from '../../../../../../assets/icons/pencil.svg';
import close from '../../../../../../assets/icons/close.svg';

export default function SettingsCell(props) {
  const {
    row: {
      values: { id },
    },
    initialState: { handleEditConfiguration, handleDeleteConfiguration },
  } = props;

  const actions = [
    {
      src: pencil,
      alt: 'Edit configuration',
      text: 'Edit',
      handler: () => handleEditConfiguration(id),
    },
    {
      src: close,
      alt: 'Delete configuration',
      text: 'Clear',
      handler: () => handleDeleteConfiguration(id),
    },
  ];

  const tooltipContent = actions.map((action) => (
    <PopoverItem
      onClick={action.handler}
      onKeyDown={action.handler}
      role="button"
      tabIndex={0}
      key={action.text}
    >
      <PopoverIcon src={action.src} alt={action.alt} />
      <PopoverText>{action.text}</PopoverText>
    </PopoverItem>
  ));

  return (
    <Container className="settings-icon-container">
      <Popover content={tooltipContent} placement="bottom-end">
        <SettingsHoverable width={17} height={22} lineHeight={22} />
      </Popover>
    </Container>
  );
}

SettingsCell.propTypes = {
  row: PropTypes.shape({
    values: PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  }).isRequired,
  initialState: PropTypes.shape({
    handleEditConfiguration: PropTypes.func.isRequired,
    handleDeleteConfiguration: PropTypes.func.isRequired,
  }).isRequired,
};
