import { withFormik } from 'formik';
import { useSelector } from 'react-redux';
import { compose } from 'recompose';
import withHooks from 'hook-hoc';
import { useCallback, useMemo } from 'react';
import DemographicData from './DemographicData';
import store from '../../../../../../store';
import {
  hasRequirementsByGroup,
  selectRequirementsByGrup,
  selectRequirements,
  selectDemographicType,
  setRequirementsByGroup,
  DEMOGRAPHIC_TYPE,
  selectViewAs,
  DEMOGRAPHIC_VIEW_AS,
  hasAggregatedRequirements,
  selectAggregatedRequirements,
  setAggregatedRequirements,
} from '../../../../../../store/ducks/projectDemographicRequirements';
import { selectAll as selectAllHiringTargets } from '../../../../../../store/ducks/hiringTargets';
import { selectAll as selectAllRequirements } from '../../../../../../store/ducks/demographicRequirements';
import { toPercent } from '../../helpers';
import { withErrorPopover } from '../../../../../ErrorPopover';
import resetForm from '../../../../../../pages/ProjectSetup/helpers/resetForm';

function countAggregatedGroups(tree) {
  let searchTree = { ...tree };
  let groups = 1;

  while (!searchTree.groups.find((g) => g.leaf)) {
    groups *= searchTree.groups.length;
    searchTree = searchTree.groups[0].value;
  }

  return groups * searchTree.groups.length;
}

// Formik Functionality

export function getOrInitializeRequirementsByGroup() {
  const state = store.getState();

  if (hasRequirementsByGroup(state)) {
    return selectRequirementsByGrup(state);
  }

  const allRequirements = selectAllRequirements(state);
  const selectedRequirements = selectRequirements(state);
  const hiringTargets = selectAllHiringTargets(state);

  const groups = [];

  selectedRequirements.forEach((requirement) => {
    const completeRequirement = allRequirements.find(
      (r) => r.id === requirement.value,
    );

    completeRequirement.groups.forEach((g) => {
      const group = {
        requirementID: completeRequirement.id,
        groupID: g.id,
        hiringTargets: {},
      };

      hiringTargets.forEach((ht) => {
        group.hiringTargets[ht.id] = 0;
      });

      groups.push(group);
    });
  });

  return groups;
}

function createAggregatedRequirements(
  requirements,
  levels,
  hiringTargets,
  calculateInitialValue,
) {
  if (requirements.length <= 0) {
    return {};
  }

  const requirement = requirements[requirements.length - levels];

  return {
    id: requirement.id,
    label: requirement.name,
    levels,
    groups: requirement.groups.map((g) => {
      const group = {
        id: g.id,
        label: g.label,
        leaf: levels === 1,
      };

      if (group.leaf) {
        group.hiringTargets = {};

        hiringTargets.forEach((ht) => {
          group.hiringTargets[ht.id] =
            typeof calculateInitialValue === 'function'
              ? calculateInitialValue(parseInt(ht.target, 10))
              : 0;
        });
      } else {
        group.value = createAggregatedRequirements(
          requirements,
          levels - 1,
          hiringTargets,
          calculateInitialValue,
        );
      }

      return group;
    }),
  };
}

export function getOrInitializeAggregatedRequirements() {
  const state = store.getState();

  if (hasAggregatedRequirements(state)) {
    return selectAggregatedRequirements(state);
  }

  const allRequirements = selectAllRequirements(state);
  const selectedRequirements = selectRequirements(state);
  const hiringTargets = selectAllHiringTargets(state);

  return createAggregatedRequirements(
    selectedRequirements.map((requirement) =>
      allRequirements.find((r) => r.id === requirement.value),
    ),
    selectedRequirements.length,
    hiringTargets,
  );
}

function mapPropsToValues(outerProps) {
  const { modalData } = outerProps;

  if (modalData) {
    return modalData;
  }

  return {
    requirementsByGroup: getOrInitializeRequirementsByGroup(),
    aggregatedRequirements: getOrInitializeAggregatedRequirements(),
    distributedEvenly: false,
  };
}

function handleSubmit(values, formikBag) {
  const {
    props: { closeModal, showError, hideError },
  } = formikBag;

  const demographicType = selectDemographicType(store.getState());

  return new Promise((resolve) => {
    function onSuccess() {
      hideError();
      closeModal();

      setTimeout(() => {
        resetForm(
          formikBag,
          {
            lenguage: '',
            dialect: '',
            country: '',
            owner: '',
          },
          resolve,
        );
      }, 250);
    }

    function onError(message) {
      showError(new Error(message));

      setImmediate(() => resolve());
    }

    if (demographicType === DEMOGRAPHIC_TYPE.BY_GROUP) {
      store.dispatch(
        setRequirementsByGroup(values.requirementsByGroup, {
          onSuccess,
          onError,
        }),
      );
    } else {
      store.dispatch(
        setAggregatedRequirements(values.aggregatedRequirements, {
          onSuccess,
          onError,
        }),
      );
    }
  });
}

// Hooks

function useHooksAsProps(outerProps) {
  const { setFieldValue, values, errors } = outerProps;

  const allRequirements = useSelector(selectAllRequirements);
  const selectedRequirements = useSelector(selectRequirements);

  const setRequirementsByGroupValue = useCallback(
    (value) => {
      setFieldValue('requirementsByGroup', value);

      if (values.distributedEvenly) {
        setFieldValue('distributedEvenly', false);
      }
    },
    [setFieldValue, values.distributedEvenly],
  );

  const demographicType = useSelector(selectDemographicType);
  const viewAs = useSelector(selectViewAs);
  const hiringTargets = useSelector(selectAllHiringTargets);
  const requirementHeaders = selectedRequirements.map((sr) =>
    allRequirements.find((r) => r.id === sr.value),
  );

  const setAggregatedRequirementsValue = useCallback(
    (value) => {
      setFieldValue('aggregatedRequirements', value);

      if (values.distributedEvenly) {
        setFieldValue('distributedEvenly', false);
      }
    },
    [setFieldValue, values.distributedEvenly],
  );

  const distributeEvenly = useCallback(() => {
    if (demographicType === DEMOGRAPHIC_TYPE.BY_GROUP) {
      const nextValue = [];

      requirementHeaders.forEach((requirement) => {
        requirement.groups.forEach((g) => {
          const group = {
            requirementID: requirement.id,
            groupID: g.id,
            hiringTargets: {},
          };

          hiringTargets.forEach((hiringTarget) => {
            group.hiringTargets[hiringTarget.id] = Math.floor(
              parseInt(hiringTarget.target, 10) /
                (requirement.groups.length || 0),
            );

            if (viewAs === DEMOGRAPHIC_VIEW_AS.PERCENTAGE) {
              group.hiringTargets[hiringTarget.id] = toPercent(
                group.hiringTargets[hiringTarget.id],
                parseInt(hiringTarget.target, 10),
              );
            }
          });

          nextValue.push(group);
        });
      });

      setRequirementsByGroupValue(nextValue);
    } else {
      const tree = createAggregatedRequirements(
        requirementHeaders,
        requirementHeaders.length,
        hiringTargets,
      );

      const groups = countAggregatedGroups(tree);

      setAggregatedRequirementsValue(
        createAggregatedRequirements(
          requirementHeaders,
          requirementHeaders.length,
          hiringTargets,
          (target) => {
            let value = Math.floor(target / groups);

            if (viewAs === DEMOGRAPHIC_VIEW_AS.PERCENTAGE) {
              value = toPercent(value, target);
            }

            return value;
          },
        ),
      );
    }
    setFieldValue('distributedEvenly', true);
  }, [
    demographicType,
    hiringTargets,
    requirementHeaders,
    setAggregatedRequirementsValue,
    setFieldValue,
    setRequirementsByGroupValue,
    viewAs,
  ]);

  const submittionError = useMemo(() => {
    return errors.submittionError ? new Error(errors.submittionError) : null;
  }, [errors.submittionError]);

  return {
    setRequirementsByGroupValue,
    setAggregatedRequirementsValue,
    requirementHeaders,
    demographicType,
    distributeEvenly,
    submittionError,
  };
}

export default compose(
  withErrorPopover,
  withFormik({
    mapPropsToValues,
    handleSubmit,
  }),
  withHooks(useHooksAsProps),
)(DemographicData);
